import React, {useState} from 'react';
import {Route, Switch} from 'react-router-dom';

// import Dashboard from '@pages/Dashboard';
import DashboardContainer from '@app/containers/dashboard/DashboardContainer';
import BulkContainer from '@app/containers/bulk/BulkContainer';
import BulkDetailContainer from '@app/containers/bulk/BulkDetailContainer';
import ServerContainer from '@app/containers/server/ServerContainer';
import ScenarioContainer from '@app/containers/scenario/ScenarioContainer';
import UitestContainer from '@app/containers/uitest/UitestContainer';
import LayoutListContainer from '@app/containers/layout/LayoutListContainer';
