/* eslint-disable no-unused-vars */
import React from 'react';
import Table from '../../components/table/Table';
import Button from '../../components/button/Button';
import {FaSearch} from 'react-icons/fa';
import {useTranslation} from 'react-i18next';
import {getSuccessImage} from '../../lib/commonUiUtils';
import {changeToDate} from '../../lib/moment';

const ScHistoryTable = ({data, movePage, onClickDetail}) => {
    const [t] = useTranslation();

    const tableName = t('pages.scenario.scHistoryList');
    const rowKey = 'recordId';

    const columns = [
        {
            title: t('pages.scenario.scenarioName'),
            key: 'scenarioName',
            align: 'left' // left, center, right (default:center)
        },
        {
            title: ' ',
            key: 'testResult',
            align: 'center',
            render: (id, row) => {
                if (row.recordResult.isSuccess) {
                    return (
                        <Button
                            theme="success"
                            style={{
                                minWidth: '80px',
                                maxHeight: 'calc(3.5rem + 2px)'
                            }}
                        >
                            
                        </Button>
                    );
                }
                return (
                    <Button
                        theme="danger"
                        style={{
                            minWidth: '80px',
                            maxHeight: 'calc(3.5rem + 2px)'
                        }}
                    >
                        
                    </Button>
                );
            }
        },
        {
            title: 'AP ',
            key: 'wasServerName',
            align: 'center' // left, center, right (default:center)
        },
        {
            title: 'Database',
            key: 'dbServerName',
            align: 'center' // left, center, right (default:center)
        },
        {
            title: t('pages.mass.startTime'),
            width: '10%',
            key: 'startTime',
            render: (id, row) => {
                return changeToDate(row.recordResult.startDateTime);
            }
        },

        {
            title: t('pages.mass.executeSecond'),
            key: 'executeSecond',
            width: '5%',
            // eslint-disable-next-line no-unused-vars
            render: (id, row, column) => {
                return `${Math.round(row.recordResult.processTime)} ${t(
                    'common.text.second'
                )}`;
            }
        },

        // {
        //     title: t('pages.scenario.caseCnt'),
        //     // eslint-disable-next-line no-unused-vars
        //     render: (id, row, column) => {
        //         return `${row.caseCnt} ${t('common.text.cases')}`;
        //     }
        // },
        // {
        //     title: t('common.text.createdDate'),
        //     key: 'createdDate',
        //     align: 'left' // left, center, right (default:center)
        // },
        // {
        //     title: t('common.text.modifiedDate'),
        //     key: 'modifiedDate',
        //     align: 'left'
        // },
        {
            title: '',
            key: 'button',
            align: 'right',
            width: '5%',
            // eslint-disable-next-line no-unused-vars
            render: (id, row, column) => {
                return (
                    <>
                        <Button
                            theme="outline-success"
                            onClick={() => onClickDetail(id)}
                            // style={{float: 'right'}}
                        >
                            <FaSearch />
                        </Button>
                    </>
                );
            }
        }
    ];
    return (
        <>
            <Table
                tableName={tableName}
                movePage={movePage}
                rowKey={rowKey}
                columns={columns}
                data={data}
                // onDoubleClick={moveUiTest}
                onDoubleClick={(id, row) => onClickDetail(id)}
            />
        </>
    );
};

export default ScHistoryTable;
