import React from 'react';
import ReactDOM from 'react-dom';
import {Provider} from 'react-redux';
import {Router} from 'react-router-dom';
import axios from 'axios';
import {toast} from 'react-toastify';
import App from '@app/App';
import store, {getCustomHistory} from '@app/store/store'; //  store  .
import './index.scss';
import './bootstrap.min.css';
import './i18n';
import {processLogout} from '@app/store/auth';
import * as serviceWorker from '@app/serviceWorker';
import {setCookie} from 'lib/commonUiUtils';

/** ***********************************************
 * axios settting
 ************************************************ */
//   baseURL 
axios.defaults.baseURL =
    process.env.NODE_ENV === 'development' ? '/api/v1/' : '/api/v1/';

// axios.defaults.timeout = 2500;

axios.interceptors.request.use(
    (request) => {
        const {token} = store.getState().auth;
        if (token) {
            request.headers.Authorization = `Bearer ${token}`;
        }
        return request;
    },
    (error) => {
        return Promise.reject(error);
    }
);

axios.interceptors.response.use(
    (response) => {
        // /authenticate
        if (
            response.status === 200 &&
            response.request.responseURL.indexOf('/authenticate') !== -1
        ) {
            const msg =
                '' +
                '' +
                '' +
                '' +
                '' +
                ' ' +
                '' +
                ' ' +
                '' +
                '' +
                '' +
                '.';

            try {
                // const offset = new Date().getTimezoneOffset() * 60000;
                // const today = new Date(Date.now() - offset);
                // const dueDate = new Date(response.headers.duedate);
                // if (
                //     response.headers.duedate === null ||
                //     dueDate < today ||
                //     response.headers.decstr.substring(31, 35) !== '3383'
                // ) {
                //     alert(msg);
                //     store.dispatch(processLogout());
                //     return null;
                // }

                setCookie('decstr', response.headers.decstr, 1);
            } catch (e) {
                // eslint-disable-next-line no-alert
                alert(msg);
                store.dispatch(processLogout());
                return null;
            }
        }

        return response;
    },
    (error) => {
        if (error.response.status === 401) {
            // alert('  .');
            store.dispatch(processLogout());
        }
        return Promise.reject(error);
    }
);

/** ***********************************************
 * toast settting
 ************************************************ */
toast.configure({
    autoClose: 3000,
    draggable: false,
    position: 'top-right',
    hideProgressBar: false,
    newestOnTop: true,
    closeOnClick: true,
    rtl: false,
    pauseOnVisibilityChange: true,
    pauseOnHover: true
});

/** ***********************************************
 * APP Render
 ************************************************ */
ReactDOM.render(
    <Router history={getCustomHistory()}>
        <Provider store={store}>
            <App />
        </Provider>
    </Router>,
    document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
