/* eslint-disable no-unused-vars */
import React from 'react';
import {FaSearch} from 'react-icons/fa';
import {useTranslation} from 'react-i18next';
import {Container, Row, Col, Form, FloatingLabel} from 'react-bootstrap';
import Table from '../../components/table/Table';
import Button from '../../components/button/Button';
import Card from '../../components/card/Card';
import {
    renderSystemInfo,
    renderHttpMethod,
    renderCompareResult,
    renderServiceInfo
} from '../../lib/buttonRender';
import {changeToDate} from '../../lib/moment';
import {takeSelectOptionbyCode} from '../../lib/commonUiUtils';

/*
 *  >   ->   
 */
function BulkDetailBody({
    bulkTestType,
    data,
    movePage,
    onClickSearchBulk,
    bulkDetailSearchForm,
    onSetSearchForm,
    onClickSearch,
    bulkSnapshot,
    codeData
}) {
    const [t] = useTranslation();
    const tableName = t('pages.mass.resultList');
    const rowKey = 'bulkId';
    // eslint-disable-next-line no-unused-vars
    const onDoubleClick = (id, row) => {
        onClickSearchBulk(row);
    };

    const getSuccessString = (row, column) => {
        if (row.testProcessStatus !== 'FINISHED') {
            return <></>;
        }

        if (row[column.key] === true) {
            return (
                <button type="submit" className="btn btn-success">
                    {t('common.text.success')}
                </button>
            );
        }
        return (
            <button type="submit" className="btn btn-danger">
                {t('common.text.fail')}
            </button>
        );
    };

    /**
     * Enter  search 
     * @param {*} e
     */
    const onCheckEnter = (e) => {
        if (e.key === 'Enter') {
            e.preventDefault();
            onClickSearch();
        }
    };

    const getInterfaceId = (row) => {
        return `${row.serviceName}`;
    };

    const columnsSingleTest = [
        {
            title: t('pages.scenario.httpMethod'),
            width: '5%',
            key: 'httpMethod',
            align: 'center', // left, center, right (default:center)
            render: (_id, row) => {
                return renderHttpMethod(row.urlInfo?.httpMethod);
            }
        },
        {
            title: 'Service Info',
            key: 'serviceId',
            align: 'left', // left, center, right (default:center)
            render: (id, row) => {
                return renderServiceInfo(row.serviceInfo, row.urlInfo?.urlPath);
            }
        },
        {
            title: t('common.text.resCode'),
            key: 'systemAHttpResponseCode',
            width: '10%',
            render: (id, row) => {
                return renderSystemInfo(row.systemA?.systemInfo);
            }
        },

        {
            title: t('pages.mass.startTime'),
            width: '10%',
            key: 'sendDateTime',
            render: (id, row) => {
                return changeToDate(row.systemA?.responseTime?.sendDateTime);
            }
        },
        {
            title: t('pages.mass.executeSecond'),
            key: 'processTime',
            width: '10%',
            render: (id, row) => {
                return row.systemA?.responseTime?.processTime;
            }
        },
        {
            title: '',
            key: 'button',
            width: '5%',
            align: 'right',
            // eslint-disable-next-line no-unused-vars
            render: (id, row, column) => {
                return (
                    <>
                        <Button
                            theme="outline-success"
                            onClick={() => onClickSearchBulk(row)}
                        >
                            <FaSearch />
                        </Button>
                    </>
                );
            }
        }
    ];

    const columnsCompareTest = [
        {
            title: t('pages.scenario.httpMethod'),
            width: '5%',
            key: 'httpMethod',
            align: 'center', // left, center, right (default:center)
            render: (_id, row) => {
                return renderHttpMethod(row.urlInfo?.httpMethod);
            }
        },
        {
            title: 'Service Info',
            key: 'serviceId',
            align: 'left', // left, center, right (default:center)
            render: (id, row) => {
                return renderServiceInfo(row.serviceInfo, row.urlInfo?.urlPath);
            }
        },
        {
            title: ` `,
            key: 'compareResult',
            width: '10%',
            render: (id, row) => {
                // ,"systemA":{"systemInfo":
                return renderCompareResult(row.compareResult);
            }
        },
        {
            title: `(A) ${t('common.text.testResult')}`,
            key: 'systemAHttpResponseCode',
            width: '10%',
            render: (id, row) => {
                // ,"systemA":{"systemInfo":
                return renderSystemInfo(row.systemA?.systemInfo);
            }
        },
        {
            title: `(A) ${t('pages.mass.executeSecond')}`,
            key: 'processTime',
            width: '10%',
            render: (id, row) => {
                return row.systemA?.responseTime?.processTime;
            }
        },
        {
            title: `(B) ${t('common.text.testResult')}`,
            key: 'systemAHttpResponseCode',
            width: '10%',
            render: (id, row) => {
                // ,"systemA":{"systemInfo":
                return renderSystemInfo(row.systemB?.systemInfo);
            }
        },
        {
            title: `(B) ${t('pages.mass.executeSecond')}`,
            key: 'processTime',
            width: '10%',
            render: (id, row) => {
                return row.systemB?.responseTime?.processTime;
            }
        },
        {
            title: '',
            key: 'button',
            align: 'right',
            width: '5%',
            render: (id, row) => {
                return (
                    <>
                        <Button
                            theme="outline-success"
                            onClick={() => onClickSearchBulk(row)}
                        >
                            <FaSearch />
                        </Button>
                    </>
                );
            }
        }
    ];

    const getColumnList = () => {
        const {singleOrDual} = bulkSnapshot?.data?.bulkOptions;
        if (singleOrDual === 'SINGLE') {
            return columnsSingleTest;
        }
        return columnsCompareTest;
    };

    /**
     * search form data  
     * @returns
     */
    const onChangeSearchForm = (e) => {
        const initDataT = {...bulkDetailSearchForm};
        initDataT[e.target.id] = e.target.value;

        onSetSearchForm(initDataT);
    };

    return (
        <>
            <Card
                imgIcon="menu2"
                title={tableName}
                body={
                    <>
                        <Container fluid>
                            <Row onKeyPress={onCheckEnter} className="mb-2">
                                <Col xs="3">
                                    <FloatingLabel
                                        label="API "
                                        className="mb-2"
                                    >
                                        <Form.Control
                                            type="text"
                                            maxLength="50"
                                            id="serviceId"
                                            onChange={onChangeSearchForm}
                                            value={
                                                bulkDetailSearchForm.serviceId ??
                                                ''
                                            }
                                        />
                                    </FloatingLabel>
                                </Col>
                                <Col xs="2">
                                    <FloatingLabel
                                        label=" "
                                        className="mb-2"
                                    >
                                        <Form.Select
                                            id="compareResult"
                                            // onChange={onChangeStepJsonObj}
                                            onChange={onChangeSearchForm}
                                            value={
                                                bulkDetailSearchForm.compareResult ??
                                                ''
                                            }
                                        >
                                            <option value="ALL">
                                                {t('common.text.optionAll')}
                                            </option>
                                            {takeSelectOptionbyCode(
                                                codeData,
                                                'CompareResult'
                                            )}
                                        </Form.Select>
                                        {/* <Form.Select
                                            id="retrieveType"
                                            // onChange={onChangeStepJsonObj}
                                            onChange={onChangeSearchForm}
                                            value={
                                                bulkDetailSearchForm.retrieveType ??
                                                ''
                                            }
                                        >
                                            <option value="ALL">
                                                {t('common.text.optionAll')}
                                            </option>
                                            <option value="testSuccess">
                                                 
                                            </option>
                                            <option value="testFail">
                                                 
                                            </option>
                                            <option value="equal">
                                                 
                                            </option>
                                            <option value="notEqual">
                                                 
                                            </option>
                                        </Form.Select> */}
                                    </FloatingLabel>
                                </Col>
                                <Col xs="6"> </Col>
                                <Col xs="1">
                                    <Form.Group>
                                        <Form.Label />
                                        <Button
                                            type="button"
                                            className="btn btn-block btn-secondary btn-search"
                                            onClick={onClickSearch}
                                        >
                                            
                                        </Button>
                                    </Form.Group>
                                </Col>
                            </Row>
                            <Row>
                                <Col xs="12">
                                    <Table
                                        // tableName={tableName}
                                        isCard="N"
                                        onDoubleClick={onDoubleClick}
                                        movePage={movePage}
                                        rowKey={rowKey}
                                        columns={getColumnList()}
                                        data={data}
                                    />
                                </Col>
                            </Row>
                        </Container>
                    </>
                }
            />
        </>
    );
}

export default BulkDetailBody;
