import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import BulkDetailBody from '@app/pages/bulk/BulkDetailBody';
import {toast} from 'react-toastify';
import {useTranslation} from 'react-i18next';
import Spinner from '../../components/spinner/Spinner';
import {usedMultiTestModal} from '../../store/common';
import {
    searchBulkDetail,
    clearSearchBulkDetail,
    searchBulk,
    clearSearchBulk,
    setBulkDetailForm
} from '../../store/bulk';
// searchBulkListRes
import {getErrorMsg} from '../../lib/commonUiUtils';

/*
 *  >   ->   
 */
function BulkDetailBodyContainer() {
    const dispatch = useDispatch();
    const [t] = useTranslation();

    const {data: codeData} = useSelector((state) => state.common.searchCode);

    const {
        data: searchData,
        loading: searchLoading,
        error: searchError
    } = useSelector((state) => state.bulk.searchBulkDetailRes);

    const {
        data: searchBulkData,
        loading: searchBulkLoading,
        error: searchBulkError
    } = useSelector((state) => state.bulk.searchBulkRes);

    const {data: bulkSnapshot} = useSelector(
        (state) => state.bulk.searchBulkSnapshotRes
    );

    const {selectedBulkInfo} = useSelector((state) => state.bulk);
    const {bulkDetailSearchForm} = useSelector((state) => state.bulk);

    const movePage = (page) => {
        const bulkDetailSearchFormT = {...bulkDetailSearchForm};
        bulkDetailSearchFormT.page = page;
        dispatch(searchBulkDetail(bulkDetailSearchFormT));
    };

    const onClickSearch = () => {
        dispatch(searchBulkDetail(bulkDetailSearchForm));
    };

    const onSetSearchForm = (bulkDetailSearchFormT) => {
        dispatch(setBulkDetailForm(bulkDetailSearchFormT));
    };

    const onClickSearchBulk = (data) => {
        // 2022-01-23 kimtaehan    
        //      .
        dispatch(searchBulk(data.bulkId));
    };

    /*
     *     (, )
     */
    const showResponseModal = (data) => {
        const {singleOrDual} = bulkSnapshot.data?.bulkOptions;
        if (singleOrDual === 'SINGLE') {
            dispatch(usedMultiTestModal(true, data.responseA, null));
        } else if (singleOrDual === 'DUAL') {
            dispatch(
                usedMultiTestModal(true, data.responseA, data.responseB, true)
            );
        }
    };

    useEffect(() => {
        if (!searchBulkLoading && searchBulkError) {
            toast.error(
                getErrorMsg(searchBulkError, t('common.alert.searchFail'))
            );
            dispatch(clearSearchBulk());
        }
        if (!searchLoading && searchBulkData) {
            showResponseModal(searchBulkData.data);
            dispatch(clearSearchBulk());
        }
    }, [searchBulkData, searchBulkError]);

    useEffect(() => {
        if (!searchLoading && searchError) {
            toast.error(getErrorMsg(searchError, t('common.alert.searchFail')));
            dispatch(clearSearchBulkDetail());
        }
    }, [searchError]);

    if (!searchData) {
        return <></>;
    }

    return (
        <>
            <BulkDetailBody
                bulkTestType={selectedBulkInfo && selectedBulkInfo.bulkTestType}
                movePage={movePage}
                data={searchData}
                onClickSearchBulk={onClickSearchBulk}
                bulkDetailSearchForm={bulkDetailSearchForm}
                onSetSearchForm={onSetSearchForm}
                onClickSearch={onClickSearch}
                bulkSnapshot={bulkSnapshot}
                codeData={codeData}
            />
            <Spinner isLoading={searchLoading || searchBulkLoading} />
        </>
    );
    ///
}

export default BulkDetailBodyContainer;
