import React from 'react';
import {FaSearch, FaTrash, FaLock, FaUnlockAlt} from 'react-icons/fa';
import {useTranslation} from 'react-i18next';
import Table from '../../components/table/Table';
import Button from '../../components/button/Button';
import {changeToDate} from '../../lib/moment';

import {renderClubType} from '../../lib/buttonRender';

const ClubTable = ({
    data,
    movePage,
    onClickDetail,
    onClickDelete,
    onClickLock
}) => {
    const [t] = useTranslation();
    const tableName = t('pages.history.historyList');
    const rowKey = 'clubId';

    //   
    const renderLockButton = (id, row) => {
        if (row.isLock) {
            return (
                <Button
                    theme="outline-primary"
                    onClick={() => onClickLock(id, row.isLock)}
                >
                    <FaLock />
                </Button>
            );
        }
        return (
            <Button
                theme="outline-primary"
                onClick={() => onClickLock(id, row.isLock)}
            >
                <FaUnlockAlt />
            </Button>
        );
    };
    const columns = [
        {
            title: ' ',
            key: 'clubTypeName',
            width: '5%',
            render: (id, row) => {
                return renderClubType(row.clubType);
            }
        },

        {
            title: ' ',
            key: 'clubName',
            align: 'left' // left, center, right (default:center)
        },
        //   {
        //         title: ' ',
        //         width: '10%',
        //         key: 'scenarioCnt',
        //         // eslint-disable-next-line no-unused-vars
        //         render: (id, row, column) => {
        //             return `${row.scenarioCnt} ${t('common.text.cases')}`;
        //         }
        //     },
        //     {
        //         title: ' ',
        //         width: '10%',
        //         key: 'scheduleCnt',
        //         // eslint-disable-next-line no-unused-vars
        //         render: (id, row) => {
        //             return `${row.scheduleCnt} ${t('common.text.cases')}`;
        //         }
        //     },
        //     {
        //         title: ' ',
        //         width: '10%',
        //         key: 'lastTestD  ate'
        //     },
        {
            title: t('common.text.createdDate'),
            width: '10%',
            key: 'createdDate',
            render: (id, row) => {
                return changeToDate(row.createdDate);
            }
        },
        {
            title: t('common.text.modifiedDate'),
            width: '10%',
            key: 'modifiedDate',
            render: (id, row) => {
                return changeToDate(row.modifiedDate);
            }
        },
        {
            title: '',
            key: 'button',
            width: '5%',
            align: 'right',
            // eslint-disable-next-line no-unused-vars
            render: (id, row, column) => {
                return (
                    <>
                        <Button
                            theme="outline-success"
                            onClick={() => onClickDetail(id)}
                            style={{float: 'rigth'}}
                        >
                            <FaSearch />
                        </Button>
                        {renderLockButton(id, row)}
                        <Button
                            // disabled={!memberInfoData.data.isAdmin}
                            disabled={row.isLock}
                            theme="outline-danger"
                            onClick={() => onClickDelete(id)}
                        >
                            <FaTrash />
                        </Button>
                    </>
                );
            }
        }
    ];

    // eslint-disable-next-line no-unused-vars
    const onDoubleClick = (id, row) => {
        onClickDetail(id);
    };

    return (
        <>
            <Table
                tableName={tableName}
                movePage={movePage}
                rowKey={rowKey}
                columns={columns}
                data={data}
                onDoubleClick={onDoubleClick}
            />
        </>
    );
};

export default ClubTable;
