/* eslint-disable import/prefer-default-export */

import axios from 'axios';

/**
 * LAYOUT List Paging 
 * searchLayout (SEARCH_LAYOUT) : 
 * clearSearchLayout (CLEAR_SEARCH_LAYOUT) : 
 * setSearchLayoutName (SET_SEARCH_LAYOUT_NAME) :   1
 * setSearchHttpMethod (SET_SEARCH_HTTP_METHOD) :   2
 */
export const searchLayout = async ({
    layoutQuery,
    httpMethod,
    transactionType,
    page = 1,
    size = 10
}) => {
    const response = await axios.get(
        `/layouts?sort=modifiedDate,desc&layoutQuery=${encodeURI(
            layoutQuery
        )}&transactionType=${transactionType}&httpMethod=${httpMethod}&page=${
            page - 1
        }&size=${size}`
    );
    return response;
};

/**
 * LAYOUT  
 * updateLayout (UPDATE_LAYOUT) : 
 * clearUpdateLayout (CLEAR_UPDATE_LAYOUT) : 
 */
// update layout (input,output,serviceName)
export const updateLayout = async ({
    layoutId,
    urlInfo,
    serviceInfo,
    transactionType,
    cols
}) => {
    const response = await axios.patch(`/layouts/${layoutId}`, {
        urlInfo,
        serviceInfo,
        transactionType,
        cols
    });
    return response;
};

/*
 * deleteLayout : layout  (DELETE_LAYOUT)
 * deleteLayoutDataClear : layout   (DELETE_LAYOUT_DATA_CLEAR)
 */
export const deleteLayout = async ({layoutId}) => {
    const response = await axios.delete(`/layouts/${layoutId}`, null);
    return response;
};

/**
 * LAYOUT   (TR)
 * searchLayoutDetail (SEARCH_LAYOUT_DETAIL) : 
 * clearsearchLayoutDetail (CLEAR_SEARCH_LAYOUT_DETAIL) : 
 */
export const searchLayoutDetail = async ({id}) => {
    const response = await axios.get(`/layouts/${id}`);
    return response;
};

/**
 *    's   's 
 * api -> combineRequestValue (/layout/search/random-value)
 * combineRequestValue (GET_RANDOM_VALUE) : 
 * clearCombineRequestValue (CLEAR_GET_RANDOM_VALUE) : 
 */
export const combineRequestValue = async ({cols, layoutId}) => {
    const response = await axios.post(`/layouts/${layoutId}/combine`, {
        cols
    });
    return response;
};

/**
 * LAYOUT   (TR)
 * createLayoutManual (CREATE_LAYOUT_MANUAL) : 
 * clearCreateLayoutManual (CLEAR_CREATE_LAYOUT_MANUAL) : 
 */
export const createLayoutManual = async ({
    serviceId,
    serviceDesc,
    urlPath,
    httpMethod
}) => {
    const response = await axios.post('/layouts', {
        serviceId,
        serviceDesc,
        urlPath,
        httpMethod
    });
    return response;
};
