import React, {useEffect} from 'react';
import MemberUpdate from '@pages/member/popup/MemberUpdate';
import {useSelector, useDispatch} from 'react-redux';
import {toast} from 'react-toastify';
import {
    updateMember,
    processJoin,
    clearUpdateMember,
    hideModifyMemberInfoModal,
    searchUserInfoList,
    clearMemberDataForUpdate,
    clearProcessJoin
} from '../../../store/auth';
import Spinner from '../../../components/spinner/Spinner';
import {getErrorMsg} from '../../../lib/commonUiUtils';

/*
 * MemberUpdateContainer
 *  / container
 */
function MemberUpdateContainer() {
    const dispatch = useDispatch();

    const {visible: modalShow} = useSelector((state) => state.auth.modifyModal);
    const {
        loading: updateLoading,
        data: updateResponse,
        error: updateError
    } = useSelector((state) => state.auth.update);

    const {
        loading: createLoading,
        data: createResponse,
        error: createError
    } = useSelector((state) => state.auth.join);

    const {memberSearchForm} = useSelector((state) => state.auth);
    const {memberDataForUpdate: memberData} = useSelector(
        (state) => state.auth
    );

    const handleClose = () => dispatch(hideModifyMemberInfoModal());

    const onUpdate = (updateForm) => {
        if (memberData.id !== -1) {
            dispatch(updateMember(updateForm));
        } else {
            dispatch(processJoin(updateForm));
        }
    };

    const onClearUpdate = () => dispatch(clearUpdateMember());
    const onClearJoin = () => dispatch(clearProcessJoin());
    const onClearMemberDataForUpdate = () =>
        dispatch(clearMemberDataForUpdate());

    const onSearch = () => dispatch(searchUserInfoList(memberSearchForm));

    useEffect(() => {
        // success
        if (!updateLoading && updateResponse) {
            onClearUpdate();
            onClearMemberDataForUpdate();
            onSearch();
            handleClose();
        }
        // fail
        if (!updateLoading && updateError) {
            toast.error(getErrorMsg(updateError, 'save'));
            onClearUpdate();
        }
    }, [updateResponse, updateError]);

    useEffect(() => {
        // success
        if (!createLoading && createResponse) {
            onClearJoin();
            onSearch();
            handleClose();
        }

        // fail
        if (!createLoading && createError) {
            toast.error(getErrorMsg(createError, 'save'));
            onClearJoin();
        }
    }, [createResponse, createError]);

    return (
        <>
            <MemberUpdate
                memberData={memberData}
                updateLoading={updateLoading}
                processUpdate={onUpdate}
                modalShow={modalShow}
                handleClose={handleClose}
            />
            <Spinner isLoading={updateLoading || createLoading} />
        </>
    );
    ///
}

export default MemberUpdateContainer;
