/* eslint-disable no-alert */
/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import SourceTable from '@pages/source/SourceTable';
import {useHistory} from 'react-router-dom';
import {useTranslation} from 'react-i18next';
import {toast} from 'react-toastify';
import {getErrorMsg} from '../../lib/commonUiUtils';
import {
    deleteSourceSnapshot,
    clearDeleteSourceSnapshot,
    setSearchSourceSnapshotForm
} from '../../store/source';
/*
 *   Container
 */
function SourceTableContainer({onSearch}) {
    const dispatch = useDispatch();
    const [t] = useTranslation();
    const history = useHistory();

    //  
    const {data: searchData} = useSelector(
        (state) => state.source.searchSourceSnapshotRes
    );

    const {searchSourceSnapshotForm} = useSelector((state) => state.source);

    //    
    const {data: memberInfoData} = useSelector(
        (state) => state.auth.memberInfo
    );

    const movePage = (page) => {
        const searchSourceSnapshotFormT = {...searchSourceSnapshotForm};
        searchSourceSnapshotFormT.page = page;
        dispatch(setSearchSourceSnapshotForm(searchSourceSnapshotFormT));
        onSearch(searchSourceSnapshotFormT);
    };

    const onClickMove = (id) => {
        history.push({
            pathname: '/source/detail',
            state: {
                id
            }
        });
    };
    const onClickDelete = (id) => {
        if (window.confirm(t('common.alert.checkDelete'))) {
            dispatch(deleteSourceSnapshot(id));
        }
    };

    //   
    const {
        data: deleteData,
        loading: deleteLoading,
        error: deleteError
    } = useSelector((state) => state.source.deleteSourceSnapshotRes);

    useEffect(() => {
        // Log snapshot delete error
        if (!deleteLoading && deleteError) {
            toast.error(getErrorMsg(deleteError, 'save'));
            dispatch(clearDeleteSourceSnapshot());
        }
        // Log snapshot delete success
        else if (!deleteLoading && deleteData) {
            toast.success(t('common.alert.updateSuccess'));
            dispatch(clearDeleteSourceSnapshot());
            onSearch(); // 
        }
    }, [deleteData, deleteError]);

    return (
        <>
            <SourceTable
                movePage={movePage}
                data={searchData}
                onClickDelete={onClickDelete}
                memberInfoData={memberInfoData}
                onClickMove={onClickMove}
            />
            {/* <Spinner isLoading={deleteLogSnapshotLoading} /> */}
        </>
    );
    ///
}

export default SourceTableContainer;
