/* eslint-disable no-multi-assign */
import {combineReducers} from 'redux';
import {all} from 'redux-saga/effects';
import commonUI from './commonUi'; //  store  .
import auth, {authSaga} from './auth';
import server, {serverSaga} from './server';
import scenario, {scenarioSaga} from './scenario';
import uitest, {uitestSaga} from './uitest';
import layout, {layoutSaga} from './layout';
import history, {historySaga} from './history';
import bulk, {bulkSaga} from './bulk';
import common, {commonSaga} from './common';
import random, {randomSaga} from './random';
import dashboard, {dashboardSaga} from './dashboard';
import credentialInfo, {credentialInfoSaga} from './credentialInfo';
import setting, {settingSaga} from './setting';
import stat, {statSaga} from './stat';
import log, {logSaga} from './log';
import club, {clubSaga} from './club';
import schedule, {scheduleSaga} from './schedule';
import source, {sourceSaga} from './source';
// import sourceDetail, {sourceDetailSaga} from './source';

const rootReducer = combineReducers({
    commonUI,
    auth,
    server,
    scenario,
    layout,
    history,
    uitest,
    bulk,
    common,
    random,
    dashboard,
    credentialInfo,
    setting,
    stat,
    log,
    club,
    schedule,
    source
});
export function* rootSaga() {
    yield all([
        authSaga(),
        serverSaga(),
        scenarioSaga(),
        layoutSaga(),
        historySaga(),
        uitestSaga(),
        bulkSaga(),
        commonSaga(),
        randomSaga(),
        dashboardSaga(),
        credentialInfoSaga(),
        settingSaga(),
        statSaga(),
        logSaga(),
        clubSaga(),
        scheduleSaga(),
        sourceSaga()
    ]);
}

export default rootReducer;
