/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import {toast} from 'react-toastify';
import {useTranslation} from 'react-i18next';
import ClubExecutePopup from '@app/pages/club/popup/ClubExecutePopup';
import Spinner from '../../../components/spinner/Spinner';
import {getErrorMsg} from '../../../lib/commonUiUtils';
import {setUpdateServerInitData} from '../../../store/server';

import {
    clearClubExecuteModal,
    executeClub,
    clearExecuteClub,
    searchClubHistory
} from '../../../store/club';
/*
 *  -     Container
 */
function ClubExecutePopupContainer() {
    const dispatch = useDispatch();
    const [t] = useTranslation();

    const [modalShow, setModalShow] = React.useState(false);
    const handleClose = () => setModalShow(false);
    const handleShow = () => setModalShow(true);

    const [clubId, setClubId] = React.useState(-1);

    const {clubExecuteInitData} = useSelector((state) => state.club);

    //   
    const {data: serverData} = useSelector(
        (state) => state.common.searchServer
    );

    const onExecuteClub = (values) => {
        const data = {...values};
        data.clubId = clubId;

        dispatch(executeClub(data));
    };

    const onClickServerInfo = (serverId) => {
        const selectedServer = serverData.data.content.filter(
            (obj) => obj.id === Number(serverId)
        );

        if (selectedServer.length !== 1) {
            // eslint-disable-next-line no-alert
            alert('   .');
            return;
        }

        const tempData = {...selectedServer[0]};
        tempData.parentId = 'ClubExecutePopup';
        dispatch(setUpdateServerInitData(tempData));
    };

    const {searchClubHistoryForm} = useSelector((state) => state.club);

    // Club execute Callback [TR]
    const {
        loading: executeClubLoading,
        data: executeClubData,
        error: executeClubError
    } = useSelector((state) => state.club.executeClubRes);

    useEffect(() => {
        if (!executeClubLoading) {
            if (executeClubData) {
                toast.success(t('common.alert.testSuccess'));

                //   
                dispatch(searchClubHistory(searchClubHistoryForm));
                handleClose();
            } else if (executeClubError) {
                toast.error(getErrorMsg(executeClubError, 'execute'));
            }
            dispatch(clearExecuteClub());
        }
    }, [executeClubLoading]);

    /*
     *  Open event
     */
    useEffect(() => {
        if (clubExecuteInitData.showModal && !modalShow) {
            setClubId(clubExecuteInitData.initData.clubId);
            dispatch(clearClubExecuteModal());
            handleShow();
        }
    }, [clubExecuteInitData.showModal]);

    return (
        <>
            <ClubExecutePopup
                modalShow={modalShow}
                handleClose={handleClose}
                serverData={serverData}
                onExecuteClub={onExecuteClub}
                onClickServerInfo={onClickServerInfo}
            />
            <Spinner isLoading={executeClubLoading} />
        </>
    );
    ///
}

export default ClubExecutePopupContainer;
