/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import {toast} from 'react-toastify';
import {useTranslation} from 'react-i18next';
import ClubAddScenarioPopup from '@app/pages/club/popup/ClubAddScenarioPopup';
import Spinner from '../../../components/spinner/Spinner';
import {getErrorMsg} from '../../../lib/commonUiUtils';

import {
    clearClubAddScenarioModal,
    searchScenariosInClub,
    addScenario,
    clearAddScenario,
    searchScenariosInClubSetForm,
    searchScenariosInClubFormInit,
    searchClubScenarios
} from '../../../store/club';
/*
 *  -     Container
 */
function ClubAddScenarioPopupContainer() {
    const dispatch = useDispatch();
    const [t] = useTranslation();

    const [modalShow, setModalShow] = React.useState(false);
    const [clubId, setClubId] = React.useState(false);

    // const handleClose = () => setModalShow(false);
    const handleShow = () => setModalShow(true);

    const {searchClubScenariosForm} = useSelector((state) => state.club);

    const handleClose = () => {
        dispatch(searchClubScenarios(searchClubScenariosForm));
        setModalShow(false);
    };
    const {searchScenariosInClubForm} = useSelector((state) => state.club);

    const {clubAddScenarioInitData} = useSelector((state) => state.club);

    const onSearchScenarioInClub = (
        _searchScenariosInClubForm = searchScenariosInClubForm
    ) => {
        dispatch(searchScenariosInClub(_searchScenariosInClubForm));
    };

    const movePage = (page) => {
        // onSearchScenarioInClub(formik.values.scenarioName, clubId, page);
        const searchFormT = {...searchScenariosInClubForm};
        searchFormT.page = page;
        dispatch(searchScenariosInClubSetForm(searchFormT));
        dispatch(searchScenariosInClub(searchFormT));
    };

    const onClickSearch = () => {
        movePage(0);
    };

    const {
        loading: addScenarioLoading,
        data: addScenarioResponse,
        error: addScenarioError
    } = useSelector((state) => state.club.addScenarioRes);

    /**
     * Scenario Add
     * @param {*} id
     */
    const onClickAddScenario = (row) => {
        dispatch(addScenario(clubId, row.scenarioId));
    };

    const onChangerFormData = (e) => {
        const searchFormT = {...searchScenariosInClubForm};
        searchFormT[e.target.id] = e.target.value;
        dispatch(searchScenariosInClubSetForm(searchFormT));
    };
    /*
     *  Open event
     */
    useEffect(() => {
        if (clubAddScenarioInitData.showModal && !modalShow) {
            setClubId(clubAddScenarioInitData.initData.clubId);

            const searchFormT = searchScenariosInClubFormInit();
            searchFormT.clubId = clubAddScenarioInitData.initData.clubId;
            dispatch(searchScenariosInClubSetForm(searchFormT));

            onSearchScenarioInClub(searchFormT);
            dispatch(clearClubAddScenarioModal());

            handleShow();
        }
    }, [clubAddScenarioInitData.showModal]);

    // const onClickCreateClub = (values) => {
    //     dispatch(createClub(values));
    // };

    const {data: searchScenarioData} = useSelector(
        (state) => state.club.searchClubScenariosRes
    );

    const {
        loading: searchScenariosInClubLoading,
        data: searchScenariosInClubResponse,
        error: searchScenariosInClubError
    } = useSelector((state) => state.club.searchScenariosInClubRes);

    useEffect(() => {
        if (!searchScenariosInClubLoading && searchScenariosInClubError) {
            toast.error(getErrorMsg(searchScenariosInClubError, 'select'));
        }
        // dispatch(clearSearchScenariosInClub());
    }, [searchScenariosInClubResponse, searchScenariosInClubError]);

    useEffect(() => {
        if (!addScenarioLoading && addScenarioResponse) {
            toast.success(t('common.alert.updateSuccess'));
            onSearchScenarioInClub();
            handleClose();
        }
        if (!addScenarioLoading && addScenarioError) {
            toast.error(getErrorMsg(addScenarioError, 'save'));
        }

        dispatch(clearAddScenario());
    }, [addScenarioResponse, addScenarioError]);

    return (
        <>
            <ClubAddScenarioPopup
                modalShow={modalShow}
                handleClose={handleClose}
                onSearchScenarioInClub={onSearchScenarioInClub}
                searchScenariosInClubResponse={searchScenariosInClubResponse}
                onClickAddScenario={onClickAddScenario}
                movePage={movePage}
                searchScenariosInClubForm={searchScenariosInClubForm}
                onChangerFormData={onChangerFormData}
                onClickSearch={onClickSearch}
                searchScenarioData={searchScenarioData}
            />
            <Spinner
                isLoading={searchScenariosInClubLoading || addScenarioLoading}
            />
        </>
    );
}

export default ClubAddScenarioPopupContainer;
