/* eslint-disable no-unused-vars */
import {useSelector, useDispatch} from 'react-redux';
import React, {useState, useEffect} from 'react';
import {toast} from 'react-toastify';
import {useTranslation} from 'react-i18next';
import StatRenewalModal from '@pages/stat/popup/StatRenewalModal';
import {renewalStat, clearRenewalStat} from '../../../store/stat';
import Spinner from '../../../components/spinner/Spinner';

function StatRenewalModalContainer() {
    const dispatch = useDispatch();

    const [t] = useTranslation();
    const [dDay, setDday] = useState('');

    /** **************************
     * initializer
     ************************** */

    /** **************************
     * component state
     ************************** */
    const [modalShow, setModalShow] = React.useState(false);
    const handleClose = () => setModalShow(false);
    const handleShow = () => {
        const offset = new Date().getTimezoneOffset() * 60000;

        // today -1
        const today = new Date(Date.now() - offset);
        setDday(today.toISOString().slice(0, -1).slice(0, 10));
        setModalShow(true);
    };

    const onClickRenewal = (e) => {
        e.preventDefault();
        dispatch(renewalStat(dDay));
    };

    const {data: renewalStatData, loading: renewalStatLoading} = useSelector(
        (state) => state.stat.renewalStat
    );

    useEffect(() => {
        if (!renewalStatLoading && renewalStatData) {
            if (renewalStatData.data) {
                toast.success(t('common.alert.updateSuccess'));
            }

            dispatch(clearRenewalStat());
            handleClose();
        }
    }, [renewalStatData]);

    return (
        <>
            <StatRenewalModal
                modalShow={modalShow}
                handleShow={handleShow}
                handleClose={handleClose}
                dDay={dDay}
                setDday={setDday}
                onClickRenewal={onClickRenewal}
                // statDetailData={statDetailData}
                // moveHistory={moveHistory}
                // searchFormT2={searchFormT2}
                // searchT2={searchT2}
            />
            <Spinner isLoading={renewalStatLoading} />
        </>
    );
    ///
}

export default StatRenewalModalContainer;
