/* eslint-disable import/prefer-default-export */

import axios from 'axios';

/**
 * (TR)     
 * searchScenarioDetail (SEARCH_SCENARIO_DETAIL) : CALL
 * searchScenarioDetailClear (SEARCH_SCENARIO_DETAIL_CLEAR) : CLEAR
 */
export const searchScenarioDetail = async ({id}) => {
    const response = await axios.get(`/scenarios/${id}`);
    return response;
};

/**
 * (TR)   
 * saveScenario (SAVE_SCENARIO) : CALL
 * saveScenarioDataClear (SAVE_SCENARIO_DATA_CLEAR) : CLEAR
 */
export const saveScenario = async ({
    scenarioName,
    scenarioDesc,
    cases,
    variables
}) => {
    const response = await axios.post('/scenarios', {
        scenarioName,
        scenarioDesc,
        cases,
        variables
    });
    return response;
};

/**
 * (TR)   
 * saveScenario (SAVE_SCENARIO) : CALL
 * saveScenarioDataClear (SAVE_SCENARIO_DATA_CLEAR) : CLEAR
 */
export const updateScenario = async ({
    scenarioId,
    scenarioName,
    scenarioDesc,
    cases,
    manager,
    variables
}) => {
    const response = await axios.put(`/scenarios/${scenarioId}`, {
        scenarioName,
        scenarioDesc,
        cases,
        manager,
        variables
    });
    return response;
};

/**
 * (TR)  PAGING  (   )
 * searchLayout (SEARCH_LAYOUT) : CALL
 */
export const searchLayout = async ({layoutQuery, page = 1, size = 10}) => {
    const response = await axios.get(
        `/layouts?sort=modifiedDate,asc&layoutQuery=${layoutQuery}&page=${
            page - 1
        }&size=${size}`
    );
    return response;
};

/**
 * (TR)    (   )
 * searchLayout (SEARCH_LAYOUT) : CALL
 * clearsearchLayoutDetail (CLEAR_SEARCH_LAYOUT_DETAIL) : CLEAR
 */
export const searchLayoutDetail = async ({id}) => {
    const response = await axios.get(`/layouts/${id}`);
    return response;
};

/**
 * (TR)    (   )
 * api -> combineRequestValue (/layout/search/random-value)
 * combineRequestValue (GET_RANDOM_VALUE) : CALL
 * clearCombineRequestValue (CLEAR_GET_RANDOM_VALUE) : CLEAR
 */
export const combineRequestValue = async ({cols, layoutId}) => {
    const response = await axios.post(`/layouts/${layoutId}/combine`, {
        cols
    });
    return response;
};

/**
 * (TR) payload converter xhr event
 * convertPayload (CONVERT_PAYLOAD) : CALL
 * clearConvertPayload (CLEAR_CONVERT_PAYLOAD) : CLEAR
 */
export const convertPayload = async ({payload}) => {
    const response = await axios.post('/layout/convert/payload', {
        payload
    });
    return response;
};

/**
 * (TR) xhr  convert layout 
 * api -> /layout/convert/xhrList
 * convertXhrList (CONVERT_XHRLIST) : CALL
 * clearConvertXhrList (CLEAR_CONVERT_XHRLIST) : CLEAR
 */
export const convertXhrList = async ({caseId, caseName, xhrs}) => {
    const response = await axios.post('/layouts/convert-record', {
        caseId,
        caseName,
        xhrs
    });
    return response;
};
