/* eslint-disable import/prefer-default-export */

import axios from 'axios';

/**
 * (TR) Login 
 * processLogin (PROCESS_LOGIN) : CALL
 * processLoginDataClear (PROCESS_LOGIN_DATA_CLEAR) : CLEAR
 * settingLoginInfo (LOGIN_USER) : CONDITION CHANGE
 * processLogout (LOGOUT_USER) : CONDITION CHANGE
 * onUserLoad (LOAD_USER) : CONDITION CHANGE
 */
export const loginByAuth = async ({userId, password}) => {
    const response = await axios.post('/authenticate', {
        userId,
        password
    });
    return response;
};

/**
 * (TR) 
 * processJoin (PROCESS_JOIN) : CALL
 * clearProcessJoin (CLEAR_PROCESS_JOIN) : CLEAR
 */
export const registerByAuth = async ({
    userId,
    password,
    name,
    role,
    contactInfo
}) => {
    const response = await axios.post('/members', {
        userId,
        password,
        name,
        role,
        contactInfo
    });

    return response;
};

/**
 * (TR)  
 * updateMember (UPDATE_MEMBER) : CALL
 * clearUpdateMember (CLEAR_UPDATE_MEMBER) : CLEAR
 */
export const updateMember = async ({
    userId,
    password,
    name,
    role,
    contactInfo
}) => {
    const response = await axios.patch(`/members/${userId}`, {
        password,
        name,
        role,
        contactInfo
    });

    return response;
};

/**
 * (TR)  
 * deleteMember (DELETE_MEMBER) : CALL
 * clearDeleteMember (CLEAR_DELETE_MEMBER) : CLEAR
 */
export const deleteMember = async ({userId}) => {
    const response = await axios.delete(`/members/${userId}`);

    return response;
};

/**
 * (TR) 
 * getUserInfo (GET_MEMBER_INFO) : CALL
 */
export const getMemberInfo = async () => {
    const response = await axios.get('/members/self');
    return response;
};

export const getMemberInfoList = async ({searchText, page = 1, size = 10}) => {
    const response = await axios.get(
        `/members?sort=modifiedDate,desc&searchText=${searchText}&page=${
            page - 1
        }&size=${size}`
    );

    return response;
};
