import React, {useEffect} from 'react';
import {useHistory} from 'react-router-dom';
import {useSelector, useDispatch} from 'react-redux';
import RandomSearch from '@pages/random/RandomSearch';
import {toast} from 'react-toastify';
import {
    searchRandom,
    clearSearchRandom,
    setSearchForm,
    usedRamdomSavaModal,
    initSearchRandomForm
} from '../../store/random';
import Spinner from '../../components/spinner/Spinner';
import {getErrorMsg} from '../../lib/commonUiUtils';

/*
 *    Container
 */
function RandomSearchContainer() {
    const dispatch = useDispatch();
    const history = useHistory();

    const {searchForm} = useSelector((state) => state.random);
    const {loading: searchLoading, error: searchError} = useSelector(
        (state) => state.random.search
    );
    const onSearch = (_searchForm = searchForm) => {
        dispatch(searchRandom(_searchForm));
    };

    const onChangeSearchForm = (newSearchForm) => {
        dispatch(setSearchForm(newSearchForm));
    };

    const {data: codeData} = useSelector((state) => state.common.searchCode);
    /*
     *    
     */
    const addNewRandom = () => {
        dispatch(usedRamdomSavaModal(true, -1));
    };
    useEffect(() => {
        // 2021-10-06 screen  
        if (history.action === 'PUSH') {
            const initSearchForm = initSearchRandomForm();
            onChangeSearchForm(initSearchForm);
            onSearch(initSearchForm);
        }
        if (history.action === 'POP') {
            onSearch();
        }
    }, []);

    useEffect(() => {
        if (!searchLoading && searchError) {
            toast.error(getErrorMsg(searchError, 'search'));
            dispatch(clearSearchRandom());
        }
    }, [searchError]);

    return (
        <>
            <RandomSearch
                onSearch={onSearch}
                searchForm={searchForm}
                onChangeSearchForm={onChangeSearchForm}
                addNewRandom={addNewRandom}
                codeData={codeData}
            />
            <Spinner isLoading={searchLoading} />
        </>
    );
}

export default RandomSearchContainer;
