import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import CredentialInfoTable from '@pages/credentialInfo/CredentialInfoTable';
import {toast} from 'react-toastify';
import {
    searchCredentialInfo,
    updateCredentialInfo,
    clearUpdateCredentialInfo,
    deleteCredentialInfo,
    clearDeleteCredentialInfo
} from '../../store/credentialInfo';
import Spinner from '../../components/spinner/Spinner';
import {getErrorMsg} from '../../lib/commonUiUtils';

function CredentialInfoTableContainer() {
    const dispatch = useDispatch();

    const {selectedServer} = useSelector((state) => state.credentialInfo);

    const {loading: searchLoading, data: searchData} = useSelector(
        (state) => state.credentialInfo.search
    );

    const {
        loading: updateLoading,
        data: updateResponse,
        error: updateError
    } = useSelector((state) => state.credentialInfo.update);

    const {
        loading: deleteLoading,
        data: deleteResponse,
        error: deleteError
    } = useSelector((state) => state.credentialInfo.delete);

    useEffect(() => {
        dispatch(searchCredentialInfo(selectedServer.id));
    }, []);

    // [START] ----------------------------------------
    const movePage = (page) => {
        dispatch(searchCredentialInfo(selectedServer.id, page));
    };

    const onUpdate = (updateForm) => {
        dispatch(updateCredentialInfo(updateForm));
    };

    const onDelete = (row) => {
        dispatch(deleteCredentialInfo(row.credentialId));
    };
    // [END]] ----------------------------------------

    // [START] ----------------------------------------
    // 
    if (!updateLoading && updateResponse) {
        dispatch(clearUpdateCredentialInfo());
        dispatch(searchCredentialInfo(selectedServer.id));
    }

    // fail
    if (!updateLoading && updateError) {
        dispatch(clearUpdateCredentialInfo());
        toast.error(getErrorMsg(updateError, 'save'));
    }

    // 
    if (!deleteLoading && deleteResponse) {
        dispatch(clearDeleteCredentialInfo());
        dispatch(searchCredentialInfo(selectedServer.id));
    }

    // fail
    if (!deleteLoading && deleteError) {
        dispatch(clearDeleteCredentialInfo());
        toast.error(getErrorMsg(deleteError, 'save'));
    }
    // [END] ------------------------------------------------

    return (
        <>
            <CredentialInfoTable
                data={searchData}
                onUpdate={onUpdate}
                onDelete={onDelete}
                movePage={movePage}
            />
            <Spinner
                isLoading={searchLoading || updateLoading || deleteLoading}
            />
        </>
    );
    ///
}

export default CredentialInfoTableContainer;
