/* eslint-disable jsx-a11y/label-has-associated-control */
/* eslint-disable no-unused-vars */
import React from 'react';
import {useTranslation} from 'react-i18next';
import {Row, Col, Form, FloatingLabel} from 'react-bootstrap';
import Button from '../../components/button/Button';
import Card from '../../components/card/Card';
import {takeSelectOptionbyServer} from '../../lib/commonUiUtils';

const HistorySearch = ({
    searchForm,
    setSearchForm,
    onSearch,
    serverData,
    showExecuteModal
}) => {
    const [t] = useTranslation();

    const onChangerSearchForm = (e) => {
        e.preventDefault();
        const searchFormT = {...searchForm};
        searchFormT[e.target.id] = e.target.value;
        setSearchForm(searchFormT);
    };

    const onClickSearch = (e) => {
        e.preventDefault();
        const searchFormT = {...searchForm};
        searchFormT.page = 1;
        setSearchForm(searchFormT);
        onSearch(searchFormT);
    };

    const onCheckEnter = (e) => {
        if (e.key === 'Enter') {
            e.preventDefault();
            onSearch();
        }
    };

    return (
        <Card
            title={t('common.text.searchCardTitleName')}
            body={
                <>
                    <Row onKeyPress={onCheckEnter}>
                        <Col xs="5">
                            <Row>
                                <Col xs="3">
                                    <FloatingLabel
                                        label={t('common.text.testResult')}
                                        className="mb-2"
                                    >
                                        <Form.Select
                                            id="systemResult"
                                            onChange={onChangerSearchForm}
                                            value={
                                                searchForm.systemResult || ''
                                            }
                                        >
                                            <option value="">
                                                {t('common.text.optionAll')}
                                            </option>
                                            <option value="SUCCESS">
                                                
                                            </option>
                                            <option value="FAILURE">
                                                
                                            </option>
                                        </Form.Select>
                                    </FloatingLabel>
                                </Col>
                                <Col xs="9">
                                    {/*   */}
                                    <FloatingLabel
                                        label={t('pages.layout.interfaceId')}
                                        className="mb-2"
                                    >
                                        <Form.Control
                                            type="text"
                                            maxLength="50"
                                            id="serviceId"
                                            onChange={onChangerSearchForm}
                                            value={searchForm.serviceId ?? ''}
                                        />
                                    </FloatingLabel>
                                </Col>
                            </Row>
                        </Col>
                        <Col xs="5">
                            <Row>
                                <Col xs="4">
                                    <FloatingLabel
                                        label={t('common.text.serverInfo')}
                                        className="mb-2"
                                    >
                                        <Form.Select
                                            id="serverId"
                                            onChange={onChangerSearchForm}
                                            value={searchForm?.serverId}
                                        >
                                            <option value="-1">
                                                {t('common.text.optionAll')}
                                            </option>
                                            {takeSelectOptionbyServer(
                                                serverData,
                                                'HTTP'
                                            )}
                                            {takeSelectOptionbyServer(
                                                serverData,
                                                'TCP_IP'
                                            )}
                                        </Form.Select>
                                    </FloatingLabel>
                                </Col>
                                <Col xs="4">
                                    <FloatingLabel
                                        label="From"
                                        className="mb-2"
                                    >
                                        <Form.Control
                                            type="datetime-local"
                                            id="fromDateTime"
                                            onChange={onChangerSearchForm}
                                            value={searchForm?.fromDateTime}
                                        />
                                    </FloatingLabel>
                                </Col>
                                <Col xs="4">
                                    <FloatingLabel label="To" className="mb-2">
                                        <Form.Control
                                            type="datetime-local"
                                            id="toDateTime"
                                            onChange={onChangerSearchForm}
                                            value={searchForm?.toDateTime}
                                        />
                                    </FloatingLabel>
                                </Col>
                            </Row>
                        </Col>
                        <Col xs="1">
                            <Form.Group>
                                <Form.Label />
                                <Button
                                    type="button"
                                    className="btn btn-block btn-primary btn-search"
                                    // isLoading={searchLoading}
                                    onClick={showExecuteModal}
                                >
                                    
                                </Button>
                            </Form.Group>
                        </Col>
                        <Col xs="1">
                            <Form.Group>
                                <Form.Label />
                                <Button
                                    type="button"
                                    className="btn btn-block btn-secondary btn-search"
                                    onClick={onClickSearch}
                                >
                                    {t('common.button.search')}
                                </Button>
                            </Form.Group>
                        </Col>
                    </Row>
                </>
            }
        />
    );
};

export default HistorySearch;
