import React, {useEffect} from 'react';
import {useHistory} from 'react-router-dom';
import {useSelector, useDispatch} from 'react-redux';
import MemberSearch from '@pages/member/MemberSearch';
import {toast} from 'react-toastify';
import {
    setMemberSearchForm,
    searchUserInfoList,
    clearSearchUserInfoList,
    getMemberSearchFormInitialState,
    setMemberDataForUpdate,
    showModifyMemberInfoModal
} from '../../store/auth';
import Spinner from '../../components/spinner/Spinner';
import {getErrorMsg} from '../../lib/commonUiUtils';

/*
 * MemberSearchContainer
 *   container
 */
function MemberSearchContainer() {
    const dispatch = useDispatch();
    const history = useHistory();

    const {loading: searchLoading, error: searchError} = useSelector(
        (state) => state.auth.memberInfoList
    );

    const {memberSearchForm} = useSelector((state) => state.auth);

    const onChangeSearchForm = (searchForm) =>
        dispatch(setMemberSearchForm({...searchForm}));

    const onSearch = (_memberSearchForm = memberSearchForm) => {
        dispatch(searchUserInfoList(_memberSearchForm));
    };

    /*
     *     
     */
    const onClickNewMember = () => {
        const data = {
            id: -1,
            userId: '',
            password: '',
            name: '',
            role: 'USER',
            contactInfo: {
                mobile: '',
                email: ''
            }
        };
        dispatch(setMemberDataForUpdate(data));
        dispatch(showModifyMemberInfoModal());
    };

    useEffect(() => {
        if (history.action === 'PUSH') {
            const searchFormT = getMemberSearchFormInitialState();
            onChangeSearchForm(searchFormT);
            onSearch(searchFormT.userId);
        }
        if (history.action === 'POP') {
            onSearch();
        }
    }, []);

    useEffect(() => {
        // search fail
        if (!searchLoading && searchError) {
            toast.error(getErrorMsg(searchError, 'search'));
            dispatch(clearSearchUserInfoList());
        }
    }, [searchError]);

    return (
        <>
            <MemberSearch
                onSearch={onSearch}
                searchForm={memberSearchForm}
                onChangeSearchForm={onChangeSearchForm}
                onClickNewMember={onClickNewMember}
            />
            <Spinner isLoading={searchLoading} />
        </>
    );
    ///
}

export default MemberSearchContainer;
