import React, {useEffect} from 'react';
import {useHistory} from 'react-router-dom';
import {useSelector, useDispatch} from 'react-redux';
import {toast} from 'react-toastify';
import ScenarioTable from '@pages/scenario/ScenarioTable';
import {useTranslation} from 'react-i18next';
import {
    searchScenario,
    setSearchForm,
    deleteScenario,
    deleteScenarioDataClear,
    usedScenarioLockModal
} from '../../store/scenario';
import Spinner from '../../components/spinner/Spinner';
import {getErrorMsg} from '../../lib/commonUiUtils';
import ScenarioLockContainer from './popup/ScenarioLockContainer';

/*
 * ScenarioContainer
 *    container
 */
function ScenarioTableContainer() {
    const dispatch = useDispatch();
    const [t] = useTranslation();
    const history = useHistory();
    const {data: searchData} = useSelector((state) => state.scenario.search);

    const {
        loading: deleteLoading,
        data: deleteResponse,
        error: deleteError
    } = useSelector((state) => state.scenario.delete);

    const moveUiTest = (key) => {
        history.push({
            pathname: '/uitest',
            state: {
                key
            }
        });
    };

    const {searchForm} = useSelector((state) => state.scenario);
    const movePage = (page) => {
        const searchFormT = {...searchForm};
        searchFormT.page = page;
        dispatch(setSearchForm(searchFormT));
        dispatch(searchScenario(searchFormT));
    };

    const onDelete = (scenarioId) => {
        // eslint-disable-next-line no-alert
        if (window.confirm(t('common.alert.checkDelete'))) {
            dispatch(deleteScenario(scenarioId));
        }
    };

    /**
     * 2022-03-17 kimtaehan Scenario Lock  
     * @param {*} id
     * @param {*} state
     */
    const onClickLock = (id, lock) => {
        const date = {
            scenarioId: id,
            lock,
            parent: 'scenario'
        };

        dispatch(usedScenarioLockModal(true, date));
    };

    useEffect(() => {
        if (!deleteLoading && deleteResponse) {
            dispatch(deleteScenarioDataClear());
            dispatch(searchScenario(searchForm));
            toast.success(t('common.alert.updateSuccess'));
        }
        if (!deleteLoading && deleteError) {
            toast.error(getErrorMsg(deleteError, 'save'));
            dispatch(deleteScenarioDataClear());
        }
    }, [deleteResponse, deleteError]);

    /*
     *  
     */
    return (
        <>
            <ScenarioTable
                movePage={movePage}
                data={searchData}
                moveUiTest={moveUiTest}
                onDelete={onDelete}
                onClickLock={onClickLock}
            />
            <Spinner isLoading={deleteLoading} />
            <ScenarioLockContainer />
        </>
    );
}

export default ScenarioTableContainer;
