import React from 'react';
import {FaSearch} from 'react-icons/fa';
import {useTranslation} from 'react-i18next';
import Table from '../../components/table/Table';
import Button from '../../components/button/Button';
import {
    renderHttpMethod,
    renderStatusCode2
    // ,
    // getSuccessImage
} from '../../lib/commonUiUtils';
import {changeToDate} from '../../lib/moment';

const LogDetailTable = ({data, movePage, showUndoFormModal}) => {
    const [t] = useTranslation();
    const tableName = t('pages.history.historyList');
    const rowKey = 'logId';
    const columns = [
        // {
        //     title: '',
        //     key: 'testResult',
        //     width: '5%',
        //     render: (id, row) => {
        //         return getSuccessImage(row.isSuccess);
        //     }
        // },
        {
            title: t('pages.scenario.httpMethod'),
            width: '5%',
            key: 'httpMethod',
            align: 'center', // left, center, right (default:center)
            render: (id, row) => {
                return renderHttpMethod(row.httpMethod);
            }
        },
        {
            title: 'API (URL )',
            key: 'serviceId',
            align: 'left'
        },

        {
            title: t('pages.server.serverName'),
            width: '20%',
            key: 'serverName'
        },

        {
            title: t('common.text.resCode'),
            key: 'systemAHttpResponseCode',
            width: '10%',
            render: (id, row) => {
                return renderStatusCode2(row.responseInfo);
            }
        },

        {
            title: t('pages.mass.startTime'),
            width: '10%',
            key: 'sendDateTime',
            render: (id, row) => {
                return changeToDate(row.responseTime?.sendDateTime);
            }
        },
        {
            title: t('pages.mass.executeSecond'),
            key: 'processTime',
            width: '10%',
            render: (id, row) => {
                return row.responseTime?.processTime;
            }
        },
        {
            title: '',
            key: 'button',
            width: '5%',
            align: 'right',
            render: (id, row) => {
                return (
                    <Button
                        theme="outline-success"
                        onClick={() => showUndoFormModal(row)}
                    >
                        <FaSearch />
                    </Button>
                );
            }
        }
    ];

    const onDoubleClick = (id, row) => {
        showUndoFormModal(row);
    };

    return (
        <>
            <Table
                tableName={tableName}
                movePage={movePage}
                rowKey={rowKey}
                columns={columns}
                data={data}
                onDoubleClick={onDoubleClick}
            />
        </>
    );
};

export default LogDetailTable;
