/* eslint-disable no-unused-vars */
import React, {useState, useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import ScheduleSave from '@app/pages/schedule/popup/ScheduleSave';
import {toast} from 'react-toastify';
import {useTranslation} from 'react-i18next';
import {
    clearSchedualSaveModal,
    saveSchedule,
    clearSaveSchedule,
    updateSchedule,
    clearUpdateSchedule,
    searchSchedule
} from '../../../store/schedule';
import Spinner from '../../../components/spinner/Spinner';
import {getErrorMsg} from '../../../lib/commonUiUtils';

/*
 * SchedulSaveContainer : schedul create/update 
 */
const SchedulSaveContainer = () => {
    const dispatch = useDispatch();
    const [t] = useTranslation();
    const [modalShow, setModalShow] = React.useState(false);
    const {schedualSaveModalInitData} = useSelector((state) => state.schedule);
    const [scheduleData, setScheduleData] = useState({
        id: -1,
        scenarioId: -1,
        clubId: -1,
        scenarioName: ''
    });
    const {searchScheduleForm} = useSelector((state) => state.schedule);

    const handleClose = () => {
        setModalShow(false);
    };
    const handleShow = () => setModalShow(true);

    const {data: codeData} = useSelector((state) => state.common.searchCode);
    const {data: serverData} = useSelector(
        (state) => state.common.searchServer
    );

    const onSubmit = (values) => {
        if (values.scheduleId === -1) dispatch(saveSchedule(values));
        else dispatch(updateSchedule(values));
    };

    const {
        data: saveData,
        loading: saveLoading,
        error: saveError
    } = useSelector((state) => state.schedule.saveScheduleRes);

    const {
        data: updateData,
        loading: updateLoading,
        error: updateError
    } = useSelector((state) => state.schedule.updateScheduleRes);

    useEffect(() => {
        if (schedualSaveModalInitData.showModal) {
            setScheduleData({...schedualSaveModalInitData.initData});
            dispatch(clearSchedualSaveModal());
            handleShow();
        }
    }, [schedualSaveModalInitData.showModal]);

    useEffect(() => {
        if (!saveLoading && saveData) {
            dispatch(clearSaveSchedule());
            toast.success(t('common.alert.updateSuccess'));
            handleClose();
        }
        if (!saveLoading && saveError) {
            toast.error(getErrorMsg(saveError, 'save'));
            dispatch(clearSaveSchedule());
        }
    }, [saveData, saveError]);

    useEffect(() => {
        if (!updateLoading && updateData) {
            dispatch(clearUpdateSchedule());
            dispatch(searchSchedule(searchScheduleForm));
            toast.success(t('common.alert.updateSuccess'));
            handleClose();
        }
        if (!updateLoading && updateError) {
            toast.error(getErrorMsg(updateError, 'save'));
            dispatch(clearUpdateSchedule());
        }
    }, [updateData, updateError]);

    return (
        <>
            <ScheduleSave
                modalShow={modalShow}
                handleClose={handleClose}
                scheduleData={scheduleData}
                codeData={codeData}
                serverData={serverData}
                onSubmit={onSubmit}
            />
            <Spinner isLoading={saveLoading || updateLoading} />
        </>
    );
};
export default SchedulSaveContainer;
