/* eslint-disable no-unused-vars */
import React from 'react';
import {
    LineChart,
    Line,
    XAxis,
    YAxis,
    CartesianGrid,
    Tooltip,
    Legend,
    ResponsiveContainer
} from 'recharts';
import {useTranslation} from 'react-i18next';
import Card2 from '../../components/card/Card';

const colorList = ['#8884d8', '#ff7300', '#413ea0', '#82ca9d'];

const DashboardTestCnt = ({searchStatData, serverData}) => {
    const [t] = useTranslation();
    const renderLine = () => {
        return serverData?.data
            .filter(
                (obj) =>
                    obj.serverType === 'HTTP' || obj.serverType === 'TCP_IP'
            )
            .map((obj, idx) => (
                <Line
                    id={idx}
                    key={obj.serverId}
                    type="monotone"
                    dataKey={obj.serverName}
                    stroke={colorList[idx]}
                />
            ));
    };

    const getInitData = () => {
        const result = {};
        if (serverData) {
            serverData.data
                .filter(
                    (obj) =>
                        obj.serverType === 'HTTP' || obj.serverType === 'TCP_IP'
                )
                .forEach((obj) => {
                    result[obj.serverName] = 0;
                });
        }
        return result;
    };
    // eslint-disable-next-line no-unused-vars
    const renderChart = () => {
        if (searchStatData) {
            const data = [];
            searchStatData.data.forEach((obj) => {
                const tempObj = getInitData();
                tempObj.name = obj.date;
                obj.list.forEach((serverObj) => {
                    tempObj[serverObj.serverName] = serverObj.count;
                });

                data.push(tempObj);
            });
            return (
                <>
                    <ResponsiveContainer width="100%" height="100%">
                        <LineChart width={500} height={300} data={data}>
                            <CartesianGrid strokeDasharray="3 3" />
                            <XAxis
                                dataKey="name"
                                padding={{left: 30, right: 30}}
                            />
                            <YAxis />
                            <Tooltip />
                            <Legend />
                            {renderLine()}
                        </LineChart>
                    </ResponsiveContainer>
                </>
            );
        }
        return '';
    };
    return (
        <>
            <Card2
                title={t('pages.dashboard.testCntFromDay')}
                body={
                    <>
                        <div style={{height: '600px'}}>{renderChart()}</div>
                    </>
                }
            />
        </>
    );
};

export default DashboardTestCnt;
