import React from 'react';
import {useSelector, useDispatch} from 'react-redux';
import CredentialInfoCreate from '@pages/credentialInfo/CredentialInfoCreate';
import {toast} from 'react-toastify';
import {useTranslation} from 'react-i18next';
import {
    searchCredentialInfo,
    createCredentialInfo,
    clearCreateCredentialInfo
} from '../../store/credentialInfo';
import {getErrorMsg} from '../../lib/commonUiUtils';

function CredentialInfoCreateContainer() {
    const dispatch = useDispatch();
    const [t] = useTranslation();

    const {selectedServer} = useSelector((state) => state.credentialInfo);

    const {
        loading: createLoading,
        data: createResponse,
        error: createError
    } = useSelector((state) => state.credentialInfo.create);

    // [START] ----------------------------------------
    const onCreate = (createForm) => {
        dispatch(createCredentialInfo(createForm));
    };
    // [END]] ----------------------------------------

    // [START] ----------------------------------------
    // 
    if (!createLoading && createResponse) {
        dispatch(clearCreateCredentialInfo());
        toast.success(t('common.alert.updateSuccess'));
        dispatch(searchCredentialInfo(selectedServer.id));
    }

    // fail
    if (!createLoading && createError) {
        dispatch(clearCreateCredentialInfo());
        toast.error(getErrorMsg(createError, 'save'));
    }
    // [END] ------------------------------------------------
    return (
        <>
            <CredentialInfoCreate
                selectedServer={selectedServer}
                createLoading={createLoading}
                onCreate={onCreate}
            />
        </>
    );
    ///
}

export default CredentialInfoCreateContainer;
