import React from 'react';
import {useTranslation} from 'react-i18next';
import {Accordion, FloatingLabel, Modal, Form, Row, Col} from 'react-bootstrap';
import {useFormik} from 'formik';
import * as Yup from 'yup';
import Button from '../../../components/button/Button';
import {
    printFormError,
    getInputValidMsg,
    renderModalHeader
} from '../../../lib/commonUiUtils';
import {renderOptionsByCode} from '../../../lib/codeUtils';

const LayoutCreatePopup = ({modalShow, handleClose, createLayout}) => {
    const [t] = useTranslation();

    const formik = useFormik({
        enableReinitialize: true,
        initialValues: {
            urlPath: '',
            httpMethod: 'POST',
            serviceId: '',
            serviceName: '',
            fnCode: ''
        },
        validationSchema: Yup.object({
            serviceId: Yup.string().required(
                getInputValidMsg(t('pages.layout.interfaceId'))
            )
        }),
        onSubmit: (values) => {
            createLayout(values);
        }
    });

    return (
        <>
            <Modal
                show={modalShow}
                onHide={handleClose}
                onExited={() => formik.resetForm()}
                dialogClassName="modal-25w"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Form onSubmit={formik.handleSubmit}>
                    {renderModalHeader(t('pages.layout.newInterface'))}
                    <Modal.Body>
                        <Accordion defaultActiveKey="0">
                            <Accordion.Item eventKey="0">
                                <Accordion.Header>API </Accordion.Header>
                                <Accordion.Body>
                                    <Row>
                                        <Col xs="9">
                                            <FloatingLabel
                                                label="URL PATH"
                                                className="mb-1"
                                            >
                                                <Form.Control
                                                    type="text"
                                                    {...formik.getFieldProps(
                                                        'urlPath'
                                                    )}
                                                    maxLength="100"
                                                />
                                                {printFormError(
                                                    formik,
                                                    'urlPath'
                                                )}
                                            </FloatingLabel>
                                        </Col>
                                        <Col xs="3">
                                            <FloatingLabel
                                                label={t(
                                                    'pages.scenario.httpMethod'
                                                )}
                                                className="mb-3"
                                            >
                                                <Form.Select
                                                    {...formik.getFieldProps(
                                                        'httpMethod'
                                                    )}
                                                >
                                                    {renderOptionsByCode(
                                                        'HttpMethods'
                                                    )}
                                                </Form.Select>
                                            </FloatingLabel>
                                        </Col>

                                        <Col xs="5">
                                            <FloatingLabel
                                                label="Service ID"
                                                className="mb-1"
                                            >
                                                <Form.Control
                                                    type="text"
                                                    {...formik.getFieldProps(
                                                        'serviceId'
                                                    )}
                                                    maxLength="100"
                                                />
                                                {printFormError(
                                                    formik,
                                                    'serviceId'
                                                )}
                                            </FloatingLabel>
                                        </Col>
                                        <Col xs="7">
                                            <FloatingLabel
                                                label="Service Desc"
                                                className="mb-1"
                                            >
                                                <Form.Control
                                                    type="text"
                                                    {...formik.getFieldProps(
                                                        'serviceDesc'
                                                    )}
                                                    maxLength="100"
                                                />
                                            </FloatingLabel>
                                        </Col>
                                    </Row>
                                </Accordion.Body>
                            </Accordion.Item>
                        </Accordion>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button theme="primary" type="submit">
                            {t('common.button.save')}
                        </Button>
                        <Button theme="default" onClick={handleClose}>
                            {t('common.button.close')}
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>
        </>
    );
};
export default LayoutCreatePopup;
