import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import UitestStepPopXhr2 from '@pages/uitest/step/UitestStepPopXhr2';
import {toast} from 'react-toastify';
import {useTranslation} from 'react-i18next';
import {
    setDetailCaseList,
    convertPayload,
    clearConvertPayload,
    saveScenario
} from '../../../store/uitest';
import {setTempKey} from '../../../store/common';
import Spinner from '../../../components/spinner/Spinner';
import {jsonStrFormatPretty} from '../../../lib/jsonUtils';
import {getErrorMsg} from '../../../lib/commonUiUtils';

/**
 * copy to fetch container
 * @param {} param0
 * @returns
 */
function UitestStepPopXhr2Container({handleClose}) {
    const dispatch = useDispatch();
    const [t] = useTranslation();
    const {data: scenarioData} = useSelector(
        (state) => state.uitest.scenarioInfo
    );

    const {caseId} = useSelector((state) => state.uitest.selectedCaseId);
    const saveDetailCaseList = (caseList) => {
        dispatch(setDetailCaseList(caseList));

        // 2022-03-09 kimtaehan     Scenario 
        const tempData = scenarioData.data;
        tempData.caseList = caseList;
        if (!scenarioData.data.lock) {
            dispatch(saveScenario(tempData));
        }
    };

    const {tempkey} = useSelector((state) => state.common);
    const setTempKeyRezen = () => {
        if (tempkey < -9999) {
            dispatch(setTempKey(-1));
        } else {
            dispatch(setTempKey(tempkey - 1));
        }
    };

    const [payload, setPayload] = React.useState('');
    const [layoutDetail, setLayoutDetail] = React.useState('');

    /*
     * onClickTransform -> payload converter json
     */
    const onClickTransform = () => {
        dispatch(convertPayload(payload));
    };

    // layout list 
    const {
        data: convertPayloadData,
        loading: convertPayloadLoading,
        error: convertPayloadError
    } = useSelector((state) => state.uitest.convertPayload);

    useEffect(() => {
        if (!convertPayloadLoading && convertPayloadData) {
            const tempLayoutDetail = {...convertPayloadData};
            tempLayoutDetail.data.inputData = jsonStrFormatPretty(
                convertPayloadData.data.reqeustBodyIR
            );

            setLayoutDetail(tempLayoutDetail.data);

            //  
            dispatch(clearConvertPayload());
        }
        if (!convertPayloadLoading && convertPayloadError) {
            toast.error(
                getErrorMsg(convertPayloadError, t('common.alert.searchFail'))
            );
            dispatch(clearConvertPayload());
        }
    }, [convertPayloadData, convertPayloadError]);

    const onChangeRequestBodyIR = (e) => {
        const tempLayoutDetail = {...layoutDetail};
        tempLayoutDetail.inputData = jsonStrFormatPretty(e.target.value);
        setLayoutDetail(tempLayoutDetail);
    };
    return (
        <>
            <UitestStepPopXhr2
                scenarioData={scenarioData}
                selectedCaseId={caseId}
                setDetailCaseList={saveDetailCaseList}
                handleClose={handleClose}
                tempkey={tempkey}
                setTempKeyRezen={setTempKeyRezen}
                payload={payload}
                setPayload={setPayload}
                onClickTransform={onClickTransform}
                layoutDetail={layoutDetail}
                onChangeRequestBodyIR={onChangeRequestBodyIR}
            />
            <Spinner isLoading={convertPayloadLoading} />
        </>
    );
}

export default UitestStepPopXhr2Container;
