/* eslint-disable no-unused-vars */
import {useSelector, useDispatch} from 'react-redux';
import {useHistory} from 'react-router-dom';
import React, {useState, useEffect} from 'react';
import {useTranslation} from 'react-i18next';
import StatTab2ServerModal from '@pages/stat/popup/StatTab2ServerModal';
import {clearStatDetailModal2} from '../../../store/stat';

function StatTab2ServerModalContainer() {
    const [t] = useTranslation();
    const dispatch = useDispatch();
    const history = useHistory();

    const [statDetailData, setStatDetailData] = useState();

    const {searchFormT2} = useSelector((state) => state.stat);
    const {searchT2} = useSelector((state) => state.stat);

    /** **************************
     * initializer
     ************************** */

    /** **************************
     * component state
     ************************** */
    const [modalShow, setModalShow] = React.useState(false);
    const handleClose = () => setModalShow(false);
    const handleShow = () => setModalShow(true);

    // const {searchFormT1} = useSelector((state) => state.stat);

    const moveHistory = () => {
        history.push({
            pathname: '/history',
            state: {
                serviceId: '',
                fromDate: searchFormT2.fromDateOld,
                toDate: searchFormT2.toDateOld,
                serverId: statDetailData.serverId,
                regCode: 'real'
            }
        });
    };

    /** **************************
     * Redux  
     ************************** */
    const {statDetailModalInitData2} = useSelector((state) => state.stat);

    /** **************************
     * API   
     ************************** */
    useEffect(() => {
        if (statDetailModalInitData2.showModal && !modalShow) {
            setStatDetailData({...statDetailModalInitData2.initData});
            dispatch(clearStatDetailModal2());
            handleShow();
        }
    }, [statDetailModalInitData2.showModal]);

    return (
        <>
            <StatTab2ServerModal
                modalShow={modalShow}
                handleClose={handleClose}
                statDetailData={statDetailData}
                moveHistory={moveHistory}
                searchFormT2={searchFormT2}
                searchT2={searchT2}
            />
            {/* <Spinner isLoading={changeLoading} /> */}
        </>
    );
    ///
}

export default StatTab2ServerModalContainer;
