/* eslint-disable indent */
import {takeEvery, takeLatest} from 'redux-saga/effects';
import * as settingAPI from '../api/setting'; // api/auth    
import {
    reducerUtils,
    handleAsyncActions,
    createPromiseSaga
} from '../lib/asyncUtils';

const PREFIX = 'SETTING';

/**
 *   (TR)
 * uploadClientExecuteFile (UPLOAD_CLIENT_EXECUTE_FILE) : 
 * clearUploadClientExecuteFile (CLEAR_UPLOAD_CLIENT_EXECUTE_FILE) : 
 */
const UPLOAD_CLIENT_EXECUTE_FILE = `${PREFIX}/UPLOAD_CLIENT_EXECUTE_FILE`;
const UPLOAD_CLIENT_EXECUTE_FILE_SUCCESS = `${PREFIX}/UPLOAD_CLIENT_EXECUTE_FILE_SUCCESS`;
const UPLOAD_CLIENT_EXECUTE_FILE_ERROR = `${PREFIX}/UPLOAD_CLIENT_EXECUTE_FILE_ERROR`;
const CLEAR_UPLOAD_CLIENT_EXECUTE_FILE = `${PREFIX}/CLEAR_UPLOAD_CLIENT_EXECUTE_FILE`;

export const uploadClientExecuteFile = (file) => ({
    type: UPLOAD_CLIENT_EXECUTE_FILE,
    payload: {
        file
    }
});

export const clearUploadClientExecuteFile = () => ({
    type: CLEAR_UPLOAD_CLIENT_EXECUTE_FILE
});

/**
 *    (TR)
 * searchNowClientInstallFileVersion (SEARCH_NOW_CLIENT_INSTALL_FILE_VERSION) : 
 */
const SEARCH_NOW_CLIENT_INSTALL_FILE_VERSION = `${PREFIX}/SEARCH_NOW_CLIENT_INSTALL_FILE_VERSION`;
const SEARCH_NOW_CLIENT_INSTALL_FILE_VERSION_SUCCESS = `${PREFIX}/SEARCH_NOW_CLIENT_INSTALL_FILE_VERSION_SUCCESS`;
const SEARCH_NOW_CLIENT_INSTALL_FILE_VERSION_ERROR = `${PREFIX}/SEARCH_NOW_CLIENT_INSTALL_FILE_VERSION_ERROR`;

export const searchNowClientInstallFileVersion = () => ({
    type: SEARCH_NOW_CLIENT_INSTALL_FILE_VERSION
});

/**
 *   (TR)
 * uploadClientInstallFile (UPLOAD_CLIENT_INSTALL_FILE) : 
 * clearUploadClientInstallFile (CLEAR_UPLOAD_CLIENT_INSTALL_FILE) : 
 */
const UPLOAD_CLIENT_INSTALL_FILE = `${PREFIX}/UPLOAD_CLIENT_INSTALL_FILE`;
const UPLOAD_CLIENT_INSTALL_FILE_SUCCESS = `${PREFIX}/UPLOAD_CLIENT_INSTALL_FILE_SUCCESS`;
const UPLOAD_CLIENT_INSTALL_FILE_ERROR = `${PREFIX}/UPLOAD_CLIENT_INSTALL_FILE_ERROR`;
const CLEAR_UPLOAD_CLIENT_INSTALL_FILE = `${PREFIX}/CLEAR_UPLOAD_CLIENT_INSTALL_FILE`;
export const uploadClientInstallFile = (newVersion, file) => ({
    type: UPLOAD_CLIENT_INSTALL_FILE,
    payload: {
        newVersion,
        file
    }
});

export const clearUploadClientInstallFile = () => ({
    type: CLEAR_UPLOAD_CLIENT_INSTALL_FILE
});

/**
 *    (TR)
 * searchChromeDriverFileInfo (SEARCH_CHROME_DRIVER_FILE_VERSION) : 
 */
const SEARCH_CHROME_DRIVER_FILE_VERSION = `${PREFIX}/SEARCH_CHROME_DRIVER_FILE_VERSION`;
const SEARCH_CHROME_DRIVER_FILE_VERSION_SUCCESS = `${PREFIX}/SEARCH_CHROME_DRIVER_FILE_VERSION_SUCCESS`;
const SEARCH_CHROME_DRIVER_FILE_VERSION_ERROR = `${PREFIX}/SEARCH_CHROME_DRIVER_FILE_VERSION_ERROR`;

export const searchChromeDriverFileInfo = () => ({
    type: SEARCH_CHROME_DRIVER_FILE_VERSION
});

/**
 *   (TR)
 * uploadChromeDriverFile (UPLOAD_CHROME_DRIVER_FILE) : 
 * clearUploadChromeDriverFile (CLEAR_UPLOAD_CHROME_DRIVER_FILE) : 
 */
const UPLOAD_CHROME_DRIVER_FILE = `${PREFIX}/UPLOAD_CHROME_DRIVER_FILE`;
const UPLOAD_CHROME_DRIVER_FILE_SUCCESS = `${PREFIX}/UPLOAD_CHROME_DRIVER_FILE_SUCCESS`;
const UPLOAD_CHROME_DRIVER_FILE_ERROR = `${PREFIX}/UPLOAD_CHROME_DRIVER_FILE_ERROR`;
const CLEAR_UPLOAD_CHROME_DRIVER_FILE = `${PREFIX}/CLEAR_UPLOAD_CHROME_DRIVER_FILE`;

export const uploadChromeDriverFile = (version, os, file) => ({
    type: UPLOAD_CHROME_DRIVER_FILE,
    payload: {
        version,
        os,
        file
    }
});

export const clearUploadChromeDriverFile = () => ({
    type: CLEAR_UPLOAD_CHROME_DRIVER_FILE
});

/**
 *   (TR)
 * searchEnvironmentVariable (SEARCH_ENVIRONMENT_VARIABLE) : 
 */
const SEARCH_ENVIRONMENT_VARIABLE = `${PREFIX}/SEARCH_ENVIRONMENT_VARIABLE`;
const SEARCH_ENVIRONMENT_VARIABLE_SUCCESS = `${PREFIX}/SEARCH_ENVIRONMENT_VARIABLE_SUCCESS`;
const SEARCH_ENVIRONMENT_VARIABLE_ERROR = `${PREFIX}/SEARCH_ENVIRONMENT_VARIABLE_ERROR`;

export const searchEnvironmentVariable = () => ({
    type: SEARCH_ENVIRONMENT_VARIABLE
});

/**
 *   (TR)
 * saveEnvironmentVariable (SAVE_ENVIRONMENT_VARIABLE) : 
 * clearSaveEnvironmentVariable (CLEAR_SAVE_ENVIRONMENT_VARIABLE) : 
 */
const SAVE_ENVIRONMENT_VARIABLE = `${PREFIX}/SAVE_ENVIRONMENT_VARIABLE`;
const SAVE_ENVIRONMENT_VARIABLE_SUCCESS = `${PREFIX}/SAVE_ENVIRONMENT_VARIABLE_SUCCESS`;
const SAVE_ENVIRONMENT_VARIABLE_ERROR = `${PREFIX}/SAVE_ENVIRONMENT_VARIABLE_ERROR`;
const CLEAR_SAVE_ENVIRONMENT_VARIABLE = `${PREFIX}/CLEAR_SAVE_ENVIRONMENT_VARIABLE`;

export const saveEnvironmentVariable = (
    environmentVariableId,
    environmentValue
) => ({
    type: SAVE_ENVIRONMENT_VARIABLE,
    payload: {
        environmentVariableId,
        environmentValue
    }
});

export const clearSaveEnvironmentVariable = () => ({
    type: CLEAR_SAVE_ENVIRONMENT_VARIABLE
});

/**
 * Redux-saga 
 */
export function* settingSaga() {
    yield takeEvery(
        UPLOAD_CLIENT_EXECUTE_FILE,
        createPromiseSaga(
            UPLOAD_CLIENT_EXECUTE_FILE,
            settingAPI.uploadClientExecuteFile
        )
    );
    yield takeLatest(
        SEARCH_NOW_CLIENT_INSTALL_FILE_VERSION,
        createPromiseSaga(
            SEARCH_NOW_CLIENT_INSTALL_FILE_VERSION,
            settingAPI.searchNowClientInstallFileVersion
        )
    );
    yield takeEvery(
        UPLOAD_CLIENT_INSTALL_FILE,
        createPromiseSaga(
            UPLOAD_CLIENT_INSTALL_FILE,
            settingAPI.uploadClientInstallFile
        )
    );
    yield takeLatest(
        SEARCH_CHROME_DRIVER_FILE_VERSION,
        createPromiseSaga(
            SEARCH_CHROME_DRIVER_FILE_VERSION,
            settingAPI.searchChromeDriverFileInfo
        )
    );
    yield takeEvery(
        UPLOAD_CHROME_DRIVER_FILE,
        createPromiseSaga(
            UPLOAD_CHROME_DRIVER_FILE,
            settingAPI.uploadChromeDriverFile
        )
    );

    yield takeLatest(
        SEARCH_ENVIRONMENT_VARIABLE,
        createPromiseSaga(
            SEARCH_ENVIRONMENT_VARIABLE,
            settingAPI.searchEnvironmentVariable
        )
    );
    yield takeEvery(
        SAVE_ENVIRONMENT_VARIABLE,
        createPromiseSaga(
            SAVE_ENVIRONMENT_VARIABLE,
            settingAPI.saveEnvironmentVariable
        )
    );
}

/**
 * State 
 */
const initialState = {
    uploadClientExecuteFile: reducerUtils.initial(),
    searchNowClientInstallFileVersion: reducerUtils.initial(),
    uploadClientInstallFile: reducerUtils.initial(),
    searchChromeDriverFileInfo: reducerUtils.initial(),
    uploadChromeDriverFile: reducerUtils.initial(),
    executeLogColletor: reducerUtils.initial(),
    searchEnvironmentVariable: reducerUtils.initial(),
    saveEnvironmentVariable: reducerUtils.initial()
};

export default function setting(state = initialState, action) {
    switch (action.type) {
        case UPLOAD_CLIENT_EXECUTE_FILE:
        case UPLOAD_CLIENT_EXECUTE_FILE_SUCCESS:
        case UPLOAD_CLIENT_EXECUTE_FILE_ERROR:
            return handleAsyncActions(
                UPLOAD_CLIENT_EXECUTE_FILE,
                'uploadClientExecuteFile',
                true
            )(state, action);
        case CLEAR_UPLOAD_CLIENT_EXECUTE_FILE:
            return {
                ...state,
                uploadClientExecuteFile: reducerUtils.initial()
            };

        case SEARCH_NOW_CLIENT_INSTALL_FILE_VERSION:
        case SEARCH_NOW_CLIENT_INSTALL_FILE_VERSION_SUCCESS:
        case SEARCH_NOW_CLIENT_INSTALL_FILE_VERSION_ERROR:
            return handleAsyncActions(
                SEARCH_NOW_CLIENT_INSTALL_FILE_VERSION,
                'searchNowClientInstallFileVersion',
                true
            )(state, action);

        case UPLOAD_CLIENT_INSTALL_FILE:
        case UPLOAD_CLIENT_INSTALL_FILE_SUCCESS:
        case UPLOAD_CLIENT_INSTALL_FILE_ERROR:
            return handleAsyncActions(
                UPLOAD_CLIENT_INSTALL_FILE,
                'uploadClientInstallFile',
                true
            )(state, action);
        case CLEAR_UPLOAD_CLIENT_INSTALL_FILE:
            return {
                ...state,
                uploadClientInstallFile: reducerUtils.initial()
            };

        case SEARCH_CHROME_DRIVER_FILE_VERSION:
        case SEARCH_CHROME_DRIVER_FILE_VERSION_SUCCESS:
        case SEARCH_CHROME_DRIVER_FILE_VERSION_ERROR:
            return handleAsyncActions(
                SEARCH_CHROME_DRIVER_FILE_VERSION,
                'searchChromeDriverFileInfo',
                true
            )(state, action);

        case UPLOAD_CHROME_DRIVER_FILE:
        case UPLOAD_CHROME_DRIVER_FILE_SUCCESS:
        case UPLOAD_CHROME_DRIVER_FILE_ERROR:
            return handleAsyncActions(
                UPLOAD_CHROME_DRIVER_FILE,
                'uploadChromeDriverFile',
                true
            )(state, action);
        case CLEAR_UPLOAD_CHROME_DRIVER_FILE:
            return {
                ...state,
                uploadChromeDriverFile: reducerUtils.initial()
            };

        case SEARCH_ENVIRONMENT_VARIABLE:
        case SEARCH_ENVIRONMENT_VARIABLE_SUCCESS:
        case SEARCH_ENVIRONMENT_VARIABLE_ERROR:
            return handleAsyncActions(
                SEARCH_ENVIRONMENT_VARIABLE,
                'searchEnvironmentVariable',
                true
            )(state, action);

        case SAVE_ENVIRONMENT_VARIABLE:
        case SAVE_ENVIRONMENT_VARIABLE_SUCCESS:
        case SAVE_ENVIRONMENT_VARIABLE_ERROR:
            return handleAsyncActions(
                SAVE_ENVIRONMENT_VARIABLE,
                'saveEnvironmentVariable',
                true
            )(state, action);
        case CLEAR_SAVE_ENVIRONMENT_VARIABLE:
            return {
                ...state,
                saveEnvironmentVariable: reducerUtils.initial()
            };

        default:
            return state;
    }
}
