import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import ScHistoryTable from '@pages/scHistory/ScHistoryTable';
import {toast} from 'react-toastify';
import {
    searchScenarioHistory,
    clearScenarioHistoryDetail,
    searchScenarioHistoryDetail,
    setSearchFormH
} from '../../store/scenario';
import Spinner from '../../components/spinner/Spinner';
import {usedScenarioTestModal} from '../../store/common';
import {getErrorMsg} from '../../lib/commonUiUtils';

function ScHistoryTableContainer() {
    const {data: searchData} = useSelector((state) => state.scenario.searchH);

    const {searchFormH} = useSelector((state) => state.scenario);

    const dispatch = useDispatch();

    const movePage = (page) => {
        const searchFormHT = {...searchFormH};
        searchFormHT.page = page;
        dispatch(setSearchFormH(searchFormHT));
        dispatch(searchScenarioHistory(searchFormHT));
    };

    const onClickDetail = (id) => {
        dispatch(searchScenarioHistoryDetail(id));
    };

    const {
        data: searchDetailH,
        loading: searchDetailHLoading,
        error: searchDetailHError
    } = useSelector((state) => state.scenario.searchDetailH);

    useEffect(() => {
        if (!searchDetailHLoading && searchDetailH) {
            const steps = [];
            searchDetailH.data?.units?.forEach((unit) => {
                steps.push(JSON.parse(unit));
            });

            // searchDetailH.data?.units
            const data = {
                //  
                scenarioId: searchDetailH.data.scenarioId,
                scenarioName: searchDetailH.data.scenarioName,
                cases: [],
                wasServerId: searchDetailH.data.wasServerId,
                steps,
                variables: JSON.parse(searchDetailH.data?.variableJson),
                parentId: 'ScHistoryTable',
                isSuccess: searchDetailH.data?.recordResult?.isSuccess,
                executeSecond: searchDetailH.data?.recordResult?.processTime,
                startTime: searchDetailH.data?.recordResult?.startDateTime,
                endTime: searchDetailH.data?.recordResult?.endDateTime
            };

            dispatch(usedScenarioTestModal(true, data));
            dispatch(clearScenarioHistoryDetail());
        }
        if (!searchDetailHLoading && searchDetailHError) {
            toast.error(getErrorMsg(searchDetailHError, 'search'));
            dispatch(clearScenarioHistoryDetail());
        }
    }, [searchDetailH, searchDetailHError]);

    return (
        <>
            <ScHistoryTable
                movePage={movePage}
                data={searchData}
                onClickDetail={onClickDetail}
            />
            <Spinner isLoading={searchDetailHLoading} />
        </>
    );
}

export default ScHistoryTableContainer;
