/* eslint-disable no-unused-vars */
import React, {useState, useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import SourceAnalysisModal from '@pages/source/popup/SourceAnalysisModal';
import {toast} from 'react-toastify';
import {useTranslation} from 'react-i18next';
import Spinner from '../../../components/spinner/Spinner';
import {getErrorMsg} from '../../../lib/commonUiUtils';
import {
    clearSourceAnalysisModal,
    clearanalysisSource,
    analysisSource
} from '../../../store/source';

/*
 *     Container
 */
function SourceAnalysisModalContainer() {
    const dispatch = useDispatch();
    const [t] = useTranslation();

    //  Modal show/hide
    const [modalShow, setModalShow] = React.useState(false);
    const handleClose = () => {
        setModalShow(false);
        // dispatch(clearExecuteLogColletor());
    };
    const handleShow = () => setModalShow(true);

    const {usedSourceAnalysisModalInitData} = useSelector(
        (state) => state.source
    );

    /**
     *  
     */
    const onAnalysisSource = (values) => {
        dispatch(analysisSource(values));
    };

    /**
     *  Callback
     */
    const {
        data: analysisSourceResData,
        loading: analysisSourceResLoading,
        error: analysisSourceResError
    } = useSelector((state) => state.source.analysisSourceRes);

    useEffect(() => {
        if (!analysisSourceResLoading && analysisSourceResError) {
            toast.error(getErrorMsg(analysisSourceResError, 'execute'));
        }
        // Log snapshot delete success
        else if (!analysisSourceResLoading && analysisSourceResData) {
            toast.success(t('common.alert.updateSuccess'));

            // dispatch(
            //     searchLogSnapshot(
            //         searchLogSnapshotForm.fromDate,
            //         searchLogSnapshotForm.toDate,
            //         searchLogSnapshotForm.logMethodCode
            //     )
            // );

            // Modal Hide
            handleClose();
        }
        dispatch(clearanalysisSource());
    }, [analysisSourceResData, analysisSourceResLoading]);

    useEffect(() => {
        if (usedSourceAnalysisModalInitData.showModal && !modalShow) {
            // scheduleDataData({...scSchedualModalInitData.initData});
            dispatch(clearSourceAnalysisModal());
            handleShow();
        }
    }, [usedSourceAnalysisModalInitData.showModal]);

    return (
        <>
            <SourceAnalysisModal
                modalShow={modalShow}
                handleClose={handleClose}
                onAnalysisSource={onAnalysisSource}
            />
            {/* <executeLogColletorResLoading */}
            <Spinner isLoading={analysisSourceResLoading} />
        </>
    );
}

export default SourceAnalysisModalContainer;
