/* eslint-disable no-unused-vars */
/* eslint-disable jsx-a11y/label-has-associated-control */
import React from 'react';
import {useTranslation} from 'react-i18next';
import {Row, Col, Form, FloatingLabel} from 'react-bootstrap';
import Card from '../../components/card/Card';
import Button from '../../components/button/Button';
import {takeSelectOptionbyCode} from '../../lib/commonUiUtils';

const ScheduleSearch = ({
    searchFormS,
    onChangeForm,
    onClickSearch,
    codeData,
    memberData
}) => {
    const [t] = useTranslation();
    const onCheckEnter = (e) => {
        if (e.key === 'Enter') {
            e.preventDefault();
            onClickSearch();
        }
    };
    return (
        <Card
            title={t('common.text.searchCardTitleName')}
            body={
                <>
                    <Row>
                        <Col xs="6">
                            <Row onKeyPress={onCheckEnter}>
                                <Col xs="6">
                                    <FloatingLabel
                                        label=" "
                                        className="mb-2"
                                    >
                                        <Form.Control
                                            type="text"
                                            maxLength="30"
                                            id="clubName"
                                            onChange={onChangeForm}
                                            value={searchFormS.clubName}
                                        />
                                    </FloatingLabel>
                                </Col>

                                <Col xs="3">
                                    <FloatingLabel
                                        label={t('pages.scenario.scheduleType')}
                                        className="mb-2"
                                    >
                                        <Form.Select
                                            id="scheduleType"
                                            onChange={onChangeForm}
                                            value={
                                                searchFormS.scheduleType ?? ''
                                            }
                                            onKeyPress={onCheckEnter}
                                        >
                                            <option value="">
                                                {t('common.text.optionAll')}
                                            </option>
                                            {takeSelectOptionbyCode(
                                                codeData,
                                                'ScheduleType'
                                            )}
                                        </Form.Select>
                                    </FloatingLabel>
                                </Col>
                            </Row>
                        </Col>
                        <Col xs="5" />
                        <Col xs="1">
                            <div className="form-group">
                                <label htmlFor="myinput" />
                                <Button
                                    type="button"
                                    className="btn btn-block btn-secondary btn-search"
                                    // isLoading={searchLoading}
                                    onClick={(e) => onClickSearch()}
                                >
                                    {t('common.button.search')}
                                </Button>
                            </div>
                        </Col>
                    </Row>
                </>
            }
        />
    );
};

export default ScheduleSearch;
