import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import {useTranslation} from 'react-i18next';
import MemberTable from '@pages/member/MemberTable';
import {toast} from 'react-toastify';
import Spinner from '../../components/spinner/Spinner';
import {
    deleteMember,
    clearDeleteMember,
    showModifyMemberInfoModal,
    searchUserInfoList,
    setMemberDataForUpdate,
    setMemberSearchForm
} from '../../store/auth';

function MemberTableContainer() {
    const dispatch = useDispatch();
    const [t] = useTranslation();

    const {memberSearchForm} = useSelector((state) => state.auth);
    const {data: searchData} = useSelector(
        (state) => state.auth.memberInfoList
    );

    const {
        loading: deleteLoading,
        data: deleteResponse,
        error: deleteError
    } = useSelector((state) => state.auth.delete);

    const showUpdateFormModal = (data) => {
        dispatch(setMemberDataForUpdate(data));
        dispatch(showModifyMemberInfoModal());
    };

    const movePage = (page) => {
        const memberSearchFormT = {...memberSearchForm};
        memberSearchFormT.page = page;
        dispatch(setMemberSearchForm(memberSearchFormT));
        dispatch(searchUserInfoList(memberSearchFormT));
    };

    const onDelete = (serverId) => {
        // eslint-disable-next-line no-alert
        if (window.confirm(t('common.alert.checkDelete'))) {
            dispatch(deleteMember(serverId));
        }
    };

    useEffect(() => {
        // success
        if (!deleteLoading && deleteResponse) {
            dispatch(clearDeleteMember());

            dispatch(searchUserInfoList(memberSearchForm.userId));
        }

        // fail
        if (!deleteLoading && deleteError) {
            dispatch(clearDeleteMember());
            toast.error(
                (deleteError.response &&
                    deleteError.response.data &&
                    deleteError.response.data.message) ||
                    'Delete Failed'
            );
        }
    }, [deleteResponse]);

    return (
        <>
            <MemberTable
                showUpdateFormModal={showUpdateFormModal}
                onDelete={onDelete}
                movePage={movePage}
                data={searchData}
            />
            <Spinner isLoading={deleteLoading} />
        </>
    );
    ///
}

export default MemberTableContainer;
