/* eslint-disable no-unused-vars */
import React from 'react';

import Modal from 'react-bootstrap/Modal';
import {useFormik} from 'formik';
import * as Yup from 'yup';
import {useTranslation} from 'react-i18next';
import {Form, Row, Col, InputGroup, FormControl} from 'react-bootstrap';
import Button from '../../../components/button/Button';
import {renderModalHeader} from '../../../lib/commonUiUtils';

const LayoutDetailRandomUpdate = ({
    modalShow,
    handleClose,
    codeData,
    randData,
    testLoading,
    testRadomCall,
    data,
    setData,
    onClickSave //  
}) => {
    const [t] = useTranslation();

    /*
     * Test button click
     */
    const onClickRandomTest = () => {
        testRadomCall(data);
    };

    const renderRandCode = () => {
        return codeData?.data
            .filter((obj) => obj.codeGroup === 'rand_cd')
            .map((obj) => (
                <option value={obj.codeKey} key={obj.codeKey}>
                    {obj.codeName}
                </option>
            ));
    };

    const renderRandNameDp = () => {
        if (randData === null || data.randCode === '') {
            return '';
        }

        // 20220101 kimtaehan isDelete     .
        return randData.data
            .filter(
                (obj) =>
                    (obj.randCode === data.randCode && !obj.delete) ||
                    data.randomId === obj.id
            )
            .map((obj) => (
                <option value={obj.id} key={obj.id}>
                    {obj.randName}
                </option>
            ));
    };

    /*
     * onChangeRandCode -> rand code change
     */
    const onChangeRandCode = (e) => {
        const tempData = {...data};
        tempData.randCode = e.target.value;

        const randList = randData.data.filter(
            (obj) => obj.randCode === e.target.value
        );

        if (randList.length > 0) {
            tempData.randomId = randList[0].id;
            tempData.randKey = randList[0].key;
        } else {
            tempData.randomId = '';
            tempData.randKey = '';
        }
        tempData.testData = '';
        setData(tempData);
    };

    /*
     * onChangeRandCode -> rand code change
     */
    const onChangeRandId = (e) => {
        const tempData = {...data};
        tempData.randId = e.target.value;
        const randList = randData.data.filter(
            (obj) => obj.id === Number(e.target.value)
        );

        if (randList.length > 0) {
            tempData.randomId = randList[0].id;
            tempData.randKey = randList[0].key;
        } else {
            tempData.randKey = '';
        }
        tempData.testData = '';
        setData(tempData);
    };

    const renderTestData = () => {
        // eslint-disable-next-line no-debugger

        return data && data.testData;
    };

    return (
        <>
            <Modal
                show={modalShow}
                onHide={handleClose}
                size="lg"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                {/* <Form> */}
                {renderModalHeader(t('pages.layout.randChange'))}

                <Modal.Body>
                    <Row>
                        <Col xs="4">
                            <Form.Label>
                                {t('pages.random.randCode')}
                            </Form.Label>
                            <Form.Control
                                as="select"
                                onChange={onChangeRandCode}
                                value={data && data.randCode}
                            >
                                <option value="">
                                    {t('common.text.optionNone')}
                                </option>
                                {renderRandCode()}
                            </Form.Control>
                        </Col>
                        <Col xs="8">
                            <Form.Label>
                                {t('pages.random.randName')}
                            </Form.Label>
                            <Form.Control
                                as="select"
                                onChange={onChangeRandId}
                                value={data && data.randomId}
                            >
                                {renderRandNameDp()}
                            </Form.Control>
                        </Col>
                        <Col xs="8">
                            <Form.Group controlId="key">
                                <Form.Label>{t('pages.random.key')}</Form.Label>
                                <Row>
                                    <Col xs="6">
                                        <FormControl
                                            disabled
                                            value={data && data.randKey}
                                        />
                                        {/* {printFormError(formik, 'key')} */}
                                    </Col>
                                    <Col xs="6">
                                        <InputGroup>
                                            <FormControl
                                                disabled
                                                // value={data && data.testData}
                                                value={renderTestData()}
                                            />
                                            <Button
                                                theme="danger"
                                                onClick={onClickRandomTest}
                                                isLoading={testLoading}
                                            >
                                                {t('common.button.test')}
                                            </Button>
                                        </InputGroup>
                                    </Col>
                                </Row>
                            </Form.Group>
                        </Col>
                    </Row>
                </Modal.Body>
                <Modal.Footer>
                    <Button
                        theme="primary"
                        onClick={onClickSave}
                        // isLoading={updateLoading}
                    >
                        {t('common.button.update')}
                    </Button>
                    <Button theme="default" onClick={handleClose}>
                        {t('common.button.close')}
                    </Button>
                </Modal.Footer>
            </Modal>
        </>
    );
};

export default LayoutDetailRandomUpdate;
