/* eslint-disable no-unused-vars */
/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
import React from 'react';
import {Row, Col, Form, FloatingLabel} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
import Card from '../../components/card/Card';
import {takeSelectOptionbyCode} from '../../lib/commonUiUtils';
/**
 * Scnarion Test Club
 *  Container  .
 * @since 2022-03-29
 * @returns
 */
const ClubDetailInfo = ({
    infoData,
    setInfoData,
    codeData,
    onClickSave,
    onClickPlay,
    onClickSchedual,
    onClickRefresh,
    onClickLock
}) => {
    const [t] = useTranslation();
    const onChangerSearchForm = (e) => {
        const tempData = {...infoData};
        tempData[e.target.id] = e.target.value;

        setInfoData(tempData);
    };

    const renderLockButton = () => {
        if (!infoData) {
            return '';
        }

        if (infoData.isLock) {
            return (
                <a className="btn btn-app" onClick={() => onClickLock()}>
                    <i className="fas fa-lock" />
                    
                </a>
            );
        }
        return (
            <a className="btn btn-app" onClick={() => onClickLock()}>
                <i className="fas fa-unlock" />
                
            </a>
        );
    };

    return (
        <>
            <Card
                title=" "
                body={
                    <>
                        <Row>
                            <Col xs="4">
                                <FloatingLabel
                                    label=" "
                                    className="mb-2"
                                >
                                    <Form.Select
                                        id="clubType"
                                        onChange={onChangerSearchForm}
                                        disabled
                                        value={infoData?.clubType?.name}
                                    >
                                        {takeSelectOptionbyCode(
                                            codeData,
                                            'ClubType'
                                        )}
                                    </Form.Select>
                                </FloatingLabel>
                            </Col>
                            <Col xs="8">
                                <FloatingLabel label=" " className="mb-2">
                                    <Form.Control
                                        type="text"
                                        id="clubName"
                                        maxLength="50"
                                        onChange={onChangerSearchForm}
                                        value={infoData?.clubName}
                                    />
                                </FloatingLabel>
                            </Col>
                            <Col xs="12">
                                <FloatingLabel
                                    label="Description"
                                    className="mb-2"
                                >
                                    <Form.Control
                                        as="textarea"
                                        style={{
                                            height: '100px'
                                        }}
                                        id="clubDesc"
                                        onChange={onChangerSearchForm}
                                        value={infoData?.clubDesc}
                                    />
                                </FloatingLabel>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs="12">
                                <a
                                    className="btn btn-app"
                                    onClick={onClickSave}
                                    disabled={infoData && infoData.lock}
                                    style={{marginLeft: '0px'}}
                                >
                                    <i className="fas fa-save" />
                                    {t('common.button.save')}
                                </a>
                                <a
                                    className="btn btn-app"
                                    onClick={onClickPlay}
                                >
                                    <i className="fas fa-play" />
                                    {t('common.button.play')}
                                </a>
                                <a
                                    className="btn btn-app"
                                    onClick={onClickSchedual}
                                >
                                    <i className="fas fa-clock" />
                                    {t('common.button.schedule')}
                                </a>
                                <a
                                    className="btn btn-app"
                                    onClick={onClickRefresh}
                                >
                                    <i className="fas fa-reply" />
                                    
                                </a>
                                {renderLockButton()}
                            </Col>
                        </Row>
                    </>
                }
            />
        </>
    );
};
export default ClubDetailInfo;
