/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import SourceSearch from '@pages/source/SourceSearch';

import {
    usedSourceAnalysisModal,
    setSearchSourceSnapshotForm
} from '../../store/source';

/*
 *   Container
 */
function SourceSearchContainer({onSearch}) {
    const dispatch = useDispatch();

    const {searchSourceSnapshotForm} = useSelector((state) => state.source);

    /**
     *   
     */
    const showAnalysisSourceModal = () => {
        //         .
        dispatch(usedSourceAnalysisModal(true));
    };

    const onChangeEvent = (form) => {
        dispatch(setSearchSourceSnapshotForm({...form}));
    };

    return (
        <>
            <SourceSearch
                onSearch={onSearch}
                showAnalysisSourceModal={showAnalysisSourceModal}
                searchSourceSnapshotForm={searchSourceSnapshotForm}
                onChangeEvent={onChangeEvent}
            />
            {/* <Spinner isLoading={searchLogSnapshotResLoading} /> */}
        </>
    );
    ///
}

export default SourceSearchContainer;
