import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import ChromeDriverVersionManagement from '@pages/setting/ChromeDriverVersionManagement';
import {toast} from 'react-toastify';
import {useTranslation} from 'react-i18next';
import {
    searchChromeDriverFileInfo,
    uploadChromeDriverFile,
    clearUploadChromeDriverFile
} from '../../store/setting';
import Spinner from '../../components/spinner/Spinner';

function ChromeDriverVersionManagementContainer() {
    const [t] = useTranslation();
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(searchChromeDriverFileInfo());
        return () => {};
    }, []);

    const {loading: searchLoading, data: searchData} = useSelector(
        (state) => state.setting.searchChromeDriverFileInfo
    );

    const {
        loading: uploadLoading,
        data: uploadData,
        error: uploadError
    } = useSelector((state) => state.setting.uploadChromeDriverFile);

    const onUploadChromeDriverFile = (version, os, file) => {
        dispatch(uploadChromeDriverFile(version, os, file));
    };

    useEffect(() => {
        /* Fail process */
        if (!uploadLoading && uploadError) {
            toast.error(
                (uploadError.response &&
                    uploadError.response.data &&
                    uploadError.response.data.message) ||
                    t('common.alert.uploadFail')
            );
            dispatch(clearUploadChromeDriverFile());
        }

        /* Success process */
        if (!uploadLoading && uploadData) {
            toast.success(t('common.alert.uploadSuccess'));
            dispatch(clearUploadChromeDriverFile());
            dispatch(searchChromeDriverFileInfo());
        }
    }, [uploadError, uploadData]);

    return (
        <>
            <ChromeDriverVersionManagement
                chromeDirverInfo={searchData}
                uploadChromeDriverFile={onUploadChromeDriverFile}
            />
            <Spinner isLoading={searchLoading || uploadLoading} />
        </>
    );
    ///
}

export default ChromeDriverVersionManagementContainer;
