/* eslint-disable no-unused-vars */
import React, {useState, useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import BulkCompareValue from '@app/pages/bulk/popup/BulkCompareValue';
import {clearBulkCompareValueModal} from '../../../store/bulk';

/*
 *    
 */
function BulkCompareValueContainer() {
    const dispatch = useDispatch();

    const [modalShow, setModalShow] = React.useState(false);
    const handleClose = () => {
        dispatch(clearBulkCompareValueModal());
        setModalShow(false);
    };
    const handleShow = () => {
        setModalShow(true);
    };

    const {bulkCompareValueModalInitData} = useSelector((state) => state.bulk);

    /*
     *   
     */
    useEffect(() => {
        if (bulkCompareValueModalInitData.showModal && !modalShow) {
            //     setId(bulkStateInitData.id);
            //     dispatch(searchBulkState(bulkStateInitData.id));
            handleShow();
            //     dispatch(clearBulkStateModal());
        }
    }, [bulkCompareValueModalInitData.showModal]);

    return (
        <>
            <BulkCompareValue
                modalShow={modalShow}
                handleClose={handleClose}
                initData={bulkCompareValueModalInitData.initData}
            />
        </>
    );
}

export default BulkCompareValueContainer;
