/* eslint-disable no-unused-vars */
import React, {useState, useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import BulkState from '@app/pages/bulk/popup/BulkState';
import {toast} from 'react-toastify';
import {
    clearBulkStateModal,
    searchBulkState,
    clearSearchBulkState,
    replayBulkTest,
    clearReplayBulkTest,
    reCompareBulkTest,
    clearReCompareBulkTest
} from '../../../store/bulk';
import {getErrorMsg} from '../../../lib/commonUiUtils';
import Spinner from '../../../components/spinner/Spinner';

/*
 *   -    Container
 */
function BulkStateContainer() {
    const dispatch = useDispatch();

    const [id, setId] = useState('');

    const [modalShow, setModalShow] = React.useState(false);
    const handleClose = () => {
        dispatch(clearSearchBulkState());
        setModalShow(false);
    };
    const handleShow = () => {
        setModalShow(true);
    };

    const {bulkStateInitData} = useSelector((state) => state.bulk);

    /*
     *   
     */
    const onClickReplay = () => {
        dispatch(replayBulkTest(id));
    };
    const onClickReroad = () => {
        dispatch(searchBulkState(id));
    };
    const onClickReCompare = () => {
        dispatch(reCompareBulkTest(id));
    };

    //   ()
    const {
        loading: searchStateLoading,
        data: searchStateData,
        error: searchStateError
    } = useSelector((state) => state.bulk.searchBulkStateRes);

    /*
     *      
     */
    useEffect(() => {
        // search
        // if (!searchStateLoading && searchStateData) {
        //     console.log(' .');
        // }
        // search fail
        if (!searchStateLoading && searchStateError) {
            toast.error(getErrorMsg(searchStateError, 'select'));
            handleClose();
        }
    }, [searchStateData, searchStateError]);

    //  
    const {
        loading: replayLoading,
        data: replayData,
        error: replayError
    } = useSelector((state) => state.bulk.replayBulkTestRes);

    /*
     *   
     */
    useEffect(() => {
        // search
        if (!replayLoading && replayData) {
            dispatch(clearReplayBulkTest());
            handleClose();
        }
        // search fail
        if (!replayLoading && replayError) {
            dispatch(clearReplayBulkTest());
            toast.error(getErrorMsg(replayError, 'save'));
        }
    }, [replayData, replayError]);

    //  
    const {
        loading: reCompareLoading,
        data: reCompareData,
        error: reCompareError
    } = useSelector((state) => state.bulk.reCompareBulkTestRes);

    useEffect(() => {
        // search
        if (!reCompareLoading && reCompareData) {
            dispatch(clearReCompareBulkTest());
            handleClose();
        }
        // search fail
        if (!reCompareLoading && reCompareError) {
            dispatch(clearReCompareBulkTest());
            toast.error(getErrorMsg(replayError, 'save'));
        }
    }, [reCompareData, reCompareError]);
    /*
     *   
     */
    useEffect(() => {
        if (bulkStateInitData.showModal && !modalShow) {
            setId(bulkStateInitData.id);
            dispatch(searchBulkState(bulkStateInitData.id));
            handleShow();
            dispatch(clearBulkStateModal());
        }
    }, [bulkStateInitData.showModal]);

    return (
        <>
            <BulkState
                modalShow={modalShow}
                handleClose={handleClose}
                searchStateData={searchStateData}
                onClickReplay={onClickReplay}
                onClickReroad={onClickReroad}
                onClickReCompare={onClickReCompare}
            />
            <Spinner
                isLoading={
                    searchStateLoading || replayLoading || reCompareLoading
                }
            />
        </>
    );
}

export default BulkStateContainer;
