import React, {useState, useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import {toast} from 'react-toastify';
import BulkUpdate from '@app/pages/bulk/popup/BulkUpdate';
import {useTranslation} from 'react-i18next';
import {useHistory} from 'react-router-dom';
import {getErrorMsg} from '../../../lib/commonUiUtils';
import {
    clearBulktestModal,
    //   ()
    executeBulkUsingHistory,
    clearExecuteBulkUsingHistory,
    //   ()
    clearExecuteBulkUsingLayout,
    searchBulkList //    
} from '../../../store/bulk';
import {setUpdateServerInitData} from '../../../store/server';
import Spinner from '../../../components/spinner/Spinner';

/*
 * (Log, excel),   
 */
function BulkUpdateContainer() {
    const dispatch = useDispatch();
    const history = useHistory();
    const [t] = useTranslation();

    const [modalShow, setModalShow] = React.useState(false);

    const handleClose = () => {
        setModalShow(false);
    };
    const handleShow = () => setModalShow(true);

    const getInitData = () => {
        const offset = new Date().getTimezoneOffset() * 60000 - 60 * 60 * 1000;
        const offset2 = 60 * 60 * 1000;
        const today = new Date(Date.now() - offset + offset2);

        return {
            bulkOptions: {
                singleOrDual: 'SINGLE',
                compareValue: false,
                executionPoint: 'IMMEDIATE',
                reserveDateTime: today.toISOString().slice(0, -1).slice(0, 16)
            },
            bulkSnapshotName: '',
            basicData: '',
            server1Id: '',
            server2Id: ''
        };
    };

    //   
    const [initData, setInitData] = useState();

    //   
    const {data: codeData} = useSelector((state) => state.common.searchCode);

    //   
    const {data: serverData} = useSelector(
        (state) => state.common.searchServer
    );

    const {bulktestModalInitData} = useSelector((state) => state.bulk);

    // Search form data
    const {bulkListSearchForm} = useSelector((state) => state.bulk);

    //  (Search)
    const {
        loading: executeBulkHistoryLoading,
        data: executeBulkHistoryData,
        error: executeBulkHistoryError
    } = useSelector((state) => state.bulk.executeBulkUsingHistoryRes);

    //  (layout)
    const {
        loading: executeBulkLayoutLoading,
        data: executeBulkLayoutData,
        error: executeBulkLayoutError
    } = useSelector((state) => state.bulk.executeBulkUsingLayoutRes);

    /*
     *  
     */
    const getBulkSnapshotName = () => {
        const offset = new Date().getTimezoneOffset() * 60000;
        const today = new Date(Date.now() - offset)
            .toISOString()
            .slice(0, -1)
            .slice(0, 16);

        return ` ${today.slice(0, 10)} ${today.slice(11, 16)}`;
    };

    /*
     * /   
     */
    const onExecute = (values) => {
        const req = {...values};
        if (initData.bulkSnapshotName === '') {
            req.bulkSnapshotName = getBulkSnapshotName(values?.basicData);
            setInitData(req);
        }
        dispatch(executeBulkUsingHistory(req));
    };

    const onClickServerInfo = (serverId) => {
        const selectedServer = serverData.data.content.filter(
            (obj) => obj.id === Number(serverId)
        );

        if (selectedServer.length !== 1) {
            // eslint-disable-next-line no-alert
            alert('   .');
            return;
        }

        const tempData = {...selectedServer[0]};
        tempData.parentId = 'HttpTestView';
        dispatch(setUpdateServerInitData(tempData));
    };

    /*
     * popup call event
     */
    useEffect(() => {
        if (bulktestModalInitData.showModal) {
            // scheduleDataData({...scSchedualModalInitData.initData});
            const initDataT = getInitData();
            initDataT.bulkSnapshotName = getBulkSnapshotName(
                initDataT.basicData
            );

            initDataT.basicData = bulktestModalInitData.initData?.basicData;
            initDataT.predictCount =
                bulktestModalInitData.initData?.predictCount;
            initDataT.historiesFindCondition =
                bulktestModalInitData.initData?.historiesFindCondition;
            initDataT.logsFindCondition =
                bulktestModalInitData.initData?.logsFindCondition;

            setInitData(initDataT);
            dispatch(clearBulktestModal());
            handleShow();
        }
    }, [bulktestModalInitData.showModal]);

    /*
     *  (Search)  
     */
    useEffect(() => {
        if (!executeBulkHistoryLoading && executeBulkHistoryData) {
            dispatch(clearExecuteBulkUsingHistory());
            dispatch(searchBulkList(bulkListSearchForm));

            // modal close
            handleClose();
            toast.success(t('common.alert.testSuccess'));
        }
        if (!executeBulkHistoryLoading && executeBulkHistoryError) {
            toast.error(getErrorMsg(executeBulkHistoryError, 'execute'));
            dispatch(clearExecuteBulkUsingHistory());
        }
    }, [executeBulkHistoryData, executeBulkHistoryError]);

    /*
     *  (Layout))  
     */
    useEffect(() => {
        if (!executeBulkLayoutLoading && executeBulkLayoutData) {
            toast.success(t('common.alert.testSuccess'));
            dispatch(clearExecuteBulkUsingLayout());
            // layout    
            // trigger testgroup search
            // eslint-disable-next-line no-alert
            if (window.confirm(t('pages.layout.massTestMoveQuestion'))) {
                history.push({
                    pathname: '/bulk'
                });
            }
            // modal close
            handleClose();
        }
        if (!executeBulkLayoutLoading && executeBulkLayoutError) {
            toast.error(
                getErrorMsg(executeBulkLayoutError, t('common.alert.testFaile'))
            );
            dispatch(clearExecuteBulkUsingLayout());
        }
    }, [executeBulkLayoutData, executeBulkLayoutError]);

    return (
        <>
            <BulkUpdate
                modalShow={modalShow}
                handleClose={handleClose}
                initData={initData}
                setInitData={setInitData}
                serverData={serverData}
                codeData={codeData}
                getBulkSnapshotName={getBulkSnapshotName}
                onExecute={onExecute}
                onClickServerInfo={onClickServerInfo}
            />
            <Spinner
                isLoading={
                    executeBulkLayoutLoading || executeBulkHistoryLoading
                }
            />
        </>
    );
}

export default BulkUpdateContainer;
