/**
 * (TR)        (PAGING )
 * searchBulkDetail (SEARCH_BULK_DETAIL) : CALL
 * clearSearchBulkDetail (CLEAR_SEARCH_BULK_DETAIL) : CLEAR
 * setBulkDetailForm (SET_SEARCH_BULK_DETAIL_FORM) : CONDITION CHANGE
 */
export const searchBulkDetail = async ({
    bulkSnapshotId,
    serviceId,
    compareResult,
    page = 1,
    size = 10
}) => {
    const response = await axios.get(
        `/bulks?bulkSnapshotId=${bulkSnapshotId}&serviceId=${encodeURI(
            serviceId
        )}&compareResult=${compareResult}&sort=id,asc&page=${
            page - 1
        }&size=${size}`
    );
    return response;
};

/**
 * (TR)     
 * downloadExcelReport (DOWNLOAD_EXCEL_REPORT) : CALL
 */
export const downloadExcelReport = async ({bulkTestGroupId}) => {
    await axios({
        url: `/bulk-test/download/excel-report/${bulkTestGroupId}`,
        method: 'GET',
        responseType: 'blob' // important
    }).then((response) => {
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement('a');
        link.href = url;
        link.setAttribute('download', 'file.xlsx');
        document.body.appendChild(link);
        link.click();
    });
};

