/**
 * (TR)   (LOG DATA)
 * executeBulkUsingHistory (EXECUTE_BULK_USE_HISTORY) : CALL
 * clearExecuteBulkUsingHistory (CLEAR_EXECUTE_BULK_USE_HISTORY) : CLEAR
 */
export const executeBulkUsingHistory = async ({
    bulkSnapshotName,
    basicData,
    server1Id,
    server2Id,
    bulkOptions,
    historiesFindCondition,
    logsFindCondition
}) => {
    const response = await axios.post('/bulks/execute', {
        bulkSnapshotName,
        basicData,
        server1Id,
        server2Id,
        bulkOptions,
        historiesFindCondition,
        logsFindCondition
    });
    return response;
};

