/**
 * (TR)   (EXCEL)
 * validateBulkExcelFile (VALIDATE_BULK_EXCEL_FILE) : CALL
 * clearValidateBulkExcelFile (CLEAR_VALIDATE_BULK_EXCEL_FILE) : CLEAR
 */
export const validateBulkUsingExcel = async ({file}) => {
    const formData = new FormData();
    formData.append('file', file);
    const response = await axios.post('/bulk-test/validation-excel', formData, {
        headers: {
            'Content-Type': 'multipart/form-data'
        }
    });

    return response;
};

/**
 * (TR)   (EXCEL) -    
 * executeBulkUsingExcel (EXECUTE_BULK_USE_EXCEL) : CALL
 * clearExecuteBulkUsingExcel (CLEAR_EXECUTE_BULK_USE_EXCEL) : CLEAR
 */
export const executeBulkUsingExcel = async ({
    file,
    testName,
    predictTestCount,
    bulkTestExecutionTimeType,
    reserveDate,
    bulkTestType,
    server1Id,
    server2Id,
    testType,
    isCompareValue
}) => {
    const formData = new FormData();
    formData.append('file', file);
    formData.append('testName', testName);
    formData.append('predictTestCount', predictTestCount);
    formData.append('bulkTestExecutionTimeType', bulkTestExecutionTimeType);
    formData.append('reserveDate', reserveDate);
    formData.append('bulkTestType', bulkTestType);
    formData.append('server1Id', server1Id);
    formData.append('server2Id', server2Id);
    formData.append('testType', testType);
    formData.append('isCompareValue', isCompareValue);

    const response = await axios.post('/bulk-test/excel', formData, {
        headers: {
            'Content-Type': 'multipart/form-data'
        }
    });

    return response;
};
