import React from 'react';
import {useTranslation} from 'react-i18next';
import {Row, Col, Form, FloatingLabel} from 'react-bootstrap';
import Button from '../../components/button/Button';
import Card from '../../components/card/Card';
import {takeSelectOptionbyServer} from '../../lib/commonUiUtils';

// onSearch={onSearch}
// searchForm={searchLogsForm}
// setSearchForm={saveSearchForm}
// serverData={serverData}
// showExecuteModal={showExecuteModal}
const LogDetailSearch = ({
    onSearch,
    searchForm,
    setSearchForm,
    serverData,
    showExecuteModal
}) => {
    const [t] = useTranslation();

    const onChangerSearchForm = (e) => {
        e.preventDefault();
        const searchFormT = {...searchForm};
        searchFormT[e.target.id] = e.target.value;
        setSearchForm(searchFormT);
    };
    const onClickSearch = (e) => {
        e.preventDefault();
        onSearch();
    };

    const onCheckEnter = (e) => {
        if (e.key === 'Enter') {
            e.preventDefault();
            onSearch();
        }
    };

    return (
        <Card
            title={t('common.text.searchCardTitleName')}
            body={
                <>
                    <Row onKeyPress={onCheckEnter}>
                        <Col xs="3">
                            {/*   */}
                            <FloatingLabel
                                label={t('pages.layout.interfaceId')}
                                className="mb-2"
                            >
                                <Form.Control
                                    type="text"
                                    maxLength="50"
                                    id="serviceId"
                                    onChange={onChangerSearchForm}
                                    value={searchForm.serviceId ?? ''}
                                />
                            </FloatingLabel>
                        </Col>
                        <Col xs="7">
                            <Row>
                                <Col xs="3">
                                    <FloatingLabel
                                        label="From"
                                        className="mb-2"
                                    >
                                        <Form.Control
                                            type="datetime-local"
                                            id="fromDateTime"
                                            onChange={onChangerSearchForm}
                                            value={searchForm?.fromDateTime}
                                        />
                                    </FloatingLabel>
                                </Col>
                                <Col xs="3">
                                    <FloatingLabel label="To" className="mb-2">
                                        <Form.Control
                                            type="datetime-local"
                                            id="toDateTime"
                                            onChange={onChangerSearchForm}
                                            value={searchForm?.toDateTime}
                                        />
                                    </FloatingLabel>
                                </Col>
                            </Row>
                        </Col>
                        <Col xs="1">
                            <Form.Group>
                                <Form.Label />
                                <Button
                                    type="button"
                                    className="btn btn-block btn-primary btn-search"
                                    // isLoading={searchLoading}
                                    onClick={showExecuteModal}
                                >
                                    
                                </Button>
                            </Form.Group>
                        </Col>
                        <Col xs="1">
                            <Form.Group>
                                <Form.Label />
                                <Button
                                    type="button"
                                    className="btn btn-block btn-secondary btn-search"
                                    onClick={onClickSearch}
                                >
                                    {t('common.button.search')}
                                </Button>
                            </Form.Group>
                        </Col>
                    </Row>
                </>
            }
        />
    );
};

export default LogDetailSearch;
