import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import EnvironmentVariableManagement from '@pages/setting/EnvironmentVariableManagement';
import {toast} from 'react-toastify';
import {
    searchEnvironmentVariable,
    saveEnvironmentVariable,
    clearSaveEnvironmentVariable
} from '../../store/setting';
import Spinner from '../../components/spinner/Spinner';
import {getErrorMsg} from '../../lib/commonUiUtils';

function EnvironmentVariableManagementContainer() {
    const dispatch = useDispatch();

    const {loading: searchLoading, data: searchData} = useSelector(
        (state) => state.setting.searchEnvironmentVariable
    );

    const {
        loading: saveLoading,
        data: saveData,
        error: saveError
    } = useSelector((state) => state.setting.saveEnvironmentVariable);

    const onSearch = () => dispatch(searchEnvironmentVariable());

    const onSave = (environmentVariableId, environmentValue) =>
        dispatch(
            saveEnvironmentVariable(environmentVariableId, environmentValue)
        );

    useEffect(() => {
        onSearch();
        return () => {};
    }, []);

    useEffect(() => {
        /* Fail process */
        if (!saveLoading && saveError) {
            toast.error(getErrorMsg(saveError, 'save'));
            dispatch(clearSaveEnvironmentVariable());
        }

        /* Success process */
        if (!saveLoading && saveData) {
            toast.success('Success');
            dispatch(clearSaveEnvironmentVariable());
            onSearch();
        }
    }, [saveData, saveError]);

    return (
        <>
            <EnvironmentVariableManagement
                environmentVariableData={searchData}
                save={onSave}
            />
            <Spinner isLoading={searchLoading || saveLoading} />
        </>
    );
    ///
}

export default EnvironmentVariableManagementContainer;
