/* eslint-disable react-hooks/rules-of-hooks */
/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import UitestVariable from '@pages/uitest/UitestVariable';
// import UitestVariable from '@pages/uitest/popup/UitestSaveVariable';
import {useSelector, useDispatch} from 'react-redux';
import UitestSaveVariableContainer from './popup/UitestSaveVariableContainer';

// import {useLocation} from 'react-router-dom';
import {
    usedVariableSaveModal, // '' /  
    setDetailVariableList
} from '../../store/uitest';
import {setTempKey} from '../../store/common';

const UitestVariableContainer = () => {
    const dispatch = useDispatch();
    const {data: scenarioData} = useSelector(
        (state) => state.uitest.scenarioInfo
    );

    const {tempkey} = useSelector((state) => state.common);
    const setTempKeyRezen = () => {
        if (tempkey < -9999) {
            dispatch(setTempKey(-1));
        } else {
            dispatch(setTempKey(tempkey - 1));
        }
    };

    /*
     *    
     */
    const openNewVariable = (e) => {
        e.preventDefault();

        if (
            scenarioData.data.variableList &&
            scenarioData.data.variableList.length > 5
        ) {
            // eslint-disable-next-line no-alert
            alert('  5 .');
            return;
        }

        const initData = {
            type: 'create',
            id: tempkey,
            variableName: '',
            initialValue: '',
            variableDesc: ''
        };
        setTempKeyRezen();
        dispatch(usedVariableSaveModal(true, initData));

        //    5 
    };

    const openUpdateVariable = (record) => {
        const data = {...record};
        data.type = 'update';
        dispatch(usedVariableSaveModal(true, data));
    };

    const saveDetailVariableList = (variableList) => {
        dispatch(setDetailVariableList(variableList));
    };
    return (
        <>
            <UitestSaveVariableContainer />
            <UitestVariable
                scenarioData={scenarioData}
                openNewVariable={openNewVariable}
                openUpdateVariable={openUpdateVariable}
                saveDetailVariableList={saveDetailVariableList}
            />
        </>
    );
};
export default UitestVariableContainer;
