/* eslint-disable no-unused-vars */
import React from 'react';
import Modal from 'react-bootstrap/Modal';
import {Form, Row, Col, FloatingLabel} from 'react-bootstrap';
import {useFormik} from 'formik';
import * as Yup from 'yup';
import {useTranslation} from 'react-i18next';
import Button from '../../../components/button/Button';
import {
    printFormError,
    renderModalHeader,
    takeSelectOptionbyCode
} from '../../../lib/commonUiUtils';

const UitestSaveVariable = ({
    modalShow,
    handleShow,
    handleClose,
    scenarioData,
    selectedVariable,
    codeData,
    saveDetailVariableList
}) => {
    const [t] = useTranslation();

    const formik = useFormik({
        enableReinitialize: true,
        initialValues: {
            // id: selectedVariable && selectedVariable.id,
            variableType: selectedVariable?.variableType || '',
            variableName: selectedVariable?.variableName || '',
            initialValue: selectedVariable?.initialValue || ''
            // type: '1234'
        },
        validationSchema: Yup.object({
            // caseName: Yup.string().required(
            //     getInputValidMsg(t('pages.scenario.caseName'))
            // )
        }),
        onSubmit: (values) => {
            const valuesT = {...values};
            // const tempVariable = codeData.data.filter(
            //     (obj) =>
            //         obj.codeGroup === 'variable_cd' &&
            //         obj.codeKey === valuesT.variableCodeKey
            // )[0].codeName;
            // valuesT.variableCodeName = tempVariable;
            valuesT.variableId = selectedVariable.variableId;

            //   
            if (selectedVariable.type === 'create') {
                let variables = [];
                if (scenarioData.data.variables) {
                    variables = [...scenarioData.data.variables];
                }

                variables.push(valuesT);
                saveDetailVariableList(variables);
            } else {
                const variables = [...scenarioData.data?.variables];

                const variableLoop = variables.map((unit, index) => {
                    const theVariable = {...unit};
                    if (theVariable.variableId === valuesT.variableId) {
                        return valuesT;
                    }
                    return theVariable;
                });

                saveDetailVariableList(variableLoop);
            }
            handleClose();
        }
    });

    const renderTitle = () => {
        if (!selectedVariable) return '';
        if (selectedVariable.type === 'create') {
            return t('pages.scenario.registerVariable');
        }
        return t('pages.scenario.updateVariable');
    };

    const renderInitialValue = () => {
        if (!selectedVariable) return '';

        if (formik.values.variableCodeKey === 'String') {
            return (
                <>
                    <Form.Control
                        type="text"
                        placeholder=""
                        {...formik.getFieldProps('initialValue')}
                        maxLength="30"
                    />
                </>
            );
        }
        return (
            <>
                <Form.Control
                    type="text"
                    placeholder=""
                    {...formik.getFieldProps('initialValue')}
                    maxLength="30"
                />
            </>
        );
    };

    return (
        <>
            <Modal
                show={modalShow}
                onHide={handleClose}
                onExited={() => formik.resetForm()}
                dialogClassName="modal-25w"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Form onSubmit={formik.handleSubmit}>
                    {renderModalHeader(renderTitle())}

                    <Modal.Body>
                        <Row>
                            <Col xs="4">
                                <FloatingLabel
                                    label=" "
                                    className="mb-2"
                                >
                                    <Form.Select
                                        as="select"
                                        // onChange={handleInputChange}
                                        {...formik.getFieldProps(
                                            'variableType'
                                        )}
                                    >
                                        {takeSelectOptionbyCode(
                                            codeData,
                                            'VariableType'
                                        )}
                                    </Form.Select>
                                </FloatingLabel>
                            </Col>
                            <Col xs="8">
                                <FloatingLabel label=" " className="mb-2">
                                    <Form.Control
                                        type="text"
                                        placeholder=""
                                        {...formik.getFieldProps(
                                            'variableName'
                                        )}
                                        maxLength="50"
                                    />
                                    {printFormError(formik, 'variableName')}
                                </FloatingLabel>
                            </Col>
                            <Col xs="8">
                                <FloatingLabel
                                    label=" (Optional)"
                                    className="mb-2"
                                >
                                    {renderInitialValue()}
                                </FloatingLabel>
                            </Col>
                        </Row>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button
                            theme="primary"
                            type="submit"
                            // isLoading={createLoading}
                        >
                            {t('common.button.register')}
                        </Button>
                        <Button theme="default" onClick={handleClose}>
                            {t('common.button.close')}
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>
        </>
    );
};

export default UitestSaveVariable;
