/* eslint-disable jsx-a11y/label-has-associated-control */
import React from 'react';
import {useTranslation} from 'react-i18next';
import {Row, Col, Form, FloatingLabel} from 'react-bootstrap';
import {useHistory} from 'react-router-dom';
import Card from '../../components/card/Card';
import Button from '../../components/button/Button';

const ScenarioSearch = ({
    onSearch,
    searchForm,
    saveSearchForm,
    memberInfoData
}) => {
    const [t] = useTranslation();
    const history = useHistory();
    const onChangerEvent = (e) => {
        e.preventDefault();
        const searchFormT = {...searchForm};
        searchFormT[e.target.id] = e.target.value;
        saveSearchForm(searchFormT);
    };

    const onClickSearch = (e) => {
        e.preventDefault();
        onSearch();
    };

    const onCheckEnter = (e) => {
        if (e.key === 'Enter') {
            e.preventDefault();
            onSearch();
        }
    };

    //    (    )
    const handleShow = () => {
        history.push('/uitest');
    };

    /*
     *  
     */
    return (
        <Card
            title={t('common.text.searchCardTitleName')}
            body={
                <>
                    <Row onKeyPress={onCheckEnter}>
                        <Col xs="6">
                            <Row>
                                <Col xs="6">
                                    <FloatingLabel
                                        label={t('pages.scenario.scenarioName')}
                                        className="mb-2"
                                    >
                                        <Form.Control
                                            type="text"
                                            maxLength="20"
                                            id="scenarioName"
                                            onChange={onChangerEvent}
                                            value={searchForm?.scenarioName}
                                        />
                                    </FloatingLabel>
                                </Col>
                                <Col xs="3">
                                    <FloatingLabel
                                        label={t('pages.scenario.manager')}
                                        className="mb-2"
                                    >
                                        <Form.Select
                                            id="manager"
                                            onChange={onChangerEvent}
                                            value={searchForm?.manager}
                                        >
                                            <option value="">
                                                {t('common.text.optionAll')}
                                            </option>
                                            <option
                                                value={
                                                    memberInfoData?.data?.userId
                                                }
                                            >
                                                {memberInfoData?.data?.name}
                                            </option>
                                        </Form.Select>
                                    </FloatingLabel>
                                </Col>
                            </Row>
                        </Col>
                        <Col xs="4" />
                        <Col xs="1">
                            <div className="form-group">
                                <label htmlFor="myinput" />
                                <Button
                                    type="button"
                                    className="btn btn-block btn-secondary btn-search"
                                    onClick={onClickSearch}
                                >
                                    {t('common.button.search')}
                                </Button>
                            </div>
                        </Col>
                        <Col xs="1">
                            <Form.Group>
                                <Form.Label />
                                <Button
                                    type="button"
                                    className="btn btn-block btn-success btn-search"
                                    onClick={handleShow}
                                >
                                    + 
                                </Button>
                            </Form.Group>
                        </Col>
                    </Row>
                </>
            }
        />
    );
};

export default ScenarioSearch;
