import axios from 'axios';

/**
 * Server List Paging 
 * searchServer (SEARCH_SERVER) : 
 * clearSearchServer (CLEAR_SEARCH_SERVER) : 
 * setSearchServerName (SET_SEARCH_SERVER_NAME) :  
 * setSearchServerType (SET_SEARCH_SERVER_TYPE) :  
 */
export const searchServer = async ({
    serverName,
    serverType,
    page = 1,
    size = 10
}) => {
    const response = await axios.get(
        `/servers?sort=id,asc&serverName=${serverName}&serverType=${serverType}&page=${
            page - 1
        }&size=${size}`
    );
    return response;
};

export const findServer = async ({serverId}) => {
    const response = await axios.get(`/servers/${serverId}`, null);
    return response;
};

