/**
 *  Server 
 * createServer (CREATE_SERVER) : 
 * createServerDataClear (CREATE_SERVER_DATA_CLEAR) : 
 */
export const createServer = async ({
    serverName,
    url,
    serverType,
    dataFormat,
    loginProcess,
    dbms,
    connectionInfo,
    ipPort,
    referenceServerId
}) => {
    const response = await axios.post('/servers', {
        serverName,
        url,
        serverType,
        dataFormat,
        loginProcess,
        dbms,
        connectionInfo,
        ipPort,
        referenceServerId
    });
    return response;
};

/**
 * Server  
 * updateServer (UPDATE_SERVER) : 
 * updateServerDataClear (UPDATE_SERVER_DATA_CLEAR) : 
 */
export const updateServer = async ({
    id,
    serverName,
    url,
    serverType,
    dataFormat,
    loginProcess,
    dbms,
    connectionInfo,
    ipPort,
    referenceServerId
}) => {
    const response = await axios.patch(`/servers/${id}`, {
        serverName,
        url,
        serverType,
        dataFormat,
        loginProcess,
        dbms,
        connectionInfo,
        ipPort,
        referenceServerId
    });
    return response;
};

/**
 * Server  
 * deleteServer (DELETE_SERVER) : 
 * deleteServerDataClear (DELETE_SERVER_DATA_CLEAR) : 
 */
export const deleteServer = async ({serverId}) => {
    const response = await axios.delete(`/servers/${serverId}`, null);
    return response;
};
