import React, {useState} from 'react';

import {Form, Row, Col, FloatingLabel} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';

// import Form from 'react-bootstrap/Form';
// import Media from 'react-bootstrap/Media';
import Alert from 'react-bootstrap/Alert';
import Card from '../../components/card/Card';

// scenarioData={scenarioData} // detail scenario data
// selectedCaseId={selectedCaseId} // list selected case id
const UitestCaseInfo = ({
    scenarioData,
    selectedCaseId,
    setDetailCaseName,
    setDetailCaseDesc
}) => {
    // const [key, setKey] = useState('caseInfo');
    const [t] = useTranslation();
    let caseName = '';
    let caseDesc = '';
    if (scenarioData && selectedCaseId !== '' && selectedCaseId !== undefined) {
        const theCase = scenarioData.data?.cases?.filter(
            (info) => info.caseId === selectedCaseId
        );

        if (theCase != null) {
            caseName = theCase[0].caseName;
            caseDesc = theCase[0].caseDesc;
            if (theCase[0].caseDesc === null) {
                caseDesc = '';
            }
        }
    }

    const onChangeCaseName = (e) => {
        e.preventDefault();
        setDetailCaseName(e.target.value);
    };
    const onChangeCaseDesc = (e) => {
        e.preventDefault();
        setDetailCaseDesc(e.target.value);
    };
    return (
        <>
            <Row>
                <Col xs="8">
                    <FloatingLabel
                        label={t('pages.scenario.caseName')}
                        className="mb-2"
                    >
                        <Form.Control
                            type="text"
                            placeholder=""
                            value={caseName}
                            onChange={onChangeCaseName}
                            maxLength="20"
                        />
                    </FloatingLabel>
                </Col>
                <Col xs="4" />
                <Col xs="12">
                    <FloatingLabel
                        label={t('pages.scenario.caseDesc')}
                        className="mb-2"
                    >
                        <Form.Control
                            as="textarea"
                            placeholder="Leave a comment here"
                            style={{height: '300px'}}
                            value={caseDesc}
                            onChange={onChangeCaseDesc}
                        />
                    </FloatingLabel>
                </Col>
            </Row>

            <br />
            {/* <InputGroup className="mb-3">
                <InputGroup.Prepend>
                    <InputGroup.Text style={{width: '150px'}}>
                        Case Name
                    </InputGroup.Text>
                </InputGroup.Prepend>
                <FormControl
                    id="basic-url"
                    aria-describedby="basic-addon3"
                    value={caseName}
                    onChange={onChangeCaseName}
                />
            </InputGroup>
            <InputGroup className="mb-3">
                <InputGroup.Prepend>
                    <InputGroup.Text style={{width: '150px'}}>
                        Case Desc
                    </InputGroup.Text>
                </InputGroup.Prepend>
                <FormControl
                    as="textarea"
                    aria-label="With textarea"
                    value={caseDesc}
                    style={{height: '300px'}}
                    onChange={onChangeCaseDesc}
                />
            </InputGroup> */}
            {/* <Alert key="1" variant="success">
                2021-06-25 : last test sucess
            </Alert> */}
        </>
    );
};

export default UitestCaseInfo;
