import React from 'react';
import {Row, Col, Form, FloatingLabel} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
import Button from '../../components/button/Button';
import Card from '../../components/card/Card';
import {renderOptionsByCode} from '../../lib/codeUtils';

const ServerSearch = ({
    searchLoading,
    onSearch,
    searchForm,
    saveSearchServerForm,
    onClickNewServer
}) => {
    const [t] = useTranslation();

    const onChangerData = (e) => {
        e.preventDefault();
        const searchFormT = {...searchForm};
        searchFormT[e.target.id] = e.target.value;
        saveSearchServerForm(searchFormT);
    };

    const onClickSearch = (e) => {
        e.preventDefault();
        onSearch();
    };

    const onCheckEnter = (e) => {
        if (e.key === 'Enter') {
            e.preventDefault();
            onSearch();
        }
    };

    return (
        <Card
            title={t('common.text.searchCardTitleName')}
            body={
                <>
                    <Row onKeyPress={onCheckEnter}>
                        <Col xs="6">
                            <Row>
                                <Col xs="4">
                                    <FloatingLabel
                                        label={t('pages.server.serverType')}
                                        className="mb-3"
                                    >
                                        <Form.Select
                                            as="select"
                                            id="serverType"
                                            onChange={onChangerData}
                                            value={searchForm?.serverType ?? ''}
                                        >
                                            <option value="">
                                                {t('common.text.optionAll')}
                                            </option>
                                            {renderOptionsByCode('ServerType')}
                                        </Form.Select>
                                    </FloatingLabel>
                                </Col>
                                <Col xs="8">
                                    <FloatingLabel
                                        label={t('pages.server.serverName')}
                                        className="mb-3"
                                    >
                                        <Form.Control
                                            type="text"
                                            aria-describedby="passwordHelpBlock"
                                            maxLength="20"
                                            id="serverName"
                                            onChange={onChangerData}
                                            value={searchForm?.serverName ?? ''}
                                        />
                                    </FloatingLabel>
                                </Col>
                            </Row>
                        </Col>
                        <Col xs="4" />
                        <Col xs="1">
                            <Form.Group>
                                <Form.Label />
                                <Button
                                    type="button"
                                    className="btn btn-block btn-secondary btn-search"
                                    isLoading={searchLoading}
                                    onClick={onClickSearch}
                                >
                                    {t('common.button.search')}
                                </Button>
                            </Form.Group>
                        </Col>
                        <Col xs="1">
                            <Form.Group>
                                <Form.Label />
                                <Button
                                    type="button"
                                    className="btn btn-block btn-success btn-search"
                                    isLoading={searchLoading}
                                    onClick={onClickNewServer}
                                >
                                    + 
                                </Button>
                            </Form.Group>
                        </Col>
                    </Row>
                </>
            }
        />
    );
};

export default ServerSearch;
