/* eslint-disable no-unused-vars */
import React, {useState, useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import ScenarioLock from '@pages/scenario/popup/ScenarioLock';
import {useTranslation} from 'react-i18next';
import {toast} from 'react-toastify';
import {
    clearScenarioLockModal,
    saveScenarioLock,
    clearScenarioLock,
    searchScenario
} from '../../../store/scenario';
import {searchScenarioDetail, setSelectedCaseId} from '../../../store/uitest';
import {getErrorMsg} from '../../../lib/commonUiUtils';

/*
 * 2022-03-17 kimtaehan 
 *  / 
 */
function ScenarioLockContainer() {
    const dispatch = useDispatch();
    const [t] = useTranslation();

    const {scenarioLockInitData} = useSelector((state) => state.scenario);

    // popup show/hide
    const [modalShow, setModalShow] = React.useState(false);

    const handleClose = () => {
        setModalShow(false);
    };
    const handleShow = () => setModalShow(true);

    const [openData, setOpenData] = useState({
        scenarioId: -1,
        lock: false,
        parent: ''
    });

    //    
    const {data: memberInfoData} = useSelector(
        (state) => state.auth.memberInfo
    );

    const onclickLock = (lockPassword) => {
        dispatch(
            saveScenarioLock({
                scenarioId: openData.scenarioId,
                lock: openData.lock,
                lockPassword
            })
        );
    };

    const {
        loading: updateLoading,
        data: updateResponse,
        error: updateError
    } = useSelector((state) => state.scenario.saveScenrioLockRes);

    /*
     *  Open event
     */
    useEffect(() => {
        if (scenarioLockInitData.showModal && !modalShow) {
            setOpenData({...scenarioLockInitData.initData});
            dispatch(clearScenarioLockModal());
            handleShow();
        }
    }, [scenarioLockInitData.showModal]);

    /**
     * lock/unlock server callback
     */

    const {searchForm} = useSelector((state) => state.scenario);
    const {caseId} = useSelector((state) => state.uitest.selectedCaseId);
    useEffect(() => {
        if (!updateLoading && updateResponse) {
            dispatch(clearScenarioLock());
            // dispatch(searchServer(serverName, serverType));

            // // 20220109 kimtaehan   
            toast.success(t('common.alert.updateSuccess'));

            if (openData.parent === 'scenario') {
                dispatch(searchScenario(searchForm));
            } else if (openData.parent === 'uitest') {
                // 2022-03-10   Case iD 
                if (caseId < 0) {
                    dispatch(setSelectedCaseId(''));
                }
                dispatch(searchScenarioDetail(openData.scenarioId));
            }
            handleClose();
        }

        // fail
        if (!updateLoading && updateError) {
            dispatch(clearScenarioLock());
            toast.error(getErrorMsg(updateError, 'save'));
        }
    }, [updateResponse, updateError]);

    return (
        <>
            <ScenarioLock
                modalShow={modalShow}
                handleClose={handleClose}
                openData={openData}
                onclickLock={onclickLock}
                updateLoading={updateLoading}
            />
        </>
    );
}

export default ScenarioLockContainer;
