import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import BulkTable from '@app/pages/bulk/BulkTable';
import {useTranslation} from 'react-i18next';
import {toast} from 'react-toastify';
import {useHistory} from 'react-router-dom';
import {
    searchBulkList, // bulk List select
    setBulkListForm,
    cancelBulktest, //  
    clearCancelBulktest,
    usedBulkStateModal,
    deleteBulkTestGroup,
    cleardeleteBulkTestGroup
} from '../../store/bulk';
import Spinner from '../../components/spinner/Spinner';
import {getErrorMsg} from '../../lib/commonUiUtils';

/*
 * BulkTableContainer
 *   ->  Table
 */
function BulkTableContainer() {
    const dispatch = useDispatch();
    const history = useHistory();
    const [t] = useTranslation();
    //   
    const {data: codeData} = useSelector((state) => state.common.searchCode);

    const {data: searchData, loading: searchLoading} = useSelector(
        (state) => state.bulk.searchBulkListRes
    );

    const {
        data: cancelData,
        loading: cancelLoading,
        error: cancelError
    } = useSelector((state) => state.bulk.cancelBulktestRes);

    const {bulkListSearchForm} = useSelector((state) => state.bulk);

    const movePage = (page) => {
        const bulkListSearchFormT = {...bulkListSearchForm};
        bulkListSearchFormT.page = page;
        dispatch(setBulkListForm(bulkListSearchFormT));
        dispatch(searchBulkList(bulkListSearchFormT));
    };

    const selectBulkTestGroup = (selectedBulkTest) => {
        history.push({
            pathname: '/bulk/detail',
            state: selectedBulkTest
        });
    };

    const onCancelBulktestGroup = (bulktestGroupId) => {
        dispatch(cancelBulktest(bulktestGroupId));
    };

    const onClickDelete = (bulktestGroupId) => {
        // eslint-disable-next-line no-alert
        if (window.confirm(t('common.alert.checkDelete'))) {
            dispatch(deleteBulkTestGroup(bulktestGroupId));
        }
    };

    /*
     *  /        .
     */
    const showStatePopup = (id) => {
        dispatch(usedBulkStateModal(id));
    };

    const {
        data: deleteData,
        loading: deleteLoading,
        error: deleteError
    } = useSelector((state) => state.bulk.deleteBulkTestRes);

    useEffect(() => {
        if (!deleteLoading && deleteError) {
            dispatch(cleardeleteBulkTestGroup());
            toast.error(getErrorMsg(deleteError, 'save'));
            dispatch(searchBulkList({...bulkListSearchForm}));
        }

        if (!deleteLoading && deleteData) {
            dispatch(cleardeleteBulkTestGroup());
            dispatch(searchBulkList({...bulkListSearchForm}));
        }
    }, [deleteData, deleteError]);

    useEffect(() => {
        if (!cancelLoading && cancelError) {
            dispatch(clearCancelBulktest());
            toast.error(getErrorMsg(cancelError, 'save'));
            dispatch(searchBulkList({...bulkListSearchForm}));
        }

        if (!cancelLoading && cancelData) {
            dispatch(clearCancelBulktest());
            dispatch(searchBulkList({...bulkListSearchForm}));
        }
    }, [cancelData, cancelError]);

    return (
        <>
            <BulkTable
                movePage={movePage}
                data={searchData}
                selectBulkTestGroup={selectBulkTestGroup}
                cancelBulktestGroup={onCancelBulktestGroup}
                onClickDelete={onClickDelete}
                showStatePopup={showStatePopup}
                codeData={codeData}
            />
            <Spinner
                isLoading={searchLoading || cancelLoading || deleteLoading}
            />
        </>
    );
    ///
}

export default BulkTableContainer;
