const SEARCH_BULK_COMPARE = `${PREFIX}/SEARCH_BULK_COMPARE`;
const SEARCH_BULK_COMPARE_SUCCESS = `${PREFIX}/SEARCH_BULK_COMPARE_SUCCESS`;
const SEARCH_BULK_COMPARE_ERROR = `${PREFIX}/SEARCH_BULK_COMPARE_ERROR`;
