/* eslint-disable no-unused-vars */
import React from 'react';
import {useTranslation} from 'react-i18next';
import {FaSearch} from 'react-icons/fa';
import Table from '../../components/table/Table';
import Button from '../../components/button/Button';
import {renderServerType} from '../../lib/buttonRender';

const StatTab2Table = ({searchT2Data, movePage, showStatDetailModal}) => {
    const [t] = useTranslation();

    const columns = [
        {
            title: t('pages.server.serverType'),
            width: '8%',
            key: 'serverType',
            render: (id, row) => {
                return renderServerType(row?.serverType);
            }
        },
        {
            title: '',
            align: 'left', // left, center, right (default:center)
            key: 'serverName',
            width: '15%'
        },
        {
            title: t('pages.stat.allCnt'),
            key: 'total',
            render: (id, row, column) => {
                return `${row?.totalCount}${t('common.text.cases')} `;
            }
        },
        {
            title: t('pages.stat.successCnt'),
            key: 'successCnt',
            render: (id, row, column) => {
                return `${row.successCount}${t('common.text.cases')} `;
            }
        },
        {
            title: t('pages.stat.failCnt'),
            key: 'failCnt',
            render: (id, row, column) => {
                return `${row.failCount}${t('common.text.cases')} `;
            }
        },
        {
            title: t('pages.stat.failRate'),
            key: 'failRate',
            render: (id, row, column) => {
                return `${row.failRate.toFixed(1)}% `;
            }
        },
        {
            title: t('pages.stat.processMillisecond'),
            key: 'processMillisecond',
            render: (id, row, column) => {
                return `${row.totalTimeAvgMs.toFixed(5)} ${t(
                    'common.text.second'
                )}`;
            }
        },
        {
            title: '',
            key: 'button',
            align: 'right',
            render: (id, row, column) => {
                return (
                    <>
                        <Button
                            theme="outline-success"
                            onClick={(e) => showStatDetailModal(id, row)}
                        >
                            <FaSearch />
                        </Button>
                    </>
                );
            }
        }
    ];

    return (
        <>
            <Table
                tableName={t('pages.stat.statList')}
                onDoubleClick={(id, row) => showStatDetailModal(id, row)}
                movePage={movePage}
                rowKey="serverId"
                columns={columns}
                data={searchT2Data}
            />
        </>
    );
};

export default StatTab2Table;
