/* eslint-disable no-unused-vars */
import React from 'react';
import {FaCheck} from 'react-icons/fa';
import {Modal, Form, Row, Col, FloatingLabel, Accordion} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
import Table from '../../components/table/Table';
import Button from '../../components/button/Button';
import {jsonStrFormatPretty} from '../../lib/jsonUtils';
import {renderModalHeader} from '../../lib/commonUiUtils';

const MemberSelect = ({
    modalShow,
    handleClose,
    searchData,
    managerId,
    onDoubleClick
}) => {
    const [t] = useTranslation();
    const tableName = t('pages.member.userList');
    const rowKey = 'userId';

    const columns = [
        {
            title: '',
            width: '4%',
            render: (id, row) => {
                return <>{row.userId === managerId && <FaCheck />}</>;
            }
        },
        {
            title: '',
            key: 'role',
            width: '5%',
            render: (id, row) => {
                if (row.role === 'ADMIN') {
                    return (
                        <Button
                            theme="primary"
                            style={{
                                minWidth: '80px',
                                maxHeight: 'calc(3.5rem + 2px)'
                            }}
                        >
                            Admin
                        </Button>
                    );
                }
                return (
                    <Button
                        theme="warning"
                        style={{
                            minWidth: '80px',
                            maxHeight: 'calc(3.5rem + 2px)'
                        }}
                    >
                        User
                    </Button>
                );
            }
        },
        {
            title: t('common.text.id'),
            key: 'userId',
            width: '20%'
            // align: 'left' // left, center, right (default:center)
        },
        {
            title: t('pages.member.userName'),
            key: 'name',
            width: '20%'
            // align: 'left' // left, center, right (default:center)
        },
        {
            title: 'Mobile',
            key: 'mobile',
            width: '20%',
            render: (id, row) => {
                return row.contactInfo?.mobile;
            }
            // align: 'left' // left, center, right (default:center)
        },
        {
            title: 'Email',
            key: 'email',
            width: '20%',
            render: (id, row) => {
                return row.contactInfo?.email;
            }
        }
    ];

    return (
        <>
            <Modal
                show={modalShow}
                onHide={handleClose}
                size="xl"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                {renderModalHeader(t('common.text.responseDiff'))}
                <Modal.Body>
                    <Accordion defaultActiveKey="0">
                        <Accordion.Item eventKey="0">
                            <Accordion.Header className="accordion-header">
                                  
                            </Accordion.Header>
                            <Accordion.Body>
                                <Row>
                                    <Col xs="12">
                                        <Table
                                            tableName={tableName}
                                            isCard="N"
                                            onDoubleClick={onDoubleClick}
                                            // movePage={movePage}
                                            rowKey={rowKey}
                                            columns={columns}
                                            data={searchData}
                                        />
                                    </Col>
                                </Row>
                            </Accordion.Body>
                        </Accordion.Item>
                    </Accordion>
                </Modal.Body>
                <Modal.Footer>
                    <Button theme="default" onClick={handleClose}>
                        {t('common.button.close')}
                    </Button>
                </Modal.Footer>
            </Modal>
        </>
    );
};
export default MemberSelect;
