import React from 'react';
import {Modal, Form, Row, Col, FormControl, FloatingLabel} from 'react-bootstrap';
import {useFormik,} from 'formik'; 
import * as Yup from 'yup';
import {useTranslation} from 'react-i18next';
import Button from '../../../components/button/Button';
import {printFormError, getInputValidMsg, renderModalHeader, takeSelectOptionbyCode, takeSelectOptionbyServer} from '../../../lib/commonUiUtils';

const LogAnalysisModal = ({modalShow, handleClose, codeData, serverData, memberInfoData, onExecuteLogColletor}) => {
    
    const [t] = useTranslation();
    const formik = useFormik({
        enableReinitialize: true,
        initialValues: {
            referenceServerId:'',
            collectionMethod:'FILE',
            directoryPath:''
        },
        validationSchema: Yup.object({

            referenceServerId: Yup.string().required(
                getInputValidMsg("")
            ) 
        }),
        onSubmit: (values) => { 
            onExecuteLogColletor(values);
            
        }
    });
    return (
        <>
            <Modal
                show={modalShow}
                onHide={handleClose}
                // size="lg"
                onExited={() => formik.resetForm()}
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Form onSubmit={formik.handleSubmit}>
                    {renderModalHeader(t('pages.setting.logCollectorExecute'))}
                  
                    <Modal.Body>
                        <Row>
                            <Col xs="6">
                                <FloatingLabel
                                    label=" "
                                    className="mb-2"
                                >
                                    <Form.Select
                                        {...formik.getFieldProps(
                                            'collectionMethod'
                                        )}
                                    >
                                        {/* takeSelectOptionbyCode */}
                                        {takeSelectOptionbyCode(
                                            codeData,
                                            'CollectionMethod'
                                        )}
                                    </Form.Select>
                                </FloatingLabel>
                            </Col>
                            <Col xs="6">
                                <FloatingLabel
                                    label={t('pages.setting.targetServer')}
                                    className="mb-2"
                                >
                                    <Form.Select
                                        {...formik.getFieldProps(
                                            'referenceServerId'
                                        )}
                                    >
                                        <option value="">
                                            {t('common.text.optionNone')}
                                        </option>
                                        {serverData?.data
                                            ?.filter(
                                                (obj) =>
                                                    obj.serverType === 'HTTP' || obj.serverType === 'TCP_IP'
                                            )
                                            .map((obj) => (
                                                <option key={obj.serverId} value={obj.serverId}>
                                                    {obj.serverName}
                                                </option>
                                            ))}
                                    </Form.Select>
                                    {printFormError(formik, 'referenceServerId')}
                                </FloatingLabel>
                            </Col>
                            <Col xs="12">
                                <FloatingLabel
                                    label="  (/cgapp/atworks/   )"
                                    className="mb-2"
                                >
                                    <Form.Control
                                        type="text"  
                                        {...formik.getFieldProps('directoryPath')}
                                        maxLength="100"
                                    />
                                </FloatingLabel>
                            </Col>
                        </Row>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button
                            theme="primary"
                            type="submit"
                            //  
                            disabled={memberInfoData?.data?.role !== 'ADMIN'}
                        >
                            {t('common.button.register')}
                        </Button>
                        <Button theme="default" onClick={handleClose}>
                            {t('common.button.close')}
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>
        </>
    );
};

export default LogAnalysisModal;
