import React from 'react';
import {useSelector, useDispatch} from 'react-redux';
import LayoutDetailIoTable from '@pages/layout/LayoutDetailIoTable';
import {setSelectedLayout, usedRandomUpdateModal} from '../../store/layout';

/*
 *    -> Container
 */
function LayoutDetailIoTableContainer({ioType}) {
    const dispatch = useDispatch();
    const {selectedLayout} = useSelector((state) => state.layout);

    const [tempId, setTempId] = React.useState(-1);

    const onChangeColumn = (changeSelectedLayout) => {
        dispatch(setSelectedLayout(changeSelectedLayout));
    };

    //    
    const onClickUpdateRand = (row) => {
        dispatch(usedRandomUpdateModal(true, row));
    };

    //   
    const {data: codeData} = useSelector((state) => state.common.searchCode);

    if (!selectedLayout) {
        return <></>;
    }

    return (
        <>
            <LayoutDetailIoTable
                selectedLayout={selectedLayout}
                ioType={ioType}
                onChangeColumn={onChangeColumn}
                tempId={tempId}
                setTempId={setTempId}
                onClickUpdateRand={onClickUpdateRand}
                codeData={codeData}
            />
        </>
    );
}

export default LayoutDetailIoTableContainer;
