import React, {useEffect} from 'react';
import {useDispatch, useSelector} from 'react-redux';
import {useLocation, useHistory} from 'react-router-dom';
import LayoutDetail from '@pages/layout/LayoutDetail';
import {toast} from 'react-toastify';
import {
    searchLayoutDetail,
    setSelectedLayout,
    clearsearchLayoutDetail
} from '../../store/layout';
import Spinner from '../../components/spinner/Spinner';
import {getErrorMsg} from '../../lib/commonUiUtils';

/*
 *   Container
 */
function LayoutDetailContainer() {
    const dispatch = useDispatch();
    const history = useHistory();
    const location = useLocation();

    const {
        data: searchData,
        loading: searchLoading,
        error: searchError
    } = useSelector((state) => state.layout.layoutInfo);

    useEffect(() => {
        if (location.state === null || location.state.id === null) {
            history.push('/layout');
        } else {
            dispatch(searchLayoutDetail(location.state.id));
        }
    }, []);

    useEffect(() => {
        if (!searchLoading && searchData) {
            dispatch(setSelectedLayout(searchData.data));
            dispatch(clearsearchLayoutDetail());
        }

        if (!searchLoading && searchError) {
            dispatch(clearsearchLayoutDetail());
            toast.error(getErrorMsg(searchError, 'search'));
        }
    }, [searchData, searchError]);

    return (
        <>
            <LayoutDetail />
            <Spinner isLoading={searchLoading} />
        </>
    );
    ///
}

export default LayoutDetailContainer;
