/* eslint-disable no-unused-vars */
import React, {useState} from 'react';
import {useTranslation} from 'react-i18next';
import {Row, Col, Form, FloatingLabel} from 'react-bootstrap';
import Card from '../../components/card/Card';
import Button from '../../components/button/Button';

const StatTab2Search = ({searchFormT2, saveSearchFormT2, onSearch}) => {
    const [t] = useTranslation();

    /*
     * '' button evnet
     */
    const onClickSearch = (e) => {
        // serverId, fromDate, toDate
        e.preventDefault();
        onSearch();
    };

    const onCheckEnter = (e) => {
        if (e.key === 'Enter') {
            e.preventDefault();
            onSearch();
        }
    };

    /*
     * form data change event
     */
    const onChangerSearchFormT2 = (e) => {
        const searchFormT2Temp = {...searchFormT2};
        searchFormT2Temp[e.target.id] = e.target.value;
        saveSearchFormT2(searchFormT2Temp);
    };

    return (
        <>
            <Card
                title={t('common.text.searchCardTitleName')}
                body={
                    <>
                        <Row onKeyPress={onCheckEnter}>
                            <Col xs="6">
                                <Row>
                                    <Col xs="4">
                                        <FloatingLabel
                                            label="From"
                                            className="mb-2"
                                        >
                                            <Form.Control
                                                type="date"
                                                id="fromDate"
                                                onChange={onChangerSearchFormT2}
                                                value={
                                                    searchFormT2 &&
                                                    searchFormT2.fromDate
                                                }
                                            />
                                        </FloatingLabel>
                                    </Col>
                                    <Col xs="4">
                                        <FloatingLabel
                                            label="To"
                                            className="mb-2"
                                        >
                                            <Form.Control
                                                type="date"
                                                id="toDate"
                                                onChange={onChangerSearchFormT2}
                                                value={
                                                    searchFormT2 &&
                                                    searchFormT2.toDate
                                                }
                                            />
                                        </FloatingLabel>
                                    </Col>
                                </Row>
                            </Col>
                            <Col xs="5" />
                            <Col xs="1">
                                <Form.Group>
                                    <Form.Label />
                                    <Button
                                        type="button"
                                        className="btn btn-block btn-secondary btn-search"
                                        // isLoading={searchLoading}
                                        onClick={onClickSearch}
                                    >
                                        {t('common.button.search')}
                                    </Button>
                                </Form.Group>
                            </Col>
                        </Row>
                    </>
                }
            />
        </>
    );
};

export default StatTab2Search;
