import React from 'react';
import {
    Modal,
    Form,
    Row,
    Col,
    FormControl,
    FloatingLabel,
    Accordion
} from 'react-bootstrap';
import {useFormik} from 'formik';
import * as Yup from 'yup';
import {useTranslation} from 'react-i18next';
import Button from '../../../components/button/Button';
import {
    printFormError,
    getInputValidMsg,
    renderModalHeader
} from '../../../lib/commonUiUtils';

const SourceAnalysisModal = ({modalShow, handleClose, onAnalysisSource}) => {
    const [t] = useTranslation();
    const formik = useFormik({
        enableReinitialize: true,
        initialValues: {
            directoryPath: ''
        },
        validationSchema: Yup.object({
            directoryPath: Yup.string().required(getInputValidMsg(''))
        }),
        onSubmit: (values) => {
            onAnalysisSource(values);
        }
    });
    return (
        <>
            <Modal
                show={modalShow}
                onHide={handleClose}
                // size="lg"
                onExited={() => formik.resetForm()}
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Form onSubmit={formik.handleSubmit}>
                    {renderModalHeader()}
                    <Modal.Body>
                        <Accordion defaultActiveKey="0">
                            <Accordion.Item eventKey="0">
                                <Accordion.Header>
                                    Source  
                                </Accordion.Header>
                                <Accordion.Body>
                                    <Row>
                                        <Col xs="12">
                                            <FloatingLabel
                                                label=" "
                                                className="mb-1"
                                            >
                                                <FormControl
                                                    {...formik.getFieldProps(
                                                        'directoryPath'
                                                    )}
                                                    maxLength="100"
                                                />
                                                {printFormError(
                                                    formik,
                                                    'directoryPath'
                                                )}
                                            </FloatingLabel>
                                        </Col>
                                    </Row>
                                </Accordion.Body>
                            </Accordion.Item>
                        </Accordion>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button theme="primary" type="submit">
                            {t('common.button.register')}
                        </Button>
                        <Button theme="default" onClick={handleClose}>
                            {t('common.button.close')}
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>
        </>
    );
};

export default SourceAnalysisModal;
