/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import UitestSaveVariable from '@app/pages/uitest/popup/UitestSaveVariable';
import {
    clearVariableSaveModal,
    setDetailVariableList,
    saveScenario
} from '../../../store/uitest';

function UitestSaveVariableContainer() {
    const dispatch = useDispatch();
    const {data: scenarioData} = useSelector(
        (state) => state.uitest.scenarioInfo
    );

    const [modalShow, setModalShow] = React.useState(false);

    const handleClose = () => setModalShow(false);
    const handleShow = () => setModalShow(true);

    const [selectedVariable, setSelectedVariable] = React.useState();

    const {data: codeData} = useSelector((state) => state.common.searchCode);

    /*
     * Redux  
     */
    const {variableSaveInitData} = useSelector((state) => state.uitest);

    useEffect(() => {
        if (variableSaveInitData.showModal && !modalShow) {
            const temp = {...variableSaveInitData}.initData;

            if (!temp.variableType || temp.variableType === '') {
                temp.variableType = 'STRING';
            }
            setSelectedVariable(temp);
            handleShow();
            dispatch(clearVariableSaveModal());
        }
    }, [variableSaveInitData.showModal]);

    const saveDetailVariableList = (variableList) => {
        dispatch(setDetailVariableList(variableList));

        // 2022-03-09 kimtaehan     Scenario 
        // const tempData = scenarioData.data;
        // tempData.variableList = variableList;
        // if (!scenarioData.data.lock) {
        //     dispatch(saveScenario(tempData));
        // }
    };

    return (
        <>
            <UitestSaveVariable
                modalShow={modalShow}
                handleShow={handleShow}
                handleClose={handleClose}
                scenarioData={scenarioData}
                selectedVariable={selectedVariable}
                codeData={codeData}
                saveDetailVariableList={saveDetailVariableList}
            />
        </>
    );
}

export default UitestSaveVariableContainer;
