import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import LayoutSearch from '@app/pages/layout/LayoutListSearch';
import {toast} from 'react-toastify';
import {useHistory} from 'react-router-dom';
import {
    searchLayout,
    setSearchLayoutForm,
    getInitSearchLayout,
    clearSearchLayout,
    showCreateModal
} from '../../store/layout';
import Spinner from '../../components/spinner/Spinner';
import {getErrorMsg} from '../../lib/commonUiUtils';

/*
 *   
 */
function LayoutSearchContainer() {
    const history = useHistory();
    const {loading: searchLoading, error: searchError} = useSelector(
        (state) => state.layout.search
    );

    // const {layoutName, httpMethod} = useSelector(
    //     (state) => state.layout.searchForm
    // );

    const searchLayoutForm = useSelector((state) => state.layout.searchForm);

    const dispatch = useDispatch();

    // const onSearch = (_layoutName = layoutName, _httpMethod = httpMethod) => {
    //     dispatch(searchLayout(_layoutName, _httpMethod));
    // };
    const onSearch = (_searchForm = searchLayoutForm) => {
        dispatch(searchLayout(_searchForm));
    };

    const saveSearchLayoutForm = (_searchForm) => {
        dispatch(setSearchLayoutForm(_searchForm));
    };

    const onShowCreateModal = () => {
        dispatch(showCreateModal());
    };
    useEffect(() => {
        // search fail
        if (!searchLoading && searchError) {
            toast.error(getErrorMsg(searchError, 'search'));
            dispatch(clearSearchLayout());
        }
    }, [searchError]);

    useEffect(() => {
        // 2021-10-06 screen  

        if (history.action === 'PUSH') {
            const tempForm = getInitSearchLayout();
            dispatch(setSearchLayoutForm(tempForm));
            onSearch(tempForm);
        }
        if (history.action === 'POP') {
            onSearch();
        }
    }, []);
    return (
        <>
            <LayoutSearch
                onSearch={onSearch}
                searchLayoutForm={searchLayoutForm}
                saveSearchLayoutForm={saveSearchLayoutForm}
                showCreateModal={onShowCreateModal}
            />
            <Spinner isLoading={searchLoading} />
        </>
    );
    ///
}

export default LayoutSearchContainer;
