/* eslint-disable no-unused-vars */
import React, {useState, useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import RandomSaveModal from '@pages/random/RandomSaveModal';
import {toast} from 'react-toastify';

import {useTranslation} from 'react-i18next';
import Spinner from '../../components/spinner/Spinner';
import {
    clearRamdomSavaModal,
    saveRandom,
    updateRandom,
    testRandom,
    searchRandom,
    saveRandomDataClear,
    updateRandomDataClear,
    testRandomDataClear,
    findRandom,
    clearfindRandom
} from '../../store/random';
import {getErrorMsg} from '../../lib/commonUiUtils';

/*
 *  /  Container
 */
function RandomSaveModalContainer() {
    const [t] = useTranslation();
    const dispatch = useDispatch();

    const [testData, setTestData] = useState('');
    const [randomStyle, setRandomStyle] = useState('LIST');
    const [modalShow, setModalShow] = React.useState(false);

    const handleShow = () => setModalShow(true);

    const {ramdonSaveInitData} = useSelector((state) => state.random);

    const initData = {
        id: -1,
        randomKey: '',
        randomDesc: '',
        conditionsList: {
            cond1: '',
            cond2: 'string',
            cond3: '-'
        },
        conditionsNumber: {
            cond1: '0',
            cond2: '100',
            cond3: ''
        },
        conditionsDate: {
            cond1: '',
            cond2: '',
            cond3: 'yyyymmdd'
        },
        conditionsDday: {
            cond1: '0',
            cond2: '',
            cond3: 'yyyymmdd'
        },
        conditionsSsn: {
            cond1: '19',
            cond2: '65',
            cond3: 'AAAAAA-BBBBBBB'
        }
    };
    const [singleTestData, setSingleTestData] = useState(initData);

    const handleClose = () => {
        setModalShow(false);
        setTestData('');
        setRandomStyle('LIST');
        dispatch(clearRamdomSavaModal());
    };

    /*
     *  Open event
     */
    useEffect(() => {
        if (ramdonSaveInitData.showModal && !modalShow) {
            if (ramdonSaveInitData.id === -1) {
                setRandomStyle('LIST');
                setSingleTestData(initData);
                handleShow();
            } else {
                dispatch(findRandom(ramdonSaveInitData.id));
            }
        }
    }, [ramdonSaveInitData.showModal]);

    const changeData = (data) => {
        const tempData = {...data};

        if (randomStyle === 'LIST') {
            tempData.conditions = tempData.conditionsList;
        } else if (randomStyle === 'NUMBER') {
            tempData.conditions = tempData.conditionsNumber;
        } else if (randomStyle === 'DATE') {
            tempData.conditions = tempData.conditionsDate;
        } else if (randomStyle === 'SSN') {
            tempData.conditions = tempData.conditionsSsn;
        } else if (randomStyle === 'D_DAY') {
            tempData.conditions = tempData.conditionsDday;
        }
        tempData.randomStyle = randomStyle;

        return tempData;
    };
    /*
     * random data save
     */
    const saveRandomCall = (saveData) => {
        const tempData = changeData(saveData);
        dispatch(saveRandom(tempData));
    };

    /*
     * random data save
     */
    const updateRandomCall = (saveData) => {
        const tempData = changeData(saveData);
        dispatch(updateRandom(tempData));
    };

    /*
     * random data test
     */
    const testRadomCall = (saveData) => {
        const tempData = changeData(saveData);
        dispatch(testRandom(tempData));
    };

    const onChangeRandomStyle = (data) => {
        setTestData('');
        setRandomStyle(data);
    };

    /*
     * find randmom data
     */
    const {
        loading: findLoading,
        data: findResponse,
        error: findError
    } = useSelector((state) => state.random.find);

    useEffect(() => {
        if (!findLoading && findResponse) {
            const tempData = {...findResponse?.data};
            const tempRandomStyle = tempData.randomStyle;

            if (tempRandomStyle === 'LIST') {
                tempData.conditionsList = tempData.conditions;
            } else if (tempRandomStyle === 'NUMBER') {
                tempData.conditionsNumber = tempData.conditions;
            } else if (tempRandomStyle === 'DATE') {
                tempData.conditionsDate = tempData.conditions;
            } else if (tempRandomStyle === 'SSN') {
                tempData.conditionsSsn = tempData.conditions;
            } else if (tempRandomStyle === 'D_DAY') {
                tempData.conditionsDday = tempData.conditions;
            }
            setRandomStyle(tempRandomStyle);
            setSingleTestData(tempData);
            handleShow();
        }
        // fail
        if (!findLoading && findError) {
            toast.error(getErrorMsg(findError, 'select'));
            dispatch(saveRandomDataClear());
        }
        dispatch(clearfindRandom());
    }, [findResponse, findError]);

    /*
     * sava randmom data
     */
    const {
        loading: saveLoading,
        data: saveResponse,
        error: saveError
    } = useSelector((state) => state.random.save);
    const {searchForm} = useSelector((state) => state.random);

    /*
     * sava randmom data
     */
    useEffect(() => {
        if (!saveLoading && saveResponse) {
            dispatch(saveRandomDataClear());
            dispatch(searchRandom(searchForm));
            handleClose();

            toast.success(t('common.alert.updateSuccess'));
        }
        // fail
        if (!saveLoading && saveError) {
            toast.error(getErrorMsg(saveError, t('common.alert.updateFail')));
            dispatch(saveRandomDataClear());
        }
    }, [saveResponse, saveError]);

    /*
     * sava randmom data
     */
    const {
        loading: updateLoading,
        data: updateResponse,
        error: updateError
    } = useSelector((state) => state.random.update);

    /*
     * sava randmom data
     */
    useEffect(() => {
        if (!updateLoading && updateResponse) {
            dispatch(updateRandomDataClear());
            dispatch(searchRandom(searchForm));
            handleClose();

            toast.success(t('common.alert.updateSuccess'));
        }
        // fail
        if (!updateLoading && updateError) {
            toast.error(getErrorMsg(saveError, t('common.alert.updateFail')));
            dispatch(updateRandomDataClear());
        }
    }, [updateResponse, updateError]);

    const {
        loading: testLoading,
        data: testResponse,
        error: testError
    } = useSelector((state) => state.random.test);

    /*
     * sava randmom data
     */
    useEffect(() => {
        if (!testLoading && testResponse) {
            setTestData(testResponse.data);
            if (testResponse.data === null || testResponse.data === '') {
                toast.error(
                    getErrorMsg(testResponse, t('common.alert.testFail'))
                );
            }
            dispatch(testRandomDataClear());
        }

        if (!testLoading && testError) {
            toast.error(getErrorMsg(testError, t('common.alert.testFail')));
            dispatch(testRandomDataClear());
            // toast.error(t('common.alert.testFail'));
        }
    }, [testResponse, testError]);

    const {data: codeData} = useSelector((state) => state.common.searchCode);

    return (
        <>
            <RandomSaveModal
                modalShow={modalShow}
                handleClose={handleClose}
                singleTestData={singleTestData}
                setSingleTestData={setSingleTestData}
                saveRandomCall={saveRandomCall}
                updateRandomCall={updateRandomCall}
                testRadomCall={testRadomCall}
                codeData={codeData}
                testData={testData}
                randomStyle={randomStyle}
                onChangeRandomStyle={onChangeRandomStyle}
            />
            <Spinner isLoading={updateLoading || saveLoading || testLoading} />
        </>
    );
}

export default RandomSaveModalContainer;
