/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import Stat from '@pages/stat/Stat';
import {useSelector, useDispatch} from 'react-redux';
import StatTab1Table from '@pages/stat/StatTab1Table';
import {
    searchStatT1Cnt,
    searchStatT1Time,
    searchStatT1Rate,
    usedStatDetailModal
} from '../../store/stat';

function StatTab1TableContainer({type}) {
    const dispatch = useDispatch();

    const {data: cntData} = useSelector((state) => state.stat.searchT1Cnt);
    const {data: rateData} = useSelector((state) => state.stat.searchT1Rate);
    const {data: timeData} = useSelector((state) => state.stat.searchT1Time);

    const {searchFormT1} = useSelector((state) => state.stat);

    const movePage = (page) => {
        if (type === 'cnt') {
            // 1   
            dispatch(
                searchStatT1Cnt(
                    searchFormT1.serverId,
                    searchFormT1.fromDate,
                    searchFormT1.toDate,
                    page
                )
            );
        }
        if (type === 'rate') {
            // 1   
            dispatch(
                searchStatT1Rate(
                    searchFormT1.serverId,
                    searchFormT1.fromDate,
                    searchFormT1.toDate,
                    page
                )
            );
        }
        if (type === 'time') {
            // 1   
            dispatch(
                searchStatT1Time(
                    searchFormT1.serverId,
                    searchFormT1.fromDate,
                    searchFormT1.toDate,
                    page
                )
            );
        }
    };

    /*
     *    
     */
    const showStatDetailModal = (layoutId, row) => {
        //  "layoutId":6,
        //  "layoutName":"/user/insertUser.do",
        //  "allCnt":128,
        //  "successCnt":0,
        //  "failCnt":128,
        //  "noResCnt":0,
        //  "processMillisecond":"0.63",
        //  "failRate":"100"
        dispatch(usedStatDetailModal(true, row));
    };

    const renderMain = () => {
        if (type === 'cnt') {
            return (
                <>
                    <StatTab1Table
                        type={type}
                        searchData={cntData}
                        movePage={movePage}
                        showStatDetailModal={showStatDetailModal}
                    />
                </>
            );
        }
        if (type === 'rate') {
            return (
                <>
                    <StatTab1Table
                        type={type}
                        searchData={rateData}
                        movePage={movePage}
                        showStatDetailModal={showStatDetailModal}
                    />
                </>
            );
        }
        if (type === 'time') {
            return (
                <>
                    <StatTab1Table
                        type={type}
                        searchData={timeData}
                        movePage={movePage}
                        showStatDetailModal={showStatDetailModal}
                    />
                </>
            );
        }
        return '';
    };
    return <>{renderMain()}</>;
}

export default StatTab1TableContainer;
