import React, {useEffect} from 'react';
import {useHistory} from 'react-router-dom';
import {useSelector, useDispatch} from 'react-redux';
import ScenarioSearch from '@pages/scenario/ScenarioSearch';
import {toast} from 'react-toastify';
import {
    setSearchForm,
    getInitSearchForm,
    searchScenario,
    clearSearchScenario
} from '../../store/scenario';
import Spinner from '../../components/spinner/Spinner';
import {getErrorMsg} from '../../lib/commonUiUtils';

/*
 * ScenarioContainer
 *    container
 */
function ScenarioSearchContainer() {
    const dispatch = useDispatch();
    const history = useHistory();

    const {loading: searchLoading, error: searchError} = useSelector(
        (state) => state.scenario.search
    );

    const {searchForm} = useSelector((state) => state.scenario);

    const saveSearchForm = (_searchForm) => {
        dispatch(setSearchForm(_searchForm));
    };

    const onSearch = (_searchForm = searchForm) => {
        const searchFormT = {..._searchForm};
        searchFormT.page = 0;
        dispatch(searchScenario(searchFormT));
    };

    useEffect(() => {
        // // search fail
        if (!searchLoading && searchError) {
            toast.error(getErrorMsg(searchError, 'search'));
            dispatch(clearSearchScenario());
        }
    }, [searchError]);

    //    
    const {data: memberInfoData} = useSelector(
        (state) => state.auth.memberInfo
    );

    /*
     *   Loding 
     */
    useEffect(() => {
        if (history.action === 'PUSH') {
            const searchFormT = getInitSearchForm();

            if (memberInfoData.data?.userId) {
                searchFormT.manager = memberInfoData.data?.userId;
            }
            saveSearchForm(searchFormT);
            onSearch(searchFormT);
        }
        if (history.action === 'POP') {
            onSearch(searchForm);
        }
    }, []);

    /*
     *  
     */
    return (
        <>
            <ScenarioSearch
                onSearch={onSearch}
                searchForm={searchForm}
                saveSearchForm={saveSearchForm}
                memberInfoData={memberInfoData}
            />
            <Spinner isLoading={searchLoading} />
        </>
    );
    ///
}

export default ScenarioSearchContainer;
