import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import LogDetailTable from '@pages/log/LogDetailTable';
import Spinner from '../../components/spinner/Spinner';
import {
    searchLogs,
    setSearchLogsForm,
    searchLog,
    clearSearchLog
} from '../../store/log';
import {usedMultiTestModal} from '../../store/common';
/*
 *   Container
 */
function LogDetailTableContainer() {
    const dispatch = useDispatch();

    //  
    const {data: searchData, loading: searchLoading} = useSelector(
        (state) => state.log.searchLogsRes
    );

    const {searchLogsForm} = useSelector((state) => state.log);

    const movePage = (page) => {
        const searchFormT = {...searchLogsForm};
        searchFormT.page = page;
        dispatch(setSearchLogsForm(searchFormT));
        dispatch(searchLogs(searchFormT));
    };

    const showUndoFormModal = (data) => {
        dispatch(searchLog(data.logId));
    };

    //    
    const {
        data: searchByIdData,
        loading: searchByIdLoading,
        error: searchByIdError
    } = useSelector((state) => state.log.searchLogRes);

    // search fail
    useEffect(() => {
        if (!searchByIdLoading && searchByIdError) {
            // toast.error(getErrorMsg(searchError, 'search'));
            dispatch(clearSearchLog());
        } else if (!searchByIdLoading && searchByIdData) {
            const {data} = searchByIdData;

            const initData = {
                logId: data.logId,
                serverId: data.serverId,
                layoutId: data.layoutId,

                // Layout info
                urlInfo: data.urlInfo,
                serviceInfo: data.serviceInfo,
                // loginProcess: data.loginProcess,
                responseInfo: data?.responseInfo,
                responseTime: data?.responseTime,
                headerAndBodyResponse: data?.headerAndBodyResponse,
                // requestHeaderAndBody: data?.requestHeaderAndBody,
                input: data?.headerAndBodyRequest?.body
                //     input: data.input,
                //     output: data.output,
            };

            dispatch(usedMultiTestModal(true, initData, null));
            dispatch(clearSearchLog());
        }
    }, [searchByIdData, searchByIdError]);

    return (
        <>
            <LogDetailTable
                showUndoFormModal={showUndoFormModal}
                movePage={movePage}
                data={searchData}
            />
            <Spinner isLoading={searchLoading} />
        </>
    );
    ///
}

export default LogDetailTableContainer;
