/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import {toast} from 'react-toastify';
import {useTranslation} from 'react-i18next';
import ClubCreatePopup from '@app/pages/club/popup/ClubCreatePopup';
import Spinner from '../../../components/spinner/Spinner';
import {getErrorMsg} from '../../../lib/commonUiUtils';

import {
    clearClubCreateModal,
    createClub,
    clearCreateClub
} from '../../../store/club';
/*
 *  -     Container
 */
function ClubCreatePopupContainer({onSearch, onMoveDetail}) {
    const dispatch = useDispatch();
    const [t] = useTranslation();

    const [modalShow, setModalShow] = React.useState(false);
    const handleClose = () => setModalShow(false);
    const handleShow = () => setModalShow(true);
    const {clubCreateInitData} = useSelector((state) => state.club);

    const {data: codeData} = useSelector((state) => state.common.searchCode);
    /*
     *  Open event
     */
    useEffect(() => {
        if (clubCreateInitData.showModal && !modalShow) {
            // setOpenData({...scenarioLockInitData.initData});
            dispatch(clearClubCreateModal());
            handleShow();
        }
    }, [clubCreateInitData.showModal]);

    const onClickCreateClub = (values) => {
        dispatch(createClub(values));
    };

    const {
        loading: createLoading,
        data: createResponse,
        error: createError
    } = useSelector((state) => state.club.createClubRes);

    useEffect(() => {
        if (!createLoading && createResponse) {
            dispatch(clearCreateClub());
            toast.success(t('common.alert.updateSuccess'));
            onSearch();
            handleClose();
        }
        // fail
        if (!createLoading && createError) {
            dispatch(clearCreateClub());
            toast.error(getErrorMsg(createError, 'save'));
        }
    }, [createResponse, createError]);
    return (
        <>
            <ClubCreatePopup
                modalShow={modalShow}
                handleClose={handleClose}
                codeData={codeData}
                onClickCreateClub={onClickCreateClub}
            />
            <Spinner isLoading={createLoading} />
        </>
    );
    ///
}

export default ClubCreatePopupContainer;
