/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import SourceDetailSearch from '@pages/source/SourceDetailSearch';
import {setSearchSourcesForm} from '../../store/source';

function SourceDetailSearchContainer({onSearch}) {
    const dispatch = useDispatch();

    const {data: codeData} = useSelector((state) => state.common.searchCode);
    const {searchSourcesForm} = useSelector((state) => state.source);

    const onChangeEvent = (form) => {
        dispatch(setSearchSourcesForm({...form}));
    };

    return (
        <>
            <SourceDetailSearch
                onSearch={onSearch}
                searchSourcesForm={searchSourcesForm}
                onChangeEvent={onChangeEvent}
                codeData={codeData}
            />
        </>
    );
}

export default SourceDetailSearchContainer;
