import React, {useEffect} from 'react';
import {useHistory} from 'react-router-dom';
import {useSelector, useDispatch} from 'react-redux';
import ServerSearch from '@pages/server/ServerSearch';
import {toast} from 'react-toastify';
import {
    initSearchServerForm,
    setSearchServerForm,
    searchServer,
    clearSearchServer,
    setUpdateServerInitData
} from '../../store/server';
import {getErrorMsg} from '../../lib/commonUiUtils';
import Spinner from '../../components/spinner/Spinner';

/**
 * Server Seaerch Container
 */
function ServerSearchContainer() {
    const history = useHistory();

    const {loading: searchLoading, error: searchError} = useSelector(
        (state) => state.server.search
    );

    const {searchForm} = useSelector((state) => state.server);

    const dispatch = useDispatch();

    const saveSearchServerForm = (_searchForm) => {
        dispatch(setSearchServerForm(_searchForm));
    };

    const onSearch = (_searchForm = searchForm) => {
        dispatch(searchServer(_searchForm));
    };

    const onClickNewServer = () => {
        const data = {
            id: -1
        };

        dispatch(setUpdateServerInitData(data));
    };

    /*
     *     
     */
    useEffect(() => {
        if (!searchLoading && searchError) {
            toast.error(getErrorMsg(searchError, 'search'));
            dispatch(clearSearchServer());
        }
    }, [searchError]);

    /*
     *   Loding 
     */
    useEffect(() => {
        // 2021-10-06 screen  
        if (history.action === 'PUSH') {
            const initSearchServerFormT = initSearchServerForm();
            saveSearchServerForm(initSearchServerFormT);
            onSearch(initSearchServerFormT);
        }
        if (history.action === 'POP') {
            onSearch();
        }
    }, []);

    return (
        <>
            <ServerSearch
                searchLoading={searchLoading}
                onSearch={onSearch}
                searchForm={searchForm}
                saveSearchServerForm={saveSearchServerForm}
                onClickNewServer={onClickNewServer}
            />
            <Spinner isLoading={searchLoading} />
        </>
    );
    ///
}

export default ServerSearchContainer;
