/* eslint-disable no-unused-vars */
import React from 'react';
import {useTranslation} from 'react-i18next';
import {Container, Row, Col, Form, FloatingLabel} from 'react-bootstrap';
import Card from '../../components/card/Card';
import {renderBatchStatus} from '../../lib/buttonRender';

const BulkDetailHeader = ({bulkTestGroupInfo, onClickCompareValue}) => {
    const [t] = useTranslation();

    const renderTime = () => {
        const startedDateTime = bulkTestGroupInfo?.batchInfo?.startedDateTime;

        try {
            const result = `${startedDateTime.slice(
                0,
                10
            )} ${startedDateTime.slice(11, 16)} (${
                bulkTestGroupInfo?.batchInfo?.processTime
            } )`;
            return result;
        } catch (e) {
            return '';
        }
    };

    return (
        <>
            <Card
                title="  "
                body={
                    <>
                        <Container fluid>
                            <Row>
                                <Col xs="12">
                                    <div className="textAlignLeft">
                                        <h6 style={{display: 'inline-block'}}>
                                            {renderTime()}
                                        </h6>
                                    </div>
                                </Col>
                                <Col xs="8">
                                    <FloatingLabel
                                        label={t('pages.mass.testName')}
                                        className="mb-3"
                                    >
                                        <Form.Control
                                            type="text"
                                            maxLength="30"
                                            readOnly
                                            value={
                                                bulkTestGroupInfo?.bulkSnapshotName
                                            }
                                        />
                                    </FloatingLabel>
                                </Col>
                                <Col xs="2">
                                    <FloatingLabel
                                        label=""
                                        className="mb-3"
                                    >
                                        <Form.Control
                                            type="text"
                                            maxLength="30"
                                            readOnly
                                            value={bulkTestGroupInfo?.bulkCount}
                                        />
                                    </FloatingLabel>
                                </Col>
                                <Col xs="2">
                                    {renderBatchStatus(
                                        bulkTestGroupInfo?.batchInfo
                                            ?.batchStatus
                                    )}
                                </Col>
                            </Row>
                        </Container>
                    </>
                }
            />
        </>
    );
};

export default BulkDetailHeader;
