/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import Source from '@pages/source/Source';
import {useHistory} from 'react-router-dom';
import {useDispatch, useSelector} from 'react-redux';
import Spinner from '../../components/spinner/Spinner';

import {
    searchSourceSnapshot,
    setSearchSourceSnapshotForm,
    searchSoruceSnapshotFormInit
} from '../../store/source';
/*
 * Log data  Container
 */
function SourceContainer() {
    const dispatch = useDispatch();
    const history = useHistory();

    /**
     * 
     */
    const {searchSourceSnapshotForm} = useSelector((state) => state.source);
    const onSearch = (_searchSourceSnapshotForm = searchSourceSnapshotForm) => {
        dispatch(searchSourceSnapshot(_searchSourceSnapshotForm));
    };

    //  
    const {loading: searchLoading} = useSelector(
        (state) => state.source.searchSourceSnapshotRes
    );

    /**
     * 
     */
    useEffect(() => {
        if (history.action === 'PUSH') {
            const searchSourceSnapshotFormT = searchSoruceSnapshotFormInit();
            dispatch(setSearchSourceSnapshotForm(searchSourceSnapshotFormT));
            onSearch(searchSourceSnapshotFormT);
        }

        if (history.action === 'POP') {
            onSearch();
        }
    }, []);

    return (
        <>
            <Source onSearch={onSearch} />
            <Spinner isLoading={searchLoading} />
        </>
    );
}

export default SourceContainer;
