/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import {useHistory, useLocation} from 'react-router-dom';
import {useSelector, useDispatch} from 'react-redux';
import LogDetailSearch from '@pages/log/LogDetailSearch';
import {toast} from 'react-toastify';
import {
    searchLogs,
    clearSearchLogs,
    // getInitSearchForm,
    setSearchLogsForm,
    initSearchLogsForm
} from '../../store/log';
import {usedBulktestModal} from '../../store/bulk';
import Spinner from '../../components/spinner/Spinner';
import {getErrorMsg} from '../../lib/commonUiUtils';

/*
 *   Container
 */
function LogDetailSearchContainer() {
    const dispatch = useDispatch();
    const history = useHistory();
    const location = useLocation();

    // const location = useLocation();
    const {data: serverData} = useSelector(
        (state) => state.common.searchServer
    );
    // const {data: codeData} = useSelector((state) => state.common.searchCode);

    const {searchLogsForm} = useSelector((state) => state.log);
    const saveSearchForm = (_searchForm) => {
        dispatch(setSearchLogsForm(_searchForm));
    };

    const onSearch = (_searchLogsForm = searchLogsForm) => {
        dispatch(searchLogs(_searchLogsForm));
    };

    //  
    //  
    const {data: searchData, loading: searchLoading} = useSelector(
        (state) => state.log.searchLogsRes
    );

    /*
     *    
     */
    const showExecuteModal = () => {
        dispatch(
            usedBulktestModal(true, {
                basicData: 'LOG',
                predictCount: searchData?.data?.totalElements,
                logsFindCondition: searchLogsForm
            })
        );
    };

    // // search fail
    // useEffect(() => {
    //     if (!searchLoading && searchError) {
    //         toast.error(getErrorMsg(searchError, 'search'));
    //         dispatch(clearSearchHistory());
    //     }
    // }, [searchError]);

    useEffect(() => {
        const initData = initSearchLogsForm();
        initData.logSnapshotId = location.state.logSnapshotId;
        initData.fromDateTime = location.state?.sendDateTime?.fastest.slice(
            0,
            16
        );
        initData.toDateTime = location.state?.sendDateTime?.slowest.slice(
            0,
            16
        );

        dispatch(setSearchLogsForm(initData));
        onSearch(initData);
    }, []);

    return (
        <>
            <LogDetailSearch
                onSearch={onSearch}
                searchForm={searchLogsForm}
                setSearchForm={saveSearchForm}
                serverData={serverData}
                showExecuteModal={showExecuteModal}
            />
            <Spinner isLoading={searchLoading} />
        </>
    );
}

export default LogDetailSearchContainer;
