/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import BulkDetail from '@app/pages/bulk/BulkDetail';
import {toast} from 'react-toastify';
import {useLocation, useHistory} from 'react-router-dom';
import {useTranslation} from 'react-i18next';
import Spinner from '../../components/spinner/Spinner';
import {
    searchBulkDetail, //   
    searchBulkSnapshot,
    clearSearchBulkDetail, //    
    setBulkDetailForm, //     
    downloadExcelReport, //     
    downloadExcelReportClear,
    setSelectedBulkInfo, //
    requestApiExcel,
    initBulkDetailSearchForm
} from '../../store/bulk';
import {getErrorMsg} from '../../lib/commonUiUtils';

/*
 *   
 */
function BulkDetailContainer() {
    const dispatch = useDispatch();
    const location = useLocation();
    const history = useHistory();
    const [t] = useTranslation();

    // const {selectedBulktestGroup} = useSelector((state) => state.bulktest);\

    //    
    const {data: memberInfoData} = useSelector(
        (state) => state.auth.memberInfo
    );

    const {bulkDetailSearchForm} = useSelector((state) => state.bulk);
    const {loading: downloadLoading, error: downlaodError} = useSelector(
        (state) => state.bulk.downloadExcelReportRes
    );

    const {loading: searchLoading} = useSelector(
        (state) => state.bulk.searchBulkDetailRes
    );

    //    
    const onDownloadExcelReport = () => {
        if (!bulkDetailSearchForm) {
            return;
        }
        if (memberInfoData && !memberInfoData.data.isAdmin) {
            // eslint-disable-next-line no-alert
            alert(' .');
            return;
        }
        dispatch(downloadExcelReport(bulkDetailSearchForm.id));
    };

    const onRequestApiExcel = () => {
        if (!bulkDetailSearchForm) {
            return;
        }
        dispatch(requestApiExcel(bulkDetailSearchForm.id));
    };

    const {
        data: searchBulkSnapshotData,
        error: searchBulkSnapshotError,
        loading: searchSnapshotLoading
    } = useSelector((state) => state.bulk.searchBulkSnapshotRes);

    /*
     *   Loding 
     */
    useEffect(() => {
        if (location.state != null) {
            // bulkDetailHeader  
            // stat    selectedBulkInfo  

            const {bulkSnapshotId} = location.state;

            // bulkDetailBody  
            // dispatch(searchBulktest(selectedBulkTest.id, searchBulktestType));
            dispatch(clearSearchBulkDetail());

            const bulkDetailSearchFormT = initBulkDetailSearchForm();
            bulkDetailSearchFormT.bulkSnapshotId = bulkSnapshotId;
            // bulkDetailSearchFormT.retrieveType = '';

            dispatch(searchBulkSnapshot(bulkSnapshotId));

            dispatch(setBulkDetailForm(bulkDetailSearchFormT));
            dispatch(searchBulkDetail(bulkDetailSearchFormT));
        } else {
            // eslint-disable-next-line no-alert
            alert(' .');
            history.goBack();
        }
    }, []);

    useEffect(() => {
        if (!searchSnapshotLoading && searchBulkSnapshotError) {
            // toast.error(
            //     getErrorMsg(searchDetailByIdError, t('common.alert.searchFail'))
            // );
        }
        if (!searchSnapshotLoading && searchBulkSnapshotData) {
            dispatch(setSelectedBulkInfo(searchBulkSnapshotData.data));
        }
    }, [searchBulkSnapshotData, searchBulkSnapshotError]);

    //   
    useEffect(() => {
        if (!downloadLoading && downlaodError) {
            toast.error(
                getErrorMsg(downlaodError, t('common.alert.failFileDown'))
            );
            dispatch(downloadExcelReportClear());
        }
    }, [downlaodError]);

    return (
        <>
            <BulkDetail
                downloadExcelReport={onDownloadExcelReport}
                requestApiExcel={onRequestApiExcel}
            />
            <Spinner
                isLoading={
                    downloadLoading || searchLoading || searchSnapshotLoading
                }
            />
        </>
    );
}

export default BulkDetailContainer;
