/* eslint-disable no-unused-vars */
import React from 'react';
import 'react-simple-tree-menu/dist/main.css';
import {FaSearch, FaTrash, FaClone} from 'react-icons/fa';
import {Table} from 'antd';
import ReactDragListView from 'react-drag-listview';
import {useTranslation} from 'react-i18next';
import UitestStepUpdateContainer from '../../containers/uitest/UitestStepUpdateContainer';
import Button from '../../components/button/Button';
import UitestStepCreateContainer from '../../containers/uitest/UitestStepCreateContainer';
import {deleteStep} from '../../lib/UitestCommon';

const UitestStepList = ({
    scenarioData,
    selectedCaseId,
    setDetailCases,
    showStepUpdatePopup,
    onClickMove
}) => {
    const [t] = useTranslation();
    const renderStepTableBody = () => {
        if (!scenarioData || !selectedCaseId || selectedCaseId === '') {
            return [];
        }

        const theCase = scenarioData?.data?.cases?.filter(
            (info) => info.caseId === selectedCaseId
        );

        return theCase[0].steps;
    };

    // SET_SELECTED_CASEID ->  case  
    const onClickStepColumn = (record) => {
        showStepUpdatePopup(record);
    };

    const onClickDelete = (record) => {
        // eslint-disable-next-line no-alert
        if (window.confirm(t('common.alert.checkDelete'))) {
            const cases = [...scenarioData.data?.cases];
            const caseLoop = deleteStep(cases, record, selectedCaseId);
            setDetailCases(caseLoop);
        }
    };

    const renderStepCodeButton = (row) => {
        let className = 'btn btn-info';

        if (row.stepType === 'API') {
            className = 'btn btn-success';
        }
        if (row.stepType === 'QUERY') {
            className = 'btn btn-primary';
        }
        if (row.stepType === 'SCRIPT') {
            className = 'btn btn-warning';
        }
        return (
            <button
                type="submit"
                onClick={() => onClickStepColumn(row)}
                className={className}
                style={{width: '100%'}}
            >
                {row.stepType}
            </button>
        );
    };

    const renderStepName = (record) => {
        if (record.stepType === 'LOGIN') {
            return ' ';
        }
        return (
            <h4 style={{color: 'black', fontSize: '1.0rem'}}>
                {record.stepName}
            </h4>
        );
    };

    const columns = [
        {
            title: 'stepType',
            key: 'stepType',
            dataIndex: 'idx',
            width: '5%',
            render: (text, row) => renderStepCodeButton(row)
        },
        {
            title: 'stepName',
            key: 'stepName',
            dataIndex: 'stepName',

            render: (text, row) => renderStepName(row)
        },
        {
            title: 'Operates',
            key: 'operate',
            render: (text, record) => (
                <>
                    <Button
                        theme="outline-success"
                        onClick={() => onClickStepColumn(record)}
                    >
                        <FaSearch />
                    </Button>
                    <Button
                        theme="outline-primary"
                        onClick={() => onClickMove(record)}
                    >
                        <FaClone />
                    </Button>
                    <Button
                        theme="outline-danger"
                        onClick={() => onClickDelete(record)}
                    >
                        <FaTrash />
                    </Button>
                </>
            )
        }
    ];

    const dragProps = {
        onDragEnd(fromIndex, toIndex) {
            const cases = [...scenarioData.data.cases];
            const caseLoop = cases.map((cUnit) => {
                const theCase = cUnit;
                if (theCase.caseId === selectedCaseId) {
                    const data = theCase.steps;
                    const item = data.splice(fromIndex, 1)[0];
                    data.splice(toIndex, 0, item);

                    const aLoop = data.map((unit, index) => {
                        const temp = unit;
                        temp.index = index;
                        return temp;
                    });
                    theCase.steps = aLoop;
                }
                return theCase;
            });
            setDetailCases(caseLoop);
        },
        handleSelector: 'tr'
    };

    return (
        <>
            <UitestStepCreateContainer />
            <UitestStepUpdateContainer />
            <div
                style={{
                    overflowY: 'auto',
                    maxHeight: '800px',
                    minHeight: '600px'
                }}
            >
                <ReactDragListView {...dragProps}>
                    <Table
                        rowKey="stepId"
                        columns={columns}
                        pagination={false}
                        dataSource={renderStepTableBody()}
                    />
                </ReactDragListView>
            </div>
            {/* */}
        </>
    );
};

export default UitestStepList;
