const UPDATE_SCENARIO = `${PREFIX}/UPDATE_SCENARIO`;
const UPDATE_SCENARIO_SUCCESS = `${PREFIX}/UPDATE_SCENARIO_SUCCESS`;
const UPDATE_SCENARIO_ERROR = `${PREFIX}/UPDATE_SCENARIO_ERROR`;
export const updateScenario = (data) => {
    const tempData = {...data};
    if (data.scenarioName === '') {
        //       
        const offset = new Date().getTimezoneOffset() * 60000;
        const today = new Date(Date.now() - offset)
            .toISOString()
            .slice(0, -1)
            .slice(0, 10);
        tempData.scenarioName = `Undefined (${today})`;
    }
    return {
        type: UPDATE_SCENARIO,
        payload: {
            ...tempData
        }
    };
};
/**
 * (TR)   
 * saveScenario (SAVE_SCENARIO) : CALL
 * saveScenarioDataClear (SAVE_SCENARIO_DATA_CLEAR) : CLEAR
 * setDetailScenarioName (SET_DETAIL_SCENARIONAME) :  
 * setDetailScenarioDesc (SET_DETAIL_SCENARIODESC) :  
 * setDetailManager (SET_DETAIL_MANAGER) :  
 * setDetailCaseList (SET_DETAIL_CASELIST) :  
 * setDetailVariableList (SET_DETAIL_VARIABLELIST) :  
 */
const SAVE_SCENARIO = `${PREFIX}/SAVE_SCENARIO`;
const SAVE_SCENARIO_SUCCESS = `${PREFIX}/SAVE_SCENARIO_SUCCESS`;
const SAVE_SCENARIO_ERROR = `${PREFIX}/SAVE_SCENARIO_ERROR`;
const SAVE_SCENARIO_DATA_CLEAR = `${PREFIX}/SAVE_SCENARIO_DATA_CLEAR`;
const SET_DETAIL_SCENARIONAME = `${PREFIX}/SET_DETAIL_SCENARIONAME`;
const SET_DETAIL_SCENARIODESC = `${PREFIX}/SET_DETAIL_SCENARIODESC`;
const SET_DETAIL_MANAGER = `${PREFIX}/SET_DETAIL_MANAGER`;
const SET_DETAIL_CASELIST = `${PREFIX}/SET_DETAIL_CASELIST`;
const SET_DETAIL_VARIABLELIST = `${PREFIX}/SET_DETAIL_VARIABLELIST`;

export const saveScenario = (data) => {
    const tempData = {...data};
    if (data.scenarioName === '') {
        //       
        const offset = new Date().getTimezoneOffset() * 60000;
        const today = new Date(Date.now() - offset)
            .toISOString()
            .slice(0, -1)
            .slice(0, 10);
        tempData.scenarioName = `Undefined (${today})`;
    }
    return {
        type: SAVE_SCENARIO,
        payload: {
            ...tempData
        }
    };
};

export const saveScenarioDataClear = () => ({
    type: SAVE_SCENARIO_DATA_CLEAR
});

