/**
 * State 
 */
const initialState = {
    saveScenario: reducerUtils.initial(),
    scenarioInfo: reducerUtils.initial(),
    selectedCaseId: {caseId: ''},
    searchServer: reducerUtils.initial(),
    executeList: {
        seq: 0,
        serverId: -1,
        dbId: -1,
        selectedId: 0,
        executeMethod: true, //   : true (), false ()
        stepList: []
    },

    stepUpdateInitData: {
        useData: true
    },
    stepScriptCallback: {
        isFinish: false
    },
    searchLayout: reducerUtils.initial(),
    searchLayoutDetail: reducerUtils.initial(),
    //    's   's 
    colListByRandVal: reducerUtils.initial(),
    convertPayload: reducerUtils.initial(),
    convertXhrList: reducerUtils.initial(),
    // move step event popup
    moveStepInitData: {
        showModal: false,
        selectedStep: {}
    },
    uiTestInitData: {
        showModal: false,
        xhrInfo: {}
    },
    // '' / 
    variableSaveInitData: {
        showModal: false,
        initData: {}
    }
};

/**
 * Redux-saga 
 */
export function* uitestSaga() {
    yield takeEvery(
        SAVE_SCENARIO,
        createPromiseSaga(SAVE_SCENARIO, uitestAPI.saveScenario)
    );
    yield takeEvery(
        UPDATE_SCENARIO,
        createPromiseSaga(SAVE_SCENARIO, uitestAPI.updateScenario)
    );
    yield takeLatest(
        SEARCH_SCENARIO_DETAIL,
        createPromiseSaga(
            SEARCH_SCENARIO_DETAIL,
            uitestAPI.searchScenarioDetail
        )
    );
    yield takeLatest(
        SEARCH_LAYOUT,
        createPromiseSaga(SEARCH_LAYOUT, uitestAPI.searchLayout)
    );
    yield takeLatest(
        SEARCH_LAYOUT_DETAIL,
        createPromiseSaga(SEARCH_LAYOUT_DETAIL, uitestAPI.searchLayoutDetail)
    );
    yield takeEvery(
        GET_RANDOM_VALUE,
        createPromiseSaga(GET_RANDOM_VALUE, uitestAPI.combineRequestValue)
    );
    yield takeEvery(
        CONVERT_PAYLOAD,
        createPromiseSaga(CONVERT_PAYLOAD, uitestAPI.convertPayload)
    );
    yield takeEvery(
        CONVERT_XHRLIST,
        createPromiseSaga(CONVERT_XHRLIST, uitestAPI.convertXhrList)
    );
}

export default function uitest(state = initialState, action) {
    switch (action.type) {
        case SET_SELECTED_CASEID:
            return {
                ...state,
                selectedCaseId: {
                    ...state.selectedCaseId,
                    caseId: action.payload.caseId
                }
            };
        case SET_DETAIL_SCENARIONAME:
            return {
                ...state,
                scenarioInfo: {
                    ...state.scenarioInfo,
                    data: {
                        ...state.scenarioInfo.data,
                        data: {
                            ...state.scenarioInfo.data.data,
                            scenarioName: action.payload.scenarioName
                        }
                    }
                }
            };
        case SET_DETAIL_SCENARIODESC:
            return {
                ...state,
                scenarioInfo: {
                    ...state.scenarioInfo,
                    data: {
                        ...state.scenarioInfo.data,
                        data: {
                            ...state.scenarioInfo.data.data,
                            scenarioDesc: action.payload.scenarioDesc
                        }
                    }
                }
            };
        case SET_DETAIL_MANAGER:
            return {
                ...state,
                scenarioInfo: {
                    ...state.scenarioInfo,
                    data: {
                        ...state.scenarioInfo.data,
                        data: {
                            ...state.scenarioInfo.data.data,
                            manager: action.payload.manager
                        }
                    }
                }
            };
        case SET_DETAIL_CASELIST:
            return {
                ...state,
                scenarioInfo: {
                    ...state.scenarioInfo,
                    data: {
                        ...state.scenarioInfo.data,
                        data: {
                            ...state.scenarioInfo.data.data,
                            cases: action.payload.cases
                        }
                    }
                }
            };

        case SET_DETAIL_VARIABLELIST:
            return {
                ...state,
                scenarioInfo: {
                    ...state.scenarioInfo,
                    data: {
                        ...state.scenarioInfo.data,
                        data: {
                            ...state.scenarioInfo.data.data,
                            variables: action.payload.variables
                        }
                    }
                }
            };

        case SAVE_SCENARIO:
        case SAVE_SCENARIO_SUCCESS:
        case SAVE_SCENARIO_ERROR:
            return handleAsyncActions(
                SAVE_SCENARIO,
                'saveScenario',
                true
            )(state, action);

        case UPDATE_SCENARIO:
        case UPDATE_SCENARIO_SUCCESS:
        case UPDATE_SCENARIO_ERROR:
            return handleAsyncActions(
                UPDATE_SCENARIO,
                'saveScenario',
                true
            )(state, action);

        case SAVE_SCENARIO_DATA_CLEAR:
            return {
                ...state,
                saveScenario: reducerUtils.initial()
            };

        case SET_EXECUTE_LIST:
            return {
                ...state,
                executeList: action.payload.data
            };

        case CLEAR_EXECUTE_LIST:
            return {
                ...state,
                executeList: {
                    seq: 0,
                    serverId: -1,
                    dbId: -1,
                    selectedId: 0,
                    executeMethod: true, //   : true (), false ()
                    stepList: []
                }
            };

        case SEARCH_SCENARIO_DETAIL:
        case SEARCH_SCENARIO_DETAIL_SUCCESS:
        case SEARCH_SCENARIO_DETAIL_ERROR:
            return handleAsyncActions(
                SEARCH_SCENARIO_DETAIL,
                'scenarioInfo',
                true
            )(state, action);

        case SEARCH_SCENARIO_DETAIL_CLEAR:
            return {
                ...state,
                scenarioInfo: {
                    data: {
                        data: {
                            scenarioName: '',
                            id: -1,
                            caseList: []
                        }
                    }
                }
            };

        case CLEAR_STEP_UPDATE_INITDATA:
            return {
                ...state,
                stepUpdateInitData: {
                    useData: true
                }
            };
        case SET_STEP_UPDATE_INITDATA:
            return {
                ...state,
                stepUpdateInitData: {
                    ...state.stepUpdateInitData,
                    ...action.payload.data
                }
            };
        case SET_STEP_SCRIPT_CALLBACK:
            return {
                ...state,
                stepScriptCallback: {
                    isFinish: action.payload.isFinish
                }
            };

        case SEARCH_LAYOUT:
        case SEARCH_LAYOUT_SUCCESS:
        case SEARCH_LAYOUT_ERROR:
            return handleAsyncActions(
                SEARCH_LAYOUT,
                'searchLayout',
                true
            )(state, action);

        case SEARCH_LAYOUT_DETAIL:
        case SEARCH_LAYOUT_DETAIL_SUCCESS:
        case SEARCH_LAYOUT_DETAIL_ERROR:
            return handleAsyncActions(
                SEARCH_LAYOUT_DETAIL,
                'searchLayoutDetail',
                true
            )(state, action);

        case CLEAR_SEARCH_LAYOUT_DETAIL:
            return {
                ...state,
                searchLayoutDetail: reducerUtils.initial()
            };

        case GET_RANDOM_VALUE:
        case GET_RANDOM_VALUE_SUCCESS:
        case GET_RANDOM_VALUE_ERROR:
            return handleAsyncActions(
                GET_RANDOM_VALUE,
                'colListByRandVal',
                true
            )(state, action);

        case CLEAR_GET_RANDOM_VALUE:
            return {
                ...state,
                colListByRandVal: reducerUtils.initial()
            };

        case CONVERT_PAYLOAD:
        case CONVERT_PAYLOAD_SUCCESS:
        case CONVERT_PAYLOAD_ERROR:
            return handleAsyncActions(
                CONVERT_PAYLOAD,
                'convertPayload',
                true
            )(state, action);

        case CLEAR_CONVERT_PAYLOAD:
            return {
                ...state,
                convertPayload: reducerUtils.initial()
            };
        case CLEAR_MOVE_STEP_INITDATA:
            return {
                ...state,
                moveStepInitData: {
                    showModal: false
                }
            };
        case SET_MOVE_STEP_INITDATA:
            return {
                ...state,
                moveStepInitData: {
                    showModal: true,
                    selectedStep: action.payload.selectedStep
                }
            };
        case CLEAR_UI_TEST_INITDATA:
            return {
                ...state,
                uiTestInitData: {
                    showModal: false
                }
            };
        case SET_UI_TEST_INITDATA:
            return {
                ...state,
                uiTestInitData: {
                    showModal: true,
                    xhrInfo: action.payload.xhrInfo
                }
            };

        case CONVERT_XHRLIST:
        case CONVERT_XHRLIST_SUCCESS:
        case CONVERT_XHRLIST_ERROR:
            return handleAsyncActions(
                CONVERT_XHRLIST,
                'convertXhrList',
                true
            )(state, action);

        case CLEAR_CONVERT_XHRLIST:
            return {
                ...state,
                convertXhrList: reducerUtils.initial()
            };

        // '' /  
        case USE_VARIABLE_SAVE_MODAL:
            return {
                ...state,
                variableSaveInitData: {
                    showModal: action.payload.showModal,
                    initData: action.payload.initData,
                    readOnly: action.payload.readOnly
                }
            };

        case CLEAR_VARIABLE_SAVE_MODAL:
            return {
                ...state,
                variableSaveInitData: {
                    showModal: false
                }
            };
        default:
            return state;
    }
}
