/* eslint-disable no-unused-vars */
import React from 'react';
import {FaSearch, FaTrash} from 'react-icons/fa';
import {useTranslation} from 'react-i18next';
import Table from '../../components/table/Table';
import Button from '../../components/button/Button';
import {displayNumber} from '../../lib/commonUiUtils';
import {renderSourceType} from '../../lib/buttonRender';

const SourceDetailTable = ({data, movePage, onClickMove}) => {
    const [t] = useTranslation();
    const tableName = t('pages.history.historyList');
    const columns = [
        {
            title: 'Source Type',
            key: 'sourceType',
            width: '10%',
            render: (id, row) => {
                return renderSourceType(row.sourceType);
            }
        },
        {
            title: 'Source Name',
            key: 'sourceName',
            align: 'left',
            render: (id, row) => {
                return row.fileInfo?.fileName;
            }
        },
        {
            title: 'Package Name',
            key: 'packageName',
            align: 'left',
            render: (id, row) => {
                return row.classInfo?.packageName;
            }
        },
        {
            title: 'Class',
            key: 'className',
            align: 'left',
            render: (id, row) => {
                return row.classInfo?.className;
            }
        },

        {
            title: '',
            width: '5%',
            key: 'button',
            render: (id, row, column) => {
                return (
                    <>
                        <Button
                            theme="outline-success"
                            onClick={() => onClickMove(id)}
                        >
                            <FaSearch />
                        </Button>
                    </>
                );
            }
        }
    ];

    return (
        <>
            <Table
                tableName=" "
                movePage={movePage}
                rowKey="sourceId"
                columns={columns}
                data={data}
                // onDoubleClick={onClickMove}
            />
        </>
    );
};

export default SourceDetailTable;
