import React from 'react';
import {Link} from 'react-router-dom';
import {useFormik} from 'formik';
import {useTranslation} from 'react-i18next';
import * as Yup from 'yup';
import {printFormError, getInputValidMsg} from '../../lib/commonUiUtils';
import Button from '../../components/button/Button';

/*
 *  
 */
const Login = ({loading, onLogin}) => {
    const [t] = useTranslation();

    const formik = useFormik({
        initialValues: {
            userId: '',
            password: ''
        },
        validationSchema: Yup.object({
            password: Yup.string().required(getInputValidMsg('password')),
            userId: Yup.string().required(getInputValidMsg('ID'))
        }),
        onSubmit: (values) => {
            onLogin(values.userId, values.password);
        }
    });

    document.getElementById('root').classList = 'hold-transition login-page';

    return (
        <div style={{width: '450px'}}>
            <div className="card card-outline card-primary">
                <div className="card-header text-center">
                    <Link to="/" className="h1">
                        <b>aTworks</b>
                    </Link>
                </div>
                <div className="card-body">
                    <p>{t('login.label.signIn')}</p>
                    <form onSubmit={formik.handleSubmit}>
                        <div className="mb-3">
                            <div className="input-group">
                                <div className="input-group-append">
                                    <div className="input-group-text">
                                        <span className="fas fa-user" />
                                    </div>
                                </div>

                                <input
                                    type="string"
                                    className="form-control"
                                    placeholder="ID"
                                    {...formik.getFieldProps('userId')}
                                    maxLength="20"
                                />
                            </div>
                            {printFormError(formik, 'userId')}
                        </div>
                        <div className="mb-3">
                            <div className="input-group">
                                <div className="input-group-append">
                                    <div className="input-group-text">
                                        <span className="fas fa-lock" />
                                    </div>
                                </div>
                                <input
                                    type="password"
                                    className="form-control"
                                    placeholder="Password"
                                    {...formik.getFieldProps('password')}
                                    maxLength="20"
                                />
                            </div>
                            {printFormError(formik, 'password')}
                        </div>

                        <div className="row">
                            <div className="col-8">
                                {/* <div className="icheck-primary">
                                    <input type="checkbox" id="remember" />
                                    <label htmlFor="remember">
                                        {t('login.label.rememberMe')}
                                    </label>
                                </div> */}
                            </div>
                            <div className="col-4">
                                <Button block type="submit" isLoading={loading}>
                                    {t('login.button.signIn.label')}
                                </Button>
                            </div>
                        </div>
                    </form>
                </div>
                <div className="card-footer">
                    COPYRIGHT2023 BY SK INC. ALL RIGHTS RESERVED
                </div>
            </div>
        </div>
    );
};
export default Login;
