/* eslint-disable no-unused-vars */
import React from 'react';
import {
    Row,
    Col,
    Form,
    Modal,
    InputGroup,
    FormControl,
    FloatingLabel
} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
import Table from '../../../components/table/Table';
import Button from '../../../components/button/Button';
import DashboardTestCnt from '../../../components/chart/PieChartWithPaddingAngle';
import SimpleBarChart from '../../../components/chart/SimpleBarChart';
import {
    displayNumber,
    renderModalHeader,
    renderHttpMethod
} from '../../../lib/commonUiUtils';

/*
 * StatTab1LayoutModal :     
 */
const StatTab1LayoutModal = ({
    modalShow,
    handleClose,
    statDetailData,
    searchFormT1,
    moveHistory
}) => {
    const [t] = useTranslation();
    const columns = [
        {
            title: '',
            key: 'total',
            render: (id, row) => {
                return `${row.targetNumbers.totalCount}${t(
                    'common.text.cases'
                )} `;
            }
        },
        {
            title: '',
            key: 'success',
            render: (id, row) => {
                return `${displayNumber(row.targetNumbers.successCount)}${t(
                    'common.text.cases'
                )} `;
            }
        },
        {
            title: '',
            key: 'fail',
            render: (id, row) => {
                return `${displayNumber(row.targetNumbers.failCount)}${t(
                    'common.text.cases'
                )} `;
            }
        },
        {
            title: '',
            key: 'failRate',
            render: (id, row) => {
                return `${(
                    (row.targetNumbers.failCount /
                        row.targetNumbers.totalCount) *
                    100
                ).toFixed(1)}% `;
            }
        },
        {
            title: '',
            key: 'totalTimeSumMs',
            render: (id, row) => {
                return `${row.targetNumbers.totalTimeSumMs.toFixed(5)} ${t(
                    'common.text.second'
                )}`;
            }
        },
        {
            title: t('pages.stat.processMillisecond'),
            key: 'totalTimeAvgMs',
            render: (id, row) => {
                return `${row.targetNumbers.totalTimeAvgMs.toFixed(5)} ${t(
                    'common.text.second'
                )}`;
            }
        }
    ];

    const renderTableData = () => {
        if (!statDetailData) {
            return {data: {content: []}};
        }

        return {data: {content: [statDetailData]}};
    };

    const renderModalDesc = () => {
        if (!searchFormT1) return '';

        return `(${searchFormT1.fromDateOld} ~ ${searchFormT1.toDateOld}) 
        ${searchFormT1.serverNameOld}   `;
    };

    /*
     *    chart 
     */
    const renderThisData = () => {
        if (!statDetailData) {
            return [];
        }

        return [
            {name: '', value: statDetailData?.targetNumbers.successCount},
            {name: '', value: statDetailData?.targetNumbers.failCount}
        ];
    };

    // <Bar dataKey="Total" fill="#FFBB28" />
    // <Bar dataKey="Success" fill="#0088FE" />
    // <Bar dataKey="Fail" fill="#FF8042" />
    const renderSimpleBarChartData = () => {
        if (!statDetailData) {
            return [];
        }
        return [
            {
                name: statDetailData?.serviceInfo?.serviceId,
                Total: statDetailData?.targetNumbers?.totalTimeAvgMs.toFixed(5),
                Success: (
                    statDetailData?.targetNumbers?.successTimeSumMs /
                    statDetailData?.targetNumbers?.successCount
                ).toFixed(5),
                Fail: (
                    statDetailData?.targetNumbers?.failTimeSumMs /
                    statDetailData?.targetNumbers?.failCount
                ).toFixed(5)
            },
            {
                name: ' API ',
                Total: statDetailData?.allNumbers?.totalTimeAvgMs.toFixed(5),
                Success: (
                    statDetailData?.allNumbers?.successTimeSumMs /
                    statDetailData?.allNumbers?.successCount
                ).toFixed(5),
                Fail: (
                    statDetailData?.allNumbers?.failTimeSumMs /
                    statDetailData?.allNumbers?.failCount
                ).toFixed(5)
            }
        ];
    };

    return (
        <>
            <Modal
                show={modalShow}
                onHide={handleClose}
                size="xl"
                style={{maxHeight: '1000px'}}
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Form>
                    {renderModalHeader(t('pages.stat.statDetailModalTitle'))}
                    <Modal.Body>
                        <Row
                            style={{
                                // borderBottom: '1px solid #ddd',
                                marginBottom: '30px'
                            }}
                        >
                            <Col xs="12">
                                <Form.Label>{renderModalDesc()}</Form.Label>
                            </Col>
                            <Col xs="5">
                                <InputGroup>
                                    {renderHttpMethod(
                                        statDetailData?.urlInfo.httpMethod
                                    )}
                                    <FloatingLabel label="URL Path">
                                        <FormControl
                                            disabled
                                            value={
                                                statDetailData?.serviceInfo
                                                    ?.serviceId ?? ''
                                            }
                                        />
                                    </FloatingLabel>
                                </InputGroup>
                            </Col>
                            <Col xs="3">
                                <FloatingLabel label="Service ID">
                                    <FormControl
                                        type="text"
                                        disabled
                                        value={
                                            statDetailData?.serviceInfo
                                                .serviceId ?? ''
                                        }
                                    />
                                </FloatingLabel>
                            </Col>
                            <Col xs="4">
                                <FloatingLabel label="Service ">
                                    <FormControl
                                        type="text"
                                        disabled
                                        value={
                                            statDetailData?.serviceInfo
                                                .serviceDesc ?? ''
                                        }
                                    />
                                </FloatingLabel>
                            </Col>
                        </Row>

                        <Row>
                            <Col xs="12">
                                <Table
                                    tableName=""
                                    rowKey="layoutId"
                                    isCard="N"
                                    columns={columns}
                                    disablePageView
                                    // data={}
                                    data={renderTableData()}
                                />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs="6">
                                <Form.Label className="label-center">
                                    /
                                </Form.Label>
                                <DashboardTestCnt data={renderThisData()} />
                            </Col>
                            <Col xs="6">
                                <Form.Label className="label-center">
                                    
                                </Form.Label>
                                <SimpleBarChart
                                    data={renderSimpleBarChartData()}
                                />
                            </Col>
                        </Row>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button theme="info" onClick={moveHistory}>
                            {t('common.button.showHistory')}
                        </Button>
                        <Button theme="default" onClick={handleClose}>
                            {t('common.button.close')}
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>
        </>
    );
};
export default StatTab1LayoutModal;
