import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
// import {toast} from 'react-toastify';
import LayoutListTable from '@pages/layout/LayoutListTable';
import {useHistory} from 'react-router-dom';
import {useTranslation} from 'react-i18next';
import {toast} from 'react-toastify';
import Spinner from '../../components/spinner/Spinner';
import {
    searchLayout,
    setSearchLayoutForm,
    deleteLayout,
    deleteLayoutDataClear
} from '../../store/layout';
import {getErrorMsg} from '../../lib/commonUiUtils';

/*
 *   Container
 */
function LayoutTableContainer() {
    const history = useHistory();
    const dispatch = useDispatch();
    const [t] = useTranslation();

    const {data: searchData} = useSelector((state) => state.layout.search);
    const searchLayoutForm = useSelector((state) => state.layout.searchForm);

    const {
        data: deleteData,
        loading: deleteLoading,
        error: deleteError
    } = useSelector((state) => state.layout.delete);

    const moveLayoutDetail = (selectedLayout) => {
        history.push({
            pathname: '/layout/detail',
            state: {
                id: selectedLayout.id
            }
        });
    };

    const movePage = (page) => {
        const searchLayoutFormT = {...searchLayoutForm};
        searchLayoutFormT.page = page;
        dispatch(setSearchLayoutForm(searchLayoutFormT));
        dispatch(searchLayout(searchLayoutFormT));
    };

    /*
     *  .
     */
    const onDelete = (id) => {
        // eslint-disable-next-line no-alert
        if (window.confirm(t('common.alert.checkDelete'))) {
            dispatch(deleteLayout(id));
        }
    };

    useEffect(() => {
        if (!deleteLoading && deleteError) {
            toast.error(getErrorMsg(deleteError, 'search'));
            dispatch(deleteLayoutDataClear());
        }
        if (!deleteLoading && deleteData) {
            // toast.error(getErrorMsg(deleteError, 'search'));
            // dispatch(deleteRandomDataClear());
            dispatch(deleteLayoutDataClear());
            dispatch(searchLayout(searchLayoutForm));
            toast.success(t('common.alert.updateSuccess'));
        }
    }, [deleteData, deleteError]);

    return (
        <>
            <LayoutListTable
                moveLayoutDetail={moveLayoutDetail}
                movePage={movePage}
                data={searchData}
                onDelete={onDelete}
            />
            <Spinner isLoading={deleteLoading} />
        </>
    );
}

export default LayoutTableContainer;
