// eslint-disable-next-line import/prefer-default-export
import moment from 'moment';

// eslint-disable-next-line import/prefer-default-export
export const changeToDate = (datetime) => {
    if (datetime === null || datetime === undefined) {
        return '';
    }
    //  
    const now = moment(new Date());
    //   
    const duration = moment.duration(now.diff(datetime));
    // 
    // asSeconds    
    //  float datatype   (3.82 )

    const seconds = duration.asSeconds();
    const minute = duration.asMinutes();
    const hours = duration.asHours();
    const days = duration.asDays();
    const weeks = duration.asWeeks();
    const month = duration.asMonths();
    const year = duration.asYears();

    //    parseInt   int   .

    if (seconds < 31) {
        return 'NOW';
    }
    if (minute < 1) {
        // 1    ,
        return `${parseInt(`${seconds}`, 10)} `;
    }
    if (hours < 1) {
        // 1    
        return `${parseInt(`${minute}`, 10)} `;
    }
    if (days < 1) {
        // 1    
        return `${parseInt(`${hours}`, 10)} `;
    }
    if (weeks < 1) {
        //     
        return `${parseInt(`${days}`, 10)} `;
    }
    if (month < 1) {
        //      
        return `${parseInt(`${weeks}`, 10)} `;
    }
    if (year < 1) {
        // 1    
        return `${parseInt(`${month}`, 10)} `;
    }

    // 1    
    return `${parseInt(`${year}`, 10)} `;
};

export const changeToCommonFormat = (datetime) => {
    if (datetime === null || datetime === undefined) {
        return '';
    }
    const date = datetime.slice(0, 10);
    const time = datetime.slice(11, 16);

    // const amPm = Number(datetime.slice(11, 13)) > 12 ? '' : '';

    return `${date} ${time}`;
    // return `${datetime.slice(0, 10)} ${datetime.slice(11, 16)}`;
};

export const changeToProcessTime = (processTime) => {
    if (processTime > 36000) {
        return `${(processTime / 3600).toFixed()} `;
    }
    if (processTime > 3600) {
        return `${(processTime / 3600).toFixed()}  ${(
            (processTime % 3600) /
            60
        ).toFixed()} `;
    }
    if (processTime > 600) {
        return `${(processTime / 60).toFixed()} `;
    }
    if (processTime > 60) {
        return `${(processTime / 60).toFixed()}  ${(
            processTime % 60
        ).toFixed()} `;
    }
    if (processTime > 10) {
        return `${processTime.toFixed()} `;
    }
    return `${processTime} `;
};
