/* eslint-disable no-unused-vars */
/* eslint-disable indent */
import {takeEvery, takeLatest} from 'redux-saga/effects';
import * as sourceApi from '../api/source';
import {
    reducerUtils,
    handleAsyncActions,
    createPromiseSaga
} from '../lib/asyncUtils';

const PREFIX = 'SOURCE';

const USE_SOURCE_ANALYSIS_MODAL = `${PREFIX}/USE_SOURCE_ANALYSIS_MODAL`; //  Source  
const CLEAR_SOURCE_ANALYSIS_MODAL = `${PREFIX}/CLEAR_SOURCE_ANALYSIS_MODAL`; //  Source    Clear
export const usedSourceAnalysisModal = (showModal) => ({
    type: USE_SOURCE_ANALYSIS_MODAL,
    payload: {
        showModal
    }
});
export const clearSourceAnalysisModal = () => ({
    type: CLEAR_SOURCE_ANALYSIS_MODAL
});

const ANALYSIS_SOURCE = `${PREFIX}/ANALYSIS_SOURCE`; // 
const ANALYSIS_SOURCE_SUCCESS = `${PREFIX}/ANALYSIS_SOURCE_SUCCESS`;
const ANALYSIS_SOURCE_ERROR = `${PREFIX}/ANALYSIS_SOURCE_ERROR`;
const CLEAR_ANALYSIS_SOURCE = `${PREFIX}/CLEAR_ANALYSIS_SOURCE`;
export const analysisSource = (values) => ({
    type: ANALYSIS_SOURCE,
    payload: values
});
export const clearanalysisSource = () => ({
    type: CLEAR_ANALYSIS_SOURCE
});

const SEARCH_SOURCE_SNAPSHOT = `${PREFIX}/SEARCH_SOURCE_SNAPSHOT`;
const SEARCH_SOURCE_SNAPSHOT_SUCCESS = `${PREFIX}/SEARCH_SOURCE_SNAPSHOT_SUCCESS`;
const SEARCH_SOURCE_SNAPSHOT_ERROR = `${PREFIX}/SEARCH_SOURCE_SNAPSHOT_ERROR`;
const CLEAR_SEARCH_SOURCE_SNAPSHOT = `${PREFIX}/CLEAR_SEARCH_SOURCE_SNAPSHOT`;
const SET_SEARCH_SOURCE_SNAPSHOT_FORM = `${PREFIX}/SET_SEARCH_SOURCE_SNAPSHOT_FORM`; // search data setting
export const searchSourceSnapshot = (searchForm) => ({
    type: SEARCH_SOURCE_SNAPSHOT,
    payload: searchForm
});
export const clearSearchSourceSnapshot = () => ({
    type: CLEAR_SEARCH_SOURCE_SNAPSHOT
});
export const setSearchSourceSnapshotForm = (searchSourceSnapshotForm) => ({
    type: SET_SEARCH_SOURCE_SNAPSHOT_FORM,
    payload: {
        searchSourceSnapshotForm
    }
});
export const searchSoruceSnapshotFormInit = () => {
    const offset = new Date().getTimezoneOffset() * 60000;
    const today = new Date(Date.now() - offset);
    const offset2 = 31 * 24 * 60 * 60 * 1000;
    const beforeMonth = new Date(Date.now() - offset - offset2);
    return {
        fromDate: beforeMonth.toISOString().slice(0, -1).slice(0, 10),
        toDate: today.toISOString().slice(0, -1).slice(0, 10),
        page: 1,
        size: 10
    };
};

const DELETE_SOURCE_SNAPTHOT = `${PREFIX}/DELETE_SOURCE_SNAPTHOT`;
const DELETE_SOURCE_SNAPTHOT_SUCCESS = `${PREFIX}/DELETE_SOURCE_SNAPTHOT_SUCCESS`;
const DELETE_SOURCE_SNAPTHOT_ERROR = `${PREFIX}/DELETE_SOURCE_SNAPTHOT_ERROR`;
const CLEAR_DELETE_SOURCE_SNAPTHOT = `${PREFIX}/CLEAR_DELETE_SOURCE_SNAPTHOT`;
export const deleteSourceSnapshot = (id) => ({
    type: DELETE_SOURCE_SNAPTHOT,
    payload: {
        id
    }
});
export const clearDeleteSourceSnapshot = () => ({
    type: CLEAR_DELETE_SOURCE_SNAPTHOT
});

const SEARCH_SOURCES = `${PREFIX}/SEARCH_SOURCES`;
const SEARCH_SOURCES_SUCCESS = `${PREFIX}/SEARCH_SOURCES_SUCCESS`;
const SEARCH_SOURCES_ERROR = `${PREFIX}/SEARCH_SOURCES_ERROR`;
const CLEAR_SEARCH_SOURCES = `${PREFIX}/CLEAR_SEARCH_SOURCES`;
const SET_SEARCH_SOURCES_FORM = `${PREFIX}/SET_SEARCH_SOURCES_FORM`; // search data setting
export const searchSources = (searchForm) => ({
    type: SEARCH_SOURCES,
    payload: searchForm
});
export const clearSearchSources = () => ({
    type: CLEAR_SEARCH_SOURCES
});
export const setSearchSourcesForm = (searchSourcesForm) => ({
    type: SET_SEARCH_SOURCES_FORM,
    payload: {
        searchSourcesForm
    }
});
export const searchSorucesFormInit = () => {
    return {
        sourceSnapshotId: '',
        sourceType: '',
        sourceName: '',
        page: 1,
        size: 10
    };
};

export function* sourceSaga() {
    yield takeEvery(
        ANALYSIS_SOURCE,
        createPromiseSaga(ANALYSIS_SOURCE, sourceApi.analysisSource)
    );
    yield takeLatest(
        SEARCH_SOURCE_SNAPSHOT,
        createPromiseSaga(
            SEARCH_SOURCE_SNAPSHOT,
            sourceApi.searchSourceSnapshot
        )
    );
    yield takeEvery(
        DELETE_SOURCE_SNAPTHOT,
        createPromiseSaga(
            DELETE_SOURCE_SNAPTHOT,
            sourceApi.deleteSourceSnapshot
        )
    );
    yield takeLatest(
        SEARCH_SOURCES,
        createPromiseSaga(SEARCH_SOURCES, sourceApi.searchSources)
    );
}

const initialState = {
    usedSourceAnalysisModalInitData: {
        showModal: false,
        initData: {}
    },
    analysisSourceRes: reducerUtils.initial(),
    searchSourceSnapshotForm: searchSoruceSnapshotFormInit(),
    searchSourceSnapshotRes: reducerUtils.initial(),
    deleteSourceSnapshotRes: reducerUtils.initial(),
    searchSourcesForm: searchSorucesFormInit(),
    searchSourcesRes: reducerUtils.initial()
};

export default function source(state = initialState, action) {
    switch (action.type) {
        case USE_SOURCE_ANALYSIS_MODAL:
            return {
                ...state,
                usedSourceAnalysisModalInitData: {
                    showModal: action.payload.showModal,
                    initData: action.payload.initData
                }
            };
        case CLEAR_SOURCE_ANALYSIS_MODAL:
            return {
                ...state,
                usedSourceAnalysisModalInitData: {
                    showModal: false,
                    initData: {}
                }
            };

        case ANALYSIS_SOURCE:
        case ANALYSIS_SOURCE_SUCCESS:
        case ANALYSIS_SOURCE_ERROR:
            return handleAsyncActions(
                ANALYSIS_SOURCE,
                'analysisSourceRes',
                true
            )(state, action);
        case CLEAR_ANALYSIS_SOURCE:
            return {
                ...state,
                analysisSourceRes: reducerUtils.initial()
            };

        case SEARCH_SOURCE_SNAPSHOT:
        case SEARCH_SOURCE_SNAPSHOT_SUCCESS:
        case SEARCH_SOURCE_SNAPSHOT_ERROR:
            return handleAsyncActions(
                SEARCH_SOURCE_SNAPSHOT,
                'searchSourceSnapshotRes',
                true
            )(state, action);

        case CLEAR_SEARCH_SOURCE_SNAPSHOT:
            return {
                ...state,
                searchSourceSnapshotRes: reducerUtils.initial()
            };

        case SET_SEARCH_SOURCE_SNAPSHOT_FORM:
            return {
                ...state,
                searchSourceSnapshotForm: {
                    ...action.payload.searchSourceSnapshotForm
                }
            };

        case DELETE_SOURCE_SNAPTHOT:
        case DELETE_SOURCE_SNAPTHOT_SUCCESS:
        case DELETE_SOURCE_SNAPTHOT_ERROR:
            return handleAsyncActions(
                DELETE_SOURCE_SNAPTHOT,
                'deleteSourceSnapshotRes',
                true
            )(state, action);
        case CLEAR_DELETE_SOURCE_SNAPTHOT:
            return {
                ...state,
                deleteSourceSnapshotRes: reducerUtils.initial()
            };

        case SEARCH_SOURCES:
        case SEARCH_SOURCES_SUCCESS:
        case SEARCH_SOURCES_ERROR:
            return handleAsyncActions(
                SEARCH_SOURCES,
                'searchSourcesRes',
                true
            )(state, action);
        case CLEAR_SEARCH_SOURCES:
            return {
                ...state,
                searchSourcesRes: reducerUtils.initial()
            };
        case SET_SEARCH_SOURCES_FORM:
            return {
                ...state,
                searchSourcesForm: {
                    ...action.payload.searchSourcesForm
                }
            };
        default:
            return state;
    }
}
