import React, {useEffect} from 'react';
import {useHistory, useLocation} from 'react-router-dom';
import {useSelector, useDispatch} from 'react-redux';
import ScHistorySearch from '@pages/scHistory/ScHistorySearch';
import {toast} from 'react-toastify';
import {
    setSearchFormH,
    searchScenarioHistory,
    clearSearchScenarioHistory,
    getInitSearchFormH
} from '../../store/scenario';
import Spinner from '../../components/spinner/Spinner';
import {getErrorMsg} from '../../lib/commonUiUtils';

/*
 *     container
 */
function ScHistorySearchContainer() {
    const dispatch = useDispatch();
    const history = useHistory();
    const location = useLocation();

    const {searchFormH} = useSelector((state) => state.scenario);
    const {data: codeData} = useSelector((state) => state.common.searchCode);
    const {loading: searchLoading, error: searchError} = useSelector(
        (state) => state.scenario.searchH
    );

    const onChangeForm = (searchForm) => {
        dispatch(setSearchFormH({...searchForm}));
    };

    // search scenario test history (for scHistory.js)
    const onSearch = (_searchFormH = searchFormH) => {
        dispatch(searchScenarioHistory(_searchFormH));
    };
    //    
    const {data: memberInfoData} = useSelector(
        (state) => state.auth.memberInfo
    );

    //    
    // const {data: memberInfoData} = useSelector(
    //     (state) => state.auth.memberInfo
    // );

    useEffect(() => {
        if (history.action === 'PUSH') {
            const tempSearchFormH = getInitSearchFormH();
            if (location.state != null) {
                tempSearchFormH.clubSnapshotId = location.state.clubSnapshotId;
            } else {
                if (memberInfoData.data?.userId) {
                    tempSearchFormH.manager = memberInfoData.data?.userId;
                }
                tempSearchFormH.clubSnapshotId = -1;
            }
            onChangeForm(tempSearchFormH);
            onSearch(tempSearchFormH);
        }
        if (history.action === 'POP') {
            onSearch();
        }
    }, []);

    //   
    useEffect(() => {
        if (!searchLoading && searchError) {
            toast.error(getErrorMsg(searchError, 'search'));
            dispatch(clearSearchScenarioHistory());
        }
    }, [searchError]);

    return (
        <>
            <ScHistorySearch
                searchFormH={searchFormH}
                onChangeForm={onChangeForm}
                onClickSearch={onSearch}
                codeData={codeData}
                memberInfoData={memberInfoData}
            />
            <Spinner isLoading={searchLoading} />
        </>
    );
    ///
}

export default ScHistorySearchContainer;
