/* eslint-disable import/prefer-default-export */
import axios from 'axios';

/**
 * dashboard Same-day test results (searchDayTestResult, TR)
 * searchDayTestResult (SEARCH_DAY_TESTRESULT) : 
 */
export const searchDayTestResult = async ({searchDate}) => {
    const response = await axios.get(`/dashboards/${searchDate}/summarize`);
    return response;
};

/**
 * dashboard Number of tests per server (searchServerTestCnt, TR)
 * searchServerTestCnt (SEARCH_SERVER_TESTCNT) : 
 */
export const searchServerTestCnt = async ({searchDate}) => {
    const response = await axios.get(`/dashboards/${searchDate}/daily`);
    return response;
};

/**
 * Server test Fail rate (searchFailRate, TR)
 * searchServerTestCnt (SEARCH_SERVER_TESTCNT) : 
 */
export const searchFailRate = async ({searchDate, searchServer}) => {
    const response = await axios.get(
        `/dashboards/${searchDate}/${searchServer}/rate`
    );
    return response;
};
