/* eslint-disable no-unused-vars */
import React from 'react';
import {FaEdit, FaTrash, FaCheck} from 'react-icons/fa';
import {useTranslation} from 'react-i18next';
import Table from '../../components/table/Table';
import Button from '../../components/button/Button';
import {changeToDate} from '../../lib/moment';

const MemberTable = ({data, showUpdateFormModal, movePage, onDelete}) => {
    const [t] = useTranslation();
    const tableName = t('pages.member.userList');
    const rowKey = 'userId';

    const onDoubleClick = (id, row) => {
        showUpdateFormModal(row);
    };
    const columns = [
        {
            title: '',
            key: 'role',
            align: 'left',
            width: '5%',
            render: (id, row) => {
                if (row.role === 'ADMIN') {
                    return (
                        <Button
                            theme="primary"
                            style={{
                                minWidth: '80px',
                                maxHeight: 'calc(3.5rem + 2px)'
                            }}
                        >
                            Admin
                        </Button>
                    );
                }
                return (
                    <Button
                        theme="warning"
                        style={{
                            minWidth: '80px',
                            maxHeight: 'calc(3.5rem + 2px)'
                        }}
                    >
                        User
                    </Button>
                );
            }
        },
        {
            title: t('common.text.id'),
            key: 'userId',
            width: '20%'
            // align: 'left' // left, center, right (default:center)
        },
        {
            title: t('pages.member.userName'),
            key: 'name',
            width: '20%'
            // align: 'left' // left, center, right (default:center)
        },
        {
            title: 'Mobile',
            key: 'mobile',
            width: '20%',
            render: (id, row) => {
                return row.contactInfo?.mobile;
            }
            // align: 'left' // left, center, right (default:center)
        },
        {
            title: 'Email',
            key: 'email',
            width: '20%',
            render: (id, row) => {
                return row.contactInfo?.email;
            }
        },
        {
            title: ' ',
            width: '8%',
            key: 'modifiedDate',
            // eslint-disable-next-line no-unused-vars
            render: (id, row, column) => {
                return changeToDate(row.modifiedDate);
            }
        },
        {
            title: '',
            key: 'button',
            align: 'right',
            render: (id, row) => {
                return (
                    <>
                        <Button
                            theme="outline-success"
                            onClick={() => showUpdateFormModal(row)}
                        >
                            <FaEdit />
                        </Button>
                        <Button
                            theme="outline-danger"
                            onClick={() => onDelete(id)}
                        >
                            <FaTrash />
                        </Button>
                    </>
                );
            },
            width: '35%'
        }
    ];

    return (
        <>
            <Table
                tableName={tableName}
                onDoubleClick={onDoubleClick}
                movePage={movePage}
                rowKey={rowKey}
                columns={columns}
                data={data}
            />
        </>
    );
};

export default MemberTable;
