/* eslint-disable indent */
import {takeEvery, takeLatest} from 'redux-saga/effects';
import * as logApi from '../api/log'; // api/auth    
import {
    reducerUtils,
    handleAsyncActions,
    createPromiseSaga
} from '../lib/asyncUtils';

const PREFIX = 'LOG';

/*   */
const SEARCH_LOG_SNAPSHOT = `${PREFIX}/SEARCH_LOG_SNAPSHOT`;
const SEARCH_LOG_SNAPSHOT_SUCCESS = `${PREFIX}/SEARCH_LOG_SNAPSHOT_SUCCESS`;
const SEARCH_LOG_SNAPSHOT_ERROR = `${PREFIX}/SEARCH_LOG_SNAPSHOT_ERROR`;
const CLEAR_SEARCH_LOG_SNAPSHOT = `${PREFIX}/CLEAR_SEARCH_LOG_SNAPSHOT`;
const SET_SEARCH_LOG_SNAPSHOT_FORM = 'random/SET_SEARCH_LOG_SNAPSHOT_FORM'; // search data setting

export const searchLogSnapshot = (searchForm) => ({
    type: SEARCH_LOG_SNAPSHOT,
    payload: searchForm
});
export const clearSearchRandom = () => ({
    type: CLEAR_SEARCH_LOG_SNAPSHOT
});

export const setSearchLogSnapshotForm = (searchForm) => ({
    type: SET_SEARCH_LOG_SNAPSHOT_FORM,
    payload: {
        searchForm
    }
});

export const initSearchLogSnapshotForm = () => {
    const offset = new Date().getTimezoneOffset() * 60000;
    const today = new Date(Date.now() - offset + 60 * 60 * 1000);
    const offset2 = 31 * 24 * 60 * 60 * 1000;
    const sevenDay = new Date(Date.now() - offset - offset2);
    return {
        fromDate: sevenDay.toISOString().slice(0, -1).slice(0, 10),
        toDate: today.toISOString().slice(0, -1).slice(0, 10),
        collectionMethod: '',
        page: 1,
        size: 10
    };
};

/**
 * log analysis popup open signal
 * USE_LOG_ANALYSIS_MODAL :      
 * CLEAR_LOG_ANALYSIS_MODAL :      Clear
 */
const USE_LOG_ANALYSIS_MODAL = `${PREFIX}/USE_LOG_ANALYSIS_MODAL`; //    
const CLEAR_LOG_ANALYSIS_MODAL = `${PREFIX}/CLEAR_LOG_ANALYSIS_MODAL`; //      Clear

export const usedLogAnalysisModal = (showModal) => ({
    type: USE_LOG_ANALYSIS_MODAL,
    payload: {
        showModal
    }
});

export const clearLogAnalysisModal = () => ({
    type: CLEAR_LOG_ANALYSIS_MODAL
});

/**
 * Log Analysis tool Start
 * EXECUTE_LOG_COLLECTOR :          
 * CLEAR_EXECUTE_LOG_COLLECTOR :     Clear
 */
const EXECUTE_LOG_COLLECTOR = `${PREFIX}/EXECUTE_LOG_COLLECTOR`; //    
const EXECUTE_LOG_COLLECTOR_SUCCESS = `${PREFIX}/EXECUTE_LOG_COLLECTOR_SUCCESS`; //    
const EXECUTE_LOG_COLLECTOR_ERROR = `${PREFIX}/EXECUTE_LOG_COLLECTOR_ERROR`; //    
const CLEAR_EXECUTE_LOG_COLLECTOR = `${PREFIX}/CLEAR_EXECUTE_LOG_COLLECTOR`; //    

export const executeLogColletor = (values) => ({
    type: EXECUTE_LOG_COLLECTOR,
    payload: values
});

export const clearExecuteLogColletor = () => ({
    type: CLEAR_EXECUTE_LOG_COLLECTOR
});

/**
 *    
 * DELETE_LOG_SNAPSHOT :          
 * CLEAR_DELETE_LOG_SNAPSHOT :     Clear
 */
const DELETE_LOG_SNAPSHOT = `${PREFIX}/DELETE_LOG_SNAPSHOT`;
const DELETE_LOG_SNAPSHOT_SUCCESS = `${PREFIX}/DELETE_LOG_SNAPSHOT_SUCCESS`;
const DELETE_LOG_SNAPSHOT_ERROR = `${PREFIX}/DELETE_LOG_SNAPSHOT_ERROR`;
const CLEAR_DELETE_LOG_SNAPSHOT = `${PREFIX}/CLEAR_DELETE_LOG_SNAPSHOT`;

export const deleteLogSnapshot = (id) => ({
    type: DELETE_LOG_SNAPSHOT,
    payload: {
        id
    }
});

export const clearDeleteLogSnapshot = () => ({
    type: CLEAR_DELETE_LOG_SNAPSHOT
});

/*   */
const SEARCH_LOGS = `${PREFIX}/SEARCH_LOGS`;
const SEARCH_LOGS_SUCCESS = `${PREFIX}/SEARCH_LOGS_SUCCESS`;
const SEARCH_LOGS_ERROR = `${PREFIX}/SEARCH_LOGS_ERROR`;
const CLEAR_SEARCH_LOGS = `${PREFIX}/CLEAR_SEARCH_LOGS`;
const SET_SEARCH_LOGS_FORM = `${PREFIX}/SET_SEARCH_LOGS_FORM`; // search data setting

// export const searchLogSnapshot = (searchForm) => ({
//     type: SEARCH_LOG_SNAPSHOT,
//     payload: searchForm
// });

export const searchLogs = (searchForm) => ({
    type: SEARCH_LOGS,
    payload: searchForm
});
export const clearSearchLogs = () => ({
    type: CLEAR_SEARCH_LOGS
});

export const setSearchLogsForm = (searchForm) => ({
    type: SET_SEARCH_LOGS_FORM,
    payload: {
        searchForm
    }
});

export const initSearchLogsForm = () => {
    return {
        logSnapshotId: -1,
        serviceId: '',
        page: 1,
        size: 10
    };
};

/*   */
const SEARCH_LOG = `${PREFIX}/SEARCH_LOG`;
const SEARCH_LOG_SUCCESS = `${PREFIX}/SEARCH_LOG_SUCCESS`;
const SEARCH_LOG_ERROR = `${PREFIX}/SEARCH_LOG_ERROR`;
const CLEAR_SEARCH_LOG = `${PREFIX}/CLEAR_SEARCH_LOG`;

// export const searchLogSnapshot = (searchForm) => ({
//     type: SEARCH_LOG_SNAPSHOT,
//     payload: searchForm
// });
export const searchLog = (logId) => ({
    type: SEARCH_LOG,
    payload: {logId}
});

export const clearSearchLog = () => ({
    type: CLEAR_SEARCH_LOG
});

/**
 * Redux-saga 
 */
export function* logSaga() {
    yield takeLatest(
        SEARCH_LOG_SNAPSHOT,
        createPromiseSaga(SEARCH_LOG_SNAPSHOT, logApi.searchLogSnapshot)
    );
    yield takeLatest(
        SEARCH_LOGS,
        createPromiseSaga(SEARCH_LOGS, logApi.searchLogs)
    );
    yield takeLatest(
        SEARCH_LOG,
        createPromiseSaga(SEARCH_LOG, logApi.searchLog)
    );
    yield takeEvery(
        EXECUTE_LOG_COLLECTOR,
        createPromiseSaga(EXECUTE_LOG_COLLECTOR, logApi.executeLogCollector)
    );
    yield takeEvery(
        DELETE_LOG_SNAPSHOT,
        createPromiseSaga(DELETE_LOG_SNAPSHOT, logApi.deleteLogSnapshot)
    );
}

/**
 * State 
 */
const initialState = {
    searchLogSnapshotForm: initSearchLogSnapshotForm(),
    searchLogSnapshotRes: reducerUtils.initial(),
    usedLogAnalysisModalInitData: {
        showModal: false,
        initData: {}
    },
    // Log  
    executeLogColletorRes: reducerUtils.initial(),
    deleteLogSnapshotRes: reducerUtils.initial(),
    searchLogsForm: initSearchLogsForm(),
    searchLogsRes: reducerUtils.initial(),
    searchLogRes: reducerUtils.initial()
};

export default function scenario(state = initialState, action) {
    switch (action.type) {
        case SEARCH_LOG_SNAPSHOT:
        case SEARCH_LOG_SNAPSHOT_SUCCESS:
        case SEARCH_LOG_SNAPSHOT_ERROR:
            return handleAsyncActions(
                SEARCH_LOG_SNAPSHOT,
                'searchLogSnapshotRes',
                true
            )(state, action);

        case CLEAR_SEARCH_LOG_SNAPSHOT:
            return {
                ...state,
                searchLogSnapshotRes: reducerUtils.initial()
            };

        case SET_SEARCH_LOG_SNAPSHOT_FORM:
            return {
                ...state,
                searchLogSnapshotForm: {
                    ...action.payload.searchForm
                }
            };

        /**
         * log analysis popup open signal
         * USE_LOG_ANALYSIS_MODAL :    
         * CLEAR_LOG_ANALYSIS_MODAL :     Clear
         */
        case USE_LOG_ANALYSIS_MODAL:
            return {
                ...state,
                usedLogAnalysisModalInitData: {
                    showModal: action.payload.showModal,
                    initData: action.payload.initData
                }
            };

        case CLEAR_LOG_ANALYSIS_MODAL:
            return {
                ...state,
                usedLogAnalysisModalInitData: {
                    showModal: false,
                    initData: {}
                }
            };

        /**
         * Log Analysis tool Start
         * EXECUTE_LOG_COLLECTOR :          
         * CLEAR_EXECUTE_LOG_COLLECTOR :     Clear
         */
        case EXECUTE_LOG_COLLECTOR:
        case EXECUTE_LOG_COLLECTOR_SUCCESS:
        case EXECUTE_LOG_COLLECTOR_ERROR:
            return handleAsyncActions(
                EXECUTE_LOG_COLLECTOR,
                'executeLogColletorRes',
                true
            )(state, action);
        case CLEAR_EXECUTE_LOG_COLLECTOR:
            return {
                ...state,
                executeLogColletorRes: reducerUtils.initial()
            };

        /**
         *    
         * DELETE_LOG_SNAPSHOT :          
         * CLEAR_DELETE_LOG_SNAPSHOT :     Clear
         */
        case DELETE_LOG_SNAPSHOT:
        case DELETE_LOG_SNAPSHOT_SUCCESS:
        case DELETE_LOG_SNAPSHOT_ERROR:
            return handleAsyncActions(
                DELETE_LOG_SNAPSHOT,
                'deleteLogSnapshotRes',
                true
            )(state, action);
        case CLEAR_DELETE_LOG_SNAPSHOT:
            return {
                ...state,
                deleteLogSnapshotRes: reducerUtils.initial()
            };

        case SEARCH_LOGS:
        case SEARCH_LOGS_SUCCESS:
        case SEARCH_LOGS_ERROR:
            return handleAsyncActions(
                SEARCH_LOGS,
                'searchLogsRes',
                true
            )(state, action);

        case CLEAR_SEARCH_LOGS:
            return {
                ...state,
                searchLogsRes: reducerUtils.initial()
            };

        case SET_SEARCH_LOGS_FORM:
            return {
                ...state,
                searchLogsForm: {
                    ...action.payload.searchForm
                }
            };

        case SEARCH_LOG:
        case SEARCH_LOG_SUCCESS:
        case SEARCH_LOG_ERROR:
            return handleAsyncActions(
                SEARCH_LOG,
                'searchLogRes',
                true
            )(state, action);

        case CLEAR_SEARCH_LOG:
            return {
                ...state,
                searchLogRes: reducerUtils.initial()
            };
        default:
            return state;
    }
}
