/* eslint-disable no-shadow */
/* eslint-disable no-unused-vars */
import React from 'react';
import {
    Form,
    Modal,
    Row,
    Col,
    InputGroup,
    FormControl,
    FloatingLabel,
    Accordion
} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
import {useFormik} from 'formik';
import {FaArrowRight} from 'react-icons/fa';
import Button from '../../../components/button/Button';
import Table from '../../../components/table/Table';
import {jsonStrFormatNotPretty, validJsonString} from '../../../lib/jsonUtils';
import {renderHttpMethod} from '../../../lib/commonUiUtils';

const UitestStepPopXhr1 = ({
    scenarioData,
    selectedCaseId,
    setDetailCaseList,
    handleClose,
    tempkey,
    setTempKeyRezen,
    searchLayoutData,
    layoutDetail,
    layoutName,
    setLayoutName,
    onClickSearch,
    moveLayoutDetail,
    movePage,
    handleInputChange
}) => {
    const [t] = useTranslation();

    const onChangerLayoutName = (e) => {
        setLayoutName(e.target.value);
    };

    // Enter   ->  
    const onCheckEnter = (e) => {
        if (e.key === 'Enter') {
            e.preventDefault();
            onClickSearch();
        }
    };

    const formik = useFormik({
        initialValues: {
            isSkipError: layoutDetail?.isSkipError ?? false,
            urlPath: layoutDetail ? layoutDetail.urlPath : '',
            fnCode: layoutDetail ? layoutDetail.fnCode : 'NONE'
        },

        onSubmit: (values) => {
            const cases = [...scenarioData.data.cases];

            const caseLoop = cases.map((cUnit) => {
                const theCase = cUnit;

                if (cUnit.caseId === selectedCaseId) {
                    let data = theCase.steps;
                    if (data == null) {
                        data = [];
                    }

                    const newStep = {
                        stepId: tempkey,
                        isSkipError: values.isSkipError,
                        stepType: 'API',
                        layoutId: layoutDetail.layoutId,
                        inputData: jsonStrFormatNotPretty(
                            layoutDetail.inputData
                        ),
                        stepName: layoutDetail.serviceInfo.serviceId,
                        httpMethod: layoutDetail.urlInfo?.httpMethod
                        // inputData2: '',
                    };
                    setTempKeyRezen();

                    data.push(newStep);

                    const aLoop = data.map((unit, index) => {
                        const temp = unit;
                        temp.idx = index;
                        return temp;
                    });

                    theCase.steps = aLoop;
                }
                return theCase;
            });
            setDetailCaseList(caseLoop);
            handleClose();
        }
    });

    const rowKey = 'id';
    const columns = [
        {
            title: t('pages.layout.interface'),
            key: 'interface',
            render: (id, row) => {
                return `(${row.urlInfo?.httpMethod}) ${row.serviceInfo?.serviceId} `;
            },
            align: 'left'
        },
        {
            title: '',
            key: 'button',
            align: 'right',
            // eslint-disable-next-line no-unused-vars
            render: (id, row) => {
                return (
                    <>
                        <Button
                            theme="outline-primary"
                            onClick={() => moveLayoutDetail(id)}
                        >
                            <FaArrowRight />
                        </Button>
                    </>
                );
            }
        }
    ];

    const onDoubleClick = (id, row) => {
        moveLayoutDetail(id);
    };

    const renderSaveButton = () => {
        if (!layoutDetail) return '';
        return (
            <Button theme="primary" type="submit">
                {t('common.button.register')}
            </Button>
        );
    };
    const renderInputParsingResult = () => {
        const isValid = validJsonString(layoutDetail.inputData);
        if (isValid) {
            return (
                <Form.Label style={{float: 'right'}}>
                    {t('common.text.validJsonFormat')}
                </Form.Label>
            );
        }
        return (
            <Form.Label style={{float: 'right', color: 'red'}}>
                {t('common.text.invalidJsonFormat')}
            </Form.Label>
        );
    };

    const renderStepDetail = () => {
        if (!layoutDetail) return '';

        return (
            <>
                <Accordion defaultActiveKey="0">
                    <Accordion.Item eventKey="0">
                        <Accordion.Header className="accordion-header3">
                            {t('pages.scenario.infoXhr')}
                        </Accordion.Header>
                        <Accordion.Body>
                            <Row>
                                <Col xs="12" className="mb-3">
                                    <Form.Group controlId="urlPath">
                                        <InputGroup>
                                            {renderHttpMethod(
                                                layoutDetail?.urlInfo
                                                    ?.httpMethod
                                            )}
                                            <FloatingLabel
                                                label={t(
                                                    'pages.scenario.urlPath'
                                                )}
                                            >
                                                <FormControl
                                                    disabled
                                                    value={
                                                        layoutDetail?.urlInfo
                                                            ?.urlPath
                                                    }
                                                />
                                            </FloatingLabel>
                                        </InputGroup>
                                    </Form.Group>
                                </Col>
                                <Col xs="12">
                                    <div key="isSkipError" className="mb-3">
                                        <Form.Check
                                            type="checkbox"
                                            id="isSkipError"
                                            label={t(
                                                'pages.scenario.isSkipError'
                                            )}
                                            {...formik.getFieldProps(
                                                'isSkipError'
                                            )}
                                            checked={formik.values.isSkipError}
                                        />
                                    </div>
                                </Col>
                                <Col xs="12">
                                    <Form.Group controlId="inputData">
                                        <Form.Label>Reqeust Body</Form.Label>
                                        {renderInputParsingResult()}
                                        <Form.Control
                                            as="textarea"
                                            style={{height: '400px'}}
                                            placeholder=""
                                            onChange={handleInputChange}
                                            value={
                                                layoutDetail &&
                                                layoutDetail.inputData
                                            }
                                        />
                                        {/* {printFormError(formik, 'input')} */}
                                    </Form.Group>
                                </Col>
                            </Row>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
            </>
        );
    };
    return (
        <>
            <Form onSubmit={formik.handleSubmit} onKeyPress={onCheckEnter}>
                <Row>
                    <Col xs="6">
                        <Accordion defaultActiveKey="0">
                            <Accordion.Item eventKey="0">
                                <Accordion.Header className="accordion-header2">
                                    {t('pages.scenario.infoXhr')}
                                </Accordion.Header>
                                <Accordion.Body>
                                    <Row>
                                        <Col xs="7">
                                            <input
                                                type="text"
                                                className="form-control"
                                                placeholder="Interface Name or Id"
                                                onChange={onChangerLayoutName}
                                                value={layoutName}
                                                maxLength="50"
                                            />
                                        </Col>
                                        <Col xs="3" />
                                        <Col xs="2">
                                            <Button
                                                type="button"
                                                className="btn btn-block btn-secondary btn-search"
                                                // isLoading={searchLoading}
                                                onClick={onClickSearch}
                                            >
                                                {t('common.button.search')}
                                            </Button>
                                        </Col>
                                    </Row>
                                    <br />
                                    <Row>
                                        <Col xs="12">
                                            <Table
                                                onDoubleClick={onDoubleClick}
                                                movePage={movePage}
                                                rowKey={rowKey}
                                                columns={columns}
                                                data={searchLayoutData}
                                                customTableHeader=""
                                                customTableHeaderRowCount={1}
                                                isCard="N"
                                            />
                                        </Col>
                                    </Row>
                                </Accordion.Body>
                            </Accordion.Item>
                        </Accordion>
                    </Col>
                    <Col xs="6">{renderStepDetail()}</Col>
                </Row>
                <Modal.Footer>
                    {renderSaveButton()}
                    <Button theme="default" onClick={handleClose}>
                        {t('common.button.close')}
                    </Button>
                </Modal.Footer>
            </Form>
        </>
    );
};

export default UitestStepPopXhr1;
