import axios from 'axios';

/**
 *   ID/PW  (TR)
 * searchCredentialInfo (SEARCH_CREDENTIAL_INFO) : 
 * clearSearchCredentialInfo (CLEAR_SEARCH_CREDENTIAL_INFO) : 
 */
export const searchCredentialInfo = async ({serverId, page = 1, size = 10}) => {
    const response = await axios.get(
        `/credentials?serverId=${serverId}&sort=id,desc&page=${
            page - 1
        }&size=${size}`
    );
    return response;
};

/**
 *   ID/PW  (TR)
 * createCredentialInfo (CREATE_CREDENTIAL_INFO) : 
 * clearCreateCredentialInfo (CLEAR_CREATE_CREDENTIAL_INFO) : 
 */
export const createCredentialInfo = async ({
    credentialName,
    serverId,
    connectionInfo
}) => {
    const response = await axios.post('/credentials', {
        credentialName,
        serverId,
        connectionInfo
    });
    return response;
};

/**
 *   ID/PW  (TR)
 * updateCredentialInfo (UPDATE_CREDENTIAL_INFO) : 
 * clearUpdateCredentialInfo (CLEAR_UPDATE_CREDENTIAL_INFO) : 
 */
export const updateCredentialInfo = async ({serverId, credentialId}) => {
    const response = await axios.patch(
        `/credentials/${serverId}/${credentialId}`,
        {}
    );
    return response;
};

/**
 *   ID/PW  (TR)
 * deleteCredentialInfo (DELETE_CREDENTIAL_INFO) : 
 * clearDeleteCredentialInfo (CLEAR_DELETE_CREDENTIAL_INFO) : 
 */
export const deleteCredentialInfo = async ({credentialId}) => {
    const response = await axios.delete(`/credentials/${credentialId}`, null);
    return response;
};
