import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import {useHistory} from 'react-router-dom';
import LayoutDetailHeader from '@pages/layout/LayoutDetailHeader';
import {toast} from 'react-toastify';
import {useTranslation} from 'react-i18next';
import {
    setSelectedLayout,
    updateLayout,
    clearUpdateLayout,
    combineRequestValue,
    clearCombineRequestValue
} from '../../store/layout';
import {usedBulktestModal} from '../../store/bulk';
import {usedMultiTestModal} from '../../store/common';
import Spinner from '../../components/spinner/Spinner';
import {convertCollListToJson} from '../../lib/jsonUtils';
import {getErrorMsg} from '../../lib/commonUiUtils';

/*
 *    Container
 */
function LayoutDetailHeaderContainer() {
    const history = useHistory();
    const dispatch = useDispatch();
    const [t] = useTranslation();

    const {selectedLayout} = useSelector((state) => state.layout);

    //   
    const {data: codeData} = useSelector((state) => state.common.searchCode);

    const combineRequestValueAndShowTestModal = () => {
        if (!selectedLayout) {
            return;
        }

        //    
        dispatch(
            combineRequestValue(
                selectedLayout.layoutId,
                selectedLayout.cols?.filter(
                    (obj) =>
                        obj.inOut === 'IN' &&
                        obj.crud !== 'D' &&
                        (obj.colAttribute?.pyhsicalName !== '' ||
                            obj.colAttribute?.dataType === 'root list')
                )
            )
        );
    };

    /*
     * onShowUnittestModal ->    
     */
    const onShowUnittestModal = () => {
        combineRequestValueAndShowTestModal();
    };

    /*
     *   
     */
    const onShowMultitestModal = () => {
        const initData = {
            testType: 'LAYOUT',
            layoutId: selectedLayout.id,
            layoutName: selectedLayout.serviceName,
            testCount: 0,
            colList: selectedLayout.colList.filter(
                (obj) =>
                    obj.ioType === 'in' &&
                    obj.crud !== 'D' &&
                    obj.pyhsicalName !== ''
            )
        };
        dispatch(usedBulktestModal(true, initData));
    };

    /**
     *   .
     * Service ID   .
     * @returns
     */
    const moveHistory = () => {
        if (!selectedLayout) {
            return;
        }

        history.push({
            pathname: '/history',
            state: {
                serviceId: selectedLayout.serviceId
            }
        });
    };

    /*
     * select layout data change
     */
    const saveSelectedLayout = (preSelectedLayout) => {
        dispatch(setSelectedLayout(preSelectedLayout));
    };

    /*
     * layout update
     */
    const saveLayout = () => {
        dispatch(updateLayout(selectedLayout));
    };

    const {
        loading: updateLoading,
        error: updateError,
        data: updateResponse
    } = useSelector((state) => state.layout.update);

    useEffect(() => {
        if (!updateLoading && updateResponse) {
            if (updateResponse) {
                toast.success(t('common.alert.updateSuccess'));
                dispatch(clearUpdateLayout());
            }
        }
        // search fail
        if (!updateLoading && updateError) {
            toast.error(getErrorMsg(updateError, 'save'));
            dispatch(clearUpdateLayout());
        }
    }, [updateResponse, updateError]);

    const {
        loading: getRandomLoading,
        error: getRandomError,
        data: getRandomResponse
    } = useSelector((state) => state.layout.colListByRandVal);

    //   
    const {data: serverData} = useSelector(
        (state) => state.common.searchServer
    );

    useEffect(() => {
        if (!getRandomLoading && getRandomResponse) {
            const initData = {
                title: t('common.text.singleTranTest'),
                serverId: serverData?.data?.filter(
                    (obj) => obj.serverType === 'HTTP'
                )[0].serverId,
                layoutId: selectedLayout.layoutId,
                urlInfo: selectedLayout.urlInfo,
                serviceInfo: selectedLayout.serviceInfo,
                loginProcess: selectedLayout.loginProcess,
                httpResponseCode: 'None',
                stausCode: '',
                input: JSON.stringify(
                    convertCollListToJson(getRandomResponse.data?.cols)
                ),
                output: '{\n}',
                startTime: '',
                executeSecond: ''
            };
            dispatch(usedMultiTestModal(true, initData, null));
            dispatch(clearCombineRequestValue());
        }
        if (!getRandomLoading && getRandomError) {
            toast.error(
                getErrorMsg(getRandomError, t('common.alert.failTestData'))
            );
            dispatch(clearCombineRequestValue());
        }
    }, [getRandomResponse, getRandomError]);

    // clearCombineRequestValue
    return (
        <>
            <LayoutDetailHeader
                selectedLayout={selectedLayout}
                showUnittestModal={onShowUnittestModal}
                showMultitestModal={onShowMultitestModal}
                moveHistory={moveHistory}
                saveSelectedLayout={saveSelectedLayout}
                saveLayout={saveLayout}
                codeData={codeData}
            />
            <Spinner isLoading={updateLoading || getRandomLoading} />
        </>
    );
    ///
}

export default LayoutDetailHeaderContainer;
