/* eslint-disable no-unused-vars */
import React from 'react';
import {useTranslation} from 'react-i18next';
import {FaSearch, FaTrash} from 'react-icons/fa';
import Table from '../../components/table/Table';
import Button from '../../components/button/Button';
import {changeToDate} from '../../lib/moment';
import {renderRandomStyle} from '../../lib/buttonRender';

// Random table UI
const RandomTable = ({data, movePage, updateRandom, onDelete}) => {
    const [t] = useTranslation();

    const renderButton = (id, row) => {
        const isDisabled = row.user;

        return (
            <>
                <Button
                    theme="outline-success"
                    onClick={() => updateRandom(row)}
                >
                    <FaSearch />
                </Button>
                <Button
                    theme="outline-danger"
                    onClick={() => onDelete(id)}
                    disabled={!isDisabled}
                >
                    <FaTrash />
                </Button>
            </>
        );
    };
    const columns = [
        {
            title: t('pages.random.randCode'),
            key: 'randomStyle',
            width: '10%',
            align: 'center',
            render: (id, row) => {
                return renderRandomStyle(row.randomStyle);
            }
        },
        {
            title: t('pages.random.key'),
            key: 'randomKey',
            align: 'left'
        },
        {
            title: 'Desc',
            key: 'randomDesc',
            align: 'left'
        },
        {
            title: ' ',
            key: 'modifiedDate',
            width: '8%',
            render: (id, row) => {
                return changeToDate(row.modifiedDate);
            }
        },
        {
            title: '',
            key: 'button',
            align: 'right',
            width: '5%',
            // eslint-disable-next-line no-unused-vars
            render: (id, row, column) => {
                return <>{renderButton(id, row)}</>;
            }
        }
    ];

    const onDoubleClick = (id, row) => {
        updateRandom(row);
    };
    return (
        <>
            <Table
                tableName={t('pages.random.randList')}
                onDoubleClick={onDoubleClick}
                movePage={movePage}
                rowKey="randomKey"
                columns={columns}
                data={data}
            />
        </>
    );
};

export default RandomTable;
