/* eslint-disable no-unused-vars */
import React from 'react';
import {useSelector, useDispatch} from 'react-redux';
import UitestCaseInfo from '@pages/uitest/UitestCaseInfo';
import {setDetailCaseList} from '../../store/uitest';

// eslint-disable-next-line no-unused-vars
function UitestCaseInfoContainer() {
    const dispatch = useDispatch();
    const {data: scenarioData} = useSelector(
        (state) => state.uitest.scenarioInfo
    );

    const {caseId} = useSelector((state) => state.uitest.selectedCaseId);
    // eslint-disable-next-line no-debugger
    // const saveDetailCaseList = (caseList) => {
    //     dispatch(setDetailCaseList(caseList));
    // };

    const saveDetailCaseName = (caseName) => {
        const cases = [...scenarioData.data.cases];

        const aLoop = cases.map((unit) => {
            const temp = unit;
            if (temp.caseId === caseId) {
                temp.caseName = caseName;
            }
            // temp.idx = index;
            return temp;
        });
        dispatch(setDetailCaseList(aLoop));
    };
    const saveDetailCaseDesc = (caseDesc) => {
        const cases = [...scenarioData.data.cases];

        const aLoop = cases.map((unit) => {
            const temp = unit;
            if (temp.caseId === caseId) {
                temp.caseDesc = caseDesc;
            }
            // temp.idx = index;
            return temp;
        });
        dispatch(setDetailCaseList(aLoop));
    };
    return (
        <>
            <UitestCaseInfo
                scenarioData={scenarioData} // detail scenario data
                selectedCaseId={caseId} // list selected case id
                setDetailCaseName={saveDetailCaseName}
                setDetailCaseDesc={saveDetailCaseDesc}
            />
        </>
    );
}

export default UitestCaseInfoContainer;
