import React from 'react';
import {useSelector, useDispatch} from 'react-redux';
import CredentialInfo from '@pages/credentialInfo/CredentialInfo';

import {clearSelectedServer} from '../../store/credentialInfo';

function CredentialInfoContainer() {
    const dispatch = useDispatch();

    const {selectedServer} = useSelector((state) => state.credentialInfo);

    const handleClose = () => {
        dispatch(clearSelectedServer());
    };

    if (!selectedServer) {
        return <></>;
    }

    return (
        <>
            <CredentialInfo
                selectedServer={selectedServer}
                modalShow={selectedServer !== null}
                handleClose={handleClose}
            />
        </>
    );
    ///
}

export default CredentialInfoContainer;
