/**
 *       (TR)
 * renewalStat (RENEWAL_STAT_DATA) : 
 * clearRenewalStat (CLEAR_RENEWAL_STAT_DATA) : CLEAR
 */
const RENEWAL_STAT_DATA = `${PREFIX}/RENEWAL_STAT_DATA`;
const RENEWAL_STAT_DATA_SUCCESS = `${PREFIX}/RENEWAL_STAT_DATA_SUCCESS`;
const RENEWAL_STAT_DATA_ERROR = `${PREFIX}/RENEWAL_STAT_DATA_ERROR`;
const CLEAR_RENEWAL_STAT_DATA = `${PREFIX}/CLEAR_RENEWAL_STAT_DATA`;

export const renewalStat = (dDay) => ({
    type: RENEWAL_STAT_DATA,
    payload: {
        dDay
    }
});

export const clearRenewalStat = () => ({
    type: CLEAR_RENEWAL_STAT_DATA
});

/**
 * Redux-saga 
 */
export function* statSaga() {
    yield takeLatest(
        SEARCH_FORM_T1_CNT,
        createPromiseSaga(SEARCH_FORM_T1_CNT, statApi.searchStatOfNumber)
    );
    yield takeLatest(
        SEARCH_FORM_T1_RATE,
        createPromiseSaga(SEARCH_FORM_T1_RATE, statApi.searchStatOfRate)
    );
    yield takeLatest(
        SEARCH_FORM_T1_TIME,
        createPromiseSaga(SEARCH_FORM_T1_TIME, statApi.searchStatOfTime)
    );
    yield takeLatest(
        SEARCH_FORM_T2,
        createPromiseSaga(SEARCH_FORM_T2, statApi.searchStatT2)
    );
    yield takeEvery(
        RENEWAL_STAT_DATA,
        createPromiseSaga(RENEWAL_STAT_DATA, statApi.renewalStat)
    );
    yield takeLatest(
        SEARCH_DETAIL_STAT_BY_LAYOUT,
        createPromiseSaga(
            SEARCH_DETAIL_STAT_BY_LAYOUT,
            statApi.searchDetailStatByLayout
        )
    );
}

// initialState    initial()    .
export const initSearchFormT1 = () => {
    //  
    const offset = new Date().getTimezoneOffset() * 60000;

    // today -1
    // const offset2 = 1 * 24 * 60 * 60 * 1000;
    const today = new Date(Date.now() - offset);
    // today -8
    const offset3 = 7 * 24 * 60 * 60 * 1000;
    const fromDay = new Date(Date.now() - offset - offset3);

    return {
        serverId: '-1',
        fromDate: fromDay.toISOString().slice(0, -1).slice(0, 10),
        toDate: today.toISOString().slice(0, -1).slice(0, 10),
        modalDesc: '',
        serverIdOld: '-1',
        fromDateOld: fromDay.toISOString().slice(0, -1).slice(0, 10),
        toDateOld: today.toISOString().slice(0, -1).slice(0, 10),
        page: 1,
        size: 10
    };
};

// initialState    initial()    .
export const initSearchFormT2 = () => {
    //  
    const offset = new Date().getTimezoneOffset() * 60000;

    // today -1
    // const offset2 = 1 * 24 * 60 * 60 * 1000;
    const today = new Date(Date.now() - offset);
    // today -8
    const offset3 = 7 * 24 * 60 * 60 * 1000;
    const fromDay = new Date(Date.now() - offset - offset3);

    return {
        fromDate: fromDay.toISOString().slice(0, -1).slice(0, 10),
        toDate: today.toISOString().slice(0, -1).slice(0, 10),
        fromDateOld: fromDay.toISOString().slice(0, -1).slice(0, 10),
        toDateOld: today.toISOString().slice(0, -1).slice(0, 10),
        page: 1,
        size: 10
    };
};

/**
 * State 
 */
const initialState = {
    searchFormT1: initSearchFormT1(),
    searchFormT2: initSearchFormT2(),
    searchT1Cnt: reducerUtils.initial(),
    searchT1Rate: reducerUtils.initial(),
    searchT1Time: reducerUtils.initial(),
    // stat detail modal init data
    statDetailModalInitData: {
        showModal: false,
        initData: {}
    },
    searchT2: reducerUtils.initial(),
    // stat detail modal init data
    statDetailModalInitData2: {
        showModal: false,
        initData: {}
    },
    renewalStat: reducerUtils.initial(),
    searchDetailStatByLayoutRes: reducerUtils.initial()
};

export default function stat(state = initialState, action) {
    switch (action.type) {
        case SET_SEARCH_FORM_T1:
            return {
                ...state,
                searchFormT1: {
                    ...action.payload.searchFormT1
                }
            };

        case SEARCH_FORM_T1_CNT:
        case SEARCH_FORM_T1_CNT_SUCCESS:
        case SEARCH_FORM_T1_CNT_ERROR:
            return handleAsyncActions(
                SEARCH_FORM_T1_CNT,
                'searchT1Cnt',
                true
            )(state, action);

        case SEARCH_FORM_T1_RATE:
        case SEARCH_FORM_T1_RATE_SUCCESS:
        case SEARCH_FORM_T1_RATE_ERROR:
            return handleAsyncActions(
                SEARCH_FORM_T1_RATE,
                'searchT1Rate',
                true
            )(state, action);

        case SEARCH_FORM_T1_TIME:
        case SEARCH_FORM_T1_TIME_SUCCESS:
        case SEARCH_FORM_T1_TIME_ERROR:
            return handleAsyncActions(
                SEARCH_FORM_T1_TIME,
                'searchT1Time',
                true
            )(state, action);
        case USE_STAT_DETAIL_MODAL:
            return {
                ...state,
                statDetailModalInitData: {
                    showModal: action.payload.showModal,
                    initData: action.payload.initData
                }
            };
        case CLEAR_STAT_DETAIL_MODAL:
            return {
                ...state,
                statDetailModalInitData: {
                    showModal: false
                }
            };

        // T2 Tab
        case SET_SEARCH_FORM_T2:
            return {
                ...state,
                searchFormT2: {
                    ...action.payload.searchFormT2
                }
            };

        case SEARCH_FORM_T2:
        case SEARCH_FORM_T2_SUCCESS:
        case SEARCH_FORM_T2_ERROR:
            return handleAsyncActions(
                SEARCH_FORM_T2,
                'searchT2',
                true
            )(state, action);

        case USE_STAT_DETAIL_MODAL2:
            return {
                ...state,
                statDetailModalInitData2: {
                    showModal: action.payload.showModal,
                    initData: action.payload.initData
                }
            };
        case CLEAR_STAT_DETAIL_MODAL2:
            return {
                ...state,
                statDetailModalInitData2: {
                    showModal: false
                }
            };

        case RENEWAL_STAT_DATA:
        case RENEWAL_STAT_DATA_SUCCESS:
        case RENEWAL_STAT_DATA_ERROR:
            return handleAsyncActions(
                RENEWAL_STAT_DATA,
                'renewalStat',
                true
            )(state, action);
        case CLEAR_RENEWAL_STAT_DATA:
            return {
                ...state,
                renewalStat: reducerUtils.initial()
            };

        case SEARCH_DETAIL_STAT_BY_LAYOUT:
        case SEARCH_DETAIL_STAT_BY_LAYOUT_SUCCESS:
        case SEARCH_DETAIL_STAT_BY_LAYOUT_ERROR:
            return handleAsyncActions(
                SEARCH_DETAIL_STAT_BY_LAYOUT,
                'searchDetailStatByLayoutRes',
                true
            )(state, action);

        case CLEAR_SEARCH_DETAIL_STAT_BY_LAYOUT:
            return {
                ...state,
                searchDetailStatByLayoutRes: reducerUtils.initial()
            };
        default:
            return state;
    }
}
