import axios from 'axios';

/**
 * (TR)       
 * searchServerCommon (SEARCH_SERVER) : 
 */
export const searchServer = async () => {
    const response = await axios.get(`common/servers`);
    return response;
};

/**
 * (TR)       
 * searchServerCommon (SEARCH_SERVER) : 
 */
export const searchCode = async () => {
    const response = await axios.get(`common/enums`);
    return response;
};

/**
 * (TR)       
 * searchMember (SEARCH_MEMBER) : 
 */
export const searchMember = async () => {
    const response = await axios.get(`/common/search/member`);
    return response;
};

/**
 * (TR) DB JDBC    
 * checkDbConnection (CHECK_DB_CONNECTION) : 
 * checkDbConnectionClear (CLEAR_CHECK_DB_CONNECTION) : 
 */
export const checkDbConnection = async ({url, dbms, connectionInfo}) => {
    const response = await axios.post('/dbms/connect', {
        url,
        dbms,
        connectionInfo
    });
    return response;
};

/**
 * (TR) DB query   (executeSqlTest) EXECUTE_SQL_TEST
 * executeSqlTest (EXECUTE_SQL_TEST) : 
 * clearExecuteSqlTest (CLEAR_EXECUTE_SQL_TEST) : 
 */
export const executeSqlTest = async ({serverId, query}) => {
    const response = await axios.post('/dbms/execute', {
        serverId,
        query
    });
    return response;
};

/**
 * (TR)    (1 )
 * executeSingleTest (EXECUTE_SINGLE_TEST) : SHOW
 * clearExecuteSingleTest (CLEAR_EXECUTE_SINGLE_TEST) : CLEAR
 */
export const executeSingleTest = async ({
    layoutId,
    serverId,
    requestData,
    serverKey,
    tokenInfo
}) => {
    const response = await axios.post(
        `/layouts/${layoutId}/${serverId}/execute`,
        {
            requestData,
            serverKey,
            tokenInfo
        }
    );
    return response;
};
/**
 * (TR)    (1 )
 * getSampleRequest (GET_SAMPLE_REQUEST_A) : SHOW
 * cleargetSampleRequest (CLEAR_GET_SAMPLE_REQUEST_A) : CLEAR
 */
export const getSampleRequest = async ({layoutId, serverId}) => {
    const response = await axios.get(
        `/layouts/${layoutId}/${serverId}/sample-data`,
        null
    );
    return response;
};
/**
 * (TR) get inputdata (json/xml/fixedLength with server id)
 * changeInputData (CHANGE_INPUT_DATA) : SHOW
 * clearChangeInputData (CLEAR_CHANGE_INPUT_DATA) : CLEAR
 */
export const changeInputData = async ({
    beforeServer,
    afterServer,
    layoutId,
    inputData
}) => {
    const response = await axios.post(`/layouts/${layoutId}/changeBody`, {
        beforeServer,
        afterServer,
        inputData
    });
    return response;
};

/**
 * (TR) HISTORY  
 * searchHistoryCompare (SEARCH_HISTORY_COMPARE) : CALL
 * clearSearchHistoryCompare (CLEAR_SEARCH_HISTORY_COMPARE) : CLEAR
 */
export const searchHistoryCompare = async ({
    basicData,
    compareIdA,
    compareIdB
}) => {
    const response = await axios.post(`/histories/compare-result`, {
        basicData,
        compareIdA,
        compareIdB
    });

    return response;
};

/**
 * (TR)   Step 
 * executeStepByServer (EXECUTE_STEP) : SHOW
 * executeStepDataClear (EXECUTE_STEP_DATA_CLEAR) : CLEAR
 */
export const executeStepByServer = async ({
    stepId,
    wasServerId,
    dbServerId,
    step,
    sessionInfo
}) => {
    const response = await axios.post(`/steps/${stepId}/execute`, {
        wasServerId,
        dbServerId,
        step,
        sessionInfo
    });
    return response;
};
