/* eslint-disable no-unused-vars */
import {useSelector, useDispatch} from 'react-redux';
import {useHistory} from 'react-router-dom';
import React, {useState, useEffect} from 'react';
import {useTranslation} from 'react-i18next';
import StatTab1LayoutModal from '@pages/stat/popup/StatTab1LayoutModal';
import Spinner from '../../../components/spinner/Spinner';
import {
    clearStatDetailModal,
    searchDetailStatByLayout,
    clearsDetailStatByLayout
} from '../../../store/stat';

function StatTab1LayoutModalContainer() {
    const [t] = useTranslation();
    const dispatch = useDispatch();
    const history = useHistory();

    const [statDetailData, setStatDetailData] = useState();

    /** **************************
     * initializer
     ************************** */

    /** **************************
     * component state
     ************************** */
    const [modalShow, setModalShow] = React.useState(false);
    const handleClose = () => {
        dispatch(clearsDetailStatByLayout());
        dispatch(clearStatDetailModal());
        setModalShow(false);
    };
    const handleShow = () => setModalShow(true);

    const {searchFormT1} = useSelector((state) => state.stat);
    const {searchT1Cnt} = useSelector((state) => state.stat);

    /*
     *   
     */
    const moveHistory = () => {
        history.push({
            pathname: '/history',
            state: {
                serviceId: statDetailData.serviceInfo.serviceId,
                fromDate: searchFormT1.fromDateOld,
                toDate: searchFormT1.toDateOld,
                serverId: searchFormT1.serverIdOld,
                regCode: 'real'
            }
        });
    };

    /** **************************
     * Redux  
     ************************** */
    const {statDetailModalInitData} = useSelector((state) => state.stat);

    /** **************************
     * API   
     ************************** */

    const {
        loading: selectStatLoding,
        data: selectStatResponse,
        error: selectStatError
    } = useSelector((state) => state.stat.searchDetailStatByLayoutRes);

    useEffect(() => {
        if (!selectStatLoding && selectStatResponse) {
            const {data} = selectStatResponse;
            const temp = {
                urlInfo: data.urlInfo,
                serviceInfo: data.serviceInfo,
                targetNumbers: data.targetNumbers,
                allNumbers: data.allNumbers,
                layoutId: data.layoutId
            };

            setStatDetailData(temp);
        }

        // fail
        if (!selectStatLoding && selectStatError) {
            // toast.error(getErrorMsg(selectStatError, 'select'));
        }
    }, [selectStatResponse, selectStatError]);

    useEffect(() => {
        if (statDetailModalInitData.showModal && !modalShow) {
            dispatch(
                searchDetailStatByLayout(
                    statDetailModalInitData.initData.layoutId,
                    searchFormT1.serverId,
                    searchFormT1.fromDate,
                    searchFormT1.toDate
                )
            );
            dispatch(clearStatDetailModal());
            handleShow();
        }
    }, [statDetailModalInitData.showModal]);

    return (
        <>
            <StatTab1LayoutModal
                modalShow={modalShow}
                handleClose={handleClose}
                statDetailData={statDetailData}
                searchT1Cnt={searchT1Cnt}
                searchFormT1={searchFormT1}
                moveHistory={moveHistory}
            />
            <Spinner isLoading={selectStatLoding} />
        </>
    );
    ///
}

export default StatTab1LayoutModalContainer;
