import React from 'react';
import Club from '@pages/club/Club';
import {useHistory} from 'react-router-dom';
import {useDispatch, useSelector} from 'react-redux';

import {searchClub} from '../../store/club';

/*
 *  Container
 */
function ClubContainer() {
    const dispatch = useDispatch();
    // eslint-disable-next-line no-unused-vars
    const history = useHistory();

    /**
     *  
     */
    const {searchClubForm} = useSelector((state) => state.club);
    const onSearch = (_searchClubForm = searchClubForm) => {
        dispatch(searchClub(_searchClubForm));
    };

    /**
     *  PAGE 
     */
    const onMoveDetail = (id) => {
        history.push({
            pathname: '/club/detail',
            state: {
                clubId: id
            }
        });
    };

    return (
        <>
            <Club onSearch={onSearch} onMoveDetail={onMoveDetail} />
        </>
    );
}

export default ClubContainer;
