/* eslint-disable no-debugger */
/* eslint-disable no-unused-vars */
import React from 'react';
import {FaSearch} from 'react-icons/fa';
import {
    Modal,
    Form,
    Row,
    Col,
    InputGroup,
    FormControl,
    Nav,
    Accordion,
    FloatingLabel
} from 'react-bootstrap';
import {useFormik} from 'formik';
import * as Yup from 'yup';
import {useTranslation} from 'react-i18next';
import {EXECUTE_STATE} from '@app/lib/UitestCommon';
import Button from '../../components/button/Button';
import {jsonStrFormatPretty, validJsonString} from '../../lib/jsonUtils';
import {
    printFormError,
    getSelectValidMsg,
    renderModalHeader,
    renderHttpMethod,
    renderStatusCode2,
    takeSelectOptionbyCode
} from '../../lib/commonUiUtils';
import SmallBox from '../../components/small-box/SmallBox2';
import ServerUpdateContainer from '../../containers/server/ServerUpdateContainer';

const ScenarioTestPopup = ({
    modalShow,
    handleClose,
    serverData,
    codeData,
    randData,
    executeList,
    setExecuteList,
    selectedNavId,
    setSelectedNavId,
    playExecuteTrigger,
    isProcessTest,
    onClickInit,
    onClickMoveScenario,
    onClickServerInfo
}) => {
    const [t] = useTranslation();

    const formik = useFormik({
        enableReinitialize: true,
        initialValues: {
            scenarioId: executeList ? executeList.scenarioId : '',
            scenarioName: executeList ? executeList.scenarioName : '',
            wasServerId:
                executeList && executeList.wasServerId
                    ? executeList.wasServerId
                    : '',
            dbServerId:
                executeList && executeList.dbServerId
                    ? executeList.dbServerId
                    : '',
            xhrCount: executeList ? executeList.xhrCount : '',
            queryCount: executeList ? executeList.queryCount : '',
            startTime: executeList ? executeList.startTime : '',
            isSuccess: executeList ? executeList.isSuccess : '',
            executeSecond: executeList ? executeList.executeSecond : '',
            tokenInfo: executeList ? executeList.tokenInfo : ''
        },
        validationSchema: Yup.object({
            // TARGET SERVER
            wasServerId: Yup.string().when(['xhrCount'], {
                is: (xhrCount) => xhrCount > 0,
                then: Yup.string().required(
                    getSelectValidMsg(t('pages.scenario.wasServer'))
                )
            }),

            dbServerId: Yup.string().when(['queryCount'], {
                is: (queryCount) => queryCount > 0,
                then: Yup.string().required(
                    getSelectValidMsg(t('pages.scenario.dbServer'))
                )
            })
        }),
        onSubmit: (values) => {
            if (executeList.isSuccess) {
                return;
            }

            const executeListTemp = {...executeList};
            executeListTemp.wasServerId = values.wasServerId;
            executeListTemp.tokenInfo = values.tokenInfo;
            executeListTemp.dbServerId = values.dbServerId;

            setExecuteList(executeListTemp);

            //   
            // onClickInit();
            //   
            playExecuteTrigger();
        }
    });

    /*
     * Step execute data change event
     */
    const onChangeExecuteData = (e) => {
        if (executeList === null) {
            return;
        }

        const executeListTemp = {...executeList};
        executeListTemp[e.target.id] = e.target.value;

        setExecuteList(executeListTemp);
    };

    /*
     *    
     */
    const onChangeVariable = (e, index) => {
        const executeListTemp = {...executeList};

        const variables = executeList.variables.map((entity) => {
            const variable = entity;
            if (variable.variableId === index) {
                variable.changeValue = e.target.value;
            }
            return variable;
        });
        executeListTemp.variables = variables;
        setExecuteList(executeListTemp);
    };

    const onChangeStepObj = (e) => {
        if (executeList === null || executeList.steps === undefined) {
            return;
        }

        const steps = executeList.steps.map((step) => {
            const obj = step;
            if (obj.seq === Number(selectedNavId)) {
                if (e.target.type === 'checkbox') {
                    obj[e.target.id] = e.target.checked;
                } else {
                    obj[e.target.id] = e.target.value;
                }
            }
            return obj;
        });
        const executeListTemp = {...executeList};
        executeListTemp.steps = steps;
        setExecuteList(executeListTemp);
    };

    const onChangeStepJsonObj = (e) => {
        if (executeList === null || executeList.steps === undefined) {
            return;
        }

        const steps = executeList.steps.map((step) => {
            const obj = {...step};
            if (obj.seq === Number(selectedNavId)) {
                const splits = e.target.id.split('.');

                if (splits.length === 1) {
                    obj[e.target.id] = e.target.value;
                } else if (splits.length === 2) {
                    obj[splits[0]][splits[1]] = e.target.value;
                } else if (splits.length === 3) {
                    obj[splits[0]][splits[1]][splits[2]] = e.target.value;
                } else if (splits.length === 4) {
                    obj[splits[0]][splits[1]][splits[2]][splits[3]] =
                        e.target.value;
                }
            }
            return obj;
        });
        const executeListTemp = {...executeList};
        executeListTemp.steps = steps;
        setExecuteList(executeListTemp);
    };

    /*
     * renderWasServerInfo -> was   
     *  http     .
     */
    const renderWasServerInfo = () => {
        if (formik.values.xhrCount === 0) {
            return '';
        }

        return (
            <>
                <Row>
                    <Col xs="12">
                        <InputGroup className="mb-2">
                            <FloatingLabel className="mb-2" label="AP ">
                                <Form.Select
                                    id="wasServerId"
                                    onChange={onChangeExecuteData}
                                    value={
                                        executeList && executeList.wasServerId
                                    }
                                >
                                    <option value="">
                                        {t('common.text.optionNone')}
                                    </option>
                                    {serverData?.data
                                        ?.filter(
                                            (obj) =>
                                                obj.serverType === 'HTTP' ||
                                                obj.serverType === 'TCP_IP'
                                        )
                                        .map((obj) => (
                                            <option
                                                value={obj.serverId}
                                                key={obj.serverId}
                                            >
                                                {obj.serverName}
                                            </option>
                                        ))}
                                </Form.Select>
                                {printFormError(formik, 'wasServerId')}
                            </FloatingLabel>
                            <Button
                                type="button"
                                style={{maxWidth: '50px', marginBottom: '10px'}}
                                className="btn btn-block btn-secondary btn-search"
                                onClick={(e) =>
                                    onClickServerInfo(executeList?.wasServerId)
                                }
                            >
                                <FaSearch />
                            </Button>
                        </InputGroup>
                    </Col>
                </Row>
            </>
        );
    };

    /*
     * renderDBServerInfo -> DB   
     *  query     .
     */
    const renderDBServerInfo = () => {
        if (formik.values.queryCount === 0) {
            return '';
        }

        return (
            <>
                <Row>
                    <Col xs="12">
                        <InputGroup className="mb-2">
                            <FloatingLabel
                                className="mb-2"
                                label={t('pages.scenario.dbServer')}
                            >
                                <Form.Select
                                    id="dbServerId"
                                    onChange={onChangeExecuteData}
                                    value={
                                        executeList && executeList.dbServerId
                                    }
                                >
                                    <option value="">
                                        {t('common.text.optionNone')}
                                    </option>
                                    {serverData?.data
                                        ?.filter(
                                            (obj) =>
                                                obj.serverType === 'DATABASE'
                                        )
                                        .map((obj) => (
                                            <option
                                                value={obj.serverId}
                                                key={obj.serverId}
                                            >
                                                {obj.serverName}
                                            </option>
                                        ))}
                                </Form.Select>
                                {printFormError(formik, 'dbServerId')}
                            </FloatingLabel>
                            <Button
                                type="button"
                                style={{maxWidth: '50px', marginBottom: '10px'}}
                                className="btn btn-block btn-secondary btn-search"
                                onClick={(e) =>
                                    onClickServerInfo(executeList?.dbServerId)
                                }
                            >
                                <FaSearch />
                            </Button>
                        </InputGroup>
                    </Col>
                </Row>
            </>
        );
    };

    /*
     * render -> / 
     */
    const renderExecuteButton = () => {
        // 20220121         
        if (executeList.parentId === 'ScHistoryTable') {
            return (
                <>
                    {/* <Col xs="2"> */}
                    {/* <br /> */}
                    <Button
                        theme="outline-secondary"
                        // style={{float: 'right'}}
                        style={{float: 'left', width: '200px'}}
                        onClick={onClickMoveScenario}
                    >
                         
                    </Button>
                    {/* <br /> */}
                    {/* </Col> */}
                </>
            );
        }
        if (executeList.isSuccess === true || executeList.isSuccess === false) {
            return (
                <>
                    {/* <Col xs="2"> */}
                    <Button
                        theme="outline-secondary"
                        style={{float: 'left', width: '200px'}}
                        onClick={onClickInit}
                    >
                        {t('common.button.reset')}
                    </Button>
                </>
            );
        }
        return (
            <>
                <Button
                    theme="outline-secondary"
                    style={{float: 'left', width: '200px'}}
                    type="submit"
                    disabled={
                        executeList.isSuccess === true ||
                        executeList.isSuccess === false
                    }
                    isLoading={isProcessTest}
                >
                    {t('common.button.play')}
                </Button>
            </>
        );
    };

    /*
     * render -> Step  
     */
    const renderState = (state) => {
        // "stateProcess":""
        let clazzName = '';
        let buttonName = '';
        if (state === EXECUTE_STATE.WAIT) {
            clazzName = 'btn btn-secondary';
            buttonName = t('pages.scenario.stateWait');
        } else if (state === EXECUTE_STATE.PROCESS) {
            clazzName = 'btn btn-warning';
            buttonName = t('pages.scenario.stateProcess');
        } else if (state === EXECUTE_STATE.SUCCESS) {
            clazzName = 'btn btn-success';
            buttonName = t('pages.scenario.stateSuccess');
        } else if (state === EXECUTE_STATE.ERROR) {
            clazzName = 'btn btn-danger';
            buttonName = t('pages.scenario.stateFail');
        } else if (state === EXECUTE_STATE.SKIP) {
            clazzName = 'btn btn-secondary';
            buttonName = t('pages.scenario.stateSkip');
        } else {
            return '';
        }

        return (
            <button
                type="submit"
                className={clazzName}
                style={{float: 'right'}}
                // style={{width: '60px'}}
            >
                {buttonName}
            </button>
        );
    };

    const renderStepCodeButton = (stepType) => {
        let className = 'btn btn-info';

        if (stepType === 'API') {
            className = 'btn btn-success';
        }
        if (stepType === 'QUERY') {
            className = 'btn btn-danger';
        }
        if (stepType === 'SCRIPT') {
            className = 'btn btn-warning';
        }
        return (
            <button
                type="submit"
                className={className}
                style={{
                    float: 'left',
                    width: '70px',
                    marginRight: '10px',
                    marginLeft: '0px'
                }}
            >
                {stepType}
            </button>
        );
    };

    /*
     * render -> Step 
     */
    const renderStepList = () => {
        if (executeList === null || executeList.steps === undefined) {
            return '';
        }
        return executeList.steps.map((step) => {
            let {stepName} = step;

            if (step.stepType === 'LOGIN') {
                stepName = ' ';
            }

            return (
                <Nav.Link
                    key={step.seq}
                    eventKey={step.seq}
                    style={{
                        marginTop: '6px',
                        marginBottom: '6px',
                        paddingLeft: '3px',
                        paddingRight: '3px',
                        // marginRight: '0px',
                        borderBottom: '1px solid #dddddd'
                    }}
                >
                    {renderStepCodeButton(step.stepType)}
                    <div style={{float: 'left', fontSize: '16px'}}>
                        {step.caseName} <br /> {stepName}
                    </div>
                    {renderState(step.state)}
                </Nav.Link>
            );
        });
    };

    const renderIsSkipCheckBox = (selectedStep) => {
        return (
            <Form.Check
                type="checkbox"
                label={t('pages.scenario.isSkipError')}
                id="isSkipError"
                onChange={onChangeStepObj}
                checked={selectedStep[0].isSkipError}
            />
        );
    };

    const renderStep = (stepId) => {
        if (!stepId) return '';
        const steps = [];
        executeList?.cases?.forEach((obj) => {
            obj.steps
                .filter((obj2) => obj2.stepId > -1)
                .forEach((obj2) => {
                    steps.push(obj2);
                });
        });

        return (
            <>
                <FloatingLabel label="Step " className="mb-2">
                    <Form.Select value={stepId} disabled>
                        <option value=""></option>
                        {steps.map((obj) => {
                            return (
                                <option value={obj.stepId} key={obj.stepId}>
                                    {`[${obj.stepType}]  ${obj.stepName}`}
                                </option>
                            );
                        })}
                    </Form.Select>
                </FloatingLabel>
            </>
        );
    };

    const renderCopyDetail = (selectedStep, fromOrTo, copyOrIf) => {
        let item;
        let dataId;
        if (copyOrIf === 'COPY') {
            item =
                fromOrTo === 'FROM'
                    ? selectedStep.script.copyAttr?.fromItem
                    : selectedStep.script.copyAttr?.toItem;
            dataId =
                fromOrTo === 'FROM'
                    ? 'script.copyAttr.fromItem.data'
                    : 'script.copyAttr.toItem.data';
        } else if (copyOrIf === 'IF') {
            item =
                fromOrTo === 'FROM'
                    ? selectedStep.script.ifAttr?.itemA
                    : selectedStep.script.ifAttr?.itemB;

            dataId =
                fromOrTo === 'FROM'
                    ? 'script.ifAttr.itemA.data'
                    : 'script.ifAttr.itemB.data';
        }

        if (item.option === 'TEXT' || item.option === 'NUMBER') {
            return (
                <>
                    <Col xs="12">
                        <FloatingLabel
                            label={
                                item.option === 'TEXT'
                                    ? 'Text  '
                                    : ' '
                            }
                            className="mb-2"
                        >
                            <Form.Control
                                type={
                                    item.option === 'TEXT' ? 'text' : 'number'
                                }
                                maxLength="100"
                                value={item.data}
                                id={dataId}
                                onChange={onChangeStepJsonObj}
                            />
                        </FloatingLabel>
                    </Col>
                </>
            );
        }
        if (item.option === 'VARIABLE') {
            return (
                <>
                    <Col xs="12">
                        <FloatingLabel label="" className="mb-2">
                            <Form.Select
                                value={item.variableId}
                                id={dataId}
                                onChange={onChangeStepJsonObj}
                            >
                                <option value="">
                                    {t('common.text.optionNone')}
                                </option>
                                {executeList?.variables
                                    .filter((obj) => obj.variableId > -1)
                                    .map((obj) => {
                                        return (
                                            <option
                                                value={obj.variableId}
                                                key={obj.variableId}
                                            >
                                                [{obj.variableType}]
                                                {obj.variableName}
                                            </option>
                                        );
                                    })}
                            </Form.Select>
                        </FloatingLabel>
                    </Col>
                </>
            );
        }
        if (item.option === 'RANDOM') {
            const {randomStyle} = selectedStep.script.copyAttr;
            return (
                <>
                    <Col xs="5">
                        <FloatingLabel label=" " className="mb-2">
                            <Form.Select value={randomStyle} disabled>
                                <option> {t('common.text.optionNone')}</option>
                                {takeSelectOptionbyCode(
                                    codeData,
                                    'RandomStyle'
                                )}
                            </Form.Select>
                        </FloatingLabel>
                    </Col>
                    <Col xs="7">
                        <FloatingLabel label=" " className="mb-2">
                            <Form.Select
                                value={selectedStep.script.copyAttr.randomId}
                                id="script.copyAttr.randomId"
                                onChange={onChangeStepJsonObj}
                            >
                                <option> {t('common.text.optionNone')}</option>
                                {randData.data
                                    .filter(
                                        (obj) => obj.randomStyle === randomStyle
                                    )
                                    .map((obj) => (
                                        <option
                                            value={obj.randomId}
                                            key={obj.randomId}
                                        >
                                            {obj.randomDesc}
                                        </option>
                                    ))}
                            </Form.Select>
                        </FloatingLabel>
                    </Col>
                </>
            );
        }

        if (item.option === 'STEP') {
            const {stepId} = item;

            let bodyData = '';
            const steps = [];
            executeList?.cases?.forEach((obj) => {
                obj.steps
                    .filter((obj2) => obj2.stepId > -1)
                    .forEach((obj2) => {
                        steps.push(obj2);
                    });
            });

            steps
                .filter((step) => step.stepId === stepId)
                .forEach((step) => {
                    bodyData =
                        fromOrTo === 'FROM' ? step.outputData : step.inputData;
                });

            return (
                <>
                    <Col xs="12">{renderStep(stepId)} </Col>
                    <Col xs="12">
                        <FloatingLabel
                            label="Text  "
                            className="mb-2"
                        >
                            <Form.Control
                                type="text"
                                maxLength="100"
                                value={item.data}
                                id={dataId}
                                onChange={onChangeStepJsonObj}
                            />
                        </FloatingLabel>
                    </Col>
                </>
            );
        }

        return '';
    };

    /*
     * render -> Step  
     */
    const renderStepListData = () => {
        if (
            selectedNavId === '' ||
            executeList.steps === undefined ||
            executeList.steps.length === 0
        ) {
            return '';
        }

        const selectedStep = executeList.steps.filter(
            (step) => step.seq === Number(selectedNavId)
        );

        const renderInputParsingResult = (data) => {
            const isValid = validJsonString(data);
            if (isValid) {
                return (
                    <Form.Label style={{float: 'right'}}>
                        {t('common.text.validJsonFormat')}
                    </Form.Label>
                );
            }
            return (
                <Form.Label style={{float: 'right', color: 'red'}}>
                    {t('common.text.invalidJsonFormat')}
                </Form.Label>
            );
        };

        /** ************************************************************************
         * 1. Login
         ************************************************************************* */
        if (selectedStep[0].stepType === 'LOGIN') {
            const renderLoginInfo = () => {
                if (selectedStep[0].defaultLogin === 'Y') {
                    return '';
                }
                return (
                    <>
                        <Col xs="4">
                            <FloatingLabel
                                label={t('common.text.id')}
                                className="mb-1"
                            >
                                <Form.Control
                                    type="text"
                                    maxLength="20"
                                    id="connectionInfo.connectedId"
                                    onChange={onChangeStepJsonObj}
                                    value={
                                        selectedStep[0].connectionInfo
                                            ?.connectedId
                                    }
                                />
                            </FloatingLabel>
                        </Col>
                        <Col xs="4">
                            <FloatingLabel
                                label={t('common.text.password')}
                                className="mb-1"
                            >
                                <Form.Control
                                    type="text"
                                    maxLength="100"
                                    id="connectionInfo.connectedPw"
                                    onChange={onChangeStepJsonObj}
                                    value={
                                        selectedStep[0].connectionInfo
                                            ?.connectedPw
                                    }
                                />
                            </FloatingLabel>
                        </Col>
                    </>
                );
            };
            return (
                <>
                    <Col xs="3">
                        <FloatingLabel
                            className="mb-2"
                            label={t('pages.server.loginInfo')}
                        >
                            <Form.Select
                                value={selectedStep[0].defaultLogin}
                                id="defaultLogin"
                                onChange={onChangeStepJsonObj}
                            >
                                {takeSelectOptionbyCode(
                                    codeData,
                                    'DefaultLogin'
                                )}
                            </Form.Select>
                        </FloatingLabel>
                    </Col>
                    {renderLoginInfo()}
                    <Col xs="12">{renderIsSkipCheckBox(selectedStep)}</Col>
                </>
            );
        }
        /** ************************************************************************
         * 2  (xhr)
         ** *********************************************************************** */
        if (selectedStep[0].stepType === 'API') {
            return (
                <>
                    <Col xs="6">
                        <InputGroup>
                            {renderHttpMethod(selectedStep[0]?.httpMethod)}
                            <FloatingLabel
                                className="mb-2"
                                label={t('pages.scenario.urlPath')}
                            >
                                <FormControl
                                    disabled
                                    value={selectedStep[0]?.stepName}
                                />
                            </FloatingLabel>
                        </InputGroup>
                    </Col>
                    <Col xs="2">
                        {renderStatusCode2(selectedStep[0]?.responseInfo)}
                    </Col>
                    <Col xs="12" className="mb-2">
                        {renderIsSkipCheckBox(selectedStep)}
                    </Col>
                    <Col xs="6">
                        <FloatingLabel label="Request Body" className="mb-2">
                            <Form.Control
                                as="textarea"
                                style={{height: '433px', fontSize: '20px'}}
                                value={jsonStrFormatPretty(
                                    selectedStep[0].inputData
                                )}
                                id="inputData"
                                onChange={onChangeStepObj}
                            />
                        </FloatingLabel>
                    </Col>
                    <Col xs="6">
                        <FloatingLabel label="Response Body" className="mb-2">
                            <Form.Control
                                as="textarea"
                                style={{height: '433px', fontSize: '20px'}}
                                value={jsonStrFormatPretty(
                                    selectedStep[0].outputData
                                )}
                                readOnly
                            />
                        </FloatingLabel>
                    </Col>
                </>
            );
        }

        /** ************************************************************************
         * 3. Sql  (query)
         ************************************************************************* */
        if (selectedStep[0].stepType === 'QUERY') {
            return (
                <>
                    <Col xs="5">
                        <FloatingLabel
                            className="mb-2"
                            label={t('pages.scenario.title')}
                        >
                            <FormControl
                                disabled
                                value={selectedStep[0]?.stepName}
                            />
                        </FloatingLabel>
                    </Col>
                    <Col xs="12">{renderIsSkipCheckBox(selectedStep)}</Col>
                    <Col xs="6">
                        <FloatingLabel label="Query" className="mb-2">
                            <Form.Control
                                as="textarea"
                                style={{height: '433px', fontSize: '20px'}}
                                value={jsonStrFormatPretty(
                                    selectedStep[0].inputData
                                )}
                                id="inputData"
                                onChange={onChangeStepObj}
                            />
                        </FloatingLabel>
                    </Col>
                    <Col xs="6">
                        <FloatingLabel label="Result" className="mb-2">
                            <Form.Control
                                as="textarea"
                                style={{height: '433px', fontSize: '20px'}}
                                value={jsonStrFormatPretty(
                                    selectedStep[0].outputData
                                )}
                                readOnly
                            />
                        </FloatingLabel>
                    </Col>
                </>
            );
        }

        /** ************************************************************************
         * 4.  (COPY, IF, SLEEP)
         * 4.1 COPY
         * 4.2 IF
         * 4.3 SLEEP
         ************************************************************************* */
        if (selectedStep[0].stepType === 'SCRIPT') {
            /** ************************************************************************
             * 4.1
             ************************************************************************* */
            if (selectedStep[0].script.scriptType === 'COPY') {
                return (
                    <>
                        <Col xs="6">
                            <Row>
                                <Col xs="12">
                                    <FloatingLabel
                                        label="From  "
                                        className="mb-2"
                                    >
                                        <Form.Select
                                            value={
                                                selectedStep[0].script?.copyAttr
                                                    .fromItem?.option
                                            }
                                            disabled
                                        >
                                            {takeSelectOptionbyCode(
                                                codeData,
                                                'ScriptOption'
                                            )}
                                        </Form.Select>
                                    </FloatingLabel>
                                </Col>
                                {renderCopyDetail(
                                    selectedStep[0],
                                    'FROM',
                                    'COPY'
                                )}
                            </Row>
                        </Col>
                        <Col xs="6">
                            <Row>
                                <Col xs="12">
                                    <FloatingLabel
                                        label="To  "
                                        className="mb-2"
                                    >
                                        <Form.Select
                                            value={
                                                selectedStep[0].script?.copyAttr
                                                    .toItem?.option
                                            }
                                            disabled
                                        >
                                            {takeSelectOptionbyCode(
                                                codeData,
                                                'ScriptOption',
                                                2
                                            )}
                                        </Form.Select>
                                    </FloatingLabel>
                                </Col>
                                {renderCopyDetail(
                                    selectedStep[0],
                                    'TO',
                                    'COPY'
                                )}
                            </Row>
                        </Col>
                    </>
                );
            }

            if (selectedStep[0].script.scriptType === 'IF') {
                return (
                    <>
                        <Col xs="5">
                            <Row>
                                <Col xs="12">
                                    <FloatingLabel
                                        label="Item A"
                                        className="mb-2"
                                    >
                                        <Form.Select
                                            value={
                                                selectedStep[0].script?.ifAttr
                                                    .itemA.option
                                            }
                                            disabled
                                        >
                                            {takeSelectOptionbyCode(
                                                codeData,
                                                'ScriptOption',
                                                4
                                            )}
                                        </Form.Select>
                                    </FloatingLabel>
                                </Col>
                                {renderCopyDetail(
                                    selectedStep[0],
                                    'FROM',
                                    'IF'
                                )}
                            </Row>
                        </Col>
                        <Col xs="2">
                            <FloatingLabel label="" className="mb-2">
                                <Form.Select
                                    value={
                                        selectedStep[0].script?.ifAttr.IfFormula
                                    }
                                    id="script.ifAttr.ifFormula"
                                    onChange={onChangeStepJsonObj}
                                >
                                    {takeSelectOptionbyCode(
                                        codeData,
                                        'IfFormula'
                                    )}
                                </Form.Select>
                            </FloatingLabel>
                        </Col>
                        <Col xs="5">
                            <Row>
                                <Col xs="12">
                                    <FloatingLabel
                                        label="Item B"
                                        className="mb-2"
                                    >
                                        <Form.Select
                                            value={
                                                selectedStep[0].script?.ifAttr
                                                    ?.ifCondB
                                            }
                                            disabled
                                        >
                                            {takeSelectOptionbyCode(
                                                codeData,
                                                'ScriptOption',
                                                4
                                            )}
                                        </Form.Select>
                                    </FloatingLabel>
                                </Col>
                                {renderCopyDetail(selectedStep[0], 'TO', 'IF')}
                            </Row>
                        </Col>
                        <Col xs="5">
                            <Form.Label>
                                <b>  </b>
                            </Form.Label>
                            <FloatingLabel label=" " className="mb-2">
                                <Form.Select
                                    value={
                                        selectedStep[0].script?.ifAttr.trueItem
                                            .postProcessingType
                                    }
                                    id="script.ifAttr.trueItem?.postProcessingType"
                                    onChange={onChangeStepJsonObj}
                                >
                                    {takeSelectOptionbyCode(
                                        codeData,
                                        'PostProcessingType'
                                    )}
                                </Form.Select>
                            </FloatingLabel>
                            {renderStep(
                                // selectedStep[0]
                                selectedStep[0].script.ifAttr.trueItem.stepId
                            )}
                            {/* {renderStep(selectedStep[0], 'T')} */}
                        </Col>
                        <Col xs="5">
                            <Form.Label>
                                <b>  </b>
                            </Form.Label>
                            <FloatingLabel label=" " className="mb-2">
                                <Form.Select
                                    value={
                                        selectedStep[0].script?.ifAttr.falseItem
                                            .postProcessingType
                                    }
                                    id="script.ifAttr.falseItem?.postProcessingType"
                                    onChange={onChangeStepJsonObj}
                                >
                                    {takeSelectOptionbyCode(
                                        codeData,
                                        'PostProcessingType'
                                    )}
                                </Form.Select>
                            </FloatingLabel>
                            {renderStep(
                                selectedStep[0].script.ifAttr.falseItem.stepId
                            )}
                            {/* {renderStep(selectedStep[0], 'F')} */}
                        </Col>
                    </>
                );
            }

            /** ************************************************************************
             * 4.3 SLEEP
             ************************************************************************* */
            if (selectedStep[0].script.scriptType === 'SLEEP') {
                return (
                    <Col xs="5">
                        <FloatingLabel
                            label=" (max 3000ms)"
                            className="mb-1"
                        >
                            <Form.Control
                                type="number"
                                id="sleepMs"
                                value={selectedStep[0].script.sleepMs}
                                Min={0}
                                Max={3000}
                                onChange={onChangeStepJsonObj}
                            />
                        </FloatingLabel>
                    </Col>
                );
            }
        }

        return <></>;
    };

    /*
     * render ->   
     */
    const renderTestResult = () => {
        if (executeList) {
            if (
                executeList.isSuccess === undefined ||
                executeList.isSuccess === ''
            ) {
                return '';
            }
            // console.log();
            // console.log(executeList.executeSecond);

            let timeStamp = '';
            if (executeList.startTime !== '') {
                timeStamp = `${executeList.startTime.slice(
                    5,
                    10
                )} ${executeList.startTime.slice(11, 16)} (${
                    executeList.executeSecond
                }s)`;
            } else {
                const offset = new Date().getTimezoneOffset() * 60000;
                const today = new Date(Date.now() - offset);
                timeStamp = `${today
                    .toISOString()
                    .slice(0, -1)
                    .slice(5, 10)} ${today
                    .toISOString()
                    .slice(0, -1)
                    .slice(11, 16)}`;
            }

            if (executeList.isSuccess) {
                return (
                    <SmallBox
                        title={timeStamp}
                        count={t('common.text.success')}
                        type="success"
                        navigateTo="/"
                    />
                );
            }
            return (
                <SmallBox
                    title={timeStamp}
                    count={t('common.text.fail')}
                    type="danger"
                    navigateTo="/"
                />
            );
        }
        return '';
    };

    /*
     * render ->   
     */
    const renderVariableList = () => {
        if (!executeList || executeList.variables === undefined) return '';
        const renderVariable = (index) => {
            const variable = executeList.variables[index];

            const value = variable.changeValue;
            return (
                <Col xs="4" key={variable.variableId}>
                    <FloatingLabel
                        label={variable.variableName}
                        className="mb-1"
                    >
                        <Form.Control
                            type="text"
                            value={value}
                            onChange={(e) =>
                                onChangeVariable(e, variable.variableId)
                            }
                        />
                    </FloatingLabel>
                </Col>
            );
        };
        return (
            <>
                <Accordion defaultActiveKey="0">
                    <Accordion.Item eventKey="0">
                        <Accordion.Header className="accordion-header2">
                             
                        </Accordion.Header>
                        <Accordion.Body>
                            <Row>
                                {executeList.variables.map((obj, index) =>
                                    renderVariable(index)
                                )}
                            </Row>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
            </>
        );
    };

    return (
        <>
            <Modal
                show={modalShow}
                onHide={handleClose}
                dialogClassName="modal-70w"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Form onSubmit={formik.handleSubmit}>
                    {renderModalHeader(t('pages.scenario.scenarioTest'))}
                    <Modal.Body>
                        <Accordion defaultActiveKey="0">
                            <Accordion.Item eventKey="0">
                                <Accordion.Header className="accordion-header1">
                                     
                                </Accordion.Header>
                                <Accordion.Body>
                                    <Row>
                                        <Col xs="3">
                                            <FloatingLabel
                                                label={t(
                                                    'pages.scenario.scenarioName'
                                                )}
                                                className="mb-1"
                                            >
                                                <Form.Control
                                                    type="text"
                                                    value={
                                                        executeList.scenarioName ??
                                                        ''
                                                    }
                                                    readOnly
                                                />
                                            </FloatingLabel>
                                        </Col>

                                        <Col xs="3">
                                            {renderWasServerInfo()}
                                        </Col>
                                        <Col xs="3">{renderDBServerInfo()}</Col>
                                        <Col xs="3">{renderTestResult()}</Col>
                                    </Row>
                                </Accordion.Body>
                            </Accordion.Item>
                        </Accordion>
                        <br />
                        <Row>
                            <Col xs="5">
                                {renderVariableList()}
                                <br />
                                <Accordion defaultActiveKey="0">
                                    <Accordion.Item eventKey="0">
                                        <Accordion.Header className="accordion-header3">
                                            {t('pages.scenario.stepList')}
                                        </Accordion.Header>
                                        <Accordion.Body
                                            style={{padding: '3px'}}
                                        >
                                            <div
                                                style={{
                                                    overflowY: 'auto',
                                                    height: '600px'
                                                }}
                                            >
                                                <Nav
                                                    defaultActiveKey="0"
                                                    className="flex-column nav-pills"
                                                    onSelect={(selectedKey) =>
                                                        setSelectedNavId(
                                                            `${selectedKey}`
                                                        )
                                                    }
                                                >
                                                    <Nav.Item />
                                                    {renderStepList()}
                                                </Nav>
                                            </div>
                                        </Accordion.Body>
                                    </Accordion.Item>
                                </Accordion>
                            </Col>
                            <Col xs="7">
                                <Accordion defaultActiveKey="0">
                                    <Accordion.Item eventKey="0">
                                        <Accordion.Header className="accordion-header4">
                                            STEP  
                                        </Accordion.Header>
                                        <Accordion.Body>
                                            <Row>{renderStepListData()}</Row>
                                        </Accordion.Body>
                                    </Accordion.Item>
                                </Accordion>
                            </Col>
                        </Row>
                    </Modal.Body>
                    <Modal.Footer>
                        {renderExecuteButton()}
                        <Button theme="default" onClick={handleClose}>
                            {t('common.button.close')}
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>
            <ServerUpdateContainer />
        </>
    );
};
export default ScenarioTestPopup;
