/* eslint-disable no-alert */
/* eslint-disable no-unused-vars */
/* eslint-disable jsx-a11y/label-has-associated-control */
import React, {useState} from 'react';
import {Container, Row, Col, Form} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
import Button from '../../components/button/Button';
import Card from '../../components/card/Card';
import Help from '../../components/help/Help';

const ChromeDriverVersionManagement = ({
    chromeDirverInfo,
    uploadChromeDriverFile
}) => {
    const [t] = useTranslation();

    const [chromeDriverUploadForm, setChromeDriverUploadForm] = useState({
        version: null,
        os: 'window',
        file: null
    });

    const onChangeChromeDriverFile = (e) => {
        e.preventDefault();
        const newForm = {
            ...chromeDriverUploadForm,
            file: e.target.files[0]
        };
        setChromeDriverUploadForm(newForm);
    };

    const onChangeChromeDriverVersion = (e) => {
        e.preventDefault();
        const newForm = {
            ...chromeDriverUploadForm,
            version: e.target.value
        };
        setChromeDriverUploadForm(newForm);
    };

    const onChangeOsSystem = (e) => {
        e.preventDefault();
        const newForm = {
            ...chromeDriverUploadForm,
            os: e.target.value
        };
        setChromeDriverUploadForm(newForm);
    };

    const onClickChromeDriverUploadButton = () => {
        if (!chromeDriverUploadForm.file) {
            alert('upload file is not selected');
            return;
        }
        if (!chromeDriverUploadForm.version) {
            alert('version is empty');
            return;
        }

        if (!/^[0-9]+$/.test(chromeDriverUploadForm.version)) {
            alert('version is not number format');
            return;
        }

        if (!uploadChromeDriverFile) {
            // loading 
            alert('now Install version info is yet recieved');
            return;
        }

        uploadChromeDriverFile(
            Number(chromeDriverUploadForm.version),
            chromeDriverUploadForm.os,
            chromeDriverUploadForm.file
        );
    };

    const displayInstallVersionDownloadButton = () => {
        if (!chromeDirverInfo || !chromeDirverInfo.data) {
            return <></>;
        }

        return (
            <>
                {chromeDirverInfo.data
                    .sort((firstEl, secondEl) => {
                        return secondEl.version - firstEl.version;
                    })
                    .map((chromeDriver) => {
                        return (
                            <form
                                method="get"
                                action={chromeDriver.downloadUrl}
                                key={chromeDriver.version}
                            >
                                <Button
                                    className="float-right"
                                    type="submit"
                                    theme="link"
                                >
                                    {/* {`${chromeDriver.os}_${chromeDriver.browser}_${chromeDriver.version}`} */}
                                    {`${chromeDriver.version}`}
                                </Button>
                            </form>
                        );
                    })}
            </>
        );
    };

    return (
        <>
            <Card
                title={t('pages.setting.chromeDriverManagement')}
                body={
                    <Container fluid>
                        <Row>
                            <Col xs="3">
                                <h5>
                                    {t('pages.setting.chromeDriverFile')}
                                    <Help
                                        text={t(
                                            'pages.setting.chromeDriverFileHelp'
                                        )}
                                    />
                                </h5>
                            </Col>
                            <Col xs="9">
                                {displayInstallVersionDownloadButton()}
                            </Col>
                        </Row>
                        <Row>
                            <Col xs="4">
                                <Form.Group controlId="input" className="mb-3">
                                    <Form.Label />
                                    <Form.Control
                                        type="file"
                                        onChange={onChangeChromeDriverFile}
                                    />
                                </Form.Group>
                            </Col>
                            <Col xs="3">
                                <Form.Group>
                                    <Form.Label />
                                    <Form.Control
                                        as="select"
                                        id="bulkTestType"
                                        onChange={onChangeOsSystem}
                                        value={
                                            chromeDriverUploadForm &&
                                            chromeDriverUploadForm.os
                                        }
                                    >
                                        <option value="window">Window</option>
                                        <option value="mac">Mac OS</option>
                                    </Form.Control>
                                </Form.Group>
                            </Col>
                            <Col xs="3">
                                <Form.Group
                                    controlId="input"
                                    className="mb-3 d-inline-block"
                                >
                                    <Form.Label />
                                    <Form.Control
                                        type="input"
                                        placeholder={`${t(
                                            'pages.setting.version'
                                        )} ex) 63`}
                                        onChange={onChangeChromeDriverVersion}
                                    />
                                </Form.Group>
                            </Col>
                            <Col xs="2">
                                <div className="form-group">
                                    <label htmlFor="" />
                                    <Button
                                        type="button"
                                        className="btn btn-block btn-secondary btn-search"
                                        onClick={
                                            onClickChromeDriverUploadButton
                                        }
                                    >
                                        {t('common.button.register')}
                                    </Button>
                                </div>
                            </Col>
                        </Row>
                    </Container>
                }
            />
        </>
    );
};

export default ChromeDriverVersionManagement;
