/* eslint-disable no-unused-vars */
import React from 'react';
import {
    Form,
    InputGroup,
    FormControl,
    Modal,
    Row,
    Col,
    FloatingLabel,
    Accordion
} from 'react-bootstrap';
import {useFormik} from 'formik';
import {useTranslation} from 'react-i18next';
import Button from '../../../components/button/Button';
import {renderIsSkipCheckBox} from '../../../lib/UitestCommon';
import {jsonStrFormatPretty, validJsonString} from '../../../lib/jsonUtils';
import {renderHttpMethod} from '../../../lib/commonUiUtils';

// eslint-disable-next-line no-unused-vars
const UitestStepPopXhr = ({
    scenarioData,
    selectedCaseId,
    setDetailCaseList,
    handleClose,
    stepUpdateInitData,
    onChangeStepUpdataInitData
}) => {
    const [t] = useTranslation();
    const onChangeForm = (e) => {
        e.preventDefault();
        const newStepUpdateInitData = {
            ...stepUpdateInitData
        };
        newStepUpdateInitData[e.target.id] = e.target.value;
        onChangeStepUpdataInitData(newStepUpdateInitData);
    };

    // step code setting
    const formik = useFormik({
        initialValues: {
            stepId: stepUpdateInitData.stepId ?? -1,
            index: stepUpdateInitData?.index ?? -1,
            urlPath: stepUpdateInitData?.stepName ?? '',
            httpMethod: stepUpdateInitData?.httpMethod ?? 'NONE',
            inputData: stepUpdateInitData
                ? jsonStrFormatPretty(stepUpdateInitData.inputData)
                : '',
            outputData: stepUpdateInitData
                ? jsonStrFormatPretty(stepUpdateInitData.outputData)
                : '',
            stepType: stepUpdateInitData?.stepType,
            isSkipError: stepUpdateInitData?.isSkipError ?? false
        },
        // validationSchema: Yup.object({
        //     caseName: Yup.string().required('Required')
        // }),
        onSubmit: (values) => {
            const cases = [...scenarioData.data.cases];
            const caseLoop = cases.map((cUnit, cIndex) => {
                const theCase = cUnit;

                if (cUnit.caseId === selectedCaseId) {
                    let data = theCase.steps;
                    if (data == null) {
                        data = [];
                    }

                    const newStep = values;
                    const aLoop = data.map((unit) => {
                        if (unit.stepId === newStep.stepId) {
                            const unitT = {...unit};
                            unitT.inputData = newStep.inputData;
                            unitT.outputData = newStep.outputData;
                            unitT.isSkipError = newStep.isSkipError;
                            return unitT;
                        }
                        return unit;
                    });

                    theCase.steps = aLoop;
                }
                return theCase;
            });
            setDetailCaseList(caseLoop);
            handleClose();
        }
    });

    const onClickCheckBox = (e) => {
        const newStepUpdateInitData = {
            ...stepUpdateInitData
        };
        formik.values.isSkipError = !formik.values.isSkipError;
        newStepUpdateInitData.isSkipError = formik.values.isSkipError;
        onChangeStepUpdataInitData(newStepUpdateInitData);
    };

    const renderInputParsingResult = () => {
        const isValid = validJsonString(
            formik.getFieldProps('inputData').value
        );
        if (isValid) {
            return (
                <Form.Label style={{float: 'right'}}>
                    {t('common.text.validJsonFormat')}
                </Form.Label>
            );
        }
        return (
            <Form.Label style={{float: 'right', color: 'red'}}>
                {t('common.text.invalidJsonFormat')}
            </Form.Label>
        );
    };
    return (
        <>
            <Form onSubmit={formik.handleSubmit}>
                <Accordion defaultActiveKey="0">
                    <Accordion.Item eventKey="0">
                        <Accordion.Header className="accordion-header3">
                            {t('pages.scenario.infoXhr')}
                        </Accordion.Header>
                        <Accordion.Body>
                            <Row>
                                <Col xs="6">
                                    <InputGroup>
                                        {renderHttpMethod(
                                            stepUpdateInitData?.httpMethod
                                        )}
                                        <FloatingLabel
                                            label={t('pages.scenario.urlPath')}
                                        >
                                            <FormControl
                                                disabled
                                                value={
                                                    stepUpdateInitData?.stepName
                                                }
                                            />
                                        </FloatingLabel>
                                    </InputGroup>
                                </Col>
                                <Col xs="6">
                                    {renderIsSkipCheckBox(
                                        formik.values.isSkipError,
                                        onClickCheckBox,
                                        t('pages.scenario.isSkipError')
                                    )}
                                </Col>
                            </Row>

                            <Row>
                                {renderInputParsingResult()}
                                <Col xs="6">
                                    <FloatingLabel
                                        label="Request Body"
                                        className="mb-2"
                                    >
                                        <Form.Control
                                            as="textarea"
                                            style={{height: '433px'}}
                                            {...formik.getFieldProps(
                                                'inputData'
                                            )}
                                        />
                                    </FloatingLabel>
                                </Col>
                                <Col xs="6">
                                    <FloatingLabel
                                        label="Response Body"
                                        className="mb-2"
                                    >
                                        <Form.Control
                                            as="textarea"
                                            style={{height: '433px'}}
                                            {...formik.getFieldProps(
                                                'outputData'
                                            )}
                                        />
                                    </FloatingLabel>
                                </Col>
                            </Row>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
                <Modal.Footer>
                    <Button theme="primary" type="submit">
                        {t('common.button.register')}
                    </Button>
                    <Button theme="default" onClick={handleClose}>
                        {t('common.button.close')}
                    </Button>
                </Modal.Footer>
            </Form>
        </>
    );
};

export default UitestStepPopXhr;
