/* eslint-disable no-unused-vars */
import React from 'react';
import {FaSearch, FaTrash} from 'react-icons/fa';
import {useTranslation} from 'react-i18next';
import Table from '../../components/table/Table';
import Button from '../../components/button/Button';
import {displayNumber} from '../../lib/commonUiUtils';
import {renderBatchStatus} from '../../lib/buttonRender';
import {changeToDate, changeToProcessTime} from '../../lib/moment';

const SourceTable = ({
    data,
    movePage,
    onClickDelete,
    memberInfoData,
    onClickMove
}) => {
    const [t] = useTranslation();
    const tableName = t('pages.history.historyList');
    const rowKey = 'sourceSnapshotId';

    const columns = [
        {
            title: ' ',
            key: 'batchStatus',
            width: '10%',
            render: (id, row) => {
                return renderBatchStatus(row.batchInfo?.batchStatus);
            }
        },
        {
            title: ' ',
            key: 'startDateTime',
            width: '10%',
            render: (id, row) => {
                return changeToDate(row.batchInfo?.startedDateTime);
            }
        },
        {
            title: ' ',
            key: 'processTime',
            width: '10%',
            render: (id, row) => {
                return changeToProcessTime(row.batchInfo?.processTime);
            }
        },
        {
            title: ' ',
            align: 'left', // left, center, right (default:center)
            key: 'directoryPath'
        },
        {
            title: 'Controller ',
            key: 'controllerCount',
            render: (id, row) => {
                return `${row.sourceAnalysisReport?.controllerCount} `;
            }
        },
        {
            title: 'Data ',
            key: 'dataCount',
            render: (id, row) => {
                return `${
                    row.sourceAnalysisReport?.dataCount +
                    row.sourceAnalysisReport?.enumCount
                } `;
            }
        },
        {
            title: '',
            width: '5%',
            key: 'button',
            render: (id, row, column) => {
                return (
                    <>
                        {/* Move History Page */}
                        <Button
                            theme="outline-success"
                            onClick={() => onClickMove(id)}
                        >
                            <FaSearch />
                        </Button>
                        <Button
                            theme="outline-danger"
                            onClick={() => onClickDelete(id)}
                            //  
                            disabled={memberInfoData?.data?.role !== 'ADMIN'}
                        >
                            <FaTrash />
                        </Button>
                    </>
                );
            }
        }
    ];

    return (
        <>
            <Table
                tableName={tableName}
                movePage={movePage}
                rowKey={rowKey}
                columns={columns}
                data={data}
                // onDoubleClick={onClickMove}
            />
        </>
    );
};

export default SourceTable;
