/* eslint-disable no-shadow */
/* eslint-disable no-unused-vars */
import React from 'react';
import {Form, Col, Row, Accordion, FloatingLabel} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
import {
    renderCopyBlankData,
    renderVariable,
    renderCase,
    renderStep,
    renderSampleObject
} from '../../../lib/UitestCommon';
import {
    printFormError,
    takeSelectOptionbyCode
} from '../../../lib/commonUiUtils';

// eslint-disable-next-line no-unused-vars
const UitestScriptIf = ({formik, executeList, scenarioData, codeData}) => {
    const [t] = useTranslation();
    const [selectedAB, setSelectedAB] = React.useState('A');

    /*
     * renderIfBDetail -> script type -> copy -> from
     */
    const renderIfDetail = (gubun) => {
        // renderCopyBlankData
        const option =
            gubun === 'A'
                ? formik.values.script?.ifAttr?.itemA.option
                : formik.values.script?.ifAttr?.itemB.option;

        const renderPrintFormErrorValue = () => {
            if (gubun === 'A') {
                return (
                    <>{printFormError(formik, 'script.ifAttr.itemA.data')}</>
                );
            }
            return <>{printFormError(formik, 'script.ifAttr.itemB.data')}</>;
        };
        if (option === 'TEXT' || option === 'NUMBER') {
            return (
                <>
                    <Col xs="12">
                        {renderCopyBlankData(
                            option === 'TEXT'
                                ? 'Text  '
                                : ' ',
                            option,
                            gubun === 'A'
                                ? 'script.ifAttr.itemA.data'
                                : 'script.ifAttr.itemB.data',
                            formik
                        )}
                        {renderPrintFormErrorValue()}
                    </Col>
                </>
            );
        }
        if (option === 'VARIABLE') {
            return (
                <>
                    <Col xs="12">
                        {renderVariable(
                            gubun === 'A'
                                ? 'script.ifAttr.itemA.variableId'
                                : 'script.ifAttr.itemB.variableId',
                            formik,
                            scenarioData
                        )}
                    </Col>
                </>
            );
        }
        if (option === 'STEP') {
            return (
                <>
                    <Col xs="6">
                        {renderCase(
                            gubun === 'A'
                                ? 'script.ifAttr.itemA.caseId'
                                : 'script.ifAttr.itemB.caseId',
                            formik,
                            scenarioData
                        )}
                    </Col>
                    <Col xs="6">
                        {renderStep(
                            gubun === 'A'
                                ? formik.values.script.ifAttr?.itemA.caseId
                                : formik.values.script.ifAttr?.itemB.caseId,
                            gubun === 'A'
                                ? 'script.ifAttr.itemA.stepId'
                                : 'script.ifAttr.itemB.stepId',
                            formik,
                            scenarioData
                        )}
                    </Col>
                    <Col xs="12">
                        {renderCopyBlankData(
                            '',
                            'TEXT',
                            gubun === 'A'
                                ? 'script.ifAttr.itemA.data'
                                : 'script.ifAttr.itemB.data',
                            formik
                        )}
                    </Col>
                    <Col xs="12">
                        {renderSampleObject(
                            gubun === 'A'
                                ? formik.values.script.ifAttr?.itemA.stepId
                                : formik.values.script.ifAttr?.itemB.stepId,
                            'response',
                            gubun === 'A'
                                ? formik.values.script.ifAttr?.itemA.data
                                : formik.values.script.ifAttr?.itemB.data,
                            scenarioData
                        )}
                    </Col>
                </>
            );
        }
        return '';
    };

    const renderExecuteDeatil = (type) => {
        const postProcessingType =
            type === 'T'
                ? formik.values.script.ifAttr.trueItem?.postProcessingType
                : formik.values.script.ifAttr.falseItem?.postProcessingType;

        if (postProcessingType !== 'SKIP') {
            return '';
        }

        return (
            <>
                <Col xs="6">
                    {renderCase(
                        type === 'T'
                            ? 'script.ifAttr.trueItem.stepId'
                            : 'script.ifAttr.falseItem.stepId',
                        formik,
                        scenarioData
                    )}
                </Col>
                <Col xs="6">
                    {renderStep(
                        type === 'T'
                            ? formik.values.script.ifAttr?.trueItem.caseId
                            : formik.values.script.ifAttr?.falseItem.caseId,
                        type === 'T'
                            ? 'script.ifAttr.trueItem.stepId'
                            : 'script.ifAttr.falseItem.stepId',
                        formik,
                        scenarioData,
                        true
                    )}
                </Col>
            </>
        );
    };

    const renderExecuteDeatilOld = (type, id, caseId) => {
        if (type !== 'jump') {
            return <Col xs="8" />;
        }

        const caseIdValue =
            caseId === 'ifExecuteJumpCaseId'
                ? formik.values.ifExecuteJumpCaseId
                : formik.values.ifExecuteJumpCaseIdF;
        return (
            <>
                <Col xs="4">
                    <Form.Label>Case</Form.Label>
                    <Form.Control as="select" {...formik.getFieldProps(caseId)}>
                        <option value="" selected>
                            {t('common.text.optionAll')}
                        </option>
                        {scenarioData &&
                            scenarioData.data.caseList
                                .filter((obj) => obj.id > -1)
                                .map((obj) => {
                                    return (
                                        <option value={obj.id}>
                                            {`${obj.idx + 1} - ${obj.caseName}`}
                                        </option>
                                    );
                                })}
                    </Form.Control>
                </Col>
                <Col xs="4">
                    <Form.Label>Step</Form.Label>
                    <Form.Control as="select" {...formik.getFieldProps(id)}>
                        <option value="" selected>
                            {t('common.text.optionNone')}
                        </option>
                        {executeList &&
                            executeList.stepList
                                .filter(
                                    (obj) =>
                                        obj.id > -1 &&
                                        (caseIdValue === '' ||
                                            obj.cazsId === Number(caseIdValue))
                                )
                                // .filter((obj) => obj.id > -1 && )
                                .map((obj) => {
                                    return (
                                        <option value={obj.id}>
                                            {`[${obj.stepCode}]  ${obj.stepName}`}
                                        </option>
                                    );
                                })}
                    </Form.Control>
                    {printFormError(formik, id)}
                </Col>
            </>
        );
    };
    return (
        <>
            <Row>
                <Col xs="5">
                    <Accordion defaultActiveKey="0">
                        <Accordion.Item eventKey="0">
                            <Accordion.Header className="accordion-header2">
                                 A
                            </Accordion.Header>
                            <Accordion.Body>
                                <Row>
                                    <Col xs="12">
                                        <FloatingLabel
                                            label=" A "
                                            className="mb-2"
                                        >
                                            <Form.Select
                                                {...formik.getFieldProps(
                                                    'script.ifAttr.itemA.option'
                                                )}
                                            >
                                                {takeSelectOptionbyCode(
                                                    codeData,
                                                    'ScriptOption',
                                                    4
                                                )}
                                            </Form.Select>
                                        </FloatingLabel>
                                    </Col>
                                    {renderIfDetail('A')}
                                </Row>
                            </Accordion.Body>
                        </Accordion.Item>
                    </Accordion>
                </Col>
                <Col xs="1">
                    <FloatingLabel label="" className="mb-2">
                        <Form.Select
                            {...formik.getFieldProps('script.ifAttr.ifFormula')}
                        >
                            {takeSelectOptionbyCode(codeData, 'IfFormula')}
                        </Form.Select>
                    </FloatingLabel>
                </Col>
                <Col xs="5">
                    <Accordion defaultActiveKey="0">
                        <Accordion.Item eventKey="0">
                            <Accordion.Header className="accordion-header3">
                                 B
                            </Accordion.Header>
                            <Accordion.Body>
                                <Row>
                                    <Col xs="12">
                                        <FloatingLabel
                                            label=" B "
                                            className="mb-2"
                                        >
                                            <Form.Select
                                                {...formik.getFieldProps(
                                                    'script.ifAttr.itemB.option'
                                                )}
                                            >
                                                {takeSelectOptionbyCode(
                                                    codeData,
                                                    'ScriptOption',
                                                    4
                                                )}
                                            </Form.Select>
                                        </FloatingLabel>
                                    </Col>
                                    {renderIfDetail('B')}
                                </Row>
                            </Accordion.Body>
                        </Accordion.Item>
                    </Accordion>
                </Col>
                <Col xs="11">
                    <br />
                    <Accordion defaultActiveKey="0">
                        <Accordion.Item eventKey="0">
                            <Accordion.Header className="accordion-header4">
                                 
                            </Accordion.Header>
                            <Accordion.Body>
                                <Row>
                                    <Col xs="6">
                                        <Form.Label>
                                            <b>  </b>
                                        </Form.Label>
                                        <Row>
                                            <Col xs="12">
                                                <FloatingLabel
                                                    label=" "
                                                    className="mb-2"
                                                >
                                                    <Form.Select
                                                        {...formik.getFieldProps(
                                                            'script.ifAttr.trueItem.postProcessingType'
                                                        )}
                                                    >
                                                        {takeSelectOptionbyCode(
                                                            codeData,
                                                            'PostProcessingType'
                                                        )}
                                                    </Form.Select>
                                                </FloatingLabel>
                                            </Col>
                                            {renderExecuteDeatil('T')}
                                        </Row>
                                    </Col>
                                    <Col xs="6">
                                        <Form.Label>
                                            <b>  </b>
                                        </Form.Label>
                                        <Row>
                                            <Col xs="12">
                                                <FloatingLabel
                                                    label=" "
                                                    className="mb-2"
                                                >
                                                    <Form.Select
                                                        {...formik.getFieldProps(
                                                            'script.ifAttr.falseItem.postProcessingType'
                                                        )}
                                                    >
                                                        {takeSelectOptionbyCode(
                                                            codeData,
                                                            'PostProcessingType'
                                                        )}
                                                    </Form.Select>
                                                </FloatingLabel>
                                            </Col>
                                            {renderExecuteDeatil('F')}
                                        </Row>
                                    </Col>
                                </Row>
                            </Accordion.Body>
                        </Accordion.Item>
                    </Accordion>
                </Col>
            </Row>
        </>
    );
};

export default UitestScriptIf;
