/* eslint-disable import/prefer-default-export */

import axios from 'axios';

export const searchLogSnapshot = async ({
    fromDate,
    toDate,
    collectionMethod,
    page = 1,
    size = 10
}) => {
    const response = await axios.get(
        `/logs/snapshots?sort=id,desc&fromDate=${fromDate}&toDate=${toDate}&collectionMethod=${collectionMethod}&page=${
            page - 1
        }&size=${size}`
    );
    return response;
};

// execute log collector
export const executeLogCollector = async ({
    collectionMethod,
    referenceServerId,
    directoryPath
}) => {
    const response = await axios.post('/logs/analysis', {
        collectionMethod,
        referenceServerId,
        directoryPath
    });
    return response;
};

export const deleteLogSnapshot = async ({id}) => {
    const response = await axios.delete(`/logs/snapshots/${id}`, null);
    return response;
};

export const searchLogs = async ({
    fromDateTime,
    toDateTime,
    logSnapshotId,
    serviceId,
    page = 1,
    size = 10
}) => {
    const response = await axios.get(
        `/logs?sort=id,desc&fromDateTime=${fromDateTime}&toDateTime=${toDateTime}&logSnapshotId=${logSnapshotId}&serviceId=${serviceId}&page=${
            page - 1
        }&size=${size}`
    );
    return response;
};

export const searchLog = async ({logId}) => {
    const response = await axios.get(`/logs/${logId}`, null);
    return response;
};
