/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import {toast} from 'react-toastify';
import ServerUpdate from '@app/pages/server/ServerUpdate';
import {useTranslation} from 'react-i18next';
import Spinner from '../../components/spinner/Spinner';
import {getErrorMsg} from '../../lib/commonUiUtils';
import {
    checkDbConnection,
    checkDbConnectionClear,
    searchServerCommon
} from '../../store/common';
import {
    createServer,
    createServerDataClear,
    updateServer,
    updateServerDataClear,
    searchServer,
    usedUpdateServerInitData,
    findServer,
    clearFindServer
} from '../../store/server';

function ServerUpdateContainer() {
    const dispatch = useDispatch();
    const [t] = useTranslation();

    const initData = {
        id: -1,
        serverName: '',
        url: '',
        serverType: 'HTTP',
        dataFormat: 'Json',
        loginProcess: {
            loginMethod: 'NONE',
            sessionKey: ''
        },
        // DATABASE
        dbms: 'Oracle',
        connectionInfo: {
            connectedId: '',
            connectedPw: ''
        },
        // TCP
        ipPort: {
            ip: '',
            port: 0
        },
        // UI
        referenceServerId: ''
    };

    const [modalShow, setModalShow] = React.useState(false);
    const [serverData, setServerData] = React.useState(initData);

    const handleClose = () => {
        setServerData(initData);
        setModalShow(false);
    };
    const handleShow = () => setModalShow(true);

    const {updateFormInitData} = useSelector((state) => state.server);

    const {data: codeData} = useSelector((state) => state.common.searchCode);

    const {data: serverList, loading: searchServerListLoading} = useSelector(
        (state) => state.common.searchServer
    );

    //    
    const {data: memberInfoData} = useSelector(
        (state) => state.auth.memberInfo
    );

    const [parentId, setParentId] = React.useState('ServerTable');

    useEffect(() => {
        if (!updateFormInitData.useData && !modalShow) {
            //   
            if (updateFormInitData.id !== -1) {
                setParentId(updateFormInitData.id);
                dispatch(findServer(updateFormInitData.id));

                setServerData(initData);
                // if (updateFormInitData.parentId) {
                //     setParentId(updateFormInitData.parentId);
                // }
                // setServerData({...updateFormInitData});
            } else {
                handleShow();
            }
            dispatch(usedUpdateServerInitData());
        }
    }, [updateFormInitData.useData]);

    const {
        loading: createLoading,
        data: createResponse,
        error: createError
    } = useSelector((state) => state.server.create);

    const {
        loading: updateLoading,
        data: updateResponse,
        error: updateError
    } = useSelector((state) => state.server.update);

    const {searchForm} = useSelector((state) => state.server);

    const onUpdate = (values) => {
        if (values.id === -1) {
            dispatch(createServer(values));
        } else {
            dispatch(updateServer(values));
        }
    };

    useEffect(() => {
        if (!createLoading && createResponse) {
            dispatch(createServerDataClear());
            dispatch(searchServer(searchForm));

            // 20220109 kimtaehan   
            dispatch(searchServerCommon(''));
            toast.success(t('common.alert.updateSuccess'));
            handleClose();
        }

        // fail
        if (!createLoading && createError) {
            dispatch(createServerDataClear());
            toast.error(getErrorMsg(createError, 'save'));
        }
    }, [createResponse, createError]);

    useEffect(() => {
        if (!updateLoading && updateResponse) {
            dispatch(updateServerDataClear());
            dispatch(searchServer(searchForm));

            // 20220109 kimtaehan   
            dispatch(searchServerCommon(''));
            toast.success(t('common.alert.updateSuccess'));
            handleClose();
        }

        // fail
        if (!updateLoading && updateError) {
            dispatch(updateServerDataClear());
            toast.error(getErrorMsg(updateError, 'save'));
        }
    }, [updateResponse, updateError]);

    const {
        loading: selectLoading,
        data: selectResponse,
        error: selectError
    } = useSelector((state) => state.server.searchDetail);

    useEffect(() => {
        if (!selectLoading && selectResponse) {
            setServerData({...selectResponse?.data});
            handleShow();
        }

        // fail
        if (!selectLoading && selectError) {
            toast.error(getErrorMsg(selectError, 'select'));
        }

        dispatch(clearFindServer());
    }, [selectResponse, selectError]);

    const onCheckDbConnection = (obj) => {
        dispatch(checkDbConnection(obj));
    };
    const {
        loading: checkLoading,
        data: checkResponse,
        error: checkError
    } = useSelector((state) => state.common.checkDbConnection);

    useEffect(() => {
        if (!checkLoading && checkResponse) {
            if (checkResponse.data) {
                toast.success('DB Connection Success');
            } else toast.error(getErrorMsg(checkError, 'DB Connection Fail'));
            dispatch(checkDbConnectionClear());
        }

        // fail
        if (!checkLoading && checkError) {
            toast.error(getErrorMsg(checkError, 'DB Connection Fail'));
            dispatch(checkDbConnectionClear());
        }
    }, [checkResponse, checkError]);
    return (
        <>
            <ServerUpdate
                serverData={serverData}
                updateLoading={updateLoading}
                onUpdate={onUpdate}
                modalShow={modalShow}
                handleClose={handleClose}
                onCheckDbConnection={onCheckDbConnection} // DB  
                serverList={serverList}
                codeData={codeData}
                // memberInfoData={memberInfoData}
                // parentId={parentId}
            />
            <Spinner
                isLoading={
                    updateLoading ||
                    checkLoading ||
                    searchServerListLoading ||
                    createLoading
                }
            />
        </>
    );
}

export default ServerUpdateContainer;
