/* eslint-disable no-unused-vars */
import React from 'react';
import Modal from 'react-bootstrap/Modal';
import {Form, Row, Col, FloatingLabel} from 'react-bootstrap';
import {useFormik} from 'formik';
import * as Yup from 'yup';
import {useTranslation} from 'react-i18next';
import Button from '../../../components/button/Button';
import {printFormError, renderModalHeader} from '../../../lib/commonUiUtils';

const UitestResultPopup = ({
    modalShow,
    handleClose,
    xhrInfo,
    scenarioData,
    saveXhrList
}) => {
    const [t] = useTranslation();
    const formik = useFormik({
        initialValues: {
            caseId: '',
            caseName: ''
        },
        validationSchema: Yup.object({
            caseId: Yup.number().required(
                'xhr   case .'
            )
        }),
        onSubmit: (values) => {
            saveXhrList(values);
        }
    });

    const renderRregisterButtonShow = () => {
        if (!modalShow) return true;

        if (xhrInfo.list.length > 0) {
            return false;
        }
        return true;
    };
    const renderXhrInfo = () => {
        if (!modalShow) return '';
        return `${xhrInfo.list.length}  .`;
    };
    const renderCaseList = () => {
        if (!modalShow) return '';
        return scenarioData.data.cases.map((caze) => {
            return (
                <option value={caze.caseId}>
                    {caze.caseName} ({caze.steps ? caze.steps.length : 0}
                    step)
                </option>
            );
        });
    };
    const renderCaseName = () => {
        if (!modalShow) return '';
        if (formik.values.caseId !== '-1') {
            return '';
        }
        return (
            <FloatingLabel label="case name" className="mb-3">
                <Form.Control
                    type="text"
                    maxLength="100"
                    {...formik.getFieldProps('caseName')}
                />
            </FloatingLabel>
        );
    };

    return (
        <>
            <Modal
                show={modalShow}
                onHide={handleClose}
                dialogClassName="modal-20w"
                onExited={() => formik.resetForm()}
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Form onSubmit={formik.handleSubmit}>
                    {renderModalHeader(t('pages.scenario.caseSelect'))}

                    <Modal.Body>
                        <Row>
                            <Col xs="12">
                                <FloatingLabel
                                    label={renderXhrInfo()}
                                    className="mb-3"
                                >
                                    <Form.Select
                                        as="select"
                                        {...formik.getFieldProps('caseId')}
                                    >
                                        <option value="">
                                            {t('common.text.optionNone')}
                                        </option>
                                        <option value="-1">
                                             Case 
                                        </option>
                                        {renderCaseList()}
                                    </Form.Select>
                                    {printFormError(formik, 'caseId')}
                                </FloatingLabel>
                            </Col>
                            <Col xs="12">{renderCaseName()}</Col>
                        </Row>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button
                            theme="primary"
                            type="submit"
                            disabled={renderRregisterButtonShow()}
                            // isLoading={createLoading}
                        >
                            {t('common.button.register')}
                        </Button>
                        <Button theme="default" onClick={handleClose}>
                            {t('common.button.cancle')}
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>
        </>
    );
};

export default UitestResultPopup;
