/* eslint-disable no-shadow */
/* eslint-disable no-unused-vars */
import React from 'react';
import {Form, Modal, Col, Row, FloatingLabel, Accordion} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
import {useFormik} from 'formik';
import * as Yup from 'yup';
import Button from '../../../components/button/Button';
import {printFormError, getInputValidMsg} from '../../../lib/commonUiUtils';
import {jsonStrFormatPretty} from '../../../lib/jsonUtils';

// eslint-disable-next-line no-unused-vars
const UitestStepPopQuery = ({
    scenarioData,
    selectedCaseId,
    setDetailCaseList,
    handleClose,
    stepUpdateInitData,
    onChangeStepUpdataInitData,
    tempkey,
    setTempKeyRezen,
    serverData,
    onClickDbTest,
    selectedServer,
    setSelectedServer,
    executeSqlTestLoading,
    resultData
}) => {
    const [t] = useTranslation();
    const onChangeForm = (e) => {
        if (stepUpdateInitData.id) {
            e.preventDefault();
            const newStepUpdateInitData = {
                ...stepUpdateInitData
            };
            newStepUpdateInitData[e.target.id] = e.target.value;
            onChangeStepUpdataInitData(newStepUpdateInitData);
        }
    };

    const formik = useFormik({
        initialValues: {
            stepId: stepUpdateInitData?.stepId ?? -1,
            index: stepUpdateInitData?.index ?? -1,
            stepName: stepUpdateInitData?.stepName ?? '',
            inputData: stepUpdateInitData?.inputData ?? '',
            outputData: jsonStrFormatPretty(stepUpdateInitData?.outputData),
            stepType: 'QUERY',
            isSkipError: stepUpdateInitData?.isSkipError ?? false
        },

        validationSchema: Yup.object({
            // inputData1: Yup.string().required(getInputValidMsg('Query'))
            stepName: Yup.string().required(
                getInputValidMsg(t('pages.scenario.title'))
            )
        }),
        onSubmit: (values) => {
            const cases = [...scenarioData.data.cases];

            const caseLoop = cases.map((cUnit, cIndex) => {
                const theCase = cUnit;

                if (cUnit.caseId === selectedCaseId) {
                    let data = theCase.steps;
                    if (data == null) {
                        data = [];
                    }

                    const newStep = values;
                    //    
                    if (!stepUpdateInitData.stepId) {
                        // setTempKeyRezen();
                        newStep.stepId = tempkey;
                        setTempKeyRezen();
                        newStep.outputData = resultData;
                        data.push(newStep);
                        const aLoop = data.map((unit, index) => {
                            const temp = unit;
                            temp.index = index;
                            return temp;
                        });

                        theCase.steps = aLoop;
                    }
                    //   
                    else {
                        const aLoop = data.map((unit) => {
                            if (
                                unit.stepId === newStep.stepId &&
                                unit.index === newStep.index
                            ) {
                                newStep.outputData = resultData;
                                return newStep;
                            }
                            return unit;
                        });

                        theCase.steps = aLoop;
                    }
                }
                return theCase;
            });
            setDetailCaseList(caseLoop);
            handleClose();
        }
    });

    const renderTestButton = () => {
        // if (selectedServer === '') return '';
        return (
            <Button
                theme="danger"
                onClick={(e) => onClickDbTest(formik.values.inputData)}
                isLoading={executeSqlTestLoading}
                disabled={selectedServer === ''}
            >
                Test
            </Button>
        );
    };

    return (
        <>
            <Form onSubmit={formik.handleSubmit}>
                <Accordion defaultActiveKey="0">
                    <Accordion.Item eventKey="0">
                        <Accordion.Header className="accordion-header3">
                            {t('pages.scenario.infoQuery')}
                        </Accordion.Header>
                        <Accordion.Body>
                            <Row>
                                <Col xs="5">
                                    <FloatingLabel
                                        label={t('pages.scenario.title')}
                                        className="mb-2"
                                    >
                                        <Form.Control
                                            type="text"
                                            {...formik.getFieldProps(
                                                'stepName'
                                            )}
                                            maxLength="100"
                                        />
                                        {printFormError(formik, 'stepName')}
                                    </FloatingLabel>
                                </Col>
                                <Col xs="3">
                                    <FloatingLabel
                                        className="mb-2"
                                        label=""
                                    >
                                        <Form.Select
                                            value={selectedServer}
                                            onChange={(e) =>
                                                setSelectedServer(
                                                    e.target.value
                                                )
                                            }
                                        >
                                            <option value="">
                                                {t('common.text.optionNone')}
                                            </option>
                                            {serverData?.data
                                                ?.filter(
                                                    (obj) =>
                                                        obj.serverType ===
                                                        'DATABASE'
                                                )
                                                .map((obj) => (
                                                    <option
                                                        value={obj.serverId}
                                                        key={obj.serverId}
                                                    >
                                                        {obj.serverName}
                                                    </option>
                                                ))}
                                        </Form.Select>
                                    </FloatingLabel>
                                </Col>
                                <Col xs="1">{renderTestButton()}</Col>

                                <Col xs="12">
                                    <div key="isSkipError" className="mb-3">
                                        <Form.Check
                                            type="checkbox"
                                            id="isSkipError"
                                            label={t(
                                                'pages.scenario.isSkipError'
                                            )}
                                            {...formik.getFieldProps(
                                                'isSkipError'
                                            )}
                                            checked={formik.values.isSkipError}
                                        />
                                    </div>
                                </Col>
                                <Col xs="6">
                                    <FloatingLabel
                                        label="Request Body"
                                        className="mb-2"
                                    >
                                        <Form.Control
                                            as="textarea"
                                            style={{height: '433px'}}
                                            {...formik.getFieldProps(
                                                'inputData'
                                            )}
                                        />
                                    </FloatingLabel>
                                </Col>
                                <Col xs="6">
                                    <Row>
                                        <Col xs="4" />
                                        <Col xs="12">
                                            <FloatingLabel
                                                label="Response Body"
                                                className="mb-2"
                                            >
                                                <Form.Control
                                                    as="textarea"
                                                    disabled
                                                    style={{height: '433px'}}
                                                    value={
                                                        (resultData &&
                                                            jsonStrFormatPretty(
                                                                resultData
                                                            )) ||
                                                        ''
                                                    }
                                                />
                                            </FloatingLabel>
                                        </Col>
                                    </Row>
                                </Col>
                            </Row>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
                <Row />

                <Modal.Footer>
                    <Button
                        theme="primary"
                        type="submit"
                        // isLoading={createLoading}
                    >
                        {t('common.button.register')}
                    </Button>
                    <Button theme="default" onClick={handleClose}>
                        {t('common.button.close')}
                    </Button>
                </Modal.Footer>
            </Form>
        </>
    );
};

export default UitestStepPopQuery;
