/* eslint-disable no-unused-vars */
/* eslint-disable jsx-a11y/label-has-associated-control */
import React from 'react';
import {useTranslation} from 'react-i18next';
import {Row, Col, Form, FloatingLabel} from 'react-bootstrap';
import Card from '../../components/card/Card';
import Button from '../../components/button/Button';

const ScHistorySearch = ({
    searchFormH,
    onChangeForm,
    onClickSearch,
    codeData,
    memberInfoData
}) => {
    const [t] = useTranslation();
    const onChangerSearchFormH = (e) => {
        e.preventDefault();
        const preSearchFormH = {...searchFormH};
        preSearchFormH[e.target.id] = e.target.value;
        onChangeForm(preSearchFormH);
    };

    const renderScTestCode = () => {
        return codeData?.data
            .filter((obj) => obj.codeGroup === 'sctest_cd')
            .map((obj) => (
                <option value={obj.codeKey} key={obj.codeKey}>
                    {obj.codeName}
                </option>
            ));
    };

    const onCheckEnter = (e) => {
        if (e.key === 'Enter') {
            e.preventDefault();
            onClickSearch();
        }
    };

    return (
        <Card
            title={t('common.text.searchCardTitleName')}
            body={
                <>
                    <Row>
                        <Col xs="6">
                            <Row onKeyPress={onCheckEnter}>
                                <Col xs="6">
                                    <FloatingLabel
                                        label={t('pages.scenario.scenarioName')}
                                        className="mb-2"
                                    >
                                        <Form.Control
                                            type="text"
                                            maxLength="30"
                                            id="scenarioName"
                                            onChange={onChangerSearchFormH}
                                            value={searchFormH.scenarioName}
                                        />
                                    </FloatingLabel>
                                </Col>
                                <Col xs="3">
                                    <FloatingLabel
                                        label={t('pages.scenario.manager')}
                                        className="mb-2"
                                    >
                                        <Form.Select
                                            id="manager"
                                            onChange={onChangerSearchFormH}
                                            value={searchFormH.manager}
                                        >
                                            <option value="">
                                                {t('common.text.optionAll')}
                                            </option>
                                            <option
                                                value={
                                                    memberInfoData?.data?.userId
                                                }
                                            >
                                                {memberInfoData?.data?.name}
                                            </option>
                                        </Form.Select>
                                    </FloatingLabel>
                                </Col>
                                <Col xs="3">
                                    <FloatingLabel
                                        label={t('common.text.testResult')}
                                        className="mb-2"
                                    >
                                        <Form.Select
                                            id="isSuccess"
                                            onChange={onChangerSearchFormH}
                                            value={searchFormH.isSuccess ?? ''}
                                        >
                                            <option value="">
                                                {t('common.text.optionAll')}
                                            </option>
                                            <option value></option>
                                            <option value={false}></option>
                                        </Form.Select>
                                    </FloatingLabel>
                                </Col>
                            </Row>
                        </Col>
                        <Col xs="3">
                            <Row>
                                <Col xs="6">
                                    <FloatingLabel
                                        label="From"
                                        className="mb-2"
                                    >
                                        <Form.Control
                                            type="date"
                                            id="startTime"
                                            onChange={onChangerSearchFormH}
                                            value={searchFormH.startTime}
                                        />
                                    </FloatingLabel>
                                </Col>
                                <Col xs="6">
                                    <FloatingLabel label="To" className="mb-2">
                                        <Form.Control
                                            type="date"
                                            id="endTime"
                                            onChange={onChangerSearchFormH}
                                            value={searchFormH.endTime}
                                        />
                                    </FloatingLabel>
                                </Col>
                            </Row>
                        </Col>
                        <Col xs="2" />
                        <Col xs="1">
                            <div className="form-group">
                                <label htmlFor="myinput" />
                                <Button
                                    type="button"
                                    className="btn btn-block btn-secondary btn-search"
                                    onClick={(e) => onClickSearch()}
                                >
                                    {t('common.button.search')}
                                </Button>
                            </div>
                        </Col>
                    </Row>
                </>
            }
        />
    );
};

export default ScHistorySearch;
