/* eslint-disable no-unused-vars */
import React from 'react';
import {FaSearch, FaTrash} from 'react-icons/fa';
import {useTranslation} from 'react-i18next';
import Table from '../../components/table/Table';
import Button from '../../components/button/Button';
import {getValue} from '../../lib/codeUtils';
import {
    changeToDate,
    changeToProcessTime,
    changeToCommonFormat
} from '../../lib/moment';
import {renderBatchStatus} from '../../lib/buttonRender';

const LogTable = ({
    codeData,
    data,
    movePage,
    onClickMove,
    onClickDelete,
    memberInfoData
}) => {
    const [t] = useTranslation();
    const tableName = t('pages.history.historyList');
    const rowKey = 'logSnapshotId';

    const columns = [
        {
            title: ' ',
            key: 'batchStatus',
            width: '10%',
            render: (id, row) => {
                return renderBatchStatus(row.batchInfo?.batchStatus);
            }
        },
        {
            title: ' ',
            key: 'startedDateTime',
            width: '10%',
            render: (id, row) => {
                return changeToDate(row.batchInfo?.startedDateTime);
            }
        },
        {
            title: ' ',
            key: 'processTime',
            width: '10%',
            render: (id, row) => {
                return changeToProcessTime(row.batchInfo?.processTime);
            }
        },
        {
            title: '',
            key: 'serverName',
            render: (id, row) => {
                return row.serverName;
            }
        },
        {
            title: ' ',
            key: 'collectionMethod',
            width: '10%',
            render: (id, row) => {
                return getValue(
                    codeData,
                    'CollectionMethod',
                    row.collectionMethod
                );
            }
        },

        {
            title: ' ',
            key: 'logFileCount',
            width: '10%',
            render: (id, row) => {
                return `${row.logFileCount}`;
            }
        },
        {
            title: ' ',
            key: 'logCount',
            width: '10%',
            render: (id, row) => {
                return `${row.logCount}`;
            }
        },
        // "sendDateTime":{"fastest":"2023-03-07T15:08:10.088465","slowest":"2023-03-07T15:14:30.380165"}}
        {
            title: ' ',
            key: 'sendDateTime',
            width: '10%',
            render: (id, row) => {
                return `${changeToCommonFormat(
                    row.sendDateTime?.fastest
                )} ~ ${changeToCommonFormat(row.sendDateTime?.slowest)}`;
            }
        },
        {
            title: '',
            width: '5%',
            key: 'button',
            // eslint-disable-next-line no-unused-vars
            render: (id, row, column) => {
                return (
                    <>
                        {/* Move History Page */}
                        <Button
                            theme="outline-success"
                            onClick={() => onClickMove(id, row)}
                        >
                            <FaSearch />
                        </Button>
                        <Button
                            theme="outline-danger"
                            onClick={() => onClickDelete(id)}
                            //  
                            disabled={memberInfoData?.data?.role !== 'ADMIN'}
                        >
                            <FaTrash />
                        </Button>
                    </>
                );
            }
        }
    ];

    return (
        <>
            <Table
                tableName={tableName}
                movePage={movePage}
                rowKey={rowKey}
                columns={columns}
                data={data}
                onDoubleClick={onClickMove}
            />
        </>
    );
};

export default LogTable;
