/* eslint-disable no-debugger */
/* eslint-disable no-unused-vars */
import {useSelector, useDispatch} from 'react-redux';
import {useHistory} from 'react-router-dom';
import React, {useState, useEffect} from 'react';
import ScenarioTestPopup from '@pages/common/ScenarioTestPopup';
import {EXECUTE_STATE} from '@app/lib/UitestCommon';
import {useTranslation} from 'react-i18next';
import {
    clearScenarioTestModal,
    executeStepByServer,
    executeStepDataClear,
    setExecuteStepCallBack
} from '../../store/common';
import {setUpdateServerInitData} from '../../store/server';
import {
    searchRandomAll,
    testRandom,
    testRandomDataClear
} from '../../store/random';
import {jsonStrFormatPretty, jsonStrFormatNotPretty} from '../../lib/jsonUtils';
import {setDetailCaseList} from '../../store/uitest';
import {saveScenarioHistory} from '../../store/scenario';
import {checkFomular, getJsonObj} from '../../lib/UitestCommon';

/*
 *   /  
 * ,   
 */
function ScenarioTestPopupContainer() {
    const [t] = useTranslation();
    const dispatch = useDispatch();
    const history = useHistory();

    const {data: codeData} = useSelector((state) => state.common.searchCode);

    const [modalShow, setModalShow] = React.useState(false);

    //  
    const [executeList, setExecuteList] = React.useState([]);

    // execute List selected 
    const [selectedNavId, setSelectedNavId] = React.useState(0);

    //   
    const [isProcessTest, setIsProcessTest] = useState(false);

    // Session id 
    const [sessionInfo, setSessionInfo] = React.useState({});

    //   
    const [startTime, setStartTime] = React.useState('');

    const {isFinish} = useSelector(
        (state) => state.common.setExecuteStepCallBack
    );

    //   
    const {data: scenarioData} = useSelector(
        (state) => state.uitest.scenarioInfo
    );

    const handleClose = () => {
        setModalShow(false);
    };
    const handleShow = () => {
        setSelectedNavId(0);
        setIsProcessTest(false);
        setModalShow(true);
    };

    const {data: serverData} = useSelector(
        (state) => state.common.searchServer
    );

    const {loading: searchLoading, data: randData} = useSelector(
        (state) => state.random.searchList
    );

    const {
        loading: testLoading,
        data: testResponse,
        error: testError
    } = useSelector((state) => state.random.test);

    const {scenarioTestInitData} = useSelector((state) => state.common);

    /**
     *    .
     */
    const onClickMoveScenario2 = () => {
        history.push({
            pathname: '/uitest',
            state: {
                key: executeList.scenarioId
            }
        });
    };

    const onClickServerInfo = (serverId) => {
        const selectedServer = serverData.data.content.filter(
            (obj) => obj.id === Number(serverId)
        );

        if (selectedServer.length !== 1) {
            // eslint-disable-next-line no-alert
            alert('   .');
            return;
        }

        const tempData = {...selectedServer[0]};
        tempData.parentId = 'ClubExecutePopup';
        dispatch(setUpdateServerInitData(tempData));
    };

    const initExecuteData = (e) => {
        e?.preventDefault();

        const executeListTemp = {...scenarioTestInitData.executeList};
        executeListTemp.xhrCount = 0;
        executeListTemp.queryCount = 0;
        const tempSteps = [];
        const tempVariables = [];

        // 20220427 kimtaehan  initialValue -> changeValue
        executeListTemp.variables.forEach((variable) => {
            const variableT = {...variable};
            if (executeListTemp.parentId !== 'ScHistoryTable') {
                variableT.changeValue = variableT.initialValue;
            }

            tempVariables.push(variableT);
        });
        executeListTemp.variables = tempVariables;
        if (executeListTemp.parentId === 'UitestScenario') {
            if (scenarioTestInitData.executeList.cases) {
                scenarioTestInitData.executeList.cases.forEach(
                    (caze, cazeIdx) => {
                        const {caseName} = caze;
                        if (caze.steps) {
                            caze.steps.forEach((step, stepIdx) => {
                                const tempStep = {...step};

                                //   
                                tempStep.cazeIdx = cazeIdx;
                                tempStep.stepIdx = stepIdx;
                                tempStep.caseName = caseName;
                                tempStep.stepName = step.stepName;
                                tempStep.outputData = '';
                                if (
                                    tempStep.stepType === 'API' ||
                                    tempStep.stepType === 'LOGIN'
                                ) {
                                    executeListTemp.xhrCount += 1;
                                } else if (tempStep.stepType === 'QUERY') {
                                    executeListTemp.queryCount += 1;
                                }

                                tempStep.seq = tempSteps.length;
                                tempStep.state = EXECUTE_STATE.WAIT;
                                tempSteps.push(tempStep);
                            });
                        }
                    }
                );
            }
        }
        if (executeListTemp.parentId === 'ScHistoryTable') {
            executeListTemp.steps.forEach((step, stepIdx) => {
                const tempStep = {...step};

                tempStep.cazeIdx = -1;
                tempStep.stepIdx = stepIdx;
                tempStep.caseName = step.caseName;
                tempStep.stepName = step.stepName;
                if (tempStep.stepType === 'API') {
                    executeListTemp.xhrCount += 1;
                } else if (tempStep.stepType === 'QUERY') {
                    executeListTemp.queryCount += 1;
                }
                tempStep.seq = step.seq;
                tempSteps.push(tempStep);
            });
        }
        if (executeList.dbServerId != null) {
            executeListTemp.dbServerId = executeList.dbServerId;
        }
        if (executeList.wasServerId != null) {
            executeListTemp.wasServerId = executeList.wasServerId;
        }
        if (executeList.tokenInfo != null) {
            executeListTemp.tokenInfo = executeList.tokenInfo;
        }

        executeListTemp.steps = tempSteps;

        setExecuteList(executeListTemp);
    };

    useEffect(() => {
        if (scenarioTestInitData.showModal && !modalShow) {
            setSessionInfo({});
            initExecuteData();
            dispatch(clearScenarioTestModal());
            handleShow();
        }
    }, [scenarioTestInitData.showModal]);

    /** ****************************   execute service . */

    /*
     * afterTestService  :    
     * 1. scenario       (   )
     * 2. scHistory table    
     * 3.      executeList  
     */
    const afterTestService = (isSuccess) => {
        setIsProcessTest(false);

        //  
        const offset = new Date().getTimezoneOffset() * 60000;
        const today = new Date(Date.now() - offset);

        // 1. scenario       (   )
        if (executeList.parentId === 'UitestScenario') {
            const scenarioDataNew = {...scenarioData};

            const caseListNew = scenarioDataNew.data.cases.map((_caze) => {
                const caze = _caze;
                const stepListNew = caze.steps.map((_step) => {
                    const step = _step;
                    const changeStep = executeList.steps.filter(
                        (obj) => obj.stepId === step.stepId
                    );
                    if (changeStep.length > 0) {
                        step.outputData = changeStep[0].outputData;
                    }
                    return step;
                });
                caze.steps = stepListNew;
                return caze;
            });
            dispatch(setDetailCaseList(caseListNew));
        }

        // 2. scHistory table    
        // 20211025     
        // const saveVariableList = executeList

        const units = [];

        executeList.steps?.forEach((caze, cazeIdx) => {
            units.push(JSON.stringify(caze));
        });

        const data = {
            scenarioId: executeList.scenarioId,
            wasServerId: executeList.wasServerId,
            dbServerId: executeList.dbServerId,
            recordResult: {
                isSuccess,
                startDateTime: startTime,
                endDateTime: today.toISOString().slice(0, -1)
            },
            // sctestCd: 'online', // 
            units,
            variableJson: JSON.stringify(executeList.variables)
        };

        dispatch(saveScenarioHistory(data));

        // 3.        executeList  
        //  +  
        const executeListTemp = {...executeList};
        executeListTemp.startTime = startTime;
        executeListTemp.endTime = today.toISOString().slice(0, -1);

        const temp =
            Date.parse(executeListTemp.endTime) -
            Date.parse(executeListTemp.startTime);
        executeListTemp.isSuccess = isSuccess;
        // TODO endTime - startTime
        executeListTemp.executeSecond = temp / 1000;

        setExecuteList(executeListTemp);
    };

    /*
     * step (xhr )   
     */
    const onExecuteStep = (serverId, step) => {
        dispatch(
            executeStepByServer({
                stepId: step.stepId,
                wasServerId: serverId,
                step,
                sessionInfo
            })
        );
    };
    /*
     * step ( query)   
     */
    const onExecuteStepQuery = (serverId, step) => {
        dispatch(
            executeStepByServer({
                stepId: step.stepId,
                dbServerId: serverId,
                step
            })
        );
    };

    /*
     * step (login)   
     */
    const onExecuteLogin = (serverId, step) => {
        dispatch(
            executeStepByServer({
                stepId: step.stepId,
                wasServerId: serverId,
                step
            })
        );
    };

    /*
     * stateChange :  Step Data 
     */
    const stateChange = (step, outputData) => {
        const executeListTemp = {...executeList};
        const aLoop = executeListTemp.steps.map((unit, index) => {
            // eslint-disable-next-line no-underscore-dangle
            const _step = unit;
            if (_step.index === step.index && _step.stepId === step.stepId) {
                _step.state = EXECUTE_STATE.SUCCESS;
                _step.outputData = outputData; // 'Sleep finish';
            }
            return _step;
        });
        executeListTemp.steps = aLoop;
        setExecuteList(executeListTemp);
    };

    /*
     * ifExecuteProcess : execute   
     */
    const ifExecuteProcess = (step, executeType, type, stepId) => {
        // SUCCESS("  "),
        // FAIL("  "),
        // SKIP(" Step "),
        // NONE(" ");
        if (type === 'SUCCESS') {
            stateChange(step, executeType);
            afterTestService(true);
        } else if (type === 'FAIL') {
            stateChange(step, executeType);
            afterTestService(false);
        } else if (type === 'SKIP') {
            let isSkip = true;
            const executeListTemp = {...executeList};
            const aLoop = executeListTemp.steps.map((unit, index) => {
                // eslint-disable-next-line no-underscore-dangle
                const _step = unit;
                // state = EXECUTE_STATE.WAIT
                if (
                    _step.index === step.index &&
                    _step.stepId === step.stepId
                ) {
                    _step.state = EXECUTE_STATE.SUCCESS;
                    _step.outputData = executeType;
                }
                if (Number(stepId) === Number(_step.stepId)) {
                    isSkip = false;
                }
                if (isSkip && _step.state === EXECUTE_STATE.WAIT) {
                    _step.state = EXECUTE_STATE.SKIP;
                    _step.outputData = 'Skip Case';
                }
                return _step;
            });
            executeListTemp.steps = aLoop;
            setExecuteList(executeListTemp);

            dispatch(setExecuteStepCallBack(true));
        } else {
            // test
            stateChange(step, executeType);
            dispatch(setExecuteStepCallBack(true));
        }
    };

    const getToJsonObject = (jsonKeys, fromData, jsonObj, idx) => {
        const key = jsonKeys[idx];

        // jsonKeys    
        if (jsonKeys.length === idx + 1) {
            // eslint-disable-next-line no-param-reassign
            jsonObj[key] = fromData;
        } else if (
            key.length > 2 &&
            key.substring(0, 1) === '[' &&
            key.substring(key.length - 1, key.length) === ']'
        ) {
            const intKey = Number(
                key.replace('[', '').replace(']', '').replace(/ /gi, '')
            );

            const temp = jsonObj.map((obj, index) => {
                const tempCol = obj;
                if (index === intKey) {
                    return getToJsonObject(
                        jsonKeys,
                        fromData,
                        jsonObj[intKey],
                        idx + 1
                    );
                }
                return tempCol;
            });
            return temp;
        } else {
            const temp = getToJsonObject(
                jsonKeys,
                fromData,
                jsonObj[key],
                idx + 1
            );
            // eslint-disable-next-line no-param-reassign
            jsonObj[key] = temp;
        }
        return jsonObj;
    };

    const onExecuteStepScriptError = (step) => {
        const executeListTemp = {...executeList};
        const {isSkipError} = step;
        const aLoop = executeListTemp.steps.map((unit, index) => {
            // eslint-disable-next-line no-underscore-dangle
            const _step = unit;
            // state = EXECUTE_STATE.WAIT
            if (_step.index === step.index && _step.stepId === step.stepId) {
                _step.state = EXECUTE_STATE.ERROR;
                _step.outputData = 'Script Error';
            }

            return _step;
        });
        executeListTemp.stepList = aLoop;
        setExecuteList(executeListTemp);

        if (isSkipError) {
            dispatch(setExecuteStepCallBack(true));
        } else {
            afterTestService(false);
        }
    };

    const getValue = (scriptObject) => {
        if (scriptObject.option === 'TEXT') {
            return scriptObject.data;
        }
        if (scriptObject.option === 'NUMBER') {
            return Number(scriptObject.data);
        }
        if (scriptObject.option === 'VARIABLE') {
            const tempVariable = executeList.variables.filter(
                (obj) => obj.variableId === Number(scriptObject.variableId)
            );
            if (tempVariable[0].variableType === 'NUMBER') {
                return Number(tempVariable[0].changeValue);
            }
            return tempVariable[0].changeValue;
        }
        if (scriptObject.option === 'STEP') {
            const selectedFromStep = executeList.steps.filter(
                (obj) => obj.stepId === Number(scriptObject.stepId)
            );
            return getJsonObj(
                selectedFromStep[0].outputData,
                scriptObject.data
            );
        }
        return '';
    };

    const setToValue = (scriptObject, value, step) => {
        const executeListTemp = {...executeList};
        if (scriptObject.option === 'VARIABLE') {
            const variableLoop = executeListTemp.variables.map((unit) => {
                const variable = unit;
                if (unit.variableId === Number(scriptObject.variableId)) {
                    variable.changeValue = value;
                }
                return variable;
            });
            executeListTemp.variables = variableLoop;

            const aLoop = executeListTemp.steps.map((unit, index) => {
                // eslint-disable-next-line no-underscore-dangle
                const _step = unit;
                // if (_step.state === EXECUTE_STATE.PROCESS) {
                if (
                    _step.index === step.index &&
                    _step.stepId === step.stepId
                ) {
                    _step.state = EXECUTE_STATE.SUCCESS;
                    _step.outputData = 'Copy finish';
                }

                return _step;
            });
            executeListTemp.steps = aLoop;
        } else {
            const aLoop = executeListTemp.steps.map((unit, index) => {
                // eslint-disable-next-line no-underscore-dangle
                const _step = unit;

                // if (_step.state === EXECUTE_STATE.PROCESS) {
                if (
                    _step.index === step.index &&
                    _step.stepId === step.stepId
                ) {
                    _step.state = EXECUTE_STATE.SUCCESS;
                    _step.outputData = 'Copy finish';
                }
                if (_step.stepId === Number(scriptObject.stepId)) {
                    if (_step.stepType === 'QUERY') {
                        _step.inputData = _step.inputData.replace(
                            // jsonInput.copyToValue,
                            scriptObject.data,
                            `'${value}'`
                        );
                    } else if (_step.stepType === 'API') {
                        const temp = getToJsonObject(
                            scriptObject.data.split('.'),
                            value,
                            JSON.parse(_step.inputData),
                            0
                        );
                        _step.inputData = JSON.stringify(temp);
                    }
                }
                return _step;
            });
        }
        setExecuteList(executeListTemp);
    };

    /*
     * step ( Script 
     */
    const onExecuteStepScript = (step) => {
        const {scriptType} = step.script;
        let fromData = '';
        //  Sleep
        if (scriptType === 'SLEEP') {
            // sleepMs ms   step   step 
            setTimeout(() => {
                //  step 
                stateChange(step, 'Sleep finish');
                //  step 
                dispatch(setExecuteStepCallBack(true));
            }, Number(step.script.sleepMs));
        } else if (scriptType === 'COPY') {
            // FROM data  random, variable, step, string, number
            const {fromItem, toItem, randomId} = step.script.copyAttr;

            if (fromItem.option === 'RANDOM') {
                if (!testLoading && testResponse) {
                    fromData = testResponse.data;
                    dispatch(testRandomDataClear());
                } else {
                    dispatch(
                        testRandom({
                            randomId: Number(randomId)
                        })
                    );
                    return;
                }
            } else {
                fromData = getValue(fromItem);
            }

            setToValue(toItem, fromData, step);
            dispatch(setExecuteStepCallBack(true));
        } else if (scriptType === 'IF') {
            const {itemA, itemB, ifFormula, trueItem, falseItem} =
                step.script.ifAttr;
            const valueA = getValue(itemA);
            const valueB = getValue(itemB);
            const result = checkFomular(valueA, valueB, ifFormula);
            if (result) {
                ifExecuteProcess(
                    step,
                    'executeTrue',
                    trueItem.postProcessingType,
                    trueItem.ifExecuteJumpStepId
                );
            } else {
                ifExecuteProcess(
                    step,
                    'executeFalse',
                    falseItem.postProcessingType,
                    falseItem.ifExecuteJumpStepId
                );
            }
        }
    };

    /*
     * processStep ->   
     */
    const processStep = () => {
        const executeListTemp = {...executeList};

        //    
        let seq = -1;
        const aLoop = executeListTemp.steps.map((unit, index) => {
            // eslint-disable-next-line no-underscore-dangle
            const _step = unit;
            // state = EXECUTE_STATE.WAIT
            if (
                (_step.state === EXECUTE_STATE.WAIT ||
                    _step.state === EXECUTE_STATE.PROCESS) &&
                seq === -1
            ) {
                _step.state = EXECUTE_STATE.PROCESS;
                seq = index;
            }
            return _step;
        });

        //  
        if (seq === -1) {
            // testSuccess();
            afterTestService(true);
            return;
        }
        executeListTemp.steps = aLoop;
        // const

        // xhr   
        if (aLoop[seq].stepType === 'xhr') {
            const bLoop = executeListTemp.stepList.map((unit, index) => {
                // eslint-disable-next-line no-underscore-dangle
                const _step = unit;
                if (_step.stepType === 'xhr') {
                    executeListTemp.variableList.forEach((variable) => {
                        const regexAllCase = new RegExp(
                            `\\$\\$${variable.variableName}`,
                            'gi'
                        );
                        if (variable.variableCodeKey === 'string') {
                            _step.inputData1 = _step.inputData1.replace(
                                regexAllCase,
                                `"${variable.changeValue}"`
                            );
                        }
                        if (variable.variableCodeKey === 'number') {
                            _step.inputData1 = _step.inputData1.replace(
                                regexAllCase,
                                `${variable.changeValue}`
                            );
                        }
                    });
                }
                return _step;
            });
            executeListTemp.stepList = bLoop;
        }

        setExecuteList(executeListTemp);

        const step = executeListTemp.steps[seq];
        if (step.stepType === 'API') {
            onExecuteStep(executeList.wasServerId, step);
        } else if (step.stepType === 'QUERY') {
            onExecuteStepQuery(executeList.dbServerId, step);
        } else if (step.stepType === 'SCRIPT') {
            try {
                onExecuteStepScript(step);
            } catch (e) {
                //  STEP     
                onExecuteStepScriptError(step);
            }
        } else if (step.stepType === 'LOGIN') {
            // 20220713 kimtaehan login    
            //    .
            onExecuteLogin(executeList.wasServerId, step);
        }
    };

    /*
     * playExecuteTrigger ->   
     */
    const playExecuteTrigger = () => {
        dispatch(testRandomDataClear());

        //   
        const offset = new Date().getTimezoneOffset() * 60000;
        const today = new Date(Date.now() - offset);
        setStartTime(today.toISOString().slice(0, -1));
        setIsProcessTest(true);

        // 20211022    
        const executeListTemp = {...executeList};
        const variableLoop = executeListTemp.variables.map((unit) => {
            const variable = unit;
            variable.testValue =
                variable.changeValue && variable.changeValue !== ''
                    ? variable.changeValue
                    : variable.initialValue;
            variable.changeValue = variable.testValue;
            return variable;
        });

        executeListTemp.variables = variableLoop;
        setExecuteList(executeListTemp);
        processStep();
    };

    // execute test (xhr, query)
    const {
        loading: executeLoading,
        error: executeError,
        data: executeResponse
    } = useSelector((state) => state.common.executeStepByServer);

    /**
     * Execute Test Callback
     */
    useEffect(() => {
        // execute step success
        if (!executeLoading && executeResponse) {
            if (
                executeResponse.data.sessionInfo &&
                executeResponse.data.sessionInfo !== null &&
                executeResponse.data.sessionInfo !== ''
            ) {
                setSessionInfo(executeResponse.data.sessionInfo);
            }

            // executeResponse.data.sessionInfo
            const executeListTemp = {...executeList};
            const aLoop = executeListTemp.steps.map((unit, index) => {
                // eslint-disable-next-line no-underscore-dangle
                const _step = unit;
                if (_step.state === EXECUTE_STATE.PROCESS) {
                    if (_step.stepType === 'QUERY') {
                        if (executeResponse.data.isSuccess) {
                            _step.state = EXECUTE_STATE.SUCCESS;
                            _step.outputData =
                                executeResponse.data.dbmsExecute?.dataOutput;
                        }
                    } else if (executeResponse.data.isSuccess) {
                        _step.state = EXECUTE_STATE.SUCCESS;
                        _step.outputData = jsonStrFormatPretty(
                            executeResponse.data.httpResponse
                                ?.responseBodyIntermediate
                        );
                    } else {
                        _step.state = EXECUTE_STATE.ERROR;
                        if (executeResponse.data.httpResponse?.responseBody) {
                            _step.outputData = jsonStrFormatPretty(
                                executeResponse.data.httpResponse
                                    ?.responseBodyIntermediate
                            );
                        }

                        // fail    
                        executeResponse.data.isSuccess = _step.isSkipError;
                    }

                    _step.historyId = executeResponse.data.historyId;
                    _step.responseInfo = executeResponse.data?.httpResponse;
                }

                return _step;
            });
            executeListTemp.steps = aLoop;
            setExecuteList(executeListTemp);
            dispatch(executeStepDataClear());
            if (executeResponse.data.isSuccess) {
                dispatch(setExecuteStepCallBack(true));
            } else {
                afterTestService(false);
            }
        }

        // fail
        if (!executeLoading && executeError) {
            const executeListTemp = {...executeList};
            let isSkipError = false;
            const aLoop = executeListTemp.steps.map((unit, index) => {
                // eslint-disable-next-line no-underscore-dangle
                const _step = unit;

                if (_step.state === EXECUTE_STATE.PROCESS) {
                    _step.state = EXECUTE_STATE.ERROR;

                    // _step.outputData1 = executeError.data.errorMessage;

                    // // fail    
                    // executeResponse.data.isSuccess = _step.isSkipError;
                    isSkipError = _step.isSkipError;

                    // // _step.historyId = executeResponse.data.historyId;
                    _step.result = 'Error';
                }
                return _step;
            });
            executeListTemp.steps = aLoop;
            setExecuteList(executeListTemp);
            dispatch(executeStepDataClear());

            if (isSkipError) {
                dispatch(setExecuteStepCallBack(true));
            } else {
                afterTestService(false);
            }
        }
    }, [executeResponse, executeError]);

    /*
     *
     */
    useEffect(() => {
        if (!testLoading && testResponse) {
            if (modalShow) {
                processStep();
            }
        }
    }, [testResponse]);

    /*
     *  Step 
     */
    useEffect(() => {
        if (isFinish) {
            dispatch(setExecuteStepCallBack(false));
            processStep();
        }
    }, [isFinish]);

    useEffect(() => {
        dispatch(searchRandomAll());
    }, []);

    return (
        <ScenarioTestPopup
            modalShow={modalShow}
            handleClose={handleClose}
            serverData={serverData}
            codeData={codeData}
            randData={randData}
            executeList={executeList}
            setExecuteList={setExecuteList}
            selectedNavId={selectedNavId}
            setSelectedNavId={setSelectedNavId}
            playExecuteTrigger={playExecuteTrigger}
            isProcessTest={isProcessTest}
            onClickInit={initExecuteData}
            onClickMoveScenario={onClickMoveScenario2}
            onClickServerInfo={onClickServerInfo}
        />
    );
}

export default ScenarioTestPopupContainer;
