/* eslint-disable no-debugger */
/* eslint-disable jsx-a11y/label-has-associated-control */
/* eslint-disable no-unused-vars */
import React from 'react';
import {useTranslation} from 'react-i18next';
import {Row, Col, Form, FloatingLabel} from 'react-bootstrap';
import Button from '../../components/button/Button';
import Card from '../../components/card/Card';

import {takeSelectOptionbyCode} from '../../lib/commonUiUtils';

const LogSearch = ({
    onSearch,
    codeData,
    searchLogSnapshotForm,
    onChangeEvent,
    addNewLog
}) => {
    const [t] = useTranslation();

    const onChangerSearchForm = (e) => {
        e.preventDefault();
        const searchLogSnapshotFormT = {...searchLogSnapshotForm};
        searchLogSnapshotFormT[e.target.id] = e.target.value;
        onChangeEvent(searchLogSnapshotFormT);
    };

    const onClickSearch = (e) => {
        e.preventDefault();
        onSearch();
    };

    const onCheckEnter = (e) => {
        if (e.key === 'Enter') {
            e.preventDefault();
            onSearch();
        }
    };

    return (
        <Card
            title={t('common.text.searchCardTitleName')}
            body={
                <>
                    <Row onKeyPress={onCheckEnter}>
                        {/*  From ~ To */}
                        <Col xs="4">
                            <Row>
                                <Col xs="6">
                                    <FloatingLabel
                                        label="From"
                                        className="mb-2"
                                    >
                                        <Form.Control
                                            type="date"
                                            id="fromDate"
                                            onChange={onChangerSearchForm}
                                            value={
                                                searchLogSnapshotForm?.fromDate
                                            }
                                        />
                                    </FloatingLabel>
                                </Col>
                                <Col xs="6">
                                    <FloatingLabel label="To" className="mb-2">
                                        <Form.Control
                                            type="date"
                                            id="toDate"
                                            onChange={onChangerSearchForm}
                                            value={
                                                searchLogSnapshotForm?.toDate
                                            }
                                        />
                                    </FloatingLabel>
                                </Col>
                            </Row>
                        </Col>
                        <Col xs="3">
                            <Row>
                                <Col xs="6">
                                    <FloatingLabel
                                        label=" "
                                        className="mb-2"
                                    >
                                        <Form.Select
                                            id="collectionMethod"
                                            onChange={onChangerSearchForm}
                                            value={
                                                searchLogSnapshotForm.collectionMethod ??
                                                ''
                                            }
                                        >
                                            <option value="">
                                                {t('common.text.optionAll')}
                                            </option>
                                            {takeSelectOptionbyCode(
                                                codeData,
                                                'CollectionMethod'
                                            )}
                                        </Form.Select>
                                    </FloatingLabel>
                                </Col>
                            </Row>
                        </Col>
                        <Col xs="3" />
                        <Col xs="1">
                            <Form.Group>
                                <Form.Label />
                                <Button
                                    type="button"
                                    className="btn btn-block btn-secondary btn-search"
                                    onClick={onClickSearch}
                                >
                                    {t('common.button.search')}
                                </Button>
                            </Form.Group>
                        </Col>
                        <Col xs="1">
                            <Form.Group>
                                <Form.Label />
                                <Button
                                    type="button"
                                    className="btn btn-block btn-success btn-search"
                                    onClick={addNewLog}
                                >
                                    + 
                                </Button>
                            </Form.Group>
                        </Col>
                    </Row>
                </>
            }
        />
    );
};

export default LogSearch;
