/* eslint-disable indent */
import {takeEvery, takeLatest} from 'redux-saga/effects';
import * as layoutAPI from '../api/layout'; // api/auth    
import {
    reducerUtils,
    handleAsyncActions,
    createPromiseSaga
} from '../lib/asyncUtils';

const PREFIX = 'LAYOUT';

/**
 * LAYOUT List Paging 
 * searchLayout (SEARCH_LAYOUT) : 
 * clearSearchLayout (CLEAR_SEARCH_LAYOUT) : 
 * setSearchLayoutName (SET_SEARCH_LAYOUT_NAME) :   1
 * setSearchHttpMethod (SET_SEARCH_HTTP_METHOD) :   2
 */
const SEARCH_LAYOUT = `${PREFIX}/SEARCH_LAYOUT`;
const SEARCH_LAYOUT_SUCCESS = `${PREFIX}/SEARCH_LAYOUT_SUCCESS`;
const SEARCH_LAYOUT_ERROR = `${PREFIX}/SEARCH_LAYOUT_ERROR`;
const CLEAR_SEARCH_LAYOUT = `${PREFIX}/CLEAR_SEARCH_LAYOUT`;
const SET_SEARCH_LAYOUT_FORM = `${PREFIX}/SET_SEARCH_LAYOUT_FORM`;

export const searchLayout = (searchForm) => ({
    type: SEARCH_LAYOUT,
    payload: searchForm
});

export const clearSearchLayout = () => ({
    type: CLEAR_SEARCH_LAYOUT
});

export const getInitSearchLayout = () => ({
    layoutQuery: '',
    httpMethod: '',
    transactionType: '',
    page: 1,
    size: 10
});

export const setSearchLayoutForm = (searchForm) => ({
    type: SET_SEARCH_LAYOUT_FORM,
    payload: {searchForm}
});

/**
 *    redux  
 * clearSearchLayout (SET_SELECTED_LAYOUT) : 
 */
const SET_SELECTED_LAYOUT = `${PREFIX}/SET_SELECTED_LAYOUT`;

export const setSelectedLayout = (selectedLayout) => ({
    type: SET_SELECTED_LAYOUT,
    payload: {
        selectedLayout
    }
});

/**
 * LAYOUT  
 * updateLayout (UPDATE_LAYOUT) : 
 * clearUpdateLayout (CLEAR_UPDATE_LAYOUT) : 
 */
const UPDATE_LAYOUT = `${PREFIX}/UPDATE_LAYOUT`;
const UPDATE_LAYOUT_SUCCESS = `${PREFIX}/UPDATE_LAYOUT_SUCCESS`;
const UPDATE_LAYOUT_ERROR = `${PREFIX}/UPDATE_LAYOUT_ERROR`;
const CLEAR_UPDATE_LAYOUT = `${PREFIX}/CLEAR_UPDATE_LAYOUT`;

export const updateLayout = (updateData) => ({
    type: UPDATE_LAYOUT,
    payload: {
        ...updateData
    }
});

export const clearUpdateLayout = () => ({
    type: CLEAR_UPDATE_LAYOUT
});

/**
 *    (MODAL)
 * showUnittestModal (SHOW_UNITTEST_MODAL) : SHOW
 * hideUnittestModal (HIDE_UNITTEST_MODAL) : HIDE
 */
const SHOW_UNITTEST_MODAL = `${PREFIX}/SHOW_UNITTEST_MODAL`;
const HIDE_UNITTEST_MODAL = `${PREFIX}/HIDE_UNITTEST_MODAL`;

export const showUnittestModal = () => ({
    type: SHOW_UNITTEST_MODAL
});

export const hideUnittestModal = () => ({
    type: HIDE_UNITTEST_MODAL
});

/**
 * LAYOUT   (TR)
 * searchLayoutDetail (SEARCH_LAYOUT_DETAIL) : 
 * clearsearchLayoutDetail (CLEAR_SEARCH_LAYOUT_DETAIL) : 
 */
const SEARCH_LAYOUT_DETAIL = `${PREFIX}/SEARCH_LAYOUT_DETAIL`;
const SEARCH_LAYOUT_DETAIL_SUCCESS = `${PREFIX}/SEARCH_LAYOUT_DETAIL_SUCCESS`;
const SEARCH_LAYOUT_DETAIL_ERROR = `${PREFIX}/SEARCH_LAYOUT_DETAIL_ERROR`;
const CLEAR_SEARCH_LAYOUT_DETAIL = `${PREFIX}/CLEAR_SEARCH_LAYOUT_DETAIL`;

// id     .
export const searchLayoutDetail = (id) => ({
    type: SEARCH_LAYOUT_DETAIL,
    payload: {
        id
    }
});

export const clearsearchLayoutDetail = () => ({
    type: CLEAR_SEARCH_LAYOUT_DETAIL
});

/**
 *      (MODAL)
 * usedRandomUpdateModal (USE_RANDOM_UPDATE_MODAL) : SHOW
 * clearRandomUpdateModal (CLEAR_RANDOM_UPDATE_MODAL) : HIDE
 */
const USE_RANDOM_UPDATE_MODAL = `${PREFIX}/USE_RANDOM_UPDATE_MODAL`;
const CLEAR_RANDOM_UPDATE_MODAL = `${PREFIX}/CLEAR_RANDOM_UPDATE_MODAL`;

export const usedRandomUpdateModal = (showModal, initData) => ({
    type: USE_RANDOM_UPDATE_MODAL,
    payload: {
        showModal,
        initData
    }
});

export const clearRandomUpdateModal = () => ({
    type: CLEAR_RANDOM_UPDATE_MODAL
});

/**
 *    's   's 
 * api -> combineRequestValue (/layout/search/random-value)
 * combineRequestValue (GET_RANDOM_VALUE) : 
 * clearCombineRequestValue (CLEAR_GET_RANDOM_VALUE) : 
 */
const GET_RANDOM_VALUE = `${PREFIX}/GET_RANDOM_VALUE`;
const GET_RANDOM_VALUE_SUCCESS = `${PREFIX}/GET_RANDOM_VALUE_SUCCESS`;
const GET_RANDOM_VALUE_ERROR = `${PREFIX}/GET_RANDOM_VALUE_ERROR`;
const CLEAR_GET_RANDOM_VALUE = `${PREFIX}/CLEAR_GET_RANDOM_VALUE`;

export const combineRequestValue = (layoutId, cols) => ({
    type: GET_RANDOM_VALUE,
    payload: {
        layoutId,
        cols
    }
});
export const clearCombineRequestValue = () => ({
    type: CLEAR_GET_RANDOM_VALUE
});

/**
 * New Layout popup (MODAL)
 * showCreateModal (SHOW_CREATE_MODAL) : SHOW
 * hideCreateModal (HIDE_CREATE_MODAL) : HIDE
 */
const SHOW_CREATE_MODAL = `${PREFIX}/SHOW_CREATE_MODAL`;
const HIDE_CREATE_MODAL = `${PREFIX}/HIDE_CREATE_MODAL`;

export const showCreateModal = () => ({
    type: SHOW_CREATE_MODAL
});

export const hideCreateModal = () => ({
    type: HIDE_CREATE_MODAL
});

/**
 * LAYOUT   (TR)
 * createLayoutManual (CREATE_LAYOUT_MANUAL) : 
 * clearCreateLayoutManual (CLEAR_CREATE_LAYOUT_MANUAL) : 
 */
const CREATE_LAYOUT_MANUAL = `${PREFIX}/CREATE_LAYOUT_MANUAL`;
const CREATE_LAYOUT_MANUAL_SUCCESS = `${PREFIX}/CREATE_LAYOUT_MANUAL_SUCCESS`;
const CREATE_LAYOUT_MANUAL_ERROR = `${PREFIX}/CREATE_LAYOUT_MANUAL_ERROR`;
const CLEAR_CREATE_LAYOUT_MANUAL = `${PREFIX}/CLEAR_CREATE_LAYOUT_MANUAL`;

export const createLayoutManual = (saveData) => ({
    type: CREATE_LAYOUT_MANUAL,
    payload: {
        ...saveData
    }
});

export const clearCreateLayoutManual = () => ({
    type: CLEAR_CREATE_LAYOUT_MANUAL
});

/*
 * deleteLayout : layout  (DELETE_LAYOUT)
 * deleteLayoutDataClear : layout   (DELETE_LAYOUT_DATA_CLEAR)
 */
const DELETE_LAYOUT = 'layout/DELETE_LAYOUT';
const DELETE_LAYOUT_SUCCESS = 'layout/DELETE_LAYOUT_SUCCESS';
const DELETE_LAYOUT_ERROR = 'layout/DELETE_LAYOUT_ERROR';
const DELETE_LAYOUT_DATA_CLEAR = 'layout/DELETE_LAYOUT_DATA_CLEAR';

export const deleteLayout = (layoutId) => ({
    type: DELETE_LAYOUT,
    payload: {
        layoutId
    }
});

export const deleteLayoutDataClear = () => ({
    type: DELETE_LAYOUT_DATA_CLEAR
});

/**
 * Redux-saga 
 */
export function* layoutSaga() {
    yield takeLatest(
        SEARCH_LAYOUT,
        createPromiseSaga(SEARCH_LAYOUT, layoutAPI.searchLayout)
    );
    yield takeEvery(
        UPDATE_LAYOUT,
        createPromiseSaga(UPDATE_LAYOUT, layoutAPI.updateLayout)
    );
    yield takeLatest(
        SEARCH_LAYOUT_DETAIL,
        createPromiseSaga(SEARCH_LAYOUT_DETAIL, layoutAPI.searchLayoutDetail)
    );
    yield takeEvery(
        GET_RANDOM_VALUE,
        createPromiseSaga(GET_RANDOM_VALUE, layoutAPI.combineRequestValue)
    );
    yield takeEvery(
        CREATE_LAYOUT_MANUAL,
        createPromiseSaga(CREATE_LAYOUT_MANUAL, layoutAPI.createLayoutManual)
    );
    yield takeEvery(
        DELETE_LAYOUT,
        createPromiseSaga(DELETE_LAYOUT, layoutAPI.deleteLayout)
    );
}

/**
 * State 
 */
const initialState = {
    selectedLayout: null,
    searchForm: getInitSearchLayout(),
    search: reducerUtils.initial(),
    update: reducerUtils.initial(),
    delete: reducerUtils.initial(),
    // updateInputFormat: reducerUtils.initial(),
    unittestModal: {visible: false},
    // executeUnittest: reducerUtils.initial(),
    layoutInfo: reducerUtils.initial(),
    // random update popup call
    randUpdateInitData: {
        showModal: false,
        initData: {}
    },
    //    's   's 
    colListByRandVal: reducerUtils.initial(),
    createModal: {visible: false},
    createLayoutManual: reducerUtils.initial()
};

export default function layout(state = initialState, action) {
    switch (action.type) {
        case SET_SELECTED_LAYOUT:
            return {
                ...state,
                selectedLayout: {
                    ...action.payload.selectedLayout
                }
            };

        case SET_SEARCH_LAYOUT_FORM:
            return {
                ...state,
                searchForm: action.payload.searchForm
            };
        // case SET_SEARCH_CLUB_HISTORY_FORM:
        //     return {
        //         ...state,
        //         searchClubHistoryForm: action.payload.searchClubHistoryForm
        //     };
        case SEARCH_LAYOUT:
        case SEARCH_LAYOUT_SUCCESS:
        case SEARCH_LAYOUT_ERROR:
            return handleAsyncActions(
                SEARCH_LAYOUT,
                'search',
                true
            )(state, action);

        case CLEAR_SEARCH_LAYOUT:
            return {
                ...state,
                search: getInitSearchLayout()
            };
        case SEARCH_LAYOUT_DETAIL:
        case SEARCH_LAYOUT_DETAIL_SUCCESS:
        case SEARCH_LAYOUT_DETAIL_ERROR:
            return handleAsyncActions(
                SEARCH_LAYOUT_DETAIL,
                'layoutInfo',
                true
            )(state, action);

        case CLEAR_SEARCH_LAYOUT_DETAIL:
            return {
                ...state,
                layoutInfo: reducerUtils.initial()
            };

        case CLEAR_UPDATE_LAYOUT:
            return {
                ...state,
                update: reducerUtils.initial()
            };

        case SHOW_UNITTEST_MODAL:
            return {
                ...state,
                unittestModal: {visible: true}
            };
        case HIDE_UNITTEST_MODAL:
            return {
                ...state,
                unittestModal: {visible: false}
            };

        case UPDATE_LAYOUT:
        case UPDATE_LAYOUT_SUCCESS:
        case UPDATE_LAYOUT_ERROR:
            return handleAsyncActions(
                UPDATE_LAYOUT,
                'update',
                true
            )(state, action);

        // random update popup call
        case USE_RANDOM_UPDATE_MODAL:
            return {
                ...state,
                randUpdateInitData: {
                    showModal: action.payload.showModal,
                    initData: action.payload.initData
                }
            };

        // random update popup clear
        case CLEAR_RANDOM_UPDATE_MODAL:
            return {
                ...state,
                randUpdateInitData: {
                    showModal: false
                }
            };

        //    's   's 

        case GET_RANDOM_VALUE:
        case GET_RANDOM_VALUE_SUCCESS:
        case GET_RANDOM_VALUE_ERROR:
            return handleAsyncActions(
                GET_RANDOM_VALUE,
                'colListByRandVal',
                true
            )(state, action);

        case CLEAR_GET_RANDOM_VALUE:
            return {
                ...state,
                colListByRandVal: reducerUtils.initial()
            };

        case SHOW_CREATE_MODAL:
            return {
                ...state,
                createModal: {visible: true}
            };
        case HIDE_CREATE_MODAL:
            return {
                ...state,
                createModal: {visible: false}
            };

        case CREATE_LAYOUT_MANUAL:
        case CREATE_LAYOUT_MANUAL_SUCCESS:
        case CREATE_LAYOUT_MANUAL_ERROR:
            return handleAsyncActions(
                CREATE_LAYOUT_MANUAL,
                'createLayoutManual',
                true
            )(state, action);

        case CLEAR_CREATE_LAYOUT_MANUAL:
            return {
                ...state,
                createLayoutManual: reducerUtils.initial()
            };

        /*
         * deleteLayout : layout  (DELETE_LAYOUT)
         * deleteLayoutDataClear : layout   (DELETE_LAYOUT_DATA_CLEAR)
         */
        case DELETE_LAYOUT:
        case DELETE_LAYOUT_SUCCESS:
        case DELETE_LAYOUT_ERROR:
            return handleAsyncActions(
                DELETE_LAYOUT,
                'delete',
                true
            )(state, action);
        case DELETE_LAYOUT_DATA_CLEAR:
            return {
                ...state,
                delete: reducerUtils.initial()
            };

        default:
            return state;
    }
}
