/* eslint-disable import/prefer-default-export */

import axios from 'axios';

/**
 * SCENARIO List Paging  (TR)
 * searchScenario (SEARCH_SERVER) : 
 * clearSearchScenario (CLEAR_SEARCH_SERVER) : 
 * setSearchScenarioName (SET_SEARCH_SCENARIO_NAME) :  
 * setSearchOwner (SET_SEARCH_OWNER) :  
 */
export const searchScenario = async ({
    scenarioName,
    manager,
    page = 1,
    size = 10
}) => {
    const response = await axios.get(
        `/scenarios?sort=id,desc&scenarioName=${scenarioName}&manager=${manager}&page=${
            page - 1
        }&size=${size}`
    );
    return response;
};

/**
 * SCENARIO  (TR)
 * deleteScenario (DELETE_SCENARIO) : 
 * deleteScenarioDataClear (DELETE_SCENARIO_DATA_CLEAR) : 
 */
export const deleteScenario = async ({scenarioId}) => {
    const response = await axios.delete(`/scenarios/${scenarioId}`, null);
    return response;
};

/**
 * search scenario test history (for scHistory.js)
 * searchScenarioHistory (SEARCH_SCENARIO_HISTORY) : 
 * clearSearchScenarioHistory (CLEAR_SEARCH_SCENARIO_HISTORY) : 
 */
export const searchScenarioHistory = async ({
    scenarioName,
    manager,
    isSuccess,
    startTime,
    endTime,
    clubSnapshotId = -1,
    page = 1,
    size = 10
}) => {
    const response = await axios.get(
        `/records?sort=id,desc&clubSnapshotId=${clubSnapshotId}&scenarioName=${scenarioName}&manager=${manager}&isSuccess=${isSuccess}&startTime=${startTime}&endTime=${endTime}&page=${
            page - 1
        }&size=${size}`
    );
    return response;
};

export const saveScenarioHistoryDetail = async ({recordId}) => {
    const response = await axios.get(`/records/${recordId}`);
    return response;
};

/**
 * save scenario test history (for uitest.js) (   )
 * saveScenarioHistory (SAVE_SCENARIO_HISTORY) : 
 */
export const saveScenarioHistory = async ({
    scenarioId,
    wasServerId,
    dbServerId,
    recordResult,
    variableJson,
    units
}) => {
    const response = await axios.post('/records', {
        scenarioId,
        wasServerId,
        dbServerId,
        recordResult,
        variableJson,
        units
    });
    return response;
};

/**
 * (TR) 2022-03-17  Lock   ()
 * saveScenarioLock (SAVE_SCENARIO_LOCK) : 
 * clearScenarioLock (CLEAR_SAVE_SCENARIO_LOCK) : 
 */
export const saveScenarioLock = async ({scenarioId, lock, lockPassword}) => {
    const response = await axios.patch(`/scenarios/${scenarioId}/lock`, {
        isLock: lock,
        lockPassword
    });
    return response;
};
