/* eslint-disable no-unused-vars */
import React, {useState, useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import {useTranslation} from 'react-i18next';
import {toast} from 'react-toastify';
import HttpTestView from '../../pages/common/HttpTestView';
import {
    changeInputDataB,
    clearChangeInputDataB,
    executeSingleTestB,
    clearExecuteSingleTestB,
    getSampleRequestB,
    clearGetSampleRequestB
} from '../../store/common';
import {getErrorMsg} from '../../lib/commonUiUtils';
import {setUpdateServerInitData} from '../../store/server';

export default function HttpTestViewBContainer({
    singleTestData,
    requestDataFormat,
    responseDataFormat,
    setSingleTestData,
    isdualMode,
    serverInfo
}) {
    const dispatch = useDispatch();
    const [t] = useTranslation();

    // server list info
    const {data: serverData} = useSelector(
        (state) => state.common.searchServer
    );

    const {
        loading: executeLoading,
        data: executeResponse,
        error: executeError
    } = useSelector((state) => state.common.executeSingleTestBRes);

    const {
        loading: changeLoading,
        data: changeResponse,
        error: changeError
    } = useSelector((state) => state.common.changeInputDataB);

    const {
        loading: getLoading,
        data: getResponse,
        error: getError
    } = useSelector((state) => state.common.getSampleRequestBRes);

    /** HTTP TEST  */
    const onReSend = (e) => {
        setSingleTestData({
            ...singleTestData,
            output: t('common.text.testPending'),
            startTime: '',
            endTime: '',
            executeSecond: '',
            httpResponseCode: '',
            httpResponseText: ''
        });

        const selectedServer = serverData?.data?.filter(
            (obj) => obj.serverId === Number(singleTestData.serverId)
        );

        if (
            selectedServer[0]?.loginProcess?.loginMethod === 'DIRECT_INPUT' &&
            (singleTestData.tokenInfo === '' || !singleTestData.tokenInfo)
        ) {
            // eslint-disable-next-line no-alert
            alert(
                ' Server   Session   .'
            );
            return;
        }

        const data = {
            requestData: singleTestData.input,
            layoutId: singleTestData.layoutId,
            serverId: singleTestData.serverId,
            tokenInfo: singleTestData.tokenInfo
        };

        dispatch(executeSingleTestB(data));
    };

    useEffect(() => {
        //    setTimeout .
        if (!executeLoading && executeResponse) {
            if (executeResponse.data.httpResponseDto?.httpStatusCode) {
                toast.success(t('common.alert.testSuccess'));
                const {data} = executeResponse;
                setSingleTestData({
                    ...singleTestData,
                    historyId: data.historyId,
                    responseInfo: data.responseInfo,
                    responseTime: data.responseTime,
                    output: data.headerAndBodyResponse.intermediateBody
                });
            } else {
                toast.error(executeResponse.data.errorMessage);
                const {data} = executeResponse;
                setSingleTestData({
                    ...singleTestData,
                    historyId: data.historyId,
                    responseInfo: data.responseInfo,
                    responseTime: data.responseTime,
                    output: data.headerAndBodyResponse.intermediateBody
                });
            }
        }
        if (!executeLoading && executeError) {
            toast.error(getErrorMsg(executeError, 'execute'));
            setSingleTestData({
                ...singleTestData,
                output: t('common.text.testReady'),
                responseInfo: {
                    httpStatusCode: '',
                    httpStatus: ''
                },
                responseTime: {
                    receiveDateTime: '',
                    processTime: ''
                }
            });
        }
        dispatch(clearExecuteSingleTestB());
    }, [executeResponse, executeError]);
    /** HTTP TEST  END */

    /** server change -> input format change */
    const onChangeServerInfo = (_beforeServer, _afterServer) => {
        const beforeServer = Number(_beforeServer);
        const afterServer = _afterServer === 0 ? '' : Number(_afterServer);
        const inputData = singleTestData.input;
        setSingleTestData({
            ...singleTestData,
            input: '',
            headerAndBodyResponse: {body: '', header: ''}, // t('common.text.testReady'),
            serverId: afterServer,
            responseInfo: {
                httpStatusCode: '',
                httpStatus: ''
            },
            responseTime: {
                receiveDateTime: '',
                processTime: ''
            }
        });
        // Target Server   
        dispatch(
            changeInputDataB({
                beforeServer,
                afterServer,
                layoutId: singleTestData.layoutId,
                inputData
            })
        );
    };

    /** GET SAMPLE REQUEST */
    const onGetSample = (e) => {
        setSingleTestData({
            ...singleTestData,
            output: ' ',
            startTime: '',
            endTime: '',
            executeSecond: '',
            httpResponseCode: '',
            httpResponseText: ''
        });

        const data = {
            layoutId: singleTestData.layoutId,
            serverId: singleTestData.serverId
        };

        dispatch(getSampleRequestB(data));
    };

    const onClickServerInfo = (serverId) => {
        const selectedServer = serverData.data.content.filter(
            (obj) => obj.serverId === Number(singleTestData.serverId)
        );
        if (selectedServer.length !== 1) {
            // eslint-disable-next-line no-alert
            alert('   .');
            return;
        }
        const tempData = {...selectedServer[0]};
        tempData.parentId = 'HttpTestView';
        dispatch(setUpdateServerInitData(tempData));
    };
    /**
     * rand, test data  Request Data 
     */
    useEffect(() => {
        if (!changeLoading && changeResponse) {
            setSingleTestData({
                ...singleTestData,
                input: changeResponse.data.inputData
            });
        }
        if (!changeLoading && changeError) {
            toast.error(getErrorMsg(changeError, 'select'));
        }
        dispatch(clearChangeInputDataB());
    }, [changeResponse, changeError]);

    /**
     *    request sample data 
     */
    useEffect(() => {
        if (!getLoading && getResponse) {
            setSingleTestData({
                ...singleTestData,
                input: JSON.stringify(getResponse.data)
            });
            toast.success(t('common.alert.testSuccess'));
        }
        if (!getLoading && getError) {
            toast.error(getErrorMsg(getError, 'select'));
        }
        dispatch(clearGetSampleRequestB());
    }, [getResponse, getError]);

    return (
        <>
            <HttpTestView
                singleTestData={singleTestData}
                setSingleTestData={setSingleTestData}
                requestDataFormat={requestDataFormat}
                responseDataFormat={responseDataFormat}
                serverData={serverData}
                loading={executeLoading || changeLoading}
                onReSend={onReSend}
                onGetSample={onGetSample}
                onChangeServerInfo={onChangeServerInfo}
                isdualMode={isdualMode}
                serverInfo={serverInfo}
                onClickServerInfo={onClickServerInfo}
            />
        </>
    );
}
