/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import MemberSelect from '../../pages/common/MemberSelect';
import Spinner from '../../components/spinner/Spinner';
import {clearMemberSelectModal} from '../../store/common';
import {
    setMemberSearchTempForm,
    getMemberSearchFormInitialState,
    searchUserInfoList
} from '../../store/auth';

function MemberSelectContainer({changeManager}) {
    const dispatch = useDispatch();

    const [modalShow, setModalShow] = React.useState(false);
    const {memberSelectInitData} = useSelector((state) => state.common);
    const {memberSearchTempForm} = useSelector((state) => state.auth);
    const onChangeSearchTempForm = (searchForm) =>
        dispatch(setMemberSearchTempForm({...searchForm}));

    const {
        data: searchData,
        loading: searchLoading,
        error: searchError
    } = useSelector((state) => state.auth.memberInfoList);

    const handleClose = () => setModalShow(false);
    const handleShow = () => setModalShow(true);
    // step code setting
    const [managerId, setManagerId] = React.useState('');

    const onDoubleClick = (id, row) => {
        if (id === managerId) {
            // eslint-disable-next-line no-alert
            window.alert('  .');
        } else {
            changeManager(row);
            handleClose();
            // dispatch(deleteBulkTestGroup(bulktestGroupId));
        }
    };

    useEffect(() => {
        if (memberSelectInitData.showModal && !modalShow) {
            const memberSearchTempFormT = getMemberSearchFormInitialState();
            setManagerId(memberSelectInitData.userId);
            dispatch(setMemberSearchTempForm(memberSearchTempFormT));
            dispatch(searchUserInfoList(memberSearchTempFormT));
            dispatch(clearMemberSelectModal());
            handleShow();
        }
    }, [memberSelectInitData.showModal]);

    return (
        <>
            <MemberSelect
                modalShow={modalShow}
                handleClose={handleClose}
                searchData={searchData}
                managerId={managerId}
                onDoubleClick={onDoubleClick}
            />
            <Spinner isLoading={searchLoading} />
        </>
    );
}

export default MemberSelectContainer;
