import React, {useEffect} from 'react';
import {useHistory} from 'react-router-dom';
import {useSelector, useDispatch} from 'react-redux';
import ClubSearch from '@pages/club/ClubSearch';
import Spinner from '../../components/spinner/Spinner';
import {
    setSearchClubForm,
    searchClubFormInit,
    usedClubCreateModal
} from '../../store/club';

/*
 *   Container
 */
function ClubSearchContainer({onSearch}) {
    const dispatch = useDispatch();
    const history = useHistory();

    const {searchClubForm} = useSelector((state) => state.club);

    const onChangerSearchForm = (e) => {
        const searchClubFormT = {...searchClubForm};
        searchClubFormT[e.target.id] = e.target.value;
        dispatch(setSearchClubForm(searchClubFormT));
    };

    const {loading: searchLoading} = useSelector(
        (state) => state.club.searchClubRes
    );

    const {data: codeData} = useSelector((state) => state.common.searchCode);

    /**
     *   
     */
    const onClickCreate = () => {
        const data = {
            parent: 'club'
        };
        dispatch(usedClubCreateModal(true, data));
    };

    useEffect(() => {
        if (history.action === 'PUSH') {
            const searchClubFormT = searchClubFormInit();
            dispatch(setSearchClubForm(searchClubFormT));
            onSearch();
        }
        if (history.action === 'POP') {
            onSearch();
        }
    }, []);

    return (
        <>
            <ClubSearch
                onSearch={onSearch}
                searchClubForm={searchClubForm}
                onChangerSearchForm={onChangerSearchForm}
                codeData={codeData}
                onClickCreate={onClickCreate}
            />
            <Spinner isLoading={searchLoading} />
        </>
    );
}

export default ClubSearchContainer;
