import React, {useEffect} from 'react';
import SourceDetail from '@pages/source/SourceDetail';
import {useHistory, useLocation} from 'react-router-dom';
import {useDispatch, useSelector} from 'react-redux';
import Spinner from '../../components/spinner/Spinner';

import {
    searchSources,
    setSearchSourcesForm,
    searchSorucesFormInit
} from '../../store/source';

function SourceDetailContainer() {
    const dispatch = useDispatch();
    const history = useHistory();
    const location = useLocation();

    const {searchSourcesForm} = useSelector((state) => state.source);
    const onSearch = (_searchSourcesForm = searchSourcesForm) => {
        dispatch(searchSources(_searchSourcesForm));
    };

    const {loading: searchLoading} = useSelector(
        (state) => state.source.searchSourcesRes
    );

    useEffect(() => {
        if (location.state === null || location.state.id === null) {
            history.push('/source');
        } else {
            let searchSourcesFormT = {...searchSourcesForm};
            if (history.action === 'PUSH')
                searchSourcesFormT = searchSorucesFormInit();
            searchSourcesFormT.sourceSnapshotId = location.state?.id;
            dispatch(setSearchSourcesForm(searchSourcesFormT));
            onSearch(searchSourcesFormT);
        }
    }, []);

    return (
        <>
            <SourceDetail onSearch={onSearch} />
            <Spinner isLoading={searchLoading} />
        </>
    );
}

export default SourceDetailContainer;
