/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import DashboardTestCnt from '@pages/dashboard/DashboardTestCnt';
import {useSelector, useDispatch} from 'react-redux';
import {searchServerTestCnt} from '../../store/dashboard';

function DashboardTestCntContainer() {
    const dispatch = useDispatch();

    const offset = new Date().getTimezoneOffset() * 60000;
    const today = new Date(Date.now() - offset)
        .toISOString()
        .slice(0, -1)
        .slice(0, 10);

    const {data: searchStatData} = useSelector(
        (state) => state.dashboard.searchServerTestCnt
    );

    const {data: serverData} = useSelector(
        (state) => state.common.searchServer
    );

    useEffect(() => {
        dispatch(searchServerTestCnt(today));
        return () => {};
    }, []);
    return (
        <>
            <DashboardTestCnt
                searchStatData={searchStatData}
                serverData={serverData}
            />
        </>
    );
    ///
}

export default DashboardTestCntContainer;
