/* eslint-disable no-unused-vars */
import React from 'react';
import {useSelector, useDispatch} from 'react-redux';
import UitestCaseCreate from '@app/pages/uitest/popup/UitestCaseCreate';
import {setDetailCaseList} from '../../../store/uitest';
import {setTempKey} from '../../../store/common';

function UitestCaseCreateContainer() {
    const dispatch = useDispatch();
    const {data: scenarioData} = useSelector(
        (state) => state.uitest.scenarioInfo
    );

    const [modalShow, setModalShow] = React.useState(false);

    const handleClose = () => setModalShow(false);
    const handleShow = () => setModalShow(true);
    const saveDetailCaseList = (cases) => {
        dispatch(setDetailCaseList(cases));
        handleClose();
    };

    const {tempkey} = useSelector((state) => state.common);
    const setTempKeyRezen = () => {
        if (tempkey < -9999) {
            dispatch(setTempKey(-1));
        } else {
            dispatch(setTempKey(tempkey - 1));
        }
    };
    return (
        <>
            <UitestCaseCreate
                modalShow={modalShow}
                handleShow={handleShow}
                handleClose={handleClose}
                scenarioData={scenarioData}
                setDetailCaseList={saveDetailCaseList}
                tempkey={tempkey}
                setTempKeyRezen={setTempKeyRezen}
            />
        </>
    );
}

export default UitestCaseCreateContainer;
