/* eslint-disable no-shadow */
/* eslint-disable no-unused-vars */
import React from 'react';
import {Form, Modal, Row, Col, FloatingLabel, Accordion} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
import {useFormik} from 'formik';
import * as Yup from 'yup';
import Button from '../../../components/button/Button';
import {jsonStrFormatNotPretty} from '../../../lib/jsonUtils';
import {
    printFormError,
    getInputValidMsg,
    takeSelectOptionbyCode
} from '../../../lib/commonUiUtils';
import UitestScriptSleep from '../components/UitestScriptSleep';
import UitestScriptCopy from '../components/UitestScriptCopy';
import UitestScriptIf from '../components/UitestScriptIf';

/**
 * Scenario Test  script  
 * @param {*} param0
 * @returns
 */
const UitestStepPopScript = ({
    scenarioData,
    selectedCaseId,
    setDetailCaseList,
    handleClose,
    stepUpdateInitData,
    onChangeStepUpdataInitData,
    executeList,
    tempkey,
    setTempKeyRezen,
    codeData
}) => {
    const [t] = useTranslation();
    const formik = useFormik({
        //  
        initialValues: {
            stepId: stepUpdateInitData?.stepId ?? -1,
            stepType: 'SCRIPT',
            isSkipError: stepUpdateInitData?.isSkipError ?? false,
            script: stepUpdateInitData?.script ?? {
                scriptType: 'COPY',
                copyAttr: {
                    fromItem: {option: 'TEXT', data: ''},
                    toItem: {option: 'STEP', data: ''}
                },
                ifAttr: {
                    itemA: {option: 'TEXT', data: ''},
                    itemB: {option: 'TEXT', data: ''},
                    ifFormula: 'EQUAL',
                    trueItem: {postProcessingType: 'NONE'},
                    falseItem: {postProcessingType: 'NONE'}
                    // postProcessingFalse: 'NONE'
                },
                commonAttr: {}
            },
            stepName: stepUpdateInitData?.stepName ?? ''
        },
        validationSchema: Yup.object({
            stepName: Yup.string().required(
                getInputValidMsg(t('pages.scenario.title'))
            )
        }),
        onSubmit: (values) => {
            const cases = [...scenarioData.data.cases];
            const caseLoop = cases.map((cUnit) => {
                const theCase = cUnit;

                if (cUnit.caseId === selectedCaseId) {
                    let data = theCase.steps;
                    if (data == null) {
                        data = [];
                    }
                    //    
                    if (!stepUpdateInitData.stepId) {
                        const newStep = {...values};
                        newStep.stepId = tempkey;
                        setTempKeyRezen();
                        data.push(newStep);

                        const aLoop = data.map((unit, index) => {
                            const temp = unit;
                            temp.index = index;
                            return temp;
                        });
                        theCase.steps = aLoop;
                    } else {
                        const aLoop = data.map((unit) => {
                            const temp = unit;
                            if (stepUpdateInitData.stepId === unit.stepId) {
                                temp.stepName = values.stepName;
                                temp.isSkipError = values.isSkipError;
                                temp.script = values.script;
                            }
                            return temp;
                        });

                        theCase.steps = aLoop;
                    }
                }

                return theCase;
            });

            setDetailCaseList(caseLoop);
            handleClose();
        }
    });

    /*
     * renderScriptType -> script type  
     */
    const renderScriptType = () => {
        if (formik.values.script.scriptType === 'COPY') {
            return (
                <>
                    <UitestScriptCopy
                        formik={formik}
                        executeList={executeList}
                        scenarioData={scenarioData}
                        codeData={codeData}
                    />
                </>
            );
        }
        if (formik.values.script.scriptType === 'IF') {
            return (
                <>
                    <UitestScriptIf
                        formik={formik}
                        executeList={executeList}
                        scenarioData={scenarioData}
                        codeData={codeData}
                    />
                </>
            );
        }
        if (formik.values.script.scriptType === 'SLEEP') {
            return (
                <>
                    <UitestScriptSleep
                        formik={formik}
                        stepUpdateInitData={stepUpdateInitData}
                        onChangeStepUpdataInitData={onChangeStepUpdataInitData}
                    />
                </>
            );
        }
        return '';
    };

    return (
        <>
            <Form onSubmit={formik.handleSubmit}>
                <Accordion defaultActiveKey="0">
                    <Accordion.Item eventKey="0">
                        <Accordion.Header>Script </Accordion.Header>
                        <Accordion.Body>
                            <Row>
                                <Col xs="3">
                                    <FloatingLabel
                                        label={t('pages.scenario.scriptType')}
                                    >
                                        <Form.Select
                                            disabled={
                                                stepUpdateInitData?.stepId
                                            }
                                            {...formik.getFieldProps(
                                                'script.scriptType'
                                            )}
                                        >
                                            {takeSelectOptionbyCode(
                                                codeData,
                                                'ScriptType'
                                            )}
                                        </Form.Select>
                                    </FloatingLabel>
                                </Col>
                                {/* script name (inputData2) */}
                                <Col xs="5">
                                    <FloatingLabel
                                        label={t('pages.scenario.title')}
                                        className="mb-1"
                                    >
                                        <Form.Control
                                            type="text"
                                            maxLength="30"
                                            {...formik.getFieldProps(
                                                'stepName'
                                            )}
                                        />
                                        {printFormError(formik, 'stepName')}
                                    </FloatingLabel>
                                </Col>

                                <Col xs="12">
                                    <div key="isSkipError" className="mb-3">
                                        <Form.Check
                                            type="checkbox"
                                            id="isSkipError"
                                            label={t(
                                                'pages.scenario.isSkipError'
                                            )}
                                            {...formik.getFieldProps(
                                                'isSkipError'
                                            )}
                                            checked={formik.values.isSkipError}
                                        />
                                    </div>
                                </Col>
                            </Row>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
                <br />
                {renderScriptType()}
                <br />

                <Modal.Footer>
                    <Button
                        theme="primary"
                        type="submit"
                        // isLoading={createLoading}
                    >
                        {t('common.button.register')}
                    </Button>
                    <Button theme="default" onClick={handleClose}>
                        {t('common.button.close')}
                    </Button>
                </Modal.Footer>
            </Form>
        </>
    );
};

export default UitestStepPopScript;
