/* eslint-disable no-unused-vars */
import React from 'react';
import {
    ResponsiveContainer,
    BarChart,
    Bar,
    XAxis,
    YAxis,
    CartesianGrid,
    Tooltip,
    Legend
} from 'recharts';
import {useTranslation} from 'react-i18next';
// import Card2 from '../../components/card/Card';
import {Card, Form} from 'react-bootstrap';

const DashboardFailCnt = ({
    serverData,
    selectedServer,
    searchStatData,
    onChangeServerId
}) => {
    const [t] = useTranslation();

    const renderServerSelect = () => {
        return serverData?.data
            ?.filter((obj) => obj.serverType === 'HTTP')
            .map((obj) => (
                <option value={obj.serverId} key={obj.serverId}>
                    {obj.serverName}
                </option>
            ));
    };

    // {
    //     name: 'Page G',
    //     uv: 3490,
    //     pv: 4300
    // }

    const getInitData = () => {
        const result = {
            Success: 0,
            Fail: 0
        };

        return result;
    };

    // eslint-disable-next-line no-unused-vars
    const renderChart = () => {
        if (searchStatData) {
            const data = [];
            searchStatData.data.forEach((obj) => {
                const tempObj = getInitData();
                tempObj.name = obj.date;
                tempObj.Success = obj.successCnt;
                tempObj.Fail = obj.failCnt;
                data.push(tempObj);
            });

            return (
                <>
                    <ResponsiveContainer width="100%" height="100%">
                        <BarChart width={500} height={300} data={data}>
                            <CartesianGrid strokeDasharray="3 3" />
                            <XAxis dataKey="name" />
                            <YAxis />
                            <Tooltip />
                            <Legend />
                            <Bar dataKey="Success" fill="#16AA52" />
                            <Bar dataKey="Fail" fill="#ff7f7f" />
                        </BarChart>
                    </ResponsiveContainer>
                </>
            );
        }
        return '';
    };

    return (
        <>
            <Card style={{borderTop: '3px solid #007bff'}}>
                <Card.Header style={{padding: '0.2rem 1.25rem'}}>
                    <>
                        <div style={{float: 'left'}}>
                            <img
                                src="/img/menu3.png"
                                alt="atworks Logo"
                                className="brand-image"
                                style={{maxWidth: '22px'}}
                            />

                            <label style={{paddingLeft: '11px'}}>
                                {t('pages.dashboard.failCntFromServer')}
                            </label>
                        </div>

                        <Form.Select
                            style={{width: '300px', float: 'right'}}
                            value={selectedServer}
                            onChange={onChangeServerId}
                        >
                            {renderServerSelect()}
                        </Form.Select>
                    </>
                </Card.Header>
                <Card.Body>
                    <>
                        <div style={{height: '600px'}}>{renderChart()}</div>
                    </>
                </Card.Body>
            </Card>
        </>
    );
};

export default DashboardFailCnt;
