/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import {useHistory} from 'react-router-dom';
import {useSelector, useDispatch} from 'react-redux';
import StatTab2Search from '@pages/stat/StatTab2Search';
import Spinner from '../../components/spinner/Spinner';
import {
    setSearchFormT2,
    searchStatT2,
    initSearchFormT2
} from '../../store/stat';

function StatTab2SearchContainer() {
    const dispatch = useDispatch();
    const history = useHistory();

    const {searchFormT2} = useSelector((state) => state.stat);

    const {loading: searchT2Loading} = useSelector(
        (state) => state.stat.searchT2
    );

    const saveSearchFormT2 = (_searchFormT2) => {
        dispatch(setSearchFormT2(_searchFormT2));
    };

    /*
     * T1 search interface
     */
    const onSearch = (_searchFormT2 = searchFormT2) => {
        const tempSearchForm = {..._searchFormT2};
        tempSearchForm.fromDateOld = tempSearchForm.fromDate;
        tempSearchForm.toDateOld = tempSearchForm.toDate;
        saveSearchFormT2(tempSearchForm);

        dispatch(searchStatT2(_searchFormT2.fromDate, _searchFormT2.toDate));
    };

    useEffect(() => {
        // 2021-10-06 screen  
        if (history.action === 'PUSH') {
            const initSearchForm = initSearchFormT2();
            dispatch(setSearchFormT2(initSearchForm));
            onSearch(initSearchForm);
        }
        if (history.action === 'POP') {
            onSearch();
        }
    }, []);
    return (
        <>
            <StatTab2Search
                // serverData={serverData}
                searchFormT2={searchFormT2}
                saveSearchFormT2={saveSearchFormT2}
                onSearch={onSearch}
            />
            <Spinner isLoading={searchT2Loading} />
        </>
    );
}

export default StatTab2SearchContainer;
