/* eslint-disable jsx-a11y/label-has-associated-control */
/* eslint-disable no-unused-vars */
import React from 'react';
import {useTranslation} from 'react-i18next';
import {Row, Col, Form, FloatingLabel} from 'react-bootstrap';
import Button from '../../components/button/Button';
import Card from '../../components/card/Card';
import {renderOptionsByCode} from '../../lib/codeUtils';

const LayoutListSearch = ({
    onSearch,
    searchLayoutForm,
    saveSearchLayoutForm,
    showCreateModal
}) => {
    const [t] = useTranslation();
    const onChangerSearchForm = (e) => {
        e.preventDefault();
        const searchLayoutFormT = {...searchLayoutForm};
        searchLayoutFormT[e.target.id] = e.target.value;
        saveSearchLayoutForm(searchLayoutFormT);
    };

    const onClickSearch = (e) => {
        e.preventDefault();
        const searchLayoutFormT = {...searchLayoutForm};
        searchLayoutFormT.page = 1;
        saveSearchLayoutForm(searchLayoutFormT);
        onSearch(searchLayoutFormT);
    };

    const onCheckEnter = (e) => {
        if (e.key === 'Enter') {
            onClickSearch(e);
        }
    };

    return (
        <Card
            title={t('common.text.searchCardTitleName')}
            body={
                <>
                    <Row>
                        <Col xs="6">
                            <Row>
                                <Col xs="8">
                                    <FloatingLabel
                                        label="API "
                                        className="mb-2"
                                    >
                                        <Form.Control
                                            type="text"
                                            id="layoutQuery"
                                            onChange={onChangerSearchForm}
                                            value={
                                                searchLayoutForm?.layoutQuery
                                            }
                                            onKeyPress={onCheckEnter}
                                            maxLength="50"
                                        />
                                    </FloatingLabel>
                                </Col>
                                <Col xs="4">
                                    <FloatingLabel
                                        label={t('pages.scenario.httpMethod')}
                                        className="mb-2"
                                    >
                                        <Form.Select
                                            value={searchLayoutForm?.httpMethod}
                                            id="httpMethod"
                                            onChange={onChangerSearchForm}
                                            onKeyPress={onCheckEnter}
                                        >
                                            <option value="">
                                                {t('common.text.optionAll')}
                                            </option>
                                            {renderOptionsByCode('HttpMethods')}
                                        </Form.Select>
                                    </FloatingLabel>
                                </Col>
                            </Row>
                        </Col>
                        <Col xs="4">
                            <Row>
                                <Col xs="6">
                                    <FloatingLabel label="API ">
                                        <Form.Select
                                            value={
                                                searchLayoutForm?.transactionType
                                            }
                                            id="transactionType"
                                            onChange={onChangerSearchForm}
                                            onKeyPress={onCheckEnter}
                                        >
                                            <option value="">
                                                {t('common.text.optionAll')}
                                            </option>
                                            {renderOptionsByCode(
                                                'TransactionType'
                                            )}
                                        </Form.Select>
                                    </FloatingLabel>
                                </Col>
                            </Row>
                        </Col>
                        <Col xs="1">
                            <Form.Group>
                                <Form.Label />
                                <Button
                                    type="button"
                                    className="btn btn-block btn-secondary btn-search"
                                    // isLoading={searchLoading}
                                    // onClick={onSearch}
                                    onClick={onClickSearch}
                                >
                                    {t('common.button.search')}
                                </Button>
                            </Form.Group>
                        </Col>
                        <Col xs="1">
                            <Form.Group>
                                <Form.Label />
                                <Button
                                    type="button"
                                    className="btn btn-block btn-success btn-search"
                                    // isLoading={searchLoading}
                                    onClick={showCreateModal}
                                >
                                    + 
                                </Button>
                            </Form.Group>
                        </Col>
                    </Row>
                </>
            }
        />
    );
};

export default LayoutListSearch;
