/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import {useHistory} from 'react-router-dom';
import StatTab1Search from '@pages/stat/StatTab1Search';
import {useSelector, useDispatch} from 'react-redux';
import {
    setSearchFormT1,
    searchStatT1Cnt,
    searchStatT1Time,
    searchStatT1Rate,
    initSearchFormT1
} from '../../store/stat';
import Spinner from '../../components/spinner/Spinner';

function StatTab1SearchContainer() {
    const dispatch = useDispatch();
    const history = useHistory();

    // loding bar
    // searchT1Cnt: reducerUtils.initial(),
    // searchT1Rate: reducerUtils.initial(),
    // searchT1Time: reducerUtils.initial()
    const {loading: cntLoading} = useSelector(
        (state) => state.stat.searchT1Cnt
    );

    const {loading: rateLoading} = useSelector(
        (state) => state.stat.searchT1Rate
    );
    const {loading: timeLoading} = useSelector(
        (state) => state.stat.searchT1Time
    );

    const {data: serverData} = useSelector(
        (state) => state.common.searchServer
    );

    const {searchFormT1} = useSelector((state) => state.stat);

    const saveSearchFormT1 = (_searchFormT1) => {
        dispatch(setSearchFormT1(_searchFormT1));
    };

    /*
     * T1 search interface
     */
    const onSearch = (_searchFormT1 = searchFormT1) => {
        // serverId, fromDate, toDate
        // 1   
        dispatch(
            searchStatT1Cnt(
                _searchFormT1.serverId,
                _searchFormT1.fromDate,
                _searchFormT1.toDate
            )
        );

        // 2   
        dispatch(
            searchStatT1Rate(
                _searchFormT1.serverId,
                _searchFormT1.fromDate,
                _searchFormT1.toDate
            )
        );

        // 3   
        dispatch(
            searchStatT1Time(
                _searchFormT1.serverId,
                _searchFormT1.fromDate,
                _searchFormT1.toDate
            )
        );

        // 2021-10-10 ~ 2021-10-11    
        const tempSearchForm = {..._searchFormT1};
        tempSearchForm.serverIdOld = tempSearchForm.serverId;
        tempSearchForm.fromDateOld = tempSearchForm.fromDate;
        tempSearchForm.toDateOld = tempSearchForm.toDate;

        if (_searchFormT1.serverId === '-1') {
            tempSearchForm.serverNameOld = '';
        } else {
            const selectedServer = serverData.data?.filter(
                (obj) => obj.serverId === Number(_searchFormT1.serverId)
            );

            tempSearchForm.serverNameOld = selectedServer[0]?.serverName;
        }
        saveSearchFormT1(tempSearchForm);
    };

    useEffect(() => {
        // 2021-10-06 screen  
        if (history.action === 'PUSH') {
            const initSearchForm = initSearchFormT1();
            dispatch(setSearchFormT1(initSearchForm));
            onSearch(initSearchForm);
        }
        if (history.action === 'POP') {
            onSearch();
        }
    }, []);

    return (
        <>
            <StatTab1Search
                serverData={serverData}
                searchFormT1={searchFormT1}
                saveSearchFormT1={saveSearchFormT1}
                onSearch={onSearch}
            />
            <Spinner isLoading={cntLoading || rateLoading || timeLoading} />
        </>
    );
}

export default StatTab1SearchContainer;
