/* eslint-disable import/prefer-default-export */

import axios from 'axios';

export const analysisSource = async ({directoryPath}) => {
    const response = await axios.post('/sources/analysis', {
        directoryPath
    });

    return response;
};

export const searchSourceSnapshot = async ({
    fromDate,
    toDate,
    page = 1,
    size = 10
}) => {
    const response = await axios.get(
        `/sources/snapshots?sort=id,desc&fromDate=${fromDate}&toDate=${toDate}&page=${
            page - 1
        }&size=${size}`
    );
    return response;
};

export const deleteSourceSnapshot = async ({id}) => {
    const response = await axios.delete(`/sources/snapshots/${id}`, null);
    return response;
};

export const searchSources = async ({
    sourceSnapshotId,
    sourceType,
    sourceName,
    page = 1,
    size = 10
}) => {
    const response = await axios.get(
        `/sources?sort=id,desc&sourceSnapshotId=${sourceSnapshotId}&sourceType=${sourceType}&sourceName=${sourceName}&page=${
            page - 1
        }&size=${size}`
    );
    return response;
};
