/* eslint-disable import/prefer-default-export */

import axios from 'axios';

/**
 * History Paging 
 * searchHistory (SEARCH_HISTORY) : 
 * clearSearchHistory (CLEAR_SEARCH_HISTORY) : 
 * setSearchForm (SET_SEARCH_FORM) :  
 */
export const searchHistory = async ({
    serviceId,
    pageId,
    regCode,
    fromDateTime,
    toDateTime,
    systemResult,
    serverId,
    logSnapShotId,
    page = 1,
    size = 10
}) => {
    const response = await axios.get(
        `/histories?sort=id,desc&serviceId=${encodeURI(
            serviceId
        )}&pageId=${pageId}&regCode=${regCode}&fromDateTime=${fromDateTime}&toDateTime=${toDateTime}&systemResult=${systemResult}&serverId=${serverId}&logSnapShotId=${logSnapShotId}&page=${
            page - 1
        }&size=${size}`
    );
    return response;
};

/**
 * history id    
 * searchHistoryById (SEARCH_HISTORY_BYID) : 
 * clearSearchHistoryById (CLEAR_SEARCH_HISTORY_BYID) : 
 */
export const searchHistoryById = async ({historyId}) => {
    const response = await axios.get(`/histories/${historyId}`);
    return response;
};
