import axios from 'axios';

/**
 * (TR)  
 * uploadClientExecuteFile (UPLOAD_CLIENT_EXECUTE_FILE) : 
 * clearUploadClientExecuteFile (CLEAR_UPLOAD_CLIENT_EXECUTE_FILE) : 
 */
export const uploadClientExecuteFile = async ({file}) => {
    const formData = new FormData();
    formData.append('file', file);
    const response = await axios.post(
        '/client-install/upload/execute-file',
        formData,
        {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        }
    );

    return response;
};

/**
 * (TR)   
 * searchNowClientInstallFileVersion (SEARCH_NOW_CLIENT_INSTALL_FILE_VERSION) : 
 */
export const searchNowClientInstallFileVersion = async () => {
    const response = await axios.get(
        `/client-install/lastest-install-file-info`
    );
    return response;
};

/**
 * (TR)  
 * uploadClientInstallFile (UPLOAD_CLIENT_INSTALL_FILE) : 
 * clearUploadClientInstallFile (CLEAR_UPLOAD_CLIENT_INSTALL_FILE) : 
 */
export const uploadClientInstallFile = async ({newVersion, file}) => {
    const formData = new FormData();
    formData.append('file', file);
    const response = await axios.post(
        `/client-install/upload/install-file/${newVersion}`,
        formData,
        {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        }
    );

    return response;
};

/**
 * (TR)   
 * searchChromeDriverFileInfo (SEARCH_CHROME_DRIVER_FILE_VERSION) : 
 */
export const searchChromeDriverFileInfo = async () => {
    const response = await axios.get(`/webdrivers?browser=CHROME&os=WINDOW`);
    return response;
};

/**
 * (TR)  
 * uploadChromeDriverFile (UPLOAD_CHROME_DRIVER_FILE) : 
 * clearUploadChromeDriverFile (CLEAR_UPLOAD_CHROME_DRIVER_FILE) : 
 */
// eslint-disable-next-line no-unused-vars
export const uploadChromeDriverFile = async ({version, os, file}) => {
    const formData = new FormData();
    formData.append('file', file);
    formData.append('os', 'WINDOW');
    formData.append('version', version);
    formData.append('browser', 'CHROME');

    const response = await axios.post(`/webdrivers`, formData, {
        headers: {
            'Content-Type': 'multipart/form-data'
        }
    });

    return response;
};

/**
 * (TR)  
 * searchEnvironmentVariable (SEARCH_ENVIRONMENT_VARIABLE) : 
 */
export const searchEnvironmentVariable = async () => {
    const response = await axios.get(`/environment`);
    return response;
};

/**
 * (TR)  
 * saveEnvironmentVariable (SAVE_ENVIRONMENT_VARIABLE) : 
 * clearSaveEnvironmentVariable (CLEAR_SAVE_ENVIRONMENT_VARIABLE) : 
 */
export const saveEnvironmentVariable = async ({
    environmentVariableId,
    environmentValue
}) => {
    const response = await axios.post(
        `/environment/${environmentVariableId}`,
        environmentValue
    );
    return response;
};
