/* eslint-disable no-unused-vars */
import React, {useState} from 'react';

import {Row, Col} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
// import {Table} from 'antd';
import {FaSearch, FaTrash} from 'react-icons/fa';
import Table from '../../components/table/Table';
import Button from '../../components/button/Button';

// scenarioData={scenarioData} // detail scenario data
// selectedCaseId={selectedCaseId} // list selected case id
const UitestVariable = ({
    scenarioData,
    openNewVariable,
    openUpdateVariable,
    saveDetailVariableList
}) => {
    const [t] = useTranslation();

    const deleteVariable = (record) => {
        // eslint-disable-next-line no-alert
        if (window.confirm(t('common.alert.checkDelete'))) {
            const variables = [...scenarioData.data?.variables];
            const variablesExcept = variables.filter(
                (info) => info.variableId !== record.variableId
            );
            saveDetailVariableList(variablesExcept);
        }
    };

    const columns = [
        {
            title: '',
            key: 'variableType',
            width: '20%'
        },
        {
            title: '',
            key: 'variableName',
            width: '20%'
        },
        {
            title: '',
            key: 'initialValue',
            align: 'left'
        },
        {
            title: 'Operates',
            key: 'button',
            align: 'right', // left, center, right (default:center)
            render: (text, record, index) => (
                <>
                    <Button
                        theme="outline-success"
                        onClick={(e) => openUpdateVariable(record)}
                    >
                        <FaSearch />
                    </Button>
                    <Button
                        theme="outline-danger"
                        onClick={(e) => deleteVariable(record)}
                    >
                        <FaTrash />
                    </Button>
                </>
            )
        }
    ];

    const renderTableBody = () => {
        if (!scenarioData || !scenarioData.data.variables) {
            return {
                data: {
                    // data: {
                    content: []
                    // }
                }
            };
        }

        const variables = scenarioData.data.variables.map((obj, index) => {
            const tempCol = obj;
            tempCol.key = tempCol.variableId;
            return tempCol;
        });

        return {
            data: {
                content: variables
            }
        };
    };

    return (
        <>
            <Row>
                <Col xs="12">
                    <Button
                        type="button"
                        className="btn btn-block btn-secondary btn-search"
                        onClick={openNewVariable}
                    >
                        {t('common.button.newVariable')}
                    </Button>
                </Col>
            </Row>
            <div
                style={{
                    overflowY: 'auto',
                    maxHeight: '433px',
                    minHeight: '433px'
                }}
            >
                <Table
                    columns={columns}
                    rowKey="variableName"
                    pagination={false}
                    data={renderTableBody()}
                    isCard="N"
                    disablePageView
                />
            </div>
        </>
    );
};

export default UitestVariable;
