/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import {toast} from 'react-toastify';
import TextDiffViewerPopup from '../../pages/common/TextDiffViewerPopup';
import {
    clearTextDiffModal,
    searchHistoryCompare,
    clearSearchHistoryCompare
} from '../../store/common';
import {searchBulkCompare, clearSearchBulkCompare} from '../../store/bulk';
import Spinner from '../../components/spinner/Spinner';
import {getErrorMsg} from '../../lib/commonUiUtils';

function TextDiffViewerPopupContainer() {
    const dispatch = useDispatch();

    const [modalShow, setModalShow] = React.useState(false);

    // const handleClose = () => dispatch(clearTextDiffModal());
    const handleClose = () => {
        setModalShow(false);
        dispatch(clearTextDiffModal());
        dispatch(clearSearchBulkCompare());
    };
    const handleShow = () => setModalShow(true);

    const {textDiffData} = useSelector((state) => state.common);

    const {
        loading: searchLoading,
        error: searchError,
        data: searchData
    } = useSelector((state) => state.bulk.searchBulkCompareRes);

    const {
        loading: searchLoading2,
        error: searchError2,
        data: searchData2
    } = useSelector((state) => state.common.searchHistoryCompareRes);
    const [searchDataUse, setSearchDataUse] = React.useState(searchData);
    /*
     * popup call event
     */
    useEffect(() => {
        if (textDiffData.showModal && !modalShow) {
            dispatch(clearSearchBulkCompare());

            if (textDiffData.data.bulkeId) {
                dispatch(searchBulkCompare(textDiffData.data.bulkeId));
            } else {
                dispatch(
                    searchHistoryCompare(
                        textDiffData.data.basicData,
                        textDiffData.data.compareIdA,
                        textDiffData.data.compareIdB
                    )
                );
            }

            handleShow();
        }
    }, [textDiffData.showModal]);

    /*
     *      
     */
    useEffect(() => {
        // search
        if (!searchLoading && searchError) {
            // data setting && move next frame
            toast.error(getErrorMsg(searchError, 'search'));
            handleClose();
            // dispatch(clearSearchBulkDetail());
        }
        // search fail
        if (!searchLoading && searchData) {
            setSearchDataUse(searchData);

            // toast.error(
            //     (searchPredictError.response &&
            //         searchPredictError.response.data &&
            //         searchPredictError.response.data.message) ||
            //         'Failed'
            // );
            // const initDataT = {...initData};
            // initDataT.predictTestCount = 0;
            // setInitData(initDataT);
            // dispatch(clearPredictExecuteCount());
        }
    }, [searchData, searchError]);

    /*
     *      
     */
    useEffect(() => {
        // search
        if (!searchLoading2 && searchError2) {
            // data setting && move next frame
            toast.error(getErrorMsg(searchError2, 'search'));
            dispatch(clearSearchHistoryCompare());
            handleClose();
            //
        }
        // search fail
        if (!searchLoading2 && searchData2) {
            setSearchDataUse(searchData2);
        }
    }, [searchData2, searchError2]);

    return (
        <>
            <TextDiffViewerPopup
                modalShow={modalShow}
                handleClose={handleClose}
                searchData={searchDataUse}
            />
            <Spinner isLoading={searchLoading || searchLoading2} />
        </>
    );
    ///
}

export default TextDiffViewerPopupContainer;
