import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import Login from '@modules/login/Login';
import {toast} from 'react-toastify';
import {
    processLogin,
    processLoginDataClear,
    settingLoginInfo,
    goToMain
} from '../store/auth';
import {getErrorMsg} from '../lib/commonUiUtils';

/*
 *  
 */
function LoginContainer() {
    const dispatch = useDispatch();

    const {data, loading, error} = useSelector((state) => state.auth.login);
    /*
     *   
     */
    const onLogin = (email, password) => {
        dispatch(processLogin(email, password));
    };

    /*
     *   
     */
    useEffect(() => {
        // login success
        if (!loading && data) {
            const {token} = data.data;

            document.getElementById('root').classList.remove('login-page');
            document.getElementById('root').classList.remove('hold-transition');

            dispatch(processLoginDataClear());
            dispatch(settingLoginInfo(token));
            dispatch(goToMain());
        }
        // login fail
        if (!loading && error) {
            // eslint-disable-next-line no-debugger
            toast.error(getErrorMsg(error, 'login'));
            dispatch(processLoginDataClear());
        }
    }, [data, error]);

    return (
        <>
            <Login loading={loading} onLogin={onLogin} />
        </>
    );
}

export default LoginContainer;
