/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import DashBoardHead from '@pages/dashboard/DashboardHead';
import {useSelector, useDispatch} from 'react-redux';
import {searchDayTestResult} from '../../store/dashboard';

function DashboardHeadContainer() {
    const dispatch = useDispatch();

    const offset = new Date().getTimezoneOffset() * 60000;
    const today = new Date(Date.now() - offset);

    const {data: searchStatData} = useSelector(
        (state) => state.dashboard.searchDayTestResult
    );

    const [searchDate, setSearchDate] = React.useState(
        today.toISOString().slice(0, -1).slice(0, 10)
    );

    const onChangerSearchDate = (e) => {
        e.preventDefault();
        setSearchDate(e.target.value);
        dispatch(searchDayTestResult(e.target.value));
    };
    useEffect(() => {
        dispatch(searchDayTestResult(searchDate));
        return () => {};
    }, []);
    return (
        <>
            <DashBoardHead
                searchDate={searchDate}
                searchStatData={searchStatData}
                onChangerSearchDate={onChangerSearchDate}
            />
        </>
    );
    ///
}

export default DashboardHeadContainer;
