/* eslint-disable jsx-a11y/no-noninteractive-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
import React from 'react';
import {Table} from 'antd';
import 'antd/dist/antd.css';
import {Col, Row} from 'react-bootstrap';
import ReactDragListView from 'react-drag-listview';
import 'react-simple-tree-menu/dist/main.css';
import {FaSearch, FaTrash, FaForward} from 'react-icons/fa';
import {useTranslation} from 'react-i18next';
import Button from '../../components/button/Button';
import UitestCaseCreateContainer from '../../containers/uitest/popup/UitestCaseCreateContainer';
//
//
//
const UitestCaseList = ({
    scenarioData,
    setDetailCaseList,
    setSelectedCaseId,
    onClickPlay,
    caseId
}) => {
    // json to react-drag-listview data
    const [t] = useTranslation();
    const renderTableBody = () => {
        if (!scenarioData) {
            return [];
        }

        // const caseListT = [...scenarioData.data.caseList];
        const caseListT = scenarioData.data?.cases?.map((unit) => {
            const temp = unit;
            temp.key = temp.id;
            return temp;
        });

        return caseListT;
    };
    // checkDelete
    const onClickDelete = (record) => {
        // eslint-disable-next-line no-alert
        if (window.confirm(t('common.alert.checkDelete'))) {
            if (caseId < 0 || record.caseId === caseId) {
                setSelectedCaseId('');
            }

            const data = [...scenarioData.data.cases];
            const newArr2 = data.filter(
                (info) => info.caseId !== record.caseId
            );

            const aLoop = newArr2.map((unit, index) => {
                const temp = unit;
                temp.index = index;
                return temp;
            });

            setDetailCaseList(aLoop);
        }
    };

    // SET_SELECTED_CASEID ->  case  
    const onClickColumn = (record) => {
        setSelectedCaseId(record.caseId);
    };

    const columns = [
        {
            title: 'index',
            dataIndex: 'index',
            key: 'index',
            width: '5%',
            render: (text) => (
                <h4 style={{color: 'black', fontSize: '1.0rem'}}>{text + 1}</h4>
            )
        },
        {
            title: 'Name',
            dataIndex: 'caseName',
            key: 'caseName',
            // width: '60%',
            render: (text, record) => (
                <h4
                    style={{color: 'black', fontSize: '1.0rem'}}
                    onClick={() => onClickColumn(record)}
                >
                    {text} ({record.steps ? record.steps.length : 0} Step)
                </h4>
            )
        },
        {
            title: 'Operates',
            key: 'operate',
            // width: '20%',
            render: (text, record) => (
                <>
                    <Button
                        theme="outline-success"
                        onClick={() => onClickColumn(record)}
                    >
                        <FaSearch />
                    </Button>
                    <Button
                        theme="outline-primary"
                        onClick={() => onClickPlay(record)}
                    >
                        <FaForward />
                    </Button>
                    <Button
                        theme="outline-danger"
                        onClick={() => onClickDelete(record)}
                    >
                        <FaTrash />
                    </Button>
                </>
            )
        }
    ];

    const dragProps = {
        onDragEnd(fromIndex, toIndex) {
            const data = [...scenarioData.data?.cases];
            const item = data.splice(fromIndex, 1)[0];
            data.splice(toIndex, 0, item);

            const aLoop = data.map((unit, index) => {
                const temp = unit;
                temp.index = index;
                return temp;
            });

            setDetailCaseList(aLoop);
        },
        handleSelector: 'tr'
    };

    return (
        <>
            <Row>
                <Col xs="12">
                    <UitestCaseCreateContainer />
                </Col>
            </Row>
            <div
                style={{
                    overflowY: 'auto',
                    maxHeight: '433px',
                    minHeight: '433px'
                }}
            >
                <ReactDragListView {...dragProps}>
                    <Table
                        rowKey="caseId"
                        columns={columns}
                        pagination={false}
                        dataSource={renderTableBody()}
                    />
                </ReactDragListView>
            </div>
        </>
    );
};

export default UitestCaseList;
