//        .
export const reducerUtils = {
    //  .  data   null 
    //   .
    initial: (initialData = null) => ({
        loading: false,
        data: initialData,
        error: null
    }),
    //  . prevState   null 
    //    null        .
    loading: (prevState = null) => ({
        loading: true,
        data: prevState,
        error: null
    }),
    //  
    success: (payload) => ({
        loading: false,
        data: payload,
        error: null
    }),
    //  
    error: (error) => ({
        loading: false,
        data: null,
        error
    })
};

//      .
// type   , key   key (: posts, post) .
export const handleAsyncActions = (
    type,
    key,
    keepData = false
    // showGlobalLoading = true
) => {
    const [SUCCESS, ERROR] = [`${type}_SUCCESS`, `${type}_ERROR`];
    return (state, action) => {
        switch (action.type) {
            case type:
                return {
                    ...state,
                    [key]: reducerUtils.loading(
                        keepData ? state[key].data : null
                    )
                };
            case SUCCESS:
                // if (showGlobalLoading) {
                //     releaseFinishLoadaingTask();
                // }
                return {
                    ...state,
                    [key]: reducerUtils.success(action.payload)
                };
            case ERROR:
                // if (showGlobalLoading) {
                //     releaseFinishLoadaingTask();
                // }
                return {
                    ...state,
                    [key]: reducerUtils.error(action.payload)
                };
            default:
                return state;
        }
    };
};

