/* eslint-disable no-unused-vars */
import React from 'react';
import {FaPlus, FaMinus} from 'react-icons/fa';
import {useTranslation} from 'react-i18next';
import {Form} from 'react-bootstrap';
import Button from '../../components/button/Button';

const LayoutDetailIoTable = ({
    selectedLayout,
    ioType,
    onChangeColumn,
    tempId,
    setTempId,
    codeData
}) => {
    const [t] = useTranslation();

    //  col   data
    // eslint-disable-next-line no-unused-vars
    const PARENT_DATA_TYPE = [
        'OBJECT',
        'LIST',
        'QUERY_STRING',
        'PATH_VARIABLE'
    ];
    const NO_SIZE_DATA = [
        'OBJECT',
        'LIST',
        'LIST_STRING',
        'LIST_NUMBER',
        'LIST_BOOLEAN',
        'BOOLEAN',
        'QUERY_STRING',
        'PATH_VARIABLE'
    ];

    const getTempId = () => {
        setTempId(tempId - 1);
        return tempId;
    };

    const setColList = (e, cols) => {
        cols.map((col) => {
            const tempCol = col;
            if (
                tempCol.colId ===
                Number(e.target.parentElement.parentElement.id)
            ) {
                tempCol.colAttribute[e.target.name] = e.target.value;
            }

            if (col.cols.length > 0) {
                // if (PARENT_DATA_TYPE.includes(col.colAttribute?.dataType)) {
                setColList(e, col.cols);
            }
            return tempCol;
        });
    };

    const onChangeInput = (e) => {
        const preSelectedLayout = {...selectedLayout};
        setColList(e, preSelectedLayout.cols);
        onChangeColumn(preSelectedLayout);
    };

    const initColAttribute = () => {
        return {
            logicalName: '',
            pyhsicalName: '',
            length: 0,
            dataType: '',
            seq: null
        };
    };

    const addColList = (colId, type, cols) => {
        const filterCols = [];

        cols.forEach((col) => {
            const tempCol = col;
            if (type === 0 || tempCol.colId !== colId) {
                filterCols.push(col);
            }

            if (tempCol.colId === colId) {
                const temp = {
                    colId: getTempId(),
                    colAttribute: initColAttribute(),
                    inOut: ioType,
                    cols: []
                };
                if (type === 0) {
                    filterCols.push(temp);
                } else {
                    col.cols.push(temp);
                    filterCols.push(col);
                }
            } else if (PARENT_DATA_TYPE.includes(tempCol.dataType)) {
                tempCol.cols = addColList(colId, type, col.cols);
            }
        });

        return filterCols;
    };

    const onClickAdd = (row) => {
        const preSelectedLayout = {...selectedLayout};
        if (PARENT_DATA_TYPE.includes(row.colAttribute?.dataType)) {
            if (
                // eslint-disable-next-line no-alert
                window.confirm(
                    `${row.colAttribute?.dataType}  ?`
                )
            ) {
                // if (window.confirm(t('common.alert.checkDelete'))) {
                preSelectedLayout.cols = addColList(
                    row.colId,
                    1,
                    preSelectedLayout.cols
                );
            } else {
                preSelectedLayout.cols = addColList(
                    row.colId,
                    0,
                    preSelectedLayout.cols
                );
            }
        } else if (row.type === 'root list') {
            preSelectedLayout.cols = addColList(
                row.colId,
                1,
                preSelectedLayout.cols
            );
        } else {
            preSelectedLayout.cols = addColList(
                row.colId,
                0,
                preSelectedLayout.cols
            );
        }
        onChangeColumn(preSelectedLayout);
    };

    const onClickAddNew = () => {
        const preSelectedLayout = {...selectedLayout};
        const temp = {
            colId: getTempId(),
            colAttribute: initColAttribute(),
            inOut: ioType,
            cols: []
        };
        preSelectedLayout.cols.push(temp);
        onChangeColumn(preSelectedLayout);
    };

    const delColList = (id, cols) => {
        return cols
            .filter((col) => col.colId !== id)
            .map((col) => {
                const tempCol = col;
                if (PARENT_DATA_TYPE.includes(tempCol.dataType)) {
                    tempCol.cols = delColList(id, col.cols);
                }
                return tempCol;
            });
    };

    const onClickDelete = (row) => {
        const preSelectedLayout = {...selectedLayout};
        preSelectedLayout.cols = delColList(row.colId, preSelectedLayout.cols);
        onChangeColumn(preSelectedLayout);
    };

    const serializeCols = (cols, depth) => {
        //     elementCol.padL = `${element.depth * 30}px`;
        const resultList = [];
        cols?.forEach((col) => {
            if (col.crud !== 'D') {
                // eslint-disable-next-line no-param-reassign
                col.padL = String(depth * 30);
                // eslint-disable-next-line no-param-reassign
                col.padL += 'px';
                resultList.push(col);
                if (PARENT_DATA_TYPE.includes(col.colAttribute?.dataType)) {
                    serializeCols(col.cols, depth + 1).forEach((_col) => {
                        resultList.push(_col);
                    });
                }
            }
        });
        return resultList;
    };

    /*
     * renderPyhsicalName : PyhsicalName render / 
     */
    const renderPhysicalName = (row) => {
        return (
            <td className="text-left">
                <Form.Control
                    type="text"
                    placeholder=""
                    name="physicalName"
                    value={row.colAttribute?.physicalName}
                    onChange={onChangeInput}
                />
            </td>
        );
    };

    const renderSize = (row) => {
        if (
            row.inOut === 'OUT' ||
            NO_SIZE_DATA.includes(row.colAttribute?.dataType) ||
            row.colAttribute?.dataType === 'root list'
        ) {
            return '';
        }
        return (
            <Form.Control
                type="number"
                placeholder=""
                name="length"
                value={row.colAttribute?.length || 0}
                onChange={onChangeInput}
                style={{maxWidth: '100px'}}
            />
        );
    };

    const renderSelectOptionDataType = (dataType, row) => {
        const selectedData = codeData?.data?.filter(
            (obj) => obj.group === 'DataType'
        );

        let selectedOption = [...selectedData[0]?.list];

        const isRootCol = row.padL === '0px';
        // getParentRow  Col dataType QUERY_STRING, PATH_VARIABLE   String, boolean, number datatype  
        // getParentRow(preSelectedLayout, row.colId);

        if (dataType === 'QUERY_STRING' || dataType === 'PATH_VARIABLE') {
            selectedOption = selectedOption.filter(
                (obj) => obj.name === dataType
            );
        } else if (dataType === 'LIST' || dataType === 'OBJECT') {
            selectedOption = selectedOption.filter(
                (obj) => obj.name === 'LIST' || obj.name === 'OBJECT'
            );
        } else if (dataType === '') {
            if (!isRootCol || ioType === 'OUT') {
                selectedOption = selectedOption.filter(
                    (obj) =>
                        obj.name !== 'PATH_VARIABLE' &&
                        obj.name !== 'QUERY_STRING'
                );
            }

            selectedOption.push({name: '', value: ''});
        } else {
            selectedOption = selectedOption.filter(
                (obj) =>
                    obj.name !== 'LIST' &&
                    obj.name !== 'OBJECT' &&
                    obj.name !== 'PATH_VARIABLE' &&
                    obj.name !== 'QUERY_STRING'
            );
        }

        return selectedOption.map((obj) => (
            <option key={obj.name} value={obj.name}>
                {obj.value}
            </option>
        ));
    };
    /*
     * renderType : Type render / 
     *  stinrg, number, list, object 
     */
    const renderType = (row) => {
        //  depth  .
        // const renderRootList = () => {
        //     if (row.depth === 0) {
        //         return <option value="root list">Root List</option>;
        //     }
        //     return '';
        // };

        const dataType = row.colAttribute?.dataType;
        let className = '';

        if (dataType === 'QUERY_STRING' || dataType === 'PATH_VARIABLE') {
            className = 'btn-info';
        }
        if (dataType === 'OBJECT') {
            className = 'btn-success';
        }
        if (dataType === 'LIST') {
            className = 'btn-warning';
        }

        return (
            <td className="text" style={{paddingLeft: row.padL}}>
                <Form.Select
                    value={dataType}
                    name="dataType"
                    className={className}
                    onChange={onChangeInput}
                    readOnly
                >
                    {renderSelectOptionDataType(dataType, row)}
                </Form.Select>
            </td>
        );
    };

    /**
     *  body
     */
    const renderTableBody = () => {
        const selectedLayoutTemp = {...selectedLayout};
        const colList = serializeCols(selectedLayoutTemp.cols, 0).filter(
            (obj) => obj.inOut === ioType
        );

        if (colList.length < 1) {
            return (
                <tr>
                    <td className="text-center" colSpan="8">
                        <Button
                            key="button"
                            theme="outline-success"
                            onClick={() => onClickAddNew()}
                        >
                            <FaPlus />
                            {t('pages.layout.addCol')}
                        </Button>
                    </td>
                </tr>
            );
        }

        const renderButton = (row) => {
            return (
                <>
                    <Button
                        theme="outline-success"
                        onClick={() => onClickAdd(row)}
                    >
                        <FaPlus />
                    </Button>
                    <Button
                        theme="outline-danger"
                        onClick={() => onClickDelete(row)}
                    >
                        <FaMinus />
                    </Button>
                </>
            );
        };

        return colList?.map((row) => {
            const dataType = row.colAttribute?.dataType;

            if (dataType === 'QUERY_STRING' || dataType === 'PATH_VARIABLE') {
                return (
                    <tr id={row.colId} key={row.colId}>
                        <td className="text">{row.inOut}</td>
                        {renderType(row)}
                        <td className="text" />
                        <td className="text" />
                        <td className="text" />
                        <td className="text-left">{renderButton(row)}</td>
                    </tr>
                );
            }
            return (
                <tr id={row.colId} key={row.colId}>
                    <td className="text">{row.inOut}</td>
                    {renderType(row)}
                    {renderPhysicalName(row)}
                    <td className="text-left">
                        <Form.Control
                            type="text"
                            placeholder=""
                            name="logicalName"
                            value={row.colAttribute?.logicalName}
                            onChange={onChangeInput}
                        />
                    </td>
                    <td className="text-left" width="7%">
                        {renderSize(row)}
                    </td>
                    <td className="text-left">{renderButton(row)}</td>
                </tr>
            );
        });
    };

    return (
        <>
            <table className="table table-hover text-nowrap">
                <thead>
                    <tr>
                        <th className="">in/out</th>
                        <th className="text-left">{t('pages.layout.type')}</th>
                        <th className="text-left">
                            {t('pages.layout.pyhsicalName')}
                        </th>
                        <th className="text-left">
                            {t('pages.layout.logicalName')}
                        </th>
                        <th className="text-left">{t('pages.layout.size')}</th>
                        <th className="text-left">-</th>
                    </tr>
                </thead>
                <tbody>{renderTableBody()}</tbody>
            </table>
        </>
    );
};

export default LayoutDetailIoTable;
