/* eslint-disable no-unused-vars */

import React from 'react';
import {useSelector, useDispatch} from 'react-redux';
import UitestStepPopLogin from '@pages/uitest/step/UitestStepPopLogin';
import {
    setDetailCaseList,
    setStepUpdateInitData,
    saveScenario
} from '../../../store/uitest';
import {setTempKey} from '../../../store/common';

function UitestStepPopLoginContainer({handleClose}) {
    const dispatch = useDispatch();
    const {data: codeData} = useSelector((state) => state.common.searchCode);
    const {data: scenarioData} = useSelector(
        (state) => state.uitest.scenarioInfo
    );

    const {caseId} = useSelector((state) => state.uitest.selectedCaseId);
    const saveDetailCaseList = (cases) => {
        dispatch(setDetailCaseList(cases));
    };

    const {stepUpdateInitData} = useSelector((state) => state.uitest);
    const onChangeStepUpdataInitData = (data) => {
        dispatch(setStepUpdateInitData({...data}));
    };

    const {tempkey} = useSelector((state) => state.common);
    const setTempKeyRezen = () => {
        if (tempkey < -9999) {
            dispatch(setTempKey(-1));
        } else {
            dispatch(setTempKey(tempkey - 1));
        }
    };

    return (
        <>
            <UitestStepPopLogin
                scenarioData={scenarioData}
                selectedCaseId={caseId}
                setDetailCaseList={saveDetailCaseList}
                handleClose={handleClose}
                stepUpdateInitData={stepUpdateInitData}
                onChangeStepUpdataInitData={onChangeStepUpdataInitData}
                tempkey={tempkey}
                setTempKeyRezen={setTempKeyRezen}
                codeData={codeData}
            />
        </>
    );
}

export default UitestStepPopLoginContainer;
