/* eslint-disable import/prefer-default-export */
import axios from 'axios';

export const searchDetailStatByLayout = async ({
    layoutId,
    serverId,
    fromDate,
    toDate
}) => {
    const response = await axios.get(
        `/stats/${layoutId}?serverId=${serverId}&fromDate=${fromDate}&toDate=${toDate}`
    );
    return response;
};

/**
 * LAYOUT  COUNT  (TR)
 * searchStatT1Cnt (SEARCH_FORM_T1_CNT) : 
 * setSearchFormT1 (SET_SEARCH_FORM_T1) :  
 */
export const searchStatOfNumber = async ({
    serverId,
    fromDate,
    toDate,
    page = 1,
    size = 10
}) => {
    const response = await axios.get(
        `/stats/of-number?serverId=${serverId}&fromDate=${fromDate}&toDate=${toDate}&page=${
            page - 1
        }&size=${size}`
    );
    return response;
};

