/**
 *    (TR)
 * searchStatT2 (SEARCH_FORM_T2) : 
 * setSearchFormT2 (SET_SEARCH_FORM_T2) :  
 */
export const searchStatT2 = async ({
    fromDate,
    toDate,
    page = 1,
    size = 100
}) => {
    const response = await axios.get(
        `/stats/by-server?sort=serverId,desc&fromDate=${fromDate}&toDate=${toDate}&page=${
            page - 1
        }&size=${size}`
    );
    return response;
};

/**
 *       (TR)
 * renewalStat (RENEWAL_STAT_DATA) : 
 * clearRenewalStat (CLEAR_RENEWAL_STAT_DATA) : CLEAR
 */
export const renewalStat = async ({dDay}) => {
    const response = await axios.post(`/stats/${dDay}`);
    return response;
};
