/* eslint-disable no-alert */
/* eslint-disable jsx-a11y/label-has-associated-control */
import React, {useState} from 'react';
import {Container, Row, Col, Form} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
import Button from '../../components/button/Button';
import Card from '../../components/card/Card';
import Help from '../../components/help/Help';

const ClientVersionManagement = ({
    nowInstallVersionInfoData,
    uploadClientExecuteFile,
    uploadClientInstallFile
}) => {
    const [t] = useTranslation();

    const [executeFileUploadForm, setExecuteFileUploadForm] = useState({
        file: null
    });
    const [installFileUploadForm, setInstallFileUploadForm] = useState({
        newVersion: null,
        file: null
    });

    const onChangeExecuteFile = (e) => {
        e.preventDefault();

        const newForm = {
            ...executeFileUploadForm,
            file: e.target.files[0]
        };
        setExecuteFileUploadForm(newForm);
    };

    const onChangeNewVersion = (e) => {
        e.preventDefault();
        const newForm = {
            ...installFileUploadForm,
            newVersion: e.target.value
        };
        setInstallFileUploadForm(newForm);
    };

    const onChangeInstallFile = (e) => {
        e.preventDefault();
        const newForm = {
            ...installFileUploadForm,
            file: e.target.files[0]
        };
        setInstallFileUploadForm(newForm);
    };

    const onClickExecuteFileUploadButton = () => {
        if (!executeFileUploadForm.file) {
            alert('upload file is not selected');
            return;
        }
        uploadClientExecuteFile(executeFileUploadForm.file);
    };

    const onClickInstallFileUploadButton = () => {
        if (!installFileUploadForm.file) {
            alert('upload file is not selected');
            return;
        }
        if (!installFileUploadForm.newVersion) {
            alert('newVersion is empty');
            return;
        }

        if (!/\d[.]\d[.]\d/.test(installFileUploadForm.newVersion)) {
            alert('newVersion is wrong format');
            return;
        }

        if (!nowInstallVersionInfoData) {
            // loading 
            alert('now Install version info is yet recieved');
            return;
        }

        // if (nowInstallVersionInfoData && nowInstallVersionInfoData.version) {
        //     alert('ddd');
        //     return;
        // }

        uploadClientInstallFile(
            installFileUploadForm.newVersion,
            installFileUploadForm.file
        );
    };

    const displayInstallVersionDownloadButton = () => {
        if (
            !nowInstallVersionInfoData ||
            !nowInstallVersionInfoData.data.version
        ) {
            return <></>;
        }

        return (
            <form
                method="get"
                action={nowInstallVersionInfoData.data.downloadUrl}
            >
                <Button className="float-right" type="submit" theme="link">
                    {`${t('common.button.downloadClientInstallFile')} (${
                        nowInstallVersionInfoData.data.version
                    })`}
                </Button>
            </form>
        );
    };

    const getNowVersion = () => {
        if (nowInstallVersionInfoData) {
            if (nowInstallVersionInfoData.data.version) {
                return nowInstallVersionInfoData.data.version;
            }

            return t('pages.setting.beforeUploadFirstInstallVersion');
        }

        return 'Loading...';
    };

    return (
        <>
            <Card
                title={t('pages.setting.clientVersionManagement')}
                body={
                    <Container fluid>
                        <Row>
                            <Col xs="6">
                                <h5>
                                    {t('pages.setting.executeFile')}
                                    <Help
                                        text={t(
                                            'pages.setting.executeFileHelp'
                                        )}
                                    />
                                </h5>
                            </Col>
                            <Col xs="6">
                                <form
                                    method="get"
                                    action="/no-auth-api/v1/client-install/download/execute-file"
                                >
                                    <Button
                                        className="float-right"
                                        theme="link"
                                        type="submit"
                                    >
                                        {t(
                                            'common.button.downloadClientExecuteFile'
                                        )}
                                    </Button>
                                </form>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs="10">
                                <Form.Group controlId="file" className="mb-3">
                                    <Form.Label />
                                    <Form.Control
                                        type="file"
                                        onChange={onChangeExecuteFile}
                                    />
                                </Form.Group>
                            </Col>
                            <Col xs="2">
                                <div className="form-group">
                                    <label htmlFor="myinput" />
                                    <Button
                                        type="button"
                                        className="btn btn-block btn-secondary btn-search"
                                        onClick={onClickExecuteFileUploadButton}
                                    >
                                        {t('common.button.register')}
                                    </Button>
                                </div>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs="12">
                                <hr />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs="6">
                                <h5>
                                    {`${t('pages.setting.installFile')}`}
                                    <Help
                                        text={t(
                                            'pages.setting.installFileHelp'
                                        )}
                                    />
                                </h5>
                                <h6>
                                    {`${t('pages.setting.nowVersion')}: `}
                                    <strong>{getNowVersion()}</strong>
                                </h6>
                            </Col>
                            <Col xs="6">
                                {displayInstallVersionDownloadButton()}
                            </Col>
                        </Row>
                        <Row>
                            <Col xs="5">
                                <Form.Group controlId="input" className="mb-3">
                                    <Form.Label />
                                    <Form.Control
                                        type="file"
                                        onChange={onChangeInstallFile}
                                    />
                                </Form.Group>
                            </Col>
                            <Col xs="2" />
                            <Col xs="3">
                                <Form.Group
                                    controlId="input"
                                    className="mb-3 d-inline-block"
                                >
                                    <Form.Label />
                                    <Form.Control
                                        type="input"
                                        placeholder={`${t(
                                            'pages.setting.newVersion'
                                        )} ex) 0.0.1`}
                                        onChange={onChangeNewVersion}
                                    />
                                </Form.Group>
                            </Col>
                            <Col xs="2">
                                <div className="form-group">
                                    <label htmlFor="" />
                                    <Button
                                        type="button"
                                        className="btn btn-block btn-secondary btn-search"
                                        onClick={onClickInstallFileUploadButton}
                                    >
                                        {t('common.button.register')}
                                    </Button>
                                </div>
                            </Col>
                        </Row>
                    </Container>
                }
            />
        </>
    );
};

export default ClientVersionManagement;
