/* eslint-disable no-unused-vars */
import React from 'react';
import {FaSearch, FaPause, FaTrash} from 'react-icons/fa';
import {useTranslation} from 'react-i18next';
import Table from '../../components/table/Table';
import Button from '../../components/button/Button';
import {changeToDate} from '../../lib/moment';
import {renderBatchStatus} from '../../lib/buttonRender';

function BulkTable({
    data,
    movePage,
    selectBulkTestGroup,
    cancelBulktestGroup,
    onClickDelete,
    showStatePopup,
    codeData
}) {
    const [t] = useTranslation();
    const tableName = t('pages.mass.testList');
    const rowKey = 'bulkSnapshotId';
    const onDoubleClick = (id, row) => {
        selectBulkTestGroup(row);
    };

    const onClickProcessStatus = (id) => {
        showStatePopup(id);
    };
    const columns = [
        {
            title: ' ',
            key: 'batchStatus',
            width: '10%',
            render: (id, row) => {
                return renderBatchStatus(row.batchInfo?.batchStatus);
            }
        },
        {
            title: t('pages.mass.testName'),
            key: 'bulkSnapshotName',
            width: '40%',
            align: 'left' // left, center, right (default:center)
        },
        {
            title: ' ',
            key: 'basicData',
            width: '15%'
        },
        {
            title: ' ',
            key: 'singleOrDual',
            width: '10%',
            render: (id, row) => {
                return row.bulkOptions?.singleOrDual;
            }
        },
        {
            title: '',
            key: 'bulkCount',
            render: (id, row) => {
                return `${row.bulkCount}`;
            }
        },
        {
            title: '',
            key: 'startDateTime',
            width: '10%',
            render: (id, row) => {
                return changeToDate(row.batchInfo?.startedDateTime);
            }
        },
        {
            title: '',
            key: 'button',
            align: 'right',
            width: '10%',
            render: (id, row) => {
                return (
                    <>
                        <Button
                            disabled={row.testProcessStatus !== 'READY'}
                            theme="outline-primary"
                            onClick={() => cancelBulktestGroup(id)}
                        >
                            <FaPause />
                        </Button>
                        <Button
                            theme="outline-success"
                            onClick={() => selectBulkTestGroup(row)}
                        >
                            <FaSearch />
                        </Button>
                        <Button
                            disabled={row.testProcessStatus === 'PENDING'}
                            theme="outline-danger"
                            onClick={() => onClickDelete(id)}
                        >
                            <FaTrash />
                        </Button>
                    </>
                );
            }
        }
    ];

    return (
        <>
            <Table
                tableName={tableName}
                onDoubleClick={onDoubleClick}
                movePage={movePage}
                rowKey={rowKey}
                columns={columns}
                data={data}
            />
        </>
    );
}

export default BulkTable;
