import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import HistoryTable from '@pages/history/HistoryTable';
import Spinner from '../../components/spinner/Spinner';
import {
    searchHistory,
    setSearchForm,
    searchHistoryById,
    clearSearchHistoryById
} from '../../store/history';
import {usedMultiTestModal} from '../../store/common';
/*
 *   Container
 */
function HistoryTableContainer() {
    const dispatch = useDispatch();

    //  
    const {data: searchData, loading: searchLoading} = useSelector(
        (state) => state.history.search
    );

    //    
    const {
        data: searchByIdData,
        loading: searchByIdLoading,
        error: searchByIdError
    } = useSelector((state) => state.history.searchById);

    const {searchForm} = useSelector((state) => state.history);

    const movePage = (page) => {
        const searchFormT = {...searchForm};
        searchFormT.page = page;
        dispatch(setSearchForm(searchFormT));
        dispatch(searchHistory(searchFormT));
    };

    const showUndoFormModal = (data) => {
        dispatch(searchHistoryById(data.historyId));
    };

    // search fail
    useEffect(() => {
        if (!searchByIdLoading && searchByIdError) {
            dispatch(clearSearchHistoryById());
        } else if (!searchByIdLoading && searchByIdData) {
            const {data} = searchByIdData;
            dispatch(usedMultiTestModal(true, data, null));
            dispatch(clearSearchHistoryById());
        }
    }, [searchByIdData, searchByIdError]);

    return (
        <>
            <HistoryTable
                showUndoFormModal={showUndoFormModal}
                movePage={movePage}
                data={searchData}
            />
            <Spinner isLoading={searchLoading || searchByIdLoading} />
        </>
    );
    ///
}

export default HistoryTableContainer;
