import React, {useEffect} from 'react';
import {useHistory, useLocation} from 'react-router-dom';
import {useSelector, useDispatch} from 'react-redux';
import HistorySearch from '@pages/history/HistorySearch';
import {toast} from 'react-toastify';
import {
    searchHistory,
    clearSearchHistory,
    getInitSearchForm,
    setSearchForm
} from '../../store/history';
import {usedBulktestModal} from '../../store/bulk';
import Spinner from '../../components/spinner/Spinner';
import {getErrorMsg} from '../../lib/commonUiUtils';

/*
 *   Container
 */
function HistorySearchContainer() {
    const dispatch = useDispatch();
    const history = useHistory();

    const {loading: searchLoading, error: searchError} = useSelector(
        (state) => state.history.search
    );

    const location = useLocation();
    const {data: serverData} = useSelector(
        (state) => state.common.searchServer
    );
    // const {data: codeData} = useSelector((state) => state.common.searchCode);

    const {searchForm} = useSelector((state) => state.history);
    const saveSearchForm = (_searchForm) => {
        dispatch(setSearchForm(_searchForm));
    };

    const onSearch = (_searchForm = searchForm) => {
        dispatch(searchHistory(_searchForm));
    };

    //  
    const {data: searchData} = useSelector((state) => state.history.search);

    /*
     *    
     */
    const showExecuteModal = () => {
        dispatch(
            usedBulktestModal(true, {
                basicData: 'ATWORKS',
                predictCount: searchData?.data?.totalElements,
                historiesFindCondition: searchForm
            })
        );
    };

    // search fail
    useEffect(() => {
        if (!searchLoading && searchError) {
            toast.error(getErrorMsg(searchError, 'search'));
            dispatch(clearSearchHistory());
        }
    }, [searchError]);

    useEffect(() => {
        if (history.action === 'PUSH') {
            const searchFormT = getInitSearchForm();

            // History page  Page   
            // 1. layout page -> serviceId
            // 2. stat page -> serviceId, regCode, serverId, startTime, endTime
            // 3. log page -> logSnapShot, startTime, endTime

            // Gubun prePage    .

            if (location.state != null) {
                //             serviceId: '',
                // fromDateTime: sevenDay.toISOString().slice(0, -1).slice(0, 16),
                // toDateTime: today.toISOString().slice(0, -1).slice(0, 16)
                searchFormT.serviceId = location.state.serviceId;
                // if (location.state.regCode)
                //     searchFormT.regCode = location.state.regCode;
                if (location.state.serverId)
                    searchFormT.serverId = location.state.serverId;
                if (location.state.fromDate)
                    searchFormT.fromDateTime = `${location.state.fromDate}T00:00:00`;
                if (location.state.toDate)
                    searchFormT.toDateTime = `${location.state.toDate}T23:59:59`;
            }
            saveSearchForm(searchFormT);
            onSearch(searchFormT);
        }
        if (history.action === 'POP') {
            onSearch();
        }
    }, []);

    return (
        <>
            <HistorySearch
                onSearch={onSearch}
                searchForm={searchForm}
                setSearchForm={saveSearchForm}
                serverData={serverData}
                showExecuteModal={showExecuteModal}
            />
            <Spinner isLoading={searchLoading} />
        </>
    );
}

export default HistorySearchContainer;
