/* eslint-disable no-unused-vars */
import React from 'react';
import Modal from 'react-bootstrap/Modal';
import {useFormik} from 'formik';
import {useTranslation} from 'react-i18next';
import * as Yup from 'yup';
import {Form, Row, Col, Accordion, FloatingLabel} from 'react-bootstrap';
import Button from '../../../components/button/Button';
import {
    printFormError,
    getInputValidMsg,
    getInputMinMsg,
    renderModalHeader
} from '../../../lib/commonUiUtils';
import {renderOptionsByCode} from '../../../lib/codeUtils';

const MemberUpdate = ({
    memberData,
    updateLoading,
    processUpdate,
    modalShow,
    handleClose
}) => {
    const [t] = useTranslation();

    const formik = useFormik({
        enableReinitialize: true,
        initialValues: memberData,
        validationSchema: Yup.object({
            userId: Yup.string().required(
                getInputValidMsg(t('common.text.id'))
            ),
            password: Yup.string().min(5, getInputMinMsg(5)),
            name: Yup.string().required(
                getInputValidMsg(t('pages.member.userName'))
            )
        }),
        onSubmit: (values) => {
            processUpdate(values);
        }
    });

    return (
        <>
            <Modal
                show={modalShow}
                onHide={handleClose}
                onExited={() => formik.resetForm()}
                dialogClassName="modal-30w"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Form onSubmit={formik.handleSubmit}>
                    {renderModalHeader(
                        memberData.id !== -1
                            ? t('pages.member.updateUser')
                            : t('pages.member.registerUser')
                    )}
                    <Modal.Body>
                        <Row>
                            <Col xs="12">
                                <Accordion defaultActiveKey="0">
                                    <Accordion.Item eventKey="0">
                                        <Accordion.Header>
                                             
                                        </Accordion.Header>
                                        <Accordion.Body>
                                            <Row>
                                                <Col xs="6">
                                                    <FloatingLabel
                                                        label="ID ."
                                                        className="mb-2"
                                                    >
                                                        <Form.Control
                                                            type="text"
                                                            disabled={
                                                                memberData.id !==
                                                                -1
                                                            }
                                                            {...formik.getFieldProps(
                                                                'userId'
                                                            )}
                                                            maxLength="30"
                                                        />
                                                        {printFormError(
                                                            formik,
                                                            'userId'
                                                        )}
                                                    </FloatingLabel>
                                                </Col>
                                                <Col xs="6">
                                                    <FloatingLabel
                                                        label=" ."
                                                        className="mb-2"
                                                    >
                                                        <Form.Control
                                                            type="password"
                                                            {...formik.getFieldProps(
                                                                'password'
                                                            )}
                                                            maxLength="30"
                                                        />
                                                        {printFormError(
                                                            formik,
                                                            'password'
                                                        )}
                                                    </FloatingLabel>
                                                </Col>
                                                <Col xs="8">
                                                    <FloatingLabel
                                                        label=" ."
                                                        className="mb-2"
                                                    >
                                                        <Form.Control
                                                            id="name"
                                                            type="text"
                                                            {...formik.getFieldProps(
                                                                'name'
                                                            )}
                                                            maxLength="30"
                                                        />
                                                    </FloatingLabel>
                                                </Col>
                                                <Col xs="4">
                                                    <FloatingLabel
                                                        label=""
                                                        className="mb-1"
                                                    >
                                                        <Form.Select
                                                            {...formik.getFieldProps(
                                                                'role'
                                                            )}
                                                        >
                                                            {renderOptionsByCode(
                                                                'Role'
                                                            )}
                                                        </Form.Select>
                                                    </FloatingLabel>
                                                </Col>
                                            </Row>
                                        </Accordion.Body>
                                    </Accordion.Item>
                                </Accordion>
                            </Col>
                        </Row>
                        <br />
                        <Row>
                            <Col xs="12">
                                <Accordion defaultActiveKey="0">
                                    <Accordion.Item eventKey="0">
                                        <Accordion.Header className="accordion-header3">
                                             
                                        </Accordion.Header>
                                        <Accordion.Body>
                                            <Row>
                                                <Col xs="6">
                                                    <FloatingLabel
                                                        label="  "
                                                        className="mb-2"
                                                    >
                                                        <Form.Control
                                                            type="text"
                                                            {...formik.getFieldProps(
                                                                'contactInfo.mobile'
                                                            )}
                                                            maxLength="30"
                                                        />
                                                    </FloatingLabel>
                                                </Col>
                                                <Col xs="6">
                                                    <FloatingLabel
                                                        label="e-mail "
                                                        className="mb-2"
                                                    >
                                                        <Form.Control
                                                            type="email"
                                                            {...formik.getFieldProps(
                                                                'contactInfo.email'
                                                            )}
                                                            maxLength="30"
                                                        />
                                                    </FloatingLabel>
                                                </Col>
                                            </Row>
                                        </Accordion.Body>
                                    </Accordion.Item>
                                </Accordion>
                            </Col>
                        </Row>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button
                            theme="primary"
                            type="submit"
                            isLoading={updateLoading}
                        >
                            {memberData.id !== -1
                                ? t('common.button.update')
                                : t('common.button.register')}
                        </Button>
                        <Button theme="default" onClick={handleClose}>
                            {t('common.button.close')}
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>
        </>
    );
};

export default MemberUpdate;
