/* eslint-disable import/prefer-default-export */

import axios from 'axios';

/**
 * (TR) Save scheduler (Uitest)
 * saveSchedule (SAVE_SCHEDULE) : 
 * clearSaveSchedule (CLEAR_SAVE_SCHEDULE) : 
 */
export const saveSchedule = async ({
    clubId,
    scheduleCond,
    wasServerId,
    dbServerId
}) => {
    const response = await axios.post('/schedules', {
        clubId,
        scheduleCond,
        wasServerId,
        dbServerId
    });
    return response;
};

export const updateSchedule = async ({scheduleId, scheduleCond}) => {
    const response = await axios.patch(`/schedules/${scheduleId}`, {
        scheduleCond
    });
    return response;
};

/**
 * (TR) Save Scenario scheduler (Uitest)
 * searchSchedule (SEARCH_SCHEDULE) : 
 * clearSearchSchedule (CLEAR_SEARCH_SCHEDULE) : 
 * setSearchScheduleForm (SET_SEARCH_SCHEDULE_FORM) :  
 * searchScheduleFormInit : form 
 */
export const searchSchedule = async ({
    clubName,
    scheduleType,
    page = 1,
    size = 10
}) => {
    const response = await axios.get(
        `/schedules?clubName=${clubName}&scheduleType=${scheduleType}&page=${
            page - 1
        }&size=${size}`
    );
    return response;
};

export const deleteSchedule = async ({id}) => {
    const response = await axios.delete(`/schedules/${id}`, null);
    return response;
};
