/* eslint-disable no-unused-vars */
import React from 'react';
import {Row, Col, Form, Modal} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
import Table from '../../../components/table/Table';
import Button from '../../../components/button/Button';
import DashboardTestCnt from '../../../components/chart/PieChartWithPaddingAngle';
import {displayNumber, renderModalHeader} from '../../../lib/commonUiUtils';
import SimpleBarChart from '../../../components/chart/SimpleBarChart';

/*
 * StatTab2ServerModal :     
 */
const StatTab2ServerModal = ({
    modalShow,
    handleClose,
    statDetailData,
    moveHistory,
    searchFormT2,
    searchT2
}) => {
    const [t] = useTranslation();
    const columns = [
        {
            title: t('pages.stat.allCnt'),
            key: 'total',
            render: (id, row, column) => {
                return `${row.totalCount}${t('common.text.cases')} `;
            }
        },
        {
            title: t('pages.stat.successCnt'),
            key: 'success',
            render: (id, row, column) => {
                return `${row.successCount}${t('common.text.cases')} `;
            }
        },
        {
            title: t('pages.stat.failCnt'),
            key: 'fail',
            render: (id, row, column) => {
                return `${row.failCount}${t('common.text.cases')} `;
            }
        },
        {
            title: t('pages.stat.failRate'),
            key: 'failRate',
            render: (id, row, column) => {
                return `${row.failRate.toFixed(1)}% `;
            }
        },
        {
            title: t('pages.stat.processMillisecond'),
            key: 'processMillisecond2',
            render: (id, row, column) => {
                return `${row.totalTimeAvgMs.toFixed(5)} ${t(
                    'common.text.second'
                )}`;
            }
        }
    ];

    const renderTableData = () => {
        if (!statDetailData) {
            return {data: {content: []}};
        }

        return {data: {content: [statDetailData]}};
    };

    const renderModalDesc = () => {
        if (!searchFormT2) return '';
        if (!statDetailData) return '';
        return `(${searchFormT2.fromDateOld} ~ ${searchFormT2.toDateOld}) 
        ${statDetailData.serverName}   `;
    };

    /*
     *    chart 
     */
    const renderThisData = () => {
        if (!statDetailData) {
            return [];
        }

        return [
            {name: '', value: statDetailData.successCount},
            {name: '', value: statDetailData.failCount}
        ];
    };

    const renderSimpleBarChartData = () => {
        if (!statDetailData) {
            return [];
        }

        return [
            {
                name: statDetailData?.serverName,
                Total: statDetailData?.totalTimeAvgMs.toFixed(5),
                Success: (
                    statDetailData?.successTimeSumMs /
                    statDetailData?.successCount
                ).toFixed(5),
                Fail: (
                    statDetailData?.failTimeSumMs / statDetailData?.failCount
                ).toFixed(5)
            }
        ];
    };

    /*
     *   chart 
     */
    const renderAllData = () => {
        if (!statDetailData) {
            return [];
        }
        if (!searchT2.data) {
            return [];
        }

        let successCnt = 0;
        let failCnt = 0;
        let noResCnt = 0;

        searchT2.data.data.content.forEach((obj) => {
            successCnt += obj.successCnt;
            failCnt += obj.failCnt;
            noResCnt += obj.noResCnt;
        });

        return [
            {name: '', value: successCnt},
            {name: '', value: failCnt},
            {name: '', value: noResCnt}
        ];
    };

    return (
        <>
            <Modal
                show={modalShow}
                onHide={handleClose}
                size="xl"
                style={{maxHeight: '1000px'}}
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Form>
                    {renderModalHeader(t('pages.stat.statDetailModalTitle'))}
                    <Modal.Body>
                        <Row>
                            <Col xs="12">
                                <Form.Label>{renderModalDesc()}</Form.Label>
                            </Col>
                            <br />
                            <br />
                            <Col xs="12">
                                <Table
                                    tableName=""
                                    rowKey="serverId"
                                    isCard="N"
                                    columns={columns}
                                    disablePageView
                                    data={renderTableData()}
                                />
                            </Col>
                        </Row>
                        <Row>
                            <Col xs="6">
                                <Form.Label className="label-center">
                                    /
                                </Form.Label>
                                <DashboardTestCnt data={renderThisData()} />
                            </Col>
                            <Col xs="6">
                                <Form.Label className="label-center">
                                    
                                </Form.Label>
                                <SimpleBarChart
                                    data={renderSimpleBarChartData()}
                                />
                            </Col>
                        </Row>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button theme="info" onClick={moveHistory}>
                            {t('common.button.showHistory')}
                        </Button>
                        <Button theme="default" onClick={handleClose}>
                            {t('common.button.close')}
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>
        </>
    );
};
export default StatTab2ServerModal;
