/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import ClubDetailScenario from '@pages/club/ClubDetailScenario';
import {useDispatch, useSelector} from 'react-redux';
import {toast} from 'react-toastify';
import {useHistory, useLocation} from 'react-router-dom';
import {useTranslation} from 'react-i18next';
import Spinner from '../../components/spinner/Spinner';
import {getErrorMsg} from '../../lib/commonUiUtils';

import {
    searchClubScenarios,
    usedClubAddScenarioModal,
    setSearchClubScenariosForm,
    removeScenario,
    clearRemoveScenario
} from '../../store/club';

/*
 *     Container
 * @Since 2022-03-30
 */
function ClubDetailScenarioContainer() {
    const dispatch = useDispatch();
    const history = useHistory();
    const location = useLocation();
    const [t] = useTranslation();

    const {searchClubScenariosForm} = useSelector((state) => state.club);

    const {
        data: searchScenarioData,
        loading: searchScenarioLoading,
        error: searchScenarioError
    } = useSelector((state) => state.club.searchClubScenariosRes);

    const {
        loading: removeScenarioLoading,
        data: removeScenarioResponse,
        error: removeScenarioError
    } = useSelector((state) => state.club.removeScenarioRes);

    const onChangerFormData = (e) => {
        const searchFormT = {...searchClubScenariosForm};
        searchFormT[e.target.id] = e.target.value;

        dispatch(setSearchClubScenariosForm(searchFormT));
    };

    /**
     * Club Search detail callback ()
     */
    const {data: searchClubDetailData} = useSelector(
        (state) => state.club.searchClubDetailRes
    );

    /**
     * Club   Scenario 
     * @param {*} clubId
     */
    const onSearchScenarios = (searchForm = searchClubScenariosForm) => {
        dispatch(searchClubScenarios(searchForm));
    };

    const movePage = (page) => {
        const searchForm = {...searchClubScenariosForm};
        searchForm.page = page;
        dispatch(setSearchClubScenariosForm(searchForm));
        dispatch(searchClubScenarios(searchForm));
    };

    const onClickSearch = (e) => {
        const searchForm = {...searchClubScenariosForm};
        searchForm.page = 0;
        dispatch(setSearchClubScenariosForm(searchForm));
        dispatch(searchClubScenarios(searchForm));
    };

    /**
     * Club <> Scenario     .
     * @param {*} e
     */
    const onClickAdd = (e) => {
        const data = {
            parent: 'club',
            clubId: location.state.clubId
        };
        dispatch(usedClubAddScenarioModal(true, data));
    };

    const onClickMoveScenario = (row) => {
        history.push({
            pathname: '/uitest',
            state: {
                key: row.scenarioId
            }
        });
    };

    /**
     * Scenario   .
     * @param {*} row
     */
    const onClickDeleteScenario = (row) => {
        // eslint-disable-next-line no-alert
        if (window.confirm(t('common.alert.checkDelete'))) {
            dispatch(removeScenario(row.clubScenarioId));
        }
    };

    // INIT EVENT
    useEffect(() => {
        // Club   
        // onSearchDetail(location.state.clubId);
    }, []);

    useEffect(() => {
        if (!removeScenarioLoading && removeScenarioResponse) {
            toast.success(t('common.alert.updateSuccess'));
            onSearchScenarios();
        }
        if (!removeScenarioLoading && removeScenarioError) {
            toast.error(getErrorMsg(removeScenarioError, 'save'));
        }
        dispatch(clearRemoveScenario());
    }, [removeScenarioResponse, removeScenarioError]);

    // searchClubScenarios
    return (
        <>
            <ClubDetailScenario
                searchScenarioData={searchScenarioData}
                onClickAdd={onClickAdd}
                searchClubScenariosForm={searchClubScenariosForm}
                onChangerFormData={onChangerFormData}
                onClickSearch={onClickSearch}
                movePage={movePage}
                onClickMoveScenario={onClickMoveScenario}
                onClickDeleteScenario={onClickDeleteScenario}
                searchClubDetailData={searchClubDetailData}
            />
            <Spinner
                isLoading={searchScenarioLoading || removeScenarioLoading}
            />
        </>
    );
}

export default ClubDetailScenarioContainer;
