/* eslint-disable no-unused-vars */
import React from 'react';
import Modal from 'react-bootstrap/Modal';
import {Form, Row, Col, InputGroup} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
import {FaRedoAlt} from 'react-icons/fa';
import Button from '../../../components/button/Button';
import Table from '../../../components/table/Table';
import {displayNumber, renderModalHeader} from '../../../lib/commonUiUtils';

const BulkState = ({
    modalShow,
    handleClose,
    searchStateData,
    onClickReplay,
    onClickReroad,
    onClickReCompare
}) => {
    const [t] = useTranslation();

    const gettestProcessStatus = (row) => {
        if (row.testProcessStatus === 'FINISHED') {
            return t('pages.mass.finished');
        }
        if (row.testProcessStatus === 'PENDING') {
            return t('pages.mass.pedding');
        }
        if (row.testProcessStatus === 'CANCELED') {
            return t('pages.mass.canceled');
        }
        return t('pages.mass.ready');
    };
    const columns = [
        {
            title: '',
            key: 'testProcessStatus',
            render: (id, row, column) => {
                return gettestProcessStatus(row);
            }
        },
        {
            title: '',
            key: 'cnt',
            render: (id, row, column) => {
                return `${displayNumber(row.cnt)} ${t('common.text.cases')}`;
            }
        }
    ];

    const renderTotalCount = () => {
        if (!searchStateData) {
            return '';
        }

        return `${displayNumber(searchStateData.data.totalCount)} ${t(
            'common.text.cases'
        )}`;
    };
    const renderSearchData = () => {
        if (!searchStateData) {
            return {data: {content: []}};
        }
        const temp = {data: {content: searchStateData.data.bulkTestStateList}};
        return temp;
    };

    const renderTestCancelReasonType = () => {
        if (!searchStateData) {
            return '';
        }

        if (searchStateData.data.testProcessStatus !== 'CANCELED') {
            return '';
        }
        return (
            <Row style={{paddingBottom: '10px'}}>
                <Col xs="3">
                    <Form.Label></Form.Label>
                </Col>
                <Col xs="9">
                    <Form.Control
                        type="text"
                        placeholder=""
                        disabled
                        value={
                            searchStateData &&
                            searchStateData.data.testCancelReasonType
                        }
                    />
                </Col>
            </Row>
        );
    };

    /*
     * render  
     *        Finish  
     */
    const renderRePlayButton = () => {
        if (!searchStateData) {
            return '';
        }

        // "bulkTestType":"SINGLE_SYSTEM"
        const {testProcessStatus, processRate, bulkTestType} =
            searchStateData.data;
        if (
            testProcessStatus === 'CANCELED' ||
            (testProcessStatus === 'FINISHED' && processRate !== '100%')
        ) {
            return (
                <Button theme="info" onClick={() => onClickReplay()}>
                    
                </Button>
            );
        }
        if (
            testProcessStatus === 'FINISHED' &&
            processRate === '100%' &&
            bulkTestType === 'COMPARE_TWO_SYSTEM'
        ) {
            return (
                <Button theme="info" onClick={() => onClickReCompare()}>
                    
                </Button>
            );
        }
        return '';
    };
    return (
        <>
            <Modal
                show={modalShow}
                onHide={handleClose}
                size="lg"
                // onExited={() => formik.resetForm()}
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Form>
                    {renderModalHeader('  ')}
                    <Modal.Body>
                        <Row>
                            <Col xs="6">
                                <Form.Group>
                                    <Form.Label> </Form.Label>
                                    <Form.Control
                                        type="text"
                                        placeholder=""
                                        disabled
                                        value={
                                            searchStateData &&
                                            searchStateData.data.testName
                                        }
                                    />
                                </Form.Group>
                            </Col>
                            <Col xs="3">
                                <Form.Group>
                                    <Form.Label> </Form.Label>
                                    <Form.Control
                                        type="text"
                                        placeholder=""
                                        disabled
                                        value={
                                            searchStateData &&
                                            searchStateData.data.startDateTime
                                        }
                                    />
                                </Form.Group>
                            </Col>
                            <Col xs="3">
                                <Form.Group>
                                    <Form.Label> </Form.Label>
                                    <Form.Control
                                        type="text"
                                        placeholder=""
                                        disabled
                                        value={
                                            searchStateData &&
                                            searchStateData.data.endDateTime
                                        }
                                    />
                                </Form.Group>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs="6">
                                <Row style={{paddingBottom: '10px'}}>
                                    <Col xs="3">
                                        <Form.Label></Form.Label>
                                    </Col>
                                    <Col xs="9">
                                        <Form.Control
                                            type="text"
                                            placeholder=""
                                            disabled
                                            value={renderTotalCount()}
                                        />
                                    </Col>
                                </Row>
                                <Row style={{paddingBottom: '10px'}}>
                                    <Col xs="3">
                                        <Form.Label></Form.Label>
                                    </Col>
                                    <Col xs="9">
                                        <InputGroup className="mb-2">
                                            <Form.Control
                                                type="text"
                                                placeholder=""
                                                disabled
                                                value={
                                                    searchStateData &&
                                                    searchStateData.data
                                                        .processRate
                                                }
                                            />
                                            <Button
                                                theme="link"
                                                onClick={() => onClickReroad()}
                                            >
                                                <FaRedoAlt />
                                            </Button>
                                        </InputGroup>
                                    </Col>
                                </Row>
                                {renderTestCancelReasonType()}
                            </Col>
                            <Col xs="6">
                                <Table
                                    rowKey="testProcessStatus"
                                    columns={columns}
                                    data={renderSearchData()}
                                    isCard="N"
                                    disablePageView
                                />
                            </Col>
                        </Row>
                    </Modal.Body>
                    <Modal.Footer>
                        {renderRePlayButton()}
                        <Button theme="default" onClick={handleClose}>
                            {t('common.button.close')}
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>
        </>
    );
};

export default BulkState;
