// import {takeLatest, takeEvery} from 'redux-saga/effects';
import {takeLatest} from 'redux-saga/effects';
import * as dashboardAPI from '../api/dashboard';
import {
    reducerUtils,
    handleAsyncActions,
    createPromiseSaga
} from '../lib/asyncUtils';

const PREFIX = 'DASHBOARD';

/**
 * dashboard Same-day test results (searchDayTestResult, TR)
 * searchDayTestResult (SEARCH_DAY_TESTRESULT) : 
 */
const SEARCH_DAY_TESTRESULT = `${PREFIX}/SEARCH_DAY_TESTRESULT`;
const SEARCH_DAY_TESTRESULT_SUCCESS = `${PREFIX}/SEARCH_DAY_TESTRESULT_SUCCESS`;
const SEARCH_DAY_TESTRESULT_ERROR = `${PREFIX}/SEARCH_DAY_TESTRESULT_ERROR`;

export const searchDayTestResult = (searchDate) => ({
    type: SEARCH_DAY_TESTRESULT,
    payload: {
        searchDate
    }
});

/**
 * dashboard Number of tests per server (searchServerTestCnt, TR)
 * searchServerTestCnt (SEARCH_SERVER_TESTCNT) : 
 */
const SEARCH_SERVER_TESTCNT = `${PREFIX}/SEARCH_SERVER_TESTCNT`;
const SEARCH_SERVER_TESTCNT_SUCCESS = `${PREFIX}/SEARCH_SERVER_TESTCNT_SUCCESS`;
const SEARCH_SERVER_TESTCNT_ERROR = `${PREFIX}/SEARCH_SERVER_TESTCNT_ERROR`;

export const searchServerTestCnt = (searchDate) => ({
    type: SEARCH_SERVER_TESTCNT,
    payload: {
        searchDate
    }
});

/**
 * Server test Fail rate (searchFailRate, TR)
 * searchServerTestCnt (SEARCH_SERVER_TESTCNT) : 
 */
const SEARCH_FAIL_RATE = `${PREFIX}/SEARCH_FAIL_RATE`;
const SEARCH_FAIL_RATE_SUCCESS = `${PREFIX}/SEARCH_FAIL_RATE_SUCCESS`;
const SEARCH_FAIL_RATE_ERROR = `${PREFIX}/SEARCH_FAIL_RATE_ERROR`;

export const searchFailRate = (searchDate, searchServer) => ({
    type: SEARCH_FAIL_RATE,
    payload: {
        searchDate,
        searchServer
    }
});

/**
 * Redux-saga 
 */
export function* dashboardSaga() {
    yield takeLatest(
        SEARCH_DAY_TESTRESULT,
        createPromiseSaga(
            SEARCH_DAY_TESTRESULT,
            dashboardAPI.searchDayTestResult
        )
    );
    yield takeLatest(
        SEARCH_SERVER_TESTCNT,
        createPromiseSaga(
            SEARCH_SERVER_TESTCNT,
            dashboardAPI.searchServerTestCnt
        )
    );
    yield takeLatest(
        SEARCH_FAIL_RATE,
        createPromiseSaga(SEARCH_FAIL_RATE, dashboardAPI.searchFailRate)
    );
}

/**
 * State 
 */
const initialState = {
    searchDayTestResult: reducerUtils.initial(),
    searchServerTestCnt: reducerUtils.initial(),
    searchFailRate: reducerUtils.initial()
};

export default function bulktest(state = initialState, action) {
    switch (action.type) {
        // dashboard Same-day test results
        case SEARCH_DAY_TESTRESULT:
        case SEARCH_DAY_TESTRESULT_SUCCESS:
        case SEARCH_DAY_TESTRESULT_ERROR:
            return handleAsyncActions(
                SEARCH_DAY_TESTRESULT,
                'searchDayTestResult',
                true
            )(state, action);

        // dashboard Same-day test results
        case SEARCH_SERVER_TESTCNT:
        case SEARCH_SERVER_TESTCNT_SUCCESS:
        case SEARCH_SERVER_TESTCNT_ERROR:
            return handleAsyncActions(
                SEARCH_SERVER_TESTCNT,
                'searchServerTestCnt',
                true
            )(state, action);

        // dashboard Same-day test results
        case SEARCH_FAIL_RATE:
        case SEARCH_FAIL_RATE_SUCCESS:
        case SEARCH_FAIL_RATE_ERROR:
            return handleAsyncActions(
                SEARCH_FAIL_RATE,
                'searchFailRate',
                true
            )(state, action);

        default:
            return state;
    }
}
