/* eslint-disable jsx-a11y/label-has-associated-control */
import React from 'react';
import {Container, Row, Col, Form, FloatingLabel} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
import Button from '../../components/button/Button';
import Card from '../../components/card/Card';

/*
 * BulkSearch.js
 *   ->  search
 */
function BulkSearch({
    // showExecuteModal,
    searchForm,
    onChangeSearchForm,
    onSearch
}) {
    const [t] = useTranslation();
    const onChangeForm = (e) => {
        e.preventDefault();
        const newSearchForm = {
            ...searchForm
        };
        newSearchForm[e.target.id] = e.target.value;
        onChangeSearchForm(newSearchForm);
    };

    const onClickSearch = (e) => {
        e.preventDefault();
        const searchFormT = {...searchForm};
        searchFormT.page = 1;
        onChangeSearchForm(searchFormT);
        onSearch(searchFormT);
    };

    const onCheckEnter = (e) => {
        if (e.key === 'Enter') {
            onClickSearch(e);
        }
    };

    /*
     *  
     */
    return (
        <Card
            title={t('common.text.searchCardTitleName')}
            body={
                <>
                    <Container fluid>
                        <Row onKeyPress={onCheckEnter}>
                            <Col xs="8">
                                <Row>
                                    <Col xs="6">
                                        <FloatingLabel
                                            label={t('pages.mass.testName')}
                                            className="mb-2"
                                        >
                                            <Form.Control
                                                type="text"
                                                maxLength="50"
                                                id="bulkSnapshotName"
                                                onChange={onChangeForm}
                                                value={
                                                    searchForm?.bulkSnapshotName
                                                }
                                            />
                                        </FloatingLabel>
                                    </Col>

                                    <Col xs="3">
                                        <FloatingLabel
                                            label="From"
                                            className="mb-2"
                                        >
                                            <Form.Control
                                                type="date"
                                                id="fromDate"
                                                onChange={onChangeForm}
                                                value={searchForm?.fromDate}
                                            />
                                        </FloatingLabel>
                                    </Col>
                                    <Col xs="3">
                                        <FloatingLabel
                                            label="To"
                                            className="mb-2"
                                        >
                                            <Form.Control
                                                type="date"
                                                id="toDate"
                                                onChange={onChangeForm}
                                                value={searchForm?.toDate}
                                            />
                                        </FloatingLabel>
                                    </Col>
                                </Row>
                            </Col>
                            <Col xs="3" />
                            <Col xs="1">
                                <div className="form-group">
                                    <label htmlFor="myinput" />
                                    <Button
                                        type="button"
                                        className="btn btn-block btn-secondary btn-search"
                                        onClick={onClickSearch}
                                    >
                                        {t('common.button.search')}
                                    </Button>
                                </div>
                            </Col>
                            {/* <Col xs="1">
                                <Form.Group>
                                    <Form.Label />
                                    <Button
                                        type="button"
                                        className="btn btn-block btn-success btn-search"
                                        onClick={showExecuteModal}
                                    >
                                        + 
                                    </Button>
                                </Form.Group>
                            </Col> */}
                        </Row>
                    </Container>
                </>
            }
        />
    );
}

export default BulkSearch;
