/* eslint-disable no-unused-vars */
import React from 'react';
import {
    Form,
    Row,
    Col,
    InputGroup,
    Accordion,
    FloatingLabel
} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
import {FaSearch} from 'react-icons/fa';
import {jsonStrFormatPretty, validJsonString} from '../../lib/jsonUtils';
import {xmlStrFormatPretty, validXmlString} from '../../lib/xmlUtils';
import Button from '../../components/button/Button';
import {renderHttpMethod, renderStatusCode2} from '../../lib/commonUiUtils';

const HttpTestView = ({
    singleTestData,
    requestDataFormat,
    responseDataFormat,
    serverData,
    setSingleTestData,
    loading,
    onReSend,
    onGetSample,
    onChangeServerInfo,
    isdualMode,
    serverInfo,
    onClickServerInfo
}) => {
    const [t] = useTranslation();

    const prettyFormat = (type, data) => {
        if (singleTestData?.urlInfo?.httpMethod === 'GET') {
            return jsonStrFormatPretty(data);
        }
        if (type === 'Json') {
            return jsonStrFormatPretty(data);
        }
        if (type === 'XML') {
            return xmlStrFormatPretty(data);
        }

        return jsonStrFormatPretty(data);
    };

    const validFormat = (type, data) => {
        if (singleTestData?.urlInfo?.httpMethod === 'GET') {
            return validJsonString(data);
        }
        if (type === 'Json') {
            return validJsonString(data);
        }
        if (type === 'XML') {
            return validXmlString(data);
        }

        return false;
    };

    /** evnet handler * */
    /**
     * serverA element change event
     * @param {*} e
     */
    const onChangeEvent = (e) => {
        const data = {...singleTestData};
        if (e.target.id === 'serverId') {
            // data[e.target.id] = e.target.value;
            // setSingleTestData(data);
            onChangeServerInfo(data.serverId, e.target.value);
        } else {
            data[e.target.id] = e.target.value;
            setSingleTestData(data);
        }
    };

    /** rendering * */
    /**
     * was server option rendering
     * @returns was server select option
     */
    const renderServerSelect = () => {
        return serverData?.data
            ?.filter(
                (obj) =>
                    obj.serverType === 'HTTP' || obj.serverType === 'TCP_IP'
            )
            .map((obj) => (
                <option key={obj.serverId} value={obj.serverId}>
                    {obj.serverName}
                </option>
            ));
    };

    /**
     * was server url render
     * @param {*} serverId
     * @returns was server url
     */
    const getContextRootValue = (serverId) => {
        return serverData?.data
            ?.filter((obj) => obj.serverId === Number(serverId))
            .map((obj) => obj.url);
    };

    const renderUrlPath = () => {
        return (
            <>
                <InputGroup className="mb-2">
                    {renderHttpMethod(singleTestData?.urlInfo?.httpMethod)}
                    <FloatingLabel label="URL Path">
                        <Form.Control
                            readOnly
                            type="text"
                            placeholder=""
                            value={singleTestData?.urlInfo?.urlPath}
                        />
                    </FloatingLabel>
                </InputGroup>
            </>
        );
    };

    const renderResult = () => {
        return renderStatusCode2(singleTestData?.responseInfo);
    };

    // /**
    //  * was server tr type render
    //  * @param {*} serverId
    //  * @returns was server type(xml/json/fixedLength)
    //  */
    // const renderTrType = (serverId) => {
    //     if (!serverData) return '';
    //     return serverData.data.content
    //         .filter((obj) => obj.id === Number(serverId))
    //         .map((obj) => obj.serverInfo);
    // };

    /**
     * was server Login Type
     * @param {*} serverId
     * @returns was server type(xml/json/fixedLength)
     */
    const renderLoginType = (serverId) => {
        if (!serverData) return '';
        return serverData?.data
            ?.filter((obj) => obj.serverId === Number(serverId))
            .map((obj) => obj.loginProcess?.loginMethod);
    };

    const getParsingResultText = (type, valid) => {
        if (valid) {
            // if (httpMethod === 'GET') {
            //     return '';
            // }
            if (type === 'Json') {
                return t('common.text.validJsonFormat');
            }
            if (type === 'Xml') {
                return t('common.text.validXmlFormat');
            }
            return '';
        }
        if (singleTestData?.urlInfo?.httpMethod === 'GET' || type === 'Json') {
            return t('common.text.invalidJsonFormat');
        }
        if (type === 'XML') {
            return t('common.text.invalidXmlFormat');
        }
        return '';
    };

    const convertJsonToQueryString = (json) => {
        if (!json) {
            return '';
        }

        if (!validJsonString(json)) {
            return '';
        }

        const jsonObject = JSON.parse(json);
        return Object.keys(jsonObject)
            .filter((key) => !key.startsWith('@@'))
            .map((key) => `${key}=${jsonObject[key]}`)
            .join('&');
    };

    const renderInputParsingResult = (text) => {
        // const inputText = formik.getFieldProps('input').value;

        const inputText = singleTestData.input;
        const validInputFormat = validFormat(requestDataFormat, inputText);
        const parsingResultText = getParsingResultText(
            requestDataFormat,
            validInputFormat
        );
        return `${text} (${parsingResultText})`;
    };

    const renderInput = (length) => {
        const input = prettyFormat(
            requestDataFormat,
            singleTestData?.input
            // formik.getFieldProps('input').value
        );

        // const titleLabel =
        //     httpMethod === 'GET' ? 'QueryString' : 'Reqeust Body';
        const titleLabel = 'Reqeust Body';
        return (
            <>
                {/* {renderInputParsingResult()} */}
                <FloatingLabel
                    label={renderInputParsingResult(titleLabel)}
                    className="mb-3"
                >
                    <Form.Control
                        disabled={loading}
                        as="textarea"
                        placeholder="Leave a comment here"
                        style={{height: length, fontSize: '20px'}}
                        id="input"
                        onChange={onChangeEvent}
                        value={input}
                    />
                </FloatingLabel>
            </>
        );
    };

    const renderoutput = (length) => {
        const output = prettyFormat(
            responseDataFormat,
            singleTestData.output
            // singleTestData?.headerAndBodyResponse?.body
        );

        return (
            <FloatingLabel
                className="mb-3"
                label={`Response Body ${singleTestData?.responseTime?.receiveDateTime} (${singleTestData?.responseTime?.processTime}s)`}
            >
                <Form.Control
                    as="textarea"
                    readOnly
                    style={{height: length, fontSize: '20px'}}
                    value={output}
                />
            </FloatingLabel>
        );
    };

    const renderBody = () => {
        if (isdualMode) {
            return (
                <>
                    <Row>
                        <Col xs="9">{renderUrlPath()}</Col>
                        <Col xs="3">{renderResult()}</Col>
                    </Row>
                    <Row>
                        <Col xs="12">{renderInput('200px')}</Col>
                        <Col xs="12">{renderoutput('300px')}</Col>
                    </Row>
                    <Row>
                        <Col xs="12">
                            <Button
                                theme="outline-success"
                                onClick={onGetSample}
                                style={{float: 'left', width: '200px'}}
                                isLoading={loading}
                            >
                                Sample Data
                            </Button>
                            <Button
                                theme="outline-primary"
                                onClick={onReSend}
                                style={{float: 'right', width: '100px'}}
                                isLoading={loading}
                            >
                                {t('common.button.send')}
                            </Button>
                        </Col>
                    </Row>
                </>
            );
        }
        return (
            <>
                <Row>
                    <Col xs="9">{renderUrlPath()}</Col>
                    <Col xs="3">{renderResult()}</Col>
                </Row>
                <Row>
                    <Col xs="6">{renderInput('500px')}</Col>
                    <Col xs="6">{renderoutput('500px')}</Col>
                </Row>
                <Row>
                    <Col xs="12">
                        <Button
                            theme="outline-success"
                            onClick={onGetSample}
                            style={{float: 'left', width: '200px'}}
                            isLoading={loading}
                        >
                            Sample Data
                        </Button>
                        <Button
                            theme="outline-primary"
                            onClick={onReSend}
                            style={{float: 'right', width: '100px'}}
                            isLoading={loading}
                        >
                            {t('common.button.send')}
                        </Button>{' '}
                    </Col>
                </Row>
            </>
        );
    };

    /* 20220712    Token  . */
    const renderTokenInfo = () => {
        const loginType = renderLoginType(
            singleTestData && singleTestData.serverId
        );

        if (loginType.length === 1 && loginType[0] === 'DIRECT_INPUT') {
            return (
                <>
                    <FloatingLabel label="Token or Session Infomation">
                        <Form.Control
                            type="text"
                            placeholder=""
                            id="tokenInfo"
                            onChange={onChangeEvent}
                            value={singleTestData && singleTestData.tokenInfo}
                        />
                    </FloatingLabel>
                </>
            );
        }
        return (
            <>
                <Col />
                {/* {getContextRootValue(singleTestData.serverId)} */}
            </>
        );
    };

    const renderServerInfo = () => {
        return (
            <>
                <Row>
                    <Col xs="4">
                        <FloatingLabel label=" " className="mb-3">
                            <Form.Select
                                id="serverId"
                                disabled={loading}
                                onChange={onChangeEvent}
                                value={singleTestData?.serverId}
                            >
                                {renderServerSelect()}
                            </Form.Select>
                        </FloatingLabel>
                    </Col>
                    <Col xs="8">
                        <InputGroup>
                            <FloatingLabel label="Server URL">
                                <Form.Control
                                    type="text"
                                    disabled
                                    value={getContextRootValue(
                                        singleTestData.serverId
                                    )}
                                />
                            </FloatingLabel>
                            <Button
                                type="button"
                                style={{maxWidth: '50px'}}
                                className="btn btn-block btn-secondary btn-search"
                                onClick={(e) =>
                                    onClickServerInfo(singleTestData.serverId)
                                }
                            >
                                <FaSearch />
                            </Button>
                        </InputGroup>
                    </Col>
                    {/* 20220712    Token  . */}
                    <Col xs="12">{renderTokenInfo()}</Col>
                </Row>
            </>
        );
    };

    return (
        <>
            {/* <Form.Group>
                <Form.Label>
                    <b>{serverInfo}</b>
                </Form.Label>
                
            </Form.Group> */}
            <Accordion defaultActiveKey="0">
                <Accordion.Item eventKey="0">
                    <Accordion.Header>{serverInfo}</Accordion.Header>
                    <Accordion.Body>{renderServerInfo()}</Accordion.Body>
                </Accordion.Item>
            </Accordion>
            <br />
            {/* accordion-header2 */}
            <Accordion defaultActiveKey="0">
                <Accordion.Item eventKey="0">
                    <Accordion.Header className="accordion-header3">
                        API 
                    </Accordion.Header>
                    <Accordion.Body> {renderBody()}</Accordion.Body>
                </Accordion.Item>
            </Accordion>
        </>
    );
};
export default HttpTestView;
