/* eslint-disable import/prefer-default-export */

import axios from 'axios';

/**
 * Random List Paging 
 * searchRandom (SEARCH_RANDOM) : 
 * clearSearchRandom (CLEAR_SEARCH_RANDOM) : 
 * setSearchForm (SET_SEARCH_FORM) :  
 */
export const searchRandom = async ({
    randomQuery,
    randomStyle,
    page = 1,
    size = 10
}) => {
    const response = await axios.get(
        `/randoms?sort=id,desc&randomQuery=${randomQuery}&randomStyle=${randomStyle}&page=${
            page - 1
        }&size=${size}`
    );
    return response;
};

/**
 * 1 Random  
 */
export const findRandom = async ({randomId}) => {
    const response = await axios.get(`/randoms/${randomId}`, null);
    return response;
};

/**
 * random    (   Layout, uitest)
 * searchRandomAll (SEARCH_RANDOM_ALL) : 
 */
export const searchRandomAll = async () => {
    const response = await axios.get(`common/randoms`);
    return response;
};

/**
 * sava random data
 * saveRandom (SAVE_RANDOM) : SHOW
 * saveRandomDataClear (SAVE_RANDOM_DATA_CLEAR) : CLEAR
 */
export const saveRandom = async ({
    randomKey,
    randomDesc,
    randomStyle,
    conditions
}) => {
    const response = await axios.post('/randoms', {
        // key: `$$${key}`,
        randomKey,
        randomDesc,
        randomStyle,
        conditions
    });
    return response;
};

/**
 * update random data
 * updateRandom (SAVE_RANDOM) : SHOW
 * updateRandomDataClear (SAVE_RANDOM_DATA_CLEAR) : CLEAR
 */
export const updateRandom = async ({
    id,
    randomKey,
    randomDesc,
    randomStyle,
    conditions
}) => {
    const response = await axios.patch(`/randoms/${id}`, {
        // key: `$$${key}`,
        randomKey,
        randomDesc,
        randomStyle,
        conditions
    });
    return response;
};

/**
 * random data test
 * saveRandom (SAVE_RANDOM) : SHOW
 * saveRandomDataClear (SAVE_RANDOM_DATA_CLEAR) : CLEAR
 */
export const testRandom = async ({randomId, randomStyle, conditions}) => {
    if (randomId > 0) {
        const response = await axios.post(
            `/randoms/${randomId}/calculate`,
            null
        );
        return response;
    }
    const response = await axios.post('/randoms/calculate', {
        randomStyle,
        conditions
    });
    return response;
};

/**
 * random data DELETE
 * deleteRandom (DELETE_RANDOM) : SHOW
 * deleteRandomDataClear (DELETE_RANDOM_DATA_CLEAR) : CLEAR
 */
export const deleteRandom = async ({id}) => {
    const response = await axios.delete(`/random/${id}`, null);
    return response;
};
