import {takeEvery, takeLatest} from 'redux-saga/effects';
// import * as serverAPI from '../api/server'; // api/auth    
import * as randomAPI from '../api/random'; // api/auth    
import {
    reducerUtils,
    handleAsyncActions,
    createPromiseSaga
} from '../lib/asyncUtils';

const PREFIX = 'RANDOM';

/**
 * Random List Paging 
 * searchRandom (SEARCH_RANDOM) : 
 * clearSearchRandom (CLEAR_SEARCH_RANDOM) : 
 * setSearchForm (SET_SEARCH_FORM) :  
 */
const SEARCH_RANDOM = `${PREFIX}/SEARCH_RANDOM`;
const SEARCH_RANDOM_SUCCESS = `${PREFIX}/SEARCH_RANDOM_SUCCESS`;
const SEARCH_RANDOM_ERROR = `${PREFIX}/SEARCH_RANDOM_ERROR`;
const CLEAR_SEARCH_RANDOM = `${PREFIX}/CLEAR_SEARCH_RANDOM`;
const SET_SEARCH_FORM = `${PREFIX}/SET_SEARCH_FORM`; // search data setting

export const searchRandom = (searchRandomForm) => ({
    type: SEARCH_RANDOM,
    payload: {
        ...searchRandomForm
    }
});

export const clearSearchRandom = () => ({
    type: CLEAR_SEARCH_RANDOM
});

export const setSearchForm = (searchForm) => ({
    type: SET_SEARCH_FORM,
    payload: {
        searchForm
    }
});

//    
export const initSearchRandomForm = () => {
    return {
        randomQuery: '',
        randomStyle: '',
        page: 1,
        size: 10
    };
};

