/* eslint-disable no-unused-vars */
import React from 'react';
import {useTranslation} from 'react-i18next';
import {Row, Col, Form, FloatingLabel} from 'react-bootstrap';
import Button from '../../components/button/Button';
import Card from '../../components/card/Card';
import {renderOptionsByCode} from '../../lib/codeUtils';

const ClubSearch = ({
    onSearch,
    searchClubForm,
    onChangerSearchForm,
    codeData,
    onClickCreate
}) => {
    const [t] = useTranslation();

    const onCheckEnter = (e) => {
        if (e.key === 'Enter') {
            e.preventDefault();
            onSearch();
        }
    };

    return (
        <Card
            title={t('common.text.searchCardTitleName')}
            body={
                <>
                    <Row onKeyPress={onCheckEnter}>
                        <Col xs="2">
                            <FloatingLabel label=" " className="mb-2">
                                <Form.Select
                                    id="clubType"
                                    onChange={onChangerSearchForm}
                                    value={searchClubForm.clubType}
                                >
                                    <option value="">
                                        {t('common.text.optionAll')}
                                    </option>
                                    {renderOptionsByCode(
                                        // codeData,
                                        'ClubType'
                                    )}
                                </Form.Select>
                            </FloatingLabel>
                        </Col>
                        <Col xs="3">
                            <FloatingLabel label=" " className="mb-2">
                                <Form.Control
                                    type="text"
                                    maxLength="30"
                                    id="clubName"
                                    onChange={onChangerSearchForm}
                                    value={searchClubForm.clubName}
                                />
                            </FloatingLabel>
                        </Col>
                        <Col xs="5" />
                        <Col xs="1">
                            <Form.Group>
                                <Form.Label />
                                <Button
                                    type="button"
                                    className="btn btn-block btn-secondary btn-search"
                                    onClick={(e) => onSearch()}
                                >
                                    {t('common.button.search')}
                                </Button>
                            </Form.Group>
                        </Col>
                        <Col xs="1">
                            <Form.Group>
                                <Form.Label />
                                <Button
                                    type="button"
                                    className="btn btn-block btn-success btn-search"
                                    onClick={onClickCreate}
                                >
                                    + 
                                </Button>
                            </Form.Group>
                        </Col>
                    </Row>
                </>
            }
        />
    );
};

export default ClubSearch;
