import React, {useState, useEffect} from 'react';
import {Container, Row, Col, Form} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
import Button from '../../components/button/Button';
import Card from '../../components/card/Card';
import Help from '../../components/help/Help';

const EnvironmentVariableManagement = ({environmentVariableData, save}) => {
    const [t] = useTranslation();

    const [environmentVariableMap, setEnvironmentVariableMap] = useState(
        new Map()
    );

    const upsert = (key, value) => {
        setEnvironmentVariableMap((prev) =>
            new Map(prev).set(Number(key), value)
        );
    };

    useEffect(() => {
        if (!environmentVariableData) {
            return;
        }
        environmentVariableData.data.forEach((obj) => {
            upsert(obj.id, obj.environmentValue);
        });
    }, [environmentVariableData]);

    const onChangerData = (e) => {
        e.preventDefault();
        upsert(e.target.id, e.target.value);
    };

    const onSave = (obj) => {
        const value = environmentVariableMap.get(obj.id);
        const tempData = {...obj};
        tempData.environmentValue = value;
        save(obj.id, tempData);
    };

    const getEnvironmentVariableInputComponent = (obj) => {
        const value = environmentVariableMap.get(obj.id);

        if (obj.environmentType === 'NUMBER') {
            return (
                <Form.Control
                    id={obj.id}
                    type="number"
                    value={value || 0}
                    onChange={onChangerData}
                />
            );
        }

        if (obj.environmentType === 'STRING') {
            return (
                <Form.Control
                    id={obj.id}
                    type="text"
                    value={value || ''}
                    onChange={onChangerData}
                />
            );
        }

        if (obj.environmentType === 'BOOLEAN') {
            return (
                <Form.Control
                    as="select"
                    id={obj.id}
                    onChange={onChangerData}
                    value={value || 'true'}
                >
                    <option value="true">TRUE</option>
                    <option value="false">FALSE</option>
                </Form.Control>
            );
        }

        return <></>;
    };

    const getEnvironmentVariableForm = () => {
        if (!environmentVariableData || !environmentVariableData.data) {
            return <></>;
        }

        return environmentVariableData.data.map((obj) => {
            return (
                <Row key={obj.id} style={{marginBottom: '10px'}}>
                    <Col xs="5">
                        <h6>
                            {obj.environmentDesc}
                            <Help text={obj.environmentDesc} />
                        </h6>
                    </Col>
                    <Col xs="2" />
                    <Col xs="3">
                        {getEnvironmentVariableInputComponent(obj)}
                    </Col>
                    <Col xs="2">
                        <Button
                            type="button"
                            className="btn btn-block btn-secondary btn-search"
                            onClick={() => onSave(obj)}
                        >
                            {t('common.button.save')}
                        </Button>
                    </Col>
                </Row>
            );
        });
    };

    return (
        <>
            <Card
                title={t('pages.setting.environmentVariableManagement')}
                body={
                    <Container fluid>{getEnvironmentVariableForm()}</Container>
                }
            />
        </>
    );
};

export default EnvironmentVariableManagement;
