/*
{
    "key":"1234"
}
*/
export const jsonStrFormatPretty = (data) => {
    if (data === null) {
        return '';
    }
    if (typeof data === 'string') {
        try {
            return JSON.stringify(JSON.parse(data), undefined, 4);
        } catch (e) {
            return data;
        }
    } else {
        try {
            return JSON.stringify(data, undefined, 4);
        } catch (e) {
            return '';
        }
    }
};

/*
{"key":"1234"}
*/
export const jsonStrFormatNotPretty = (data) => {
    if (typeof data === 'string') {
        try {
            return JSON.stringify(JSON.parse(data, undefined, 4));
        } catch (e) {
            return data;
        }
    } else {
        try {
            return JSON.stringify(data);
        } catch (e) {
            return '';
        }
    }
};

export const convertCollListToJson = (cols) => {
    const resultList = {};
    cols?.forEach((col) => {
        const dataType = col.colAttribute?.dataType;
        const physicalName = col.colAttribute?.physicalName;
        const initValue = col.initData?.initValue;

        if (dataType === 'LIST') {
            resultList[physicalName] = [convertCollListToJson(col.cols)];
        } else if (
            dataType === 'OBJECT' ||
            dataType === 'QUERY_STRING' ||
            dataType === 'PATH_VARIABLE'
        ) {
            resultList[physicalName] = convertCollListToJson(col.cols);
        }
        // } else if (dataType === 'root list') {
        //     const jsonArray = [convertCollListToJson(col.cols)];
        //     resultList = jsonArray;
        else if (initValue) {
            if (dataType === 'STRING') {
                resultList[physicalName] = initValue;
            } else if (dataType === 'NUMBER') {
                resultList[physicalName] = Number(initValue);
            } else if (dataType === 'BOOLEAN') {
                // 1, true, Ture, Y  -> true
                if (
                    initValue === '1' ||
                    initValue.toLowerCase() === 'true' ||
                    initValue.toLowerCase() === 'y'
                ) {
                    resultList[physicalName] = true;
                } else {
                    resultList[physicalName] = false;
                }
            }
        } else if (dataType === 'STRING') {
            resultList[physicalName] = '';
        } else if (dataType === 'NUMBER') {
            resultList[physicalName] = null;
        } else if (dataType === 'BOOLEAN') {
            resultList[physicalName] = null;
        } else if (!col.testData) {
            resultList[physicalName] = [];
        }
    });

    return resultList;
};
