import React from 'react';
import {useSelector, useDispatch} from 'react-redux';
import UitestScenarioTab from '@app/pages/uitest/UitestScenarioTab';
import {setDetailScenarioDesc} from '../../store/uitest';

function UitestScenarioTabContainer({onSaveScenario}) {
    const dispatch = useDispatch();
    const {data: scenarioData} = useSelector(
        (state) => state.uitest.scenarioInfo
    );

    const saveDetailScenarioDesc = (scenarioDesc) => {
        dispatch(setDetailScenarioDesc(scenarioDesc));
    };
    return (
        <>
            <UitestScenarioTab
                scenarioData={scenarioData} // detail scenario data
                saveDetailScenarioDesc={saveDetailScenarioDesc}
                onSaveScenario={onSaveScenario}
            />
        </>
    );
}

export default UitestScenarioTabContainer;
