/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import UitestStepMove from '@pages/uitest/popup/UitestStepMove';
import {clearMoveStepInitData, setDetailCaseList} from '../../../store/uitest';
import {setTempKey} from '../../../store/common';

/*
 * step   case /     
 */
function UitestStepMoveContainer() {
    const dispatch = useDispatch();
    // popup show/hide
    const [modalShow, setModalShow] = React.useState(false);

    const handleClose = () => setModalShow(false);
    const handleShow = () => setModalShow(true);
    // step code setting

    const {data: scenarioData} = useSelector(
        (state) => state.uitest.scenarioInfo
    );

    const [seletedStep, setSeletedStep] = React.useState();
    /*
     * Redux  
     */
    const {moveStepInitData} = useSelector((state) => state.uitest);

    useEffect(() => {
        if (moveStepInitData.showModal && !modalShow) {
            setSeletedStep(moveStepInitData.selectedStep);
            handleShow();
            dispatch(clearMoveStepInitData());
        }
    }, [moveStepInitData.showModal]);

    const saveDetailCaseList = (caseList) => {
        dispatch(setDetailCaseList(caseList));
    };

    const {tempkey} = useSelector((state) => state.common);
    const setTempKeyRezen = () => {
        if (tempkey < -9999) {
            dispatch(setTempKey(-1));
        } else {
            dispatch(setTempKey(tempkey - 1));
        }
    };

    return (
        <>
            <UitestStepMove
                modalShow={modalShow}
                handleClose={handleClose}
                seletedStep={seletedStep}
                scenarioData={scenarioData}
                setDetailCaseList={saveDetailCaseList}
                tempkey={tempkey}
                setTempKeyRezen={setTempKeyRezen}
            />
            {/* <Spinner isLoading={saveLoading} /> */}
        </>
    );
}

export default UitestStepMoveContainer;
