/* eslint-disable no-unused-vars */
import React, {useState, useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import LogAnalysisModal from '@pages/log/popup/LogAnalysisModal';
import {toast} from 'react-toastify';
import {useTranslation} from 'react-i18next';
import Spinner from '../../../components/spinner/Spinner';
import {getErrorMsg} from '../../../lib/commonUiUtils';

import {
    executeLogColletor,
    clearExecuteLogColletor,
    clearLogAnalysisModal,
    searchLogSnapshot
} from '../../../store/log';

/*
 *  /  Container
 */
function LogAnalysisModalContainer() {
    const dispatch = useDispatch();
    const [t] = useTranslation();

    //  Modal show/hide
    const [modalShow, setModalShow] = React.useState(false);
    const handleClose = () => {
        setModalShow(false);
        dispatch(clearExecuteLogColletor());
    };
    const handleShow = () => setModalShow(true);

    const {usedLogAnalysisModalInitData} = useSelector((state) => state.log);

    //   
    const {data: codeData} = useSelector((state) => state.common.searchCode);

    //   
    const {data: serverData} = useSelector(
        (state) => state.common.searchServer
    );

    //    
    const {data: memberInfoData} = useSelector(
        (state) => state.auth.memberInfo
    );

    const {searchLogSnapshotForm} = useSelector((state) => state.log);

    /**
     *  
     */
    const onExecuteLogColletor = (values) => {
        dispatch(executeLogColletor(values));
    };

    /**
     *  Callback
     */
    const {
        data: executeLogColletorResData,
        loading: executeLogColletorResLoading,
        error: executeLogColletorResError
    } = useSelector((state) => state.log.executeLogColletorRes);

    useEffect(() => {
        if (!executeLogColletorResLoading && executeLogColletorResError) {
            toast.error(getErrorMsg(executeLogColletorResError, 'execute'));
            dispatch(clearExecuteLogColletor());
        }
        // Log snapshot delete success
        else if (!executeLogColletorResLoading && executeLogColletorResData) {
            toast.success(t('common.alert.updateSuccess'));
            dispatch(clearExecuteLogColletor());
            dispatch(searchLogSnapshot(searchLogSnapshotForm));

            // Modal Hide
            handleClose();
        }
    }, [executeLogColletorResData, executeLogColletorResError]);

    useEffect(() => {
        if (usedLogAnalysisModalInitData.showModal && !modalShow) {
            // scheduleDataData({...scSchedualModalInitData.initData});
            dispatch(clearLogAnalysisModal());
            handleShow();
        }
    }, [usedLogAnalysisModalInitData.showModal]);

    return (
        <>
            <LogAnalysisModal
                modalShow={modalShow}
                handleClose={handleClose}
                codeData={codeData}
                serverData={serverData}
                memberInfoData={memberInfoData}
                onExecuteLogColletor={onExecuteLogColletor}
            />
            {/* <executeLogColletorResLoading */}
            <Spinner isLoading={executeLogColletorResLoading} />
        </>
    );
}

export default LogAnalysisModalContainer;
