/* eslint-disable no-unused-vars */
import React from 'react';
import Modal from 'react-bootstrap/Modal';
import {Form, Row, Col} from 'react-bootstrap';
import {useFormik} from 'formik';
import * as Yup from 'yup';
import {useTranslation} from 'react-i18next';
import {toast} from 'react-toastify';
import Button from '../../../components/button/Button';
import {printFormError, renderModalHeader} from '../../../lib/commonUiUtils';

/*
 * step   case /    
 */
const UitestStepMove = ({
    modalShow,
    handleClose,
    seletedStep,
    scenarioData,
    setDetailCaseList,
    tempkey,
    setTempKeyRezen
}) => {
    const [t] = useTranslation();
    const formik = useFormik({
        initialValues: {
            changeCaseId: '',
            changeStepOrder: 'last',
            deleteExisting: false
        },
        validationSchema: Yup.object({
            changeCaseId: Yup.number().required(
                ' Case   .'
            )
        }),

        onSubmit: (values) => {
            // {changeCaseId: '2', changeStepOrder: 'last', deleteExisting: true}
            // setDetailCaseList
            const valuesT = {...seletedStep};
            let caseLoop = [...scenarioData.data.caseList];

            if (values.deleteExisting) {
                caseLoop = caseLoop.map((caseUnit) => {
                    const theCase = caseUnit;

                    const newStepList = theCase.stepList.filter(
                        (obj) => obj.id !== Number(seletedStep.id)
                    );

                    const stepLoop = newStepList.map((sUnit, sIndex) => {
                        const theStep = sUnit;
                        theStep.idx = sIndex;
                        return theStep;
                    });
                    theCase.stepList = stepLoop;
                    return theCase;
                });
            } else {
                valuesT.id = tempkey;
                setTempKeyRezen();
            }

            // let isSelected = false;
            caseLoop = caseLoop.map((unit, index) => {
                const theCase = unit;
                if (unit.id === Number(values.changeCaseId)) {
                    let valuesTList = [valuesT];

                    if (theCase.stepList) {
                        if (values.changeStepOrder === 'first') {
                            valuesTList = valuesTList.concat(theCase.stepList);
                        } else {
                            valuesTList = theCase.stepList.concat(valuesTList);
                        }
                    }

                    const stepLoop = valuesTList.map((sUnit, sIndex) => {
                        const theStep = sUnit;
                        theStep.idx = sIndex;
                        return theStep;
                    });

                    theCase.stepList = stepLoop;
                }

                return theCase;
            });

            setDetailCaseList(caseLoop);

            toast.success('Step / .');
            handleClose();
        }
    });

    const renderCaseList = () => {
        if (!modalShow) return '';
        return scenarioData.data.caseList.map((caze) => {
            let isSelected = false;

            if (caze.stepList && caze.stepList.length !== 0) {
                caze.stepList.forEach((step) => {
                    if (step.id === seletedStep.id) {
                        isSelected = true;
                    }
                });
            }

            if (isSelected) {
                return (
                    <option value="" key={caze.id}>
                        (Current) {caze.caseName} (
                        {caze.stepList ? caze.stepList.length : 0} step)
                    </option>
                );
            }
            return (
                <option value={caze.id} key={caze.id}>
                    {caze.caseName} ({caze.stepList ? caze.stepList.length : 0}
                    step)
                </option>
            );
        });
    };

    return (
        <>
            {/* <Button theme="link" onClick={handleShow}>
                {t('pages.scenario.moveStep')}
            </Button> */}

            <Modal
                show={modalShow}
                onHide={handleClose}
                size="lg"
                onExited={() => formik.resetForm()}
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Form onSubmit={formik.handleSubmit}>
                    {renderModalHeader(t('pages.scenario.moveStep'))}

                    <Modal.Body>
                        <Row>
                            <Col xs="5">
                                <Form.Group>
                                    <Form.Label>Case </Form.Label>
                                    <Form.Control
                                        as="select"
                                        {...formik.getFieldProps(
                                            'changeCaseId'
                                        )}
                                    >
                                        {/* <option value="">
                                            {t('common.text.optionNone')}
                                        </option> */}
                                        {renderCaseList()}
                                    </Form.Control>
                                    {printFormError(formik, 'changeCaseId')}
                                </Form.Group>
                            </Col>
                            <Col xs="5">
                                <Form.Group>
                                    <Form.Label> </Form.Label>
                                    <Form.Control
                                        as="select"
                                        {...formik.getFieldProps(
                                            'changeStepOrder'
                                        )}
                                    >
                                        <option value="first">
                                             
                                        </option>
                                        <option value="last">
                                             
                                        </option>
                                    </Form.Control>
                                </Form.Group>
                            </Col>
                            <Col xs="12">
                                <div key="deleteExisting" className="mb-3">
                                    <Form.Check
                                        type="checkbox"
                                        id="deleteExisting"
                                        label={t(
                                            'pages.scenario.deleteExisting'
                                        )}
                                        {...formik.getFieldProps(
                                            'deleteExisting'
                                        )}
                                        checked={formik.values.deleteExisting}
                                    />
                                </div>
                            </Col>
                        </Row>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button
                            theme="primary"
                            type="submit"
                            // isLoading={createLoading}
                        >
                            {t('common.button.save')}
                        </Button>
                        <Button theme="default" onClick={handleClose}>
                            {t('common.button.close')}
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>
        </>
    );
};

export default UitestStepMove;
