//  
const SHOW_LOADING = 'commonUI/SHOW_LOADING';
const HIDE_LOADING = 'common/HIDE_LOADING';

export const showLoading = () => ({
    type: SHOW_LOADING
});

export const hideLoading = () => ({
    type: HIDE_LOADING
});

// export function* commonUISaga() {}

/**
 * State 
 */
const initialState = {
    showLoading: false
};

export default function commonUI(state = initialState, action) {
    switch (action.type) {
        case SHOW_LOADING:
            return {
                ...state,
                showLoading: true
            };

        case HIDE_LOADING:
            return {
                ...state,
                showLoading: false
            };
        default:
            return state;
    }
}
