/* eslint-disable no-shadow */
/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import {Form, Accordion, FloatingLabel, Row, Col} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
import {jsonStrFormatPretty} from '../../../lib/jsonUtils';
import {
    getJsonObj,
    renderCopyBlankData,
    renderVariable,
    renderCase,
    renderStep,
    renderSampleObject
} from '../../../lib/UitestCommon';
import {takeSelectOptionbyCode} from '../../../lib/commonUiUtils';
import {
    searchRandomAll,
    testRandom,
    testRandomDataClear
} from '../../../store/random';

// eslint-disable-next-line no-unused-vars
const UitestScriptCopy = ({formik, executeList, scenarioData, codeData}) => {
    const dispatch = useDispatch();
    const [t] = useTranslation();

    // SAMPLE DATA  in or out 
    // const [selectedFromTo, setSelectedFromTo] = React.useState('from');
    const [randTestResult, setRandTestResult] = React.useState('');

    useEffect(() => {
        dispatch(searchRandomAll());
    }, []);

    const {loading: searchLoading, data: randData} = useSelector(
        (state) => state.random.searchList
    );

    // change rand code
    const onClickRandomTest = (e) => {
        dispatch(testRandom({randomId: formik.values.copyRandKey}));
    };

    const {
        loading: testLoading,
        data: testResponse,
        error: testError
    } = useSelector((state) => state.random.test);

    if (!testLoading && testResponse) {
        setRandTestResult(testResponse.data.testData);
        dispatch(testRandomDataClear());
    }

    const renderRandomStyle = () => {
        return (
            <FloatingLabel label=" " className="mb-2">
                <Form.Select
                    {...formik.getFieldProps('script.copyAttr.randomStyle')}
                >
                    <option value="">{t('common.text.optionNone')}</option>
                    {takeSelectOptionbyCode(codeData, 'RandomStyle')}
                </Form.Select>
            </FloatingLabel>
        );
    };

    const renderRandomId = () => {
        if (randData === null) {
            return '';
        }

        const {randomStyle} = formik.values.script.copyAttr;
        return (
            <FloatingLabel label=" " className="mb-2">
                <Form.Select
                    {...formik.getFieldProps('script.copyAttr.randomId')}
                >
                    <option value="">{t('common.text.optionNone')}</option>
                    {randData.data
                        .filter(
                            (obj) =>
                                randomStyle === undefined ||
                                randomStyle === '' ||
                                obj.randomStyle ===
                                    formik.values.script.copyAttr.randomStyle
                        )
                        .map((obj) => (
                            <option value={obj.randomId} key={obj.randomId}>
                                {obj.randomDesc}
                            </option>
                        ))}
                </Form.Select>
            </FloatingLabel>
        );
    };

    /*
     * renderCopyFromCond -> script type -> copy -> from
     * copy from   
     * string, number, step, random, variable
     */
    const renderCopyDetail = (type) => {
        const {option} =
            type === 'FROM'
                ? formik.values.script.copyAttr.fromItem
                : formik.values.script.copyAttr.toItem;

        if (option === 'TEXT' || option === 'NUMBER') {
            return (
                <>
                    <Col xs="12">
                        {renderCopyBlankData(
                            option === 'TEXT'
                                ? 'Text  '
                                : ' ',
                            option,
                            type === 'FROM'
                                ? 'script.copyAttr.fromItem.data'
                                : 'script.copyAttr.toItem.data',
                            formik
                        )}
                    </Col>
                </>
            );
        }
        if (option === 'VARIABLE') {
            return (
                <>
                    <Col xs="12">
                        {renderVariable(
                            type === 'FROM'
                                ? 'script.copyAttr.fromItem.variableId'
                                : 'script.copyAttr.toItem.variableId',
                            formik,
                            scenarioData
                        )}
                    </Col>
                </>
            );
        }
        if (option === 'RANDOM') {
            return (
                <>
                    <Col xs="5">{renderRandomStyle()}</Col>
                    <Col xs="7">{renderRandomId()}</Col>
                </>
            );
        }
        if (option === 'STEP') {
            return (
                <>
                    <Col xs="6">
                        {renderCase(
                            type === 'FROM'
                                ? 'script.copyAttr.fromItem.caseId'
                                : 'script.copyAttr.toItem.caseId',
                            formik,
                            scenarioData
                        )}
                    </Col>
                    <Col xs="6">
                        {renderStep(
                            type === 'FROM'
                                ? formik.values.script.copyAttr.fromItem.caseId
                                : formik.values.script.copyAttr.toItem.caseId,
                            type === 'FROM'
                                ? 'script.copyAttr.fromItem.stepId'
                                : 'script.copyAttr.toItem.stepId',
                            formik,
                            scenarioData
                        )}
                    </Col>
                    <Col xs="12">
                        {renderCopyBlankData(
                            '',
                            'TEXT',
                            type === 'FROM'
                                ? 'script.copyAttr.fromItem.data'
                                : 'script.copyAttr.toItem.data',
                            formik
                        )}
                    </Col>
                    <Col xs="12">
                        {renderSampleObject(
                            type === 'FROM'
                                ? formik.values.script.copyAttr.fromItem.stepId
                                : formik.values.script.copyAttr.toItem.stepId,
                            type === 'FROM' ? 'response' : 'request',
                            type === 'FROM'
                                ? formik.values.script.copyAttr.fromItem.data
                                : formik.values.script.copyAttr.toItem.data,
                            scenarioData
                        )}
                    </Col>
                </>
            );
        }

        return '';
    };

    return (
        <>
            <Row>
                <Col xs="5">
                    <Accordion defaultActiveKey="0">
                        <Accordion.Item eventKey="0">
                            <Accordion.Header className="accordion-header2">
                                FROM DATA
                            </Accordion.Header>
                            <Accordion.Body>
                                <Row>
                                    <Col xs="12">
                                        <FloatingLabel
                                            label="From  "
                                            className="mb-2"
                                        >
                                            <Form.Select
                                                {...formik.getFieldProps(
                                                    'script.copyAttr.fromItem.option'
                                                )}
                                            >
                                                {takeSelectOptionbyCode(
                                                    codeData,
                                                    'ScriptOption'
                                                )}
                                            </Form.Select>
                                        </FloatingLabel>
                                    </Col>
                                    {renderCopyDetail('FROM')}
                                </Row>
                            </Accordion.Body>
                        </Accordion.Item>
                    </Accordion>
                </Col>
                <Col
                    xs="1"
                    style={{
                        verticalAlign: 'middle',
                        height: '300px',
                        lineHeight: '300px'
                    }}
                >
                    <img
                        src="/img/right_btn.png"
                        style={{width: '80px'}}
                        alt="User"
                    />
                </Col>
                <Col xs="5">
                    <Accordion defaultActiveKey="0">
                        <Accordion.Item eventKey="0">
                            <Accordion.Header className="accordion-header3">
                                TO DATA
                            </Accordion.Header>
                            <Accordion.Body>
                                <Row>
                                    <Col xs="12">
                                        <FloatingLabel
                                            label="To  "
                                            className="mb-2"
                                        >
                                            <Form.Select
                                                {...formik.getFieldProps(
                                                    'script.copyAttr.toItem.option'
                                                )}
                                            >
                                                {takeSelectOptionbyCode(
                                                    codeData,
                                                    'ScriptOption',
                                                    2
                                                )}
                                            </Form.Select>
                                        </FloatingLabel>
                                    </Col>
                                    {renderCopyDetail('TO')}
                                </Row>
                            </Accordion.Body>
                        </Accordion.Item>
                    </Accordion>
                </Col>
            </Row>
        </>
    );
};

export default UitestScriptCopy;
