/* eslint-disable no-unused-vars */
import React from 'react';
import {Card, Button} from 'react-bootstrap';

// card componet
// title -> header 
// body -> body   
// sample
const AppCard = ({title = ' .', body, imgIcon = 'menu1'}) => {
    const onClickButton = (e) => {
        let iObj = null;
        if (e.target.getAttribute('type') === 'button') {
            // eslint-disable-next-line prefer-destructuring
            iObj = e.target.children[0];
        } else {
            iObj = e.target;
        }

        if (iObj.classList.contains('fa-minus')) {
            iObj.classList.remove('fa-minus');
            iObj.classList.add('fa-plus');
            iObj.parentElement.parentElement.parentElement.children[1].classList.add(
                'hidden'
            );
        } else {
            iObj.classList.remove('fa-plus');
            iObj.classList.add('fa-minus');
            iObj.parentElement.parentElement.parentElement.children[1].classList.remove(
                'hidden'
            );
        }
    };

    const renderMenuImg = () => {
        const iconSrc = `/img/${imgIcon}.png`;
        return (
            <img
                src={iconSrc}
                alt="atworks Logo"
                className="brand-image"
                style={{maxWidth: '22px'}}
            />
        );
    };
    return (
        <Card style={{borderTop: '3px solid #007bff'}}>
            <Card.Header style={{padding: '0.2rem 1.25rem'}}>
                {renderMenuImg()}
                <label style={{paddingLeft: '11px'}}>{` ${title}`}</label>
                <button
                    style={{float: 'right', padding: '0px', margin: '8px'}}
                    type="button"
                    className="btn btn-tool"
                    onClick={onClickButton}
                >
                    <i className="fas fa-minus" />
                </button>
            </Card.Header>
            <Card.Body>{body}</Card.Body>
        </Card>
    );
};

export default AppCard;
