import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import LayoutCreatePopup from '@app/pages/layout/popup/LayoutCreatePopup';
import {toast} from 'react-toastify';
import {useTranslation} from 'react-i18next';
import {useHistory} from 'react-router-dom';
import Spinner from '../../../components/spinner/Spinner';
import {getErrorMsg} from '../../../lib/commonUiUtils';

import {
    hideCreateModal,
    createLayoutManual,
    clearCreateLayoutManual,
    searchLayout
} from '../../../store/layout';

/*
 *  -     Container
 */
function LayoutCreatePopupContainer() {
    const dispatch = useDispatch();
    const history = useHistory();
    const [t] = useTranslation();

    const {visible: modalShow} = useSelector(
        (state) => state.layout.createModal
    );
    const {loading, data, error} = useSelector(
        (state) => state.layout.createLayoutManual
    );

    const searchLayoutForm = useSelector((state) => state.layout.searchForm);

    const handleClose = () => dispatch(hideCreateModal());

    const onCreateLayout = (object) => {
        dispatch(createLayoutManual(object));
    };

    useEffect(() => {
        /* Fail process */
        if (!loading && error) {
            toast.error(getErrorMsg(error, 'save'));
            dispatch(clearCreateLayoutManual());
        }

        /* Success process */
        if (!loading && data) {
            toast.success(t('common.alert.updateSuccess'));
            const layoutId = data.data;

            dispatch(clearCreateLayoutManual());
            dispatch(hideCreateModal());
            dispatch(searchLayout(searchLayoutForm));
            history.push({
                pathname: '/layout/detail',
                state: {
                    id: layoutId
                }
            });
        }
    }, [data, error]);

    return (
        <>
            <LayoutCreatePopup
                modalShow={modalShow}
                createLayout={onCreateLayout}
                handleClose={handleClose}
            />
            <Spinner isLoading={loading} />
        </>
    );
    ///
}

export default LayoutCreatePopupContainer;
