/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import SourceDetailTable from '@pages/source/SourceDetailTable';
import {useHistory} from 'react-router-dom';
import {useTranslation} from 'react-i18next';
import {setSearchSourcesForm} from '../../store/source';

function SourceDetailTableContainer({onSearch}) {
    const dispatch = useDispatch();
    const [t] = useTranslation();
    const history = useHistory();

    //  
    const {data: searchData} = useSelector(
        (state) => state.source.searchSourcesRes
    );

    const {searchSourcesForm} = useSelector((state) => state.source);

    const movePage = (page) => {
        const searchSourcesFormT = {...searchSourcesForm};
        searchSourcesFormT.page = page;
        dispatch(setSearchSourcesForm(searchSourcesFormT));
        onSearch(searchSourcesFormT);
    };

    const onClickMove = (id) => {
        history.push({
            pathname: '/source/detail',
            state: {
                id
            }
        });
    };

    return (
        <>
            <SourceDetailTable
                movePage={movePage}
                data={searchData}
                onClickMove={onClickMove}
            />
            {/* <Spinner isLoading={deleteLogSnapshotLoading} /> */}
        </>
    );
    ///
}

export default SourceDetailTableContainer;
