/* eslint-disable no-unused-vars */
import React from 'react';
import {FaSearch} from 'react-icons/fa';
import {useTranslation} from 'react-i18next';
import Table from '../../components/table/Table';
import Button from '../../components/button/Button';
import {changeToDate} from '../../lib/moment';
import {
    renderHttpMethod,
    renderSystemInfo,
    renderServiceInfo
} from '../../lib/buttonRender';

const HistoryTable = ({data, movePage, showUndoFormModal}) => {
    const [t] = useTranslation();
    const tableName = t('pages.history.historyList');
    const rowKey = 'historyId';
    const columns = [
        {
            title: t('pages.scenario.httpMethod'),
            width: '5%',
            key: 'httpMethod',
            align: 'center', // left, center, right (default:center)
            render: (id, row) => {
                return renderHttpMethod(row.urlInfo?.httpMethod);
            }
        },
        {
            title: 'Service Info',
            key: 'serviceId',
            align: 'left', // left, center, right (default:center)
            render: (id, row) => {
                return renderServiceInfo(row.serviceInfo, row.urlInfo?.urlPath);
            }
        },
        {
            title: t('pages.server.serverName'),
            key: 'serverName',
            width: '10%'
        },
        {
            title: t('common.text.resCode'),
            key: 'systemAHttpResponseCode',
            width: '10%',
            render: (id, row) => {
                return renderSystemInfo(row.systemInfo);
            }
        },
        {
            title: t('pages.mass.startTime'),
            width: '10%',
            key: 'sendDateTime',
            render: (id, row) => {
                return changeToDate(row.responseTime?.sendDateTime);
            }
        },
        {
            title: t('pages.mass.executeSecond'),
            key: 'processTime',
            width: '10%',
            render: (id, row) => {
                return row.responseTime?.processTime;
            }
        },
        {
            title: '',
            key: 'button',
            width: '5%',
            align: 'right',
            render: (id, row) => {
                return (
                    <Button
                        theme="outline-success"
                        onClick={() => showUndoFormModal(row)}
                    >
                        <FaSearch />
                    </Button>
                );
            }
        }
    ];

    const onDoubleClick = (id, row) => {
        showUndoFormModal(row);
    };

    return (
        <>
            <Table
                tableName={tableName}
                movePage={movePage}
                rowKey={rowKey}
                columns={columns}
                data={data}
                onDoubleClick={onDoubleClick}
            />
        </>
    );
};

export default HistoryTable;
