/* eslint-disable react-hooks/rules-of-hooks */
/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import Uitest from '@pages/uitest/Uitest';
import {useLocation} from 'react-router-dom';
import {useSelector, useDispatch} from 'react-redux';
import {toast} from 'react-toastify';
import {useTranslation} from 'react-i18next';
import {
    searchScenarioDetail,
    searchScenarioDetailClear,
    setSelectedCaseId,
    setDetailCaseList,
    setDetailVariableList,
    saveScenario,
    updateScenario,
    saveScenarioDataClear
} from '../../store/uitest';
import Spinner from '../../components/spinner/Spinner';
import {getErrorMsg} from '../../lib/commonUiUtils';
import {setTempKey} from '../../store/common';

/**
 *    
 * @returns
 */
const UitestContainer = () => {
    const dispatch = useDispatch();
    const location = useLocation();
    const [t] = useTranslation();

    const {caseId} = useSelector((state) => state.uitest.selectedCaseId);
    const {data: scenarioData} = useSelector(
        (state) => state.uitest.scenarioInfo
    );

    // scenario save event
    const onSaveScenario = (_scenarioData = scenarioData?.data) => {
        if (!_scenarioData?.lock) {
            if (Number(caseId) < 0) {
                dispatch(setSelectedCaseId(''));
            }

            if (_scenarioData?.scenarioId > 0) {
                dispatch(updateScenario(_scenarioData));
            } else {
                dispatch(saveScenario(_scenarioData));
            }
        }
    };

    const {tempkey} = useSelector((state) => state.common);
    const setTempKeyRezen = () => {
        if (tempkey < -9999) {
            dispatch(setTempKey(-1));
        } else {
            dispatch(setTempKey(tempkey - 1));
        }
    };

    const saveNewCase = () => {
        const cases = [];
        const vars = [];
        const stepT = {
            stepId: -2,
            stepType: 'LOGIN',
            stepName: '(New)  API',
            isSkipError: false,
            connectionInfo: {connectedId: '', connectedPw: ''},
            defaultLogin: 'Y'
        };

        const caseT = {
            caseId: tempkey,
            index: 0,
            caseName: '[]  ',
            caseDesc: '   API  ',
            scenarioId: -1,
            steps: [stepT]
        };
        setTempKeyRezen();
        cases.push(caseT);

        dispatch(setDetailCaseList(cases));
        dispatch(setDetailVariableList(vars));
    };

    useEffect(() => {
        dispatch(searchScenarioDetailClear());
        dispatch(setSelectedCaseId(''));

        if (location.state != null) {
            dispatch(searchScenarioDetail(location.state.key));
        } else {
            // 20220630     / ( ) 
            saveNewCase();
        }
        dispatch(setTempKey(-1));
    }, []);

    const {
        data: searchData,
        loading: searchLoading,
        error: searchError
    } = useSelector((state) => state.uitest.scenarioInfo);

    useEffect(() => {
        // search fail
        if (!searchLoading && searchData) {
            if (caseId === '' && searchData.data?.cases?.length !== 0) {
                dispatch(setSelectedCaseId(searchData.data.cases[0].caseId));
            }
        }
        if (!searchLoading && searchError) {
            toast.error(getErrorMsg(searchError, 'search'));
        }
    }, [searchData]);

    const {
        loading: saveLoading,
        error: saveError,
        data: saveResponse
    } = useSelector((state) => state.uitest.saveScenario);

    useEffect(() => {
        if (!saveLoading && saveResponse) {
            if (saveResponse.data > 0) {
                toast.success(t('common.alert.updateSuccess'));
                dispatch(saveScenarioDataClear());
                dispatch(searchScenarioDetail(saveResponse.data));
            }
        }
        // search fail
        if (!saveLoading && saveError) {
            toast.error(getErrorMsg(saveError, 'save'));
        }
    }, [saveResponse, saveError]);

    return (
        <>
            <Uitest onSaveScenario={onSaveScenario} />
            <Spinner isLoading={searchLoading || saveLoading} />
        </>
    );
};
export default UitestContainer;
