/* eslint-disable no-unused-vars */
import React from 'react';
import {Modal, Row, Col, Accordion} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
import Button from '../../components/button/Button';
import {renderModalHeader} from '../../lib/commonUiUtils';
import Table from '../../components/table/Table';

const TextDiffViewerPopup = ({modalShow, handleClose, searchData}) => {
    const [t] = useTranslation();

    const renderSearchData = () => {
        if (!searchData) {
            return {data: {content: []}};
        }
        const temp = {data: {content: searchData.data.differenceList}};
        return temp;
    };

    const renderDifferenceType = (differenceType) => {
        let theme = 'secondary';
        const text = differenceType;
        // IS_NULL, TYPE, VALUE
        if (differenceType === 'VALUE') {
            theme = 'danger';
        } else if (differenceType === 'IS_NULL') {
            theme = 'warning';
        } else if (differenceType === 'TYPE') {
            theme = 'primary';
        }
        return (
            <Button
                theme={theme}
                style={{minWidth: '80px', maxHeight: 'calc(3.5rem + 2px)'}}
            >
                {text}
            </Button>
        );
    };

    const columns = [
        {
            title: '',
            key: 'key'
        },
        {
            title: 'Differnce ',
            key: 'differenceType',
            render: (id, row) => {
                return renderDifferenceType(row.differenceType);
            }
        },
        {
            title: 'Server A',
            key: 'dataA'
        },
        {
            title: 'Server B',
            key: 'dataB'
        }
    ];

    return (
        <>
            <Modal
                show={modalShow}
                onHide={handleClose}
                size="xl"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                {renderModalHeader(t('common.text.responseDiff'))}
                <Modal.Body>
                    <Row>
                        <Col>
                            <Accordion defaultActiveKey="0">
                                <Accordion.Item eventKey="0">
                                    <Accordion.Header className="accordion-header">
                                         
                                    </Accordion.Header>
                                    <Accordion.Body>
                                        <Table
                                            isCard="N"
                                            rowKey="key"
                                            columns={columns}
                                            data={renderSearchData()}
                                            disablePageView
                                        />
                                    </Accordion.Body>
                                </Accordion.Item>
                            </Accordion>
                        </Col>
                    </Row>
                </Modal.Body>
                <Modal.Footer>
                    <Button theme="default" onClick={handleClose}>
                        {t('common.button.close')}
                    </Button>
                </Modal.Footer>
            </Modal>
        </>
    );
};
export default TextDiffViewerPopup;
