/**
 * HTTP Method draw
 * @param {*} httpMethod
 * @returns
 */
export const renderRandomStyle = (randomStyle) => {
    let theme = 'secondary';
    let text = randomStyle;
    if (randomStyle === 'LIST') {
        theme = 'primary';
    } else if (randomStyle === 'NUMBER') {
        theme = 'warning';
    } else if (randomStyle === 'DATE') {
        theme = 'info';
    } else if (randomStyle === 'D_DAY') {
        theme = 'danger';
    } else if (randomStyle === '' || randomStyle == null) {
        text = 'None';
    }
    return (
        <Button
            theme={theme}
            style={{minWidth: '80px', maxHeight: 'calc(3.5rem + 2px)'}}
        >
            {text}
        </Button>
    );
};

export const renderClubType = (clubType) => {
    const {name, value} = clubType;
    if (name === 'DAILY_INSPECTION') {
        return (
            <button
                type="submit"
                className="btn btn-success"
                style={{width: '100%'}}
            >
                {value}
            </button>
        );
    }
    if (name === 'REGRESSION_TEST') {
        return (
            <button
                type="submit"
                className="btn btn-primary"
                style={{width: '100%'}}
            >
                {value}
            </button>
        );
    }
    if (name === 'UNIT_TEST') {
        return (
            <button
                type="submit"
                className="btn btn-secondary"
                style={{width: '100%'}}
            >
                {value}
            </button>
        );
    }
    return '';
};

