import React from 'react';
import {FaTrash, FaSearch} from 'react-icons/fa';
import {Row, Col, Form, FloatingLabel} from 'react-bootstrap';
import Button from '../../components/button/Button';
import Card from '../../components/card/Card';
import Table from '../../components/table/Table';
import {changeToDate} from '../../lib/moment';

/**
 * Scnarion Test Club
 *  Container  .
 * @since 2022-03-29
 * @returns
 */
const ClubDetailScenario = ({
    searchScenarioData,
    onClickAdd,
    movePage,
    searchClubScenariosForm,
    onChangerFormData,
    onClickSearch,
    onClickMoveScenario,
    onClickDeleteScenario,
    searchClubDetailData
}) => {
    const onCheckEnter = (e) => {
        if (e.key === 'Enter') {
            e.preventDefault();
            onClickSearch();
        }
    };

    const column = [
        {
            title: ' ',
            align: 'left',
            key: 'scenarioName'
        },
        {
            title: '',
            width: '15%',
            key: 'createBy',
            render: (id, row) => {
                return row.manager?.name;
            }
        },
        {
            title: '',
            width: '10%',
            key: 'modifiedDate',
            render: (id, row) => {
                return changeToDate(row.modifiedDate);
            }
        },
        {
            title: '',
            key: 'button',
            width: '10%',
            align: 'right',
            render: (id, row) => {
                return (
                    <>
                        <Button
                            theme="outline-success"
                            onClick={() => onClickMoveScenario(row)}
                        >
                            <FaSearch />
                        </Button>
                        <Button
                            theme="outline-danger"
                            disabled={searchClubDetailData?.data?.lock}
                            onClick={() => onClickDeleteScenario(row)}
                        >
                            <FaTrash />
                        </Button>
                    </>
                );
            }
        }
    ];

    return (
        <>
            <Card
                title=" "
                imgIcon="menu3"
                body={
                    <>
                        <Row onKeyPress={onCheckEnter}>
                            <Col xs="5">
                                <FloatingLabel
                                    label=" "
                                    className="mb-2"
                                >
                                    <Form.Control
                                        type="text"
                                        maxLength="30"
                                        id="scenarioName"
                                        onChange={onChangerFormData}
                                        value={
                                            searchClubScenariosForm?.scenarioName ??
                                            ''
                                        }
                                    />
                                </FloatingLabel>
                            </Col>
                            <Col xs="5"> </Col>
                            <Col xs="2" style={{float: 'rigth'}}>
                                <Form.Group>
                                    <Form.Label />
                                    <Row>
                                        <Col xs="6">
                                            <Button
                                                type="button"
                                                className="btn btn-block btn-success"
                                                onClick={onClickAdd}
                                                disabled={
                                                    searchClubDetailData &&
                                                    searchClubDetailData.data
                                                        .lock
                                                }
                                            >
                                                
                                            </Button>
                                        </Col>
                                        <Col xs="6">
                                            <Button
                                                type="button"
                                                className="btn btn-block btn-secondary btn-search"
                                                onClick={onClickSearch}
                                            >
                                                
                                            </Button>
                                        </Col>
                                    </Row>
                                </Form.Group>
                            </Col>
                        </Row>
                        <Table
                            isCard="N"
                            movePage={movePage}
                            rowKey="clubScenarioId"
                            columns={column}
                            data={searchScenarioData}
                        />
                    </>
                }
            />
        </>
    );
};
export default ClubDetailScenario;
