import React from 'react';
import {useFormik} from 'formik';
import {Row, Col, FloatingLabel} from 'react-bootstrap';
import * as Yup from 'yup';
import Form from 'react-bootstrap/Form';
import {useTranslation} from 'react-i18next';
import Button from '../../components/button/Button';

import {printFormError} from '../../lib/commonUiUtils';

const CredentialInfoCreate = ({selectedServer, createLoading, onCreate}) => {
    const [t] = useTranslation();

    const getInitData = () => {
        return {
            credentialName: '',
            connectionInfo: {
                connectedId: '',
                connectedPw: ''
            },
            isDefault: false,
            serverId: selectedServer.id
        };
    };

    const formik = useFormik({
        initialValues: getInitData(),
        validationSchema: Yup.object({
            // // t('pages.server.connectId')
            // userId: Yup.string().required(
            //     getInputValidMsg(t('pages.server.connectId'))
            // ),
            // connectedId: Yup.string().required(
            //     getInputValidMsg(t('pages.server.connectPw'))
            // )
        }),
        onSubmit: (values, {resetForm}) => {
            onCreate(values);
            resetForm(getInitData());
        }
    });

    return (
        <>
            <Form onSubmit={formik.handleSubmit}>
                <Row>
                    <Col xs="3">
                        <FloatingLabel label="ID" className="mb-3">
                            <Form.Control
                                type="text"
                                placeholder="!"
                                {...formik.getFieldProps(
                                    'connectionInfo.connectedId'
                                )}
                                maxLength="20"
                            />
                            {printFormError(formik, 'userId')}
                        </FloatingLabel>
                    </Col>
                    <Col xs="3">
                        <FloatingLabel label="Password" className="mb-3">
                            <Form.Control
                                type="password"
                                placeholder="!"
                                {...formik.getFieldProps(
                                    'connectionInfo.connectedPw'
                                )}
                                maxLength="50"
                            />
                            {printFormError(formik, 'password')}
                        </FloatingLabel>
                    </Col>
                    <Col xs="4">
                        <FloatingLabel
                            label="   "
                            className="mb-3"
                        >
                            <Form.Control
                                type="text"
                                {...formik.getFieldProps('credentialName')}
                                maxLength="50"
                            />
                        </FloatingLabel>
                    </Col>

                    <Col xs="2">
                        {/* <br /> */}
                        <Button
                            theme="primary"
                            type="submit"
                            isLoading={createLoading}
                            style={{width: '90%', height: '70%'}}
                        >
                            {t('common.button.register')}
                        </Button>
                    </Col>
                </Row>
            </Form>
        </>
    );
};
export default CredentialInfoCreate;
