/* eslint-disable no-unused-vars */
/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react';
import {
    Container,
    Row,
    Col,
    Form,
    InputGroup,
    FormControl,
    FloatingLabel
} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
import Card from '../../components/card/Card';
import {renderHttpMethod} from '../../lib/commonUiUtils';
import {renderOptionsByCode} from '../../lib/codeUtils';

const LayoutDetailHeader = ({
    selectedLayout,
    showUnittestModal,
    moveHistory,
    saveSelectedLayout,
    saveLayout
}) => {
    const [t] = useTranslation();
    const onChangeTransactionType = (e) => {
        const preSelectedLayout = {...selectedLayout};
        preSelectedLayout.transactionType = e.target.value;
        saveSelectedLayout(preSelectedLayout);
    };
    const onChangeServiceId = (e) => {
        const preSelectedLayout = {...selectedLayout};
        preSelectedLayout.serviceInfo.serviceId = e.target.value;
        saveSelectedLayout(preSelectedLayout);
    };

    const onChangeServiceDesc = (e) => {
        const preSelectedLayout = {...selectedLayout};
        preSelectedLayout.serviceInfo.serviceDesc = e.target.value;
        saveSelectedLayout(preSelectedLayout);
    };

    // url path pattern or service id
    const getUrlInfo = () => {
        return selectedLayout?.urlInfo?.urlPath;
    };

    return (
        <>
            <Row>
                <Col xs="5">
                    <Card
                        title="URL "
                        body={
                            <Container fluid>
                                <Row>
                                    <Col xs="8">
                                        <InputGroup>
                                            {renderHttpMethod(
                                                selectedLayout?.urlInfo
                                                    .httpMethod
                                            )}
                                            <FloatingLabel label="URL Path">
                                                <FormControl
                                                    disabled
                                                    value={getUrlInfo()}
                                                />
                                            </FloatingLabel>
                                        </InputGroup>
                                    </Col>
                                    <Col xs="4">
                                        <a
                                            className="btn btn-app"
                                            onClick={() => showUnittestModal()}
                                        >
                                            <i className="fas fa-play" />
                                            {t('common.button.play')}
                                        </a>
                                        <a
                                            className="btn btn-app"
                                            onClick={() => moveHistory()}
                                        >
                                            <i className="fas fa-history" />
                                            {t('common.button.history')}
                                        </a>
                                    </Col>
                                </Row>
                            </Container>
                        }
                    />
                </Col>
                <Col xs="7">
                    <Card
                        imgIcon="menu2"
                        title=" "
                        body={
                            <Container fluid>
                                <Row>
                                    <Col xs="4">
                                        <FloatingLabel label="Service ID">
                                            <FormControl
                                                type="text"
                                                value={
                                                    selectedLayout?.serviceInfo
                                                        .serviceId ?? ''
                                                }
                                                id="serviceId"
                                                onChange={onChangeServiceId}
                                                maxLength="50"
                                            />
                                        </FloatingLabel>
                                    </Col>
                                    <Col xs="4">
                                        <FloatingLabel label="Service ">
                                            <FormControl
                                                type="text"
                                                value={
                                                    selectedLayout?.serviceInfo
                                                        .serviceDesc ?? ''
                                                }
                                                id="serviceDesc"
                                                onChange={onChangeServiceDesc}
                                                maxLength="50"
                                            />
                                        </FloatingLabel>
                                    </Col>
                                    <Col xs="2">
                                        <FloatingLabel label="API ">
                                            <Form.Select
                                                value={
                                                    selectedLayout?.transactionType ??
                                                    ''
                                                }
                                                onChange={
                                                    onChangeTransactionType
                                                }
                                            >
                                                {renderOptionsByCode(
                                                    'TransactionType'
                                                )}
                                            </Form.Select>
                                        </FloatingLabel>
                                    </Col>
                                    <Col xs="2">
                                        <a
                                            className="btn btn-app"
                                            onClick={() => saveLayout()}
                                        >
                                            <i className="fas fa-save" />
                                            {t('common.button.save')}
                                        </a>
                                    </Col>
                                </Row>
                            </Container>
                        }
                    />
                </Col>
            </Row>
        </>
    );
};

export default LayoutDetailHeader;
