/* eslint-disable no-unused-vars */
/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
import React from 'react';
import {useTranslation} from 'react-i18next';
import {Row, Col, Form, FloatingLabel} from 'react-bootstrap';
import Card from '../../components/card/Card';
import UitestScenarioCaptureContainer from '../../containers/uitest/UitestScenarioCaptureContainer';

const UitestScenario = ({
    scenarioData,
    setDetailScenarioName,
    onSaveScenario,
    onClickPlay,
    onClickLock,
    onClickManager
}) => {
    const [t] = useTranslation();
    const renderLockButton = () => {
        if (!scenarioData) {
            return '';
        }

        if (scenarioData.data.lock) {
            return (
                <a className="btn btn-app" onClick={() => onClickLock()}>
                    <i className="fas fa-lock" />
                    
                </a>
            );
        }
        return (
            <a className="btn btn-app" onClick={() => onClickLock()}>
                <i className="fas fa-unlock" />
                
            </a>
        );
    };
    return (
        <>
            <Card
                title={t('pages.scenario.scenarioInfo')}
                body={
                    <>
                        <Row>
                            <Col xs="8">
                                <FloatingLabel
                                    label={t('pages.scenario.scenarioName')}
                                    className="mb-2"
                                >
                                    <Form.Control
                                        type="text"
                                        name="scenarioName"
                                        maxLength="50"
                                        value={
                                            scenarioData?.data?.scenarioName ||
                                            ''
                                        }
                                        onChange={(e) =>
                                            setDetailScenarioName(
                                                e.target.value
                                            )
                                        }
                                    />
                                </FloatingLabel>
                            </Col>
                            <Col xs="4">
                                <FloatingLabel
                                    label={t('pages.scenario.manager')}
                                    className="mb-2"
                                    onClick={onClickManager}
                                >
                                    <Form.Control
                                        type="text"
                                        disabled
                                        // onClick={}

                                        value={
                                            scenarioData?.data.manager?.name ??
                                            ''
                                        }
                                    />
                                </FloatingLabel>
                            </Col>
                        </Row>

                        <Row>
                            <Col xs="12">
                                <a
                                    className="btn btn-app"
                                    onClick={(e) => onSaveScenario()}
                                    disabled={
                                        scenarioData && scenarioData.data.lock
                                    }
                                    style={{marginLeft: '0px'}}
                                >
                                    <i className="fas fa-save" />
                                    {t('common.button.save')}
                                </a>
                                <a
                                    className="btn btn-app"
                                    onClick={onClickPlay}
                                >
                                    <i className="fas fa-play" />
                                    {t('common.button.play')}
                                </a>
                                <UitestScenarioCaptureContainer />
                                {renderLockButton()}
                            </Col>
                        </Row>
                    </>
                }
            />
        </>
    );
};

export default UitestScenario;
