/* eslint-disable no-unused-vars */
import React, {useState} from 'react';
import Tab from 'react-bootstrap/Tab';
import Tabs from 'react-bootstrap/Tabs';
import {useTranslation} from 'react-i18next';
import {Form, FloatingLabel} from 'react-bootstrap';
import Card from '../../components/card/Card';
import UitestCaseListContainer from '../../containers/uitest/UitestCaseListContainer';
import UitestVariableContainer from '../../containers/uitest/UitestVariableContainer';

const UitestScenarioTab = ({scenarioData, saveDetailScenarioDesc}) => {
    const [t] = useTranslation();
    const [key, setKey] = useState('caseList');
    return (
        <>
            <Card
                title=" "
                imgIcon="menu2"
                body={
                    <>
                        <Tabs
                            activeKey={key}
                            onSelect={(k) => setKey(k)}
                            transition={false}
                            id="noanim-tab-example"
                        >
                            <Tab eventKey="deatailDesc" title=" ">
                                <br />
                                <FloatingLabel
                                    label="  "
                                    className="mb-2"
                                >
                                    <Form.Control
                                        as="textarea"
                                        style={{height: '433px'}}
                                        placeholder=""
                                        value={
                                            scenarioData?.data?.scenarioDesc ||
                                            ''
                                        }
                                        onChange={(e) =>
                                            saveDetailScenarioDesc(
                                                e.target.value
                                            )
                                        }
                                    />
                                </FloatingLabel>
                            </Tab>
                            <Tab eventKey="caseList" title="Case ">
                                <br />
                                <UitestCaseListContainer />
                            </Tab>

                            <Tab
                                eventKey="variableList"
                                title={t('pages.scenario.variableList')}
                            >
                                <br />
                                <UitestVariableContainer />
                            </Tab>
                        </Tabs>
                    </>
                }
            />
        </>
    );
};

export default UitestScenarioTab;
