/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react';
import {Modal, Form, Row, Col, FloatingLabel} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
import {FaRedoAlt, FaDownload} from 'react-icons/fa';
import Button from '../../components/button/Button';
import {
    takeSelectOptionbyServer,
    renderModalHeader
} from '../../lib/commonUiUtils';

const UitestScenarioCapture = ({
    modalShow,
    handleShow,
    handleClose,
    serverData,
    testUrl,
    setTestUrl,
    wsStartWebPage,
    wsQuitWebPage,
    wsStartRecord,
    wsStopRecord,
    wsInitRecord,
    wsDownloadDriver,
    client
}) => {
    const [t] = useTranslation();
    const renderUiServerSelectBox = () => {
        if (serverData != null) {
            return takeSelectOptionbyServer(serverData, 'UI');
            // return serverData.data.content
            //     .filter((obj) => obj.serverCode === 'ui')
            //     .map((obj) => (
            //         <option key={obj.url} value={obj.url} name={obj.url}>
            //             {obj.serverName}
            //         </option>
            //     ));
        }
        return '';
    };
    const onChangeTestUrl = (e) => {
        // eslint-disable-next-line no-unused-vars
        const url = serverData?.data?.filter(
            (obj) => obj.serverId === Number(e.target.value)
        )[0].url;

        setTestUrl(url);
    };

    const renderConnectState = () => {
        if (client.isConnect) {
            return <Form.Label>{t('pages.scenario.connected')}</Form.Label>;
        }

        // const downloadExcelFormFile = () => {
        //     window.open('/no-auth-api/v1/client-install/download/execute-file');
        // };
        return <Form.Label>aTworks Client </Form.Label>;
        // return <Button theme="link">{t('pages.scenario.notConnected')}</Button>;
    };
    const renderChromeVersion = () => {
        return <Form.Label>{client.chromeVersion}</Form.Label>;
    };

    const renderChromeDriver = () => {
        if (client.isConnect && !client.installDriver) {
            return <Form.Label></Form.Label>;
        }
        if (!client.isConnect) {
            return <Form.Label></Form.Label>;
        }
        return <Form.Label> </Form.Label>;
    };

    const renderStartBtn = () => {
        if (client.isConnect && !client.isOpenServer) {
            return (
                <>
                    <Button theme="primary" onClick={wsStartWebPage}>
                        {t('common.button.open')}
                    </Button>
                </>
            );
        }
        if (client.isConnect) {
            return (
                <>
                    <Button theme="danger" onClick={wsQuitWebPage}>
                        {t('common.button.forceQuit')}
                    </Button>
                </>
            );
        }
        return '';
    };

    const renderRecordStopBtn = () => {
        if (client.isRecording) {
            return (
                <>
                    <a className="btn btn-app" onClick={wsInitRecord}>
                        <i className="fas fa-video" />
                        {t('common.button.reset')}
                    </a>
                    <a className="btn btn-app" onClick={wsStopRecord}>
                        <i className="fas fa-stop" /> {t('common.button.stop')}
                    </a>
                </>
            );
        }
        return (
            <a className="btn btn-app" onClick={wsStartRecord}>
                <i className="fas fa-video" />
                {t('common.button.record')}
            </a>
        );
    };

    const renderTestUrl = () => {
        if (client.isOpenServer) {
            return t('common.alert.serverOpen');
        }

        return (
            <Row>
                <Col xs="5">
                    <FloatingLabel label="Test UI Server" className="mb-2">
                        <Form.Select onChange={onChangeTestUrl}>
                            <option value="">
                                {t('common.text.optionNone')}
                            </option>
                            {renderUiServerSelectBox()}
                        </Form.Select>
                    </FloatingLabel>
                </Col>
                <Col xs="7">
                    <FloatingLabel label="URL" className="mb-2">
                        <Form.Control
                            type="text"
                            placeholder=""
                            disabled
                            value={testUrl}
                            onChange={onChangeTestUrl}
                        />
                    </FloatingLabel>
                </Col>
            </Row>
        );
    };

    const iframePart = () => {
        return {
            __html: '<iframe></iframe>'
        };
    };

    return (
        <>
            <a className="btn btn-app" onClick={handleShow}>
                <i className="fab fa-edge" />
                {t('common.button.open')}
            </a>
            {renderRecordStopBtn()}
            <Modal
                show={modalShow}
                onHide={handleClose}
                size="md"
                aria-labelledby="contained-modal-title-vcenter"
                onExited={() => setTestUrl('')}
                centered
            >
                <div
                    style={{
                        margin: 'auto',
                        position: 'relative',
                        width: '0px',
                        height: '0px',
                        overflow: 'hidden'
                    }}
                    // eslint-disable-next-line react/no-danger
                    dangerouslySetInnerHTML={iframePart()}
                />
                <Form onSubmit={wsStartWebPage}>
                    {renderModalHeader('UI Server ')}

                    <Modal.Body>
                        <Row>
                            <Col xs="4">
                                <Form.Group>
                                    <Form.Label>
                                        {t('pages.scenario.clientConnectState')}
                                    </Form.Label>
                                </Form.Group>
                            </Col>
                            <Col xs="8">
                                <Row>
                                    <Col xs="10">
                                        <Form.Group>
                                            {renderConnectState()}
                                        </Form.Group>
                                    </Col>
                                    <Col xs="2">
                                        <Button
                                            theme="link"
                                            onClick={() => handleShow()}
                                        >
                                            <FaRedoAlt />
                                        </Button>
                                    </Col>
                                </Row>
                            </Col>
                            <Col xs="4">
                                <Form.Group>
                                    <Form.Label>
                                        {t('pages.scenario.chromeVersion')}
                                    </Form.Label>
                                </Form.Group>
                            </Col>
                            <Col xs="8">
                                <Form.Group>
                                    <Row>
                                        <Col xs="10">
                                            {renderChromeVersion()}
                                        </Col>
                                    </Row>
                                </Form.Group>
                            </Col>
                            <Col xs="4">
                                <Form.Group>
                                    <Form.Label> </Form.Label>
                                </Form.Group>
                            </Col>
                            <Col xs="8">
                                <Form.Group>
                                    <Row>
                                        <Col xs="10">
                                            {renderChromeDriver()}
                                        </Col>
                                        <Col xs="2">
                                            <Button
                                                theme="link"
                                                onClick={() =>
                                                    wsDownloadDriver()
                                                }
                                            >
                                                <FaDownload />
                                            </Button>
                                        </Col>
                                    </Row>
                                </Form.Group>
                            </Col>
                            <Col xs="12">{renderTestUrl()}</Col>
                        </Row>
                    </Modal.Body>
                    <Modal.Footer>
                        {renderStartBtn()}
                        <Button theme="default" onClick={handleClose}>
                            {t('common.button.close')}
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>
        </>
    );
};

export default UitestScenarioCapture;
