/* eslint-disable no-unused-vars */
import React from 'react';
import Modal from 'react-bootstrap/Modal';

import {useTranslation} from 'react-i18next';
import {Nav, Accordion, Row, Col} from 'react-bootstrap';
import UitestStepPopXhrContainer from '../../containers/uitest/steps/UitestStepPopXhrContainer';
import UitestStepPopQueryContainer from '../../containers/uitest/steps/UitestStepPopQueryContainer';
import UitestStepPopScriptContainer from '../../containers/uitest/steps/UitestStepPopScriptContainer';
import UitestStepPopReadyContainer from '../../containers/uitest/steps/UitestStepPopReadyContainer';
import UitestStepPopLoginContainer from '../../containers/uitest/steps/UitestStepPopLoginContainer';

import {renderModalHeader} from '../../lib/commonUiUtils';

const UitestStepUpdate = ({modalShow, handleClose, stepUpdateInitData}) => {
    const [t] = useTranslation();
    const getContext = () => {
        const modalClose = () => {
            handleClose();
        };
        if (stepUpdateInitData.stepType === 'API') {
            return <UitestStepPopXhrContainer handleClose={modalClose} />;
        }
        if (stepUpdateInitData.stepType === 'QUERY') {
            return <UitestStepPopQueryContainer handleClose={modalClose} />;
        }
        if (stepUpdateInitData.stepType === 'SCRIPT') {
            return <UitestStepPopScriptContainer handleClose={modalClose} />;
        }
        if (stepUpdateInitData.stepType === 'LOGIN') {
            return <UitestStepPopLoginContainer handleClose={modalClose} />;
        }
        return <UitestStepPopReadyContainer />;
    };

    return (
        <>
            <Modal
                show={modalShow}
                onHide={handleClose}
                dialogClassName="modal-70w"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                {renderModalHeader(t('pages.scenario.updateStep'))}
                <Modal.Body style={{minHeight: '800px'}}>
                    <Row>
                        <Col xs="2">
                            <Accordion defaultActiveKey="0">
                                <Accordion.Item
                                    eventKey="0"
                                    style={{minHeight: '800px'}}
                                >
                                    <Accordion.Header className="accordion-header5">
                                        Step 
                                    </Accordion.Header>
                                    <Accordion.Body>
                                        <Nav
                                            defaultActiveKey={
                                                stepUpdateInitData.stepType
                                            }
                                            className="flex-column nav-pills"
                                        >
                                            <Nav.Item>Network</Nav.Item>
                                            <Nav.Link
                                                disabled
                                                eventKey="LOGIN"
                                                style={{margin: '8px'}}
                                            >
                                                Login
                                            </Nav.Link>
                                            <Nav.Link
                                                disabled
                                                eventKey="API"
                                                style={{margin: '8px'}}
                                            >
                                                XHR
                                            </Nav.Link>
                                            <Nav.Item>DB</Nav.Item>
                                            <Nav.Link
                                                disabled
                                                eventKey="QUERY"
                                                style={{margin: '8px'}}
                                            >
                                                Query
                                            </Nav.Link>
                                            <Nav.Item>Script</Nav.Item>
                                            <Nav.Link
                                                disabled
                                                eventKey="SCRIPT"
                                                style={{margin: '8px'}}
                                            >
                                                Script
                                            </Nav.Link>
                                        </Nav>
                                    </Accordion.Body>
                                </Accordion.Item>
                            </Accordion>
                        </Col>
                        <Col xs="10">
                            {getContext()}
                            {/* <UitestStepPopXhrContainer /> */}
                            {/* <input
                                    type="text"
                                    className="form-control"
                                    placeholder="undefined"
                                    name="scenarioName"
                                    // value={scenarioName}
                                    // onChange={onChangeScenarioName}
                                /> */}
                        </Col>
                    </Row>
                </Modal.Body>
                {/* <Modal.Footer>
                        <Button
                            theme="primary"
                            type="submit"
                            // isLoading={createLoading}
                        >
                            Register
                        </Button>
                        <Button theme="default" onClick={handleClose}>
                            Close
                        </Button>
                    </Modal.Footer> */}
            </Modal>
        </>
    );
};

export default UitestStepUpdate;
