/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import {useHistory} from 'react-router-dom';
import {useSelector, useDispatch} from 'react-redux';
import ScheduleSearch from '@pages/schedule/ScheduleSearch';
import {toast} from 'react-toastify';

import {
    setSearchScheduleForm,
    searchSchedule,
    searchScheduleFormInit,
    clearSaveSchedule
} from '../../store/schedule';

import Spinner from '../../components/spinner/Spinner';
import {getErrorMsg} from '../../lib/commonUiUtils';

/*
 *    container
 */
function ScheduleSearchContainer() {
    const dispatch = useDispatch();
    const history = useHistory();

    const {searchScheduleForm} = useSelector((state) => state.schedule);

    const {data: codeData} = useSelector((state) => state.common.searchCode);

    const {loading: searchLoading, error: searchError} = useSelector(
        (state) => state.schedule.searchScheduleRes
    );
    useEffect(() => {
        if (!searchLoading && searchError) {
            toast.error(getErrorMsg(searchError, 'search'));
            dispatch(clearSaveSchedule());
        }
    }, [searchError]);

    const onChangeForm = (e) => {
        const searchScheduleFormT = {...searchScheduleForm};
        searchScheduleFormT[e.target.id] = e.target.value;
        dispatch(setSearchScheduleForm(searchScheduleFormT));
    };

    const onSearch = (_searchScheduleForm = searchScheduleForm) => {
        dispatch(searchSchedule(_searchScheduleForm));
    };
    const {data: memberData} = useSelector(
        (state) => state.common.searchMember
    );

    //    
    // const {data: memberInfoData} = useSelector(
    //     (state) => state.auth.memberInfo
    // );

    useEffect(() => {
        if (history.action === 'PUSH') {
            const tempSearchFormS = searchScheduleFormInit();

            dispatch(setSearchScheduleForm(tempSearchFormS));
            onSearch(tempSearchFormS);
        }
        if (history.action === 'POP') {
            onSearch();
        }
    }, []);

    return (
        <>
            <ScheduleSearch
                searchFormS={searchScheduleForm}
                onChangeForm={onChangeForm}
                onClickSearch={onSearch}
                codeData={codeData}
                memberData={memberData}
            />
            <Spinner isLoading={searchLoading} />
        </>
    );
}

export default ScheduleSearchContainer;
