/* eslint-disable no-alert */
import React from 'react';
import {
    Form,
    Row,
    Col,
    InputGroup,
    FormControl,
    FloatingLabel,
    Accordion
} from 'react-bootstrap';
import Modal from 'react-bootstrap/Modal';
import {useFormik} from 'formik';
import * as Yup from 'yup';
import {useTranslation} from 'react-i18next';
import {FaSearch} from 'react-icons/fa';
import Button from '../../../components/button/Button';
import {
    printFormError,
    takeSelectOptionbyCode
} from '../../../lib/commonUiUtils';
import {renderOptionsApServer} from '../../../lib/codeUtils';

import ServerUpdateContainer from '../../../containers/server/ServerUpdateContainer';

function BulkUpdate({
    modalShow,
    handleClose,
    initData,
    setInitData,
    serverData,
    codeData,
    getBulkSnapshotName,
    onExecute,
    onClickServerInfo
}) {
    const [t] = useTranslation();

    const [compareValue, setCompareValue] = React.useState(false);

    const formik = useFormik({
        enableReinitialize: true,
        initialValues: initData,
        validationSchema: Yup.object({
            // server1Id: Yup.string().required(getSelectValidMsg('')),
            // server2Id: Yup.string().when(['singleOrDual'], {
            //     is: (singleOrDual) => singleOrDual === 'DUAL',
            //     then: Yup.string().required(getSelectValidMsg(''))
            // })
        }),
        onSubmit: (values) => {
            const tempValue = {...values};
            tempValue.bulkOptions.compareValue = compareValue;
            onExecute(tempValue);
        }
    });

    /*
     *  /   
     */
    const onChangeForm = (e) => {
        const initDataT = {...initData};
        initDataT[e.target.id] = e.target.value;

        if (e.target.id === 'testType') {
            initDataT.bulkSnapshotName = getBulkSnapshotName(e.target.value);
            initDataT.predictCount = 0;

            setCompareValue(false);
            // formik.resetForm();
        }
        setInitData(initDataT);
        //
    };

    /*
     * render ->  
     */
    const renderReserveDate = () => {
        if (formik.values?.bulkOptions?.executionPoint === 'RESERVATION') {
            return (
                <>
                    <Col xs="5">
                        <FloatingLabel label=" " className="mb-2">
                            <Form.Control
                                type="datetime-local"
                                {...formik.getFieldProps(
                                    'bulkOptions.reserveDateTime'
                                )}
                            />
                        </FloatingLabel>
                    </Col>
                </>
            );
        }
        return (
            <>
                <Col xs="5" />
            </>
        );
    };

    /*
     * render ->  
     */
    const renderServerIds = () => {
        // @@  
        //    
        // ,   JSON  .
        //    JSON, XML   
        const serverSelected = (serverkey, tokenInfo, title) => {
            return (
                <>
                    <InputGroup className="mb-2">
                        <FloatingLabel label={title}>
                            <Form.Select {...formik.getFieldProps(serverkey)}>
                                <option value="">
                                    {t('common.text.optionNone')}
                                </option>
                                {renderOptionsApServer(serverData)}
                            </Form.Select>
                        </FloatingLabel>

                        <Button
                            type="button"
                            style={{maxWidth: '50px'}}
                            className="btn btn-block btn-secondary btn-search"
                            onClick={() =>
                                onClickServerInfo(formik.values[serverkey])
                            }
                        >
                            <FaSearch />
                        </Button>
                    </InputGroup>
                    {printFormError(formik, serverkey)}
                </>
            );
        };
        if (formik.values?.bulkOptions?.singleOrDual === 'DUAL') {
            //  id="server1Id",  id="server2Id"
            return (
                <>
                    <Col xs="9">
                        <Row>
                            <Col xs="6">
                                {serverSelected(
                                    'server1Id',
                                    'tokenInfo1',
                                    'Server A'
                                )}
                            </Col>
                            <Col xs="6">
                                {serverSelected(
                                    'server2Id',
                                    'tokenInfo2',
                                    'Server B'
                                )}
                            </Col>
                        </Row>
                    </Col>
                </>
            );
        }
        return (
            <>
                <Col xs="9">
                    <Row>
                        <Col xs="6">
                            {serverSelected(
                                'server1Id',
                                'tokenInfo1',
                                'Server A'
                            )}
                        </Col>
                    </Row>
                </Col>
            </>
        );
    };

    const renderCompareValue = () => {
        if (formik.values?.bulkOptions?.singleOrDual === 'DUAL') {
            return (
                <>
                    <Form xs="3" onClick={() => setCompareValue(!compareValue)}>
                        <Form.Check
                            type="checkbox"
                            label="  "
                            checked={compareValue}
                            onChange={(e) => e.preventDefault()}
                        />
                    </Form>
                </>
            );
        }
        return '';
    };

    return (
        <>
            <Modal
                show={modalShow}
                onHide={handleClose}
                size="lg"
                onExited={() => formik.resetForm()}
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Form onSubmit={formik.handleSubmit}>
                    {/* {renderModalHeader(renderTitleName())} */}
                    <Modal.Body>
                        <Accordion defaultActiveKey="0">
                            <Accordion.Item eventKey="0">
                                <Accordion.Header> </Accordion.Header>
                                <Accordion.Body>
                                    <Row>
                                        <Col xs="8">
                                            <FloatingLabel
                                                label=" "
                                                className="mb-2"
                                            >
                                                <FormControl
                                                    id="bulkSnapshotName"
                                                    type="text"
                                                    maxLength="100"
                                                    onChange={onChangeForm}
                                                    value={
                                                        initData?.bulkSnapshotName
                                                    }
                                                />
                                            </FloatingLabel>
                                        </Col>
                                        <Col xs="4">
                                            <FloatingLabel
                                                label=""
                                                className="mb-2"
                                            >
                                                <FormControl
                                                    id="predictCount"
                                                    type="text"
                                                    maxLength="100"
                                                    onChange={onChangeForm}
                                                    value={
                                                        initData?.predictCount
                                                    }
                                                />
                                            </FloatingLabel>
                                        </Col>
                                    </Row>
                                </Accordion.Body>
                            </Accordion.Item>
                        </Accordion>
                        <br />
                        <Accordion defaultActiveKey="0">
                            <Accordion.Item eventKey="0">
                                <Accordion.Header className="accordion-header3">
                                      
                                </Accordion.Header>
                                <Accordion.Body>
                                    <Row>
                                        <Col xs="3">
                                            <FloatingLabel
                                                className="mb-2"
                                                label={t('pages.mass.testType')}
                                            >
                                                <Form.Select
                                                    {...formik.getFieldProps(
                                                        'bulkOptions.singleOrDual'
                                                    )}
                                                >
                                                    {takeSelectOptionbyCode(
                                                        codeData,
                                                        'SingleOrDual'
                                                    )}
                                                </Form.Select>
                                                {printFormError(
                                                    formik,
                                                    'bulkOptions.singleOrDual'
                                                )}
                                            </FloatingLabel>
                                            {renderCompareValue()}
                                        </Col>
                                        {renderServerIds()}
                                    </Row>
                                    <Row style={{paddingTop: '10px'}}>
                                        <Col xs="3">
                                            <FloatingLabel
                                                label={t(
                                                    'pages.mass.executionMethod'
                                                )}
                                                className="mb-3"
                                            >
                                                <Form.Select
                                                    {...formik.getFieldProps(
                                                        'bulkOptions.executionPoint'
                                                    )}
                                                >
                                                    {takeSelectOptionbyCode(
                                                        codeData,
                                                        'ExecutionPoint'
                                                    )}
                                                </Form.Select>
                                                {printFormError(
                                                    formik,
                                                    'bulkOptions.executionPoint'
                                                )}
                                            </FloatingLabel>
                                        </Col>
                                        {/*   reserveDate  . */}
                                        {renderReserveDate()}
                                    </Row>
                                </Accordion.Body>
                            </Accordion.Item>
                        </Accordion>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button
                            theme="primary"
                            type="submit"
                            // isLoading={createLoading}
                        >
                            {t('common.button.register')}
                        </Button>
                        <Button theme="default" onClick={handleClose}>
                            {t('common.button.close')}
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>
            <ServerUpdateContainer />
        </>
    );
}

export default BulkUpdate;
