import React from 'react';
import {FaSearch, FaTrash} from 'react-icons/fa';
import {useTranslation} from 'react-i18next';
import Table from '../../components/table/Table';
import Button from '../../components/button/Button';
import {renderClubType} from '../../lib/buttonRender';

const ScheduleTable = ({data, movePage, onClickDetail, onClickDelete}) => {
    const [t] = useTranslation();

    const tableName = t('pages.scenario.scHistoryList');
    const rowKey = 'scheduleId';

    const columns = [
        {
            title: ' ',
            key: 'clubType',
            width: '8%',
            render: (id, row) => {
                return renderClubType(row.clubType);
            }
        },
        {
            title: ' ',
            key: 'clubName',
            width: '35%',
            align: 'left' // left, center, right (default:center)
        },
        {
            title: t('pages.scenario.scheduleType'),
            key: 'scheduleType',
            align: 'center',
            render: (id, row) => {
                return row.scheduleType?.value;
            }
        },
        {
            title: t('common.text.targetServer'),
            key: 'wasServerName',
            align: 'center' // left, center, right (default:center)
        },
        {
            title: 'Database',
            key: 'dbServerName',
            align: 'center' // left, center, right (default:center)
        },

        {
            title: t('pages.scenario.selectedOption'),
            key: 'selectedOption',
            align: 'center',
            render: (id, row) => {
                const {scheduleType} = row.scheduleCond;
                if (scheduleType === 'SELECTED_DATE') {
                    return row.scheduleCond?.scheduleDate;
                }
                if (scheduleType === 'SELECTED_DAY') {
                    return row.scheduleCond?.scheduleDay;
                }
                return '-';
            }
        },
        {
            title: t('pages.scenario.selectedTime'),
            key: 'selectedTime',
            align: 'center',
            render: (id, row) => {
                return row.scheduleCond?.scheduleTime;
            }
        },

        {
            title: t('pages.scenario.lastExecuteDate'),
            key: 'lastExecuteDate',
            align: 'center'
        },
        {
            title: '',
            key: 'button',
            align: 'right',
            render: (id, row) => {
                return (
                    <>
                        <Button
                            theme="outline-success"
                            onClick={() => onClickDetail(row)}
                        >
                            <FaSearch />
                        </Button>
                        <Button
                            theme="outline-danger"
                            onClick={() => onClickDelete(row)}
                        >
                            <FaTrash />
                        </Button>
                    </>
                );
            }
        }
    ];

    const getDisplayData = () => {
        const tempList = [];

        if (data !== null) {
            data.data.content.forEach((obj) => {
                const tempObj = {...obj};
                // "scheduleType":"selectedDate",
                if (tempObj.scheduleType === 'selectedDate') {
                    tempObj.selectedOption = tempObj.selectedDate;
                } else if (tempObj.scheduleType === 'selectedDay') {
                    // tempObj.selectedOption = tempObj.selectedDayStr;
                    // Every
                    tempObj.selectedOption =
                        t('common.week.every') +
                        t(`common.week.${tempObj.selectedDayStr}`);
                } else {
                    tempObj.selectedOption = t(
                        `common.week.${tempObj.scheduleType}`
                    );
                }

                tempList.push(tempObj);
            });
        }
        const result = {data: {content: tempList}};
        return result;
    };
    return (
        <>
            <Table
                tableName={tableName}
                movePage={movePage}
                rowKey={rowKey}
                columns={columns}
                data={getDisplayData()}
                onDoubleClick={(id, row) => onClickDetail(row)}
            />
        </>
    );
};

export default ScheduleTable;
