/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import ClubDetailInfo from '@pages/club/ClubDetailInfo';
import {useDispatch, useSelector} from 'react-redux';
import {toast} from 'react-toastify';
import {useTranslation} from 'react-i18next';
import {useLocation} from 'react-router-dom';
import Spinner from '../../components/spinner/Spinner';
import {getErrorMsg} from '../../lib/commonUiUtils';
import {
    searchClubDetail,
    updateClub,
    clearUpdateClub,
    usedClubExecuteModal,
    usedClubLockModal
} from '../../store/club';

import {usedSchedualSaveModal} from '../../store/schedule';

/*
 *  Container
 */
function ClubDetailInfoContainer({refreshData}) {
    const dispatch = useDispatch();
    const location = useLocation();
    const [t] = useTranslation();

    const [infoData, setInfoData] = React.useState({
        clubId: -1,
        clubName: '',
        clubType: {},
        isLock: false
    });

    const onSearchDetail = (clubId) => {
        dispatch(searchClubDetail(clubId));
    };

    const {data: codeData} = useSelector((state) => state.common.searchCode);

    const onClickSave = (e) => {
        // infoData
        dispatch(updateClub(infoData));
    };

    const onClickPlay = (e) => {
        const data = {
            parent: 'club',
            clubId: location.state.clubId
        };
        dispatch(usedClubExecuteModal(true, data));
    };

    /*
     *    
     */
    const onClickSchedual = (e) => {
        e.preventDefault();

        const data = {
            id: -1,
            scheduleType: '',
            clubId: infoData.clubId,
            scenarioId: -1,
            wasServerId: -1,
            dbServerId: -1,
            selectedDate: '',
            selectedTime: '',
            selectedDay: '1'
        };

        dispatch(usedSchedualSaveModal(true, data));
    };

    const onClickRefresh = (e) => {
        refreshData();
    };

    const onClickLock = (e) => {
        const data = {
            clubId: infoData.clubId,
            isLock: infoData.isLock,
            parent: 'detail'
        };
        dispatch(usedClubLockModal(true, data));
    };

    /**
     * Club Search detail callback
     */
    const {
        loading: searchClubDetailLoading,
        data: searchClubDetailData,
        error: searchClubDetailError
    } = useSelector((state) => state.club.searchClubDetailRes);

    useEffect(() => {
        if (!searchClubDetailLoading && searchClubDetailData) {
            // onSearch();
            setInfoData(searchClubDetailData.data);
        }
    }, [searchClubDetailData, searchClubDetailError]);

    /**
     * Club update detail callback
     */
    const {
        loading: updateClubLoding,
        data: updateClubData,
        error: updateClubError
    } = useSelector((state) => state.club.updateClubRes);

    useEffect(() => {
        if (!updateClubLoding && updateClubData) {
            // dispatch(clearCreateClub());
            dispatch(clearUpdateClub());
            toast.success(t('common.alert.updateSuccess'));
        }
        if (!updateClubLoding && updateClubError) {
            dispatch(clearUpdateClub());
            toast.error(getErrorMsg(updateClubError, 'save'));
        }
    }, [updateClubData, updateClubError]);

    return (
        <>
            <ClubDetailInfo
                infoData={infoData}
                setInfoData={setInfoData}
                codeData={codeData}
                onClickSave={onClickSave}
                onClickPlay={onClickPlay}
                onClickSchedual={onClickSchedual}
                onClickRefresh={onClickRefresh}
                onClickLock={onClickLock}
            />
            <Spinner isLoading={searchClubDetailLoading || updateClubLoding} />
        </>
    );
}

export default ClubDetailInfoContainer;
