import React from 'react';
import {FaMinus, FaSearch} from 'react-icons/fa';
import {Container, Row, Col, Form, InputGroup} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
import Button from '../../components/button/Button';
import Table from '../../components/table/Table';
import {renderBatchStatus} from '../../lib/buttonRender';
import {changeToDate} from '../../lib/moment';

/**
 * Scnarion Test Club
 *  Container  .
 * @since 2022-03-29
 * @returns
 */
const ClubDetailHistory = ({
    searchClubHistoryData,
    movePage,
    onClickMoveScHistory
}) => {
    const [t] = useTranslation();

    const onCheckEnter = (e) => {
        if (e.key === 'Enter') {
            e.preventDefault();
            // onClickSearch();
        }
    };

    const columns = [
        {
            title: ' ',
            key: 'batchStatus',
            width: '10%',
            render: (id, row) => {
                return renderBatchStatus(row.batchInfo?.batchStatus);
            }
        },
        {
            title: '',
            key: 'startDateTime',
            render: (id, row) => {
                return changeToDate(row.batchInfo?.startedDateTime);
            }
        },
        {
            title: 'http ',
            key: 'wasServerName',
            width: '15%'
        },
        {
            title: 'database ',
            key: 'dbServerName',
            width: '15%'
        },
        {
            title: '',
            key: 'button',
            align: 'right',
            // eslint-disable-next-line no-unused-vars
            render: (id, row, column) => {
                return (
                    <>
                        <Button
                            theme="outline-success"
                            onClick={() => onClickMoveScHistory(id)}
                        >
                            <FaSearch />
                        </Button>
                    </>
                );
            }
        }
    ];

    return (
        <>
            <Table
                movePage={movePage}
                tableName=" "
                rowKey="clubSnapshotId"
                columns={columns}
                data={searchClubHistoryData}
            />
        </>
    );
};
export default ClubDetailHistory;
