/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import DashboardFailCnt from '@pages/dashboard/DashboardFailCnt';

import {useSelector, useDispatch} from 'react-redux';
import {searchFailRate} from '../../store/dashboard';

function DashboardFailCntContainer() {
    const dispatch = useDispatch();

    const [selectedServer, setSelectedServer] = React.useState('');

    const offset = new Date().getTimezoneOffset() * 60000;
    const today = new Date(Date.now() - offset)
        .toISOString()
        .slice(0, -1)
        .slice(0, 10);

    //  
    const {data: serverData} = useSelector(
        (state) => state.common.searchServer
    );

    const {data: searchStatData} = useSelector(
        (state) => state.dashboard.searchFailRate
    );

    const onChangeServerId = (e) => {
        e.preventDefault();
        setSelectedServer(e.target.value);
        dispatch(searchFailRate(today, e.target.value));
    };

    useEffect(() => {
        if (serverData) {
            const wasList = serverData?.data?.filter(
                (obj) => obj.serverType === 'HTTP'
            );

            if (wasList.length > 0) {
                setSelectedServer(wasList[0].serverId);
                dispatch(searchFailRate(today, wasList[0].serverId));
            }
        } else {
            dispatch(searchFailRate(today, -1));
        }
        return () => {};
    }, []);

    // serverData.data.content
    //
    //         .map((obj) => <option value={obj.id}>{obj.serverName}</option>);
    return (
        <>
            <DashboardFailCnt
                serverData={serverData}
                selectedServer={selectedServer}
                searchStatData={searchStatData}
                onChangeServerId={onChangeServerId}
            />
        </>
    );
    ///
}

export default DashboardFailCntContainer;
