/* eslint-disable no-unused-vars */
import React, {useState, useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import ClubLock from '@pages/club/popup/ClubLock';
import {useTranslation} from 'react-i18next';
import {toast} from 'react-toastify';
import {
    clearClubLockModal,
    saveClubLock,
    clearSaveClubLock,
    searchClubDetail
} from '../../../store/club';
import {getErrorMsg} from '../../../lib/commonUiUtils';

/*
 * 2022-03-17 kimtaehan 
 *  / 
 */
function ClubLockContainer({onSearch}) {
    const dispatch = useDispatch();
    const [t] = useTranslation();

    const {clubLockInitData} = useSelector((state) => state.club);

    // popup show/hide
    const [modalShow, setModalShow] = React.useState(false);

    const handleClose = () => {
        setModalShow(false);
    };
    const handleShow = () => setModalShow(true);

    const [openData, setOpenData] = useState({
        clubId: -1,
        isLock: false,
        parent: ''
    });

    //    
    const {data: memberInfoData} = useSelector(
        (state) => state.auth.memberInfo
    );

    const onclickLock = (password) => {
        dispatch(
            saveClubLock({
                clubId: openData.clubId,
                isLock: openData.isLock,
                lockPassword: password
            })
        );
    };

    const {
        loading: updateLoading,
        data: updateResponse,
        error: updateError
    } = useSelector((state) => state.club.saveClubLockRes);

    /*
     *  Open event
     */
    useEffect(() => {
        if (clubLockInitData.showModal && !modalShow) {
            setOpenData({...clubLockInitData.initData});
            dispatch(clearClubLockModal());
            handleShow();
        }
    }, [clubLockInitData.showModal]);

    /**
     * lock/unlock server callback
     */
    useEffect(() => {
        if (!updateLoading && updateResponse) {
            // dispatch(searchServer(serverName, serverType));

            // // 20220109 kimtaehan   
            toast.success(t('common.alert.updateSuccess'));

            if (openData.parent === 'detail') {
                dispatch(searchClubDetail(openData.clubId));
            } else if (openData.parent === 'club') {
                onSearch();
            }
            handleClose();
        }

        // fail
        if (!updateLoading && updateError) {
            toast.error(getErrorMsg(updateError, 'save'));
        }
        dispatch(clearSaveClubLock());
    }, [updateResponse, updateError]);

    return (
        <>
            <ClubLock
                modalShow={modalShow}
                handleClose={handleClose}
                openData={openData}
                onclickLock={onclickLock}
                updateLoading={updateLoading}
            />
        </>
    );
}

export default ClubLockContainer;
