/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import {useHistory, useLocation} from 'react-router-dom';
import {useSelector, useDispatch} from 'react-redux';
import LogSearch from '@pages/log/LogSearch';

import {toast} from 'react-toastify';
import {getErrorMsg} from '../../lib/commonUiUtils';
import Spinner from '../../components/spinner/Spinner';
import {
    searchLogSnapshot,
    setSearchLogSnapshotForm,
    usedLogAnalysisModal
} from '../../store/log';

/*
 *   Container
 */
function LogSearchContainer() {
    const dispatch = useDispatch();
    const history = useHistory();

    const {searchLogSnapshotForm} = useSelector((state) => state.log);

    const onSearch = (_searchLogSnapshotForm = searchLogSnapshotForm) => {
        dispatch(searchLogSnapshot(_searchLogSnapshotForm));
    };
    /**
     *    
     */
    const addNewLog = () => {
        //         .
        dispatch(usedLogAnalysisModal(true));
    };

    //   
    const {data: codeData} = useSelector((state) => state.common.searchCode);

    // Log snapshot list 
    const {
        data: searchLogSnapshotResData,
        loading: searchLogSnapshotResLoading,
        error: searchLogSnapshotResError
    } = useSelector((state) => state.log.searchLogSnapshotRes);

    useEffect(() => {
        // Log snapshot delete error
        if (!searchLogSnapshotResLoading && searchLogSnapshotResError) {
            toast.error(getErrorMsg(searchLogSnapshotResError, 'select'));
            // dispatch(clearDeleteLogSnapshot());
        }
    }, [searchLogSnapshotResError]);

    const onChangeEvent = (_searchLogSnapshotForm) => {
        dispatch(setSearchLogSnapshotForm({..._searchLogSnapshotForm}));
    };

    useEffect(() => {
        if (history.action === 'PUSH') {
            onSearch();
        }
        if (history.action === 'POP') {
            onSearch();
        }
    }, []);

    return (
        <>
            <LogSearch
                onSearch={onSearch}
                codeData={codeData}
                searchLogSnapshotForm={searchLogSnapshotForm}
                onChangeEvent={onChangeEvent}
                addNewLog={addNewLog}
            />
            <Spinner isLoading={searchLogSnapshotResLoading} />
        </>
    );
    ///
}

export default LogSearchContainer;
