/* eslint-disable no-alert */
import React from 'react';
import {FaTrash, FaCheck} from 'react-icons/fa';
import {useTranslation} from 'react-i18next';
import Table from '../../components/table/Table';
import Button from '../../components/button/Button';

const CredentialInfoTable = ({data, onUpdate, onDelete, movePage}) => {
    const [t] = useTranslation();
    const tableName = 'Credential Info List';
    const rowKey = 'credentialId';
    const onClickDelete = (row) => {
        if (window.confirm(t('common.alert.checkDelete'))) {
            onDelete(row);
        }
    };
    const onDoubleClick = (id, row) => {
        if (window.confirm(t('common.alert.checkDefault'))) {
            onUpdate({
                ...row
            });
        }
    };
    const columns = [
        {
            title: 'Default',
            key: 'isDefault',
            width: '8%',
            render: (id, row) => {
                return <>{row.isDefault && <FaCheck />}</>;
            }
        },
        {
            title: '',
            key: 'credentialName',
            width: '30%',
            align: 'left' // left, center, right (default:center)
        },

        {
            title: t('pages.server.connectId'),
            key: 'userId',
            align: 'left',
            render: (id, row) => {
                return row.connectionInfo?.connectedId;
            }
        },
        {
            title: t('pages.server.connectPw'),
            key: 'password',
            align: 'left',
            render: (id, row) => {
                return row.connectionInfo?.connectedPw;
            }
        },
        {
            title: '',
            key: 'button',
            width: '10%',
            // eslint-disable-next-line no-unused-vars
            render: (id, row, column) => {
                return (
                    <>
                        <Button
                            theme="link"
                            disabled={row.isDefault}
                            onClick={() => onDoubleClick(id, row)}
                        >
                            <FaCheck />
                        </Button>
                        <Button theme="link" onClick={() => onClickDelete(row)}>
                            <FaTrash />
                        </Button>
                    </>
                );
            }
        }
    ];

    return (
        <>
            <Table
                tableName={tableName}
                onDoubleClick={onDoubleClick}
                movePage={movePage}
                rowKey={rowKey}
                columns={columns}
                data={data}
                isCard="N"
            />
            {/* <p>{t('pages.server.comment3')}</p> */}
        </>
    );
};
export default CredentialInfoTable;
