import React, {useEffect} from 'react';
import {useHistory} from 'react-router-dom';
import {useSelector, useDispatch} from 'react-redux';
import {toast} from 'react-toastify';
import BulkSearch from '@app/pages/bulk/BulkSearch';
import {getErrorMsg} from '../../lib/commonUiUtils';
import {
    searchBulkList, // bulk List select
    clearSearchBulkList,
    setBulkListForm, // bulk List select 
    getBulkListSearchFormInitialState
} from '../../store/bulk';
import Spinner from '../../components/spinner/Spinner';

/*
 * BulkTestSearchContainer
 *   ->  search
 */
function BulkSearchContainer() {
    const dispatch = useDispatch();
    const history = useHistory();

    const {loading: searchLoading, error: searchError} = useSelector(
        (state) => state.bulk.searchBulkListRes
    );

    /*
     *     
     */
    const {bulkListSearchForm} = useSelector((state) => state.bulk);
    const onChangeSearchForm = (searchForm) => {
        dispatch(setBulkListForm({...searchForm}));
    };

    /*
     *   
     */
    const onSearch = (_bulkListSearchForm = {...bulkListSearchForm}) => {
        dispatch(searchBulkList(_bulkListSearchForm));
    };

    /*
     *     
     */
    useEffect(() => {
        // fail
        if (!searchLoading && searchError) {
            toast.error(getErrorMsg(searchError, 'search'));
            dispatch(clearSearchBulkList());
        }
    }, [searchError]);

    /*
     *   Loding 
     */
    useEffect(() => {
        if (history.action === 'PUSH') {
            const initSearchForm = getBulkListSearchFormInitialState();
            onChangeSearchForm(initSearchForm);
            onSearch(initSearchForm);
        }
        if (history.action === 'POP') {
            onSearch();
        }
    }, []);

    /*
     *  
     */
    return (
        <>
            <BulkSearch
                searchForm={bulkListSearchForm}
                onChangeSearchForm={onChangeSearchForm}
                onSearch={onSearch}
            />
            <Spinner isLoading={searchLoading} />
        </>
    );
}

export default BulkSearchContainer;
