/* eslint-disable indent */
import {takeEvery, getContext} from 'redux-saga/effects';
import * as authAPI from '../api/auth'; // api/auth    
import {
    reducerUtils,
    handleAsyncActions,
    createPromiseSaga
} from '../lib/asyncUtils';

const PREFIX = 'AUTH';

/*   */

/**
 * (TR) Login 
 * processLogin (PROCESS_LOGIN) : CALL
 * processLoginDataClear (PROCESS_LOGIN_DATA_CLEAR) : CLEAR
 * settingLoginInfo (LOGIN_USER) : CONDITION CHANGE
 * processLogout (LOGOUT_USER) : CONDITION CHANGE
 * onUserLoad (LOAD_USER) : CONDITION CHANGE
 */
export const LOGIN_USER = `${PREFIX}/LOGIN_USER`;
export const LOGOUT_USER = `${PREFIX}/LOGOUT_USER`;
export const LOAD_USER = `${PREFIX}/LOAD_USER`;

const PROCESS_LOGIN = `${PREFIX}/PROCESS_LOGIN`;
const PROCESS_LOGIN_SUCCESS = `${PREFIX}/PROCESS_LOGIN_SUCCESS`;
const PROCESS_LOGIN_ERROR = `${PREFIX}/PROCESS_LOGIN_ERROR`;
const PROCESS_LOGIN_DATA_CLEAR = `${PREFIX}/PROCESS_LOGIN_DATA_CLEAR`;

export const processLogin = (userId, password) => ({
    type: PROCESS_LOGIN,
    payload: {
        userId,
        password
    }
});
export const processLoginDataClear = () => ({
    type: PROCESS_LOGIN_DATA_CLEAR
});

export const settingLoginInfo = (token) => ({
    type: LOGIN_USER,
    token
});

export const processLogout = () => ({type: LOGOUT_USER});

export const onUserLoad = (user) => ({
    type: LOAD_USER,
    currentUser: user
});

/**
 * (TR) 
 * processJoin (PROCESS_JOIN) : CALL
 * clearProcessJoin (CLEAR_PROCESS_JOIN) : CLEAR
 */
const PROCESS_JOIN = `${PREFIX}/PROCESS_JOIN`;
const PROCESS_JOIN_SUCCESS = `${PREFIX}/PROCESS_JOIN_SUCCESS`;
const PROCESS_JOIN_ERROR = `${PREFIX}/PROCESS_JOIN_ERROR`;
const CLEAR_PROCESS_JOIN = `${PREFIX}/CLEAR_PROCESS_JOIN`;

export const processJoin = (updateForm) => ({
    type: PROCESS_JOIN,
    payload: updateForm
});

export const clearProcessJoin = () => ({
    type: CLEAR_PROCESS_JOIN
});

