const UPDATE_MEMBER_SUCCESS = `${PREFIX}/UPDATE_MEMBER_SUCCESS`;
const UPDATE_MEMBER_ERROR = `${PREFIX}/UPDATE_MEMBER_ERROR`;
const CLEAR_UPDATE_MEMBER = `${PREFIX}/CLEAR_UPDATE_MEMBER`;

