const GET_MEMBER_INFO_SUCCESS = `${PREFIX}/GET_MEMBER_INFO_SUCCESS`;
const GET_MEMBER_INFO_ERROR = `${PREFIX}/GET_MEMBER_INFO_ERROR`;

