/**
 * (MODAL)   ()
 * showJoinModal (SHOW_JOIN_MODAL) : SHOW
 * hideJoinModal (HIDE_JOIN_MODAL) : CLEAR
 */
const SHOW_JOIN_MODAL = `${PREFIX}/SHOW_JOIN_MODAL`;
const HIDE_JOIN_MODAL = `${PREFIX}/HIDE_JOIN_MODAL`;

export const showJoinModal = () => ({
    type: SHOW_JOIN_MODAL
});

