/* eslint-disable no-unused-vars */
import React from 'react';
import Modal from 'react-bootstrap/Modal';
import {Form, Row, Col, FloatingLabel, Accordion} from 'react-bootstrap';
import {useFormik} from 'formik';
import * as Yup from 'yup';
import {useTranslation} from 'react-i18next';
import Button from '../../../components/button/Button';
import {
    printFormError,
    getInputValidMsg,
    renderModalHeader
} from '../../../lib/commonUiUtils';

/*
 *  / 
 */
const ScenarioLock = ({
    modalShow,
    handleClose,
    openData,
    onclickLock,
    updateLoading
}) => {
    const [t] = useTranslation();

    const formik = useFormik({
        enableReinitialize: true,
        initialValues: {
            lockPassword: ''
        },
        validationSchema: Yup.object({
            lockPassword: Yup.string().required(getInputValidMsg(''))
        }),
        onSubmit: (values) => {
            onclickLock(values.lockPassword);
        }
    });

    const renderTitle = () => {
        if (openData.lock) {
            return '  ';
        }
        return ' ';
    };

    const renderLockButton = () => {
        if (openData.lock) {
            return ' ';
        }
        return '';
    };

    return (
        <>
            <Modal
                show={modalShow}
                onHide={handleClose}
                size="sm"
                onExited={() => formik.resetForm()}
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Form onSubmit={formik.handleSubmit}>
                    {renderModalHeader(renderTitle())}
                    <Modal.Body>
                        <Accordion defaultActiveKey="0">
                            <Accordion.Item eventKey="0">
                                <Accordion.Header className="accordion-header1">
                                    {renderTitle()}
                                </Accordion.Header>
                                <Accordion.Body>
                                    <Row>
                                        <Col xs="12">
                                            <FloatingLabel
                                                label=" ( 4)"
                                                className="mb-2"
                                            >
                                                <Form.Control
                                                    type="text"
                                                    maxLength="4"
                                                    {...formik.getFieldProps(
                                                        'lockPassword'
                                                    )}
                                                />
                                                {printFormError(
                                                    formik,
                                                    'lockPassword'
                                                )}
                                            </FloatingLabel>
                                        </Col>
                                    </Row>
                                </Accordion.Body>
                            </Accordion.Item>
                        </Accordion>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button
                            theme="primary"
                            type="submit"
                            isLoading={updateLoading}
                        >
                            {renderLockButton()}
                        </Button>
                        <Button theme="default" onClick={handleClose}>
                            {t('common.button.close')}
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>
        </>
    );
};

export default ScenarioLock;
