import React, {useState, useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import LayoutDetailRandomUpdate from '@app/pages/layout/popup/LayoutDetailRandomUpdate';

import {toast} from 'react-toastify';
import {
    searchRandomAll,
    testRandom,
    testRandomDataClear
} from '../../../store/random';
import Spinner from '../../../components/spinner/Spinner';
import {setSelectedLayout, clearRandomUpdateModal} from '../../../store/layout';

/*
 *   -    Container
 */
function LayoutDetailRandomUpdateContainer() {
    const dispatch = useDispatch();

    const [modalShow, setModalShow] = React.useState(false);
    const handleClose = () => setModalShow(false);
    const handleShow = () => {
        dispatch(searchRandomAll());
        setModalShow(true);
    };
    // const [testData, setTestData] = React.useState('');

    const {randUpdateInitData} = useSelector((state) => state.layout);

    const [data, setData] = useState({
        id: '',
        randomId: '',
        randCode: '',
        randKey: '',
        testData: ''
    });

    /*
     *   
     */
    useEffect(() => {
        if (randUpdateInitData.showModal && !modalShow) {
            setData({
                id: randUpdateInitData.initData.id,
                randomId: randUpdateInitData.initData.randomId,
                randCode: randUpdateInitData.initData.randomCodeKey,
                randKey: randUpdateInitData.initData.randomKey,
                testData: ''
            });
            handleShow();
            dispatch(clearRandomUpdateModal());
        }
    }, [randUpdateInitData.showModal]);

    const {data: codeData} = useSelector((state) => state.common.searchCode);
    const {loading: searchLoading, data: randData} = useSelector(
        (state) => state.random.searchList
    );

    /*
     * random data test
     */
    const testRadomCall = (saveData) => {
        dispatch(testRandom(saveData));
    };

    const {
        loading: testLoading,
        data: testResponse,
        error: testError
    } = useSelector((state) => state.random.test);

    useEffect(() => {
        if (!testLoading && testResponse) {
            const tempData = {...data};
            tempData.testData = testResponse.data.testData;

            setData(tempData);
            dispatch(testRandomDataClear());
            // toast.success('Save success');
        }

        if (!testLoading && testError) {
            dispatch(testRandomDataClear());
            toast.error('test Fail');
        }
    }, [testResponse, testError]);

    const {selectedLayout} = useSelector((state) => state.layout);

    const setColList = (colList) => {
        colList.map((col) => {
            const tempCol = col;
            if (tempCol.id === Number(data.id)) {
                tempCol.randomId = data.randomId;
                tempCol.randomCodeKey = data.randCode;
                tempCol.randomKey = data.randKey;
            }
            if (col.type === 'list' || col.type === 'object') {
                setColList(col.colList);
            }
            return tempCol;
        });
    };

    /*
     *    ''
     */
    const onClickSave = () => {
        const preSeletedLayout = {...selectedLayout};
        setColList(preSeletedLayout.colList);
        dispatch(setSelectedLayout(preSeletedLayout));
        handleClose();
    };

    return (
        <>
            <LayoutDetailRandomUpdate
                modalShow={modalShow}
                handleClose={handleClose}
                codeData={codeData}
                randData={randData}
                testRadomCall={testRadomCall}
                testLoading={testLoading}
                data={data}
                setData={setData}
                onClickSave={onClickSave}
            />
            <Spinner isLoading={searchLoading} />
        </>
    );
}

export default LayoutDetailRandomUpdateContainer;
