/* eslint-disable jsx-a11y/label-has-associated-control */
import React from 'react';
import {useTranslation} from 'react-i18next';
import {Row, Col, Form, FloatingLabel} from 'react-bootstrap';
import Button from '../../components/button/Button';
import Card from '../../components/card/Card';
import {takeSelectOptionbyCode} from '../../lib/commonUiUtils';

/*
 *   
 */
const RandomSearch = ({
    onSearch,
    searchForm,
    onChangeSearchForm,
    addNewRandom,
    codeData
}) => {
    const [t] = useTranslation();

    const onChangeForm = (e) => {
        e.preventDefault();
        const newSearchForm = {
            ...searchForm
        };

        newSearchForm[e.target.id] = e.target.value;
        onChangeSearchForm(newSearchForm);
    };
    const onClickSearch = (e) => {
        e.preventDefault();
        onSearch();
    };

    const onCheckEnter = (e) => {
        if (e.key === 'Enter') {
            onClickSearch(e);
        }
    };
    return (
        <Card
            title={t('common.text.searchCardTitleName')}
            body={
                <>
                    <Row onKeyPress={onCheckEnter}>
                        <Col xs="10">
                            <Row>
                                <Col xs="3">
                                    <FloatingLabel
                                        label=" "
                                        className="mb-3"
                                    >
                                        <Form.Select
                                            as="select"
                                            id="randomStyle"
                                            onChange={onChangeForm}
                                            value={searchForm.randomStyle}
                                        >
                                            <option value="">
                                                {t('common.text.optionAll')}
                                            </option>
                                            {takeSelectOptionbyCode(
                                                codeData,
                                                'RandomStyle'
                                            )}
                                        </Form.Select>
                                    </FloatingLabel>
                                </Col>
                                <Col xs="5">
                                    <FloatingLabel
                                        label="Key or Description"
                                        className="mb-3"
                                    >
                                        <Form.Control
                                            type="text"
                                            id="randomQuery"
                                            onChange={onChangeForm}
                                            value={searchForm.randomQuery}
                                            maxLength="30"
                                        />
                                    </FloatingLabel>
                                </Col>
                            </Row>
                        </Col>
                        <Col xs="1">
                            <div className="form-group">
                                <label htmlFor="myinput" />
                                <Button
                                    type="button"
                                    className="btn btn-block btn-secondary btn-search"
                                    // isLoading={searchLoading}
                                    onClick={onClickSearch}
                                >
                                    {t('common.button.search')}
                                </Button>
                            </div>
                        </Col>
                        <Col xs="1">
                            <Form.Group>
                                <Form.Label />
                                <Button
                                    type="button"
                                    className="btn btn-block btn-success btn-search"
                                    // isLoading={searchLoading}
                                    onClick={addNewRandom}
                                >
                                    + 
                                </Button>
                            </Form.Group>
                        </Col>
                    </Row>
                </>
            }
        />
    );
};

export default RandomSearch;
