/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import {useHistory, useLocation} from 'react-router-dom';
import ClubDetail from '@pages/club/ClubDetail';
import {useDispatch, useSelector} from 'react-redux';

import {
    searchClubDetail,
    searchClubFormInit,
    setSearchClubScenariosForm,
    searchClubScenarios,
    searchClubScenariosFormInit,
    setSearchClubHistoryForm,
    searchClubHistory,
    searchClubHistoryFormInit
} from '../../store/club';

/*
 *  Container
 */
function ClubDetailContainer() {
    const dispatch = useDispatch();
    const history = useHistory();
    const location = useLocation();

    const refreshData = () => {
        // Club   
        dispatch(searchClubDetail(location.state.clubId));

        const searchClubScenariosForm = searchClubScenariosFormInit();
        searchClubScenariosForm.clubId = location.state.clubId;
        dispatch(setSearchClubScenariosForm(searchClubScenariosForm));
        dispatch(searchClubScenarios(searchClubScenariosForm));

        // searchClubFormInit
        // Club   Scenario 
        // onSearchScenarios(searchForm);

        // searchClubHistory -> Club test history Paging
        const searchClubHistoryForm = searchClubHistoryFormInit();
        searchClubHistoryForm.clubId = location.state.clubId;
        dispatch(setSearchClubHistoryForm(searchClubHistoryForm));
        dispatch(searchClubHistory(searchClubHistoryForm));
    };

    // INIT EVENT
    useEffect(() => {
        if (location.state == null) {
            // eslint-disable-next-line no-alert
            alert(' .');
            history.goBack();
        }

        refreshData();
    }, []);

    return (
        <>
            <ClubDetail
                refreshData={refreshData}
                // onClickCreate={onClickCreate}
                // onMoveDetail={onMoveDetail}
            />
        </>
    );
}

export default ClubDetailContainer;
