/* eslint-disable no-debugger */
/* eslint-disable no-unused-vars */
import {useSelector, useDispatch} from 'react-redux';
import React, {useState, useEffect} from 'react';
import {toast} from 'react-toastify';
import {useTranslation} from 'react-i18next';
import TextDiffViewerPopupContainer from '@app/containers/common/TextDiffViewerPopupContainer';
import MultiTestPopup from '../../pages/common/MultiTestPopup';

import {clearMultiTestModal, usedTextDiffModal} from '../../store/common';

/**
 * Layout, history, bulkTest    HTTP      
 * DUAL     ,     .
 */
export default function MultiTestPopupContainer() {
    const [t] = useTranslation();
    const dispatch = useDispatch();

    const [modalShow, setModalShow] = React.useState(false);
    const handleClose = () => {
        setModalShow(false);
    };
    const handleShow = () => setModalShow(true);

    const {multiTestInitData} = useSelector((state) => state.common);

    // true (dualMode), false (singleMode)
    const [isdualMode, setDualMode] = React.useState(false);

    const {data: serverData} = useSelector(
        (state) => state.common.searchServer
    );

    const initData = () => {
        return {
            title: '',
            input: '',
            output: '',
            stausCode: '',
            serverId: '',
            serviceId: '',
            layoutId: '',
            httpMethod: '',
            url: '',
            httpResponseCode: '',
            httpResponseText: '',
            convertibleResponseBodyIR: false,
            responseBodyIR: '',
            serverName: '',
            startTime: '',
            executeSecond: '',
            tokenInfo: ''
        };
    };
    const [singleTestDataA, setSingleTestDataA] = useState(initData());
    const [singleTestDataB, setSingleTestDataB] = useState(initData());

    /*
     * popup call event
     */
    useEffect(() => {
        if (multiTestInitData.showModal && !modalShow) {
            const tempSingTestDataA = {...multiTestInitData.initDataA};
            if (tempSingTestDataA.input === undefined) {
                tempSingTestDataA.input =
                    tempSingTestDataA?.headerAndBodyRequest?.intermediateBody;
            }
            if (tempSingTestDataA.output === undefined) {
                tempSingTestDataA.output =
                    tempSingTestDataA?.headerAndBodyResponse?.intermediateBody;
            }
            setSingleTestDataA(tempSingTestDataA);

            // // layout, history, bulk(single Test)     
            if (multiTestInitData.isdualMode) {
                setDualMode(true);
                const tempSingTestDataB = {...multiTestInitData.initDataB};
                if (tempSingTestDataB.input === undefined) {
                    tempSingTestDataB.input =
                        tempSingTestDataB?.headerAndBodyRequest?.intermediateBody;
                }
                if (tempSingTestDataB.output === undefined) {
                    tempSingTestDataB.output =
                        tempSingTestDataB?.headerAndBodyResponse?.intermediateBody;
                }
                setSingleTestDataB(tempSingTestDataB);
            } else {
                setDualMode(false);
                setSingleTestDataB(initData());
            }
            handleShow();
            dispatch(clearMultiTestModal());
        }
    }, [multiTestInitData.showModal]);

    /*
     * '' evnet
     */
    const onClickDiff = () => {
        if (singleTestDataA.historyId) {
            const data = {
                basicData: 'ATWORKS',
                compareIdA: singleTestDataA.historyId,
                compareIdB: singleTestDataB.historyId
            };
            dispatch(usedTextDiffModal(data));
        } else {
            const data = {
                basicData: 'LOG',
                compareIdA: singleTestDataA.logId,
                compareIdB: singleTestDataB.historyId
            };

            dispatch(usedTextDiffModal(data));
        }
    };

    /**
     * daul mode change
     * @param {}} isDaul
     */
    const onClickDualMode = (isDaul) => {
        setDualMode(isDaul);

        const tempSingTestDataA = {...singleTestDataA};
        tempSingTestDataA.tokenInfo = '';
        tempSingTestDataA.output = '';
        setSingleTestDataB(tempSingTestDataA);
    };

    return (
        <>
            <MultiTestPopup
                modalShow={modalShow}
                handleClose={handleClose}
                serverData={serverData}
                singleTestDataA={singleTestDataA}
                singleTestDataB={singleTestDataB}
                setSingleTestDataA={setSingleTestDataA}
                setSingleTestDataB={setSingleTestDataB}
                isdualMode={isdualMode}
                onClickDualMode={onClickDualMode}
                onClickDiff={onClickDiff}
            />
            {/* <Spinner isLoading={executeLoading} /> */}
            <TextDiffViewerPopupContainer />
        </>
    );
    ///
}
