/* eslint-disable no-unused-vars */

import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import UitestStepPopXhr1 from '@pages/uitest/step/UitestStepPopXhr1';
import {toast} from 'react-toastify';
import {
    setDetailCaseList,
    setStepUpdateInitData,
    searchLayout,
    searchLayoutDetail,
    clearsearchLayoutDetail,
    combineRequestValue,
    clearCombineRequestValue,
    saveScenario
} from '../../../store/uitest';
import {setTempKey} from '../../../store/common';
import Spinner from '../../../components/spinner/Spinner';
import {
    convertCollListToJson,
    jsonStrFormatPretty
} from '../../../lib/jsonUtils';
import {getErrorMsg} from '../../../lib/commonUiUtils';

function UitestStepPopXhr1Container({handleClose}) {
    const dispatch = useDispatch();
    const {data: scenarioData} = useSelector(
        (state) => state.uitest.scenarioInfo
    );

    const {caseId} = useSelector((state) => state.uitest.selectedCaseId);
    const saveDetailCaseList = (caseList) => {
        dispatch(setDetailCaseList(caseList));
    };

    const onChangeStepUpdataInitData = (data) => {
        dispatch(setStepUpdateInitData({...data}));
    };

    const {tempkey} = useSelector((state) => state.common);
    const setTempKeyRezen = () => {
        if (tempkey < -9999) {
            dispatch(setTempKey(-1));
        } else {
            dispatch(setTempKey(tempkey - 1));
        }
    };

    const [layoutName, setLayoutName] = React.useState('');
    const [layoutDetail, setLayoutDetail] = React.useState('');

    // layout list 
    const {
        data: searchLayoutData,
        loading: searchLayoutLoading,
        error: searchLayoutError
    } = useSelector((state) => state.uitest.searchLayout);

    // layout detail 
    const {
        data: searchLayoutDetailData,
        loading: searchLayoutDetailLoading,
        error: searchLayoutDetailError
    } = useSelector((state) => state.uitest.searchLayoutDetail);

    // layout random/testdata  input request 
    // layout detail 
    const {
        data: colListData,
        loading: colListLoading,
        error: colListDetailError
    } = useSelector((state) => state.uitest.colListByRandVal);

    useEffect(() => {
        // fail
        if (!searchLayoutLoading && searchLayoutError) {
            toast.error(getErrorMsg(searchLayoutError, 'search'));
        }
        if (!searchLayoutDetailLoading && searchLayoutDetailData) {
            setLayoutDetail(searchLayoutDetailData.data);

            //    
            dispatch(
                combineRequestValue(
                    searchLayoutDetailData.data?.layoutId,
                    searchLayoutDetailData.data?.cols?.filter(
                        (obj) => obj.inOut === 'IN'
                    )
                )
            );
            //  
            dispatch(clearsearchLayoutDetail());
        }
    }, [searchLayoutDetailData, searchLayoutError]);

    useEffect(() => {
        if (!colListLoading && colListData) {
            const tempLayoutDetail = {...layoutDetail};
            tempLayoutDetail.inputData = jsonStrFormatPretty(
                JSON.stringify(convertCollListToJson(colListData.data?.cols))
            );
            setLayoutDetail(tempLayoutDetail);

            //  
            dispatch(clearCombineRequestValue());
        }
    }, [colListData]);

    const onClickSearch = () => {
        dispatch(searchLayout(layoutName));
    };

    const moveLayoutDetail = (id) => {
        dispatch(searchLayoutDetail(id));
    };

    const movePage = (page) => {
        dispatch(searchLayout(layoutName, page));
    };

    const handleInputChange = (e) => {
        const tempLayoutDetail = {...layoutDetail};
        tempLayoutDetail.inputData = jsonStrFormatPretty(e.target.value);
        setLayoutDetail(tempLayoutDetail);
    };
    useEffect(() => {
        onClickSearch();
        return () => {};
    }, []);

    return (
        <>
            <UitestStepPopXhr1
                scenarioData={scenarioData}
                selectedCaseId={caseId}
                setDetailCaseList={saveDetailCaseList}
                handleClose={handleClose}
                tempkey={tempkey}
                setTempKeyRezen={setTempKeyRezen}
                searchLayoutData={searchLayoutData}
                layoutDetail={layoutDetail}
                layoutName={layoutName}
                setLayoutName={setLayoutName}
                onClickSearch={onClickSearch}
                moveLayoutDetail={moveLayoutDetail} // layout  
                movePage={movePage}
                handleInputChange={handleInputChange}
            />
            <Spinner
                isLoading={
                    searchLayoutLoading ||
                    searchLayoutDetailLoading ||
                    colListLoading
                }
            />
        </>
    );
}

export default UitestStepPopXhr1Container;
