import React from 'react';
import {useSelector, useDispatch} from 'react-redux';
import UitestCaseTab from '@app/pages/uitest/UitestCaseTab';
import {setDetailCaseList} from '../../store/uitest';

function UitestCaseTabContainer() {
    const dispatch = useDispatch();
    const {data: scenarioData} = useSelector(
        (state) => state.uitest.scenarioInfo
    );

    const {caseId} = useSelector((state) => state.uitest.selectedCaseId);
    const saveDetailCaseList = (cases) => {
        dispatch(setDetailCaseList(cases));
    };

    return (
        <>
            <UitestCaseTab
                scenarioData={scenarioData} // detail scenario data
                selectedCaseId={caseId} // list selected case id
                setDetailCaseList={saveDetailCaseList}
            />
        </>
    );
}

export default UitestCaseTabContainer;
