import React from 'react';
import {Row, Col, Card} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
import SmallBox from '../../components/small-box/SmallBox';
import {displayNumber} from '../../lib/commonUiUtils';

const DashboardHead = ({searchDate, searchStatData, onChangerSearchDate}) => {
    const [t] = useTranslation();
    const renderCount = (type) => {
        if (searchStatData) {
            if (type === 'all') {
                return displayNumber(searchStatData.data.allCnt);
            }
            if (type === 'success') {
                return displayNumber(searchStatData.data.successCnt);
            }
            if (type === 'fail') {
                return displayNumber(searchStatData.data.failCnt);
            }
            if (type === 'successRate') {
                if (searchStatData.data.allCnt === 0) {
                    return 'N/A';
                }
                return `${Math.round(
                    (searchStatData.data.successCnt * 100) /
                        searchStatData.data.allCnt
                )}%`;
            }
        }
        return 0;
    };
    return (
        <>
            <Card style={{borderTop: '3px solid #007bff'}}>
                <Card.Header>
                    <>
                        <input
                            style={{width: '200px'}}
                            type="date"
                            className="form-control"
                            onChange={onChangerSearchDate}
                            value={searchDate}
                        />
                    </>
                </Card.Header>
                <Card.Body>
                    <Row>
                        <Col xs="3">
                            <SmallBox
                                count={renderCount('all')}
                                title={t('pages.dashboard.testAllCnt')}
                                type="info"
                                icon="ion-android-people"
                                navigateTo="/"
                            />
                        </Col>
                        <Col xs="3">
                            <SmallBox
                                count={renderCount('success')}
                                title={t('pages.dashboard.testSuccessCnt')}
                                type="success"
                                navigateTo="/"
                            />
                        </Col>
                        <Col xs="3">
                            <SmallBox
                                count={renderCount('fail')}
                                title={t('pages.dashboard.testFailCnt')}
                                type="danger"
                                navigateTo="/"
                            />
                        </Col>
                        <Col xs="3">
                            <SmallBox
                                count={renderCount('successRate')}
                                title={t('pages.dashboard.failCntFromServer')}
                                type="warning"
                                navigateTo="/"
                            />
                        </Col>
                    </Row>
                </Card.Body>
            </Card>
        </>
    );
};

export default DashboardHead;
