/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import UitestResultPopup from '@pages/uitest/popup/UitestResultPopup';
import {toast} from 'react-toastify';
import {
    clearUiTestInitData,
    setDetailCaseList,
    convertXhrList,
    clearConvertXhrList
} from '../../../store/uitest';
import {setTempKey} from '../../../store/common';
import Spinner from '../../../components/spinner/Spinner';
import {getErrorMsg} from '../../../lib/commonUiUtils';

/*
 * UI Test  xhr   
 */
function UitestResultPopupContainer() {
    const dispatch = useDispatch();
    // popup show/hide
    const [modalShow, setModalShow] = React.useState(false);
    const handleClose = () => setModalShow(false);
    const handleShow = () => setModalShow(true);

    // step code setting
    const {data: scenarioData} = useSelector(
        (state) => state.uitest.scenarioInfo
    );

    const [xhrInfo, setXhrInfo] = React.useState();
    /*
     * Redux  
     */
    const {uiTestInitData} = useSelector((state) => state.uitest);

    useEffect(() => {
        if (uiTestInitData.showModal && !modalShow) {
            setXhrInfo(uiTestInitData.xhrInfo);
            handleShow();
            dispatch(clearUiTestInitData());
        }
    }, [uiTestInitData.showModal]);

    const {tempkey} = useSelector((state) => state.common);

    const saveXhrList = (values) => {
        dispatch(convertXhrList(values.caseId, values.caseName, xhrInfo.list));
    };

    const {
        data: convertXhrListData,
        loading: convertXhrListLoading,
        error: convertXhrListError
    } = useSelector((state) => state.uitest.convertXhrList);

    useEffect(() => {
        if (!convertXhrListLoading && convertXhrListData) {
            const {caseId, caseName} = convertXhrListData.data;
            let idSeq = tempkey;
            const cases = [...scenarioData.data.cases];
            if (caseId === -1) {
                idSeq -= 1;
                const valuesT = {
                    caseId: idSeq,
                    caseName: caseName === '' ? 'Undefined' : caseName,
                    caseDesc: ''
                };

                const steps = [];
                convertXhrListData.data.steps.forEach((step) => {
                    idSeq -= 1;
                    const newStep = {
                        stepId: idSeq,
                        httpMethod: step.httpMethod,
                        stepType: 'API',
                        inputData: step.inputData,
                        outputData: step.outputData,
                        stepName: step.stepName,
                        urlPath: step.urlPath,
                        fnCode: step.fnCode,
                        serviceId: step.serviceId,
                        layoutId: step.layoutId,
                        isSkipError: false
                    };
                    steps.push(newStep);
                });

                const bLoop = steps.map((unit, index) => {
                    const temp = unit;
                    temp.idx = index;
                    return temp;
                });
                valuesT.steps = bLoop;

                cases.push(valuesT);

                const aLoop = cases.map((unit, index) => {
                    const temp = unit;
                    temp.index = index;
                    return temp;
                });
                dispatch(setDetailCaseList(aLoop));
            } else {
                // const caseList = [...scenarioData.data.caseList];

                const caseLoop = cases.map((cUnit, cIndex) => {
                    const theCase = cUnit;

                    if (cUnit.caseId === Number(caseId)) {
                        let data = theCase.steps;
                        if (data == null) {
                            data = [];
                        }

                        convertXhrListData.data.steps.forEach((step) => {
                            idSeq -= 1;
                            const newStep = {
                                stepId: idSeq,
                                httpMethod: step.httpMethod,
                                stepType: 'API',
                                inputData: step.inputData,
                                outputData: step.outputData,
                                stepName: step.stepName,
                                urlPath: step.urlPath,
                                fnCode: step.fnCode,
                                serviceId: step.serviceId,
                                layoutId: step.layoutId,
                                isSkipError: false
                            };
                            data.push(newStep);
                        });

                        const aLoop = data.map((unit, index) => {
                            const temp = unit;
                            temp.index = index;
                            return temp;
                        });
                        theCase.steps = aLoop;
                    }
                    return theCase;
                });
                dispatch(setDetailCaseList(caseLoop));
            }
            dispatch(setTempKey(idSeq));

            //  
            dispatch(clearConvertXhrList());
            handleClose();
            toast.success('Step  .');
        }

        // fail
        if (!convertXhrListLoading && convertXhrListError) {
            dispatch(clearConvertXhrList());
            toast.error(getErrorMsg(convertXhrListError, 'execute'));
        }
    }, [convertXhrListData, convertXhrListError]);

    return (
        <>
            <UitestResultPopup
                modalShow={modalShow}
                handleClose={handleClose}
                xhrInfo={xhrInfo}
                scenarioData={scenarioData}
                saveXhrList={saveXhrList}
            />
            <Spinner isLoading={convertXhrListLoading} />
        </>
    );
}

export default UitestResultPopupContainer;
