/**
 * CLUB  Scenario 
 * addScenario (ADD_SCENARIO) : 
 * clearAddScenario (CLEAR_ADD_SCENARIO) : 
 */
export const addScenario = async ({clubId, scenarioId}) => {
    const response = await axios.post(`/clubs/${clubId}/${scenarioId}/connect`);
    return response;
};

/**
 * CLUB  Scenario 
 * addScenario (ADD_SCENARIO) : 
 * clearAddScenario (CLEAR_ADD_SCENARIO) : 
 */
export const removeScenario = async ({clubScenarioId}) => {
    const response = await axios.delete(
        `/clubs/${clubScenarioId}/unconnect`,
        null
    );
    return response;
};

/**
 * club    
 * searchClubHistory (SEARCH_CLUB_HISTORY) : 
 * clearSearchClubHistory (CLEAR_SEARCH_CLUB_HISTORY) : 
 * setSearchClubHistoryForm (SET_SEARCH_CLUB_HISTORY_FORM) : Form Data 
 * searchClubHistoryFormInit : Form  
 */
export const searchClubHistory = async ({clubId, page = 1, size = 5}) => {
    const response = await axios.get(
        `/clubs/${clubId}/snapshots?sort=id,desc&page=${page - 1}&size=${size}`
    );
    return response;
};

/**
 * (TR) 2022-04-05 CLUB Lock   ()
 * saveClubLock (SAVE_CLUB_LOCK) : 
 * clearClubLock (CLEAR_SAVE_CLUB_LOCK) : 
 */
export const saveClubLock = async ({clubId, isLock, lockPassword}) => {
    const response = await axios.patch(`/clubs/${clubId}/lock`, {
        isLock,
        lockPassword
    });
    return response;
};
