import React from 'react';
import {useSelector, useDispatch} from 'react-redux';
import UserDropdown from '@modules/main/header/user-dropdown/UserDropdown';
import {processLogout, goToLogin} from '../../store/auth';

function UserDropdownContainer() {
    const dispatch = useDispatch();

    //    
    const {data: memberInfoData} = useSelector(
        (state) => state.auth.memberInfo
    );

    const onLogout = () => {
        dispatch(processLogout());
        dispatch(goToLogin());
    };

    return <UserDropdown memberInfoData={memberInfoData} onLogout={onLogout} />;
}

export default UserDropdownContainer;
