import React from 'react';
import {Form, Modal, Row, Col, FloatingLabel, Accordion} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
import {useFormik} from 'formik';
import * as Yup from 'yup';
import Button from '../../../components/button/Button';
import {
    printFormError,
    takeSelectOptionbyCode
} from '../../../lib/commonUiUtils';

// eslint-disable-next-line no-unused-vars
const UitestStepPopLogin = ({
    scenarioData,
    selectedCaseId,
    setDetailCaseList,
    handleClose,
    stepUpdateInitData,
    tempkey,
    setTempKeyRezen,
    codeData
}) => {
    const [t] = useTranslation();

    const formik = useFormik({
        initialValues: {
            isSkipError: stepUpdateInitData?.isSkipError ?? false,
            connectionInfo: stepUpdateInitData?.connectionInfo ?? {
                connectedId: '',
                connectedPw: ''
            },
            defaultLogin: stepUpdateInitData?.defaultLogin ?? 'Y'
        },
        validationSchema: Yup.object({
            // identity: Yup.string().when(['isDefault'], {
            //     is: (isDefault) => isDefault !== 'Y',
            //     then: Yup.string().required(
            //         getInputValidMsg(t('common.text.id'))
            //     )
            // }),
            // password: Yup.string().when(['isDefault'], {
            //     is: (isDefault) => isDefault !== 'Y',
            //     then: Yup.string().required(
            //         getInputValidMsg(t('common.text.password'))
            //     )
            // })
        }),
        onSubmit: (values) => {
            const cases = [...scenarioData.data.cases];
            const caseLoop = cases.map((cUnit) => {
                const theCase = cUnit;

                if (cUnit.caseId === selectedCaseId) {
                    let data = theCase.steps;
                    if (data == null) {
                        data = [];
                    }

                    //    
                    if (!stepUpdateInitData.stepId) {
                        const newStep = {...values};
                        newStep.stepType = 'LOGIN';
                        newStep.stepId = tempkey;
                        setTempKeyRezen();
                        data.push(newStep);

                        const aLoop = data.map((unit, index) => {
                            const temp = unit;
                            temp.index = index;
                            return temp;
                        });

                        theCase.steps = aLoop;
                    }
                    //   
                    else {
                        const aLoop = data.map((unit) => {
                            const temp = unit;

                            if (stepUpdateInitData.stepId === unit.stepId) {
                                temp.defaultLogin = values.defaultLogin;
                                temp.connectionInfo = values.connectionInfo;
                                temp.isSkipError = values.isSkipError;
                            }
                            return temp;
                        });

                        theCase.stepList = aLoop;
                    }
                }
                return theCase;
            });
            setDetailCaseList(caseLoop);
            handleClose();
        }
    });

    const renderId = () => {
        return (
            <Form.Control
                type="text"
                disabled={formik.values?.defaultLogin === 'Y'}
                {...(formik.getFieldProps('connectionInfo.connectedId') ?? '')}
                maxLength="20"
            />
        );
    };

    const renderPw = () => {
        return (
            <Form.Control
                type="text"
                disabled={formik.values?.defaultLogin === 'Y'}
                {...(formik.getFieldProps('connectionInfo.connectedPw') ?? '')}
                maxLength="50"
            />
        );
    };

    const renderIsDefault = () => {
        return (
            <Form.Select as="select" {...formik.getFieldProps('defaultLogin')}>
                {takeSelectOptionbyCode(codeData, 'DefaultLogin')}
            </Form.Select>
        );
    };

    return (
        <>
            <Form onSubmit={formik.handleSubmit}>
                <Accordion defaultActiveKey="0">
                    <Accordion.Item eventKey="0">
                        <Accordion.Header className="accordion-header2">
                            {t('pages.scenario.loginInterface')}
                        </Accordion.Header>
                        <Accordion.Body>
                            <Row>
                                <Col xs="2">
                                    <FloatingLabel
                                        className="mb-2"
                                        label={t('pages.server.loginInfo')}
                                    >
                                        {renderIsDefault()}
                                    </FloatingLabel>
                                </Col>
                                <Col xs="3">
                                    <FloatingLabel
                                        label={t('common.text.id')}
                                        className="mb-1"
                                    >
                                        {renderId()}
                                    </FloatingLabel>
                                    {printFormError(formik, 'identity')}
                                </Col>
                                <Col xs="3">
                                    <FloatingLabel
                                        label={t('common.text.password')}
                                        className="mb-1"
                                    >
                                        {renderPw()}
                                    </FloatingLabel>
                                    {printFormError(formik, 'password')}
                                </Col>

                                <Col xs="12">
                                    <div key="isSkipError" className="mb-3">
                                        <Form.Check
                                            type="checkbox"
                                            id="isSkipError"
                                            label={t(
                                                'pages.scenario.isSkipError'
                                            )}
                                            {...formik.getFieldProps(
                                                'isSkipError'
                                            )}
                                            checked={formik.values.isSkipError}
                                        />
                                    </div>
                                </Col>
                            </Row>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>

                <Modal.Footer>
                    <Button
                        theme="primary"
                        type="submit"
                        // isLoading={createLoading}
                    >
                        {t('common.button.register')}
                    </Button>
                    <Button theme="default" onClick={handleClose}>
                        {t('common.button.close')}
                    </Button>
                </Modal.Footer>
            </Form>
        </>
    );
};

export default UitestStepPopLogin;
