import React from 'react';
import {FaSearch, FaTrash, FaUser} from 'react-icons/fa';
import {useTranslation} from 'react-i18next';
import Table from '../../components/table/Table';
import Button from '../../components/button/Button';
import {changeToDate} from '../../lib/moment';
import {renderServerType} from '../../lib/buttonRender';

const ServerTable = ({
    data,
    showUpdateFormModal,
    showCredentialInfoModal,
    movePage,
    onDelete,
    memberInfoData
}) => {
    const [t] = useTranslation();
    const tableName = t('pages.server.serverList');
    const onDoubleClick = (id, row) => {
        showUpdateFormModal(row);
    };

    const columns = [
        {
            title: t('pages.server.serverType'),
            width: '8%',
            key: 'serverCodeName',
            render: (id, row) => {
                return renderServerType(row?.serverType);
            }
        },
        {
            title: t('pages.server.serverName'),
            key: 'serverName',
            width: '15%',
            align: 'left'
        },
        {
            title: 'URL',
            key: 'url',
            width: '30%',
            align: 'left',
            render: (id, row) => {
                if (row.serverType === 'TCP_IP') {
                    return `${row.ip}:${row.port}`;
                }
                return row.url;
            }
        },
        {
            title: '',
            key: 'attribute',
            width: '10%',
            render: (id, row) => {
                if (row.serverType === 'HTTP' || row.serverType === 'TCP_IP') {
                    return row.dataFormat;
                }
                if (row.serverType === 'DATABASE') {
                    return row.dbms;
                }
                return '';
            }
        },
        {
            title: ' ',
            key: 'modifiedDate',
            width: '8%',
            render: (id, row) => {
                return changeToDate(row.modifiedDate);
            }
        },
        {
            align: 'right',
            key: 'button',
            width: '5%',
            render: (id, row) => {
                return (
                    <>
                        <Button
                            theme="outline-success"
                            onClick={() => showUpdateFormModal(row)}
                        >
                            <FaSearch />
                        </Button>
                        <Button
                            disabled={
                                row.serverType !== 'HTTP' &&
                                row.serverType !== 'TCP_IP'
                            }
                            theme="outline-primary"
                            onClick={() => showCredentialInfoModal(row)}
                        >
                            <FaUser />
                        </Button>
                        <Button
                            disabled={memberInfoData?.data?.role !== 'ADMIN'}
                            theme="outline-danger"
                            onClick={() => onDelete(id)}
                        >
                            <FaTrash />
                        </Button>
                    </>
                );
            }
        }
    ];

    return (
        <>
            <Table
                rowKey="id"
                tableName={tableName}
                onDoubleClick={onDoubleClick}
                movePage={movePage}
                columns={columns}
                data={data}
            />
        </>
    );
};

export default ServerTable;
