import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import {useTranslation} from 'react-i18next';
import {toast} from 'react-toastify';
import ServerTable from '@pages/server/ServerTable';
import {
    deleteServer,
    deleteServerDataClear,
    searchServer,
    setSearchServerForm,
    setUpdateServerInitData
} from '../../store/server';
import {setSelectedServer} from '../../store/credentialInfo';
import {getErrorMsg} from '../../lib/commonUiUtils';
import Spinner from '../../components/spinner/Spinner';
import {searchServerCommon} from '../../store/common';

function ServerTableContainer() {
    const dispatch = useDispatch();
    const [t] = useTranslation();

    const {data: searchData} = useSelector((state) => state.server.search);

    const {
        loading: deleteLoading,
        data: deleteResponse,
        error: deleteError
    } = useSelector((state) => state.server.delete);

    const {searchForm} = useSelector((state) => state.server);

    //    
    const {data: memberInfoData} = useSelector(
        (state) => state.auth.memberInfo
    );

    const showUpdateFormModal = (data) => {
        dispatch(setUpdateServerInitData(data));
    };

    const movePage = (page) => {
        const searchFormT = {...searchForm};
        searchFormT.page = page;
        dispatch(setSearchServerForm(searchFormT));
        dispatch(searchServer(searchFormT));
    };

    const onDelete = (serverId) => {
        // eslint-disable-next-line no-alert
        if (window.confirm(t('common.alert.checkDelete'))) {
            dispatch(deleteServer(serverId));
        }
    };

    const showCredentialInfoModal = (selectedServer) => {
        dispatch(setSelectedServer(selectedServer));
    };

    useEffect(() => {
        if (!deleteLoading && deleteResponse) {
            dispatch(deleteServerDataClear());
            toast.success(t('common.alert.updateSuccess'));
            dispatch(searchServer(searchForm));

            // 20220109 kimtaehan   
            dispatch(searchServerCommon(''));
        }
        // fail
        if (!deleteLoading && deleteError) {
            toast.error(getErrorMsg(deleteError, 'save'));
            dispatch(deleteServerDataClear());
        }
    }, [deleteResponse, deleteError]);

    return (
        <>
            <ServerTable
                showUpdateFormModal={showUpdateFormModal}
                showCredentialInfoModal={showCredentialInfoModal}
                onDelete={onDelete}
                movePage={movePage}
                data={searchData}
                memberInfoData={memberInfoData}
            />
            <Spinner isLoading={deleteLoading} />
        </>
    );
}

export default ServerTableContainer;
