/* eslint-disable no-unused-vars */
import React from 'react';
import {Modal, Form, Row, Col, InputGroup, FormControl} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
import HttpTestViewAContainer from '@app/containers/common/HttpTestViewAContainer';
import HttpTestViewBContainer from '@app/containers/common/HttpTestViewBContainer';
import Button from '../../components/button/Button';
import {renderModalHeader} from '../../lib/commonUiUtils';

const MultiTestPopup = ({
    // popup 
    modalShow,
    handleClose,
    // server Data
    serverData,
    // server A 
    singleTestDataA,
    setSingleTestDataA,
    // server B 
    singleTestDataB,
    setSingleTestDataB,
    isdualMode,
    onClickDualMode,
    onClickDiff
}) => {
    const [t] = useTranslation();

    /**
     * was server tr type render
     * @param {*} serverId
     * @returns was server type(xml/json/fixedLength)
     */
    const renderTrType = (serverId) => {
        return serverData?.data
            .filter((obj) => obj.serverId === Number(serverId))
            .map((obj) => obj.dataFormat);
    };

    /**
     * single mode show button(change multi mode)
     */
    const renderModeChange = () => {
        if (isdualMode) {
            return (
                <Button
                    theme="outline-warning"
                    onClick={() => onClickDualMode(false)}
                >
                     
                </Button>
            );
        }

        return (
            <Button
                theme="outline-warning"
                onClick={() => onClickDualMode(true)}
            >
                 
            </Button>
        );
    };

    const renderDiffButton = () => {
        if (isdualMode) {
            return (
                <Button theme="secondary" onClick={() => onClickDiff(true)}>
                    
                </Button>
            );
        }
        return '';
    };

    const renderBodyDataA = () => {
        if (renderTrType(singleTestDataA.serverId) === '') {
            return '';
        }

        const dataFormat = renderTrType(singleTestDataA.serverId)[0];
        return (
            <>
                <HttpTestViewAContainer
                    singleTestData={singleTestDataA}
                    requestDataFormat={dataFormat}
                    responseDataFormat={dataFormat}
                    setSingleTestData={setSingleTestDataA}
                    isdualMode={isdualMode}
                    serverInfo={t('common.text.serverInfoA')}
                />
            </>
        );
    };

    const renderBodyDataB = () => {
        if (!isdualMode) {
            return '';
        }
        const dataFormat = renderTrType(singleTestDataB.serverId)[0];
        return (
            <>
                <HttpTestViewBContainer
                    singleTestData={singleTestDataB}
                    requestDataFormat={dataFormat}
                    responseDataFormat={dataFormat}
                    setSingleTestData={setSingleTestDataB}
                    isdualMode={isdualMode}
                    serverInfo={t('common.text.serverInfoB')}
                />
            </>
        );
    };

    return (
        <>
            <Modal
                show={modalShow}
                onHide={handleClose}
                // size="xl"
                dialogClassName={isdualMode ? 'modal-70w' : 'modal-50w'}
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Form>
                    {renderModalHeader('API ')}
                    <Modal.Body>
                        <Row>
                            <Col xs={isdualMode ? '6' : '12'}>
                                {renderBodyDataA()}
                            </Col>
                            <Col xs={isdualMode ? '6' : '0'}>
                                {renderBodyDataB()}
                            </Col>
                            {/* <Col xs="6">{renderBodyDataB()}</Col> */}
                        </Row>
                    </Modal.Body>
                    <Modal.Footer>
                        {renderModeChange()}
                        {renderDiffButton()}
                        <Button theme="default" onClick={handleClose}>
                            {t('common.button.close')}
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>
            {/* <ServerUpdateContainer /> */}
        </>
    );
};
export default MultiTestPopup;
