/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import Stat from '@pages/stat/Stat';
import {useSelector, useDispatch} from 'react-redux';
import StatTab2Table from '@pages/stat/StatTab2Table';
import {searchStatT2, usedStatDetailModal2} from '../../store/stat';

function StatTab2TableContainer() {
    const dispatch = useDispatch();

    const {data: searchT2Data} = useSelector((state) => state.stat.searchT2);
    const {searchFormT2} = useSelector((state) => state.stat);

    const movePage = (page) => {
        dispatch(
            searchStatT2(searchFormT2.fromDate, searchFormT2.toDate, page)
        );
    };

    /*
     *    
     */
    const showStatDetailModal = (layoutId, row) => {
        dispatch(usedStatDetailModal2(true, row));
    };

    return (
        <>
            <StatTab2Table
                searchT2Data={searchT2Data}
                showStatDetailModal={showStatDetailModal}
            />
        </>
    );
}

export default StatTab2TableContainer;
