import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import {toast} from 'react-toastify';
import ScheduleTable from '@pages/schedule/ScheduleTable';
import {useTranslation} from 'react-i18next';

import {
    searchSchedule,
    setSearchScheduleForm,
    deleteSchedule,
    clearDeleteSchedule,
    usedSchedualSaveModal
} from '../../store/schedule';

import Spinner from '../../components/spinner/Spinner';
import {getErrorMsg} from '../../lib/commonUiUtils';

/*
 *   table container
 */
function ScheduleTableContainer() {
    const [t] = useTranslation();
    const {data: searchData} = useSelector(
        (state) => state.schedule.searchScheduleRes
    );

    const {
        data: deleteData,
        loading: deleteLoading,
        error: deleteError
    } = useSelector((state) => state.schedule.deleteScheduleRes);

    const {searchScheduleForm} = useSelector((state) => state.schedule);

    const dispatch = useDispatch();

    const movePage = (page) => {
        const searchScheduleFormT = {...searchScheduleForm};
        searchScheduleFormT.page = page;
        dispatch(setSearchScheduleForm(searchScheduleFormT));
        dispatch(searchSchedule(searchScheduleFormT));
    };

    const onClickDetail = (row) => {
        // eslint-disable-next-line no-debugger
        dispatch(usedSchedualSaveModal(true, row));
    };

    const onClickDelete = (row) => {
        // eslint-disable-next-line no-alert
        if (window.confirm(t('common.alert.checkDelete'))) {
            dispatch(deleteSchedule(row.scheduleId));
        }
    };

    useEffect(() => {
        if (!deleteLoading && deleteData) {
            toast.success(t('common.alert.updateSuccess'));
            dispatch(searchSchedule(searchScheduleForm));
        }
        if (!deleteLoading && deleteError) {
            toast.error(getErrorMsg(deleteError, 'save'));
        }
        dispatch(clearDeleteSchedule());
    }, [deleteData, deleteError]);

    return (
        <>
            <ScheduleTable
                // onDelete={onDelete}
                movePage={movePage}
                data={searchData}
                onClickDetail={onClickDetail}
                onClickDelete={onClickDelete}
                // moveUiTest={moveUiTest}
                // onDelete={onDelete}
            />
            <Spinner isLoading={deleteLoading} />
        </>
    );
}

export default ScheduleTableContainer;
