/* eslint-disable indent */
import {takeEvery, takeLatest} from 'redux-saga/effects';
import {INIT_PAGE, INIT_PAGE_SIZE} from '../lib/constant';
import * as serverAPI from '../api/server'; // api/auth    
import {
    reducerUtils,
    handleAsyncActions,
    createPromiseSaga
} from '../lib/asyncUtils';

// `${PREFIX}/XXX`;
const PREFIX = 'SERVER';

/**
 * Server List Paging  (TR)
 * searchServer (SEARCH_SERVER) : 
 * clearSearchServer (CLEAR_SEARCH_SERVER) : 
 * setSearchServerName (SET_SEARCH_SERVER_NAME) :  
 * setSearchServerType (SET_SEARCH_SERVER_TYPE) :  
 */
const SEARCH_SERVER = `${PREFIX}/SEARCH_SERVER`;
const SEARCH_SERVER_SUCCESS = `${PREFIX}/SEARCH_SERVER_SUCCESS`;
const SEARCH_SERVER_ERROR = `${PREFIX}/SEARCH_SERVER_ERROR`;
const CLEAR_SEARCH_SERVER = `${PREFIX}/CLEAR_SEARCH_SERVER`;

const SET_SEARCH_SERVER_FORM = `${PREFIX}/SET_SEARCH_SERVER_FORM`;

export const searchServer = (searchForm) => ({
    type: SEARCH_SERVER,
    payload: searchForm
});

export const clearSearchServer = () => ({
    type: CLEAR_SEARCH_SERVER
});

export const initSearchServerForm = () => {
    return {
        serverName: '',
        serverType: '',
        page: INIT_PAGE,
        size: INIT_PAGE_SIZE
    };
};

export const setSearchServerForm = (searchForm) => ({
    type: SET_SEARCH_SERVER_FORM,
    payload: {
        searchForm
    }
});

/**
 *   
 */
const FIND_SERVER = `${PREFIX}/FIND_SERVER`;
const FIND_SERVER_SUCCESS = `${PREFIX}/FIND_SERVER_SUCCESS`;
const FIND_SERVER_ERROR = `${PREFIX}/FIND_SERVER_ERROR`;
const CLEAR_FIND_SERVER = `${PREFIX}/CLEAR_FIND_SERVER`;
export const findServer = (serverId) => ({
    type: FIND_SERVER,
    payload: {
        serverId
    }
});

export const clearFindServer = () => ({
    type: CLEAR_FIND_SERVER
});

/**
 *  Server 
 * createServer (CREATE_SERVER) : 
 * createServerDataClear (CREATE_SERVER_DATA_CLEAR) : 
 */
const CREATE_SERVER = `${PREFIX}/CREATE_SERVER`;
const CREATE_SERVER_SUCCESS = `${PREFIX}/CREATE_SERVER_SUCCESS`;
const CREATE_SERVER_ERROR = `${PREFIX}/CREATE_SERVER_ERROR`;
const CREATE_SERVER_DATA_CLEAR = `${PREFIX}/CREATE_SERVER_DATA_CLEAR`;

export const createServer = (createData) => ({
    type: CREATE_SERVER,
    payload: {
        ...createData
    }
});

export const createServerDataClear = () => ({
    type: CREATE_SERVER_DATA_CLEAR
});

/**
 * Server /  (MODAL)
 * setUpdateServerInitData (SET_UPDATE_SERVER_INITDATA) : SHOW
 * usedUpdateServerInitData (USED_UPDATE_SERVER_INITDATA) : CLEAR
 */
const SET_UPDATE_SERVER_INITDATA = `${PREFIX}/SET_UPDATE_SERVER_INITDATA`;
const USED_UPDATE_SERVER_INITDATA = `${PREFIX}/USED_UPDATE_SERVER_INITDATA`;

export const setUpdateServerInitData = (initData) => ({
    type: SET_UPDATE_SERVER_INITDATA,
    payload: {
        ...initData
    }
});

export const usedUpdateServerInitData = () => ({
    type: USED_UPDATE_SERVER_INITDATA
});

/**
 * Server  
 * updateServer (UPDATE_SERVER) : 
 * updateServerDataClear (UPDATE_SERVER_DATA_CLEAR) : 
 */
const UPDATE_SERVER = `${PREFIX}/UPDATE_SERVER`;
const UPDATE_SERVER_SUCCESS = `${PREFIX}/UPDATE_SERVER_SUCCESS`;
const UPDATE_SERVER_ERROR = `${PREFIX}/UPDATE_SERVER_ERROR`;
const UPDATE_SERVER_DATA_CLEAR = `${PREFIX}/UPDATE_SERVER_DATA_CLEAR`;

export const updateServer = (updateData) => ({
    type: UPDATE_SERVER,
    payload: {
        ...updateData
    }
});

export const updateServerDataClear = () => ({
    type: UPDATE_SERVER_DATA_CLEAR
});

/**
 * Server  
 * deleteServer (DELETE_SERVER) : 
 * deleteServerDataClear (DELETE_SERVER_DATA_CLEAR) : 
 */
const DELETE_SERVER = `${PREFIX}/DELETE_SERVER`;
const DELETE_SERVER_SUCCESS = `${PREFIX}/DELETE_SERVER_SUCCESS`;
const DELETE_SERVER_ERROR = `${PREFIX}/DELETE_SERVER_ERROR`;
const DELETE_SERVER_DATA_CLEAR = `${PREFIX}/DELETE_SERVER_DATA_CLEAR`;

export const deleteServer = (serverId) => ({
    type: DELETE_SERVER,
    payload: {
        serverId
    }
});

export const deleteServerDataClear = () => ({
    type: DELETE_SERVER_DATA_CLEAR
});

/**
 * Redux-saga 
 */
export function* serverSaga() {
    yield takeLatest(
        SEARCH_SERVER,
        createPromiseSaga(SEARCH_SERVER, serverAPI.searchServer)
    );
    yield takeEvery(
        FIND_SERVER,
        createPromiseSaga(FIND_SERVER, serverAPI.findServer)
    );
    yield takeEvery(
        CREATE_SERVER,
        createPromiseSaga(CREATE_SERVER, serverAPI.createServer)
    );
    yield takeEvery(
        UPDATE_SERVER,
        createPromiseSaga(UPDATE_SERVER, serverAPI.updateServer)
    );
    yield takeEvery(
        DELETE_SERVER,
        createPromiseSaga(DELETE_SERVER, serverAPI.deleteServer)
    );
}

/**
 * State 
 */
const initialState = {
    searchForm: initSearchServerForm(),
    updateFormInitData: {
        useData: true
    },
    search: reducerUtils.initial(),
    searchDetail: reducerUtils.initial(),
    create: reducerUtils.initial(),
    update: reducerUtils.initial(),
    delete: reducerUtils.initial()
};

export default function server(state = initialState, action) {
    switch (action.type) {
        case SET_SEARCH_SERVER_FORM:
            return {
                ...state,
                searchForm: {
                    ...state.searchForm,
                    ...action.payload.searchForm
                }
            };

        case SEARCH_SERVER:
        case SEARCH_SERVER_SUCCESS:
        case SEARCH_SERVER_ERROR:
            return handleAsyncActions(
                SEARCH_SERVER,
                'search',
                true
            )(state, action);
        case CLEAR_SEARCH_SERVER:
            return {
                ...state,
                search: reducerUtils.initial()
            };
        case FIND_SERVER:
        case FIND_SERVER_SUCCESS:
        case FIND_SERVER_ERROR:
            return handleAsyncActions(
                FIND_SERVER,
                'searchDetail',
                true
            )(state, action);

        case CLEAR_FIND_SERVER:
            return {
                ...state,
                searchDetail: reducerUtils.initial()
            };

        case CREATE_SERVER:
        case CREATE_SERVER_SUCCESS:
        case CREATE_SERVER_ERROR:
            return handleAsyncActions(
                CREATE_SERVER,
                'create',
                true
            )(state, action);
        case CREATE_SERVER_DATA_CLEAR:
            return {
                ...state,
                create: reducerUtils.initial()
            };
        case SET_UPDATE_SERVER_INITDATA:
            return {
                ...state,
                updateFormInitData: {
                    ...action.payload,
                    useData: false
                }
            };
        case USED_UPDATE_SERVER_INITDATA:
            return {
                ...state,
                updateFormInitData: {
                    ...state.updateFormInitData,
                    useData: true
                }
            };
        case UPDATE_SERVER:
        case UPDATE_SERVER_SUCCESS:
        case UPDATE_SERVER_ERROR:
            return handleAsyncActions(
                UPDATE_SERVER,
                'update',
                true
            )(state, action);
        case UPDATE_SERVER_DATA_CLEAR:
            return {
                ...state,
                update: reducerUtils.initial()
            };
        case DELETE_SERVER:
        case DELETE_SERVER_SUCCESS:
        case DELETE_SERVER_ERROR:
            return handleAsyncActions(
                DELETE_SERVER,
                'delete',
                true
            )(state, action);
        case DELETE_SERVER_DATA_CLEAR:
            return {
                ...state,
                delete: reducerUtils.initial()
            };
        default:
            return state;
    }
}
