import React from 'react';
import {Modal, Form, Row, Col, FloatingLabel, Accordion} from 'react-bootstrap';
import {useFormik} from 'formik';
import * as Yup from 'yup';
import {useTranslation} from 'react-i18next';
import Button from '../../components/button/Button';
import {
    printFormError,
    getInputValidMsg,
    takeSelectOptionbyCode
} from '../../lib/commonUiUtils';

const ServerUpdate = ({
    serverData,
    setServerData,
    updateLoading,
    onUpdate,
    modalShow,
    handleClose,
    onCheckDbConnection,
    codeData,
    serverList
}) => {
    const [t] = useTranslation();

    const formik = useFormik({
        enableReinitialize: true,
        initialValues: {...serverData},
        validationSchema: Yup.object({
            serverName: Yup.string().required(
                getInputValidMsg(t('pages.server.serverName'))
            )
        }),
        onSubmit: (values) => {
            onUpdate(values);
        }
    });

    const handleInputChange = (event) => {
        const {name, value} = event.target;
        setServerData({
            [name]: value
        });
    };

    const onClickConnectTest = () => {
        onCheckDbConnection(formik.values);
    };

    const renderSessionKey = () => {
        if (formik.values.loginProcess?.loginMethod === 'SESSION') {
            return (
                <FloatingLabel
                    label={`${t(
                        'pages.server.sessionKey'
                    )} ( default -> JSESSIONID )`}
                    className="mb-1"
                >
                    <Form.Control
                        type="text"
                        maxLength="30"
                        {...formik.getFieldProps('loginProcess.sessionKey')}
                    />
                </FloatingLabel>
            );
        }
        return '';
    };

    const renderUrl = (labelName) => {
        return (
            <FloatingLabel label={labelName} className="mb-2">
                <Form.Control
                    type="text"
                    {...formik.getFieldProps('url')}
                    maxLength="100"
                />
                {printFormError(formik, 'url')}
            </FloatingLabel>
        );
    };

    const renderDataFormat = () => {
        return (
            <Form.Group controlId="serverCreateForm.trCode">
                <FloatingLabel className="mb-2" label=" ">
                    <Form.Select
                        onChange={handleInputChange}
                        {...formik.getFieldProps('dataFormat')}
                        disabled={serverData?.id !== -1}
                    >
                        {takeSelectOptionbyCode(codeData, 'DataFormat')}
                    </Form.Select>
                </FloatingLabel>
                {printFormError(formik, 'dataFormat')}
            </Form.Group>
        );
    };

    /**
     * render http server
     * @returns
     */
    const renderHttpServer = () => {
        return (
            <>
                <Accordion defaultActiveKey="0">
                    <Accordion.Item eventKey="0">
                        <Accordion.Header className="accordion-header2">
                            Http  
                        </Accordion.Header>
                        <Accordion.Body>
                            <Row>
                                <Col xs="9">{renderUrl('URL')}</Col>
                                <Col xs="3">{renderDataFormat()}</Col>
                                <Col xs="4">
                                    <FloatingLabel
                                        label={t('pages.server.loginMethod')}
                                        className="mb-1"
                                    >
                                        <Form.Select
                                            {...formik.getFieldProps(
                                                'loginProcess.loginMethod'
                                            )}
                                        >
                                            {takeSelectOptionbyCode(
                                                codeData,
                                                'LoginMethod'
                                            )}
                                        </Form.Select>
                                    </FloatingLabel>
                                </Col>
                                <Col xs="8">{renderSessionKey()}</Col>
                            </Row>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
                <br />
            </>
        );
    };
    const renderServerSelect = () => {
        return serverList?.data
            ?.filter((obj) => obj.serverType === 'HTTP')
            .map((obj) => (
                <option value={obj.serverId} key={obj.serverId}>
                    {obj.serverName}
                </option>
            ));
    };
    // renderOptionsApServer
    /**
     * render UI server
     * @returns
     */
    const renderUiServer = () => {
        return (
            <>
                <Accordion defaultActiveKey="0">
                    <Accordion.Item eventKey="0">
                        <Accordion.Header className="accordion-header2">
                            UI Test 
                        </Accordion.Header>
                        <Accordion.Body>
                            <Row>
                                <Col xs="8">{renderUrl('URL (web)')}</Col>
                                <Col xs="4">
                                    <FloatingLabel label="Database">
                                        <Form.Select
                                            onChange={handleInputChange}
                                            {...formik.getFieldProps(
                                                'referenceServerId'
                                            )}
                                        >
                                            <option value="">
                                                {t('common.text.optionNone')}
                                            </option>
                                            {renderServerSelect()}
                                        </Form.Select>
                                        {printFormError(
                                            formik,
                                            'referenceServerId'
                                        )}
                                    </FloatingLabel>
                                </Col>
                            </Row>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
            </>
        );
    };

    /**
     * render database server
     * @returns
     */
    const renderDatabaseServer = () => {
        return (
            <>
                <Accordion defaultActiveKey="0">
                    <Accordion.Item eventKey="0">
                        <Accordion.Header className="accordion-header2">
                            Database 
                        </Accordion.Header>
                        <Accordion.Body>
                            <Row>
                                <Col xs="8">
                                    {renderUrl(t('pages.server.jdbcUrl'))}
                                </Col>
                                <Col xs="4">
                                    <FloatingLabel label="Database">
                                        <Form.Select
                                            onChange={handleInputChange}
                                            {...formik.getFieldProps('dbms')}
                                        >
                                            {takeSelectOptionbyCode(
                                                codeData,
                                                'DBMS'
                                            )}
                                        </Form.Select>
                                    </FloatingLabel>
                                </Col>
                                <Col xs="4">
                                    <FloatingLabel
                                        label={t('pages.server.connectId')}
                                        className="mb-2"
                                    >
                                        <Form.Control
                                            type="text"
                                            onChange={handleInputChange}
                                            maxLength="50"
                                            {...formik.getFieldProps(
                                                'connectionInfo.connectedId'
                                            )}
                                        />
                                    </FloatingLabel>
                                </Col>
                                <Col xs="4">
                                    <FloatingLabel
                                        label={t('pages.server.connectPw')}
                                        className="mb-1"
                                    >
                                        <Form.Control
                                            type="password"
                                            maxLength="50"
                                            onChange={handleInputChange}
                                            {...formik.getFieldProps(
                                                'connectionInfo.connectedPw'
                                            )}
                                        />
                                        {printFormError(
                                            formik,
                                            'connectPassword'
                                        )}
                                    </FloatingLabel>
                                </Col>
                                <Col xs="4">
                                    <br />
                                    <Button
                                        theme="danger"
                                        style={{
                                            width: '100px',
                                            float: 'right'
                                        }}
                                        onClick={onClickConnectTest}
                                    >
                                        Test
                                    </Button>
                                </Col>
                            </Row>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
            </>
        );
    };

    const renderTcpServer = () => {
        return (
            <>
                <Accordion defaultActiveKey="0">
                    <Accordion.Item eventKey="0">
                        <Accordion.Header className="accordion-header2">
                            TCP/IP 
                        </Accordion.Header>
                        <Accordion.Body>
                            <Row>
                                <Col xs="4">
                                    <FloatingLabel
                                        label="IP (Internet Protocol)"
                                        className="mb-1"
                                    >
                                        <Form.Control
                                            type="text"
                                            {...formik.getFieldProps(
                                                'ipPort.ip'
                                            )}
                                            maxLength="30"
                                        />
                                    </FloatingLabel>
                                </Col>
                                <Col xs="4">
                                    <FloatingLabel
                                        label="Port"
                                        className="mb-1"
                                    >
                                        <Form.Control
                                            type="number"
                                            {...formik.getFieldProps(
                                                'ipPort.port'
                                            )}
                                            maxLength="30"
                                        />
                                    </FloatingLabel>
                                </Col>
                                <Col xs="3">{renderDataFormat()}</Col>
                            </Row>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
            </>
        );
    };

    const renderDetailOption = () => {
        const {serverType} = formik.values;
        if (serverType === 'HTTP') {
            return renderHttpServer();
        }
        if (serverType === 'UI') {
            return renderUiServer();
        }
        if (serverType === 'DATABASE') {
            return renderDatabaseServer();
        }
        if (serverType === 'TCP_IP') {
            return renderTcpServer();
        }
        return 'Server Type   .';
    };

    return (
        <>
            <Modal
                show={modalShow}
                onHide={handleClose}
                onExited={() => formik.resetForm()}
                size="lg"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Form onSubmit={formik.handleSubmit}>
                    <Modal.Body>
                        <Form.Group style={{display: 'none'}}>
                            <Form.Label>ID</Form.Label>
                            <Form.Control
                                readOnly
                                type="number"
                                placeholder=""
                                onChange={handleInputChange}
                                {...formik.getFieldProps('id')}
                            />
                        </Form.Group>
                        <Accordion defaultActiveKey="0">
                            <Accordion.Item eventKey="0">
                                <Accordion.Header> </Accordion.Header>
                                <Accordion.Body>
                                    <Row>
                                        <Col xs="4">
                                            <FloatingLabel
                                                label={t(
                                                    'pages.server.serverType'
                                                )}
                                            >
                                                <Form.Select
                                                    onChange={handleInputChange}
                                                    {...formik.getFieldProps(
                                                        'serverType'
                                                    )}
                                                    disabled={
                                                        serverData?.id !== -1
                                                    }
                                                >
                                                    {takeSelectOptionbyCode(
                                                        codeData,
                                                        'ServerType'
                                                    )}
                                                </Form.Select>
                                            </FloatingLabel>
                                        </Col>
                                        <Col xs="8">
                                            <FloatingLabel
                                                label={t(
                                                    'pages.server.serverName'
                                                )}
                                                className="mb-1"
                                            >
                                                <Form.Control
                                                    type="text"
                                                    onChange={handleInputChange}
                                                    {...formik.getFieldProps(
                                                        'serverName'
                                                    )}
                                                    maxLength="30"
                                                />
                                                {printFormError(
                                                    formik,
                                                    'serverName'
                                                )}
                                            </FloatingLabel>
                                        </Col>
                                    </Row>
                                </Accordion.Body>
                            </Accordion.Item>
                        </Accordion>
                        <br />
                        {renderDetailOption()}
                    </Modal.Body>
                    <Modal.Footer>
                        <Button
                            theme="primary"
                            type="submit"
                            isLoading={updateLoading}
                        >
                            {t('common.button.save')}
                        </Button>
                        <Button theme="default" onClick={handleClose}>
                            {t('common.button.close')}
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>
        </>
    );
};
export default ServerUpdate;
