/* eslint-disable no-alert */
/* eslint-disable no-unused-vars */
import React from 'react';
import {
    Modal,
    Accordion,
    Row,
    Col,
    FloatingLabel,
    FormControl
} from 'react-bootstrap';
import Form from 'react-bootstrap/Form';
import {useTranslation} from 'react-i18next';
import {useFormik} from 'formik';
import * as Yup from 'yup';
import Button from '../../../components/button/Button';
import {
    printFormError,
    getSelectValidMsg,
    renderModalHeader,
    takeSelectOptionbyCode
} from '../../../lib/commonUiUtils';
import {
    renderOptionsApServer,
    renderOptionsDbServer
} from '../../../lib/codeUtils';

const ScheduleSave = ({
    modalShow,
    handleClose,
    scheduleData,
    codeData,
    serverData,
    onSubmit
}) => {
    const [t] = useTranslation();

    const offset = new Date().getTimezoneOffset() * 60000;
    const offset2 = 1 * 24 * 60 * 60 * 1000;
    const nextDay = new Date(Date.now() - offset + offset2)
        .toISOString()
        .slice(0, -1)
        .slice(0, 10);

    const formik = useFormik({
        enableReinitialize: true,
        initialValues: {
            scheduleId: scheduleData.scheduleId ?? -1,
            clubId: scheduleData.clubId,
            wasServerId: scheduleData.wasServerId ?? '',
            dbServerId: scheduleData.dbServerId ?? '',
            scheduleCond: {
                scheduleType:
                    scheduleData?.scheduleCond?.scheduleType ?? 'EVERYDAY',
                scheduleDay:
                    scheduleData?.scheduleCond?.scheduleDay ?? 'MONDAY',
                scheduleDate:
                    scheduleData?.scheduleCond?.scheduleDate ?? nextDay,
                scheduleTime:
                    scheduleData?.scheduleCond?.scheduleTime ?? '09:00'
            }
        },
        validationSchema: Yup.object({
            wasServerId: Yup.string().required(
                getSelectValidMsg(t('common.text.targetServer'))
            ),
            dbServerId: Yup.string().required(getSelectValidMsg('Database'))
        }),
        onSubmit: (values) => {
            if (
                values.scheduleCond.scheduleTime.toString().substr(4, 1) !== '0'
            ) {
                alert('10    .');
                return;
            }

            onSubmit(values);
        }
    });

    const renderSelectedDate = () => {
        return (
            <Col xs="4">
                <FloatingLabel
                    label={t('pages.scenario.selectedDate')}
                    className="mb-2"
                >
                    <Form.Control
                        type="date"
                        {...formik.getFieldProps('scheduleCond.scheduleDate')}
                    />

                    {printFormError(formik, 'scheduleCond.scheduleDate')}
                </FloatingLabel>
            </Col>
        );
    };

    const renderSelectedDay = () => {
        return (
            <Col xs="3">
                <FloatingLabel label={t('pages.scenario.selectedDay')}>
                    <Form.Select
                        {...formik.getFieldProps('scheduleCond.scheduleDay')}
                    >
                        {takeSelectOptionbyCode(codeData, 'ScheduleDay')}
                    </Form.Select>
                    {printFormError(formik, 'scheduleCond.scheduleDay')}
                </FloatingLabel>
            </Col>
        );
    };

    const renderSelectedTime = () => {
        return (
            <Col xs="4">
                <FloatingLabel
                    label={t('pages.scenario.selectedTime')}
                    className="mb-2"
                >
                    <Form.Control
                        type="time"
                        {...formik.getFieldProps('scheduleCond.scheduleTime')}
                    />
                </FloatingLabel>
            </Col>
        );
    };
    const renderBodyData = () => {
        if (scheduleData === null) {
            return '';
        }
        if (formik.values.scheduleCond.scheduleType === 'SELECTED_DATE') {
            return (
                <>
                    {renderSelectedDate()}
                    {renderSelectedTime()}
                </>
            );
        }
        if (formik.values.scheduleCond.scheduleType === 'SELECTED_DAY') {
            return (
                <>
                    {renderSelectedDay()}
                    {renderSelectedTime()}
                </>
            );
        }
        if (formik.values.scheduleCond.scheduleType === '') {
            return '';
        }

        return <>{renderSelectedTime()}</>;
    };
    return (
        <>
            <Modal
                show={modalShow}
                onHide={handleClose}
                size="lg"
                style={{maxHeight: '1000px'}}
                onExited={() => formik.resetForm()}
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Form onSubmit={formik.handleSubmit}>
                    {/* <Form> */}
                    {renderModalHeader(t('pages.scenario.scenarioSchedual'))}
                    <Modal.Body>
                        <Accordion defaultActiveKey="0">
                            <Accordion.Item eventKey="0">
                                <Accordion.Header className="">
                                    {t('pages.scenario.scenarioSchedual')}
                                </Accordion.Header>
                                <Accordion.Body>
                                    <Row>
                                        <Col xs="4">
                                            <FloatingLabel
                                                label={t(
                                                    'pages.scenario.scheduleType'
                                                )}
                                                className="mb-2"
                                            >
                                                <Form.Select
                                                    {...formik.getFieldProps(
                                                        'scheduleCond.scheduleType'
                                                    )}
                                                    disabled={
                                                        scheduleData.scheduleId >
                                                        0
                                                    }
                                                >
                                                    {takeSelectOptionbyCode(
                                                        codeData,
                                                        'ScheduleType'
                                                    )}
                                                </Form.Select>
                                            </FloatingLabel>
                                        </Col>
                                        <Col xs="4">
                                            <FloatingLabel
                                                label={t(
                                                    'common.text.targetServer'
                                                )}
                                                className="mb-2"
                                            >
                                                <Form.Select
                                                    {...formik.getFieldProps(
                                                        'wasServerId'
                                                    )}
                                                    disabled={
                                                        scheduleData.scheduleId >
                                                        0
                                                    }
                                                >
                                                    <option value="">
                                                        {t(
                                                            'common.text.optionNone'
                                                        )}
                                                    </option>
                                                    {renderOptionsApServer(
                                                        serverData
                                                    )}
                                                </Form.Select>
                                                {printFormError(
                                                    formik,
                                                    'wasServerId'
                                                )}
                                            </FloatingLabel>
                                        </Col>
                                        <Col xs="4">
                                            <FloatingLabel
                                                label="Database"
                                                className="mb-2"
                                            >
                                                <Form.Select
                                                    {...formik.getFieldProps(
                                                        'dbServerId'
                                                    )}
                                                    disabled={
                                                        scheduleData.scheduleId >
                                                        0
                                                    }
                                                >
                                                    <option value="">
                                                        {t(
                                                            'common.text.optionNone'
                                                        )}
                                                    </option>
                                                    {renderOptionsDbServer(
                                                        serverData
                                                    )}
                                                </Form.Select>
                                                {printFormError(
                                                    formik,
                                                    'dbServerId'
                                                )}
                                            </FloatingLabel>
                                        </Col>
                                    </Row>
                                </Accordion.Body>
                            </Accordion.Item>
                        </Accordion>
                        <br />
                        <Accordion defaultActiveKey="0">
                            <Accordion.Item eventKey="0">
                                <Accordion.Header className="accordion-header2">
                                    {formik.values.scheduleType}
                                </Accordion.Header>
                                <Accordion.Body>
                                    <Row>{renderBodyData()}</Row>
                                </Accordion.Body>
                            </Accordion.Item>
                        </Accordion>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button
                            theme="primary"
                            type="submit"
                            // isLoading={updateLoading}
                        >
                            {t('common.button.save')}
                        </Button>
                        <Button theme="default" onClick={handleClose}>
                            {t('common.button.close')}
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>
        </>
    );
};
export default ScheduleSave;
