/* eslint-disable no-unused-vars */
import React from 'react';
import {useSelector, useDispatch} from 'react-redux';
import UitestStepList from '@pages/uitest/UitestStepList';
import {
    setDetailCaseList,
    setStepUpdateInitData,
    setMoveStepInitData // Step  /
} from '../../store/uitest';
import UitestStepMoveContainer from './popup/UitestStepMoveContainer';

function UitestStepListContainer() {
    const dispatch = useDispatch();
    const {data: scenarioData} = useSelector(
        (state) => state.uitest.scenarioInfo
    );

    const {caseId} = useSelector((state) => state.uitest.selectedCaseId);
    const saveDetailCases = (cases) => {
        dispatch(setDetailCaseList(cases));
    };

    // //
    const showStepUpdatePopup = (data) => {
        dispatch(setStepUpdateInitData(data));
    };

    /*
     * onClickCopy ->  step  case .
     */
    const onClickMove = (record) => {
        if (scenarioData.data.caseList.length === 1) {
            // eslint-disable-next-line no-alert
            alert('  Case  .');
            return;
        }

        dispatch(setMoveStepInitData(record));
    };

    return (
        <>
            <UitestStepList
                scenarioData={scenarioData} // detail scenario data
                selectedCaseId={caseId} // list selected case id
                setDetailCases={saveDetailCases}
                showStepUpdatePopup={showStepUpdatePopup}
                onClickMove={onClickMove}
            />
            <UitestStepMoveContainer />
        </>
    );
}

export default UitestStepListContainer;
