/* eslint-disable no-unused-vars */
import React from 'react';
import {useSelector, useDispatch} from 'react-redux';
import BulkDetailHeader from '@app/pages/bulk/BulkDetailHeader';
import {usedBulkCompareValueModal} from '../../store/bulk';
import BulkCompareValueContainer from './popup/BulkCompareValueContainer';

/*
 *     
 */
function BulkDetailHeaderContainer() {
    const dispatch = useDispatch();

    const {selectedBulkInfo} = useSelector((state) => state.bulk);

    if (!selectedBulkInfo) {
        // history.push('/bulk-test');
        return <></>;
    }

    /**
     *     .
     */
    const onClickCompareValue = () => {
        dispatch(
            usedBulkCompareValueModal(true, {
                valueDiffCount: selectedBulkInfo.valueDiffCount,
                valueEmptyCount: selectedBulkInfo.valueEmptyCount,
                valueSameCount: selectedBulkInfo.valueSameCount,
                notTargetCount: selectedBulkInfo.notTargetCount
            })
        );
    };
    return (
        <>
            <BulkDetailHeader
                bulkTestGroupInfo={selectedBulkInfo}
                onClickCompareValue={onClickCompareValue}
            />
            {/*      */}
            <BulkCompareValueContainer />
        </>
    );
    ///
}

export default BulkDetailHeaderContainer;
