/* eslint-disable no-unused-vars */
import React, {useState} from 'react';
import {useTranslation} from 'react-i18next';
import {Row, Col, Form, FloatingLabel} from 'react-bootstrap';
import Card from '../../components/card/Card';
import Button from '../../components/button/Button';
import {takeSelectOptionbyServer} from '../../lib/commonUiUtils';

const StatTab1Search = ({
    serverData,
    searchFormT1,
    saveSearchFormT1,
    onSearch
}) => {
    const [t] = useTranslation();

    /*
     * form data change event
     */
    const onChangerSearchFormT1 = (e) => {
        const searchFormT1Temp = {...searchFormT1};
        searchFormT1Temp[e.target.id] = e.target.value;
        saveSearchFormT1(searchFormT1Temp);
    };

    /*
     * '' button evnet
     */
    const onClickSearch = (e) => {
        // serverId, fromDate, toDate
        e.preventDefault();
        onSearch();
    };

    const onCheckEnter = (e) => {
        if (e.key === 'Enter') {
            e.preventDefault();
            onSearch();
        }
    };

    return (
        <>
            <Card
                title={t('common.text.searchCardTitleName')}
                body={
                    <>
                        <Row onKeyPress={onCheckEnter}>
                            <Col xs="6">
                                <Row>
                                    <Col xs="4">
                                        <FloatingLabel
                                            label={t('common.text.serverInfo')}
                                            className="mb-2"
                                        >
                                            <Form.Select
                                                id="serverId"
                                                onChange={onChangerSearchFormT1}
                                                value={searchFormT1?.serverId}
                                            >
                                                <option value="-1">
                                                    {t('common.text.optionAll')}
                                                </option>
                                                {takeSelectOptionbyServer(
                                                    serverData,
                                                    'HTTP'
                                                )}
                                                {takeSelectOptionbyServer(
                                                    serverData,
                                                    'TCP_IP'
                                                )}
                                            </Form.Select>
                                        </FloatingLabel>
                                    </Col>
                                    <Col xs="4">
                                        <FloatingLabel
                                            label="From"
                                            className="mb-2"
                                        >
                                            <Form.Control
                                                type="date"
                                                id="fromDate"
                                                onChange={onChangerSearchFormT1}
                                                value={
                                                    searchFormT1 &&
                                                    searchFormT1.fromDate
                                                }
                                            />
                                        </FloatingLabel>
                                    </Col>
                                    <Col xs="4">
                                        <FloatingLabel
                                            label="To"
                                            className="mb-2"
                                        >
                                            <Form.Control
                                                type="date"
                                                id="toDate"
                                                onChange={onChangerSearchFormT1}
                                                value={
                                                    searchFormT1 &&
                                                    searchFormT1.toDate
                                                }
                                            />
                                        </FloatingLabel>
                                    </Col>
                                </Row>
                            </Col>
                            <Col xs="5" />
                            <Col xs="1">
                                <Form.Group>
                                    <Form.Label />
                                    <Button
                                        type="button"
                                        className="btn btn-block btn-secondary btn-search"
                                        // isLoading={searchLoading}
                                        onClick={onClickSearch}
                                    >
                                        {t('common.button.search')}
                                    </Button>
                                </Form.Group>
                            </Col>
                        </Row>
                    </>
                }
            />
        </>
    );
};

export default StatTab1Search;
