/* eslint-disable import/order */
/* eslint-disable no-unused-vars */
import React from 'react';
import Table from '../../components/table/Table';
import Button from '../../components/button/Button';
import {FaSearch, FaTrash, FaUnlockAlt, FaLock} from 'react-icons/fa';
import {useTranslation} from 'react-i18next';
import {changeToDate} from '../../lib/moment';

/*
 * ScenarioTable.js
 *   ->  search
 */
const ScenarioTable = ({data, movePage, moveUiTest, onDelete, onClickLock}) => {
    const [t] = useTranslation();

    const tableName = t('pages.scenario.scenarioList');
    const rowKey = 'scenarioId';

    const renderLockButton = (id, row) => {
        if (row.isLock) {
            return (
                <Button
                    theme="outline-primary"
                    onClick={() => onClickLock(id, row.isLock)}
                >
                    <FaLock />
                </Button>
            );
        }
        return (
            <Button
                theme="outline-primary"
                onClick={() => onClickLock(id, row.isLock)}
            >
                <FaUnlockAlt />
            </Button>
        );
    };

    const columns = [
        {
            title: t('pages.scenario.scenarioName'),
            key: 'scenarioName',
            width: '30%',
            align: 'left'
        },
        {
            title: '',
            width: '10%',
            key: 'manager',
            render: (id, row) => {
                return row.manager?.name;
            }
        },
        {
            title: ' ',
            key: 'createdDate',
            width: '8%',
            render: (id, row) => {
                return changeToDate(row.createdDate);
            }
        },
        {
            title: ' ',
            key: 'modifiedDate',
            width: '8%',
            render: (id, row) => {
                return changeToDate(row.modifiedDate);
            }
        },
        {
            title: '',
            key: 'button',
            width: '5%',
            align: 'right',
            render: (id, row) => {
                return (
                    <>
                        <Button
                            theme="outline-success"
                            onClick={() => moveUiTest(id)}
                        >
                            <FaSearch />
                        </Button>
                        {renderLockButton(id, row)}
                        <Button
                            theme="outline-danger"
                            onClick={() => onDelete(id)}
                        >
                            <FaTrash />
                        </Button>
                    </>
                );
            }
        }
    ];

    /*
     *  
     */
    return (
        <>
            <Table
                tableName={tableName}
                movePage={movePage}
                rowKey={rowKey}
                columns={columns}
                data={data}
                onDoubleClick={moveUiTest}
            />
        </>
    );
};

export default ScenarioTable;
