/* eslint-disable no-unused-vars */
import React, {useState, useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import UitestStepPopQuery from '@pages/uitest/step/UitestStepPopQuery';
import {
    setDetailCaseList,
    setStepUpdateInitData,
    saveScenario
} from '../../../store/uitest';
import {
    setTempKey,
    executeSqlTest,
    clearExecuteSqlTest
} from '../../../store/common';
// eslint-disable-next-line no-unused-vars
function UitestStepPopQueryContainer({handleClose}) {
    const dispatch = useDispatch();
    const {data: scenarioData} = useSelector(
        (state) => state.uitest.scenarioInfo
    );

    const {caseId} = useSelector((state) => state.uitest.selectedCaseId);
    const saveDetailCaseList = (caseList) => {
        dispatch(setDetailCaseList(caseList));
    };

    const {stepUpdateInitData} = useSelector((state) => state.uitest);
    const onChangeStepUpdataInitData = (data) => {
        dispatch(setStepUpdateInitData({...data}));
    };

    const {tempkey} = useSelector((state) => state.common);
    const setTempKeyRezen = () => {
        if (tempkey < -9999) {
            dispatch(setTempKey(-1));
        } else {
            dispatch(setTempKey(tempkey - 1));
        }
    };

    const {data: serverData} = useSelector(
        (state) => state.common.searchServer
    );

    const [selectedServer, setSelectedServer] = useState('');

    //    
    const onClickDbTest = (query) => {
        dispatch(executeSqlTest(Number(selectedServer), query));
    };

    const {
        data: executeSqlTestData,
        loading: executeSqlTestLoading,
        error: executeSqlTestError
    } = useSelector((state) => state.common.executeSqlTestData);

    const [resultData, setResultData] = React.useState(
        stepUpdateInitData?.outputData ?? ''
    );

    /*
     * Test    
     */
    useEffect(() => {
        if (!executeSqlTestLoading && executeSqlTestData) {
            setResultData(executeSqlTestData.data.dataOutput);
            dispatch(clearExecuteSqlTest());
        }
    }, [executeSqlTestData]);

    return (
        <>
            <UitestStepPopQuery
                scenarioData={scenarioData}
                selectedCaseId={caseId}
                setDetailCaseList={saveDetailCaseList}
                handleClose={handleClose}
                stepUpdateInitData={stepUpdateInitData}
                onChangeStepUpdataInitData={onChangeStepUpdataInitData}
                tempkey={tempkey}
                setTempKeyRezen={setTempKeyRezen}
                serverData={serverData}
                onClickDbTest={onClickDbTest}
                selectedServer={selectedServer}
                setSelectedServer={setSelectedServer}
                executeSqlTestLoading={executeSqlTestLoading}
                resultData={resultData}
            />
        </>
    );
}

export default UitestStepPopQueryContainer;
