/* eslint-disable import/named */
/* eslint-disable no-unused-vars */
import React, {useState} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import UitestScenarioCapture from '@pages/uitest/UitestScenarioCapture';
import {toast} from 'react-toastify';
import {getCookie} from '@app/lib/commonUiUtils';
import UitestResultPopupContainer from './popup/UitestResultPopupContainer';
import {setDetailCaseList, setUiTestInitData} from '../../store/uitest';
import {webSocketSend} from '../../lib/webSocketUtil';
import Spinner from '../../components/spinner/Spinner';

/*
 * uitest > python    
 */
// eslint-disable-next-line no-unused-vars
function UitestScenarioCaptureContainer() {
    const dispatch = useDispatch();
    // const {caseId} = useSelector((state) => state.uitest.selectedCaseId);

    const {caseId} = useSelector((state) => state.uitest.selectedCaseId);

    const {data: serverData} = useSelector(
        (state) => state.common.searchServer
    );

    // client module 
    const [client, setClient] = React.useState({
        isConnect: false,
        chromeVersion: '',
        isOpenServer: false,
        installDriver: false,
        isRecording: false
    });

    // URL 
    const [testUrl, setTestUrl] = useState('');

    // client module 
    const [loading, setLoading] = React.useState(false);
    const [modalShow, setModalShow] = React.useState(false);

    const handleClose = () => setModalShow(false);

    /*
     * aTworks client    + atworks client  
     */
    const handleShow = () => {
        // 2022-03-13 ///   
        // request data
        const requestJson = {
            action: 'pingTest'
        };

        // success callback
        const successCallback = (resJson) => {
            if (resJson.result) {
                let {isRecording} = client;
                if (!resJson.isOpenServer) {
                    isRecording = false;
                }
                setClient({
                    isConnect: true,
                    chromeVersion: resJson.chromeVersion,
                    isOpenServer: resJson.isOpenServer,
                    installDriver: resJson.installDriver,
                    isRecording
                });
                setModalShow(true);
            } else {
                setClient({
                    isConnect: false,
                    chromeVersion: '',
                    isOpenServer: false,
                    installDriver: false,
                    isRecording: false
                });

                setModalShow(true);
            }
        };

        // error callback
        const errorCallback = () => {
            setClient({
                isConnect: false,
                chromeVersion: '',
                isOpenServer: false,
                installDriver: false,
                isRecording: false
            });
            setModalShow(true);
        };
        webSocketSend(requestJson, setLoading, successCallback, errorCallback);
    };

    const wsStartWebPage = () => {
        // addStepByUitest(resultJson.list);
        const preClient = {...client};
        preClient.isRecording = false;
        setClient(preClient);

        // request data
        const requestJson = {
            action: 'openSite',
            url: testUrl,
            decstr: getCookie('decstr')
        };

        // success callback
        const successCallback = (resJson) => {
            if (resJson.result) {
                toast.success('Test UI  .');
                handleClose();
            } else {
                toast.error(resJson.message);
                handleClose();
            }
        };

        // error callback
        const errorCallback = () => {
            // setClient({
            //     isConnect: false,
            //     chromeVersion: ''
            // });
            // setModalShow(true);
            toast.error('   .');
        };
        webSocketSend(requestJson, setLoading, successCallback, errorCallback);
    };

    const wsInitRecord = () => {
        // request data
        const requestJson = {
            action: 'initRecord'
        };

        // success callback
        const successCallback = (resJson) => {
            if (resJson.result) {
                const preClient = {...client};
                preClient.isRecording = true;
                setClient(preClient);
                toast.success('  .');
            } else {
                // handleShow();
            }
        };

        // error callback
        const errorCallback = () => {
            setClient({
                isConnect: false,
                chromeVersion: ''
            });

            // handleShow();
        };

        webSocketSend(requestJson, setLoading, successCallback, errorCallback);
    };

    const wsStartRecord = () => {
        // request data
        const requestJson = {
            action: 'startRecord'
        };

        // success callback
        const successCallback = (resJson) => {
            if (resJson.result) {
                const preClient = {...client};
                preClient.isRecording = true;
                setClient(preClient);
                toast.success('  .');
            } else {
                handleShow();
            }
        };

        // error callback
        const errorCallback = () => {
            setClient({
                isConnect: false,
                chromeVersion: ''
            });

            handleShow();
        };

        webSocketSend(requestJson, setLoading, successCallback, errorCallback);
    };

    const wsStopRecord = () => {
        // request data
        const requestJson = {
            action: 'stopRecord'
        };

        // success callback
        const successCallback = (resJson) => {
            if (resJson.result) {
                // 20220319 kimtaehan    
                // 0     
                if (resJson.list.length > 0) {
                    //   case    .
                    dispatch(setUiTestInitData(resJson));
                } else {
                    toast.success('  .');
                }

                // addStepByUitest(resultJson.list);
                const preClient = {...client};
                preClient.isRecording = false;
                setClient(preClient);
            } else {
                toast.error('  .  ');
            }
        };

        // error callback
        const errorCallback = () => {
            toast.error('  .  ');
        };
        webSocketSend(requestJson, setLoading, successCallback, errorCallback);
    };

    /*
     * UI TEST  
     */
    const wsQuitWebPage = () => {
        // request data
        const requestJson = {
            action: 'closeSite'
        };

        // success callback
        const successCallback = (resJson) => {
            if (resJson.result) {
                // addStepByUitest(resultJson.list);
                const preClient = {...client};
                preClient.isRecording = false;
                setClient(preClient);
                handleClose();
                handleShow();
                //
            } else {
                // toast.error(resJson.message);
                toast.error(
                    ' .   .'
                );
                handleClose();
            }
        };

        // error callback
        const errorCallback = () => {
            toast.error(' .   .');
        };

        webSocketSend(requestJson, setLoading, successCallback, errorCallback);
    };

    /*
     * Chrome driver download
     */
    const wsDownloadDriver = () => {
        // request data
        const requestJson = {
            action: 'downloadDriver'
        };

        // success callback
        const successCallback = (resJson) => {
            if (resJson.result) {
                toast.success('  .');
                handleShow();
            } else {
                toast.error(resJson.message);
            }
        };

        // error callback
        const errorCallback = () => {
            toast.error('  .  ');
        };
        webSocketSend(requestJson, setLoading, successCallback, errorCallback);
    };
    return (
        <>
            <UitestResultPopupContainer />
            <UitestScenarioCapture
                modalShow={modalShow} // popup setting 1
                handleShow={handleShow} // popup setting 2
                handleClose={handleClose} // popup setting 3
                serverData={serverData}
                testUrl={testUrl} // URL  (get)
                setTestUrl={setTestUrl} // URL  (set)
                wsStartWebPage={wsStartWebPage} // start test Page
                wsQuitWebPage={wsQuitWebPage}
                wsStartRecord={wsStartRecord}
                wsStopRecord={wsStopRecord}
                wsInitRecord={wsInitRecord}
                wsDownloadDriver={wsDownloadDriver}
                client={client}
            />
            <Spinner isLoading={loading} />
        </>
    );
}

export default UitestScenarioCaptureContainer;
