/* eslint-disable no-unused-vars */
import React from 'react';
import {Form, Modal} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
import Button from '../../../components/button/Button';
import {renderModalHeader} from '../../../lib/commonUiUtils';
/*
 * StatTab1LayoutModal :     
 */
const StatRenewalModal = ({
    modalShow,
    handleShow,
    handleClose,
    dDay,
    setDday,
    onClickRenewal
}) => {
    const [t] = useTranslation();

    const onChangerDday = (e) => {
        e.preventDefault();

        setDday(e.target.value);
    };

    return (
        <>
            <Button theme="link" style={{float: 'right'}} onClick={handleShow}>
                 
            </Button>
            <Modal
                show={modalShow}
                onHide={handleClose}
                size="sm"
                // onExited={() => formik.resetForm()}
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                {/* <Form onSubmit={formik.handleSubmit}> */}
                <Form>
                    {renderModalHeader(' ')}

                    <Modal.Body>
                        <Form.Control
                            type="date"
                            value={dDay}
                            onChange={onChangerDday}
                            // value={searchFormT1 && searchFormT1.toDate}
                        />
                    </Modal.Body>
                    <Modal.Footer>
                        <Button theme="info" onClick={onClickRenewal}>
                            
                        </Button>
                        <Button theme="default" onClick={handleClose}>
                            {t('common.button.close')}
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>
        </>
    );
};
export default StatRenewalModal;
