import React, {useEffect} from 'react';
import RandomTable from '@pages/random/RandomTable';
import {useSelector, useDispatch} from 'react-redux';
import {useTranslation} from 'react-i18next';
import {toast} from 'react-toastify';
import {
    searchRandom,
    setSearchForm,
    usedRamdomSavaModal,
    deleteRandom,
    deleteRandomDataClear
} from '../../store/random';
import Spinner from '../../components/spinner/Spinner';
import {getErrorMsg} from '../../lib/commonUiUtils';

/*
 *   Container
 */
function RandomTableContainer() {
    const dispatch = useDispatch();
    const [t] = useTranslation();

    const {data: searchData} = useSelector((state) => state.random.search);
    const {searchForm} = useSelector((state) => state.random);
    const {
        data: deleteData,
        loading: deleteLoading,
        error: deleteError
    } = useSelector((state) => state.random.delete);

    const updateRandom = (row) => {
        dispatch(usedRamdomSavaModal(true, row.id));
    };

    const movePage = (page) => {
        const searchFormT = {...searchForm};
        searchFormT.page = page;
        dispatch(setSearchForm(searchFormT));
        dispatch(searchRandom(searchFormT));
    };

    /*
     *     .
     */
    const onDelete = (id) => {
        // eslint-disable-next-line no-alert
        if (window.confirm(t('common.alert.checkDelete'))) {
            dispatch(deleteRandom(id));
        }
    };

    useEffect(() => {
        if (!deleteLoading && deleteError) {
            toast.error(getErrorMsg(deleteError, 'search'));
            dispatch(deleteRandomDataClear());
        }
        if (!deleteLoading && deleteData) {
            // toast.error(getErrorMsg(deleteError, 'search'));
            // dispatch(deleteRandomDataClear());
            dispatch(deleteRandomDataClear());
            dispatch(searchRandom(searchForm.key, searchForm.randName));
            toast.success(t('common.alert.updateSuccess'));
        }
    }, [deleteData, deleteError]);
    return (
        <>
            <Spinner isLoading={deleteLoading} />
            <RandomTable
                data={searchData}
                updateRandom={updateRandom}
                movePage={movePage}
                onDelete={onDelete}
            />
        </>
    );
    ///
}

export default RandomTableContainer;
