/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import {useTranslation} from 'react-i18next';
import ClubTable from '@app/pages/club/ClubTable';
import {toast} from 'react-toastify';
import Spinner from '../../components/spinner/Spinner';
import {getErrorMsg} from '../../lib/commonUiUtils';
import {
    deleteClub,
    clearDeleteClub,
    setSearchClubForm,
    searchClub,
    usedClubLockModal
} from '../../store/club';
/*
 *   Container
 */
function ClubTableContainer({onSearch, onMoveDetail}) {
    const dispatch = useDispatch();
    const [t] = useTranslation();
    const {data: codeData} = useSelector((state) => state.common.searchCode);

    //  
    const {data: searchData, loading: searchLoading} = useSelector(
        (state) => state.club.searchClubRes
    );
    const {searchClubForm} = useSelector((state) => state.club);
    const movePage = (page) => {
        const searchClubFormT = {...searchClubForm};
        searchClubFormT.page = page;
        dispatch(setSearchClubForm(searchClubFormT));
        dispatch(searchClub(searchClubFormT));
    };

    const onClickDelete = (id) => {
        // eslint-disable-next-line no-alert
        if (window.confirm(t('common.alert.checkDelete'))) {
            dispatch(deleteClub(id));
        }
    };

    const onClickLock = (id, lock) => {
        const date = {
            clubId: id,
            isLock: lock,
            parent: 'club'
        };

        dispatch(usedClubLockModal(true, date));
    };

    const {
        loading: deleteLoading,
        data: deleteResponse,
        error: deleteError
    } = useSelector((state) => state.club.deleteClubRes);

    useEffect(() => {
        if (!deleteLoading && deleteResponse) {
            dispatch(clearDeleteClub());
            toast.success(t('common.alert.updateSuccess'));
            onSearch();
        }
        // fail
        if (!deleteLoading && deleteError) {
            dispatch(clearDeleteClub());
            toast.error(getErrorMsg(deleteError, 'save'));
        }
    }, [deleteResponse, deleteError]);

    return (
        <>
            <ClubTable
                movePage={movePage}
                data={searchData}
                onClickDetail={onMoveDetail}
                onClickDelete={onClickDelete}
                onClickLock={onClickLock}
                codeData={codeData}
            />
            <Spinner isLoading={deleteLoading} />
        </>
    );
    ///
}

export default ClubTableContainer;
