import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import ClientVersionManagement from '@pages/setting/ClientVersionManagement';
import {toast} from 'react-toastify';
import {useTranslation} from 'react-i18next';
import {
    uploadClientExecuteFile,
    clearUploadClientExecuteFile,
    searchNowClientInstallFileVersion,
    uploadClientInstallFile,
    clearUploadClientInstallFile
} from '../../store/setting';
import Spinner from '../../components/spinner/Spinner';
import {getErrorMsg} from '../../lib/commonUiUtils';

function ClientVersionManagementContainer() {
    const [t] = useTranslation();
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(searchNowClientInstallFileVersion());
        return () => {};
    }, []);

    const {
        loading: nowInstallVersionInfoLoading,
        data: nowInstallVersionInfoData
    } = useSelector((state) => state.setting.searchNowClientInstallFileVersion);

    const {
        loading: uploadExecuteFileLoading,
        data: uploadExecuteFileData,
        error: uploadExecuteFileError
    } = useSelector((state) => state.setting.uploadClientExecuteFile);

    const {
        loading: uploadInstallFileLoading,
        data: uploadInstallFileData,
        error: uploadInstallFileError
    } = useSelector((state) => state.setting.uploadClientInstallFile);

    const onUploadClientExecuteFile = (file) => {
        dispatch(uploadClientExecuteFile(file));
    };
    const onUploadClientInstallFile = (newVersion, file) => {
        dispatch(uploadClientInstallFile(newVersion, file));
    };

    /*
     *   
     */
    useEffect(() => {
        /* Success process */
        if (!uploadExecuteFileLoading && uploadExecuteFileData) {
            toast.success(t('common.alert.uploadSuccess'));
            dispatch(clearUploadClientExecuteFile());
        }
        /* Fail process */
        if (!uploadExecuteFileLoading && uploadExecuteFileError) {
            toast.error(
                getErrorMsg(
                    uploadExecuteFileError,
                    t('common.alert.uploadFail')
                )
            );

            dispatch(clearUploadClientExecuteFile());
        }
    }, [uploadExecuteFileData, uploadExecuteFileError]);

    /*
     *   
     */
    useEffect(() => {
        if (!uploadInstallFileLoading && uploadInstallFileError) {
            toast.error(
                getErrorMsg(
                    uploadInstallFileError,
                    t('common.alert.uploadFail')
                )
            );
            dispatch(clearUploadClientInstallFile());
            dispatch(searchNowClientInstallFileVersion());
        }

        if (!uploadInstallFileLoading && uploadInstallFileData) {
            toast.success(t('common.alert.uploadSuccess'));
            dispatch(clearUploadClientInstallFile());
            dispatch(searchNowClientInstallFileVersion());
        }
    }, [uploadInstallFileData, uploadInstallFileError]);

    return (
        <>
            <ClientVersionManagement
                nowInstallVersionInfoData={nowInstallVersionInfoData}
                uploadClientExecuteFile={onUploadClientExecuteFile}
                uploadClientInstallFile={onUploadClientInstallFile}
            />
            <Spinner
                isLoading={
                    nowInstallVersionInfoLoading ||
                    uploadExecuteFileLoading ||
                    uploadInstallFileLoading
                }
            />
        </>
    );
    ///
}

export default ClientVersionManagementContainer;
