/* eslint-disable no-unused-vars */
import React from 'react';
import Modal from 'react-bootstrap/Modal';
import {Form, Row, Col, InputGroup, FloatingLabel} from 'react-bootstrap';
import {useFormik} from 'formik';
import * as Yup from 'yup';
import {useTranslation} from 'react-i18next';
import {FaSearch} from 'react-icons/fa';
import Button from '../../../components/button/Button';
import {
    printFormError,
    getInputValidMsg,
    renderModalHeader
} from '../../../lib/commonUiUtils';
import ServerUpdateContainer from '../../../containers/server/ServerUpdateContainer';
import {
    renderOptionsApServer,
    renderOptionsDbServer
} from '../../../lib/codeUtils';

/*
 * step   case /    
 */
const ClubExecutePopup = ({
    modalShow,
    handleClose,
    serverData,
    onExecuteClub,
    onClickServerInfo
}) => {
    const [t] = useTranslation();

    const formik = useFormik({
        enableReinitialize: true,
        initialValues: {
            wasServerId: '',
            dbServerId: ''
        },
        validationSchema: Yup.object({
            wasServerId: Yup.string().required(getInputValidMsg('')),
            dbServerId: Yup.string().required(getInputValidMsg(''))
        }),
        onSubmit: (values) => {
            onExecuteClub(values);
        }
    });

    return (
        <>
            <Modal
                show={modalShow}
                onHide={handleClose}
                onExited={() => formik.resetForm()}
                dialogClassName="modal-30w"
                // aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Form onSubmit={formik.handleSubmit}>
                    {renderModalHeader(' ')}
                    <Modal.Body>
                        <Row>
                            <Col xs="6">
                                <InputGroup>
                                    <FloatingLabel label="http ">
                                        <Form.Select
                                            {...formik.getFieldProps(
                                                'wasServerId'
                                            )}
                                        >
                                            <option value="">
                                                {t('common.text.optionNone')}
                                            </option>
                                            {renderOptionsApServer(serverData)}
                                        </Form.Select>
                                    </FloatingLabel>
                                    <Button
                                        type="button"
                                        style={{maxWidth: '50px'}}
                                        className="btn btn-block btn-secondary btn-search"
                                        onClick={(e) =>
                                            onClickServerInfo(
                                                formik.values.wasServerId
                                            )
                                        }
                                    >
                                        <FaSearch />
                                    </Button>
                                </InputGroup>
                                {printFormError(formik, 'wasServerId')}
                            </Col>
                            <Col xs="6">
                                <InputGroup>
                                    <FloatingLabel label="DB ">
                                        <Form.Select
                                            {...formik.getFieldProps(
                                                'dbServerId'
                                            )}
                                        >
                                            <option value="">
                                                {t('common.text.optionNone')}
                                            </option>
                                            {renderOptionsDbServer(serverData)}
                                        </Form.Select>
                                    </FloatingLabel>
                                    <Button
                                        type="button"
                                        style={{maxWidth: '50px'}}
                                        className="btn btn-block btn-secondary btn-search"
                                        onClick={(e) =>
                                            onClickServerInfo(
                                                formik.values.dbServerId
                                            )
                                        }
                                    >
                                        <FaSearch />
                                    </Button>
                                </InputGroup>
                                {printFormError(formik, 'dbServerId')}
                            </Col>
                        </Row>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button theme="primary" type="submit">
                            
                        </Button>
                        <Button theme="default" onClick={handleClose}>
                            {t('common.button.close')}
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>
            <ServerUpdateContainer />
        </>
    );
};

export default ClubExecutePopup;
