import React from 'react';
import {useSelector, useDispatch} from 'react-redux';
import UitestCaseList from '@pages/uitest/UitestCaseList';
import {setDetailCaseList, setSelectedCaseId} from '../../store/uitest';
import {usedScenarioTestModal} from '../../store/common';

function UitestCaseListContainer() {
    const dispatch = useDispatch();
    const {data: scenarioData} = useSelector(
        (state) => state.uitest.scenarioInfo
    );

    const saveDetailCaseList = (caseList) => {
        dispatch(setDetailCaseList(caseList));
    };

    const saveSelectedCaseId = (id) => {
        dispatch(setSelectedCaseId(id));
    };

    const {caseId} = useSelector((state) => state.uitest.selectedCaseId);

    //    
    const onClickPlay = (row) => {
        const stepCnt = row.stepList.length;

        if (stepCnt === 0) {
            // eslint-disable-next-line no-alert
            alert('  Step  .');
            return;
        }

        const cazeList = scenarioData.data.caseList.filter(
            (info) => info.id === row.id
        );
        const data = {
            //  
            scenarioId: scenarioData.data.id,
            scenarioName: scenarioData.data.scenarioName,
            caseList: cazeList,
            variableList: scenarioData.data.variableList,
            parentId: 'UitestScenario'
        };

        dispatch(usedScenarioTestModal(true, data));
    };

    return (
        <>
            <UitestCaseList
                scenarioData={scenarioData}
                setDetailCaseList={saveDetailCaseList}
                setSelectedCaseId={saveSelectedCaseId}
                onClickPlay={onClickPlay}
                caseId={caseId}
            />
        </>
    );
}

export default UitestCaseListContainer;
