/* eslint-disable no-unused-vars */
import React from 'react';
import Modal from 'react-bootstrap/Modal';
import {Form, Row, Col, FloatingLabel, Accordion} from 'react-bootstrap';
import {FaPlus} from 'react-icons/fa';
import {useTranslation} from 'react-i18next';
import Button from '../../../components/button/Button';
import {renderModalHeader} from '../../../lib/commonUiUtils';
import Table from '../../../components/table/Table';
import {changeToDate} from '../../../lib/moment';

const ClubAddScenarioPopup = ({
    modalShow,
    handleClose,
    onSearchScenarioInClub,
    searchScenariosInClubResponse,
    onClickAddScenario,
    movePage,
    searchScenariosInClubForm,
    onChangerFormData,
    onClickSearch,
    searchScenarioData
}) => {
    const [t] = useTranslation();

    const onCheckEnter = (e) => {
        if (e.key === 'Enter') {
            e.preventDefault();
            onSearchScenarioInClub();
        }
    };

    const columns = [
        {
            title: '',
            key: 'isClub',
            width: '8%',
            render: (id, row) => {
                const selectedRow = searchScenarioData.data.content.filter(
                    (obj) => obj.scenarioId === row.scenarioId
                ).length;
                if (selectedRow === 1) {
                    return (
                        <button
                            type="submit"
                            onClick={(e) => e.preventDefault()}
                            className="btn btn-success"
                        >
                            
                        </button>
                    );
                }
                return (
                    <button
                        type="submit"
                        onClick={(e) => e.preventDefault()}
                        className="btn btn-warning"
                    >
                        
                    </button>
                );
            }
        },

        {
            title: ' ',
            key: 'scenarioName',
            align: 'left' // left, center, right (default:center)
        },
        {
            title: '',
            width: '10%',
            key: 'manager',
            render: (id, row) => {
                return row.manager?.name;
            }
        },
        {
            title: ' ',
            key: 'modifiedDate',
            render: (id, row) => {
                return changeToDate(row.modifiedDate);
            }
        },
        {
            title: '',
            key: 'button',
            align: 'right',
            render: (id, row) => {
                return (
                    <>
                        <Button
                            theme="link"
                            disabled={row.isClub}
                            onClick={() => onClickAddScenario(row)}
                            style={{float: 'rigth'}}
                        >
                            <FaPlus />
                        </Button>
                    </>
                );
            }
        }
    ];

    return (
        <>
            <Modal
                show={modalShow}
                onHide={handleClose}
                aria-labelledby="contained-modal-title-vcenter"
                dialogClassName="modal-50w"
                centered
            >
                <Form>
                    {/* <Form> */}
                    {renderModalHeader('   ()')}

                    <Modal.Body>
                        <Accordion defaultActiveKey="0">
                            <Accordion.Item eventKey="0">
                                <Accordion.Header className="accordion-header1">
                                      ()
                                </Accordion.Header>
                                <Accordion.Body>
                                    <Row onKeyPress={onCheckEnter}>
                                        <Col xs="6">
                                            <FloatingLabel label=" ">
                                                <Form.Control
                                                    type="text"
                                                    maxLength="30"
                                                    id="scenarioName"
                                                    onChange={onChangerFormData}
                                                    value={
                                                        searchScenariosInClubForm?.scenarioName ??
                                                        ''
                                                    }
                                                />
                                            </FloatingLabel>
                                        </Col>
                                        <Col xs="6">
                                            <Button
                                                theme="primary"
                                                onClick={() => onClickSearch()}
                                            >
                                                
                                            </Button>
                                        </Col>
                                        {/*  */}
                                    </Row>
                                    <br />
                                    <Row>
                                        <Col xs="12">
                                            <Table
                                                isCard="N"
                                                movePage={movePage}
                                                rowKey="scenarioId"
                                                columns={columns}
                                                data={
                                                    searchScenariosInClubResponse
                                                }
                                            />
                                        </Col>
                                    </Row>
                                </Accordion.Body>
                            </Accordion.Item>
                        </Accordion>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button theme="default" onClick={handleClose}>
                            {t('common.button.close')}
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>
        </>
    );
};

export default ClubAddScenarioPopup;
