/* eslint-disable indent */
import {takeEvery, takeLatest} from 'redux-saga/effects';
import * as credentialInfoAPI from '../api/credentialInfo'; // api/credentialInfo    
import {
    reducerUtils,
    handleAsyncActions,
    createPromiseSaga
} from '../lib/asyncUtils';

// `${PREFIX}/XXXX`;
const PREFIX = 'CREDENTIAL';

/**
 * Server     (MODAL)
 * setSelectedServer (SET_SELECTED_SERVER) : SHOW
 * clearSelectedServer (CLEAR_SELECTED_SERVER) : CLEAR
 */
const SET_SELECTED_SERVER = `${PREFIX}/SET_SELECTED_SERVER`;
const CLEAR_SELECTED_SERVER = `${PREFIX}/CLEAR_SELECTED_SERVER`;

export const setSelectedServer = (selectedServer) => ({
    type: SET_SELECTED_SERVER,
    payload: {
        selectedServer: {
            ...selectedServer
        }
    }
});

export const clearSelectedServer = () => ({
    type: CLEAR_SELECTED_SERVER
});

/**
 *   ID/PW  (TR)
 * searchCredentialInfo (SEARCH_CREDENTIAL_INFO) : 
 * clearSearchCredentialInfo (CLEAR_SEARCH_CREDENTIAL_INFO) : 
 */
const SEARCH_CREDENTIAL_INFO = `${PREFIX}/SEARCH_CREDENTIAL_INFO`;
const SEARCH_CREDENTIAL_INFO_SUCCESS = `${PREFIX}/SEARCH_CREDENTIAL_INFO_SUCCESS`;
const SEARCH_CREDENTIAL_INFO_ERROR = `${PREFIX}/SEARCH_CREDENTIAL_INFO_ERROR`;
const CLEAR_SEARCH_CREDENTIAL_INFO = `${PREFIX}/CLEAR_SEARCH_CREDENTIAL_INFO`;

export const searchCredentialInfo = (serverId, page, size) => ({
    type: SEARCH_CREDENTIAL_INFO,
    payload: {
        serverId,
        page,
        size
    }
});

export const clearSearchCredentialInfo = () => ({
    type: CLEAR_SEARCH_CREDENTIAL_INFO
});

/**
 *   ID/PW  (TR)
 * createCredentialInfo (CREATE_CREDENTIAL_INFO) : 
 * clearCreateCredentialInfo (CLEAR_CREATE_CREDENTIAL_INFO) : 
 */
const CREATE_CREDENTIAL_INFO = `${PREFIX}/CREATE_CREDENTIAL_INFO`;
const CREATE_CREDENTIAL_INFO_SUCCESS = `${PREFIX}/CREATE_CREDENTIAL_INFO_SUCCESS`;
const CREATE_CREDENTIAL_INFO_ERROR = `${PREFIX}/CREATE_CREDENTIAL_INFO_ERROR`;
const CLEAR_CREATE_CREDENTIAL_INFO = `${PREFIX}/CLEAR_CREATE_CREDENTIAL_INFO`;

// create
export const createCredentialInfo = (createForm) => ({
    type: CREATE_CREDENTIAL_INFO,
    payload: createForm
});

export const clearCreateCredentialInfo = () => ({
    type: CLEAR_CREATE_CREDENTIAL_INFO
});

/**
 *   ID/PW  (TR)
 * updateCredentialInfo (UPDATE_CREDENTIAL_INFO) : 
 * clearUpdateCredentialInfo (CLEAR_UPDATE_CREDENTIAL_INFO) : 
 */
const UPDATE_CREDENTIAL_INFO = `${PREFIX}/UPDATE_CREDENTIAL_INFO`;
const UPDATE_CREDENTIAL_INFO_SUCCESS = `${PREFIX}/UPDATE_CREDENTIAL_INFO_SUCCESS`;
const UPDATE_CREDENTIAL_INFO_ERROR = `${PREFIX}/UPDATE_CREDENTIAL_INFO_ERROR`;
const CLEAR_UPDATE_CREDENTIAL_INFO = `${PREFIX}/CLEAR_UPDATE_CREDENTIAL_INFO`;

export const updateCredentialInfo = (updateForm) => ({
    type: UPDATE_CREDENTIAL_INFO,
    payload: updateForm
});

export const clearUpdateCredentialInfo = () => ({
    type: CLEAR_UPDATE_CREDENTIAL_INFO
});

/**
 *   ID/PW  (TR)
 * deleteCredentialInfo (DELETE_CREDENTIAL_INFO) : 
 * clearDeleteCredentialInfo (CLEAR_DELETE_CREDENTIAL_INFO) : 
 */
const DELETE_CREDENTIAL_INFO = `${PREFIX}/DELETE_CREDENTIAL_INFO`;
const DELETE_CREDENTIAL_INFO_SUCCESS = `${PREFIX}/DELETE_CREDENTIAL_INFO_SUCCESS`;
const DELETE_CREDENTIAL_INFO_ERROR = `${PREFIX}/DELETE_CREDENTIAL_INFO_ERROR`;
const CLEAR_DELETE_CREDENTIAL_INFO = `${PREFIX}/CLEAR_DELETE_CREDENTIAL_INFO`;

// delete
export const deleteCredentialInfo = (credentialId) => ({
    type: DELETE_CREDENTIAL_INFO,
    payload: {
        credentialId
    }
});

export const clearDeleteCredentialInfo = () => ({
    type: CLEAR_DELETE_CREDENTIAL_INFO
});

/**
 * Redux-saga 
 */
export function* credentialInfoSaga() {
    yield takeLatest(
        SEARCH_CREDENTIAL_INFO,
        createPromiseSaga(
            SEARCH_CREDENTIAL_INFO,
            credentialInfoAPI.searchCredentialInfo
        )
    );
    yield takeEvery(
        CREATE_CREDENTIAL_INFO,
        createPromiseSaga(
            CREATE_CREDENTIAL_INFO,
            credentialInfoAPI.createCredentialInfo
        )
    );
    yield takeEvery(
        UPDATE_CREDENTIAL_INFO,
        createPromiseSaga(
            UPDATE_CREDENTIAL_INFO,
            credentialInfoAPI.updateCredentialInfo
        )
    );
    yield takeEvery(
        DELETE_CREDENTIAL_INFO,
        createPromiseSaga(
            DELETE_CREDENTIAL_INFO,
            credentialInfoAPI.deleteCredentialInfo
        )
    );
}

/**
 * State 
 */
const initialState = {
    selectedServer: null,
    search: reducerUtils.initial(),
    create: reducerUtils.initial(),
    update: reducerUtils.initial(),
    delete: reducerUtils.initial()
};

export default function credentialInfo(state = initialState, action) {
    switch (action.type) {
        case SET_SELECTED_SERVER:
            return {
                ...state,
                selectedServer: action.payload.selectedServer
            };
        case CLEAR_SELECTED_SERVER:
            return {
                ...state,
                selectedServer: null
            };
        case SEARCH_CREDENTIAL_INFO:
        case SEARCH_CREDENTIAL_INFO_SUCCESS:
        case SEARCH_CREDENTIAL_INFO_ERROR:
            return handleAsyncActions(
                SEARCH_CREDENTIAL_INFO,
                'search',
                true
            )(state, action);
        case CLEAR_SEARCH_CREDENTIAL_INFO:
            return {
                ...state,
                search: reducerUtils.initial()
            };
        case CREATE_CREDENTIAL_INFO:
        case CREATE_CREDENTIAL_INFO_SUCCESS:
        case CREATE_CREDENTIAL_INFO_ERROR:
            return handleAsyncActions(
                CREATE_CREDENTIAL_INFO,
                'create',
                true
            )(state, action);
        case CLEAR_CREATE_CREDENTIAL_INFO:
            return {
                ...state,
                create: reducerUtils.initial()
            };
        case UPDATE_CREDENTIAL_INFO:
        case UPDATE_CREDENTIAL_INFO_SUCCESS:
        case UPDATE_CREDENTIAL_INFO_ERROR:
            return handleAsyncActions(
                UPDATE_CREDENTIAL_INFO,
                'update',
                true
            )(state, action);
        case CLEAR_UPDATE_CREDENTIAL_INFO:
            return {
                ...state,
                update: reducerUtils.initial()
            };
        case DELETE_CREDENTIAL_INFO:
        case DELETE_CREDENTIAL_INFO_SUCCESS:
        case DELETE_CREDENTIAL_INFO_ERROR:
            return handleAsyncActions(
                DELETE_CREDENTIAL_INFO,
                'delete',
                true
            )(state, action);
        case CLEAR_DELETE_CREDENTIAL_INFO:
            return {
                ...state,
                delete: reducerUtils.initial()
            };
        default:
            return state;
    }
}
