/* eslint-disable indent */
import {takeLatest} from 'redux-saga/effects';
import * as historyAPI from '../api/history'; // api/auth    
import {
    reducerUtils,
    handleAsyncActions,
    createPromiseSaga
} from '../lib/asyncUtils';

const PREFIX = 'HISTORY';

/**
 * History Paging 
 * searchHistory (SEARCH_HISTORY) : 
 * clearSearchHistory (CLEAR_SEARCH_HISTORY) : 
 * setSearchForm (SET_SEARCH_FORM) :  
 */
const SET_SEARCH_FORM = `${PREFIX}/SET_SEARCH_FORM`;
const SEARCH_HISTORY = `${PREFIX}/SEARCH_HISTORY`;
const SEARCH_HISTORY_SUCCESS = `${PREFIX}/SEARCH_HISTORY_SUCCESS`;
const SEARCH_HISTORY_ERROR = `${PREFIX}/SEARCH_HISTORY_ERROR`;
const CLEAR_SEARCH_HISTORY = `${PREFIX}/CLEAR_SEARCH_HISTORY`;

// 2022-02-22 kimtaehan  {} 
export const searchHistory = (data) => ({
    type: SEARCH_HISTORY,
    payload: data
});

export const clearSearchHistory = () => ({
    type: CLEAR_SEARCH_HISTORY
});

export const setSearchForm = (searchForm) => ({
    type: SET_SEARCH_FORM,
    payload: {
        searchForm
    }
});

/**
 * history id    
 * searchHistoryById (SEARCH_HISTORY_BYID) : 
 * clearSearchHistoryById (CLEAR_SEARCH_HISTORY_BYID) : 
 */
const SEARCH_HISTORY_BYID = `${PREFIX}/SEARCH_HISTORY_BYID`;
const SEARCH_HISTORY_BYID_SUCCESS = `${PREFIX}/SEARCH_HISTORY_BYID_SUCCESS`;
const SEARCH_HISTORY_BYID_ERROR = `${PREFIX}/SEARCH_HISTORY_BYID_ERROR`;
const CLEAR_SEARCH_HISTORY_BYID = `${PREFIX}/CLEAR_SEARCH_HISTORY_BYID`;

export const searchHistoryById = (historyId) => ({
    type: SEARCH_HISTORY_BYID,
    payload: {
        historyId
    }
});

export const clearSearchHistoryById = () => ({
    type: CLEAR_SEARCH_HISTORY_BYID
});

/**
 *   
 * usedExecuteFormInitData (SET_EXECUTE_FORM_INITDATA) : INIT CALL
 * setExecuteFormInitData (USED_EXECUTE_FORM_INITDATA) : CHANGE CALL
 */
const SET_EXECUTE_FORM_INITDATA = 'history/SET_EXECUTE_FORM_INITDATA';
const USED_EXECUTE_FORM_INITDATA = 'history/USED_EXECUTE_FORM_INITDATA';

export const usedExecuteFormInitData = () => ({
    type: USED_EXECUTE_FORM_INITDATA
});

export const setExecuteFormInitData = (initData) => ({
    type: SET_EXECUTE_FORM_INITDATA,
    payload: {
        ...initData
    }
});

/**
 * Redux-saga 
 */
export function* historySaga() {
    yield takeLatest(
        SEARCH_HISTORY,
        createPromiseSaga(SEARCH_HISTORY, historyAPI.searchHistory)
    );

    yield takeLatest(
        SEARCH_HISTORY_BYID,
        createPromiseSaga(SEARCH_HISTORY_BYID, historyAPI.searchHistoryById)
    );
}

export const getInitSearchForm = () => {
    const offset = new Date().getTimezoneOffset() * 60000 - 60 * 60 * 1000;
    const today = new Date(Date.now() - offset);
    const offset2 = 7 * 24 * 60 * 60 * 1000;
    const sevenDay = new Date(Date.now() - offset - offset2);
    return {
        logSnapShotId: '',
        serviceId: '',
        fromDateTime: sevenDay.toISOString().slice(0, -1).slice(0, 16),
        toDateTime: today.toISOString().slice(0, -1).slice(0, 16),
        regCode: 'atworks',
        systemResult: '',
        // isSuccess: '',
        serverId: -1,
        page: 1,
        size: 10
    };
};

/**
 * State 
 */
const initialState = {
    searchForm: getInitSearchForm(),
    executeFormInitData: {
        useData: true
    },
    search: reducerUtils.initial(),
    // execute: reducerUtils.initial(),
    searchById: reducerUtils.initial()
};

export default function history(state = initialState, action) {
    switch (action.type) {
        case SET_SEARCH_FORM:
            return {
                ...state,
                searchForm: action.payload.searchForm
            };

        case SEARCH_HISTORY:
        case SEARCH_HISTORY_SUCCESS:
        case SEARCH_HISTORY_ERROR:
            return handleAsyncActions(
                SEARCH_HISTORY,
                'search',
                true
            )(state, action);
        case CLEAR_SEARCH_HISTORY:
            return {
                ...state,
                search: reducerUtils.initial()
            };
        case SEARCH_HISTORY_BYID:
        case SEARCH_HISTORY_BYID_SUCCESS:
        case SEARCH_HISTORY_BYID_ERROR:
            return handleAsyncActions(
                SEARCH_HISTORY_BYID,
                'searchById',
                true
            )(state, action);
        case CLEAR_SEARCH_HISTORY_BYID:
            return {
                ...state,
                searchById: reducerUtils.initial()
            };

        case SET_EXECUTE_FORM_INITDATA:
            return {
                ...state,
                executeFormInitData: {
                    ...action.payload,
                    useData: false
                }
            };
        case USED_EXECUTE_FORM_INITDATA:
            return {
                ...state,
                executeFormInitData: {
                    ...state.executeFormInitData,
                    useData: true
                }
            };

        default:
            return state;
    }
}
