import React from 'react';
import {useTranslation} from 'react-i18next';
import {Row, Col, Form, FloatingLabel} from 'react-bootstrap';
import Button from '../../components/button/Button';
import Card from '../../components/card/Card';

const SourceSearch = ({
    onSearch,
    showAnalysisSourceModal,
    searchSourceSnapshotForm,
    onChangeEvent
}) => {
    const [t] = useTranslation();

    const onCheckEnter = (e) => {
        if (e.key === 'Enter') {
            e.preventDefault();
            onSearch();
        }
    };

    const onChangerSearchForm = (e) => {
        e.preventDefault();
        const searchSourceSnapshotFormT = {...searchSourceSnapshotForm};
        searchSourceSnapshotFormT[e.target.id] = e.target.value;
        onChangeEvent(searchSourceSnapshotFormT);
    };

    return (
        <Card
            title={t('common.text.searchCardTitleName')}
            body={
                <>
                    <Row onKeyPress={onCheckEnter}>
                        <Col xs="10">
                            <Row>
                                <Col xs="2">
                                    <FloatingLabel
                                        label="From"
                                        className="mb-2"
                                    >
                                        <Form.Control
                                            type="date"
                                            id="fromDate"
                                            onChange={onChangerSearchForm}
                                            value={
                                                searchSourceSnapshotForm?.fromDate
                                            }
                                        />
                                    </FloatingLabel>
                                </Col>
                                <Col xs="2">
                                    <FloatingLabel label="To" className="mb-2">
                                        <Form.Control
                                            type="date"
                                            id="toDate"
                                            onChange={onChangerSearchForm}
                                            value={
                                                searchSourceSnapshotForm?.toDate
                                            }
                                        />
                                    </FloatingLabel>
                                </Col>
                            </Row>
                        </Col>
                        <Col xs="1">
                            <Form.Group>
                                <Form.Label />
                                <Button
                                    type="button"
                                    className="btn btn-block btn-secondary btn-search"
                                    onClick={() => onSearch()}
                                >
                                    {t('common.button.search')}
                                </Button>
                            </Form.Group>
                        </Col>
                        <Col xs="1">
                            <Form.Group>
                                <Form.Label />
                                <Button
                                    type="button"
                                    className="btn btn-block btn-success btn-search"
                                    onClick={showAnalysisSourceModal}
                                >
                                    + 
                                </Button>
                            </Form.Group>
                        </Col>
                    </Row>
                </>
            }
        />
    );
};

export default SourceSearch;
