/* eslint-disable indent */
// import {takeLatest, takeEvery} from 'redux-saga/effects';
import {takeLatest, takeEvery} from 'redux-saga/effects';
import * as scheduleAPI from '../api/schedule';
import {
    reducerUtils,
    handleAsyncActions,
    createPromiseSaga
} from '../lib/asyncUtils';

const PREFIX = 'SCHEDULE';

/**
 * schedulSaveContainer : schedul create/update (MODAL)
 * usedSchedualSaveModal (USE_SCHEDULE_SAVE_MODAL) : SHOW
 * clearSchedualSaveModal (CLEAR_SCHEDULE_SAVE_MODAL) : CLEAR
 */
const USE_SCHEDULE_SAVE_MODAL = `${PREFIX}/USE_SCENARIO_SCHEDULE_MODAL`;
const CLEAR_SCHEDULE_SAVE_MODAL = `${PREFIX}/CLEAR_SCHEDULE_SAVE_MODAL`;

export const usedSchedualSaveModal = (showModal, initData) => ({
    type: USE_SCHEDULE_SAVE_MODAL,
    payload: {
        showModal,
        initData
    }
});

export const clearSchedualSaveModal = () => ({
    type: CLEAR_SCHEDULE_SAVE_MODAL
});

/**
 * (TR) Save scheduler (Uitest)
 * saveSchedule (SAVE_SCHEDULE) : 
 * clearSaveSchedule (CLEAR_SAVE_SCHEDULE) : 
 */
const SAVE_SCHEDULE = `${PREFIX}/SAVE_SCHEDULE`;
const SAVE_SCHEDULE_SUCCESS = `${PREFIX}/SAVE_SCHEDULE_SUCCESS`;
const SAVE_SCHEDULE_ERROR = `${PREFIX}/SAVE_SCHEDULE_ERROR`;
const CLEAR_SAVE_SCHEDULE = `${PREFIX}/CLEAR_SAVE_SCHEDULE`;

export const saveSchedule = (data) => ({
    type: SAVE_SCHEDULE,
    payload: {
        ...data
    }
});

export const clearSaveSchedule = () => ({
    type: CLEAR_SAVE_SCHEDULE
});

/**
 * (TR) Update scheduler (Uitest)
 * updateSchedule (UPDATE_SCHEDULE) : 
 * clearUpdateSchedule (CLEAR_UPDATE_SCHEDULE) : 
 */
const UPDATE_SCHEDULE = `${PREFIX}/UPDATE_SCHEDULE`;
const UPDATE_SCHEDULE_SUCCESS = `${PREFIX}/UPDATE_SCHEDULE_SUCCESS`;
const UPDATE_SCHEDULE_ERROR = `${PREFIX}/UPDATE_SCHEDULE_ERROR`;
const CLEAR_UPDATE_SCHEDULE = `${PREFIX}/CLEAR_UPDATE_SCHEDULE`;

export const updateSchedule = (data) => ({
    type: UPDATE_SCHEDULE,
    payload: {
        ...data
    }
});

export const clearUpdateSchedule = () => ({
    type: CLEAR_UPDATE_SCHEDULE
});

/**
 * (TR) Search Scenario scheduler (Uitest)
 * searchSchedule (SEARCH_SCHEDULE) : 
 * clearSearchSchedule (CLEAR_SEARCH_SCHEDULE) : 
 * setSearchScheduleForm (SET_SEARCH_SCHEDULE_FORM) :  
 * searchScheduleFormInit : form 
 */
const SEARCH_SCHEDULE = `${PREFIX}/SEARCH_SCHEDULE`;
const SEARCH_SCHEDULE_SUCCESS = `${PREFIX}/SEARCH_SCHEDULE_SUCCESS`;
const SEARCH_SCHEDULE_ERROR = `${PREFIX}/SEARCH_SCHEDULE_ERROR`;
const CLEAR_SEARCH_SCHEDULE = `${PREFIX}/CLEAR_SEARCH_SCHEDULE`;
const SET_SEARCH_SCHEDULE_FORM = `${PREFIX}/SET_SEARCH_SCHEDULE_FORM`;

export const searchSchedule = (data) => ({
    type: SEARCH_SCHEDULE,
    payload: data
});

export const clearSearchSchedule = () => ({
    type: CLEAR_SEARCH_SCHEDULE
});

export const setSearchScheduleForm = (searchScheduleForm) => ({
    type: SET_SEARCH_SCHEDULE_FORM,
    payload: {
        searchScheduleForm
    }
});

export const searchScheduleFormInit = () => {
    return {
        clubName: '',
        manager: '',
        scheduleType: '',
        page: 1,
        size: 10
    };
};

/**
 * (TR) DELETE Scenario scheduler
 * deleteSchedule (DELETE_SCHEDULE) : 
 * deleteScheduleClear (CLEAR_DELETE_SCHEDULE) : 
 */
const DELETE_SCHEDULE = `${PREFIX}/DELETE_SCHEDULE`;
const DELETE_SCHEDULE_SUCCESS = `${PREFIX}/DELETE_SCHEDULE_SUCCESS`;
const DELETE_SCHEDULE_ERROR = `${PREFIX}/DELETE_SCHEDULE_ERROR`;
const CLEAR_DELETE_SCHEDULE = `${PREFIX}/CLEAR_DELETE_SCHEDULE`;

export const deleteSchedule = (id) => ({
    type: DELETE_SCHEDULE,
    payload: {
        id
    }
});

export const clearDeleteSchedule = () => ({
    type: CLEAR_DELETE_SCHEDULE
});

/**
 * Redux-saga 
 */
export function* scheduleSaga() {
    /**
     * (TR) Save scheduler (Uitest)
     * saveSchedule (SAVE_SCHEDULE) : 
     * clearSaveSchedule (CLEAR_SAVE_SCHEDULE) : 
     */
    yield takeEvery(
        SAVE_SCHEDULE,
        createPromiseSaga(SAVE_SCHEDULE, scheduleAPI.saveSchedule)
    );

    /**
     * (TR) Update scheduler (Uitest)
     * updateSchedule (UPDATE_SCHEDULE) : 
     * clearUpdateSchedule (CLEAR_UPDATE_SCHEDULE) : 
     */
    yield takeEvery(
        UPDATE_SCHEDULE,
        createPromiseSaga(UPDATE_SCHEDULE, scheduleAPI.updateSchedule)
    );

    /**
     * (TR) Save Scenario scheduler (Uitest)
     * searchSchedule (SEARCH_SCHEDULE) : 
     * clearSearchSchedule (CLEAR_SEARCH_SCHEDULE) : 
     * setSearchScheduleForm (SET_SEARCH_SCHEDULE_FORM) :  
     * searchScheduleFormInit : form 
     */
    yield takeLatest(
        SEARCH_SCHEDULE,
        createPromiseSaga(SEARCH_SCHEDULE, scheduleAPI.searchSchedule)
    );

    /**
     * (TR) DELETE Scenario scheduler
     * deleteSchedule (DELETE_SCHEDULE) : 
     * deleteScheduleClear (CLEAR_DELETE_SCHEDULE) : 
     */
    yield takeLatest(
        DELETE_SCHEDULE,
        createPromiseSaga(DELETE_SCHEDULE, scheduleAPI.deleteSchedule)
    );
}

/**
 * State 
 */
const initialState = {
    schedualSaveModalInitData: {
        showModal: false,
        initData: {}
    },
    saveScheduleRes: reducerUtils.initial(),
    updateScheduleRes: reducerUtils.initial(),
    searchScheduleForm: searchScheduleFormInit(),
    searchScheduleRes: reducerUtils.initial(),
    deleteScheduleRes: reducerUtils.initial()
};

export default function schedule(state = initialState, action) {
    switch (action.type) {
        case USE_SCHEDULE_SAVE_MODAL:
            return {
                ...state,
                schedualSaveModalInitData: {
                    showModal: action.payload.showModal,
                    initData: action.payload.initData
                }
            };

        case CLEAR_SCHEDULE_SAVE_MODAL:
            return {
                ...state,
                schedualSaveModalInitData: {
                    showModal: false,
                    initData: {}
                }
            };

        /**
         * (TR) Save scheduler (Uitest)
         * saveSchedule (SAVE_SCHEDULE) : 
         * clearSaveSchedule (CLEAR_SAVE_SCHEDULE) : 
         */
        case SAVE_SCHEDULE:
        case SAVE_SCHEDULE_SUCCESS:
        case SAVE_SCHEDULE_ERROR:
            return handleAsyncActions(
                SAVE_SCHEDULE,
                'saveScheduleRes',
                true
            )(state, action);

        case CLEAR_SAVE_SCHEDULE:
            return {
                ...state,
                saveScheduleRes: reducerUtils.initial()
            };

        /**
         * (TR) Update scheduler (Uitest)
         * updateSchedule (UPDATE_SCHEDULE) : 
         * clearUpdateSchedule (CLEAR_UPDATE_SCHEDULE) : 
         */
        case UPDATE_SCHEDULE:
        case UPDATE_SCHEDULE_SUCCESS:
        case UPDATE_SCHEDULE_ERROR:
            return handleAsyncActions(
                UPDATE_SCHEDULE,
                'updateScheduleRes',
                true
            )(state, action);

        case CLEAR_UPDATE_SCHEDULE:
            return {
                ...state,
                updateScheduleRes: reducerUtils.initial()
            };

        /**
         * (TR) Save Scenario scheduler (Uitest)
         * searchSchedule (SEARCH_SCHEDULE) : 
         * clearSearchSchedule (CLEAR_SEARCH_SCHEDULE) : 
         * setSearchScheduleForm (SET_SEARCH_SCHEDULE_FORM) :  
         */
        case SEARCH_SCHEDULE:
        case SEARCH_SCHEDULE_SUCCESS:
        case SEARCH_SCHEDULE_ERROR:
            return handleAsyncActions(
                SEARCH_SCHEDULE,
                'searchScheduleRes',
                true
            )(state, action);

        case CLEAR_SEARCH_SCHEDULE:
            return {
                ...state,
                searchScheduleRes: reducerUtils.initial()
            };

        case SET_SEARCH_SCHEDULE_FORM:
            return {
                ...state,
                searchScheduleForm: {
                    ...action.payload.searchScheduleForm
                }
            };

        /**
         * (TR) DELETE Scenario scheduler
         * deleteSchedule (DELETE_SCHEDULE) : 
         * deleteScheduleClear (CLEAR_DELETE_SCHEDULE) : 
         */
        case DELETE_SCHEDULE:
        case DELETE_SCHEDULE_SUCCESS:
        case DELETE_SCHEDULE_ERROR:
            return handleAsyncActions(
                DELETE_SCHEDULE,
                'deleteScheduleRes',
                true
            )(state, action);
        case CLEAR_DELETE_SCHEDULE:
            return {
                ...state,
                deleteScheduleRes: reducerUtils.initial()
            };

        default:
            return state;
    }
}
