/* eslint-disable import/named */
import React from 'react';
import {useSelector, useDispatch} from 'react-redux';
import UitestScenario from '@pages/uitest/UitestScenario';
import {setDetailScenarioName, setDetailManager} from '../../store/uitest';
import {usedScenarioLockModal} from '../../store/scenario';
import ScenarioLockContainer from '../scenario/popup/ScenarioLockContainer';
import MemberSelectContainer from '../common/MemberSelectContainer';

//    (sc)
import ScenarioTestPopupContainer from '../common/ScenarioTestPopupContainer';
import {usedScenarioTestModal, usedMemberSelectModal} from '../../store/common';

function UitestScenarioContainer({onSaveScenario}) {
    const dispatch = useDispatch();

    const {data: scenarioData} = useSelector(
        (state) => state.uitest.scenarioInfo
    );

    const onClickLock = () => {
        const data = {
            scenarioId: scenarioData.data.id,
            lock: scenarioData.data.lock,
            parent: 'uitest'
        };
        dispatch(usedScenarioLockModal(true, data));
    };

    const onClickManager = () => {
        dispatch(
            usedMemberSelectModal(true, scenarioData?.data.manager?.userId)
        );
    };

    const onClickPlay = () => {
        let stepCnt = 0;
        scenarioData.data.cases.forEach((caxe) => {
            if (caxe.steps) stepCnt += caxe.steps.length;
        });

        if (stepCnt === 0) {
            // eslint-disable-next-line no-alert
            alert('  Step  .');
            return;
        }

        const data = {
            //  
            scenarioId: scenarioData.data.scenarioId,
            scenarioName: scenarioData.data.scenarioName,
            cases: scenarioData.data.cases,
            variables: scenarioData.data.variables,
            parentId: 'UitestScenario'
        };

        dispatch(usedScenarioTestModal(true, data));
    };

    const saveDetailScenarioName = (scenarioName) => {
        dispatch(setDetailScenarioName(scenarioName));
    };

    const changeManager = (row) => {
        dispatch(setDetailManager(row));
    };

    // SAVE_SCENARIO_SUCCESS
    return (
        <>
            <UitestScenario
                scenarioData={scenarioData}
                setDetailScenarioName={saveDetailScenarioName}
                onSaveScenario={onSaveScenario}
                onClickPlay={onClickPlay}
                onClickLock={onClickLock}
                onClickManager={onClickManager}
            />
            <ScenarioTestPopupContainer />
            <ScenarioLockContainer />
            <MemberSelectContainer changeManager={changeManager} />
        </>
    );
}

export default UitestScenarioContainer;
