/* eslint-disable no-unused-vars */
import React from 'react';
import {useTranslation} from 'react-i18next';
import {FaSearch} from 'react-icons/fa';
import Table from '../../components/table/Table';
import Button from '../../components/button/Button';
import {displayNumber} from '../../lib/commonUiUtils';

const StatTab1Table = ({type, searchData, movePage, showStatDetailModal}) => {
    const [t] = useTranslation();

    /*
     *   title  .
     */
    const renderTitle = () => {
        if (type === 'cnt') {
            return t('pages.stat.tableTitleCnt');
        }
        if (type === 'rate') {
            return t('pages.stat.tableTitleRate');
        }
        if (type === 'time') {
            return t('pages.stat.tableTitleTime');
        }
        return '';
    };

    const columns = [
        {
            title: '',
            // key: renderKey()
            key: 'cnt',
            render: (id, row, column) => {
                if (type === 'cnt') {
                    return `${displayNumber(row.failCount)}${t(
                        'common.text.cases'
                    )} `;
                }
                if (type === 'rate') {
                    return `${row.failRate.toFixed(1)}%`;
                }
                if (type === 'time') {
                    return `${row.totalTimeAvgMs.toFixed(5)} ${t(
                        'common.text.second'
                    )}`;
                }
                return '';
            }
        },
        {
            title: '',
            align: 'left', // left, center, right (default:center)
            key: 'url',
            render: (id, row, column) => {
                return `${row.serviceId} (${displayNumber(row.totalCount)}) `;
            }
        },
        {
            title: '',
            key: 'button',
            align: 'right',
            render: (id, row, column) => {
                return (
                    <>
                        <Button
                            theme="outline-success"
                            onClick={(e) => showStatDetailModal(id, row)}
                        >
                            <FaSearch />
                        </Button>
                    </>
                );
            }
        }
    ];

    return (
        <>
            <Table
                tableName={renderTitle()}
                onDoubleClick={(id, row) => showStatDetailModal(id, row)}
                movePage={movePage}
                rowKey="layoutId"
                columns={columns}
                data={searchData}
                customTableHeader=""
                customTableHeaderRowCount={1}
            />
        </>
    );
};

export default StatTab1Table;
