import React from 'react';
import {useTranslation} from 'react-i18next';
import {Row, Col, Form, FloatingLabel} from 'react-bootstrap';
import Button from '../../components/button/Button';
import Card from '../../components/card/Card';
import {takeSelectOptionbyCode} from '../../lib/commonUiUtils';

const SourceDetailSearch = ({
    onSearch,
    searchSourcesForm,
    onChangeEvent,
    codeData
}) => {
    const [t] = useTranslation();

    const onCheckEnter = (e) => {
        if (e.key === 'Enter') {
            e.preventDefault();
            onSearch();
        }
    };

    // eslint-disable-next-line no-unused-vars
    const onChangerSearchForm = (e) => {
        e.preventDefault();
        const searchSourcesT = {...searchSourcesForm};
        searchSourcesT[e.target.id] = e.target.value;
        onChangeEvent(searchSourcesT);
    };

    return (
        <Card
            title={t('common.text.searchCardTitleName')}
            body={
                <>
                    <Row onKeyPress={onCheckEnter}>
                        <Col xs="11">
                            <Row>
                                <Col xs="2">
                                    <FloatingLabel
                                        label="Source Type"
                                        className="mb-2"
                                    >
                                        <Form.Select
                                            as="select"
                                            id="sourceType"
                                            onChange={onChangerSearchForm}
                                            value={
                                                searchSourcesForm?.sourceType
                                            }
                                        >
                                            <option value="">
                                                {t('common.text.optionAll')}
                                            </option>
                                            {takeSelectOptionbyCode(
                                                codeData,
                                                'SourceType'
                                            )}
                                        </Form.Select>
                                    </FloatingLabel>
                                </Col>
                                <Col xs="3">
                                    <FloatingLabel
                                        label="Class Name"
                                        className="mb-2"
                                    >
                                        <Form.Control
                                            type="text"
                                            id="sourceName"
                                            onChange={onChangerSearchForm}
                                            value={
                                                searchSourcesForm?.sourceName
                                            }
                                        />
                                    </FloatingLabel>
                                </Col>
                            </Row>
                        </Col>
                        <Col xs="1">
                            <Form.Group>
                                <Form.Label />
                                <Button
                                    type="button"
                                    className="btn btn-block btn-secondary btn-search"
                                    onClick={() => onSearch()}
                                >
                                    {t('common.button.search')}
                                </Button>
                            </Form.Group>
                        </Col>
                    </Row>
                </>
            }
        />
    );
};

export default SourceDetailSearch;
