import React from 'react';
import {Form, Row, Col, Accordion, FloatingLabel} from 'react-bootstrap';
import {printFormError} from '../../../lib/commonUiUtils';
// import {useTranslation} from 'react-i18next';

// eslint-disable-next-line no-unused-vars
const UitestScriptSleep = ({formik}) => {
    return (
        <Accordion defaultActiveKey="0">
            <Accordion.Item eventKey="0">
                <Accordion.Header className="accordion-header2">
                    Sleep 
                </Accordion.Header>
                <Accordion.Body>
                    <Row>
                        <Col xs="3">
                            <FloatingLabel
                                label=" (max 3000ms)"
                                className="mb-1"
                            >
                                <Form.Control
                                    type="number"
                                    {...formik.getFieldProps('script.sleepMs')}
                                    Min={0}
                                    Max={3000}
                                />
                                {printFormError(formik, 'script.sleepMs')}
                            </FloatingLabel>
                        </Col>
                    </Row>
                </Accordion.Body>
            </Accordion.Item>
        </Accordion>
    );
};

export default UitestScriptSleep;
