import {useSelector, useDispatch} from 'react-redux';
import React, {useEffect} from 'react';
import UitestStepUpdate from '@pages/uitest/UitestStepUpdate';
import {clearStepUpdateInitData, setExecuteList} from '../../store/uitest';
// eslint-disable-next-line no-unused-vars
function UitestStepUpdateContainer() {
    const dispatch = useDispatch();
    // popup show/hide
    const [modalShow, setModalShow] = React.useState(false);

    // step code setting
    // const {caseId} = useSelector((state) => state.uitest.selectedCaseId);
    const {stepUpdateInitData} = useSelector((state) => state.uitest);
    const handleClose = () => {
        dispatch(clearStepUpdateInitData());
        setModalShow(false);
    };

    const {data: scenarioData} = useSelector(
        (state) => state.uitest.scenarioInfo
    );
    const executeList = useSelector((state) => state.uitest.executeList);
    const saveExecuteList = (key, value) => {
        const newExecuteList = {
            ...executeList
        };
        newExecuteList[key] = value;
        dispatch(setExecuteList(newExecuteList));
    };
    const handleShow = () => {
        if (!modalShow) {
            const tempStepList = [];
            if (scenarioData.data.caseList) {
                scenarioData.data.caseList.forEach((caze, cazeIdx) => {
                    const {caseName} = caze;
                    if (caze.stepList) {
                        caze.stepList.forEach((step, stepIdx) => {
                            const tempStep = {...step};

                            // tempStep.outputData1 = '';
                            // tempStep.outputData2 = '';
                            tempStep.cazeIdx = cazeIdx;
                            tempStep.cazsId = caze.id;
                            tempStep.stepIdx = stepIdx;
                            tempStep.caseName = caseName;

                            if (tempStep.stepCode === 'xhr') {
                                tempStep.stepName = step.stepName;
                            } else {
                                tempStep.stepName = step.inputData2;
                            }

                            tempStep.seq = tempStepList.length;
                            tempStepList.push(tempStep);
                        });
                    }
                });
            }
            // setStepList(tempStepList);
            //   
            saveExecuteList('stepList', tempStepList);

            setModalShow(true);
        }
    };
    useEffect(() => {
        if (stepUpdateInitData.stepId) {
            handleShow();
        }
    }, [stepUpdateInitData.stepId]);

    return (
        <>
            <UitestStepUpdate
                modalShow={modalShow}
                handleClose={handleClose}
                stepUpdateInitData={stepUpdateInitData}
            />
            {/* <Spinner isLoading={saveLoading} /> */}
        </>
    );
}

export default UitestStepUpdateContainer;
