import React from 'react';
import {useSelector} from 'react-redux';

// non thread safety
// javascript  single thread  .
// [Multi Thread  ]
// https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Atomics
// https://webkit.org/blog/7846/concurrent-javascript-it-can-work/

// eslint-disable-next-line import/no-mutable-exports
export const renderOptionsByCode = (_group) => {
    const {data: enums} = useSelector((state) => state.common.searchCode);
    //   
    const selectedData = enums?.data.filter((obj) => obj.group === _group);
    if (selectedData.length === 0) return '';
    return selectedData[0]?.list?.map((obj) => (
        <option key={obj.name} value={obj.name}>
            {obj.value}
        </option>
    ));
};

export const renderOptionsByCode2 = (codeData, _group) => {
    //   
    const selectedData = codeData?.data.filter((obj) => obj.group === _group);
    if (selectedData.length === 0) return '';
    return selectedData[0]?.list?.map((obj) => (
        <option key={obj.name} value={obj.name}>
            {obj.value}
        </option>
    ));
};

export const getValue = (codeData, _group, _name) => {
    return codeData.data
        .filter((obj) => obj.group === _group)[0]
        .list?.filter((obj) => obj.name === _name)[0]?.value;
};

export const renderOptionsApServer = (serverData) => {
    //   
    return serverData?.data
        ?.filter(
            (obj) => obj.serverType === 'HTTP' || obj.serverType === 'TCP_ID'
        )
        .map((obj) => (
            <option value={obj.serverId} key={obj.serverId}>
                {obj.serverName}
            </option>
        ));
};

export const renderOptionsDbServer = (serverData) => {
    //   
    return serverData?.data
        ?.filter((obj) => obj.serverType === 'DATABASE')
        .map((obj) => (
            <option value={obj.serverId} key={obj.serverId}>
                {obj.serverName}
            </option>
        ));
};
