/* eslint-disable no-unused-vars */
import React from 'react';
import Modal from 'react-bootstrap/Modal';
import {Form, Row, Col, FloatingLabel, Accordion} from 'react-bootstrap';
import {useFormik} from 'formik';
import * as Yup from 'yup';
import {useTranslation} from 'react-i18next';
import Button from '../../../components/button/Button';
import {
    printFormError,
    getInputValidMsg,
    renderModalHeader
} from '../../../lib/commonUiUtils';
import {renderOptionsByCode2} from '../../../lib/codeUtils';

/*
 * step   case /    
 */
const ClubCreatePopup = ({
    modalShow,
    handleClose,
    codeData,
    onClickCreateClub
}) => {
    const [t] = useTranslation();

    const formik = useFormik({
        enableReinitialize: true,
        initialValues: {
            clubType: '',
            clubName: '',
            clubDesc: ''
        },
        validationSchema: Yup.object({
            clubName: Yup.string().required(getInputValidMsg('')),
            clubType: Yup.string().required(getInputValidMsg(''))
        }),
        onSubmit: (values) => {
            onClickCreateClub(values);
        }
    });

    return (
        <>
            <Modal
                show={modalShow}
                onHide={handleClose}
                onExited={() => formik.resetForm()}
                dialogClassName="modal-25w"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Form onSubmit={formik.handleSubmit}>
                    {renderModalHeader(' ')}
                    <Modal.Body>
                        <Accordion defaultActiveKey="0">
                            <Accordion.Item eventKey="0">
                                <Accordion.Header className="accordion-header3">
                                     
                                </Accordion.Header>
                                <Accordion.Body>
                                    <Row>
                                        <Col xs="4">
                                            <FloatingLabel
                                                label=" "
                                                className="mb-2"
                                            >
                                                <Form.Select
                                                    {...formik.getFieldProps(
                                                        'clubType'
                                                    )}
                                                >
                                                    <option value="">
                                                        {t(
                                                            'common.text.optionAll'
                                                        )}
                                                    </option>
                                                    {renderOptionsByCode2(
                                                        codeData,
                                                        'ClubType'
                                                    )}
                                                </Form.Select>
                                                {printFormError(
                                                    formik,
                                                    'clubType'
                                                )}
                                            </FloatingLabel>
                                        </Col>
                                        <Col xs="8">
                                            <FloatingLabel
                                                label=" "
                                                className="mb-2"
                                            >
                                                <Form.Control
                                                    type="text"
                                                    maxLength="30"
                                                    {...formik.getFieldProps(
                                                        'clubName'
                                                    )}
                                                />
                                                {printFormError(
                                                    formik,
                                                    'clubName'
                                                )}
                                            </FloatingLabel>
                                        </Col>
                                        <Col xs="12">
                                            <FloatingLabel
                                                label="Description"
                                                className="mb-2"
                                            >
                                                <Form.Control
                                                    as="textarea"
                                                    style={{height: '150px'}}
                                                    {...formik.getFieldProps(
                                                        'clubDesc'
                                                    )}
                                                />
                                            </FloatingLabel>
                                        </Col>
                                    </Row>
                                </Accordion.Body>
                            </Accordion.Item>
                        </Accordion>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button
                            theme="primary"
                            type="submit"
                            // isLoading={updateLoading}
                        >
                            
                        </Button>
                        <Button theme="default" onClick={handleClose}>
                            {t('common.button.close')}
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>
        </>
    );
};

export default ClubCreatePopup;
