/* eslint-disable no-unused-vars */
import React from 'react';
import Modal from 'react-bootstrap/Modal';
import {Form, Row, Col, FloatingLabel} from 'react-bootstrap';
import {useFormik} from 'formik';
import * as Yup from 'yup';
import {useTranslation} from 'react-i18next';
import Button from '../../../components/button/Button';

import {
    printFormError,
    getInputValidMsg,
    renderModalHeader
} from '../../../lib/commonUiUtils';

const UitestCaseCreate = ({
    scenarioData,
    setDetailCaseList,
    modalShow,
    handleShow,
    handleClose,
    tempkey,
    setTempKeyRezen
}) => {
    const [t] = useTranslation();
    // modal control

    const formik = useFormik({
        initialValues: {
            caseName: ''
        },
        validationSchema: Yup.object({
            caseName: Yup.string().required(
                getInputValidMsg(t('pages.scenario.caseName'))
            )
        }),
        onSubmit: (values) => {
            const data = [...scenarioData.data.cases];

            const valuesT = values;
            valuesT.caseId = tempkey;
            data.push(valuesT);

            const aLoop = data.map((unit, index) => {
                const temp = unit;
                temp.index = index;
                return temp;
            });

            setDetailCaseList(data);
            setTempKeyRezen();
            handleClose();
        }
    });

    return (
        <>
            <Button
                type="button"
                className="btn btn-block btn-secondary btn-search"
                onClick={handleShow}
            >
                {t('common.button.newCase')}
            </Button>
            <Modal
                show={modalShow}
                onHide={handleClose}
                // size="lg"
                onExited={() => formik.resetForm()}
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Form onSubmit={formik.handleSubmit}>
                    {renderModalHeader(t('pages.scenario.registerCase'))}
                    <Modal.Body>
                        <Row>
                            <Col xs="12">
                                <FloatingLabel
                                    label={t('pages.scenario.caseName')}
                                    className="mb-2"
                                >
                                    <Form.Control
                                        type="text"
                                        placeholder=""
                                        {...formik.getFieldProps('caseName')}
                                        maxLength="30"
                                    />
                                    {printFormError(formik, 'caseName')}
                                </FloatingLabel>
                            </Col>
                            <Col xs="12">
                                <FloatingLabel
                                    label={t('pages.scenario.caseDesc')}
                                    className="mb-2"
                                >
                                    <Form.Control
                                        as="textarea"
                                        style={{height: '200px'}}
                                        placeholder=""
                                        // onChange={handleInputChange}
                                        {...formik.getFieldProps('caseDesc')}
                                    />
                                </FloatingLabel>
                            </Col>
                        </Row>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button theme="primary" type="submit">
                            {t('common.button.register')}
                        </Button>
                        <Button theme="default" onClick={handleClose}>
                            {t('common.button.close')}
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>
        </>
    );
};

export default UitestCaseCreate;
