import React, {useState, useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import Main from '@modules/main/Main';
import PageLoading from '../../components/page-loading/PageLoading';
import {searchServerCommon, searchCode} from '../../store/common';
import {getUserInfo} from '../../store/auth';

function MainContainer() {
    const dispatch = useDispatch();

    const [appLoadingState, updateAppLoading] = useState(true);

    // common loading
    // const {showLoading} = useSelector((state) => state.commonUI);

    //    
    const {loading: memberInfoLoading} = useSelector(
        (state) => state.auth.memberInfo
    );

    const sleep = (ms) => {
        return new Promise((resolve) => setTimeout(resolve, ms));
    };

    useEffect(() => {
        dispatch(getUserInfo());
        dispatch(searchServerCommon(''));
        dispatch(searchCode());

        updateAppLoading(true);
        const fetchProfile = async () => {
            await sleep(1000);
            updateAppLoading(false);
        };
        fetchProfile();
        return () => {};
    }, []);

    if (appLoadingState || memberInfoLoading) {
        return <PageLoading />;
    }

    return (
        <>
            <Main />
            {/* <Spinner isLoading={showLoadingFlag} /> */}
        </>
    );
}

export default MainContainer;
