/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import ClubDetailHistory from '@pages/club/ClubDetailHistory';
import {useDispatch, useSelector} from 'react-redux';
import {toast} from 'react-toastify';
import {useTranslation} from 'react-i18next';
import {useHistory, useLocation} from 'react-router-dom';
import Spinner from '../../components/spinner/Spinner';

import {getErrorMsg} from '../../lib/commonUiUtils';
import {searchClubHistory, setSearchClubHistoryForm} from '../../store/club';

/*
 *  Container
 */
function ClubDetailHistoryContainer() {
    const dispatch = useDispatch();
    const location = useLocation();
    const history = useHistory();
    const [t] = useTranslation();

    const {searchClubHistoryForm} = useSelector((state) => state.club);

    const movePage = (page) => {
        const searchForm = {...searchClubHistoryForm};
        searchForm.page = page;
        dispatch(setSearchClubHistoryForm(searchForm));
        dispatch(searchClubHistory(searchForm));
    };

    const onClickMoveScHistory = (clubSnapshotId) => {
        history.push({
            pathname: '/scHistory',
            state: {
                clubSnapshotId
            }
        });
    };

    const {
        data: searchClubHistoryData,
        loading: searchClubHistoryLoading,
        error: searchClubHistoryError
    } = useSelector((state) => state.club.searchClubHistoryRes);

    return (
        <>
            <ClubDetailHistory
                searchClubHistoryData={searchClubHistoryData}
                movePage={movePage}
                onClickMoveScHistory={onClickMoveScHistory}
            />
            <Spinner isLoading={searchClubHistoryLoading} />
        </>
    );
}

export default ClubDetailHistoryContainer;
