import React from 'react';
import {Modal, Form, Row, Col, InputGroup, FormControl, FloatingLabel, Accordion} from 'react-bootstrap';
import {useFormik,} from 'formik'; 
import * as Yup from 'yup';
import {useTranslation} from 'react-i18next';
import Button from '../../components/button/Button';
import {printFormError, getInputValidMsg, renderModalHeader, takeSelectOptionbyCode} from '../../lib/commonUiUtils';

const RandomSaveModal = ({modalShow, handleClose, singleTestData, 
    saveRandomCall, updateRandomCall, testRadomCall, codeData, testData, randomStyle, onChangeRandomStyle}) => {
    
    const [t] = useTranslation();
    

    const formik = useFormik({
        enableReinitialize: true,
        initialValues: singleTestData,
        validationSchema: Yup.object({
            randomKey: Yup.string().required(
                getInputValidMsg("Key")
            ),
            randomDesc: Yup.string().required(
                getInputValidMsg(" ")
            )
        }),
        onSubmit: (values) => { 

            if(testData === ""){
                // eslint-disable-next-line no-alert
                window.alert(t('common.validation.playTest'));
                return;
            }

            if(values.id === -1){
                saveRandomCall(values);
            }
            else{
                updateRandomCall(values);
            }
        }
    });

    
    const onClickRandomTest= () => {
        testRadomCall(formik.values);
    }

 
    /*
     * renderSsnCondCol ()
     * cond1 : number 
     * cond2 : number 
     * cond3 :  ('', '-', ':', "~")
     */
    const renderSsnCondCol = () => {
        return (<>
            <Accordion defaultActiveKey="0">
                <Accordion.Item eventKey="0">
                    <Accordion.Header className="accordion-header2"> </Accordion.Header>
                    <Accordion.Body>
                        <Row>
                            <Col xs="3">
                                <FloatingLabel
                                    label={t('pages.random.minAge')}
                                    className="mb-3"
                                >
                                    <FormControl
                                        type="number"
                                        {...formik.getFieldProps("conditionsSsn.cond1")}
                                       
                                    />
                                </FloatingLabel>
                            </Col>
                            <Col xs="3">
                                <FloatingLabel
                                    label={t('pages.random.maxAge')}
                                    className="mb-3"
                                >
                                    <FormControl
                                        type="number" 
                                        {...formik.getFieldProps("conditionsSsn.cond2")}
                                      
                                    />
                                </FloatingLabel>
                            </Col>
                            <Col xs="5">
                                <FloatingLabel
                                    label="Format (AAAAAA-BBBBBBB)"
                                    className="mb-3"
                                >
                                    <FormControl
                                        type="text" 
                                        {...formik.getFieldProps("conditionsSsn.cond3")}
                                        maxLength="14"
                                    />
                                </FloatingLabel>
                            </Col></Row> 
                    </Accordion.Body>
                </Accordion.Item>
            </Accordion>
            
        </>);
    }

    /*
     * renderDateCondCol ()
     * cond1 :  
     * cond2 :  
     * cond3 : fromat
     */
    const renderDateCondCol = () => {
        return (<>
            <Accordion defaultActiveKey="0">
                <Accordion.Item eventKey="0">
                    <Accordion.Header className="accordion-header2"> </Accordion.Header>
                    <Accordion.Body>
                        <Row>
                            <Col xs="4">
                                <FloatingLabel
                                    label={t('pages.random.minDate')}
                                    className="mb-3"
                                >
                                    <FormControl
                                        type="date"
                                        {...formik.getFieldProps("conditionsDate.cond1")}
                                    />
                                </FloatingLabel>
                            </Col>
                            <Col xs="4">
                                <FloatingLabel
                                    label={t('pages.random.maxDate')}
                                    className="mb-3"
                                >
                                    <FormControl
                                        type="date"
                                        {...formik.getFieldProps("conditionsDate.cond2")}
                                       
                                    />
                                </FloatingLabel>
                            </Col>
                            <Col xs="4">

                                <FloatingLabel
                                    label="Format (yyyy-mm-dd)"
                                    className="mb-3"
                                >
                                    <FormControl
                                        type="text" 
                                        {...formik.getFieldProps("conditionsDate.cond3")}
                                        maxLength="14"
                                    />
                                </FloatingLabel>
                            </Col>

                        </Row>
                    </Accordion.Body>
                </Accordion.Item>
            </Accordion>
            
        </>);
    }


    /*
     * renderNumberCol (number)
     * cond1 : 
     * cond2 : 
     * cond3 : 
     */
    const renderNumberCol = () => {
        return (<>
            <Accordion defaultActiveKey="0">
                <Accordion.Item eventKey="0">
                    <Accordion.Header className="accordion-header2">,   </Accordion.Header>
                    <Accordion.Body>
                        <Row>
                            <Col xs="4">
                                <FloatingLabel
                                    label={t('pages.random.minNumber')}
                                    className="mb-3"
                                >
                                    <FormControl
                                        type="number"
                                        {...formik.getFieldProps("conditionsNumber.cond1")} 
                                    />
                                </FloatingLabel>
                            </Col>
                            <Col xs="4">
                                <FloatingLabel
                                    label={t('pages.random.maxNumber')}
                                    className="mb-3"
                                >
                                    <FormControl
                                        type="number"
                                        {...formik.getFieldProps("conditionsNumber.cond2")} 
                                    />
                                </FloatingLabel>
                            </Col>
                        </Row>
                    </Accordion.Body>
                </Accordion.Item>
            </Accordion>

            
        </>);
    }

    /*
     * renderNumberCol (List)
     * 
     *  
     * contents
     */
    const renderListCol = () => {
        return (<>
            <Accordion defaultActiveKey="0">
                <Accordion.Item eventKey="0">
                    <Accordion.Header className="accordion-header2"> </Accordion.Header>
                    <Accordion.Body>
                        <Row>
                            <Col xs="4">
                                <FloatingLabel label={t('pages.random.separator')}
                                    className="mb-3">
                                    <Form.Select
                                        {...formik.getFieldProps("conditionsList.cond3")}
                                    >
                                        <option value="," >,</option>
                                        <option value="-">-</option>
                                        <option value="/">/</option>
                                        <option value=":">:</option>
                                    </Form.Select>
                                </FloatingLabel>
                            </Col>
                            <Col xs="4">
                                <FloatingLabel label={t('pages.random.returnType')}
                                    className="mb-3">
                                    <Form.Select
                                        {...formik.getFieldProps("conditionsList.cond2")}
                                    >
                                        <option value="string" defaultValue>String</option>
                                        <option value="number">Number</option>
                                    </Form.Select>
                                </FloatingLabel>
                            </Col>
                            <Col xs="11">
                                <FloatingLabel
                                    label=""
                                >
                                    <FormControl
                                        type="text"
                                        {...formik.getFieldProps("conditionsList.cond1")}
                                       
                                    />
                                </FloatingLabel>
                            </Col>
                        </Row>
                    </Accordion.Body>
                </Accordion.Item>
            </Accordion>
           
        </>);
    }

    const renderDDayCol = () => {
        return (<>
            <Accordion defaultActiveKey="0">
                <Accordion.Item eventKey="0">
                    <Accordion.Header className="accordion-header2">D-day </Accordion.Header>
                    <Accordion.Body>
                        <Row>
                            <Col xs="4">
                                <FloatingLabel
                                    label="+- "
                                >
                                    <FormControl
                                        type="number"
                                        {...formik.getFieldProps("conditionsDday.cond1")} 
                                    />
                                </FloatingLabel>
                            </Col>
                            <Col xs="6">
                                <FloatingLabel
                                    label="Format (yyyy-mm-dd)"
                                >
                                    <FormControl
                                        type="text" 
                                        {...formik.getFieldProps("conditionsDday.cond3")}
                                        maxLength="14"
                                    />
                                </FloatingLabel>
                            </Col>
                        </Row>
                    </Accordion.Body>
                </Accordion.Item>
            </Accordion>
            
        </>);
    }
 
    const renderRandCodeCond = () => {
        
      
        if (randomStyle=== 'SSN') {
            return renderSsnCondCol();
        } if (randomStyle === 'NUMBER') {
            return renderNumberCol();
        } if (randomStyle === 'DATE') {
            return renderDateCondCol();
        } if (randomStyle === 'LIST') {
            return renderListCol();
        } if (randomStyle === 'D_DAY') {
            return renderDDayCol();
        }

       
        return '';
    };


    const renderRandCodeOption = () => {
       
        return (
            <>
                <FloatingLabel
                    label={t('pages.random.randType')}
                    className="mb-2"
                >
                    <Form.Select
                        // {...formik.getFieldProps('randomStyle')}
                        value={randomStyle}
                        onChange={(e)=>onChangeRandomStyle(e.target.value)}
                        disabled={formik.values.id !== -1}
                    >
                        {takeSelectOptionbyCode(codeData, 'RandomStyle')}
                    </Form.Select>
                </FloatingLabel>
               
            </>
        );
        
    }
    /*
     * sava button render
     */ 
    const renderSaveButton = () => {
        
        return (
            <Button
                theme="primary"
                type="submit"
            >
                {t('common.button.save')}
            </Button>
        );
    }

    /*
     * renderTitle ->    
     */
    const renderTitle = () => {

        if(formik.values.id === -1){
            return t('pages.random.randAdd')
        }
        return t('pages.random.randMod')
        
    }

    
    return (
        <>
            <Modal
                show={modalShow}
                onHide={handleClose}
                size="lg"
                onExited={() => formik.resetForm()}
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Form onSubmit={formik.handleSubmit}>
                    {renderModalHeader(renderTitle())}
                    <Modal.Body>
                        <Accordion defaultActiveKey="0">
                            <Accordion.Item eventKey="0">
                                <Accordion.Header> </Accordion.Header>
                                <Accordion.Body>
                                    <Row>
                                        <Col xs="4">
                                            {renderRandCodeOption()}
                                        </Col>
                                        <Col xs="5">
                                            <FloatingLabel
                                                label=" "
                                                className="mb-2"
                                            >
                                                <Form.Control
                                                    disabled
                                                    value={testData}
                                                />
                                            </FloatingLabel>
                                        </Col>   
                                        <Col xs="1">
                                            <Button
                                                theme="danger"
                                                onClick={onClickRandomTest}
                                                // isLoading={testLoading}
                                                style={{height: '80%'}}
                                            >
                                                {t('common.button.test')}
                                            </Button>
                                        </Col>
                                    </Row>
                                    <Row> 
                                        <Col xs="4">
                                            <FloatingLabel
                                                label="  ($$ )"
                                                className="mb-2"
                                            >
                                                <FormControl
                                                    {...formik.getFieldProps('randomKey')}
                                                    maxLength="15"
                                                    disabled={formik.values.id !== -1}
                                                /> {printFormError(
                                                    formik,
                                                    'randomKey'
                                                )}
                                            </FloatingLabel>
                                           
                                        </Col>
                                        <Col xs="8">
                                            <FloatingLabel
                                                label=" "
                                                className="mb-2"
                                            >
                                                <FormControl
                                                    type="text"
                                                    {...formik.getFieldProps('randomDesc')}
                                                    maxLength="30"
                                                /> {printFormError(
                                                    formik,
                                                    'randomDesc'
                                                )}
                                            </FloatingLabel>
                                           
                                        </Col>
                                    </Row> 
                                </Accordion.Body>
                            </Accordion.Item>
                        </Accordion>
                        <br />
                        {renderRandCodeCond()}
                       
                    </Modal.Body>
                    <Modal.Footer>
                        {renderSaveButton()}
                        <Button theme="default" onClick={handleClose}>
                            {t('common.button.close')}
                        </Button>
                    </Modal.Footer>
                </Form>
            </Modal>
        </>
    );
};
export default RandomSaveModal;
