import React from 'react';
import {FaSearch, FaTrash} from 'react-icons/fa';
import {useTranslation} from 'react-i18next';
import Table from '../../components/table/Table';
import Button from '../../components/button/Button';
import {renderHttpMethod} from '../../lib/commonUiUtils';
import {changeToDate} from '../../lib/moment';

const LayoutListTable = ({data, moveLayoutDetail, movePage, onDelete}) => {
    const [t] = useTranslation();
    const tableName = t('pages.layout.interfaceList');
    const rowKey = 'id';
    const onDoubleClick = (_id, row) => {
        moveLayoutDetail(row);
    };

    const columns = [
        {
            title: 'URL Path',
            align: 'left',
            key: 'urlPath',
            render: (_id, row) => {
                return row.urlInfo?.urlPath;
            }
        },
        {
            title: t('pages.scenario.httpMethod'),
            width: '5%',
            key: 'httpMethod',
            align: 'center', // left, center, right (default:center)
            render: (_id, row) => {
                return renderHttpMethod(row.urlInfo?.httpMethod);
            }
        },
        {
            title: 'Service ID',
            key: 'serviceId',
            align: 'left',
            render: (_id, row) => {
                return row.serviceInfo.serviceId;
            }
        },
        {
            title: 'Service Desc',
            key: 'serviceDesc',
            align: 'left',
            render: (_id, row) => {
                return row.serviceInfo.serviceDesc;
            }
        },
        {
            title: t('pages.layout.regCode'),
            width: '5%',
            key: 'inflowPath',
            align: 'center',
            render: (_id, row) => {
                return row.inflowPath;
            }
        },
        {
            title: 'API ',
            width: '5%',
            key: 'transactionType',
            align: 'center', // left, center, right (default:center)
            render: (_id, row) => {
                return row.transactionType;
            }
        },
        {
            title: ' ',
            key: 'modifiedDate',
            width: '8%',
            // eslint-disable-next-line no-unused-vars
            render: (id, row, column) => {
                return changeToDate(row.modifiedDate);
            }
        },
        {
            title: '',
            key: 'button',
            width: '5%',
            align: 'right',
            // eslint-disable-next-line no-unused-vars
            render: (id, row, _column) => {
                return (
                    <>
                        <Button
                            theme="outline-success"
                            onClick={() => moveLayoutDetail(row)}
                        >
                            <FaSearch />
                        </Button>
                        <Button
                            theme="outline-danger"
                            onClick={() => onDelete(id)}
                        >
                            <FaTrash />
                        </Button>
                    </>
                );
            }
        }
    ];

    return (
        <>
            <Table
                tableName={tableName}
                onDoubleClick={onDoubleClick}
                movePage={movePage}
                rowKey={rowKey}
                columns={columns}
                data={data}
            />
        </>
    );
};

export default LayoutListTable;
