/* eslint-disable no-shadow */
/* eslint-disable no-unused-vars */
import React from 'react';
import {
    Form,
    Modal,
    Row,
    Col,
    InputGroup,
    FormControl,
    Accordion
} from 'react-bootstrap';
import {useTranslation} from 'react-i18next';
import {useFormik} from 'formik';
import Button from '../../../components/button/Button';

import {jsonStrFormatNotPretty, validJsonString} from '../../../lib/jsonUtils';
// eslint-disable-next-line no-unused-vars
const UitestStepPopXhr2 = ({
    scenarioData,
    selectedCaseId,
    setDetailCaseList,
    handleClose,
    tempkey,
    setTempKeyRezen,
    payload,
    setPayload,
    onClickTransform,
    layoutDetail,
    onChangeRequestBodyIR
}) => {
    const [t] = useTranslation();

    const onChangePayload = (e) => {
        setPayload(e.target.value);
    };

    const formik = useFormik({
        initialValues: {
            urlPath: ''
        },

        onSubmit: (values) => {
            const caseList = [...scenarioData.data.caseList];

            const caseLoop = caseList.map((cUnit, cIndex) => {
                const theCase = cUnit;

                if (cUnit.id === selectedCaseId) {
                    let data = theCase.stepList;
                    if (data == null) {
                        data = [];
                    }

                    const newStep = {
                        id: tempkey,
                        urlPath: layoutDetail.urlPath,
                        fnCode: layoutDetail.fnCode,
                        httpMethod: layoutDetail.httpMethod,
                        serviceId: layoutDetail.serviceId,
                        inputData1: jsonStrFormatNotPretty(
                            layoutDetail.inputData
                        ),
                        inputData2: '',
                        isSkipError: values.isSkipError,
                        stepCode: 'xhr'
                    };
                    setTempKeyRezen();

                    data.push(newStep);

                    const aLoop = data.map((unit, index) => {
                        const temp = unit;
                        temp.idx = index;
                        return temp;
                    });

                    theCase.stepList = aLoop;
                }
                return theCase;
            });
            setDetailCaseList(caseLoop);
            handleClose();
        }
    });

    const renderSaveButton = () => {
        if (!layoutDetail) return '';
        return (
            <Button
                theme="primary"
                type="submit"
                // isLoading={createLoading}
            >
                {t('common.button.register')}
            </Button>
        );
    };

    const renderInputParsingResult = () => {
        const isValid = validJsonString(layoutDetail.inputData);
        if (isValid) {
            return (
                <Form.Label style={{float: 'right'}}>
                    {t('common.text.validJsonFormat')}
                </Form.Label>
            );
        }
        return (
            <Form.Label style={{float: 'right', color: 'red'}}>
                {t('common.text.invalidJsonFormat')}
            </Form.Label>
        );
    };

    const renderStepDetail = () => {
        if (!layoutDetail) return '';

        const renderUrlPath = () => {
            if (layoutDetail) {
                if (
                    layoutDetail.fnCode !== '' &&
                    layoutDetail.fnCode !== 'NONE'
                ) {
                    return `${layoutDetail.urlPath} (${layoutDetail.fnCode})`;
                }

                return layoutDetail.urlPath;
            }

            return '';
        };

        return (
            <>
                <Accordion defaultActiveKey="0">
                    <Accordion.Item eventKey="0">
                        <Accordion.Header className="accordion-header2">
                            {t('pages.scenario.infoXhr')}
                        </Accordion.Header>
                        <Accordion.Body>
                            <Row>
                                <Col xs="10">
                                    <Form.Group controlId="urlPath">
                                        <InputGroup className="mb-2">
                                            <FormControl
                                                disabled
                                                // 20220625 kimtaehan URL + fnCode 
                                                // value={layoutDetail && layoutDetail.urlPath}
                                                value={renderUrlPath()}
                                                // onChange={onChangeForm}
                                            />
                                            <Button
                                                theme="secondary"
                                                style={{minWidth: '100px'}}
                                            >
                                                {layoutDetail &&
                                                    layoutDetail.httpMethod}
                                            </Button>
                                        </InputGroup>
                                    </Form.Group>
                                </Col>
                                <Col xs="12">
                                    <div key="isSkipError" className="mb-3">
                                        <Form.Check
                                            type="checkbox"
                                            id="isSkipError"
                                            label={t(
                                                'pages.scenario.isSkipError'
                                            )}
                                            {...formik.getFieldProps(
                                                'isSkipError'
                                            )}
                                            checked={formik.values.isSkipError}
                                        />
                                    </div>
                                </Col>
                                <Col xs="12">
                                    <Form.Group controlId="inputData">
                                        <Form.Label>Reqeust Body</Form.Label>
                                        {renderInputParsingResult()}

                                        <Form.Control
                                            as="textarea"
                                            style={{height: '400px'}}
                                            placeholder=""
                                            onChange={onChangeRequestBodyIR}
                                            value={
                                                layoutDetail &&
                                                layoutDetail.inputData
                                            }
                                        />
                                    </Form.Group>
                                </Col>
                            </Row>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
            </>
        );
    };

    return (
        <>
            <Form onSubmit={formik.handleSubmit}>
                <Row>
                    <Col xs="6">
                        <Accordion defaultActiveKey="0">
                            <Accordion.Item eventKey="0">
                                <Accordion.Header className="accordion-header3">
                                    {t('pages.scenario.infoXhr')}
                                </Accordion.Header>
                                <Accordion.Body>
                                    <Row>
                                        <Col xs="7">
                                            <label htmlFor="myinput">
                                                PayLoad Copy
                                            </label>
                                        </Col>
                                        <Col xs="3" />
                                        <Col xs="2">
                                            <Button
                                                type="button"
                                                className="btn btn-block btn-secondary btn-search"
                                                // isLoading={searchLoading}
                                                onClick={onClickTransform}
                                            >
                                                {t('common.button.transform')}
                                            </Button>
                                        </Col>
                                        <Col xs="12">
                                            <Form.Control
                                                as="textarea"
                                                style={{height: '492px'}}
                                                placeholder=""
                                                onChange={onChangePayload}
                                                value={payload}
                                            />
                                        </Col>
                                    </Row>
                                </Accordion.Body>
                            </Accordion.Item>
                        </Accordion>
                    </Col>
                    <Col xs="6">{renderStepDetail()}</Col>
                </Row>
                <Modal.Footer>
                    {renderSaveButton()}

                    <Button theme="default" onClick={handleClose}>
                        {t('common.button.close')}
                    </Button>
                </Modal.Footer>
            </Form>
        </>
    );
};

export default UitestStepPopXhr2;
