/* eslint-disable no-unused-vars */
import React, {useEffect} from 'react';
import {useSelector, useDispatch} from 'react-redux';
import LogTable from '@pages/log/LogTable';
import {useHistory} from 'react-router-dom';
import {useTranslation} from 'react-i18next';
import {toast} from 'react-toastify';
import Spinner from '../../components/spinner/Spinner';

import {getErrorMsg} from '../../lib/commonUiUtils';
import {
    searchLogSnapshot,
    setSearchLogSnapshotForm,
    deleteLogSnapshot,
    clearDeleteLogSnapshot
} from '../../store/log';

/*
 *   Container
 */
function LogTableContainer() {
    const dispatch = useDispatch();
    const [t] = useTranslation();
    const history = useHistory();

    const {data: codeData} = useSelector((state) => state.common.searchCode);

    //  
    const {data: searchData, loading: searchLoading} = useSelector(
        (state) => state.log.searchLogSnapshotRes
    );

    const {searchLogSnapshotForm} = useSelector((state) => state.log);

    //    
    const {data: memberInfoData} = useSelector(
        (state) => state.auth.memberInfo
    );

    const movePage = (page) => {
        const searchLogSnapshotFormT = {...searchLogSnapshotForm};
        searchLogSnapshotFormT.page = page;
        dispatch(setSearchLogSnapshotForm(searchLogSnapshotFormT));
        dispatch(searchLogSnapshot(searchLogSnapshotFormT));
    };

    /**
     *  Log Snapshot 
     */
    const onClickDelete = (id) => {
        // eslint-disable-next-line no-alert
        if (window.confirm(t('common.alert.checkDelete'))) {
            dispatch(deleteLogSnapshot(id));
        }
    };

    /**
     *   .
     * Log Snapshot ID   .
     * @returns
     */
    const moveHistory = (id, row) => {
        history.push({
            pathname: '/log/detail',
            state: {
                logSnapshotId: row.logSnapshotId,
                sendDateTime: row.sendDateTime
            }
        });
    };

    //   
    const {
        data: deleteLogSnapshotData,
        loading: deleteLogSnapshotLoading,
        error: deleteLogSnapshotError
    } = useSelector((state) => state.log.deleteLogSnapshotRes);

    useEffect(() => {
        // Log snapshot delete error
        if (!deleteLogSnapshotLoading && deleteLogSnapshotError) {
            toast.error(getErrorMsg(deleteLogSnapshotError, 'save'));
            dispatch(clearDeleteLogSnapshot());
        }
        // Log snapshot delete success
        else if (!deleteLogSnapshotLoading && deleteLogSnapshotData) {
            toast.success(t('common.alert.updateSuccess'));
            dispatch(clearDeleteLogSnapshot());
            // 
            dispatch(searchLogSnapshot(searchLogSnapshotForm));
        }
    }, [deleteLogSnapshotData, deleteLogSnapshotError]);

    return (
        <>
            <LogTable
                codeData={codeData}
                movePage={movePage}
                data={searchData}
                onClickMove={moveHistory}
                onClickDelete={onClickDelete}
                memberInfoData={memberInfoData}
            />
            <Spinner isLoading={deleteLogSnapshotLoading} />
        </>
    );
    ///
}

export default LogTableContainer;
