// `${PREFIX}/XXXX`;
const PREFIX = 'SERVER';

/**
 * SCENARIO List Paging  (TR)
 * searchScenario (SEARCH_SERVER) : 
 * clearSearchScenario (CLEAR_SEARCH_SERVER) : 
 * setSearchScenarioName (SET_SEARCH_SCENARIO_NAME) :  
 * setSearchOwner (SET_SEARCH_OWNER) :  
 */
const SEARCH_SCENARIO = `${PREFIX}/SEARCH_SCENARIO`;
const SEARCH_SCENARIO_SUCCESS = `${PREFIX}/SEARCH_SCENARIO_SUCCESS`;
const SEARCH_SCENARIO_ERROR = `${PREFIX}/SEARCH_SCENARIO_ERROR`;
const CLEAR_SEARCH_SCENARIO = `${PREFIX}/CLEAR_SEARCH_SCENARIO`;

const SET_SEARCH_FORM = `${PREFIX}/SET_SEARCH_FORM`;

export const searchScenario = (searchForm) => ({
    type: SEARCH_SCENARIO,
    payload: searchForm
});

export const clearSearchScenario = () => ({
    type: CLEAR_SEARCH_SCENARIO
});

export const setSearchForm = (searchForm) => ({
    type: SET_SEARCH_FORM,
    payload: {
        searchForm
    }
});

/**
 * SCENARIO  (TR)
 * deleteScenario (DELETE_SCENARIO) : 
 * deleteScenarioDataClear (DELETE_SCENARIO_DATA_CLEAR) : 
 */
