const SEARCH_SCENARIO_HISTORY_DETAIL_SUCCESS = `${PREFIX}/SEARCH_SCENARIO_HISTORY_DETAIL_SUCCESS`;
const SEARCH_SCENARIO_HISTORY_DETAIL_ERROR = `${PREFIX}/SEARCH_SCENARIO_HISTORY_DETAIL_ERROR`;
