import React from 'react';
import {useTranslation} from 'react-i18next';
import {Row, Col, Form, FloatingLabel} from 'react-bootstrap';
import Button from '../../components/button/Button';
import Card from '../../components/card/Card';

const MemberSearch = ({
    onSearch,
    searchForm,
    onChangeSearchForm,
    onClickNewMember
}) => {
    const [t] = useTranslation();

    const onChangeForm = (e) => {
        e.preventDefault();
        const memberSearchFormT = {...searchForm};
        memberSearchFormT[e.target.id] = e.target.value;
        onChangeSearchForm(memberSearchFormT);
    };

    const onClickSearch = (e) => {
        e.preventDefault();

        const memberSearchFormT = {...searchForm};
        memberSearchFormT.page = 0;
        onChangeSearchForm(memberSearchFormT);
        onSearch(memberSearchFormT);
    };

    const onCheckEnter = (e) => {
        if (e.key === 'Enter') {
            e.preventDefault();
            onSearch();
        }
    };
    return (
        <>
            <Card
                title={t('common.text.searchCardTitleName')}
                body={
                    <>
                        <Row onKeyPress={onCheckEnter}>
                            <Col xs="6">
                                <Row>
                                    <Col xs="6">
                                        <FloatingLabel
                                            label=" ."
                                            className="mb-2"
                                        >
                                            <Form.Control
                                                type="text"
                                                maxLength="30"
                                                id="searchText"
                                                onChange={onChangeForm}
                                                value={searchForm.searchText}
                                            />
                                        </FloatingLabel>
                                    </Col>
                                </Row>
                            </Col>
                            <Col xs="4" />
                            <Col xs="1">
                                <Form.Group>
                                    <Form.Label />
                                    <Button
                                        type="button"
                                        className="btn btn-block btn-secondary btn-search"
                                        // isLoading={searchLoading}
                                        onClick={onClickSearch}
                                    >
                                        {t('common.button.search')}
                                    </Button>
                                </Form.Group>
                            </Col>
                            <Col xs="1">
                                <Form.Group>
                                    <Form.Label />
                                    <Button
                                        type="button"
                                        className="btn btn-block btn-success btn-search"
                                        onClick={onClickNewMember}
                                    >
                                        + 
                                    </Button>
                                </Form.Group>
                            </Col>
                        </Row>
                    </>
                }
            />
        </>
    );
};

export default MemberSearch;
